import React, { forwardRef, useImperativeHandle, useRef, useState, useEffect, useCallback } from 'react';
import {
  View,
  StatusBar,
  TouchableOpacity,
  Platform,
  KeyboardAvoidingView,
  Linking,
  Alert,
  AccessibilityInfo,
  BackHandler,
  PermissionsAndroid,
} from 'react-native';
import { WebView, WebViewNavigation } from 'react-native-webview';
import CookieManager from '@react-native-cookies/cookies';
import { Icon, InnerLoading, NativeModuleClient, Text } from 'superapp-common-component';
import { WebViewProgressEvent, WebViewMessageEvent } from 'react-native-webview/lib/WebViewTypes';
import { SafeAreaView, useSafeAreaInsets } from 'react-native-safe-area-context';
import { colors } from 'superapp-common-component';
import ROUTE_PATH from '@/navigators/routePath';
import APP_ENV from '@/config/app.config';
import { a11y, getFileNameFromUrl, isEmpty } from '@/utils/utils';

import Modal, { useModal } from '@/components/Modal/Modal';

import styles from './styles';
import { useTranslation } from 'react-i18next';
import { EVENT_NAMES } from '@/utils/constants';
import dayjs from 'dayjs';
import { onDowloadIcareCustomerFilePdf } from '@/utils/handleFile';
import { useDispatch } from 'react-redux';
import { logout } from '@/stores/authentication/actions';
import { getProfile } from '@/stores/global/actions';

type TSharedToken = {
  url: string;
  accessToken: string;
  refreshToken: string;
  refreshTokenOkta: string;
  domain?: string;
  expiredAccessToken?: string;
  expiredRefreshToken: string;
  expiredRefreshTokenOkta: string;
};

export const useWebViewAuthenticated = (): [React.MutableRefObject<TModalWebviewRef | null>, () => TModalWebviewRef] => {
  const ref = useRef<TModalWebviewRef>(null);
  const getInstance = () => ref.current!;

  return [ref, getInstance];
};

export type TModalWebviewRef = {
  openUri: (uri: string, options?: { sharedToken: TSharedToken }) => void;
  onBack: () => void;
  onForward: () => void;
  injectedJavaScript: (value: string) => void;
};

export type TModalWebviewProps = {
  onOpenPayment?: (url?: string) => void;
  onClose?: () => void;
  lang?: 'en' | 'vi';
};

type TWebview = WebView<{
  ref: unknown;
  source: {
    uri: string;
  };
  onNavigationStateChange: (evt: WebViewNavigation) => void;
  onLoadProgress: (evt: WebViewProgressEvent) => void;
}>;

const isPlatformIos = Platform.OS === 'ios';

const WebViewAuthenticated: React.ForwardRefRenderFunction<TModalWebviewRef, TModalWebviewProps> = (props, ref) => {
  const insets = useSafeAreaInsets();
  const { onClose, onOpenPayment, lang = 'vi' } = props;
  const webviewRef = useRef<TWebview>(null);
  const [isDidMount, setIsDidMount] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const [uri, setUri] = useState('');
  const [, setPreviewUrl] = useState('');
  const [isCanGoBack, setIsCanGoBack] = useState(false);
  const [isCanGoForward, setIsCanGoForward] = useState(false);
  const [loading, setLoading] = useState(true);
  const [injectedLangScript, setInjectedLangScript] = useState('');
  const sharedToken = useRef<TSharedToken>();
  const { t } = useTranslation();
  const isShowBackButton = useRef<boolean>(false);
  const forceReloadProfile = useRef<boolean>(false);
  const [modalRef, modalInstance] = useModal();
  const [reduceMotionEnabled, setReduceMotionEnabled] = useState(false);
  const dispatch = useDispatch();

  const onBackdropPress = () => {
    setIsVisible(false);
    setUri('');
    if (onClose) {
      onClose();
    }
    if (forceReloadProfile.current) {
      dispatch(getProfile());
    }
  };

  const onBackWebview = useCallback(() => {
    isCanGoBack && webviewRef.current?.goBack();
    // isShowBackButton.current = false;
  }, [isCanGoBack]);

  const onForwardWebview = useCallback(() => {
    isCanGoForward && webviewRef.current?.goForward();
  }, [isCanGoForward]);

  const handleNavigationRule = (canGoBack: boolean, canGoForward: boolean, url: string) => {
    try {
      setIsCanGoBack(canGoBack && !url.includes(APP_ENV.ICARE_V2_URL));
      setIsCanGoForward(canGoForward);
      setPreviewUrl(url);
    } catch {}
  };

  const onNavigationStateChange = (evt: WebViewNavigation) => {
    const { canGoBack, canGoForward, url } = evt;
    if (isPlatformIos) {
      handleNavigationRule(canGoBack, canGoForward, url);
    }
  };

  const onLoadProgress = (evt: WebViewProgressEvent) => {
    if (!isPlatformIos) {
      const {
        nativeEvent: { url, canGoBack, canGoForward },
      } = evt;
      handleNavigationRule(canGoBack, canGoForward, url);
    }
  };

  const onLoadStart = (syntheticEvent: any) => {
    const { nativeEvent } = syntheticEvent;
    if (nativeEvent?.url?.includes(APP_ENV.ICARE_V2_URL)) {
      setLoading(true);
    }
  };

  const handleCallHotline = (hotlineNumber: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NOTIFICATIONS,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Call Hotline',
    });

    Alert.alert(`${t('common.modalAML.callHotline.call')} ${hotlineNumber}`, '', [
      {
        text: t('common.modalAML.close'),
      },
      {
        text: t('common.modalAML.callHotline.call'),
        onPress: () => {
          Linking.openURL(`tel:${hotlineNumber?.replace(/\s/g, '')}`);
        },
      },
    ]);
  };

  const handleSendEmail = (email: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NOTIFICATIONS,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Send Mail',
    });
    Linking.openURL(`mailto:${email}`);
  };

  const grantAndroidCameraPermission = async () => {
    if (Platform.OS !== 'android') {
      return;
    }
    const granted = await PermissionsAndroid.check(PermissionsAndroid.PERMISSIONS.CAMERA);
    if (!granted) {
      await PermissionsAndroid.request(PermissionsAndroid.PERMISSIONS.CAMERA);
    }
  };

  const onMessage = (event: WebViewMessageEvent) => {
    // if (!__DEV__) {
    const eventName = event.nativeEvent.data;

    switch (eventName) {
      case EVENT_NAMES.LOGOUT:
        onBackdropPress();
        dispatch(logout({ neededCallApi: false, isChangePassword: true }));
        break;
      case EVENT_NAMES.UPDATE_PROFILE:
        forceReloadProfile.current = true;
        break;
      case EVENT_NAMES.SHOW_BACK_BUTTON:
        isShowBackButton.current = true;
        break;
      case EVENT_NAMES.CLOSE_ICARE_APP:
        onBackdropPress();
        break;
      case EVENT_NAMES.STOP_LOADING:
        setInjectedLangScript(`(function(){ 
          window.localStorage.removeItem('IsFirstLoadWebView');
        })();`);
        setLoading(false);
        break;
      case EVENT_NAMES.ANDROID_CAMERA_PERMISSION:
        (async () => {
          await grantAndroidCameraPermission();
        })();
        break;
      default:
        // handle message from icare-customer
        // format APPLICATION_ID|FEATURE_ID|datas
        const data = eventName ? eventName.split('|') : undefined;

        if (!data || data.length < 3) {
          break;
        }

        // check FEATURE_ID
        if (data[1] === EVENT_NAMES.FEATURE_ID_HOTLINE) {
          handleCallHotline(data[2]); // hotline number
          break;
        }

        // check PAYMENT
        if (data[1] === EVENT_NAMES.PAYMENT) {
          onOpenPayment && onOpenPayment(data[2]); // payment url
          break;
        }

        if (data[1] === EVENT_NAMES.FEATURE_ID_EMAIL) {
          handleSendEmail(data[2]); // email address
          break;
        }

        for (let i = 2; i <= data.length - 1; i++) {
          const fileName = getFileNameFromUrl(data[i]);
          fileName && onDowloadIcareCustomerFilePdf(fileName, data[i]);
        }

        break;
    }
    // }
  };

  const handleShareToken = async (options: TSharedToken) => {
    await Promise.all([
      CookieManager.set(options.url, {
        name: 'myaiaAccessToken',
        value: options.accessToken,
        expires: options.expiredAccessToken,
      }),
      CookieManager.set(options.url, {
        name: 'myaiaRefreshToken',
        value: options.refreshToken,
        expires: options.expiredRefreshToken,
      }),
      CookieManager.set(options.url, {
        name: 'myaiaRefreshTokenOkta',
        value: options.refreshTokenOkta,
        expires: options.expiredRefreshTokenOkta,
      }),
      CookieManager.set(options.url, {
        name: 'myaiaRefreshTokenOktaExpired',
        value: options.expiredRefreshTokenOkta,
        expires: options.expiredRefreshTokenOkta,
      }),
    ]);
  };

  const onInjectedJavaScript = (value: string) =>{
    if (value && webviewRef.current) {
      console.log('startingg inject value', value);
      
      webviewRef.current?.injectJavaScript(value);
    }
  }

  useEffect(() => {
    if (isVisible) {
      (async () => {
        modalInstance().show();
        sharedToken.current && (await handleShareToken(sharedToken.current));
        setIsDidMount(true);
        forceReloadProfile.current = false;
      })();
    } else {
      modalInstance()?.close();
    }
  }, [isVisible]);

  const imperativeHandle = (): TModalWebviewRef => ({
    openUri: (value: string, options?: { sharedToken: TSharedToken }) => {
      options?.sharedToken && (sharedToken.current = options?.sharedToken);
      setUri(value);
      setIsVisible(true);
    },
    onBack: onBackWebview,
    onForward: onForwardWebview,
    injectedJavaScript: onInjectedJavaScript,
  });

  const renderIconFowardBack = (isActive: boolean) => {
    return isActive ? '#DADADA' : '#969BA0';
  };

  useEffect(() => {
    setInjectedLangScript(`(function(){ 
        window.localStorage.setItem('language', '${lang}');
    })();`);
  }, [lang]);

  useEffect(() => {
    function handleBackAndroid() {
      if (isShowBackButton.current) {
        isShowBackButton.current = false;
        webviewRef.current?.goBack();
        return true;
      }
      return false;
    }

    const reduceMotionChangedSubscription = AccessibilityInfo.addEventListener('reduceMotionChanged', isReduceMotionEnabled => {
      setReduceMotionEnabled(isReduceMotionEnabled);
    });
    AccessibilityInfo.isReduceMotionEnabled().then(isReduceMotionEnabled => {
      setReduceMotionEnabled(isReduceMotionEnabled);
    });

    const subcribe = BackHandler.addEventListener('hardwareBackPress', handleBackAndroid);
    return () => {
      subcribe.remove();
      reduceMotionChangedSubscription.remove();
    };
  }, []);

  useImperativeHandle(ref, imperativeHandle, [onBackWebview, onForwardWebview]);

  return (
    <Modal ref={modalRef} overlayOpacity={0.5}>
      <StatusBar barStyle="light-content" />
      {isDidMount && (
        <SafeAreaView edges={['top']} style={{ backgroundColor: colors.primary.red[100] }}>
          <View style={{ ...styles.container, ...{ paddingBottom: insets.bottom ? Math.max(insets.bottom, 16) : 0 } }}>
            <View style={styles.commandBar}>
              <View style={styles.horizontal}>
                {isShowBackButton.current && isCanGoBack ? (
                  <TouchableOpacity
                    style={styles.btnBack}
                    disabled={!isCanGoBack}
                    onPress={onBackWebview}
                    {...a11y('WebView.btnBackWebViewModal')}
                  >
                    <Icon name="Arrow-Left" size={24} color="white" />
                  </TouchableOpacity>
                ) : (
                  <View style={styles.fakeView} />
                )}
              </View>

              <View style={[styles.flexGrow1, styles.pageInfo]}>
                <Text style={styles.title} numberOfLines={1}>
                  {t('common.title.webView')}
                </Text>
              </View>

              <TouchableOpacity onPress={onBackdropPress} {...a11y('WebView.btnCloseWebViewModal')}>
                <Icon name="Close" size={24} color="white" />
              </TouchableOpacity>
            </View>
            <View style={[styles.flexGrow1, styles.relative]}>
              <KeyboardAvoidingView
                behavior={isPlatformIos ? 'padding' : 'height'}
                keyboardVerticalOffset={60}
                style={!loading && styles.flexGrow1}
                enabled={isPlatformIos ? !reduceMotionEnabled : true}
              >
                <WebView
                  originWhitelist={['*']}
                  textZoom={100}
                  cacheMode="LOAD_NO_CACHE"
                  cacheEnabled={false}
                  ref={webviewRef}
                  source={{ uri }}
                  sharedCookiesEnabled={!isEmpty(sharedToken.current)}
                  thirdPartyCookiesEnabled={!isEmpty(sharedToken.current)}
                  onNavigationStateChange={onNavigationStateChange}
                  onLoadProgress={onLoadProgress}
                  onLoadStart={onLoadStart}
                  onLoad={() => {
                    setLoading(false);
                  }}
                  injectedJavaScript={injectedLangScript}
                  onMessage={onMessage}
                />
              </KeyboardAvoidingView>
              {loading && (
                <View style={[styles.bgrWhite, styles.loadingCenter]}>
                  <InnerLoading />
                </View>
              )}
            </View>
          </View>
        </SafeAreaView>
      )}
    </Modal>
  );
};

export default forwardRef(WebViewAuthenticated);
