import React, { useState, useImperativeHandle, useRef, useEffect } from 'react';
import { View, StatusBar } from 'react-native';
import { InnerLoading } from 'superapp-common-component';
import { WebView, WebViewMessageEvent } from 'react-native-webview';
import jwtDecode from 'jwt-decode';
import CookieManager from '@react-native-cookies/cookies';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';
import { isEmpty } from '@/utils/utils';
import { getItem } from '@/utils/storage';
import { STORAGE_NAME } from '@/utils/constants';

import styles from './styles';
import Loading from '../Loading';

export type TVideoUrlRef = {
  openURL: (value: string) => void;
};

type TVideoUrlProps = {};

type TSharedToken = {
  url: string;
  accessToken: string;
  refreshToken: string;
  domain?: string;
  expiredAccessToken?: string;
  expiredRefreshToken?: string;
};

const VideoUrl = React.forwardRef((props: TVideoUrlProps, ref: any) => {
  const [uri, setURI] = useState<string>('');
  const sharedToken = useRef<TSharedToken>();
  const [modalsRef, modalInstance] = useModal();
  const [injectedLangScript, setInjectedLangScript] = useState('');
  const [currentLanguage, setCurrentLanguage] = useState<'vi' | 'en'>();
  const [isLoadingOpenApp] = useState<boolean>(false);

  const formatDateExpired = (msSeconds: number) => {
    const d = new Date(msSeconds);
    return d.toISOString();
  };

  const onMessage = (event: WebViewMessageEvent) => {
    const eventName = event.nativeEvent.data;
    const data = eventName.split('|');
    console.log(data);
  };

  const setCookiesShareToken = async (options: TSharedToken) => {
    await Promise.all([
      CookieManager.set(options.url, {
        name: 'myaiaAccessToken',
        value: options.accessToken,
        expires: options.expiredAccessToken,
      }),
      CookieManager.set(options.url, {
        name: 'myaiaRefreshToken',
        value: options.refreshToken,
        expires: options.expiredRefreshToken,
      }),
    ]);
  };

  const onOpenURL = async (url: string) => {
    const [accessToken, refreshToken, refreshTokenExprirySeconds, language] = await Promise.all([
      getItem(STORAGE_NAME.ACCESS_TOKEN),
      getItem(STORAGE_NAME.REFRESH_TOKEN),
      getItem<number>(STORAGE_NAME.REFRESH_TOKEN_EXPRIRY_SECONDS, 0),
      getItem<'vi' | 'en'>(STORAGE_NAME.LANGUAGE, 'vi'),
    ]);

    if (!accessToken) return;

    const tokenInfo = jwtDecode<MODEL.ITokenDecode>(accessToken);

    sharedToken.current = {
      url,
      accessToken,
      refreshToken,
      expiredAccessToken: formatDateExpired(tokenInfo.exp * 1000),
      expiredRefreshToken: formatDateExpired((tokenInfo.nbf + refreshTokenExprirySeconds) * 1000),
    };

    await setCookiesShareToken(sharedToken.current);

    setURI(url);
    setCurrentLanguage(language);
    modalInstance().show();
  };

  useImperativeHandle(
    ref,
    () => ({
      openURL: (value: string) => onOpenURL(value),
    }),
    [],
  );

  useEffect(() => {
    setInjectedLangScript(`(function(){ 
      window.localStorage.setItem('language', '${currentLanguage}');
    })();`);
  }, [currentLanguage]);

  return (
    <Modal ref={modalsRef} overlayOpacity={0.5} onDismiss={() => null}>
      <StatusBar barStyle="light-content" />
      <View style={styles.container}>
        <View style={styles.content}>
          {uri !== '' && (
            <WebView
              mixedContentMode="always"
              androidLayerType="hardware"
              textZoom={100}
              source={{ uri }}
              scrollEnabled={false}
              originWhitelist={['*']}
              allowsInlineMediaPlayback
              startInLoadingState={true}
              injectedJavaScript={injectedLangScript}
              sharedCookiesEnabled={!isEmpty(sharedToken.current)}
              thirdPartyCookiesEnabled={!isEmpty(sharedToken.current)}
              renderLoading={() => (
                <View style={[styles.content, styles.bgrWhite]}>
                  <InnerLoading />
                </View>
              )}
              onMessage={onMessage}
            />
          )}
        </View>
      </View>
      <Loading visible={isLoadingOpenApp} />
    </Modal>
  );
});

export default VideoUrl;
