import EncryptedStorage from 'react-native-encrypted-storage';
import _forEach from 'lodash/forEach';
import CryptoJS from 'crypto-js';

import APP_CONFIG from '@/config/app.config';

import { isEmpty } from './utils';

const parseJson = (data: string) => {
  try {
    return JSON.parse(data);
  } catch (err) {
    return data;
  }
};

const toStringUtf8 = (data: CryptoJS.lib.WordArray, df: string) => {
  try {
    const stringData = data.toString(CryptoJS.enc.Utf8);
    return isEmpty(stringData) ? df : stringData;
  } catch (err) {
    return df;
  }
};

const setItem = async (key: string, value: string): Promise<void> => {
  try {
    const encryptData = CryptoJS.AES.encrypt(value, APP_CONFIG.SECRET_KEY_ENCRYPT).toString();
    await EncryptedStorage.setItem(key, encryptData);
  } catch (error) {
    throw new Error('Set item storage fail !');
  }
};

const getItem = async <T = string>(key: string, defaultValue: any = null): Promise<T> => {
  try {
    const data = await EncryptedStorage.getItem(key);

    if (data) {
      const bytes = CryptoJS.AES.decrypt(data!, APP_CONFIG.SECRET_KEY_ENCRYPT);
      const result = parseJson(toStringUtf8(bytes, data) || '');
      return isEmpty(result) ? defaultValue : result;
    }
    return defaultValue;
  } catch (err) {
    throw new Error('Get item storage fail !');
  }
};

const clearStorage = async (): Promise<void> => {
  try {
    await EncryptedStorage.clear();
  } catch (error) {
    throw new Error('Clear storage fail !');
  }
};

const removeItem = async (key: string): Promise<void> => {
  try {
    const isHasValue = await EncryptedStorage.getItem(key);
    if (!isEmpty(isHasValue)) {
      await EncryptedStorage.removeItem(key);
    }
  } catch (error) {
    throw new Error('Remove item storage fail !');
  }
};

const multiRemove = (_: string[]) => {
  const promises: Promise<any>[] = [];
  _forEach(async (__: any, key: any) => {
    const isHasValue = await EncryptedStorage.getItem(key);
    if (!isEmpty(isHasValue)) {
      promises.push(EncryptedStorage.removeItem(key));
    }
  });
  return Promise.all(promises);
};

export { setItem, getItem, removeItem, clearStorage, multiRemove };
