import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  PUT_CAPTCHA_ERROR,
  PUT_CAPTCHA_REQUEST,
  PUT_CAPTCHA_SUCCESS,
  PUT_OTP_ERROR,
  PUT_OTP_REQUEST,
  PUT_OTP_SUCCESS,
  RESET_CAPTCHA_ERROR,
  RESET_OTP_ERROR,
  SAVE_CAPCHA,
  SAVE_USER_ID,
  SAVE_SESSION_ID,
  SAVE_PHONE_NUMBER,
  SAVE_USERNAME,
  SAVE_DOB,
  SAVE_ID_NO,
  SAVE_OTP_ID,
} from './constants';
import initialState, { TForgotUsernameTypeState } from './state';

const requestTypeReducer = (state: TForgotUsernameTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TForgotUsernameTypeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case SAVE_CAPCHA:
        draft.infoCaptcha = action.payload;
        break;
      case PUT_OTP_REQUEST:
        draft.verifyOtpStatus = { isVerifyOtpError: false, errorType: '' };
        break;
      case PUT_OTP_SUCCESS:
        draft.verifyOtpStatus = { isVerifyOtpError: false, errorType: '' };
        break;
      case PUT_OTP_ERROR:
        draft.verifyOtpStatus = { isVerifyOtpError: true, errorType: action.payload };
        break;
      case RESET_OTP_ERROR:
        draft.verifyOtpStatus = { isVerifyOtpError: undefined, errorType: '' };
        break;
      case PUT_CAPTCHA_REQUEST:
        draft.isCaptchaError = {
          status: false,
          errorType: '',
        };
        break;
      case PUT_CAPTCHA_SUCCESS:
        draft.isCaptchaError = {
          status: false,
          errorType: '',
        };
        break;
      case PUT_CAPTCHA_ERROR:
        draft.isCaptchaError = {
          status: true,
          errorType: action.payload,
        };
        break;
      case RESET_CAPTCHA_ERROR:
        draft.isCaptchaError = {
          status: undefined,
          errorType: '',
        };
        break;
      case SAVE_USER_ID:
        draft.userId = action.payload;
        break;
      case SAVE_SESSION_ID:
        draft.sessionId = action.payload;
        break;
      case SAVE_PHONE_NUMBER:
        draft.phoneNumber = action.payload;
        break;
      case SAVE_USERNAME:
        draft.username = action.payload;
        break;
      case SAVE_DOB:
        draft.dob = action.payload;
        break;
      case SAVE_ID_NO:
        draft.idno = action.payload;
        break;
      case SAVE_OTP_ID:
        draft.otpId = action.payload;
        break;
    }
  });
};

export default requestTypeReducer;
