import React from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { Image, TouchableOpacity, View } from 'react-native';
import { Icon, NativeModuleClient, theme } from 'superapp-common-component';
import { useSelector } from 'react-redux';
import { StackNavigationProp } from '@react-navigation/stack';

import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import { makeSelectProfile } from '@/stores/global/selectors';

import Text from '@/components/Text';
import { a11y } from '@/utils/utils';
import { HITSLOP } from '@/utils/styles';

import styles from './styles';
import dayjs from 'dayjs';

const { colors } = theme;

type ProfileNavigationProp = StackNavigationProp<TStackParamList, 'Profile'>;
const MyProfile = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<ProfileNavigationProp>();
  const profile = useSelector(makeSelectProfile());
  const loginStatus = useSelector(makeSelectCheckLogin());

  const renderVerify = () => {
    if (!loginStatus.isLoged) return <Text style={styles.textCreateAccount}>{t('profile.create')}</Text>;
    if (loginStatus.isVerifyAccount)
      return (
        <View style={styles.textVerifiedWrap}>
          <Icon name="Success" color={colors.status.success} size={20} />
          <Text style={styles.textVerified}>{t('profile.verified')}</Text>
        </View>
      );
    return (
      <View style={styles.textVerifiedWrap}>
        <Icon name="Alert1" color={colors.error} size={20} />
        <Text style={styles.textVerify}>{t('profile.unauthenticated')}</Text>
      </View>
    );
  };

  const onArrowIconPress = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'My Profile',
    });
    if (loginStatus.isLoged) {
      navigation.navigate(ROUTE_PATH.MY_PROFILE);
    } else {
      navigation.navigate(ROUTE_PATH.REGISTER);
    }
  };

  return (
    <View style={styles.container}>
      <View style={styles.containerProfile}>
        <View style={[styles.wrapper, styles.marginHorizontal]}>
          <View style={styles.wrapper}>
            {profile?.gender ? (
              <Image
                style={styles.imageAvatar}
                source={
                  ['Nữ', 'F', 'Female', '100000001'].includes(profile?.gender)
                    ? require('@/assets/images/female-avatar.png')
                    : require('@/assets/images/male-avatar.png')
                }
              />
            ) : (
              <Image style={styles.imageAvatar} source={require('@/assets/images/avatarNone.png')} />
            )}

            <View>
              {loginStatus?.isVerifyAccount && profile?.fullName ? (
                <Text style={styles.textName} fontFamily="bold">
                  {t('profile.account')} {profile?.firstName}
                </Text>
              ) : (
                <Text style={styles.textName} fontFamily="bold">
                  {t('profile.guest')}
                </Text>
              )}
              {renderVerify()}
            </View>
          </View>
          <TouchableOpacity {...a11y('Profile.btnProfileDetail')} onPress={onArrowIconPress} hitSlop={HITSLOP}>
            <Icon name="Arrow-Right" color={colors.primary.charcoal[80]} onPress={onArrowIconPress} size={20} />
          </TouchableOpacity>
        </View>
      </View>
      {!loginStatus?.isVerifyAccount && loginStatus.isLoged && (
        <View style={[styles.containerVerify]}>
          <View style={[styles.wrapper, styles.marginHorizontal]}>
            <Image style={styles.imageVerify} source={require('@/assets/images/icon-dashboard-guest.png')} />
            <View style={styles.wrapperVerify}>
              <Text style={styles.textTitle} fontFamily="bold">
                {t('profile.verify.title')}
              </Text>
              <Text numberOfLines={3} style={styles.textDescription}>
                {t('profile.verify.description')}
              </Text>
              <TouchableOpacity
                style={styles.row}
                {...a11y('Profile.btnProfileChoseTypeDocument')}
                onPress={() => navigation.push(ROUTE_PATH.CHOOSE_TYPE_DOCUMENT)}
              >
                <Text style={styles.textVerifyNow} fontFamily="bold">
                  {t('profile.verify.now')}
                </Text>
                <Icon name="Arrow-Right" color={colors.primary.red[100]} size={20} />
              </TouchableOpacity>
            </View>
          </View>
        </View>
      )}
    </View>
  );
};

export default MyProfile;
