import React, { useEffect, useRef } from 'react';
import { View, Animated, StyleSheet } from 'react-native';
import { Icon } from 'superapp-common-component';

const stylesAnimation = StyleSheet.create({
  container: {
    position: 'relative',
  },
  dot: {
    width: 8,
    height: 8,
    borderRadius: 5,
    backgroundColor: 'red',
    position: 'absolute',
    top: 4,
    right: 0,
    zIndex: 100,
  },
});

interface ITabIconProps {
  focused?: boolean;
  color: string;
  size: number;
  iconName: string;
}

const TabIcon: React.FC<ITabIconProps> = ({ size, color, iconName }) => {
  return <Icon name={iconName} size={size} color={color} />;
};

interface ITabIconELetterProps extends ITabIconProps {
  hasEletterUnRead: boolean;
}

const TabIconELetter: React.FC<ITabIconELetterProps> = ({ size, color, iconName, hasEletterUnRead }) => {
  const animatedValue = useRef(new Animated.Value(0)).current;

  useEffect(() => {
    if (hasEletterUnRead) {
      Animated.loop(
        Animated.sequence([
          Animated.timing(animatedValue, { toValue: 1, duration: 400, useNativeDriver: false }),
          Animated.timing(animatedValue, { toValue: 0, duration: 400, useNativeDriver: false }),
        ]),
      ).start();
    }
  }, [hasEletterUnRead]);

  return (
    <View style={stylesAnimation.container}>
      <Animated.View style={[stylesAnimation.dot, { opacity: animatedValue }]} />
      <Icon name={iconName} size={size} color={color} />
    </View>
  );
};

export { TabIcon, TabIconELetter };
