export const removeAccents = (str: string) => {
  str = str.toUpperCase();
  str = str.replace(/A|Á|À|Ã|Ạ|Ả|Â|Ấ|Ầ|Ẩ|Ẫ|Ậ|Ă|Ắ|Ằ|Ẵ|Ặ|Ẳ/g, 'A');
  str = str.replace(/E|É|È|Ẽ|Ẻ|Ẹ|Ê|Ế|Ề|Ễ|Ể|Ệ/g, 'E');
  str = str.replace(/I|Í|Ì|Ĩ|Ị|Ỉ/g, 'I');
  str = str.replace(/O|Ó|Ò|Õ|Ỏ|Ọ|Ô|Ố|Ồ|Ỗ|Ộ|Ổ|Ơ|Ớ|Ờ|Ở|Ỡ|Ợ/g, 'O');
  str = str.replace(/U|Ú|Ù|Ũ|Ụ|Ư|Ứ|Ừ|Ữ|Ự|Ủ|Ử/g, 'U');
  str = str.replace(/Y|Ý|Ỳ|Ỹ|Ỵ|Ỷ/g, 'Y');
  str = str.replace(/Đ/g, 'D');
  // Some system encode vietnamese combining accent as individual utf-8 characters
  str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, ''); // Huyền sắc hỏi ngã nặng
  str = str.replace(/\u02C6|\u0306|\u031B/g, ''); // Â, Ê, Ă, Ơ, Ư
  str = str.replace(/\s/g, '');
  return str;
};
