import React, { ReactNode } from 'react';

import { nanoid } from '@/utils/utils';

import BaseModal, { IBaseModalProps } from './BaseModal';
import { TModalProps } from './Modal';

type IBaseModalCustomProps = IBaseModalProps & {
  close: (isSilence?: boolean) => void;
};

let modal: any;
type TModalState = {
  stack: IBaseModalCustomProps[];
};

type TPortalModal = {
  key?: string;
  visible?: boolean;
  onDismiss?: () => void;
  onTouchOutSide?: () => void;
};

class ModalPortal extends React.Component<TPortalModal, TModalState> {
  public id: number;

  constructor(props: TPortalModal) {
    super(props);
    this.state = { stack: [] };
    this.id = 0;
    modal = this;
  }

  static get ref() {
    return modal;
  }

  static get size() {
    return modal.state.stack.length;
  }

  get current() {
    if (this.state.stack.length) {
      return this.state.stack[this.state.stack.length - 1].key;
    }
  }

  static show(children: ReactNode, props: TModalProps & IBaseModalProps) {
    return modal.show({ children, ...props });
  }

  static update(key: string, props: TModalProps) {
    modal.update(key, props);
  }

  /**
   * Use to close modal
   * @param key
   * @param option
   */
  static close(key: string, option?: { isSilence?: boolean }) {
    modal.close(key, option);
  }

  /**
   * Use to close all modals
   * @returns 
   */
  static closeAll(option?: { isSilence?: boolean }) {
    return new Promise(resolve => {
      modal.closeAll(option);
      setTimeout(() => {
        resolve(null);
      }, 100);
    });
  }

  /**
   * Don't use outside Modal.tsx
   * @param key
   */
  static dismiss(key: string) {
    modal.dismiss(key);
  }

  /**
   * Don't use outside Modal.tsx
   * @param key 
   */
  static destroy(key: string) {
    modal.destroy(key);
  }

  generateKey = () => `modal-${nanoid()}`;

  getIndex = (key: string) => this.state.stack.findIndex(i => i.key === key);

  getProps = (props: TPortalModal) => {
    const key = props.key || this.generateKey();
    return { ...props, key };
  };

  show = (props: TPortalModal) => {
    const mergedProps = this.getProps(props);
    this.setState(({ stack }) => {
      return { stack: stack.concat(mergedProps as never) };
    });
    return mergedProps.key;
  };

  update = (key: string, props: TPortalModal) => {
    const mergedProps = this.getProps({ ...props, key });
    this.setState(({ stack }) => {
      const index = this.getIndex(key);
      stack[index] = { ...stack[index], ...mergedProps };
      return { stack };
    });
  };

  close = (key = this.current, option?: { isSilence?: boolean }) => {
    try {
      if (!key) return;

      const { close } = this.state.stack[this.getIndex(key)];
      close?.(option?.isSilence);
    } catch {
      console.log('Fail to close portal modal');
    }
  };

  closeAll = (option?: { isSilence?: boolean }) => {
    try {
      this.state.stack.forEach(item => {
        this.close(item.key, option);
      });
    } catch {
      console.log('Fail to close all portal modal');
    }
  };

  /**
   * after invoking close function
   * @param key
   * @returns
   */
  dismiss = (key = this.current) => {
    try {
      if (!key) return;

      const props = { ...this.state.stack[this.getIndex(key)], visible: false };
      this.update(key, props);
    } catch {
      console.log('Fail to dismiss portal modal');
    }
  };

  dismissHandler = (key: string) => {
    // dismiss hander: which will remove data from stack and call onDismiss callback
    try {
      const { onDismiss } = this.state.stack[this.getIndex(key)];
      onDismiss && onDismiss();
    } catch {
      console.log('Fail to call onDismiss');
    }

    this.setState(({ stack }) => {
      return { stack: stack.filter(i => i.key !== key) };
    });
  };

  /**
   * remove component in ModalPortal
   * @param key
   * @returns
   */
  destroy = (key = this.current) => {
    if (!key) return;

    this.setState(({ stack }) => {
      return { stack: stack.filter(i => i.key !== key) };
    });
  };

  renderModal = (props: TModalProps) => {
    return <BaseModal {...props} onDismiss={() => this.dismissHandler(props.key!)} />;
  };

  render() {
    return this.state.stack.map(this.renderModal);
  }
}

export default ModalPortal;
