import React, { useState, useRef, useLayoutEffect, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { TouchableOpacity, View, KeyboardAvoidingView, Platform, SafeAreaView } from 'react-native';
import { connect, useDispatch, useSelector } from 'react-redux';
import { SendOTP, Loading, Icon, NativeModuleClient } from 'superapp-common-component';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import { useNavigation, useIsFocused, useRoute } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp, StackScreenProps } from '@react-navigation/stack';
import dayjs from 'dayjs';

import { TConnectState } from '@/stores/types';
import { TStackParamList } from '@/navigators';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import { a11y, maskedPhoneNumber } from '@/utils/utils';
import ROUTE_PATH from '@/navigators/routePath';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { TMigrationTypeState } from '@/pages/Migration/stores/state';
import { makeSelectIsCaptchaError, makeSelectIsVerifyOtpError, makeSelectLoading } from '@/pages/Migration/stores/selectors';
import {
  requestOtpBySmsAction,
  resetOtpError as resetOtpErrorAction,
  resetCaptchaError as resetCaptchaErrorAction,
  migrateUsername as migrateUsernameAction,
} from '@/pages/Migration/stores/actions';
import { NS_MIGRATION_TYPE } from '@/pages/Migration/stores/constants';
import migrationReducer from '@/pages/Migration/stores/reducer';
import migrationSaga from '@/pages/Migration/stores/saga';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import Text from '@/components/Text';

import styles from './styles';
import { ERROR_CODE_OTP } from '@/utils/constants';

type NewPhoneOtpProps = {
  loading: boolean;
  requestOtpBySms: (payload: { params: { otpType: string; otpSendType: string }; setSeconds?: (number: number) => void; setOtpId: (value: string) => void; }) => void;
  migrateUsername: (payload: { otpValue: string; otpId: string; newUsername?: string }) => Promise<void>;
  verifyOtpStatus: {
    isVerifyOtpError: boolean | undefined;
    errorType: string;
  };
  resetOtpError: () => void;
  newUsername: string;
};
type NewPhoneOtpNavigationProp = StackNavigationProp<TStackParamList, 'NewPhoneOtp'>;
type NewPhoneOtpNativeStackProps = StackScreenProps<TStackParamList, 'NewPhoneOtp'>;
type NewPhoneOtpRouteProps = NewPhoneOtpNativeStackProps['route'];
const BLOCK_SUBMIT_OTP_ERROR_TYPE = 'request_otp_locked_out';
const OTP_TYPE = {
  voice: 'Voice',
  sms: 'SMS',
};
const NewPhoneOtp = (props: NewPhoneOtpProps) => {
  useInjectReducer({ key: NS_MIGRATION_TYPE, reducer: migrationReducer });
  useInjectSaga({ key: NS_MIGRATION_TYPE, saga: migrationSaga });
  const navigation = useNavigation<NewPhoneOtpNavigationProp>();
  const { loading, requestOtpBySms, migrateUsername, verifyOtpStatus, resetOtpError } = props;
  const dispatch = useDispatch();
  const route = useRoute<NewPhoneOtpRouteProps>();
  const isRequestOtpError = useSelector(makeSelectIsCaptchaError());
  const isFocused = useIsFocused();
  const { t } = useTranslation();
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalInvalidCapchaRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockSubmitOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockRequestOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalDisableRequestOtp = useRef<null | ModalConfirmErrorRef>(null);
  const [otpValue, setOtpValue] = useState<string>('');
  const [seconds, setSeconds] = useState<number>(0);
  const [otpId, setOtpId] = useState<string>('');

  const { isVerifyOtpError, errorType } = verifyOtpStatus;

  const onHideModalInvalidCapcha = () => {
    dispatch(resetCaptchaErrorAction());
  };

  const onGoBack = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NEW_PHONE_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText:'Go Back'
    });
    navigation.canGoBack() && navigation.goBack();
  }

  const onSubmitOtp = async (setClearInput?: (value: boolean) => void, setCodeState?: (value: string) => void) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NEW_PHONE_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Verify',
    });
    if (loading) return;
    if (otpValue.length === 6) {
      try {
        resetOtpError();
        await migrateUsername({ otpValue, otpId, newUsername: route?.params?.newUsername });
        navigation.navigate(ROUTE_PATH.MIGRATION_SUCCESS_MODAL);
        setClearInput?.(true);
        setOtpValue('');
        setCodeState?.('');
      } catch (error) {}
    } else {
      modalErrorRef?.current?.open();
    }
  };

  const onDismissLoading = () => {
    if (isVerifyOtpError === undefined && isRequestOtpError.status === undefined) return;
    if (isVerifyOtpError) {
      if (errorType === BLOCK_SUBMIT_OTP_ERROR_TYPE) {
        modalBlockSubmitOtp?.current?.open();
      } else if (errorType === ERROR_CODE_OTP.OTP_DISABLED) {
        modalDisableRequestOtp?.current?.open();
      } else {
        modalErrorRef?.current?.open();
      }
      resetOtpError();
    } else if (isRequestOtpError.status) {
      if (isRequestOtpError.errorType === BLOCK_SUBMIT_OTP_ERROR_TYPE) {
        modalBlockRequestOtp?.current?.open();
      } else {
        modalInvalidCapchaRef?.current?.open();
      }
      dispatch(resetCaptchaErrorAction());
    } else if (isFocused && isVerifyOtpError === false) {
      resetOtpError();
      dispatch(resetCaptchaErrorAction());
    }
  };

  const onRequestOtpBySms = (otpType: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NEW_PHONE_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText: otpType === OTP_TYPE.sms ? 'SMS' : 'Voice',
    });
    const params = {
      otpType: 'ChangeUsername',
      otpSendType: otpType === OTP_TYPE.sms ? 'SMS' : 'Voice',
    };
    requestOtpBySms({ params, setSeconds, setOtpId });
    setOtpValue('');
  };

  useEffect(() => {
    const params = {
      otpType: 'ChangeUsername',
      otpSendType: 'SMS',
    };
    requestOtpBySms({ params, setSeconds, setOtpId });
  }, []);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('migration.modal.otp.main.title')}</Text>,
      headerTitleStyle: styles.mainTitle,
      headerLeft: () => (
        <TouchableOpacity
          onPress={onGoBack}
          hitSlop={HITSLOP}
          {...a11y('NewPhoneOtp.iconBackHeaderLeft')}
        >
          <Icon name="Arrow-Left" color="#5B636B" size={24} style={styles.marginLeft16} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: 'white' }}>
      <View style={{ flex: 1 }}>
        <KeyboardAvoidingView
          behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
          style={{ flex: 1 }}
          keyboardVerticalOffset={Platform.OS === 'ios' ? 75 : 0}
        >
          <View style={styles.containerOtp}>
            <View style={styles.otpContent}>
              <SendOTP
                textDescription={t('common.otp.textPhone', { phone: maskedPhoneNumber(route?.params?.newUsername || '') })}
                textTimeCount={t('common.otp.text.countTimeTitle')}
                submitText={t('migration.modal.otp.button.verify')}
                onChange={code => setOtpValue(code)}
                sendBySMS={() => onRequestOtpBySms(OTP_TYPE.sms)}
                sendByVoice={() => onRequestOtpBySms(OTP_TYPE.voice)}
                onSubmit={onSubmitOtp}
                loading={loading}
                disabled={loading}
                secondNumber={seconds}
                setSecondNumber={setSeconds}
                textSMS={t('common.otp.text.resend')}
                textVoice={t('common.otp.text.viaCall')}
                textCountSecond={t('common.text.countSecond')}
                textNotSendSMS={t('common.otp.text.noSms')}
                id="Migration.otpMigration"
              />
            </View>
          </View>
        </KeyboardAvoidingView>
      </View>
      <ModalError
        ref={modalErrorRef}
        title={t('migration.modal.otp.error.title')}
        subtitle={t('migration.modal.otp.error.des')}
        btnText={t('migration.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalInvalidCapchaRef}
        title={t('migration.modal.otp.wrong.title')}
        subtitle={t('migration.modal.request.otp.fail')}
        btnText={t('migration.modal.otp.error.button.close')}
        onConfirm={onHideModalInvalidCapcha}
      />
      <ModalError
        ref={modalBlockSubmitOtp}
        title={t('migration.modal.otp.error.title')}
        subtitle={t('migration.modal.otp.error.block.otp')}
        btnText={t('migration.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalBlockRequestOtp}
        title={t('migration.modal.otp.error.title')}
        subtitle={t('migration.modal.otp.error.block.request.otp')}
        btnText={t('migration.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalDisableRequestOtp}
        title={t('migration.modal.otp.wrong.title')}
        subtitle={t('common.message.otpDisable')}
        btnText={t('migration.modal.otp.error.button.close')}
        onConfirm={onGoBack}
      />
      <Loading visible={loading} dismissAction={onDismissLoading} />
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TMigrationTypeState['loading'];
  verifyOtpStatus: TMigrationTypeState['verifyOtpStatus'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  verifyOtpStatus: makeSelectIsVerifyOtpError(),
});

type MapDispatchToPropsType = {
  requestOtpBySms: (payload: { params: { otpType: string; otpSendType: string }; setSeconds?: (number: number) => void; setOtpId: (value: string) => void; }) => any;
  migrateUsername: (payload: { otpValue: string; otpId: string }) => Promise<void>;
  resetOtpError: () => void;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  requestOtpBySms: payload => dispatch(requestOtpBySmsAction(payload)),
  migrateUsername: (payload: { otpValue: string; otpId: string; newUsername?: string }) =>
    new Promise((resolve, reject) => dispatch(migrateUsernameAction(payload, resolve, reject))),
  resetOtpError: () => dispatch(resetOtpErrorAction()),
});

export default connect(mapStateToProps, mapDispatchToProps)(NewPhoneOtp);
