import { ActivityIndicator, View } from 'react-native';
import React, { forwardRef, useImperativeHandle } from 'react';
import { WebView, WebViewMessageEvent } from 'react-native-webview';
import NetInfo from '@react-native-community/netinfo';
import { Text } from 'superapp-common-component';

import APP_ENV from '@/config/app.config';

import Modal, { useModal } from '../Modal/Modal';

import styles from './styles';
import { colors } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

type TurnstileCapchaProps = {
  onDismiss?: () => void;
  onSuccess: (token: string) => void;
  onError: () => void;
};

export interface TurnstileCapchaRef {
  open: () => void;
  close: () => void;
}

const TurnstileCapcha: React.ForwardRefRenderFunction<TurnstileCapchaRef, TurnstileCapchaProps> = (props, ref) => {
  const { onDismiss, onError, onSuccess } = props;
  const [modalRef, componentModal] = useModal();
  const netInfo = NetInfo.useNetInfo();
  const { t } = useTranslation();

  const onMessage = (event: WebViewMessageEvent) => {
    const { data } = event.nativeEvent;

    if (data) {
      data && data === 'error' ? onError() : onSuccess(data);
    }
  };

  useImperativeHandle(
    ref,
    (): TurnstileCapchaRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal()?.close(),
    }),
  );

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer} onDismiss={onDismiss}>
      <View style={styles.flex}>
        {netInfo.isConnected ? (
          <WebView
            originWhitelist={['*']}
            scrollEnabled={false}
            textZoom={100}
            onMessage={onMessage}
            source={{
              baseUrl: APP_ENV.URL_TURNSTILE,
              html: `
              <!DOCTYPE html>
              <html>
                <head>
                  <script
                    src="https://challenges.cloudflare.com/turnstile/v0/api.js?onload=_turnstileCb"
                    async
                    defer
                  ></script>
                  <meta name="viewport" content="width=device-width, initial-scale=1.0">
                  <style>
                    html, body {
                      width: 100%;
                      height: 100%;
                      margin: 0;
                    }
                    #cf-widget {
                      width: 100%
                    }
                    iframe {
                      width: 100% !important;
                    }
                  </style>
                </head>
                <body>
                  <div id="cf-widget"></div>
                  <script>
                    function _turnstileCb() {
                      turnstile.render("#cf-widget", {
                        sitekey: '${APP_ENV.SITE_KEY}',
                        callback: (token) => {
                          window.ReactNativeWebView.postMessage(token);
                        },
                        'error-callback': (error) => {
                          window.ReactNativeWebView.postMessage('error');
                        },
                        theme: 'light',
                      });
                    }
                  </script>
                </body>
              </html>
          `,
            }}
          />
        ) : (
          netInfo.isConnected === false && (
            <View style={styles.noConnect}>
              <View style={styles.row}>
                <ActivityIndicator animating={true} color={colors.primary.red[100]} />
                <Text style={{ marginLeft: 16 }}>{t('common.noInternet')}</Text>
              </View>
            </View>
          )
        )}
      </View>
    </Modal>
  );
};

export default forwardRef(TurnstileCapcha);
