import i18Next, { InitOptions, ModuleType } from 'i18next';
import { initReactI18next } from 'react-i18next';
import _keys from 'lodash/keys';
import _reduce from 'lodash/reduce';
import _values from 'lodash/values';

import { KEYS_TRANSLATE_AEM, STORAGE_NAME } from '@/utils/constants';
import { getItem, setItem } from '@/utils/storage';
import { isEmpty } from '@/utils/utils';
import AEMService from '@/services/aem';
import VitalityAEMService from '@/services/vitalityAEMService';

import AppEn from './en-us';
import AppVi from './vi-vn';

export const resourcesDefault = {
  en: { translation: AppEn },
  vi: { translation: AppVi },
};

type TCacheTranslate = {
  lastModifiedDate: number;
  resources: {
    vi: { [key: string]: string };
    en: { [key: string]: string };
  };
};

type TCacheTranslation = {
  [key: string]: TCacheTranslate;
};

class I18n {
  public i18nInstance = i18Next;

  readonly DEFAULT_LANG: string = 'vi';

  readonly NAMESPACE: string = 'translation';

  private config: InitOptions = {
    resources: {},
    fallbackLng: this.DEFAULT_LANG,
    compatibilityJSON: 'v3',
    interpolation: {
      escapeValue: false,
    },
    react: {
      useSuspense: false,
    },
  };

  constructor() {
    this.i18nInstance.use(this.languageDetector()).use(initReactI18next).init(this.config);
  }

  private languageDetector = () => {
    return {
      type: 'languageDetector' as ModuleType,
      async: true,
      init: () => {},
      detect: (callback: (lang: string) => void): Promise<void> =>
        new Promise(resolve => getItem<string>(STORAGE_NAME.LANGUAGE, this.DEFAULT_LANG).then(language => resolve(callback(language)))),
      cacheUserLanguage: async (language: string) => {
        await setItem(STORAGE_NAME.LANGUAGE, JSON.stringify(language));
      },
    };
  };

  public addResource = (lang: string, namespace: string, resources: any, deep?: boolean, overwrite?: boolean) => {
    this.i18nInstance.addResourceBundle(lang, namespace, resources, deep, overwrite);
  };

  private transformAddResources = (data: MODEL.IContentTranslateResponse, keyCache: string) => {
    const resources = { vi: {}, en: {} };

    if (data && !isEmpty(data.items.vi?.elements)) {
      resources.vi = data.items.vi.elements?.[0].value;
      resources.en = data.items.en.elements?.[0].value;
    }

    return { [keyCache]: { lastModifiedDate: data.lastModifiedDate, resources } };
  };

  private getTranslateLogin = async () => {
    const response = await AEMService.getTranslateLoginService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.LOGIN);
  };

  private getTranslateProfile = async () => {
    const response = await AEMService.getTranslateProfileService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.PROFILE);
  };

  private getTranslateRegister = async () => {
    const response = await AEMService.getTranslateRegistrationService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.REGISTER);
  };

  private getTranslateHome = async () => {
    const response = await AEMService.getTranslateHomeService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.HOME);
  };

  private getTranslateVerifyAccount = async () => {
    const response = await AEMService.getTranslateVerifyAccountService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.VERIFY_ACCOUNT);
  };

  private getTranslateCommonAccount = async () => {
    const response = await AEMService.getTranslateCommonService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.COMMON);
  };

  private getTranslateEcard = async () => {
    const response = await AEMService.getTranslateEcardService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.ECARD);
  };

  private getTranslateVitalityAssessments = async () => {
    const response = await VitalityAEMService.getTranslateVitalityAssessmentsService();
    return this.transformAddResources(response, KEYS_TRANSLATE_AEM.VITALITY_ASSESSMENTS);
  };

  private addGlobalResource = (transfromResourceObject: TCacheTranslation) => {
    _values(transfromResourceObject).forEach((item: TCacheTranslate) => {
      this.addResource('vi', this.NAMESPACE, item.resources.vi, true, true);
      this.addResource('en', this.NAMESPACE, item.resources.en, true, true);
    });
  };

  public getResourcesTranslation = async () => {
    try {
      let translationCache = await getItem<TCacheTranslation>(STORAGE_NAME.TRANSLATION_RESOURCE, {});

      // First install app: get all translate
      if (isEmpty(translationCache)) {
        // Init locale local resource
        this.addResource('vi', this.NAMESPACE, AppVi, true, true);
        this.addResource('en', this.NAMESPACE, AppEn, true, true);

        // Fetch locale from AEM content
        const resources = await Promise.all([
          this.getTranslateVitalityAssessments(),
          this.getTranslateLogin(),
          this.getTranslateProfile(),
          this.getTranslateRegister(),
          this.getTranslateVerifyAccount(),
          this.getTranslateHome(),
          this.getTranslateCommonAccount(),
          this.getTranslateEcard(),
        ]);
        const transfromResourceObject = _reduce(resources, (current, next) => ({ ...current, ...next }), {} as TCacheTranslation);
        await setItem(STORAGE_NAME.TRANSLATION_RESOURCE, JSON.stringify(transfromResourceObject));
        this.addGlobalResource(transfromResourceObject);
        return;
      }

      // Init locale cache
      if (!isEmpty(translationCache)) {
        this.addGlobalResource(translationCache);
      }

      const lastModifiedDate = await AEMService.getLastTimeModified();
      const neededUpdateTranslate: Array<Promise<TCacheTranslation>> = [];
      const mapperServiceTranslate = {
        [KEYS_TRANSLATE_AEM.LOGIN]: () => this.getTranslateLogin(),
        [KEYS_TRANSLATE_AEM.PROFILE]: () => this.getTranslateProfile(),
        [KEYS_TRANSLATE_AEM.REGISTER]: () => this.getTranslateRegister(),
        [KEYS_TRANSLATE_AEM.VERIFY_ACCOUNT]: () => this.getTranslateVerifyAccount(),
        [KEYS_TRANSLATE_AEM.HOME]: () => this.getTranslateHome(),
        [KEYS_TRANSLATE_AEM.COMMON]: () => this.getTranslateCommonAccount(),
        [KEYS_TRANSLATE_AEM.ECARD]: () => this.getTranslateEcard(),
        [KEYS_TRANSLATE_AEM.VITALITY_ASSESSMENTS]: () => this.getTranslateVitalityAssessments(),
      };

      // Check version cache translate
      if (!isEmpty(lastModifiedDate) && !isEmpty(translationCache)) {
        _keys(lastModifiedDate).forEach((item: string) => {
          if (isEmpty(translationCache[item]) && _values(KEYS_TRANSLATE_AEM).includes(item)) {
            neededUpdateTranslate.push(mapperServiceTranslate[item]());
          }
          if (
            !isEmpty(translationCache[item]) &&
            lastModifiedDate[item] !== translationCache[item].lastModifiedDate &&
            _values(KEYS_TRANSLATE_AEM).includes(item)
          ) {
            neededUpdateTranslate.push(mapperServiceTranslate[item]());
          }
        });
      }

      // Fetch translate new version, add resources
      if (!isEmpty(neededUpdateTranslate)) {
        const resources = await Promise.all(neededUpdateTranslate);
        const transfromResourceObject = _reduce(resources, (current, next) => ({ ...current, ...next }), {});
        translationCache = { ...translationCache, ...transfromResourceObject };
        await setItem(STORAGE_NAME.TRANSLATION_RESOURCE, JSON.stringify(translationCache));
        this.addGlobalResource(translationCache);
        return;
      }

      this.addGlobalResource(translationCache);
    } catch (error) {
      console.log('ERROR: Can not getResourcesTranslation', error);
    }
  };
}

export default new I18n();
