import React, { useEffect, useRef } from 'react';
import { View, Animated, Image, TouchableOpacity } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { useTranslation } from 'react-i18next';
import { useSelector } from 'react-redux';
import { Icon, NativeModuleClient, useTheme } from 'superapp-common-component';
import { utils } from 'superapp-common-component';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import ROUTE_PATH from '@/navigators/routePath';
import { Theme } from '@/config/themes.config';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';

import { HomeNavigationProp } from '../../index';
import useStyles, { CONTAINER_HEIGHT, CONTAINER_MARGIN_BOTTOM } from './styles';

const { a11y } = utils;
const VerifyAccount = () => {
  const theme = useTheme() as Theme;
  const animationVerifyAccount = useRef(new Animated.Value(0)).current;
  const styles = useStyles(theme);
  const { t } = useTranslation();
  const navigation = useNavigation<HomeNavigationProp>();
  const loginStatus = useSelector(makeSelectCheckLogin());

  useEffect(() => {
    if (loginStatus.isLoged && !loginStatus.isVerifyAccount) {
      Animated.spring(animationVerifyAccount, {
        toValue: 1,
        useNativeDriver: false,
      }).start();
    }
  }, [loginStatus]);

  return loginStatus.isLoged && !loginStatus.isVerifyAccount ? (
    <Animated.View
      style={[
        styles.containerVerify,
        {
          marginBottom: animationVerifyAccount.interpolate({
            inputRange: [0, 1],
            outputRange: [0, CONTAINER_MARGIN_BOTTOM],
          }),
          opacity: animationVerifyAccount,
          height: animationVerifyAccount.interpolate({
            inputRange: [0, 1],
            outputRange: [0, CONTAINER_HEIGHT],
          }),
        },
      ]}>
      <View style={[styles.wrapper, styles.marginHorizontal]}>
        <Image
          style={styles.imageVerify}
          source={require('@/assets/images/icon-dashboard-guest.png')}
        />
        <View style={styles.wrapperVerify}>
          <Text style={styles.textTitle} fontFamily="bold">
            {t('profile.verify.title')}
          </Text>
          <Text numberOfLines={3} style={styles.textDescription}>
            {t('profile.verify.description')}
          </Text>
          <TouchableOpacity
            style={styles.row}
            {...a11y('Home.btnVerifyAccount')}
            onPress={() => {
              NativeModuleClient.AdobeExperience.trackButton({
                screen: ROUTE_PATH.HOME,
                unixTimestamp: dayjs().unix(),
                buttonText: 'Verify now',
              });
              navigation.navigate(ROUTE_PATH.CHOOSE_TYPE_DOCUMENT);
            }}>
            <Text style={styles.textVerifyNow} fontFamily="bold">
              {t('profile.verify.now')}
            </Text>
            <Icon
              name="Arrow-Right"
              color={theme.colors.inputError}
              size={20}
            />
          </TouchableOpacity>
        </View>
      </View>
    </Animated.View>
  ) : null;
};

export default VerifyAccount;
