import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  GET_INFO_OCR_VERIFIED,
  REQUEST_OCR,
  SAVE_INFO_VERIFY_OCR,
  SUBMIT_VERIFY_ACCOUNT,
  VERIFY_ACCOUNT_SUCCESS,
  REQUEST_GET_NATIONS,
  REQUEST_GET_MASTER_DATA,
  RESET_DATA,
} from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });
export const getResultOCRVerifiedAction = (payload: any): TAction => ({ type: GET_INFO_OCR_VERIFIED, payload });
export const requestVerifyOCRAction = (payload: any): TAction => ({ type: REQUEST_OCR, payload });
export const saveInfoVerifyOCRAction = (payload: any): TAction => ({ type: SAVE_INFO_VERIFY_OCR, payload });
export const submitVerifyAccountAction = (payload: any): TAction => ({ type: SUBMIT_VERIFY_ACCOUNT, payload });
export const saveVerifyAccount = (payload: any): TAction => ({ type: VERIFY_ACCOUNT_SUCCESS, payload });
export const getMasterDataAction = (payload: any): TAction => ({ type: REQUEST_GET_MASTER_DATA, payload });
export const getNationalityAction = (payload: any): TAction => ({ type: REQUEST_GET_NATIONS, payload });
export const resetDataAction = (): TAction => ({ type: RESET_DATA });
