import React from 'react';
import { useTranslation } from 'react-i18next';
import { View, TouchableOpacity, ScrollView, Image } from 'react-native';
import { Icon, Skeleton, theme } from 'superapp-common-component';
import Text from '@/components/Text';
import { a11y } from '@/utils/utils';
import _isEmpty from 'lodash/isEmpty';

import styles from './styles';
import { TTrackerProps } from './types';
import { config, PartnerIcons } from './constants';

const { colors } = theme;

const Tracker: React.FC<TTrackerProps> = ({ loading, data = [], onPressItem, onPressRightIcon }) => {
  const { t } = useTranslation();

  return (
    <View style={styles.container}>
      <View style={styles.containerTracker}>
        <View style={styles.row}>
          <Text style={styles.textTitle}>{t('profile.account.tracker.title', { count: data.length })}</Text>
          <TouchableOpacity {...a11y('Profile.btnProfileTrackerTouch')} onPress={onPressRightIcon}>
            <Icon name="Arrow-Right" color={colors.primary.charcoal[80]} size={20} />
          </TouchableOpacity>
        </View>
        {loading ? (
          <Skeleton style={styles.skeleton} />
        ) : !_isEmpty(data) ? (
          <ScrollView
            showsHorizontalScrollIndicator={false}
            horizontal={true}
            decelerationRate={0}
            contentContainerStyle={styles.scrollViewContentContainer}
            disableIntervalMomentum={true}
          >
            {data.map(itemKey => {
              const item = config[itemKey] || {};
              const { icon, name } = item;
              return (
                <TouchableOpacity key={name} style={styles.trackerItem} onPress={() => onPressItem(item, itemKey)}>
                  <Image source={PartnerIcons[icon]} style={styles.trackerImage} />
                  <Text style={styles.trackerName}>{name}</Text>
                </TouchableOpacity>
              );
            })}
          </ScrollView>
        ) : null}
      </View>
    </View>
  );
};

export default Tracker;
