import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_VERIFY_ACCOUNT_QRCODE } from './constants';

const currentState = (state: TConnectState) => state?.[NS_VERIFY_ACCOUNT_QRCODE] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
export { currentState, makeSelectLoading};
