import { takeLatest, put, call } from 'redux-saga/effects';
import _get from 'lodash/get';

import AEMService from '@/services/aem';
import { isEmpty } from '@/utils/utils';

import { GET_CONTENT_ONBOARDING } from './constants';
import { loadingRequest, loadingSuccess, saveContentOnboarding, OnboardingTranslation } from './actions';

type TElement = { name: string; value: string };

function* getContentOnboarding() {
  try {
    yield put(loadingRequest());
    const response: MODEL.IContentOnboardingResponse = yield call(AEMService.getContentService);
    const itemsVI = _get(response, ['items', 'vi', 'items']);
    const itemsEN = _get(response, ['items', 'en', 'items']);
    let transformData: OnboardingTranslation = { vi: [], en: [] };

    if (!isEmpty(itemsVI)) {
      transformData.vi = itemsVI.map((item: { title: string; elements: Array<TElement> }) => {
        const elements = item.elements;
        const id = elements.find((element: TElement) => element.name === 'id');
        const title = elements.find((element: TElement) => element.name === 'title');
        const description = elements.find((element: TElement) => element.name === 'description');
        const imagePath = elements.find((element: TElement) => element.name === 'imagePath');

        return { id: id?.value, title: title?.value, description: description?.value, imagePath: imagePath?.value };
      });

			transformData.en = itemsEN.map((item: { title: string; elements: Array<TElement> }) => {
        const elements = item.elements;
        const id = elements.find((element: TElement) => element.name === 'id');
        const title = elements.find((element: TElement) => element.name === 'title');
        const description = elements.find((element: TElement) => element.name === 'description');
        const imagePath = elements.find((element: TElement) => element.name === 'imagePath');

        return { id: id?.value, title: title?.value, description: description?.value, imagePath: imagePath?.value };
      });

    }

    yield put(saveContentOnboarding(transformData));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingSuccess());
  }
}

function* onboardingSaga() {
  yield takeLatest(GET_CONTENT_ONBOARDING, getContentOnboarding);
}

export default onboardingSaga;
