import { put, takeLatest, call, select, takeEvery } from 'redux-saga/effects';
import _isEmpty from 'lodash/isEmpty';
import { Platform } from 'react-native';

import VitalityService from '@/services/vitalityService';
import { TAction } from '@/stores/types';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import { getItem, setItem } from '@/utils/storage';
import { VITALITY_TRACKER_PARTNERS } from '@/utils/constants';
import { getVitalityMembershipIdFromToken } from '@/utils/utils';

import * as actions from './actions';
import * as constants from './constants';
import { currentState } from './selectors';

const isIOS = Platform.OS === 'ios';

const filterLinkedDevices = ({ trackingDetails, deviceName }: API.IPreLinkDevice) =>
  trackingDetails.linkedStatus !== constants.UNLINKED && !constants.DISABLED_WORKOUT_TRACKERS_DEVICE_NAMES.includes(deviceName);

function* fetchHealthReportFeedback({ isRefresh }: TAction) {
  try {
    const { isLoged } = yield select(makeSelectCheckLogin());
    const { healthReportFeedback } = yield select(currentState);
    if ((isRefresh || healthReportFeedback === undefined) && isLoged) {
      const newHealthReportFeedback: API.IFetchHealthReportFeedback = yield call(VitalityService.fetchHealthReportFeedbackService);
      yield put(actions.setHealthReportFeedback(newHealthReportFeedback));
    } else {
      yield put(actions.fetchHealthReportFeedbackSuccess());
    }
  } catch (error) {
    yield put(actions.setHealthReportFeedback(null));
  }
}

function* initLinkedTrackers({ isRefresh }: TAction) {
  try {
    const { isLoged } = yield select(makeSelectCheckLogin());
    const { linkedTrackersLoading } = yield select(currentState);
    let linkedTrackers = [];
    if (isLoged) {
      // get trackers device by calling API
      const membershipId: string = yield call(getVitalityMembershipIdFromToken);
      if (isRefresh || linkedTrackersLoading === undefined) {
        yield put(actions.setLoadingLinkedTracker(true));
        const { devices }: API.IFetchPreLinkDevicesResponse = yield call(VitalityService.fetchPreLinkDevices);
        const newLinkedDevices = devices.filter(filterLinkedDevices);
        yield call(setItem, `${membershipId}_${constants.LIST_WORKOUT_TRACKER}`, JSON.stringify(newLinkedDevices));
      }

      // Get tracker from storage
      const IsLinkedGoogleFitTracker: string = yield call(getItem, `${membershipId}_${constants.IS_LINKED_GOOGLE_FIT}`);
      const isLinkedNativeTracker: string = yield call(getItem, `${membershipId}_${constants.IS_LINKED}`);
      const listWorkoutTrackers: { title: string }[] = yield call(getItem, `${membershipId}_${constants.LIST_WORKOUT_TRACKER}`);

      if (IsLinkedGoogleFitTracker) {
        linkedTrackers.push(VITALITY_TRACKER_PARTNERS.GOOGLE_FIT);
      }
      if (isLinkedNativeTracker && isIOS) {
        linkedTrackers.push(VITALITY_TRACKER_PARTNERS.APPLE_HEALTH);
      } else if (isLinkedNativeTracker && !isIOS) {
        linkedTrackers.push(VITALITY_TRACKER_PARTNERS.SAMSUNG_HEALTH);
      }
      if (listWorkoutTrackers && !_isEmpty(listWorkoutTrackers)) {
        const otherTrackers: string[] = listWorkoutTrackers.map(
          (tracker: { title: string; deviceName?: string }) => tracker.deviceName || tracker.title,
        );
        linkedTrackers = linkedTrackers.concat(otherTrackers);
      }
      yield put(actions.setLinkedTrackers(linkedTrackers));
    }
  } catch (error) {
  } finally {
    yield put(actions.setLoadingLinkedTracker(false));
  }
}

function* vitalitySaga() {
  yield takeLatest(constants.FETCH_HEALTH_REPORT_FEEDBACK, fetchHealthReportFeedback);
  yield takeEvery(constants.INIT_LINKED_TRACKERS, initLinkedTrackers);
}

export default vitalitySaga;
