import { TAction } from '@/stores/types';

import { 
  LOADING_REQUEST,
  LOADING_SUCCESS,
  SAVE_SESSION_ID,
  REQUEST_PROCESS_P820,
  REQUEST_PROCESS_P820_ERROR,
} from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });
export const saveSessionIdAction = (sessionId?: string): TAction => ({ type: SAVE_SESSION_ID, payload: sessionId });
export const requestProcessP820ErrorAction = (error?: string): TAction => ({ type: REQUEST_PROCESS_P820_ERROR, payload: error });
export const requestProcessP820Action = (payload: { clientId: string; sessionId: string }): TAction => ({
  type: REQUEST_PROCESS_P820,
  payload,
});
