import React, { forwardRef, useEffect, useImperativeHandle, useState } from 'react';
import { View, TouchableWithoutFeedback } from 'react-native';
import { Button, NativeModuleClient, Skeleton } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import Text from '@/components/Text';
import Modal, { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface ModalVersionProps {}

export interface ModalVersionRef {
  open: () => void;
  close: () => void;
}

const ModalVersion = (props: ModalVersionProps, ref: React.Ref<unknown> | undefined) => {
  const { t } = useTranslation();
  const [modalRef, componentModal] = useModal();
  const [isShow, setIsShow] = useState(false);
  const [appVersions, setAppVersions] = useState<{ name: string; version: string }[]>([]);

  const getMiniAppVersions = async () => {
    if (__DEV__) return [];

    const appNames = ['host', 'claim', 'policyServing', 'posRequest', 'vitality'];

    const versionApps = await Promise.all(
      appNames.map(async appName => {
        const version = await NativeModuleClient.MiniApp.getLatestVersionApp(appName);
        return { name: appName, version };
      }),
    );

    return versionApps.filter(it => it.version);
  };

  const openModal = () => {
    componentModal()?.show();
    setIsShow(true);
  };

  const closeModal = () => {
    componentModal().close();
    setIsShow(false);
  };

  useEffect(() => {
    if (isShow) {
      (async () => {
        const resultAppVersions = await getMiniAppVersions();
        setAppVersions(resultAppVersions);
      })();
    }
  }, [isShow]);

  useImperativeHandle(
    ref,
    (): ModalVersionRef => ({
      open: openModal,
      close: closeModal,
    }),
    [isShow],
  );

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer}>
      <TouchableWithoutFeedback onPress={() => {}}>
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View style={styles.wrapText}>
              <Text style={styles.primaryText}>{t('common.text.version')}</Text>
              {!appVersions.length && <Skeleton style={styles.skeleton} />}
              {appVersions.map(it => (
                <Text style={styles.secondaryText}>
                  {it.name}: {it.version}
                </Text>
              ))}
            </View>
            <View style={styles.buttonBox}>
              <Button
                text={t('common.btn.close')}
                type="secondary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                onPress={() => {
                  componentModal().close();
                }}
                id="Profile.btnVersionClose"
              />
            </View>
          </View>
        </View>
      </TouchableWithoutFeedback>
    </Modal>
  );
};

export default forwardRef(ModalVersion);
