import React, { useState, useEffect } from 'react';
import { ScrollView, View, Image, Dimensions, ViewStyle } from 'react-native';
import { SafeAreaView } from 'react-native-safe-area-context';
import { useTranslation } from 'react-i18next';
import dayjs from 'dayjs';

import Text from '@/components/Text';

import translation from './translation';
import styles from './styles';

type MaintenanceScreenProps = {
  status?: string;
  title?: string;
  description?: string;
  startTime?: string;
  endTime?: string;
};

const deviceSize = Dimensions.get('window');

const imgSource = require('@/assets/images/maintenance.png');

const MaintenanceScreen: React.FC<MaintenanceScreenProps> = props => {
  const { i18n } = useTranslation();
  const translator = translation[i18n.language as 'vi' | 'en'] || {};

  const { startTime, endTime, title, description } = props;
  const [imageSize, setImageSize] = useState({
    width: 0,
    height: 0,
  });

  const handleResizeImage = () => {
    const imageDetail = Image.resolveAssetSource(imgSource);
    const aspectRatio = imageDetail.width / imageDetail.height;
    const width = Math.min(deviceSize.width * 0.8, imageDetail.width);
    const height = width / aspectRatio;
    setImageSize({ width, height });
  };

  const computeWrapSubtitleStyle = (): ViewStyle => {
    if (deviceSize.width >= 768) return { width: deviceSize.width - 32, maxWidth: 768 };

    return { alignSelf: 'stretch' };
  };

  useEffect(() => {
    handleResizeImage();
  }, []);

  return (
    <SafeAreaView style={styles.safeArea}>
      <ScrollView contentContainerStyle={styles.container}>
        <View style={styles.content}>
          <Image
            style={{
              ...{
                width: imageSize.width,
                height: imageSize.height,
              },
              ...styles.image,
            }}
            resizeMode="contain"
            source={imgSource}
          />
          {title && (
            <Text style={styles.title} fontFamily="bold">
              {title}
            </Text>
          )}
          {startTime && (
            <View style={[styles.wrapSubtitle, computeWrapSubtitleStyle()]}>
              <Text style={styles.subtitle}>
                {translator.startTime}: {dayjs(startTime).format('DD/MM/YYYY - HH:mm')}
              </Text>
              <Text style={styles.subtitle}>
                {translator.endTime}: {dayjs(endTime).format('DD/MM/YYYY - HH:mm')}
              </Text>
            </View>
          )}
          {description && <Text style={styles.description}>{description}</Text>}
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};

export default MaintenanceScreen;
