import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class OneCommonService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ONE_COMMON_DATA);
  }

  public requestProvinces = (params: any) => this.instance.post<any>('/province', { ...params });

  public requestNations = (params: any) => this.instance.post<any>('/nation', { ...params });

  public requestEthnics = (_: any) => this.instance.post<any>('master/ethnic');
}

export default new OneCommonService();
