import React, { useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { View, TouchableOpacity, GestureResponderEvent } from 'react-native';
import { Icon, NativeModuleClient } from 'superapp-common-component';
import { StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import { useNavigation } from '@react-navigation/native';
import Text from '@/components/Text';

import PdfViewer, { TPdfViewerRef } from '@/components/PdfViewer';
import WebView from '@/components/WebView';
import ROUTE_PATH from '@/navigators/routePath';
import { a11y } from '@/utils/utils';

import styles from './styles';
import { useSelector } from 'react-redux';
import { makeSelectActionsLinks } from '@/stores/global/selectors';
import dayjs from 'dayjs';

type AboutProps = {
  onPressContact: ((event: GestureResponderEvent) => void) | undefined;
};

type AboutNavigationProp = StackNavigationProp<TStackParamList, 'Profile'>;

const About: React.FC<AboutProps> = ({ onPressContact }) => {
  const { t } = useTranslation();
  const navigation = useNavigation<AboutNavigationProp>();
  const webViewRef = useRef<any>(null);
  const termpdfViewerRef = useRef<TPdfViewerRef>();
  const actionLinks = useSelector(makeSelectActionsLinks());

  const showWebView: (url: string, name?: string) => any = (url: string, name?: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: `Action Link ${name}`,
    });
    webViewRef?.current?.openURL(url);
  };
  return (
    <View style={styles.container}>
      <View style={styles.row}>
        <Text style={styles.textTitle} xl={{fontWeight: '600', fontSize: 16, lineHeight: 24}}>{t('profile.account.about.title')}</Text>
      </View>
      <View style={styles.wrapperContent}>
        {/* <TouchableOpacity style={[styles.row, styles.marginHorizontal]} {...a11y('Profile.btnContact')} onPress={onPressContact}>
          <View style={styles.row}>
            <Icon name="Phone1" size={24} color="#5B636B" />
            <Text style={styles.textContent}>{t('profile.account.about.contact')}</Text>
          </View>
          <Icon name="Arrow-Right" size={20} color="#5B636B" style={styles.alignSelfCenter} />
        </TouchableOpacity> */}

        {!actionLinks?.faq.isHide && (
          <TouchableOpacity
            style={[styles.row, styles.marginHorizontal]}
            {...a11y('Profile.btnFaq')}
            onPress={() => {
              showWebView(actionLinks?.faq?.value, actionLinks?.faq?.name);
            }}
          >
            <View style={styles.row}>
              <Icon name="Ask-For-Help" size={24} color="#5B636B" />
              <Text style={styles.textContent}>{t('profile.account.about.faq')}</Text>
            </View>
            <Icon name="Arrow-Right" size={20} color="#5B636B" style={styles.alignSelfCenter} />
          </TouchableOpacity>
        )}

        {!actionLinks?.term.isHide && (
          <TouchableOpacity
            style={[styles.row, styles.marginHorizontal]}
            {...a11y('Profile.btnTermsConditions')}
            onPress={() => {
              showWebView(actionLinks?.term?.value, actionLinks?.term?.name);
            }}
          >
            <View style={[styles.row]}>
              <Icon name="Read" size={24} color="#5B636B" />
              <Text style={styles.textContent}>{t('profile.account.about.term')}</Text>
            </View>
            <Icon name="Arrow-Right" size={20} color="#5B636B" style={styles.alignSelfCenter} />
          </TouchableOpacity>
        )}

        {!actionLinks?.termVitality.isHide && (
          <TouchableOpacity
            style={[styles.row, styles.marginHorizontal]}
            {...a11y('Profile.btnTermsConditionsVitality')}
            onPress={() => {
              termpdfViewerRef.current?.openPdf();
            }}
          >
            <View style={[styles.row]}>
              <Icon name="Read" size={24} color="#5B636B" />
              <Text style={styles.textContent}>{t('profile.account.about.termVitality')}</Text>
            </View>
            <Icon name="Arrow-Right" size={20} color="#5B636B" style={styles.alignSelfCenter} />
          </TouchableOpacity>
        )}

        {!actionLinks?.personalData.isHide && (
          <TouchableOpacity
            style={[styles.row, styles.marginHorizontal]}
            {...a11y('Profile.btnMarketing')}
            onPress={() => navigation.navigate(ROUTE_PATH.MARKETING)}
          >
            <View style={[styles.row]}>
              <Icon name="Read" size={24} color="#5B636B" />
              <Text style={[styles.textContent, styles.flexWrap]} numberOfLines={2}>
                {t('profile.account.about.data')}
              </Text>
            </View>
            <Icon name="Arrow-Right" size={20} color="#5B636B" style={styles.alignSelfCenter} />
          </TouchableOpacity>
        )}

        {!actionLinks?.privacy.isHide && (
          <TouchableOpacity
            style={[styles.row, styles.marginHorizontal]}
            {...a11y('Profile.btnInfoCollection')}
            onPress={() => {
              actionLinks?.personalData.value && showWebView(actionLinks?.personalData.value, actionLinks?.personalData.name);
            }}
          >
            <View style={[styles.row]}>
              <Icon name="Read" size={24} color="#5B636B" />
              <Text style={[styles.textContent, styles.flexWrap]} numberOfLines={2} ellipsizeMode="tail">
                {t('profile.account.about.info')}
              </Text>
            </View>
            <Icon name="Arrow-Right" size={20} color="#5B636B" style={styles.alignSelfCenter} />
          </TouchableOpacity>
        )}
      </View>
      <WebView ref={webViewRef} />
      <PdfViewer ref={termpdfViewerRef} uri={actionLinks?.termVitality.value} type="url" />
    </View>
  );
};

export default About;
