import React from 'react';
import { Image, SafeAreaView, View } from 'react-native';
import { useTranslation } from 'react-i18next';
import { RouteProp, useNavigation, useRoute } from '@react-navigation/native';
import { Button, NativeModuleClient, Text } from 'superapp-common-component';
import { StackNavigationProp } from '@react-navigation/stack';
import dayjs from 'dayjs';

import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';

type ForgotUsernameSuccessNavigationProp = StackNavigationProp<TStackParamList, 'ForgotUsernameSuccess'>;
type ForgotUsernameSuccessRouteProps = RouteProp<TStackParamList, 'ForgotUsernameSuccess'>;

const ForgotUsernameSuccessScreen = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<ForgotUsernameSuccessNavigationProp>();
  const route = useRoute<ForgotUsernameSuccessRouteProps>();
  const { phoneNo = '' } = route.params || {};

  return (
    <SafeAreaView style={styles.container}>
      <View style={styles.content}>
        <View style={styles.wrapperContent}>
          <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/account-ready.png')} />
          <Text.Bold style={styles.textTitle}>{t('forgot.username.success.title')}</Text.Bold>
          <Text style={styles.textDescription}>
            {t('forgot.username.success.description')} <Text style={styles.phoneNo}>{phoneNo}</Text>
          </Text>
        </View>
        <Button
          text={t('forgot.username.button.login')}
          uppercase={false}
          type="primary"
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_USERNAME_SUCCESS,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Login',
            });
            navigation.navigate(ROUTE_PATH.DASHBOARD as never, { openModalLogin: true } as never);
          }}
          labelStyle={styles.buttonText}
          size="large"
          style={styles.button}
          id="ForgotUserName.btnLogin"
        />
      </View>
    </SafeAreaView>
  );
};

export default ForgotUsernameSuccessScreen;
