import React, { useState, useImperativeHandle, useRef } from 'react';
import { View, StatusBar, TouchableOpacity, SafeAreaView } from 'react-native';
import { Button, Icon, InnerLoading } from 'superapp-common-component';
import { WebView, WebViewNavigation } from 'react-native-webview';
import Text from '@/components/Text';
import { useTranslation } from 'react-i18next';
import { colors } from 'superapp-common-component';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';
import { a11y, isIOS } from '@/utils/utils';
import { HITSLOP_48 } from '@/utils/styles';

import styles from './styles';
import CircularProgress from '../CircularProgress';

export type TWebViewUrlRef = {
  openURL: (value: string) => void;
};

type WebViewPaymentProps = {
  onClose?: () => void;
};

const PAYMENT_URL_STATUS = [
  'https://wwwuat.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-khong-thanh-cong-aia.html',
  'https://wwwuat.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-khong-thanh-cong-pn.html',
  'https://wwwuat.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-khong-thanh-cong.html',
  'https://wwwuat.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-thanh-cong.html',
  'https://wwwuat.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/huy-giao-dich.html',
  'https://www.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/huy-giao-dich.html',
  'https://www.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-khong-thanh-cong-aia.html',
  'https://www.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-khong-thanh-cong-pn.html',
  'https://www.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-khong-thanh-cong.html',
  'https://www.aia.com.vn/vi/dich-vu-khach-hang/cac-kenh-dong-phi-bao-hiem/thanh-toan-thanh-cong.html',
];


const WebViewPayment = React.forwardRef(({ onClose }: WebViewPaymentProps, ref: any) => {
  const { t } = useTranslation();
  const [uri, setURI] = useState<any>('');
  const [modalsRef, modalInstance] = useModal();
  const webViewRef = useRef<WebView>(null);
  const [showCountDown, setShowCountDown] = useState<boolean>(false);

  const injectedJavaScript = `
  const notification = document.querySelector("#components-action-notification");
  const header = document.querySelector("header");
  const footer = document.querySelector("footer");
  const backgroundImage = document.getElementsByClassName("cmp-homepagehero--with-bg ")[0];
  const scriptHtml = document.getElementsByClassName("html-script")[0];

  if (notification) {
    notification.remove();
  }

  if (header) {
    header.remove();
    if (scriptHtml) {
      scriptHtml.style = "margin-top: -4rem";
    }
  }

  if (backgroundImage) {
    backgroundImage.style = "margin-top: 0";
  }

  if (footer) {
    footer.remove();
  }
  `;

  const onBackdropPress = () => {
    modalInstance().close();
    setURI('');
    onClose && onClose();
  };

  const onOpenURL = (url: string) => {
    setURI(url);
    modalInstance().show();
  };

  const onCloseNow = () => {
    if (showCountDown) {
      setShowCountDown(false);
    }
    onBackdropPress();
  };

  const onNavigationStateChange = (evt: WebViewNavigation) => {
    const { url } = evt;
    if (!url) {
      return;
    }

    if (PAYMENT_URL_STATUS.includes(url)) {
      setTimeout(() => {
        setShowCountDown(true);
      }, 5000);
    }
  };

  useImperativeHandle(
    ref,
    () => ({
      openURL: (value: string) => onOpenURL(value),
    }),
    [],
  );

  return (
    <Modal ref={modalsRef} overlayOpacity={0.5} onDismiss={() => null}>
      <StatusBar barStyle="light-content" />
      <SafeAreaView style={{ backgroundColor: colors.primary.red[100] }} />
      <View style={styles.container}>
        <View style={styles.header}>
          <TouchableOpacity
            style={{ paddingLeft: 4 }}
            onPress={onBackdropPress}
            {...a11y('WebView.btnCloseWebViewModal')}
            hitSlop={HITSLOP_48}
          >
            <Icon name="Close" size={24} color={colors.monoTone[0]} style={styles.iconClose} />
          </TouchableOpacity>
          <View>
            <Text numberOfLines={1} style={styles.titleHeader}>
              {t('common.title.webView')}
            </Text>
          </View>
          <View style={styles.fakeView} />
        </View>
        <View style={styles.content}>
          {uri !== '' && (
            <WebView
              ref={webViewRef as any}
              textZoom={100}
              source={{ uri }}
              onMessage={() => {}}
              startInLoadingState={true}
              onNavigationStateChange={onNavigationStateChange}
              injectedJavaScript={isIOS ? injectedJavaScript : ''}
              renderLoading={() => (
                <View style={[styles.content, styles.bgrWhite]}>
                  <InnerLoading />
                </View>
              )}
            />
          )}
        </View>
      </View>
      {showCountDown && (
        <View style={styles.containerCountDown}>
          <View style={styles.contentCountDown}>
            <Text style={styles.txtCountDown} numberOfLines={2}>
              {t('common.countdown.title')}
            </Text>
            <CircularProgress onDone={onCloseNow} />
            <Button text={t('common.countdown.close')} type="secondary" onPress={onCloseNow} style={styles.btnClose} />
          </View>
        </View>
      )}
      <SafeAreaView style={{ backgroundColor: 'white' }} />
    </Modal>
  );
});

export default WebViewPayment;
