export enum HealthReportItemKey {
  Vitals = 'Vitals',
  Fitness = 'Fitness',
  Nutrition = 'Nutrition',
  Sleep = 'Sleep',
  Stress = 'Stress',
  Smoking = 'Smoking',
}

export type AEMVitalityAgeType = {
  id: string;
  title?: string;
  description?: string;
  age: number,
};

export type AEMHealthReportCategoryDescriptionType = {
  feedbackIndicator?: string;
  inRangeIndicator?: string;
  description?: string;
};

export type AEMHealthReportCategoryRequiredType = {
  CategoryId: string;
  categoryTitle?: string;
};

export type AEMHealthReportCategoryType = AEMHealthReportCategoryRequiredType & {
  categoryDescription?: Array<AEMHealthReportCategoryDescriptionType>;
  extraCategories?: Array<AEMHealthReportCategoryType>;
  targetLabel?: string;
  type?: string;
};

export type AEMHealthReportListItemType = {
  id: string;
  categories?: Array<string>; // Unused stringified json
  categoryList?: AEMHealthReportCategoryType[];
  expandDescription?: string;
  relatedAssessment?: string;
  resultAnswers?: Array<string>;
  resultIdentifiers?: Array<string>;
  title?: string;
  unexpandDescription?: string;
};

export type AEMAssessmentConfigType = {
  id: string;
  assessmentID?: number;
  assessmentType?: string;
  extraAttributes?: Array<string>;
  name?: string;
};

export type FeedbackIndicatorType = {
  feedbackId: string;
  feedbackValue: string;
  feedbackTarget: string;
  feedbackUnitOfMeasure: string;
  feedbackRangeIndicator: string;
};

export type AEMHealthReportType = {
  id?: string;
  mainTitle?: string;
};

export type FeedbackType = {
  feedbackId: string;
  feedbackDesc: string;
};

type AssessmentDataType = {
  assessmentId: number;
  assessmentType: 'online' | 'offline';
  assessmentDesc: string;
  assessmentDate: string;
};

export type HealthReportFeedbackType = {
  actualAge: number;
  vitalityAge: AEMVitalityAgeType;
  feedback: FeedbackType[];
  feedbackIndicator: FeedbackIndicatorType[];
  assessmentData: AssessmentDataType;
};

export type AEMAssessmentsType = {
  'Assessment Configs'?: { [key: string]: AEMAssessmentConfigType };
  Assessments?: {};
  'Health Report'?: AEMHealthReportType;
  'Health Report List'?: { [itemKey: string]: AEMHealthReportListItemType };
  HealthReport?: {};
  'Vitality Age'?: AEMVitalityAgeType;
};

export type VitalityAgeSummaryType = {
  age: number;
  diffYears: number;
};

export type HealthReportItemCounts = {
  inRangeCount: number;
  noDataCount: number;
  totalCount: number;
};

export type HealthReportListItemType = AEMHealthReportListItemType & {
  categoryList: Array<AEMHealthReportCategoryType>,
};

export type HealthReportListType = Record<HealthReportItemKey, HealthReportListItemType> 