import React, { useLayoutEffect, useRef, useState, useEffect } from 'react';
import {
  View,
  SafeAreaView,
  KeyboardAvoidingView,
  Platform,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Keyboard,
  Animated,
  Image,
  StatusBar,
  ScrollView,
} from 'react-native';
import { Button, Icon, TextInput, theme, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { useForm } from 'react-hook-form';
import { Controller } from 'react-hook-form';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import _isEmpty from 'lodash/isEmpty';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';

import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import { checkRuleNoSequencedPassword, a11y } from '@/utils/utils';
import { FORGOT_PASS_ERROR_CODES, REGEX } from '@/utils/constants';
import ROUTE_PATH from '@/navigators/routePath';
import { HITSLOP } from '@/utils/styles';
import { TFotgotPasswordTypeState } from '@/pages/ForgotPassword/stores/state';
import { makeSelectLoading, makeSelectLoadingForgotPassword } from '@/pages/ForgotPassword/stores/selectors';
import { TConnectState } from '@/stores/types';
import { submitNewPassword as submitNewPasswordAction } from '@/pages/ForgotPassword/stores/actions';
import { loginOkta } from '@/stores/authentication/actions';
import Text from '@/components/Text';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import NoConnection from '@/components/NoConnection';
import Loading, { ILoadingRef } from '@/components/Loading';

import styles from './styles';
import ModalForgotSession, { ModalForgotSessionRef } from '@/components/ModalForgotSession';

const { colors } = theme;

interface CreateNewPassPropsType {
  loading: boolean;
  submitNewPassword: (payload: { newPassword: string }) => Promise<any>;
  loginAfterForgotPasswordSuccessDispatch: (payload: {
    username: string;
    password: string;
    setVisibleLoginModal: () => void;
    isAutoLogin?: boolean;
  }) => any;
}
type CreateNewPassNavigationProp = StackNavigationProp<TStackParamList, 'CreateNewPass'>;

const CreateNewPass = (props: CreateNewPassPropsType) => {
  const { loading, submitNewPassword, loginAfterForgotPasswordSuccessDispatch } = props;
  const navigation = useNavigation<CreateNewPassNavigationProp>();
  const { t } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const [isSecurePW, setIsSecurePW] = useState(true);
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [isSecureConfirmPW, setIsSecureConfirmPW] = useState(true);
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalErrorPassHistoryRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalForgotSessionExpiredRef = useRef<ModalForgotSessionRef>(null);
  const animation = useRef(new Animated.Value(0)).current;
  const loadingRef = useRef<ILoadingRef>(null);
  const [infoAfterResetPassSuccess, setInfoAfterResetPassSuccess] = useState<{ username: string; password: string } | undefined>(undefined);

  const {
    control,
    handleSubmit,
    formState: { errors },
    watch,
    getValues,
  } = useForm({
    defaultValues: {
      password: '',
      confirmPass: '',
    },
  });

  const hideKeyboard = () => Keyboard.dismiss();

  const handleSubmitNewPass = async (data: { password: string; confirmPass: string }) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.CREATE_NEW_PASS,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });
    const { password } = data;
    try {
      Keyboard.dismiss();
      const infoResetPassSuccess: { username: string; password: string } = await submitNewPassword({ newPassword: password });
      if (!_isEmpty(infoResetPassSuccess)) {
        Keyboard.dismiss();
        NativeModuleClient.AdobeExperience.trackScreen({
          screen: 'Forgot password success',
          unixTimestamp: dayjs().unix(),
        });
        setInfoAfterResetPassSuccess(infoResetPassSuccess);
      }
    } catch (error) {
      if (error === FORGOT_PASS_ERROR_CODES.PASSS_HISTORY_INVALID) {
        modalErrorPassHistoryRef?.current?.open();
      } else {
        modalErrorRef?.current?.open();
      }
    }
  };

  const handleAfterPasswordSuccess = () => {
    if (infoAfterResetPassSuccess) {
      loginAfterForgotPasswordSuccessDispatch({ ...infoAfterResetPassSuccess, setVisibleLoginModal: () => null, isAutoLogin: true });
      navigation.navigate(ROUTE_PATH.DASHBOARD, { isResetPassSuccess: true });
      setInfoAfterResetPassSuccess(undefined);
    }
  };

  const onCheckRulePasswordDuplicateSequenced = (value: string) => {
    if (value) {
      if (!REGEX.NO_DUPLICATE_CHARACTER.test(value) && checkRuleNoSequencedPassword(value)) return true;
    }
    return false;
  };

  const onPressLogin = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Forgot password success',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login',
    });
    navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <Animated.View style={[styles.main, { marginLeft: animation }]}>
        <Loading visible={loading} ref={loadingRef} onDismiss={handleAfterPasswordSuccess} />
        <View style={styles.pane}>
          <TouchableWithoutFeedback {...a11y('Forgot.btnCreatePassHideKeyboard')} onPress={hideKeyboard}>
            <KeyboardAvoidingView
              behavior={Platform.OS === 'ios' ? 'padding' : undefined}
              style={{ flex: 1 }}
              keyboardVerticalOffset={Platform.OS === 'ios' ? 30 : 0}
            >
              <View style={{ flex: 1, backgroundColor: 'yellow' }}>
                <View style={styles.container}>
                  <View style={styles.modalBox}>
                    <View style={{ flex: 1 }}>
                      <View style={styles.topModal}>
                        <TouchableOpacity
                          {...a11y('Forgot.btnCreatePassHeaderGoback')}
                          onPress={() => {
                            NativeModuleClient.AdobeExperience.trackButton({
                              screen: ROUTE_PATH.CREATE_NEW_PASS,
                              unixTimestamp: dayjs().unix(),
                              buttonText: 'Go back',
                            });
                            navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
                          }}
                          hitSlop={HITSLOP}
                        >
                          <Icon name="Close" size={24} color={colors.primary.charcoal[80]} />
                        </TouchableOpacity>
                        <View>
                          <Text style={styles.mainTitle} fontFamily="bold">
                            {t('forgot.password.new.password.title')}
                          </Text>
                        </View>
                        <TouchableOpacity
                          {...a11y('Forgot.btnCreatePassHeaderInfo')}
                          onPress={() => {
                            NativeModuleClient.AdobeExperience.trackButton({
                              screen: ROUTE_PATH.CREATE_NEW_PASS,
                              unixTimestamp: dayjs().unix(),
                              buttonText: 'Info',
                            });
                          }}
                          hitSlop={HITSLOP}
                        >
                          <Icon name="Info1" size={24} color={colors.primary.charcoal[80]} />
                        </TouchableOpacity>
                      </View>

                      <ScrollView style={{ flex: 1 }}>
                        <View style={styles.inputWrapper}>
                          <Controller
                            control={control}
                            rules={{
                              required: { value: true, message: t('forgot.password.form.password.required') },
                              pattern: {
                                value: REGEX.PASSWORD,
                                message: t('forgot.password.form.password.format.pass'),
                              },
                              validate: value => checkRuleNoSequencedPassword(value) || t('forgot.password.form.password.format.pass'),
                            }}
                            render={({ field: { onChange, value } }) => (
                              <TextInput
                                label={t('forgot.password.form.label.password')}
                                value={value}
                                disabled={false}
                                onChangeText={onChange}
                                type={errors.password && 'error'}
                                containerStyle={styles.marginBottomInput}
                                message={errors.password?.message || undefined}
                                secureTextEntry={isSecurePW}
                                right={{ source: isSecurePW ? 'Show' : 'Mask' }}
                                style={styles.heightInput}
                                onPressIcon={() => setIsSecurePW(!isSecurePW)}
                                maxLength={64}
                                iconColor="#000000"
                                id="Forgot.ipCreatePassHidePassword"
                                textContentType="oneTimeCode"
                                autoComplete="off"
                              />
                            )}
                            name="password"
                          />
                          {/* Re-password */}
                          <Controller
                            control={control}
                            rules={{
                              required: { value: true, message: t('forgot.password.form.confirm.pass.required') },
                              validate: value => value === watch('password') || t('forgot.password.form.confirm.pass.validate'),
                            }}
                            render={({ field: { onChange, value } }) => (
                              <TextInput
                                label={t('forgot.password.form.label.confirm')}
                                value={value}
                                disabled={false}
                                onChangeText={onChange}
                                type={errors.confirmPass && 'error'}
                                containerStyle={styles.marginBottomInput}
                                message={errors.confirmPass?.message || undefined}
                                secureTextEntry={isSecureConfirmPW}
                                right={{ source: isSecureConfirmPW ? 'Show' : 'Mask' }}
                                style={styles.heightInput}
                                onPressIcon={() => setIsSecureConfirmPW(!isSecureConfirmPW)}
                                maxLength={64}
                                iconColor="#000000"
                                id="Forgot.ipCreatePassHideConfirmPassword"
                                textContentType="oneTimeCode"
                                autoComplete="off"
                              />
                            )}
                            name="confirmPass"
                          />
                        </View>
                        <View style={styles.parentRule}>
                          <View style={styles.infoRule}>
                            <Icon
                              name={watch('password') && REGEX.AT_LEAST_9_CHARACTER.test(getValues('password')) ? 'Success' : 'Info1'}
                              color={watch('password') && REGEX.AT_LEAST_9_CHARACTER.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                              style={styles.marginRight8}
                            />
                            <Text style={styles.ruleTitle}>{t('forgot.password.form.rule1')}</Text>
                          </View>
                          <View style={styles.infoRule}>
                            <Icon
                              name={watch('password') && REGEX.AT_LEAST_UPPERCASE.test(getValues('password')) ? 'Success' : 'Info1'}
                              color={watch('password') && REGEX.AT_LEAST_UPPERCASE.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                              style={styles.marginRight8}
                            />
                            <Text style={styles.ruleTitle}>{t('forgot.password.form.rule2')}</Text>
                          </View>
                          <View style={styles.infoRule}>
                            <Icon
                              name={watch('password') && REGEX.AT_LEAST_LOWERCASE.test(getValues('password')) ? 'Success' : 'Info1'}
                              color={watch('password') && REGEX.AT_LEAST_LOWERCASE.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                              style={styles.marginRight8}
                            />
                            <Text style={styles.ruleTitle}>{t('forgot.password.form.rule3')}</Text>
                          </View>
                          <View style={styles.infoRule}>
                            <Icon
                              name={watch('password') && onCheckRulePasswordDuplicateSequenced(getValues('password')) ? 'Success' : 'Info1'}
                              color={
                                watch('password') && onCheckRulePasswordDuplicateSequenced(getValues('password')) ? '#3DA758' : '#1F78AD'
                              }
                              style={styles.marginRight8}
                            />
                            <Text style={styles.ruleTitle}>{t('forgot.password.form.rule4')}</Text>
                          </View>
                          <View style={styles.infoRule}>
                            <Icon
                              name={
                                watch('password') && REGEX.AT_LEAST_CAPTIAL_SPECIAL_NUMBER.test(getValues('password')) ? 'Success' : 'Info1'
                              }
                              color={
                                watch('password') && REGEX.AT_LEAST_CAPTIAL_SPECIAL_NUMBER.test(getValues('password'))
                                  ? '#3DA758'
                                  : '#1F78AD'
                              }
                              style={styles.marginRight8}
                            />
                            <Text style={styles.ruleTitle}>{t('forgot.password.form.rule5')}</Text>
                          </View>
                        </View>
                      </ScrollView>
                    </View>

                    <View style={styles.buttonWrapper}>
                      <Button
                        text={t('forgot.password.button.continue')}
                        labelStyle={styles.buttonText}
                        onPress={handleSubmit(handleSubmitNewPass)}
                        id="Forgot.btnCreatePassSubmit"
                      />
                    </View>
                  </View>
                </View>
              </View>
            </KeyboardAvoidingView>
          </TouchableWithoutFeedback>
          <ModalError
            ref={modalErrorRef}
            title={t('forgot.password.modal.otp.error.title')}
            subtitle={t('forgot.password.modal.change.pass.sub.error')}
            btnText={t('forgot.password.modal.otp.error.button.close')}
          />
          <ModalError
            ref={modalErrorPassHistoryRef}
            title={t('forgot.password.modal.otp.error.title')}
            subtitle={t('forgot.password.changePass.error.duplicateHistory')}
            btnText={t('forgot.password.modal.otp.error.button.close')}
          />
          <ModalForgotSession ref={modalForgotSessionExpiredRef} handleClose={() => navigation.navigate(ROUTE_PATH.FORGOT_PASSWORD)} />
        </View>

        <View style={styles.pane}>
          <StatusBar barStyle="dark-content" />
          <View style={styles.container}>
            <View style={styles.contentWrapper}>
              <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/account-ready.png')} />
              <Text style={styles.textTitle} fontFamily="bold">
                {t('forgot.password.modal.success.main.title')}
              </Text>
              <Text style={styles.textDescription}>{t('forgot.password.modal.success.sub.title')}</Text>
            </View>
            <View style={[styles.buttonSuccessWrapper]}>
              <Button
                text={t('forgot.password.login.button')}
                onPress={onPressLogin}
                uppercase={false}
                labelStyle={styles.buttonText}
                id="forgot.password.login.button"
              />
            </View>
          </View>
        </View>
      </Animated.View>
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TFotgotPasswordTypeState['loading'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoadingForgotPassword(),
});

type MapDispatchToPropsType = {
  submitNewPassword: (payload: { newPassword: string }) => Promise<any>;
  loginAfterForgotPasswordSuccessDispatch: (payload: { username: string; password: string; setVisibleLoginModal: () => void }) => any;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  submitNewPassword: (payload: { newPassword: string }) =>
    new Promise((resolve, reject) => dispatch(submitNewPasswordAction(payload, resolve, reject))),
  loginAfterForgotPasswordSuccessDispatch: payload => dispatch(loginOkta(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(CreateNewPass);
