import axios, { Canceler, CancelTokenStatic } from 'axios';
import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

type TAuthenParam = { username: string; password: string; deviceId?: { os: string; notificationToken: string }; clientId: string };
type TRefreshToken = { clientId: string; refreshToken: string; refreshTokenOkta?: string; clientIdOkta?: string; deviceId: { os: string; notificationToken: string } };
type TChangePassword = { clientId: string; currentUsername: string; currentPassword: string; newPassword: string };
type TChangePasswordPreLogin = { clientId: string; username: string; currentPassword: string; newPassword: string };
type CheckUsernameParam = { username: string; clientId: string };
type CheckIdNoParam = {
  userId: string;
  idNo: string;
  clientId: string;
  idDocType: string;
};
type RequestOtpParam = {
  userId: string;
  captchaValue: string;
  captchaHash: string;
  otpSendType: string;
  clientId: string;
};
type ConfirmForgotUsernameParam = {
  dob: string;
  clientId: string;
  sessionId: string;
  otpValue: string;
  idCardNo: string;
  phoneNo: string;
};
type VerifyOtp = {
  sid?: string;
  otpId?: string;
  otpValue: string;
  clientId: string;
};
type SubmitForgotPassParam = {
  username: string;
  idNo: string;
  clientId: string;
  captchaValue: string;
};
type ResetPassForgotPassParam = {
  newPassword: string;
  sid: string;
  clientId: string;
  captchaValue: string;
  otpId: string;
  otpValue: string;
};

type ChangeUsernameForgotPassParam = {
  sessionId: string;
  clientId: string;
  newUsername: string;
};

type SubmitForgotUsernameParam = {
  dob: string;
  idCardNo: string;
  clientId: string;
  phoneNo: string;
};

type MigrationParams = {
  clientId: string;
  otpValue: string;
  newUsername: string;
  otpId: string;
};
type RequestOtpLoginParams = { otpType: string; otpSendType: string };
type VerifyOtpLoginParams = {
  otpValue: 'string';
  otpType: 'string';
};

type TRegisterDeviceInfo = {
  clientId: string;
  os: string; // ios/android
  tag: string; // decode token, subid
  deviceId: string; // pushChannel
};

class AuthService extends Request {
  private CancelToken: CancelTokenStatic;

  public constructor() {
    super(APP_ENV.APPLICATION_API_CIAM);
    this.CancelToken = axios.CancelToken;
  }

  public checkLoginService = (params: TAuthenParam) => this.instance.post<MODEL.ICheckLogin>('/auth/checkLogin', params);

  public authenticateService = (params: TAuthenParam, effectCancel: (cancel: Canceler) => void) =>
    this.instance.post<MODEL.IAuthenticate>('/authenticate', params, {
      cancelToken: new this.CancelToken(effectCancel),
    });

  public registerDeviceInfo = (params: TRegisterDeviceInfo) => this.instance.post('/registerDeviceInfo', params);

  public authenticateOktaService = (params: any, effectCancel: (cancel: Canceler) => void) =>
    this.instance.post<MODEL.IAuthenticate>('/okta/token', params, {
      cancelToken: new this.CancelToken(effectCancel),
    });

  public logoutService = () => this.instance.post('/logout', { clientId: APP_ENV.CLIENT_ID });

  public refreshTokenService = (params: TRefreshToken) => this.instance.post<MODEL.IAuthenticate>('/refreshToken', params);

  public changePasswordService = (params: TChangePassword) => this.instance.post<any>('/changePassword', params);

  public changePasswordPreLoginService = (params: TChangePasswordPreLogin) => this.instance.post('/auth/changePasswordPreLogin', params);

  public checkUsername = (params: CheckUsernameParam) =>
    this.instance.post<MODEL.ICheckUsernameRes>('/forgotPassword/v2/checkUserName', { ...params });

  public checkIdno = (params: CheckIdNoParam) => this.instance.post<MODEL.ICheckIdNoRes>('/forgotPassword/checkIdNo', { ...params });

  public requestOtp = (params: RequestOtpParam) => this.instance.post<MODEL.IRequestOtpRes>('/forgotPassword/requestOtp', { ...params });

  public verifyOtp = (params: VerifyOtp) => this.instance.post<any>('/forgotPassword/v2/verifyOtp', params);

  public submitForgotPassword = (params: SubmitForgotPassParam) =>
    this.instance.post<MODEL.ISubmitForgotPassRes>('/forgotPassword/v2/submit', params);
  
  public resetPassForgotPassword = (params: ResetPassForgotPassParam) =>
    this.instance.post<any>('/forgotPassword/v2/resetPass', params);

  public changeUserNameForgotPassword = (params: ChangeUsernameForgotPassParam) =>
    this.instance.post<any>('/forgotPassword/changeUsername', params);

  public migrateUserName = (params: MigrationParams) => this.instance.post<any>('/changeUsername', params);

  public requestOtpLogin = (params: RequestOtpLoginParams) => this.instance.post<any>('/otp/requestMyOtp', params);

  public verifyOtpLogin = (params: VerifyOtpLoginParams) => this.instance.post<any>('/otp/verifyMyOtp', params);

  public submitForgotUsername = (params: SubmitForgotUsernameParam) =>
    this.instance.post<MODEL.ISubmitForgotUsernameRes>('/forgotUsername', params);

  public forgotUsernameReqOtp = (params: RequestOtpParam) => this.instance.post<MODEL.IRequestOtpRes>('/forgotUsername/requestOtp', params);

  public forgotUsernameConfirm = (params: ConfirmForgotUsernameParam) =>
    this.instance.post<MODEL.IForgotUsernameConfirmRes>('/forgotUsername/confirm', params);

  public requestProcessP820Service = (params: API.IRequestProcessP820): Promise<MODEL.IProcessP820> =>
    this.instance.post('/processP820/request', params);

  public submitProcessP820Service = (params: API.ISubmitProcessP820): Promise<MODEL.ISubmitP820> =>
    this.instance.post('/processP820/submit', params);

  public logActionService = (params: API.ILogAction): Promise<any> => this.instance.post('/user/logAction', params);
}

export default new AuthService();
