import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS } from './constants';
import initialState, { TVerifyAccountQRCodeState } from './state';

const verifyAccountQRCodeReducer = (state: TVerifyAccountQRCodeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TVerifyAccountQRCodeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
    }
  });
};

export default verifyAccountQRCodeReducer;
