import React, {useMemo, useRef} from 'react';
import {
  View,
  ScrollView,
  useWindowDimensions,
  ViewStyle,
  ImageStyle,
} from 'react-native';
import _reduce from 'lodash/reduce';
import {useTheme} from 'superapp-common-component';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import _set from 'lodash/set';
import Animated, {
  SharedValue,
  interpolate,
  useAnimatedScrollHandler,
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from 'react-native-reanimated';

import {a11y, isEmpty} from '@/utils/utils';
import APP_ENV from '@/config/app.config';
import {Theme} from '@/config/themes.config';

import useStyle, {DOT_SIZE, HEIGHT_SLIDER, TOTAL_MARGIN} from './styles';
import {SCROLL_TARGET} from '../../styles';

interface SliderProps {
  banners: MODEL.IBanner | undefined;
  scrollAnimation: SharedValue<number>;
}

const Slider: React.FC<SliderProps> = ({scrollAnimation, banners}) => {
  const theme = useTheme() as Theme;
  const sliderRef = useRef<ScrollView | null>(null);
  const scrollX = useSharedValue(0);
  const loadImage = useSharedValue(0);

  const scrollHandler = useAnimatedScrollHandler(
    {
      onScroll: event => {
        // scrollX.value = event.contentOffset.x;
      },
    },
    [],
  );

  const styles = useStyle(theme);
  const {width} = useWindowDimensions();

  const filterBanner = useMemo(() => {
    if (banners) {
      return banners.items.vi.items.map(item => {
        return _reduce(
          item.elements,
          (curr, next: {name: string; value: string}) => {
            _set(curr, next.name, next.value);
            return curr;
          },
          {} as {image: string; id: string},
        );
      });
    }
    return [];
  }, [banners]);

  const stylesContainerAnimation = useAnimatedStyle(
    () => ({
      zIndex: 1,
      height: interpolate(
        scrollAnimation.value,
        [0, SCROLL_TARGET],
        [HEIGHT_SLIDER, 0],
        'clamp',
      ),
    }),
    [scrollAnimation],
  );

  const styleDotAnimation = useAnimatedStyle(
    () => ({
      transform: [
        {
          translateX: interpolate(
            scrollX.value,
            [-width, 0, width],
            [-DOT_SIZE + TOTAL_MARGIN, 0, DOT_SIZE + TOTAL_MARGIN],
          ),
        },
      ],
    }),
    [scrollX],
  );

  const styleImage = useAnimatedStyle(
    () => ({
      opacity: interpolate(loadImage.value, [0, 1], [0, 1]),
      transform: [
        {
          scale: interpolate(loadImage.value, [0, 1], [0.85, 1]),
        },
      ],
    }),
    [loadImage],
  );

  const styleSkeleton = useAnimatedStyle(
    () => ({
      opacity: interpolate(loadImage.value, [0, 1], [1, 0]),
    }),
    [loadImage],
  );

  const stylePagination = () => ({
    opacity: interpolate(scrollAnimation.value, [0, HEIGHT_SLIDER / 2], [1, 0]),
  });

  const onLoadImage = () => {
    loadImage.value = withTiming(1, {duration: 300});
  };

  return (
    <Animated.View style={[styles.container, stylesContainerAnimation]}>
      {isEmpty(banners) && (
        <Animated.View style={styleSkeleton}>
          <SkeletonPlaceholder>
            <SkeletonPlaceholder.Item flexDirection="row" alignItems="center">
              <SkeletonPlaceholder.Item
                width={width}
                height={160}
                borderRadius={4}
              />
            </SkeletonPlaceholder.Item>
          </SkeletonPlaceholder>
        </Animated.View>
      )}
      <ScrollView
        horizontal
        pagingEnabled
        ref={sliderRef}
        scrollEventThrottle={16}
        showsHorizontalScrollIndicator={false}
        // onScroll={scrollHandler}
        {...a11y('Home.Banner.scrollViewAnimate')}>
        {filterBanner.map(item => (
          <Animated.Image
            key={item?.id}
            style={[styles.image, styleImage]}
            source={{uri: `${APP_ENV.CONTENT_AEM}${item.image}`}}
            onLoadEnd={onLoadImage}
          />
        ))}
      </ScrollView>

      <Animated.View style={[styles.pagination, stylePagination()]}>
        <Animated.View style={[styles.dots, styleDotAnimation]} />
        {filterBanner.map(item => (
          <View key={item?.id} style={styles.dot} />
        ))}
      </Animated.View>
    </Animated.View>
  );
};

export const WrapperSlider: React.FC<{
  scrollAnimation: SharedValue<number>;
  style?: ViewStyle;
  children?: React.ReactNode;
}> = props => {
  const {scrollAnimation, style, children} = props;

  const styleAnimationWrapperSlide = useAnimatedStyle(
    () => ({
      height: interpolate(
        scrollAnimation.value,
        [0, HEIGHT_SLIDER],
        [HEIGHT_SLIDER, 0],
        'clamp',
      ),
    }),
    [scrollAnimation],
  );

  return (
    <Animated.View style={[styleAnimationWrapperSlide, style]}>
      {children}
    </Animated.View>
  );
};

export default Slider;
