import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import {
  Keyboard,
  KeyboardAvoidingView,
  SafeAreaView,
  TouchableOpacity,
  View,
} from 'react-native';
import {
  RouteProp,
  useIsFocused,
  useNavigation,
  useRoute,
} from '@react-navigation/native';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { useTranslation } from 'react-i18next';
import { utils } from 'superapp-common-component';
import { colors } from 'superapp-common-component';
import {
  Icon,
  Loading,
  NativeModuleClient,
  SendOTP,
  Text,
} from 'superapp-common-component';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { createStructuredSelector } from 'reselect';

import { HITSLOP } from '@/utils/styles';
import { isIOS, maskedPhoneNumber } from '@/utils/utils';
import {
  BLOCK_SUBMIT_OTP_ERROR_TYPE,
  ERROR_CODE_OTP,
  FORGOT_PASS_ERROR_CODES,
  OTP_TYPE,
} from '@/utils/constants';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';

import {
  requestOtpBySmsAction,
  submitOtp,
  resetOtpError as resetOtpErrorAction,
  resetCaptchaError as resetCaptchaErrorAction,
  getCaptchaAction,
} from '@/pages/ForgotUsername/stores/actions';
import ModalError, {
  ModalConfirmErrorRef,
} from '@/components/ModalConfirmError';
import NoConnection from '@/components/NoConnection';
import ModalForgotSession, {
  ModalForgotSessionRef,
} from '@/components/ModalForgotSession';
import TurnstileCapcha, {
  TurnstileCapchaRef,
} from '@/components/TurnstileCapcha';
import { TConnectState } from '@/stores/types';

import { TForgotUsernameTypeState } from '../ForgotUsername/stores/state';
import {
  makeSelectInfoCaptcha,
  makeSelectIsCaptchaError,
  makeSelectIsVerifyOtpError,
  makeSelectLoading,
} from '../ForgotUsername/stores/selectors';

import styles from './styles';
const { a11y } = utils;

type ForgotUsernameOtpNavigationProp = StackNavigationProp<
  TStackParamList,
  'ForgotUsernameOtp'
>;
type ForgotUsernameOtpRouteProps = RouteProp<
  TStackParamList,
  'ForgotUsernameOtp'
>;
type ForgotUsernameOtpProps = {
  loading: boolean;
  infoCaptcha: any;
  requestOtpBySms: (payload: {
    params: { captchaValue: string; otpSendType: string };
    requestOtpCallback: (number: number) => void;
  }) => any;
  handleSubmitOtp: (payload: { otpValue: string }) => Promise<string>;
  resetOtpError: () => void;
  getCaptcha: () => any;
  resetCaptchaError: () => void;
  verifyOtpStatus: TForgotUsernameTypeState['verifyOtpStatus'];
  isCaptchaError: TForgotUsernameTypeState['isCaptchaError'];
};

const ForgotUsernameOtp = (props: ForgotUsernameOtpProps) => {
  const {
    isCaptchaError,
    loading,
    handleSubmitOtp,
    resetOtpError,
    resetCaptchaError,
    requestOtpBySms,
    verifyOtpStatus,
  } = props;
  const { t } = useTranslation();
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const navigation = useNavigation<ForgotUsernameOtpNavigationProp>();
  const modalInvalidCaptchaRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockSubmitOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockRequestOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalForgotSessionExpiredRef = useRef<null | ModalForgotSessionRef>(
    null,
  );
  const modalDisabledSubmitOtp = useRef<null | ModalConfirmErrorRef>(null);
  const isFocused = useIsFocused();
  const route = useRoute<ForgotUsernameOtpRouteProps>();
  const { phoneNo = '', allowCancel } = route.params || {};
  const [seconds, setSeconds] = useState<number>(0);
  const [otpValue, setOtpValue] = useState<string>('');
  const { isVerifyOtpError, errorType } = verifyOtpStatus;
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const netInfo = NetInfo.useNetInfo();
  const turnstileCapchaRef = useRef<TurnstileCapchaRef>(null);
  const [otpType, setOtpType] = useState<string>('');

  const showModalCaptch = async (type: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_USERNAME_CONFIRM,
      unixTimestamp: dayjs().unix(),
      buttonText: type === OTP_TYPE.SMS ? 'SMS' : 'Voice',
    });
    Keyboard.dismiss();
    setOtpType(type);
    setTimeout(() => turnstileCapchaRef?.current?.open(), 200);
  };

  const onSubmitOtp = async (
    setClearInput?: (value: boolean) => void,
    setCodeState?: (value: string) => void,
  ) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_USERNAME_CONFIRM,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Verify',
    });
    Keyboard.dismiss();
    if (loading) return;
    if (otpValue.length === 6) {
      try {
        resetOtpError();
        const username = await handleSubmitOtp({ otpValue });
        setClearInput?.(true);
        setOtpValue('');
        setCodeState?.('');
        navigation.navigate(ROUTE_PATH.FORGOT_USERNAME_SUCCESS as any, {
          phoneNo: username,
        });
      } catch (error) {}
    } else {
      modalErrorRef?.current?.open();
    }
  };

  const onHideModalInvalidCaptcha = () => {
    resetCaptchaError();
  };

  const onDismissLoading = () => {
    if (isVerifyOtpError === undefined && isCaptchaError.status === undefined)
      return;
    if (isVerifyOtpError) {
      switch (errorType) {
        case BLOCK_SUBMIT_OTP_ERROR_TYPE:
          modalBlockSubmitOtp?.current?.open();
          break;
        case FORGOT_PASS_ERROR_CODES.SESSION_INVALID:
          modalForgotSessionExpiredRef?.current?.open();
          break;
        case ERROR_CODE_OTP.OTP_DISABLED:
          modalDisabledSubmitOtp?.current?.open();
          break;
        default:
          modalErrorRef?.current?.open();
          break;
      }
      resetOtpError();
    } else if (isCaptchaError?.status) {
      switch (isCaptchaError?.errorType) {
        case BLOCK_SUBMIT_OTP_ERROR_TYPE:
          modalBlockRequestOtp?.current?.open();
          break;
        case FORGOT_PASS_ERROR_CODES.SESSION_INVALID:
          modalForgotSessionExpiredRef?.current?.open();
          break;
        default:
          modalInvalidCaptchaRef?.current?.open();
          break;
      }
      resetCaptchaError();
    } else if (isFocused && isVerifyOtpError === false) {
      resetOtpError();
      resetCaptchaError();
    }
  };

  const onCapcha = (token: string) => {
    turnstileCapchaRef.current?.close();
    requestOtpBySms({
      params: {
        captchaValue: token,
        otpSendType: otpType,
      },
      requestOtpCallback: (times: number) => {
        setSeconds(times);
      },
    });
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text.Bold style={styles.headerTitle}>
          {t('forgot.password.modal.otp.header')}
        </Text.Bold>
      ),
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('Forgot.btnOtpHeaderGoback')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_USERNAME_CONFIRM,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}>
          <Icon
            name="Arrow-Left"
            size={24}
            color={colors.primary.charcoal[80]}
            style={styles.marginLeft21}
          />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useEffect(() => {
    showModalCaptch(OTP_TYPE.SMS);
  }, []);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.container}>
      <KeyboardAvoidingView
        behavior={isIOS ? 'padding' : 'height'}
        style={{ flex: 1, position: 'relative' }}
        keyboardVerticalOffset={isIOS ? 75 : 0}>
        <View style={styles.flex1}>
          <View style={styles.otpContent}>
            <SendOTP
              textDescription={t('common.otp.textPhone', {
                phone: maskedPhoneNumber(phoneNo),
              })}
              textTimeCount={t('common.otp.text.countTimeTitle')}
              submitText={t('forgot.password.modal.otp.button.verify')}
              onChange={code => setOtpValue(code)}
              sendBySMS={() => showModalCaptch(OTP_TYPE.SMS)}
              sendByVoice={() => showModalCaptch(OTP_TYPE.VOICE)}
              onSubmit={onSubmitOtp}
              loading={loading}
              disabled={loading}
              secondNumber={seconds}
              setSecondNumber={setSeconds}
              textSMS={t('common.otp.text.resend')}
              textVoice={t('common.otp.text.viaCall')}
              textCountSecond={t('common.text.countSecond')}
              textNotSendSMS={t('common.otp.text.noSms')}
              id="Forgot.otpRequestOtp"
            />
          </View>
        </View>
      </KeyboardAvoidingView>
      <ModalError
        ref={modalErrorRef}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('forgot.password.modal.otp.error.des')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalInvalidCaptchaRef}
        title={t('forgot.password.modal.otp.wrong.title')}
        subtitle={t('forgot.password.modal.otp.error.captcha')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
        onConfirm={onHideModalInvalidCaptcha}
      />
      <ModalError
        ref={modalBlockSubmitOtp}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('forgot.password.modal.otp.error.block.otp')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalBlockRequestOtp}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('forgot.password.modal.otp.error.block.request.otp')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalDisabledSubmitOtp}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('common.message.otpDisable')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
        onConfirm={() => navigation.canGoBack() && navigation.goBack()}
      />
      <ModalForgotSession
        ref={modalForgotSessionExpiredRef}
        handleClose={() =>
          navigation.navigate(ROUTE_PATH.FORGOT_USERNAME as never)
        }
      />
      <Loading visible={loading} dismissAction={onDismissLoading} />
      <TurnstileCapcha
        ref={turnstileCapchaRef}
        onSuccess={onCapcha}
        onError={() => null}
      />
    </SafeAreaView>
  ) : (
    <View style={styles.flex1}>
      <NoConnection />
    </View>
  );
};

type CreateStructuredSelectorType = {
  loading: TForgotUsernameTypeState['loading'];
  verifyOtpStatus: TForgotUsernameTypeState['verifyOtpStatus'];
  infoCaptcha: TForgotUsernameTypeState['infoCaptcha'];
  isCaptchaError: TForgotUsernameTypeState['isCaptchaError'];
};

const mapStateToProps = createStructuredSelector<
  TConnectState,
  CreateStructuredSelectorType
>({
  loading: makeSelectLoading(),
  verifyOtpStatus: makeSelectIsVerifyOtpError(),
  infoCaptcha: makeSelectInfoCaptcha(),
  isCaptchaError: makeSelectIsCaptchaError(),
});

type MapDispatchToPropsType = {
  requestOtpBySms: (payload: {
    params: { captchaValue: string; otpSendType: string };
    requestOtpCallback: (number: number) => void;
  }) => any;
  handleSubmitOtp: (payload: { otpValue: string }) => Promise<string>;
  resetOtpError: () => void;
  getCaptcha: () => any;
  resetCaptchaError: () => void;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  requestOtpBySms: payload => dispatch(requestOtpBySmsAction(payload)),
  handleSubmitOtp: (payload: { otpValue: string }) =>
    new Promise((resolve, reject) =>
      dispatch(submitOtp(payload, resolve, reject)),
    ),
  resetOtpError: () => dispatch(resetOtpErrorAction()),
  getCaptcha: () => dispatch(getCaptchaAction()),
  resetCaptchaError: () => dispatch(resetCaptchaErrorAction()),
});

export default connect(mapStateToProps, mapDispatchToProps)(ForgotUsernameOtp);
