import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import {
  View,
  SafeAreaView,
  StyleSheet,
  KeyboardAvoidingView,
  Platform,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Keyboard,
  Alert,
  Linking,
} from 'react-native';
import { Icon, Button, TextInput, Loading, theme, Text as TextCommon, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { Controller } from 'react-hook-form';
import { useNavigation, useIsFocused } from '@react-navigation/native';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import _isEmpty from 'lodash/isEmpty';
import { TStackParamList } from '@/navigators';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';

import { FORGOT_PASS_ERROR_CODES, ID_NO_TYPE_FORGOT_PASS, REGEX } from '@/utils/constants';
import { submitVerifyPersonalIDNumber } from '@/pages/ForgotPassword/stores/actions';
import { makeSelectLoading, makeSelectPhoneNumber } from '@/pages/ForgotPassword/stores/selectors';
import { HEADER_TITLE_BGRWHITE, HITSLOP } from '@/utils/styles';
import Text from '@/components/Text';
import ROUTE_PATH from '@/navigators/routePath';
import { a11y } from '@/utils/utils';
import NoConnection from '@/components/NoConnection';

import ModalConfirm, { ModalConfirmRef } from '../ModalConfirm';

import styles from './styles';
import ModalForgotSession, { ModalForgotSessionRef } from '@/components/ModalForgotSession';

interface PersonalInfoPropsType {}
type PersonalInfoNavigationProp = StackNavigationProp<TStackParamList, 'PersonalInfo'>;

const { colors } = theme;

const PersonalInfo: React.FC<PersonalInfoPropsType> = () => {
  const navigation = useNavigation<PersonalInfoNavigationProp>();
  const netInfo = NetInfo.useNetInfo();
  const [passportMode, setPassportMode] = useState(false);
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const modalConfirmErrorRef = useRef<null | ModalConfirmRef>(null);
  const modalForgotSessionExpiredRef = useRef<ModalForgotSessionRef>(null);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const loading = useSelector(makeSelectLoading());
  const phoneNumber = useSelector(makeSelectPhoneNumber());
  const isFocused = useIsFocused();

  const {
    control,
    handleSubmit,
    formState: { errors, isDirty },
    reset,
    setError,
    clearErrors,
  } = useForm({
    defaultValues: {
      identificationNumber: '',
    },
  });

  const handleSubmitIdNo = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PERSONAL_INFO,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });
    const idDocType = passportMode ? ID_NO_TYPE_FORGOT_PASS.PASSPORT : ID_NO_TYPE_FORGOT_PASS.CMND;
    Keyboard.dismiss();
    handleSubmit(data => {
      dispatch(submitVerifyPersonalIDNumber({ ...data, setError, idDocType }));
    })();
  };

  const onDismissLoading = () => {
    if (!isFocused) return;

    if (!_isEmpty(errors?.identificationNumber)) {
      clearErrors();
      if (errors?.identificationNumber?.message === FORGOT_PASS_ERROR_CODES.SESSION_INVALID) {
        modalForgotSessionExpiredRef?.current?.open();
      } else {
        modalConfirmErrorRef?.current?.open();
      }
    } else if (isDirty) {
      // reset();
      if (phoneNumber) {
        navigation.navigate(ROUTE_PATH.REQUEST_OTP, {allowCancel: false});
      } else {
        navigation.navigate(ROUTE_PATH.NO_PHONE);
      }
    }
  };

  const hideKeyboard = () => Keyboard.dismiss();

  const callHotline = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PERSONAL_INFO,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Call hotline',
    });
    Alert.alert(`${t('forgot.password.call.hotline.title')} 028-3812-2777`, '', [
      {
        text: t('forgot.password.call.hotline.cancel'),
      },
      {
        text: t('forgot.password.call.hotline.call'),
        onPress: () => {
          Linking.openURL('tel:02838122777');
        },
        style: 'cancel',
      },
    ]);
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <TextCommon.Bold style={HEADER_TITLE_BGRWHITE}>{t('forgot.password.modal.personal.info.title')}</TextCommon.Bold>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('Forgot.btnInfoHeaderGoback')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.PERSONAL_INFO,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack()
          }}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" size={24} color={colors.primary.charcoal[80]} style={styles.marginLeft21} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
      headerTitleStyle: styles.mainTitle,
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    reset();
    hideKeyboard();
  }, [passportMode]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <View style={{ flex: 1 }}>
      <SafeAreaView style={styles.SafeAreaStyle}>
        <Loading visible={loading} dismissAction={onDismissLoading} />
        <TouchableWithoutFeedback {...a11y('Forgot.btnInfoHideKeyboard')} onPress={hideKeyboard}>
          <View style={StyleSheet.flatten([styles.container])}>
            <KeyboardAvoidingView
              behavior={Platform.OS === 'ios' ? 'padding' : undefined}
              style={{ flex: 1 }}
              keyboardVerticalOffset={Platform.OS === 'ios' ? 90 : 0}
            >
              <View style={styles.modalBox}>
                <View>
                  <Text style={styles.formTitle} fontFamily="bold">
                    {t('forgot.password.modal.form.sub.title')}
                  </Text>
                  <Text style={styles.subFormTitle}>{t('forgot.password.modal.form.title')}</Text>
                  <View style={styles.groupButton}>
                    <TouchableOpacity
                      onPress={() => {
                        NativeModuleClient.AdobeExperience.trackButton({
                          screen: ROUTE_PATH.PERSONAL_INFO,
                          unixTimestamp: dayjs().unix(),
                          buttonText: 'National ID',
                        });
                        setPassportMode(false);
                      }}
                      style={styles.buttonWrapper}
                      {...a11y('Forgot.btnInfoIdMode')}
                    >
                      <View style={styles.wrapperCircle}>{!passportMode && <View style={styles.circle} />}</View>
                      <Text style={styles.buttonItemText}>{t('forgot.password.modal.personal.info.button.id')}</Text>
                    </TouchableOpacity>

                    <TouchableOpacity
                      onPress={() => {
                        NativeModuleClient.AdobeExperience.trackButton({
                          screen: ROUTE_PATH.PERSONAL_INFO,
                          unixTimestamp: dayjs().unix(),
                          buttonText: 'Passport',
                        });
                        setPassportMode(true);
                      }}
                      style={styles.buttonWrapper}
                      {...a11y('Forgot.btnInfoPasportMode')}
                    >
                      <View style={styles.wrapperCircle}>{passportMode && <View style={styles.circle} />}</View>
                      <Text style={styles.buttonItemText}>{t('forgot.password.modal.personal.info.button.passport')}</Text>
                    </TouchableOpacity>
                  </View>

                  <View style={styles.formContainer}>
                    <View>
                      <Controller
                        control={control}
                        rules={{
                          required: {
                            value: true,
                            message: passportMode
                              ? t('forgot.password.modal.personal.info.guideline.passport')
                              : t('forgot.password.modal.personal.info.guideline.id'),
                          },
                          pattern: {
                            value: passportMode ? REGEX.EXACT_8_DIGITS : REGEX.EXACT_9_DIGITS_OR_12_DIGITS,
                            message: passportMode
                              ? t('forgot.password.modal.personal.info.guideline.passport')
                              : t('forgot.password.modal.personal.info.guideline.id'),
                          },
                        }}
                        render={({ field: { onChange, value } }) => (
                          <TextInput
                            label={t('forgot.password.modal.personal.info.form.label')}
                            type={errors?.identificationNumber?.message !== FORGOT_PASS_ERROR_CODES.ID_NO_INVALID && errors?.identificationNumber ? 'error' : 'info'}
                            disabled={false}
                            onChangeText={onChange}
                            value={value}
                            message={errors?.identificationNumber?.message !== FORGOT_PASS_ERROR_CODES.ID_NO_INVALID ? errors?.identificationNumber && errors?.identificationNumber?.message : ''}
                            keyboardType={passportMode ? "default" : "numeric"}
                            iconColor="#000000"
                            id="Forgot.ipInfoIdentification"
                          />
                        )}
                        name="identificationNumber"
                      />
                    </View>
                  </View>
                </View>
                <View style={[styles.buttonBox, styles.marginBottom10]}>
                  <Button
                    text={t('forgot.password.modal.personal.info.button.continue')}
                    onPress={handleSubmitIdNo}
                    uppercase={false}
                    type="primary"
                    labelStyle={styles.buttonText}
                    size="large"
                    style={styles.button}
                    id="Forgot.btnInfoSubmit"
                  />
                </View>
              </View>
            </KeyboardAvoidingView>
          </View>
        </TouchableWithoutFeedback>
      </SafeAreaView>
      <ModalConfirm
        ref={modalConfirmErrorRef}
        mainTitle={t('forgot.password.modal.confirm.error.main.title')}
        subTitle={t('forgot.password.modal.confirm.error.sub.title')}
        topButtonText={t('forgot.password.modal.confirm.error.button.close')}
        bottomButtonText={t('forgot.password.modal.registration.button.try')}
        topButtonPress={() => {
          clearErrors();
          modalConfirmErrorRef?.current?.close();
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.PERSONAL_INFO,
            unixTimestamp: dayjs().unix(),
            buttonText: 'Try again',
          });
        }}
        bottomButtonPress={callHotline}
      />
      <ModalForgotSession ref={modalForgotSessionExpiredRef} handleClose={() => {
         NativeModuleClient.AdobeExperience.trackButton({
          screen: ROUTE_PATH.PERSONAL_INFO,
          unixTimestamp: dayjs().unix(),
          buttonText: 'Close modal session',
        });
        navigation.navigate(ROUTE_PATH.FORGOT_PASSWORD);}} 
      />
    </View>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default PersonalInfo;
