import React, {useLayoutEffect, useState, useRef, useEffect} from 'react';
import {TouchableOpacity, SafeAreaView, ScrollView} from 'react-native';
import {connect} from 'react-redux';
import {StackNavigationProp, StackScreenProps} from '@react-navigation/stack';
import {Icon, NativeModuleClient} from 'superapp-common-component';
import {createStructuredSelector} from 'reselect';
import {Dispatch} from 'redux';
import {useTranslation} from 'react-i18next';
import _every from 'lodash/every';
import {TConnectState} from '@/stores/types';
import ModalError, {ModalConfirmErrorRef} from '@/components/ModalConfirmError';
import {TStackParamList} from '@/navigators';
import {DOCUMENT_TYPE, Face, trans} from '@/pages/VerifyAccount/types';
import CustomText from '@/components/Text';
import styles from './styles';
import {TVerifyAccountState} from './stores/state';
import {
  getMasterDataAction,
  getResultOCRVerifiedAction,
  requestVerifyOCRAction,
  submitVerifyAccountAction,
} from '@/pages/VerifyAccount/stores/actions';
import {makeSelectInfoVerifyAccount, makeSelectLoading} from '@/pages/VerifyAccount/stores/selectors';
import ROUTE_PATH from '@/navigators/routePath';
import {useRoute} from '@react-navigation/native';
import {useInjectReducer, useInjectSaga} from '@/utils/injectors';
import {NS_VERIFY_ACCOUNT} from '@/pages/VerifyAccount/stores/constants';
import requestTypeVerifyReducer from '@/pages/VerifyAccount/stores/reducer';
import verifyAccountSaga from '@/pages/VerifyAccount/stores/saga';
import ModalConfirmSuccess, {ModalConfirmSuccessRef} from '@/components/ModalConfirmSuccess';
import FormNationalIDCard from '@/pages/VerifyAccount/components/FormNationalIDCard';
import FormPassport from '@/pages/VerifyAccount/components/FormPassport';
import _isEmpty from 'lodash/isEmpty';
import {IRequestOCR, MasterDataType, IRequestVerifyAccount, FormVerifyValues} from '@/pages/VerifyAccount/modals';
import {a11y} from '@/utils/utils';
import {formatDateServer} from '@/utils/time-utils';
import Loading from '@/components/Loading';
import dayjs from 'dayjs';

type VerifyAccountNavigationProp = StackNavigationProp<TStackParamList, 'VerifyAccount'>;
type PropsType = {
  navigation: VerifyAccountNavigationProp;
  requestOCR: (dto: IRequestOCR) => void;
  submitVerifyAccount: (dto: IRequestVerifyAccount) => void;
  responseVerifyAccount: MODEL.IVerifyAccount;
  loading: boolean;
  getMasterData: (payload: any) => void;
};
type VerifyAccountNativeStackProps = StackScreenProps<TStackParamList, 'VerifyAccount'>;
type VerifyAccountRouteProps = VerifyAccountNativeStackProps['route'];
const VerifyAccount: React.FC<PropsType | any> = props => {
  useInjectReducer({key: NS_VERIFY_ACCOUNT, reducer: requestTypeVerifyReducer});
  useInjectSaga({key: NS_VERIFY_ACCOUNT, saga: verifyAccountSaga});
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const [dataOCRFront, setDataOCRFront] = useState<any | null>(null);
  const [dataOCRBack, setDataOCRBack] = useState<any | null>(null);
  const [error, setError] = useState<any>({});
  const [errorOCRFront, setErrorOCRFront] = useState<boolean>(false);
  const [errorOCRBack, setErrorOCRBack] = useState<boolean>(false);
  const [isVerify, setIsVerify] = useState<boolean>(false);
  const [masterData, setMasterData] = useState<MasterDataType | undefined>(undefined);
  const [imgCMNDFront, setImgCMNDFront] = useState<any>();
  const [imgCMNDBack, setImgCMNDBack] = useState<any>();
  const {navigation, responseVerifyAccount} = props;
  const {t} = useTranslation();
  const route = useRoute<VerifyAccountRouteProps>();
  const {documentType} = route.params || {};
  const modalConfirmSuccessRef = useRef<null | ModalConfirmSuccessRef>(null);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <CustomText style={styles.header}>{t('verifyAccount.page.verifyAccount')}</CustomText>,
      headerLeft: () => (
        <TouchableOpacity
          style={styles.marginLeft16}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          {...a11y('VerifyAccount.iconGoBackHeaderLeft')}
        >
          <Icon name="Arrow-Left" size={24} color="gray" />
        </TouchableOpacity>
      ),
      headerRight: () => (
        <Icon name="Info1" color={'gray'} style={styles.marginRight16} onPress={() => {}} {...a11y('VerifyAccount.iconInfoHeaderRight')} />
      ),
    });
  }, [navigation, t]);

  useEffect(() => {
    const callback = (value: any) => {
      if (value) {
        setMasterData({
          provinces: value?.provinces?.data || [],
          ethnics: value?.ethnics?.data || [],
          nations: value?.nations?.data || [],
        });
      }
    };
    props.getMasterData({skipAgency: false, callback});
  }, []);

  const closeModal = () => {
    if (modalConfirmSuccessRef) {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: 'VerifyAccountSuccess',
        unixTimestamp: dayjs().unix(),
        buttonText: 'Close',
      });
      modalConfirmSuccessRef.current?.close();
      setIsVerify(false);
      navigation.push(ROUTE_PATH.DASHBOARD);
    }
  };

  const uploadOCR = (data: any) => {
    // Call API OCR success
    const callback = (value: any) => {
      if (value) {
        setError(null);
        setErrorOCRFront(false);
        if (value?.data) {
          switch (data?.face) {
            case Face.FRONT: {
              if (/back/.test(value?.data[0]?.type_new)) {
                const err = {
                  globalError: {
                    type: 'manual',
                    message: trans.invalidNationalId,
                  },
                };
                setError(err);
                setErrorOCRFront(true);
              } else if (!value?.data[0]?.id) {
                const err = {
                  globalError: {
                    type: 'manual',
                    message: trans.cantNotFindId,
                  },
                };
                setError(err);
                setErrorOCRFront(true);
              }
              // REMOVE check overall_score
              // else if (value?.data[0]?.overall_score < parseInt(value?.data[0]?.overall_score)) {
              //   const err = {
              //     globalError: {
              //       type: 'manual',
              //       message: trans.inValidImage,
              //     },
              //   };
              //   setError(err);
              //   setErrorOCRFront(true);
              // }
              else {
                setDataOCRFront({...value?.data});
                setImgCMNDFront({
                  name: data?.name,
                  imageType: data?.type,
                  imageLayout: 1,
                  base64: data?.image_base64,
                });
              }
              break;
            }
            case Face.BACK: {
              setErrorOCRBack(false);
              if (/front/.test(value?.data[0]?.type_new)) {
                const err = {
                  globalError: {
                    type: 'manual',
                    message: trans.invalidNationalId,
                  },
                };
                setError(err);
                setErrorOCRBack(true);
              } else {
                setDataOCRBack(value?.data);
                setImgCMNDBack({
                  name: data?.name,
                  imageType: data?.type,
                  imageLayout: 2,
                  base64: data?.image_base64,
                });
              }
              break;
            }
          }
        }
      }
    };

    // Call API OCR error
    const callbackErrorOCR = async () => {
      data.face === Face.FRONT ? setErrorOCRFront(true) : setErrorOCRBack(true);
    };

    props.requestOCR({
      ...data,
      callback,
      setError,
      callbackErrorOCR,
    });
  };

  const onDismissLoading = () => {
    if (!_isEmpty(error?.globalError)) {
      modalErrorRef?.current?.open();
    } else {
      if (_every([isVerify, responseVerifyAccount?.isPreVerify])) {
        navigation.navigate(ROUTE_PATH.VERIFY_ACCOUNT_QRCODE); //
      } else if (isVerify) {
        modalConfirmSuccessRef.current?.open();
      }
    }
  };

  const onHideModalErrorGlobal = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close Modal Error',
    });
  };

  const handleSubmitVerifyAccount = (dataForm: FormVerifyValues) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Submit',
    });
    setError(null);
    const dto = {
      idDocType: documentType,
      idDocInfo: {
        ...dataForm,
        dob: formatDateServer(dataForm?.dob.toString()),
        issueDate: formatDateServer(dataForm?.issueDate.toString()),
        expirationDate: formatDateServer(dataForm?.expirationDate.toString()),
        birthPlace: dataForm?.birthPlace?.code || dataForm?.birthPlace,
        ethnic: dataForm?.ethnic?.code || dataForm?.ethnic,
        nationality: dataForm?.nationality?.code || dataForm?.nationality,
        issuePlace: dataForm?.issuePlace?.code || dataForm?.issuePlace,
        otherNationality: dataForm?.otherNationality?.code || dataForm?.otherNationality,
        gender: dataForm?.gender.value ? dataForm.gender.value : dataForm?.gender,
        type: dataForm?.type.value ? dataForm.type.value : dataForm?.type,
      },
      idDocImages: [imgCMNDFront, imgCMNDBack],
    };
    props.submitVerifyAccount({...dto, setError, setIsVerify});
  };

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView style={styles.containerScreen}>
        {documentType && documentType === DOCUMENT_TYPE.NATIONAL_ID_CARD ? (
          <FormNationalIDCard
            dataOCRFront={dataOCRFront}
            dataOCRBack={dataOCRBack}
            errorOCRFront={errorOCRFront}
            errorOCRBack={errorOCRBack}
            onSubmitOCR={(data: any) => uploadOCR(data)}
            onSubmitVerifyAccount={(dataForm: any) => handleSubmitVerifyAccount(dataForm)}
            masterData={masterData}
          />
        ) : (
          <FormPassport />
        )}
        <ModalConfirmSuccess
          ref={modalConfirmSuccessRef}
          onConfirm={closeModal}
          title={t('common.successfully')}
          subtitle={t('verifyAccount.message.success')}
          btnText={t('common.continue')}
        />

        <ModalError
          ref={modalErrorRef}
          title={t('verifyAccount.modal.error.title')}
          subtitle={t(error?.globalError?.message)}
          btnText={t('verifyAccount.modal.closeBtn')}
          onConfirm={onHideModalErrorGlobal}
        />
        <Loading visible={props.loading} onDismiss={onDismissLoading} />
      </ScrollView>
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TVerifyAccountState['loading'];
  responseVerifyAccount: TVerifyAccountState['responseVerifyAccount'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  responseVerifyAccount: makeSelectInfoVerifyAccount(),
});

type MapDispatchToPropsType = {
  getResultOCRVerified: (payload: any) => any;
  requestOCR: (payload: any) => any;
  submitVerifyAccount: (payload: any) => any;
  getMasterData: (payload: any) => any;
};
const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  requestOCR: payload => dispatch(requestVerifyOCRAction(payload)),
  getResultOCRVerified: payload => dispatch(getResultOCRVerifiedAction(payload)),
  getMasterData: payload => dispatch(getMasterDataAction(payload)),
  submitVerifyAccount: payload => dispatch(submitVerifyAccountAction(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(VerifyAccount);
