import React, { useRef, useState } from 'react';
import { Image, SafeAreaView, View } from 'react-native';
import { Button, Checkbox } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import personImage from '@/assets/images/etransaction-confirmation.png';
import Text from '@/components/Text';
import WebView from '@/components/WebView';

import styles from './styles';

interface ETransactionConfirmationProps {}

const ETransactionConfirmation = (props: ETransactionConfirmationProps) => {
  const { t } = useTranslation();
  const [isAgree, setIsAgree] = useState(false);
  const WebviewRef = useRef(null);

  const showWebViewConfirmation = () => {};

  return (
    <SafeAreaView style={styles.flex1}>
      <View style={styles.successModalContainer}>
        <View style={styles.topContent}>
          <View style={styles.warpperImage}>
            <Image style={styles.imageStyle} resizeMode="contain" source={personImage} />
          </View>
          <Text style={styles.title} fontFamily="bold">
            {t('login.etransaction.title')}
          </Text>
          <Text style={styles.subTitle}>{t('login.etransaction.sub.title')}</Text>
        </View>

        <View style={styles.wrapperButton}>
          <Checkbox
            checked={isAgree}
            onPress={() => {
              setIsAgree(!isAgree);
            }}
            size={24}
            customStyleChild={styles.customCheckbox}
            id="ETransactionConfirmation.checkboxIsAgree"
          >
            <Text style={styles.termText}>
              {t('login.etransaction.agreement1')}
              <Text style={styles.linkText} onPress={showWebViewConfirmation}>
                {' '}
                {t('login.etransaction.agreement2')}{' '}
              </Text>
              {t('login.etransaction.agreement3')}
            </Text>
          </Checkbox>
          <Button
            text={t('login.etransaction.button')}
            onPress={() => {}}
            style={styles.buttonStyle}
            labelStyle={styles.labelStyleButton}
            disabled={!isAgree}
          />
        </View>
        <WebView ref={WebviewRef} />
      </View>
    </SafeAreaView>
  );
};

export default ETransactionConfirmation;
