import { takeLatest, call, put } from 'redux-saga/effects';
import { TAction } from '@/stores/types';
import ChangePasswordService from '@/services/auth';
import APP_ENV from '@/config/app.config';

import { STORAGE_NAME } from '@/utils/constants';
import { getItem } from '@/utils/storage';

import {
  SUBMIT_CHANGE_PASSWORD,
  SUBMIT_CHANGE_PASSWORD_PRE_LOGIN,
} from './constants';
import {
  loadingRequestAction,
  loadingSuccessAction,
} from './actions';

const { CLIENT_ID } = APP_ENV;

function* handleSubmitChangePassword({payload,resolve, reject}: TAction) {
  try {
    yield put(loadingRequestAction());
    const username: string = yield getItem(STORAGE_NAME.USERNAME);
    const { currentPassword, newPassword } = payload;
    yield call(ChangePasswordService.changePasswordService, { currentUsername: username, clientId: CLIENT_ID, currentPassword, newPassword });
    resolve({ username: username, password: newPassword });
  } catch (error: any) {
    reject(error?.errorCode);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleSubmitChangePasswordPreLogin({payload,resolve, reject}: TAction) {
  try {
    yield put(loadingRequestAction());
    const username: string = yield getItem(STORAGE_NAME.USERNAME);
    const { currentPassword, newPassword } = payload;
    yield call(ChangePasswordService.changePasswordPreLoginService, { username: username, clientId: CLIENT_ID, currentPassword, newPassword });
    resolve({ username: username, password: newPassword });
  } catch (error: any) {
    reject(error?.errorCode);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* globalSaga() {
  yield takeLatest(SUBMIT_CHANGE_PASSWORD, handleSubmitChangePassword);
  yield takeLatest(SUBMIT_CHANGE_PASSWORD_PRE_LOGIN, handleSubmitChangePasswordPreLogin);
}

export default globalSaga;
