import React, { useLayoutEffect } from 'react';
import { Image, SafeAreaView, TouchableOpacity, View } from 'react-native';
import { useTranslation } from 'react-i18next';
import { RouteProp, useNavigation, useRoute } from '@react-navigation/native';
import {
  Button,
  Icon,
  NativeModuleClient,
  Text,
} from 'superapp-common-component';
import { colors } from 'superapp-common-component';
import { utils } from 'superapp-common-component';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import dayjs from 'dayjs';

import { HITSLOP } from '@/utils/styles';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';
const { a11y } = utils;
type ForgotUsernameConfirmNavigationProp = StackNavigationProp<
  TStackParamList,
  'ForgotUsernameConfirm'
>;
type ForgotUsernameConfirmRouteProps = RouteProp<
  TStackParamList,
  'ForgotUsernameConfirm'
>;

const ForgotUsernameConfirm = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<ForgotUsernameConfirmNavigationProp>();
  const route = useRoute<ForgotUsernameConfirmRouteProps>();
  const { phoneNo = '' } = route.params || {};

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text.Bold style={styles.headerTitle}>
          {t('forgot.username.header.title')}
        </Text.Bold>
      ),
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('ForgotUsername.btnForgotGoback')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_USERNAME_CONFIRM,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}>
          <Icon
            name="Arrow-Left"
            size={24}
            color={colors.primary.charcoal[80]}
            style={styles.marginLeft21}
          />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.container}>
      <View style={styles.content}>
        <View style={styles.wrapperContent}>
          <Image
            style={styles.image}
            resizeMode="contain"
            source={require('@/assets/images/logo-change-username.png')}
          />
          <Text.Bold style={styles.textTitle}>
            {t('forgot.username.confirm.title', { phone: phoneNo })}
          </Text.Bold>
          <Text style={styles.textDescription}>
            {t('forgot.username.confirm.description')}
          </Text>
        </View>
        <Button
          text={t('forgot.username.buton.confirm')}
          uppercase={false}
          type="primary"
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_USERNAME_CONFIRM,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Confirm',
            });
            navigation.navigate(ROUTE_PATH.FORGOT_USERNAME_OTP, {
              phoneNo,
              allowCancel: false,
            });
          }}
          labelStyle={styles.buttonText}
          size="large"
          style={styles.button}
          id="ForgotUsername.btnForgotVerify"
        />
      </View>
    </SafeAreaView>
  );
};

export default ForgotUsernameConfirm;
