import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_CLAIM_ELETTER_DOCUMENT_REQUEST,
  LOADING_CLAIM_ELETTER_DOCUMENT_SUCCESS,
  LOADING_CLAIM_ELETTER_REQUEST,
  LOADING_CLAIM_ELETTER_SUCCESS,
  LOADING_ELETTER_REQUEST,
  LOADING_ELETTER_SUCCESS,
  LOADING_OTHER_ELETTER_DOCUMENT_REQUEST,
  LOADING_OTHER_ELETTER_DOCUMENT_SUCCESS,
  LOADING_OTHER_ELETTER_REQUEST,
  LOADING_OTHER_ELETTER_SUCCESS,
  LOADING_REQUEST_ELETTER_DOCUMENT_REQUEST,
  LOADING_REQUEST_ELETTER_DOCUMENT_SUCCESS,
  PUT_CLAIM_ELETTER_DOCUMENT_ERROR,
  PUT_OTHER_ELETTER_DOCUMENT_ERROR,
  PUT_REQUEST_ELETTER_DOCUMENT_ERROR,
  SAVE_CLAIM_ELETTER_LIST,
  SAVE_ELETTER_LIST,
  SAVE_OTHER_ELETTER_LIST,
  SET_LOAD_MORE_CLAIM_ELETTER,
  SET_LOAD_MORE_OTHER_ELETTER,
  SET_LOAD_MORE_REQUEST_ELETTER,
} from './constants';
import initialState, { TELetterTypeState } from './state';

const eLetterReducer = (state: TELetterTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TELetterTypeState>) => {
    switch (action.type) {
      case LOADING_ELETTER_REQUEST: {
        draft.requestELetter.requestELettetLoading = true;
        break;
      }
      case LOADING_ELETTER_SUCCESS: {
        draft.requestELetter.requestELettetLoading = false;
        break;
      }
      case SAVE_ELETTER_LIST: {
        draft.requestELetter.requestELettetList = action.payload;
        break;
      }
      case SET_LOAD_MORE_REQUEST_ELETTER: {
        draft.requestELetter.isLoadMore = action.payload;
        break;
      }
      case LOADING_REQUEST_ELETTER_DOCUMENT_REQUEST:
        draft.requestELetterDocument.isLoadingDocument = true;
        break;
      case LOADING_REQUEST_ELETTER_DOCUMENT_SUCCESS:
        draft.requestELetterDocument.isLoadingDocument = false;
        break;
      case LOADING_CLAIM_ELETTER_DOCUMENT_REQUEST:
        draft.claimELetterDocument.isLoadingDocument = true;
        break;
      case LOADING_CLAIM_ELETTER_DOCUMENT_SUCCESS:
        draft.claimELetterDocument.isLoadingDocument = false;
        break;
      case PUT_CLAIM_ELETTER_DOCUMENT_ERROR:
        draft.claimELetterDocument.isError = action.payload;
        break;
      case PUT_REQUEST_ELETTER_DOCUMENT_ERROR:
        draft.requestELetterDocument.isError = action.payload;
        break;

      case LOADING_CLAIM_ELETTER_REQUEST:
        draft.claimELetter.claimELettetLoading = true;
        break;
      case LOADING_CLAIM_ELETTER_SUCCESS:
        draft.claimELetter.claimELettetLoading = false;
        break;
      case SAVE_CLAIM_ELETTER_LIST:
        draft.claimELetter.claimELettetList = action.payload;
        break;
      case SET_LOAD_MORE_CLAIM_ELETTER:
        draft.claimELetter.isClaimLoadMore = action.payload;
        break;
      case LOADING_OTHER_ELETTER_REQUEST:
        draft.otherELetter.otherELetterLoading = true;
        break;
      case LOADING_OTHER_ELETTER_SUCCESS:
        draft.otherELetter.otherELetterLoading = false;
        break;
      case SAVE_OTHER_ELETTER_LIST:
        draft.otherELetter.otherELetterList = action.payload;
        break;
      case SET_LOAD_MORE_OTHER_ELETTER:
        draft.otherELetter.isOtherLoadMore = action.payload;
        break;
      case PUT_OTHER_ELETTER_DOCUMENT_ERROR:
        draft.otherELetterDocument.isError = action.payload;
        break;
      case LOADING_OTHER_ELETTER_DOCUMENT_REQUEST:
        draft.otherELetterDocument.isLoadingDocument = true;
        break;
      case LOADING_OTHER_ELETTER_DOCUMENT_SUCCESS:
        draft.otherELetterDocument.isLoadingDocument = false;
        break;
    }
  });
};

export default eLetterReducer;
