import React, { useLayoutEffect, useRef, useState } from 'react';
import { NativeModules, SafeAreaView, TouchableOpacity, View } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { Icon, NativeModuleClient, useTheme } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import { useInjectReducer, useInjectSaga } from 'redux-injectors';
import dayjs from 'dayjs';

import ROUTE_PATH from '@/navigators/routePath';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { a11y } from '@/utils/utils';
import { Theme } from '@/config/themes.config';
import Text from '@/components/Text';

import ClaimHistory from './components/ClaimHistory';
import PaymentHistory from './components/PaymentHistory';
import RequestHistory from './components/RequestHistory';
import { NS_TRANSACTION_HISTORY_TYPE } from './stores/constants';
import transactionHistoryReducer from './stores/reducer';
import transactionHistorySaga from './stores/saga';
import useStyles from './styles';
import FilterModal, { FilterModalRef } from './components/FilterModal';
import { useSelector } from 'react-redux';
import { makeSelectTransactionFilter } from './stores/selectors';
import { makeSelectPolicies } from '@/stores/global/selectors';

type TransactionHistoryNavigationProp = StackNavigationProp<TStackParamList, 'TransactionHistory'>;
const Tab = createMaterialTopTabNavigator();
const TransactionHistory = () => {
  useInjectReducer({ key: NS_TRANSACTION_HISTORY_TYPE, reducer: transactionHistoryReducer });
  useInjectSaga({ key: NS_TRANSACTION_HISTORY_TYPE, saga: transactionHistorySaga });
  const navigation = useNavigation<TransactionHistoryNavigationProp>();
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [tabAnimating, setTabAnimating] = useState(false);
  const policies = useSelector(makeSelectPolicies());
  const filterModalRef = useRef<null | FilterModalRef>(null);
  const transactionFilter = useSelector(makeSelectTransactionFilter());

  const handleBackNavigation = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.TRANSACTION_HISTORY,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Go back',
    });
    const { MiniAppModule } = NativeModules;
    navigation.canGoBack() ? navigation.goBack() : MiniAppModule.goBack();
  }

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('home.transaction.history.header')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('TransactionHistory.btnHeaderGoback')}
          style={styles.marginLeft16}
          onPress={handleBackNavigation}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" color={theme.colors.white} size={24} />
        </TouchableOpacity>
      ),
      headerRight: () => (
        policies && policies?.length > 0 ? <TouchableOpacity
          {...a11y('TransactionHistory.btnFilter')}
          style={styles.marginRight16}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.TRANSACTION_HISTORY,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Filter',
            });
            filterModalRef.current?.setVisibleModal(true);
          }}
          hitSlop={HITSLOP}
        >
          <Icon name="Filter" color={theme.colors.white} size={24} />
          { transactionFilter && transactionFilter?.length > 0 && <View style={styles.markBagde}>
            <View style={styles.bagde} />
          </View> }
        </TouchableOpacity> : null
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t, transactionFilter]);

  return (
    <SafeAreaView style={styles.flex1}>
      <Tab.Navigator
        initialRouteName="Payment"
        screenOptions={{
          tabBarStyle: styles.tabBarStyle,
          tabBarContentContainerStyle: styles.tabBarContentContainerStyle,
          tabBarIndicatorStyle: styles.tabBarIndicatorStyle,
        }}
        screenListeners={({ navigation: navigationTab, route }) => ({
          tabPress: e => {
            // This adds a debounce to fix "switching tabs rapidly" bug
            if (tabAnimating) {
              e.preventDefault();
            } else {
              setTabAnimating(true);
              setTimeout(() => setTabAnimating(false), 300);

              // This solves the "tap same tab twice breaks tabs" bug, but not the "switching rapidly" bug
              e.preventDefault();
              navigationTab.navigate(route.name);
            }
          },
        })}
      >
        <Tab.Screen
          name="Payment"
          component={PaymentHistory}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon name={focused ? 'Application-On' : 'Application'} size={24} color={focused ? '#1F78AD' : '#5B636B'} />
                <Text style={styles.tabBarLabelText}>{t('home.transaction.history.payment')}</Text>
              </View>
            ),
          }}
        />
        <Tab.Screen
          name="Request"
          component={RequestHistory}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon name={focused ? 'Report-On' : 'Report'} size={24} color={focused ? '#1F78AD' : '#5B636B'} />
                <Text style={styles.tabBarLabelText}>{t('home.transaction.history.request')}</Text>
              </View>
            ),
          }}
        />
        <Tab.Screen
          name="Claim"
          component={ClaimHistory}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon name={focused ? 'Protect' : 'Protect1'} size={24} color={focused ? '#1F78AD' : '#5B636B'} />
                <Text style={styles.tabBarLabelText}>{t('home.transaction.history.claim')}</Text>
              </View>
            ),
          }}
        />
      </Tab.Navigator>
      <FilterModal 
        ref={filterModalRef}
      />
    </SafeAreaView>
  );
};

export default TransactionHistory;
