import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class oneDocService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ONE_DOC);
  }

  public getFilePdfUrlByLetterIdService = (letterId: string) : Promise<MODEL.IDocumentPdf> => this.instance.get(`/document/downloadPdf?id=${letterId}`);
}

export default new oneDocService();
