import { createSelector } from 'reselect';

import initialState from './state';
import { NS_ECARD_TYPE } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_ECARD_TYPE] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectEcards = () => createSelector(currentState, state => state.ecards);

export {
  makeSelectEcards,
  makeSelectLoading
};
