import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, SUBMIT_VERIFY_ACCOUNT_QRCODE } from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });

export const submitVerifyAccountQRCode = (payload: API.IPreVerifyAccount & { callback: () => void, callbackError:() => void }): TAction => ({
  type: SUBMIT_VERIFY_ACCOUNT_QRCODE,
  payload,
});
