import { createSelector } from 'reselect';

import initialState from './state';
import { NS_ONBOARDING } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_ONBOARDING] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectContents = () => createSelector(currentState, state => state.contents);

export { currentState, makeSelectLoading, makeSelectContents };
