import React, { useMemo } from 'react';
import { ScrollView, TouchableOpacity, View } from 'react-native';
import _isEmpty from 'lodash/isEmpty';
import { Icon, Skeleton, theme } from 'superapp-common-component';
import Text from '@/components/Text';
import HealthReportCard from '@/components/HealthReportCard';
import SVGIcon from '@/components/SVGIcon';

import styles, { SNAP_TO_INTERVAL } from './styles';
import { HealthReportPropsType } from './types';

const { colors } = theme;

const HealthReport: React.FC<HealthReportPropsType> = ({
  onClickItem,
  loading,
  onClickRightButtonText,
  data = [],
  leftTitle,
  rightButtonTitle,
  emptyTitle,
  emptyStateIconName = 'Lock',
}) => {
  const listHealthReportRendered = useMemo(() => {
    const listItems = data
      .filter(item => !_isEmpty(item))
      .map(report => {
        return (
          <View style={styles.cardWrapper} key={report.title}>
            <HealthReportCard data={report} onClickItem={onClickItem} />
          </View>
        );
      });

    return (
      <ScrollView
        showsHorizontalScrollIndicator={false}
        horizontal={true}
        decelerationRate={0}
        snapToInterval={SNAP_TO_INTERVAL}
        contentContainerStyle={styles.scrollViewContentContainer}
        disableIntervalMomentum={true}
      >
        {listItems}
      </ScrollView>
    );
  }, [data, onClickItem]);

  return (
    <View style={styles.container}>
      <View style={styles.headTitleContainer}>
        <Text style={styles.headTitle} xl={{fontWeight: '600', fontSize: 16, lineHeight: 24}}>{leftTitle}</Text>
        {!_isEmpty(data) && (
          <TouchableOpacity onPress={onClickRightButtonText} style={styles.rightButtonWrapper}>
            <Text style={styles.rightButtonText}>{rightButtonTitle}</Text>
            <Icon name="Arrow-Right" color={colors.status.actionable} size={20} />
          </TouchableOpacity>
        )}
      </View>
      {loading && <Skeleton style={styles.skeleton} />}
      {!loading && (
        <View>
          {_isEmpty(data) ? (
            <View style={styles.emptyStateContainer}>
              <SVGIcon group="healthReports" name={emptyStateIconName} width={62} height={62} fillColor={colors.primary.blue[100]} />
              <Text style={styles.emptyStateTitle} xl={{ fontSize: 14}}>{emptyTitle}</Text>
            </View>
          ) : (
            <View style={styles.containerFluid}>{listHealthReportRendered}</View>
          )}
        </View>
      )}
    </View>
  );
};

HealthReport.defaultProps = {
  onClickItem: () => {},
  onClickRightButtonText: () => {},
  data: [],
};

export default HealthReport;
