import React, { useCallback, useRef } from 'react';
import { View, TouchableOpacity, ScrollView, Image } from 'react-native';
import { NativeModuleClient, useTheme } from 'superapp-common-component';
import { useSelector } from 'react-redux';
import _find from 'lodash/find';
import _get from 'lodash/get';
import { useTranslation } from 'react-i18next';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import { Theme } from '@/config/themes.config';
import { a11y, isEmpty } from '@/utils/utils';
import APP_ENV from '@/config/app.config';
import ROUTE_PATH from '@/navigators/routePath';
import { makeSelectLoadingSkeleton, makeSelectProductsList } from '@/stores/global/selectors';
import WebView, { TWebViewUrlRef } from '@/components/WebView';

import { Language } from '../types';
import styles, { CARD_WIDTH, PRODUCT_ITEM_MARGIN_RIGHT } from './styles';

interface ProductsProps {
  currentLang: Language;
  isConnected: boolean;
}

const Products = (props: ProductsProps) => {
  const { currentLang, isConnected } = props;
  const { colors } = useTheme() as Theme;
  const productsList = useSelector(makeSelectProductsList());
  const isLoading = useSelector(makeSelectLoadingSkeleton());
  const webviewRef = useRef<TWebViewUrlRef | null>(null);
  const { t } = useTranslation();

  const formatProductData = (items: [{ name: string; value: any }]) => {
    const name = _find(items, (element: any) => element?.name === 'productName');
    const price = _find(items, (element: any) => element?.name === 'price');
    const imageUrl = _find(items, (element: any) => element?.name === 'appImage');
    const tags = _find(items, (element: any) => element?.name === 'featureProductTags');
    const id = _find(items, (element: any) => element?.name === 'id');
    const link = _find(items, (element: any) => element?.name === 'link');

    return {
      name,
      price,
      imageUrl,
      tags,
      id,
      link,
    };
  };

  const formatPrice = (price: number) => {
    const listPriceSplit = [];
    let index = price.toString().length;
    do {
      const sliceFrom = index - 3 >= 0 ? index - 3 : 0;
      const slicedItem = price.toString().slice(sliceFrom, index);
      listPriceSplit.unshift(slicedItem);
      index = index - 3;
    } while (index > 0);
    return listPriceSplit.join('.');
  };

  const onOpenLink = (url?: string) => {
    if (!isEmpty(url)) {
      // Linking.openURL(url!);
      webviewRef.current?.openURL(url!);
    }
  };

  const onPressViewAll = useCallback(() => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.HOME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'See all products',
    });
    const linkViewAll = _get(productsList, ['items', currentLang, 'showAllCta', 'link']);
    if (currentLang && !isEmpty(linkViewAll)) {
      onOpenLink(linkViewAll);
    }
  }, [currentLang, productsList]);

  const renderProductsList = () => {
    if (productsList?.items[currentLang]) {
      return productsList.items[currentLang].items
        .slice(0, 5)
        .map((item: { title: string; elements: [{ name: string; value: any }] }, key) => {
          const { elements } = item;
          const { name, imageUrl, tags, id, link } = formatProductData(elements);
          return (
            <View key={id?.value + key} style={styles.productItem}>
              <TouchableOpacity
                onPress={() => {
                  NativeModuleClient.AdobeExperience.trackButton({
                    screen: ROUTE_PATH.HOME,
                    unixTimestamp: dayjs().unix(),
                    buttonText: name?.value,
                  });
                  onOpenLink(link?.value);
                }}
              >
                <View style={styles.flex1}>
                  <View style={styles.mb8}>
                    <Image source={{ uri: `${APP_ENV.CONTENT_AEM}${imageUrl?.value}` }} style={styles.imageProduct} />
                  </View>
                  <View style={styles.headerProductItem}>
                    <View>
                      <Text
                        style={styles.textProductItem}
                        xl={{ fontSize: 14, fontWeight: '600', lineHeight: 20 }}
                        ellipsizeMode="tail"
                        numberOfLines={2}
                      >
                        {name?.value}
                      </Text>
                    </View>
                    <View>
                      <View style={styles.benefitProduct}>
                        {tags?.value.map(
                          (tag: any) =>
                            !isEmpty(tag.label) && (
                              <View key={tag.label} style={styles.wrapTextBenefitProduct}>
                                <Text style={styles.textBenefitProduct} xl={{ fontSize: 12, lineHeight: 16 }}>
                                  {tag.label}
                                </Text>
                              </View>
                            ),
                        )}
                      </View>
                    </View>
                  </View>
                </View>
                {/* <View style={styles.bottomProduct}>
                  <Text style={styles.textBottomProduct} fontFamily="bold">
                    {formatPrice(price?.value)} đ/năm
                  </Text>
                </View> */}
              </TouchableOpacity>
            </View>
          );
        });
    }
  };

  const renderSkeletionProduct = () => {
    return (
      <SkeletonPlaceholder>
        <View style={styles.headerComponent}>
          <View style={styles.productItemSkeleton}>
            <View style={styles.headerSkeleton} />
            <View>
              <View style={styles.mb40}>
                <View style={styles.imageProductSkeleton} />
                <View style={styles.contentProductSkeleton} />
              </View>
              <View>
                <View style={styles.bottomProductSkeleton} />
              </View>
            </View>
          </View>

          <View style={styles.productItemSkeleton}>
            <View style={styles.headerSkeleton} />
            <View>
              <View style={styles.mb40}>
                <View style={styles.imageProductSkeleton} />
                <View style={styles.contentProductSkeleton} />
              </View>
              <View>
                <View style={styles.bottomProductSkeleton} />
              </View>
            </View>
          </View>
        </View>
      </SkeletonPlaceholder>
    );
  };

  // 1. Loading skeleton
  // 2. Not connected && has data
  // 3. Connected && has data
  if (!isConnected && isEmpty(productsList)) return null;

  return isLoading && isEmpty(productsList?.items[currentLang]) ? (
    <View style={styles.marginHorizontal16}>{renderSkeletionProduct()}</View>
  ) : !isEmpty(productsList?.items[currentLang]) ? (
    <View style={styles.flex1}>
      <View style={[styles.mb16, styles.headerComponent]}>
        <Text style={styles.itemTitle} fontFamily="bold">
          {t('home.products.title')}
        </Text>
        <TouchableOpacity style={styles.itemHeaderRight} {...a11y('Home.iconSeeAllProducts')} onPress={onPressViewAll}>
          <Text fontFamily="bold" style={styles.textItemHeaderRight}>
            {t('home.seeAll')}
          </Text>
          {/* <Icon name="Arrow-Right" color={colors.primary} size={20} /> */}
        </TouchableOpacity>
      </View>
      <View>
        <ScrollView
          contentContainerStyle={styles.scrollStyle}
          disableIntervalMomentum={true}
          decelerationRate={0}
          snapToInterval={CARD_WIDTH * 2 + 2 * PRODUCT_ITEM_MARGIN_RIGHT}
          showsHorizontalScrollIndicator={false}
          horizontal
          {...a11y('Home.sliderProducts')}
        >
          {renderProductsList()}
        </ScrollView>
        <WebView ref={webviewRef} />
      </View>
    </View>
  ) : null;
};

export default Products;
