import { TAction } from '@/stores/types';

import {
  LOADING_ECARD_REQUEST,
  LOADING_ECARD_SUCCESS,
  GET_ECARDS,
  SAVE_ECARDS
} from './constants';

export const loadingEcardRequest = (): TAction => ({ type: LOADING_ECARD_REQUEST });
export const loadingEcardSuccess = (): TAction => ({ type: LOADING_ECARD_SUCCESS });
export const getEcardsAction = (): TAction => ({ type: GET_ECARDS });
export const saveEcardsAction = (payload: MODEL.IEcard[]): TAction => ({ type: SAVE_ECARDS, payload });
