import React, { forwardRef, useImperativeHandle } from 'react';
import { Alert, Image, Linking, TouchableOpacity, View } from 'react-native';
import { Button, NativeModuleClient, Text } from 'superapp-common-component';
import dayjs from 'dayjs';
import ErrorIcon from '@/assets/images/icon-warning.png';
import warningIcon from '@/assets/images/warning.png';
import { useTranslation } from 'react-i18next';

import ROUTE_PATH from '@/navigators/routePath';
import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';

import styles from './styles';
import { HITSLOP } from '@/utils/styles';

type ModalErrorPropsType = {};

export interface ModalErrorRef {
  open: () => void;
  close: () => void;
}

const Icons = {
  error: ErrorIcon,
  warning: warningIcon,
};

const ModalError = (props: ModalErrorPropsType, ref: React.Ref<unknown> | undefined) => {
  const { t } = useTranslation();
  const [modalsRef, modalInstance] = useModal();

  const onPressClose = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_USERNAME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close',
    });
    modalInstance()?.close();
  };

  const openModal = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_USERNAME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'ForgotUsername',
    });
    modalInstance().show();
  };

  const callHotline = () => {
    Alert.alert(`${t('forgot.password.call.hotline.title')} 028-3812-2777`, '', [
      {
        text: t('forgot.password.call.hotline.cancel'),
      },
      {
        text: t('forgot.password.call.hotline.call'),
        onPress: () => {
          Linking.openURL('tel:02838122777');
        },
        style: 'cancel',
      },
    ]);
  };

  useImperativeHandle(
    ref,
    (): ModalErrorRef => ({
      open: () => openModal(),
      close: () => modalInstance().close(),
    }),
    [],
  );

  return (
    <Modal ref={modalsRef} modalStyle={styles.modalStyles} overlayOpacity={0.5} onDismiss={() => null}>
      <View style={styles.modalContainer}>
        <View style={styles.wrapperModal}>
          <Image source={Icons.error} style={styles.imageWarning} />
          <Text style={styles.modalTitle}>{t('forgot.username.modal.error.title')}</Text>
          <Text style={styles.modalDescription}>{t('forgot.username.modal.error.description')}</Text>
          <View style={styles.wrapperBtn}>
            <Button
              id="Button.Confirm.No.Permission"
              style={styles.btnConfirm}
              text={t('common.tryAgain')}
              type="secondary"
              onPress={onPressClose}
            />
            <TouchableOpacity style={styles.btnCallCenter} hitSlop={HITSLOP} onPress={callHotline}>
              <Text style={styles.callCenterText}>{t('common.btn.callCenter')}</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalError);
