import { TAction } from '@/stores/types';

import {
  GET_ALL_SERVICE_REQUEST,
  GET_CLAIM_TRANSACTION,
  GET_LA_LIST,
  LOADING_CLAIM_TRANSACTION_REQUEST,
  LOADING_CLAIM_TRANSACTION_SUCCESS,
  SAVE_SERVICE_REQUEST,
  LOAD_MORE_CLAIM_TRANSACTION,
  SAVE_CLAIM_TRANSACTION,
  SAVE_LA_LIST,
  SET_LOAD_MORE_CLAIM,
  LOADING_SERVICE_REQUEST,
  DOWNLOAD_PDF_SERVICE_REQUEST,
  LOADING_PAYMENT_TRANSACTION,
  GET_PAYMENT_TRANSACTION,
  SAVE_PAYMENT_TRANSACTION,
  LOADING_DOWNLOAD_SERVICE_REQUEST,
  SET_LOAD_MORE_SERVICE_REQUEST,
  LOAD_MORE_SERVICE_REQUEST,
  LOADING_LOAD_MORE_SERVICE_REQUEST,
  SAVE_TRANSACTION_FILTER,
  TRIGGER_FILTER,
} from './constants';

export type ClaimTransactionPayload = {
  paging: { pageNo: number; pageSize: number };
  status: string;
  haveDraft: boolean;
  sortType: string;
};
// Claim transaction
export const loadingClaimTransactionRequest = (): TAction => ({ type: LOADING_CLAIM_TRANSACTION_REQUEST });
export const loadingClaimTransactionSuccess = (): TAction => ({ type: LOADING_CLAIM_TRANSACTION_SUCCESS });
export const getLaList = (): TAction => ({ type: GET_LA_LIST });
export const saveLaList = (payload: MODEL.IListLa[]): TAction => ({ type: SAVE_LA_LIST, payload });
export const getClaimTransaction = (payload?: ClaimTransactionPayload): TAction => ({ type: GET_CLAIM_TRANSACTION, payload });
export const saveClaimTransaction = (payload: MODEL.IClaimTransaction[]): TAction => ({ type: SAVE_CLAIM_TRANSACTION, payload });
export const loadMoreClaimTransaction = (payload?: ClaimTransactionPayload): TAction => ({ type: LOAD_MORE_CLAIM_TRANSACTION, payload });
export const setLoadMoreClaim = (payload: boolean): TAction => ({ type: SET_LOAD_MORE_CLAIM, payload });
// ICare transaction
export const getAllServiceRequest = (): TAction => ({ type: GET_ALL_SERVICE_REQUEST });
export const loadMoreServiceRequest = (): TAction => ({ type: LOAD_MORE_SERVICE_REQUEST });
export const saveServiceRequest = (payload: { paging: MODEL.IPaging; serviceRequests: MODEL.IServiceRequest[] }): TAction => ({
  type: SAVE_SERVICE_REQUEST,
  payload,
});
export const loadingServiceRequest = (payload: boolean): TAction => ({ type: LOADING_SERVICE_REQUEST, payload });
export const loadingLoadMoreServiceRequest = (payload: boolean): TAction => ({ type: LOADING_LOAD_MORE_SERVICE_REQUEST, payload });
export const downLoadPdfServiceRequest = (payload: { url: string, name: string, documentId?: string, documentContentType?: string }, resolve: () => void, reject: () => void): TAction => ({
  type: DOWNLOAD_PDF_SERVICE_REQUEST,
  payload,
  resolve,
  reject,
});
export const loadingDownloadServiceRequest= (payload: boolean): TAction => ({ type: LOADING_DOWNLOAD_SERVICE_REQUEST, payload });
export const setLoadMoreServiceRequest = (payload: boolean): TAction => ({ type: SET_LOAD_MORE_SERVICE_REQUEST, payload });

// Payment transaction
export const getPaymentTransaction = (): TAction => ({ type: GET_PAYMENT_TRANSACTION });
export const savePaymentTransaction = (payload: MODEL.IPaymentTransaction[]): TAction => ({
  type: SAVE_PAYMENT_TRANSACTION,
  payload,
});
export const loadingPaymentTransaction = (payload: boolean): TAction => ({ type: LOADING_PAYMENT_TRANSACTION, payload });

export const saveTransactionFilter = (payload: string[]): TAction => ({
  type: SAVE_TRANSACTION_FILTER,
  payload,
});

export const triggerFilter = (payload: boolean): TAction => ({
  type: TRIGGER_FILTER,
  payload,
});

