import { createSelector } from 'reselect';

import initialState from './state';
import { NS_TRANSACTION_HISTORY_TYPE } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_TRANSACTION_HISTORY_TYPE] || initialState;
const makeSelectClaimTransactionLoading = () =>
  createSelector(currentState, state => state.claimHistoryTransactions.claimTransactionLoading);
const makeSelectListLa = () => createSelector(currentState, state => state.claimHistoryTransactions.listLa);
const makeSelectClaimTransactions = () => createSelector(currentState, state => state.claimHistoryTransactions.data);
const makeSelectIsLoadMoreClaim = () => createSelector(currentState, state => state.claimHistoryTransactions.isLoadMore);

const makePagingServiceRequest = () => createSelector(currentState, state => state.icareRequest.paging);
const makeLoadingServiceRequest = () => createSelector(currentState, state => state.icareRequest.loading);
const makeLoadingDownloadServiceRequest = () => createSelector(currentState, state => state.icareRequest.loadingDownload);
const makeServiceRequest = () => createSelector(currentState, state => state.icareRequest.serviceRequest);
const makeServiceRequestLoadMore = () => createSelector(currentState, state => state.icareRequest.canLoadMore);
const makeLoadingLoadMoreServiceRequest = () => createSelector(currentState, state => state.icareRequest.loadingLoadMore);

const makePaymentTransaction = () => createSelector(currentState, state => state.paymentTransaction.data);
const makeLoadingPaymentTransaction = () => createSelector(currentState, state => state.paymentTransaction.loading);
const makeSelectTransactionFilter = () => createSelector(currentState, state => state.transactionFilter);
const makeSelectTriggerFilterFlag = () => createSelector(currentState, state => state.triggerFilterFlag);

export {
  // Claim transaction
  makeSelectClaimTransactionLoading,
  makeSelectListLa,
  makeSelectClaimTransactions,
  makeSelectIsLoadMoreClaim,
  // Service request
  makeServiceRequest,
  makeLoadingServiceRequest,
  makeLoadingDownloadServiceRequest,
  makePagingServiceRequest,
  makeServiceRequestLoadMore,
  makeLoadingLoadMoreServiceRequest,
  // Payment transaction
  makePaymentTransaction,
  makeLoadingPaymentTransaction,
  makeSelectTransactionFilter,
  makeSelectTriggerFilterFlag,
};
