import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS } from './constants';
import initialState, { TChangePasswordTypeState } from './state';

const requestTypeReducer = (state: TChangePasswordTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TChangePasswordTypeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
    }
  });
};

export default requestTypeReducer;
