import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_SERVICE_REQUEST,
  SAVE_SERVICE_REQUEST,
  LOADING_CLAIM_TRANSACTION_REQUEST,
  LOADING_CLAIM_TRANSACTION_SUCCESS,
  SAVE_CLAIM_TRANSACTION,
  SAVE_LA_LIST,
  SET_LOAD_MORE_CLAIM,
  SAVE_PAYMENT_TRANSACTION,
  LOADING_PAYMENT_TRANSACTION,
  LOADING_DOWNLOAD_SERVICE_REQUEST,
  LOADING_LOAD_MORE_SERVICE_REQUEST,
  SET_LOAD_MORE_SERVICE_REQUEST,
  SAVE_TRANSACTION_FILTER,
  TRIGGER_FILTER,
} from './constants';
import initialState, { TTransactionHistoryTypeState } from './state';

const transactionHistoryReducer = (state: TTransactionHistoryTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TTransactionHistoryTypeState>) => {
    switch (action.type) {
      case LOADING_CLAIM_TRANSACTION_REQUEST: {
        draft.claimHistoryTransactions.claimTransactionLoading = true;
        break;
      }
      case LOADING_CLAIM_TRANSACTION_SUCCESS: {
        draft.claimHistoryTransactions.claimTransactionLoading = false;
        break;
      }
      case SAVE_LA_LIST: {
        draft.claimHistoryTransactions.listLa = action.payload;
        break;
      }
      case SAVE_CLAIM_TRANSACTION: {
        draft.claimHistoryTransactions.data = action.payload;
        break;
      }
      case SET_LOAD_MORE_CLAIM: {
        draft.claimHistoryTransactions.isLoadMore = action.payload;
        break;
      }
      case LOADING_SERVICE_REQUEST: {
        draft.icareRequest.loading = action.payload;
        break;
      }
      case LOADING_LOAD_MORE_SERVICE_REQUEST: {
        draft.icareRequest.loadingLoadMore = action.payload;
        break;
      }
      case LOADING_DOWNLOAD_SERVICE_REQUEST: {
        draft.icareRequest.loadingDownload = action.payload;
        break;
      }
      case SAVE_SERVICE_REQUEST: {
        draft.icareRequest.paging = action.payload.paging;
        draft.icareRequest.serviceRequest = action.payload.serviceRequests;
        break;
      }
      case SAVE_PAYMENT_TRANSACTION: {
        draft.paymentTransaction.data = action.payload;
        break;
      }
      case LOADING_PAYMENT_TRANSACTION: {
        draft.paymentTransaction.loading = action.payload;
        break;
      }
      case SET_LOAD_MORE_SERVICE_REQUEST: {
        draft.icareRequest.canLoadMore = action.payload;
        break;
      }
      case SAVE_TRANSACTION_FILTER: {
        draft.transactionFilter = action.payload;
        break;
      }
      case TRIGGER_FILTER: {
        draft.triggerFilterFlag = action.payload;
        break;
      }
    }
  });
};

export default transactionHistoryReducer;
