import React, { forwardRef, useImperativeHandle } from 'react';
import { Image, View } from 'react-native';
import { Button, NativeModuleClient, Text } from 'superapp-common-component';
import dayjs from 'dayjs';
import ErrorIcon from '@/assets/images/icon-warning.png';
import { useTranslation } from 'react-i18next';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';

type ModalErrorPropsType = {
  handleLogin: () => void;
};

export interface ModalSessionExpiredRef {
  open: () => void;
  close: () => void;
}

const ModalSessionExpired = ({ handleLogin }: ModalErrorPropsType, ref?: React.MutableRefObject<ModalSessionExpiredRef>) => {
  const { t } = useTranslation();
  const [modalsRef, modalInstance] = useModal();

  const onPressClose = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.HOME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login',
    });
    modalInstance()?.close();
    handleLogin && handleLogin();
  };

  useImperativeHandle(
    ref,
    (): ModalSessionExpiredRef => ({
      open: () => modalInstance()?.show(),
      close: () => modalInstance()?.close(),
    }),
  );

  return (
    <Modal ref={modalsRef} modalStyle={styles.modalStyles} overlayOpacity={0.5} onDismiss={() => null}>
      <View style={styles.modalContainer}>
        <View style={styles.wrapperModal}>
          <Image source={ErrorIcon} style={styles.imageWarning} />
          <Text style={styles.modalTitle}>{t('common.title.session.expired')}</Text>
          <Text style={styles.modalDescription}>{t('common.description.session.expired')}</Text>
          <View style={styles.wrapperBtn}>
            <Button
              id="Button.Confirm.Login"
              style={styles.btnConfirm}
              text={t('common.btn.session.expired')}
              type="secondary"
              onPress={onPressClose}
            />
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalSessionExpired);
