import React, { useLayoutEffect, useMemo, useRef, useState } from 'react';
import {
  Image,
  SafeAreaView,
  ScrollView,
  TouchableOpacity,
  View,
} from 'react-native';
import {
  useTheme,
  Text,
  Icon,
  Checkbox,
  Button,
  NativeModuleClient,
} from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { utils } from 'superapp-common-component';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import dayjs from 'dayjs';

import { HITSLOP } from '@/utils/styles';
import { getGender } from '@/utils/utils';
import ROUTE_PATH from '@/navigators/routePath';
import { Theme } from '@/config/themes.config';
import { TStackParamList } from '@/navigators';
import PdfViewer, { TPdfViewerRef } from '@/components/PdfViewer';
import { makeSelectProfile } from '@/stores/global/selectors';
import WebView, { TWebViewUrlRef } from '@/components/WebView';

import pdfbase64 from './pdfbase64';
import useStyles from './styles';

const GENDER = {
  MALE: 'male',
  FEMALE: 'female',
};

const { a11y } = utils;

type EConsentOtpNavigationProp = StackNavigationProp<
  TStackParamList,
  'EConsent'
>;

const EConsent = () => {
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const pdfViewerRef = useRef<TPdfViewerRef>();
  const termpdfViewerRef = useRef<TPdfViewerRef>();
  const tcViewerRef = useRef<TPdfViewerRef>();
  const profile = useSelector(makeSelectProfile());
  const navigation = useNavigation<EConsentOtpNavigationProp>();
  const [confirm, setConfirm] = useState<{
    readfile: boolean;
    checked: boolean;
  }>({ readfile: false, checked: false });
  const { t } = useTranslation();
  const webviewRef = useRef<TWebViewUrlRef>();

  const isValidPdfUrl = useMemo(() => {
    const value = t('home.econsent.linkPdf') || '';
    return value.indexOf('.pdf') !== -1;
  }, [t]);

  const showWebviewTC = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ECONSENT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'T&C',
    });
    tcViewerRef?.current?.openPdf();
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text style={styles.header}>{t('home.econsent.title')}</Text>
      ),
      headerTitleStyle: styles.mainTitle,
      headerLeft: () => (
        <TouchableOpacity
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.ECONSENT,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}
          {...a11y('EConsentOtp.iconBackHeaderLeft')}>
          <Icon
            name="Arrow-Left"
            color={theme.colors.white}
            size={24}
            style={styles.marginLeft16}
          />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.safearea}>
      <ScrollView>
        <View style={styles.head}>
          <Text style={styles.intro}>{t('home.econsent.request.title')}</Text>
        </View>
        <View style={styles.container}>
          <View style={styles.content}>
            <Text style={styles.title}>{t('home.econsent.info')}</Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.name')}</Text>
            <Text style={styles.des}>{profile?.fullName}</Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.dob')}</Text>
            <Text style={styles.des}>
              {' '}
              {profile?.dob ? dayjs(profile?.dob).format('DD/MM/YYYY') : ''}
            </Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.gender')}</Text>
            <Text style={styles.des}>
              {profile?.gender
                ? (getGender(profile?.gender) === GENDER.FEMALE &&
                    t('myprofile.gender.female')) ||
                  (getGender(profile?.gender) === GENDER.MALE &&
                    t('myprofile.gender.male'))
                : ''}
            </Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.id')}</Text>
            <Text style={styles.des}>
              {/* {profile?.idCardNo ? maskedNumber(profile?.idCardNo, 4) : ''} */}
              {profile?.idCardNo ? profile?.idCardNo : ''}
            </Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.mobile')}</Text>
            <Text style={styles.des}>
              {/* {profile?.mobile ? maskedPhoneNumber(profile?.mobile) : '-'} */}
              {profile?.mobile ? profile?.mobile : ''}
            </Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.email')}</Text>
            <Text style={styles.des}> {profile?.email || ''}</Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.address')}</Text>
            <Text style={styles.des}> {profile?.address || ''}</Text>
          </View>
          <View style={styles.wrapper}>
            <Text style={styles.info}>{t('home.econsent.date')}</Text>
            <Text style={styles.des}>{dayjs().format('DD/MM/YYYY')}</Text>
          </View>
        </View>

        <View style={styles.confirminfo}>
          <View style={styles.pdhorizontal}>
            <View style={styles.content}>
              <Text style={styles.pdf}>{t('home.econsent.readform')}</Text>
            </View>
            <View style={styles.confirm}>
              <TouchableOpacity
                onPress={() => {
                  pdfViewerRef.current?.openPdf();
                  if (!confirm.readfile) {
                    setConfirm({ ...confirm, readfile: true });
                    NativeModuleClient.AdobeExperience.trackButton({
                      screen: ROUTE_PATH.ECONSENT,
                      unixTimestamp: dayjs().unix(),
                      buttonText: 'Open Pdf',
                    });
                  }
                }}>
                <View style={styles.wrapperPdf}>
                  <Image
                    style={styles.img}
                    source={require('@/assets/images/pdf.png')}
                  />
                  <Text style={[styles.filename, styles.pdf]}>
                    {t('home.econsent.filename')}
                  </Text>
                  <View
                    style={[
                      styles.icon,
                      confirm.readfile ? styles.checked : styles.uncheck,
                    ]}>
                    <Icon
                      name="Checkmark"
                      color={theme.colors.white}
                      size={14}
                    />
                  </View>
                </View>
              </TouchableOpacity>
            </View>
          </View>
          <View style={styles.line} />

          <View style={styles.box}>
            <View style={styles.confirmbox}>
              <Checkbox
                checked={confirm.checked}
                size={24}
                onPress={() =>
                  setConfirm({ ...confirm, checked: !confirm.checked })
                }
              />
              <Text style={styles.term}>
                {t('home.econsent.term')}{' '}
                <Text
                  style={[styles.term, styles.highline]}
                  onPress={() => {
                    NativeModuleClient.AdobeExperience.trackButton({
                      screen: ROUTE_PATH.ECONSENT,
                      unixTimestamp: dayjs().unix(),
                      buttonText: 'Privacy',
                    });
                    termpdfViewerRef.current?.openPdf();
                  }}>
                  {t('home.econsent.privacy')}
                </Text>
                {t('home.econsent.space')}
                <Text
                  style={[styles.term, styles.highline]}
                  onPress={showWebviewTC}>
                  {t('common.t&c.policy')}
                </Text>
              </Text>
            </View>
            <View style={styles.confirmbox}>
              <Button
                style={styles.flex}
                id="EConsent.btnConfirm"
                text={t('home.econsent.confirm')}
                onPress={() => {
                  navigation.navigate(ROUTE_PATH.ECONSENT_OTP);
                  NativeModuleClient.AdobeExperience.trackButton({
                    screen: ROUTE_PATH.ECONSENT,
                    unixTimestamp: dayjs().unix(),
                    buttonText: 'Confirm',
                  });
                }}
                disabled={!confirm.checked || !confirm.readfile}
              />
            </View>
          </View>
        </View>

        {/* <PdfViewer ref={pdfViewerRef} uri={pdfbase64} type="base64" /> */}
        <WebView ref={webviewRef} />
        <PdfViewer
          ref={pdfViewerRef}
          uri={isValidPdfUrl ? t('home.econsent.linkPdf') : pdfbase64}
          type={isValidPdfUrl ? 'url' : 'base64'}
        />
        <PdfViewer
          ref={termpdfViewerRef}
          uri={t('common.vitality.termCondition.url')}
          type="url"
        />
        <PdfViewer
          ref={tcViewerRef}
          uri={t('home.econsent.t&c.url')}
          type="url"
        />
      </ScrollView>
    </SafeAreaView>
  );
};

export default EConsent;
