import React, { forwardRef, useImperativeHandle } from 'react';
import { View, TouchableWithoutFeedback, Image } from 'react-native';
import { Button } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import Text from '@/components/Text';

import forgotPinImage from '@/assets/images/forgot-pin.png';
import { a11y } from '@/utils/utils';

import styles from './styles';
import Modal, { useModal } from '../Modal/Modal';

interface ModalForgotPinProps {
  setCurrentStep: (step: number) => void;
}
export interface ModalForgotPindRef {
  open: () => void;
  close: () => void;
}

const ModalForgotPin = (props: ModalForgotPinProps, ref: React.Ref<unknown> | undefined) => {
  const { setCurrentStep } = props;
  const { t } = useTranslation();
  const [modalRef, modalInstance] = useModal();

  useImperativeHandle(
    ref,
    (): ModalForgotPindRef => ({
      open: () => modalInstance()?.show(),
      close: () => modalInstance()?.close(),
    }),
  );

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer}>
    <TouchableWithoutFeedback
        onPress={() => {
          modalInstance()?.close();
        }}
        {...a11y('ModalForgotPin.backdropCloseModalForgotPin')}
      >
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View>
              <View style={{ alignItems: 'center' }}>
                <Image style={styles.forgotPinStyle} source={forgotPinImage} />
              </View>
              <Text style={styles.forgotText} fontFamily="bold">
                {t('login.modal.forgot.title')}
              </Text>
              <Text style={styles.subForgotText}>{t('login.modal.forgot.sub.title')}</Text>
            </View>
            <View style={{ flexDirection: 'row' }}>
              <Button
                text={t('login.modal.forgot.button.cancel')}
                type="secondary"
                uppercase={false}
                // labelStyle={styles.buttonText}
                size="large"
                style={[{ borderColor: '#FFFFFF', flex: 1 }, styles.button]}
                onPress={() => {
                  modalInstance()?.close();
                }}
                id="ModalForgotPin.btnCloseModalForgotPin"
              />
              <Button
                text={t('login.modal.forgot.button.login')}
                type="primary"
                uppercase={false}
                // labelStyle={styles.buttonText}
                size="large"
                style={[styles.button, { flex: 1 }]}
                onPress={() => {
                  modalInstance()?.close();
                  setCurrentStep(1);
                }}
                id="ModalForgotPin.btnBackToLogin"
              />
            </View>
          </View>
        </View>
      </TouchableWithoutFeedback>
    </Modal>
  );
};

export default forwardRef(ModalForgotPin);
