import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

type TAuthenParam = { username: string; password: string; deviceId?: {os: string, notificationToken: string}; clientId: string };
type TCheckExistUserNameParam = { username: string };
type TSubmitRegisterParam = { username: string; password: string; otpValue: string; otpId: string; };
type TRequestOTPSMSParam = { captchaValue: string; captchaHash: string; phoneNo: string; otpSendType: string; otpType: string; };
type TCheckEmailParam = { email: string };

class RegisterService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_CIAM);
  }

  public checkUsername = (params: TCheckExistUserNameParam) => this.instance.post<boolean>('/checkUsername', {...params});
	
  public submitRegister = (params: TSubmitRegisterParam) => this.instance.post<MODEL.IRegister>('/register', {...params});
  
  public requestOtpRegisterService = (params: TRequestOTPSMSParam) => this.instance.post<string>('/otp/requestOtpGuest', {...params}, { headers: {Authorization: " "} });

  public loginService = (params: TAuthenParam) => this.instance.post<MODEL.IAuthenticate>('/authenticate', params);

  public checkEmailService = (params: TCheckEmailParam) => this.instance.post<boolean>('/checkEmail', params);

}

export default new RegisterService();