import dayjs from 'dayjs';

export const isValidDate = (date: Date | string, format: string) => {
  return dayjs(date, format, true).isValid()
}

export function parseDDMMYYYYtoYYYYMMDD(date: string){
  if (!date) {return '';}
  var dateParts: Array<any> = date.split('/');

  var result = new Date(dateParts[2], dateParts[1] - 1, dateParts[0]);
  return result;
  // return dateParts[2] + '-' + (dateParts[1]) + '-' + dateParts[0];
}

export const formatDateLocal = (date: string, format = 'DD/MM/YYYY') => {
  return dayjs(parseDDMMYYYYtoYYYYMMDD(date)).format(format);
}

export const formatDateServer = (date: Date | string, format = 'YYYY-MM-DD') => {
  if (isValidDate(date, format)) {
    return dayjs(date, format).format(format)
  }
  return formatDateLocal(date, format)
}
