import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, GET_CONTENT_ONBOARDING, SAVE_CONTENT_ONBOARDING } from './constants';

export type TTransformData = {
  id?: string;
  title?: string;
  description?: string;
  imagePath?: string;
};

export type OnboardingTranslation = {
	vi: Array<TTransformData>,
	en: Array<TTransformData>
};

export const loadingSuccess = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequest = (): TAction => ({ type: LOADING_REQUEST });
export const getContentOnboarding = (): TAction => ({ type: GET_CONTENT_ONBOARDING });
export const saveContentOnboarding = (payload: OnboardingTranslation): TAction => ({ type: SAVE_CONTENT_ONBOARDING, payload });
