import React, { useEffect, useMemo, useRef, useState } from 'react';
import _orderBy from 'lodash/orderBy';
import _set from 'lodash/set';
import { View, ScrollView, Image, Animated } from 'react-native';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import { useTranslation } from 'react-i18next';
import { Button, useTheme, NativeModuleClient } from 'superapp-common-component';
import { useSelector, useDispatch } from 'react-redux';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import WebViewUrl from '@/components/WebView';
import { checkPWV, formatToCurrency, getImageByProductCode, getItemCoverageNumber1, isEmpty } from '@/utils/utils';
import {
  makeSelectLoadingPolicyAck,
  makeSelectPaymentMembership,
  makeSelectPoliciesAck,
  makeSelectPoliciesIncommingPayment,
  makeSelectProductImage,
  makeSelectProfile,
} from '@/stores/global/selectors';
import { Theme } from '@/config/themes.config';
import APP_ENV from '@/config/app.config';
import { APPS_NAME, FUNCTION_TYPES, OPEN_PWV_FROM, TYPE_NAVIGATION_POS_REQUEST } from '@/utils/constants';
import { encryptPaymentData, encryptPaymentMembershipData } from '@/stores/global/actions';
import ROUTE_PATH from '@/navigators/routePath';
import { ModalPermissionRef } from '@/components/ModalPermission';

import useStyles, { CARD_WIDTH, POLICY_ITEM_MARGIN_RIGHT } from './styles';

type TProductCodeImage = {
  productCodes: string[] | undefined;
  image: string | undefined;
};

interface IActionsProps {
  setLoadingOpenMiniApp: (value: boolean) => void;
  isRunningMiniApp: React.MutableRefObject<boolean>;
  isLoged: boolean;
  permissionModalRef?: React.MutableRefObject<ModalPermissionRef | null | undefined>;
}

interface IPolicyItemProps {
  item: MODEL.IPolicy;
  isPayment?: boolean;
  openMiniApp: (policyNumber?: string, isVideo?: boolean) => Promise<void>;
  permissionModalRef?: React.MutableRefObject<ModalPermissionRef | null | undefined>;
  showWebView?: (url: string) => void;
}

const FORMAT_PAY_TO_DATE = 'YYYY-MM-DD:HH:mm:ssZZ';

const PolicyItem: React.FC<IPolicyItemProps> = ({ item, isPayment, openMiniApp, permissionModalRef, showWebView }) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const productImage = useSelector(makeSelectProductImage());
  const coverageNumber1: MODEL.ICoverage | undefined = getItemCoverageNumber1(item?.coverage);

  const policyImageUrl = useMemo(() => {
    const listProductCodeByImage: TProductCodeImage[] | undefined = productImage?.items.map(codeItem => ({
      productCodes: codeItem.productCodes?.value,
      image: codeItem?.image?.value,
    }));
    return getImageByProductCode(coverageNumber1?.productCode || '', listProductCodeByImage);
  }, [productImage, coverageNumber1]);

  const handlePayment = (policyNumber: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.HOME,
      unixTimestamp: dayjs().unix(),
      buttonText: isPayment ? 'Pay now' : 'Confirm',
    });
    if (permissionModalRef?.current?.handlePermission(FUNCTION_TYPES.YOUR_ACTION)) {
      return;
    }
    if (isPayment && showWebView) {
      const callback = (url: string) => showWebView(url);
      const paymentDomain = t('common.payment.url');
      dispatch(encryptPaymentData({ polnum: policyNumber, paymentDomain, callback }));
      return;
    }

    openMiniApp(policyNumber, checkPWV(item));
  };

  return (
    <View style={styles.srollPayments} key={item?.policyId}>
      <View style={styles.overlayBackground}>
        <View style={styles.contentContainer}>
          <View style={styles.imageWrapper}>
            {policyImageUrl ? (
              <Image source={{ uri: APP_ENV.CONTENT_AEM + policyImageUrl }} style={styles.imagePolicyItem} />
            ) : (
              <Image source={require('@/assets/images/saving.png')} style={styles.imagePolicyItem} />
            )}
          </View>

          <View style={styles.leftContent}>
            {isPayment ? (
              <>
                <View style={[styles.paymentInfoContainer, styles.mb4]}>
                  <Text style={styles.textValue} xl={{ fontSize: 14 }} numberOfLines={1} fontFamily="bold">
                    {item?.insured?.fullName}
                  </Text>
                </View>
                <View style={[styles.paymentInfoContainer, styles.mb4]}>
                  <Text style={styles.textKey} xl={{ fontSize: 14 }}>
                    {t('home.incoming.payment.policy.number')}
                  </Text>
                  <Text style={styles.textValue} xl={{ fontSize: 14 }} fontFamily="bold">
                    {item?.policyNumber}
                  </Text>
                </View>
                <View style={[styles.paymentInfoContainer]}>
                  <Text style={styles.textKey} xl={{ fontSize: 14 }}>
                    {t('home.incoming.payment.policy.due.date')}
                  </Text>
                  <Text style={styles.textValue} xl={{ fontSize: 14 }} fontFamily="bold">
                    {dayjs(item?.payToDate, FORMAT_PAY_TO_DATE).format('DD/MM/YYYY')}
                  </Text>
                </View>
              </>
            ) : (
              <>
                <View style={[styles.paymentInfoContainer, styles.mb4]}>
                  <Text style={styles.textValue} xl={{ fontSize: 14 }} numberOfLines={1} fontFamily="bold">
                    {t('home.policy.policy.policy.acknowledement')}
                  </Text>
                </View>
                <View style={[styles.paymentInfoContainer, styles.mb4]}>
                  <Text style={styles.textKey} xl={{ fontSize: 14 }}>
                    {t('home.incoming.payment.policy.number')}
                  </Text>
                  <Text style={styles.textValue} xl={{ fontSize: 14 }} fontFamily="bold">
                    {item?.policyNumber}
                  </Text>
                </View>
                <View style={[styles.paymentInfoContainer]}>
                  <Text style={styles.textKey} xl={{ fontSize: 14 }}>
                    {t('home.policy.policy.issue.date')}
                  </Text>
                  <Text style={styles.textValue} xl={{ fontSize: 14 }} fontFamily="bold">
                    {dayjs(item?.applicationDate, FORMAT_PAY_TO_DATE).format('DD/MM/YYYY')}
                  </Text>
                </View>
              </>
            )}
          </View>

          <View style={styles.rightContent}>
            {isPayment && (
              <Text style={styles.valueOfPayment} xl={{ fontSize: 14 }}>
                {formatToCurrency(item?.modePremium)} đ
              </Text>
            )}
            <Button
              text={isPayment ? t('home.incoming.payment.pay') : t('home.policy.policy.confirm')}
              uppercase={false}
              labelStyle={styles.buttonText}
              size="large"
              style={styles.button}
              id={`home.btnIncomingPaymentPay${item?.policyNumber}`}
              onPress={() => handlePayment(item?.policyNumber)}
            />
          </View>
        </View>
      </View>
    </View>
  );
};

function MembershipPayment({
  paymentMembership,
  permissionModalRef,
  showWebView,
}: {
  paymentMembership?: MODEL.IMembership;
  permissionModalRef?: React.MutableRefObject<ModalPermissionRef | null | undefined>;
  showWebView?: (url: string) => void;
}) {
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const profile = useSelector(makeSelectProfile());

  const handlePayment = (membership?: MODEL.IMembership) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.HOME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Pay now',
    });
    if (permissionModalRef?.current?.handlePermission(FUNCTION_TYPES.YOUR_ACTION)) {
      return;
    }

    if (showWebView && membership) {
      const callback = (url: string) => showWebView(url);
      const paymentDomain = t('common.payment.url');
      dispatch(encryptPaymentMembershipData({ membership: membership?.no, amount: membership?.billingAmount, paymentDomain, callback }));
    }
  };

  return (
    <View style={styles.srollPayments}>
      <View style={styles.overlayBackground}>
        <View style={styles.contentContainer}>
          <View style={styles.imageWrapper}>
            <Image source={require('@/assets/images/vitality.png')} style={styles.imagePolicyItem} />
          </View>
          <View style={styles.leftContent}>
            <View style={[styles.paymentInfoContainer, styles.mb4]}>
              <Text style={styles.textValue} xl={{ fontSize: 14 }} numberOfLines={1} fontFamily="bold">
                {profile?.fullName}
              </Text>
            </View>
            <View style={[styles.paymentInfoContainer, styles.mb4]}>
              <Text style={styles.textKey} xl={{ fontSize: 14 }}>
                {t('home.incoming.payment.membership.number')}
              </Text>
              <Text style={styles.textValue} xl={{ fontSize: 14 }} fontFamily="bold">
                {paymentMembership?.no}
              </Text>
            </View>
            <View style={[styles.paymentInfoContainer]}>
              <Text style={styles.textKey} xl={{ fontSize: 14 }}>
                {t('home.incoming.payment.policy.due.date')}
              </Text>
              <Text style={styles.textValue} xl={{ fontSize: 14 }} fontFamily="bold">
                {dayjs(paymentMembership?.dueDate, FORMAT_PAY_TO_DATE).format('DD/MM/YYYY')}
              </Text>
            </View>
          </View>
          <View style={styles.rightContent}>
            <Text style={styles.valueOfPayment} xl={{ fontSize: 14 }}>
              {formatToCurrency(paymentMembership?.billingAmount) || 0} đ
            </Text>
            <Button
              text={t('home.incoming.payment.pay')}
              uppercase={false}
              labelStyle={styles.buttonText}
              size="large"
              style={styles.button}
              id={`home.btnMembershipPayment`}
              onPress={() => handlePayment(paymentMembership)}
            />
          </View>
        </View>
      </View>
    </View>
  );
}

const Actions: React.FC<IActionsProps> = ({ setLoadingOpenMiniApp, isRunningMiniApp, isLoged, permissionModalRef }) => {
  const { t } = useTranslation();
  const animation = useRef(new Animated.Value(0)).current;
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const policies = useSelector(makeSelectPoliciesAck());
  const policiesIncommingPayment = useSelector(makeSelectPoliciesIncommingPayment());
  const loadingPolicyAck = useSelector(makeSelectLoadingPolicyAck());
  const paymentMembership = useSelector(makeSelectPaymentMembership());
  const [policyList, setPolicyList] = useState<MODEL.IPolicy[]>([]);
  const WebviewRef = useRef<any>(null);

  const openMiniApp = async (policyNumber?: string, isVideo?: boolean) => {
    try {
      setLoadingOpenMiniApp(true);
      await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.POS_REQUEST);
      await NativeModuleClient.MiniApp.loadApp(
        APPS_NAME.POS_REQUEST,
        JSON.stringify({
          policyNumber,
          type: isVideo ? TYPE_NAVIGATION_POS_REQUEST.TYPE_PERSONAL_VIDEO : TYPE_NAVIGATION_POS_REQUEST.TYPE_ECERTIFICATE,
          videoFrom: OPEN_PWV_FROM.YOUR_ACTION,
        }),
      );
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', APPS_NAME.POS_REQUEST);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  useEffect(() => {
    if (policies) {
      setPolicyList(policies);
    }
  }, [policies]);

  const showWebView: (url: string) => void = (url: string) => {
    WebviewRef?.current?.openURL(url);
  };

  const getPaymentList = (payload: MODEL.IPolicy[]) => {
    payload?.map(item => {
      _set(item, 'payToDate', dayjs(item?.payToDate, FORMAT_PAY_TO_DATE).format('YYYY-MM-DDT00:00:00+07:00'));
    });
    return _orderBy(payload, ['payToDate', 'policyNumber'], ['asc', 'asc']);
  };

  const totalAction = useMemo(() => {
    const policyListTotal = policyList?.length || 0;
    const policiesIncommingPaymentTotal = policiesIncommingPayment?.length || 0;
    const paymentMembershipTotal = !isEmpty(paymentMembership) ? 1 : 0;

    return policyListTotal + policiesIncommingPaymentTotal + paymentMembershipTotal;
  }, [policyList, policiesIncommingPayment, paymentMembership]);

  const SkeletonPolicyAckCard = () => (
    <View style={styles.skeletonPolicyAckCard}>
      <SkeletonPlaceholder>
        <View style={{ flexDirection: 'row' }}>
          <View style={styles.skeletonPolicyAvatar} />
          <View>
            <SkeletonPlaceholder.Item marginTop={12}>
              <SkeletonPlaceholder.Item width={250} height={20} />
              <SkeletonPlaceholder.Item width={200} height={20} marginTop={12} />
            </SkeletonPlaceholder.Item>
          </View>
        </View>
      </SkeletonPlaceholder>
    </View>
  );

  const renderPaymentItems = () => {
    return (
      <ScrollView
        contentContainerStyle={styles.scrollViewContentContainer}
        disableIntervalMomentum={true}
        decelerationRate={0}
        snapToInterval={CARD_WIDTH + POLICY_ITEM_MARGIN_RIGHT}
        showsHorizontalScrollIndicator={false}
        horizontal
        scrollEnabled={!(totalAction === 1)}
      >
        {loadingPolicyAck ? (
          <SkeletonPolicyAckCard />
        ) : (
          policies?.map((item: MODEL.IPolicy) => (
            <PolicyItem item={item} key={item.id} openMiniApp={openMiniApp} permissionModalRef={permissionModalRef} />
          ))
        )}
        {getPaymentList(policiesIncommingPayment)?.map((item: MODEL.IPolicy) => (
          <PolicyItem
            showWebView={showWebView}
            item={item}
            key={item.id}
            isPayment
            openMiniApp={openMiniApp}
            permissionModalRef={permissionModalRef}
          />
        ))}
        {!isEmpty(paymentMembership) && (
          <MembershipPayment paymentMembership={paymentMembership} showWebView={showWebView} permissionModalRef={permissionModalRef} />
        )}
      </ScrollView>
    );
  };

  useEffect(() => {
    Animated.timing(animation, {
      toValue: totalAction !== 0 ? 1 : 0,
      useNativeDriver: false,
    }).start();
  }, [policyList, policiesIncommingPayment, paymentMembership]);

  return isLoged && totalAction !== 0 ? (
    <Animated.View style={[styles.container, { opacity: animation }]}>
      <View style={styles.titleContainer}>
        <Text style={styles.title} xl={{ fontSize: 18 }} fontFamily="bold">
          {t('home.policy.policy.action')} {`(${totalAction})`}
        </Text>
      </View>

      {renderPaymentItems()}
      <WebViewUrl ref={WebviewRef} />
    </Animated.View>
  ) : null;
};

export default Actions;
