import { combineReducers } from 'redux';

import { NS_GLOBAL } from '@/stores/global/constants';
import globalReducer from '@/stores/global/reducer';
import { NS_AUTHENTICATION } from '@/stores/authentication/constants';
import authenticationReducer from '@/stores/authentication/reducer';

import { TInjectedReducers } from './types';

export function createReducer(injectedReducers: TInjectedReducers = {}) {
  return combineReducers({
    [NS_GLOBAL]: globalReducer,
    [NS_AUTHENTICATION]: authenticationReducer,
    ...injectedReducers,
  });
}
