import { call, put, takeLatest } from 'redux-saga/effects';

import OneCustomerService from '@/services/user';
import {
  loadingEcardRequest,
  loadingEcardSuccess,
  saveEcardsAction,
} from './actions';
import { GET_ECARDS } from './constants';

function* getECards() {
  try {
    yield put(loadingEcardRequest());
    const response: MODEL.IECardResponse = yield call(OneCustomerService.getECardsService);
    yield put(saveEcardsAction(response.data));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingEcardSuccess());
  }
}

function* eCardSaga() {
  yield takeLatest(GET_ECARDS, getECards);
}

export default eCardSaga;
