const cancelerVerifyPass = (cancel: Canceler) =>
  _set(canceler, AUTHENTICATE_WITH_PASS, cancel);

function* handleForceVideo(profile: MODEL.IProfile) {
  try {
    const responseAck: MODEL.IPoliciesResponse = yield call(
      UserService.getPoliciesService,
      { types: ['ACK_PENDING'] },
    );

    const confirmEtrans = profile?.isOT === 0;

    if (confirmEtrans || !responseAck.data) return;

    const pwvPolicy = _find(responseAck.data, (policy: MODEL.IPolicy) =>
      checkPWV(policy),
    );

    if (!isEmpty(pwvPolicy) && !__DEV__) {
      yield NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.POS_REQUEST);
      yield NativeModuleClient.MiniApp.loadApp(
        APPS_NAME.POS_REQUEST,
        JSON.stringify({
          policyNumber: pwvPolicy?.policyNumber,
          type: TYPE_NAVIGATION_POS_REQUEST.TYPE_PERSONAL_VIDEO,
        }),
      );
    }
  } catch (error) {
    console.error('ERROR: Force Video ACK', error);
  }
}

function* registerNotiWithCurrentUser(accessToken: string) {
  try {
    const tags: string[] = yield call(AzureNotificationClient.getTags);
    const isHasGeneralTag: boolean = yield call(
      AzureNotificationClient.hasGeneralTag,
      tags,
    );
    if (!isHasGeneralTag) return;

    const userId = AzureNotificationClient.getUserId(accessToken);
    if (!userId) return;

    // remove tags not belong current user
    if (!tags.includes(userId)) {
      AzureNotificationClient.clearTagsWithoutGeneral();
      AzureNotificationClient.addTag(userId);
    }
    AzureNotificationClient.addCurrentUserTagService(userId);
  } catch {}
}

const MAX_RETRY = 3;
let RETRY_CHECK_LOGIN = 0;

function* handleCheckLogin(
  params: { username: string; password: string },
  setVisibleLoginModal: (visible: boolean) => void,
) {
  try {
    const body = {
      ...params,
      clientId: CLIENT_ID,
      deviceId: {
        os: Platform.OS,
        notificationToken: '',
      },
    };

    RETRY_CHECK_LOGIN = RETRY_CHECK_LOGIN + 1;

    if (RETRY_CHECK_LOGIN > MAX_RETRY) {
      RETRY_CHECK_LOGIN = 0;
      yield put(loadingSuccess());
      yield put(loginError({ isLoged: false, error: 'invalid_username' }));
      return;
    }

    const response: MODEL.ICheckLogin = yield call(
      AuthService.checkLoginService,
      body,
    );
    if (response.success) {
      yield put(loginOkta({ ...params, setVisibleLoginModal }));
    }
  } catch (error: any) {
    yield put(loadingSuccess());
    if (error?.message === 'Network Error') {
      yield put(
        loginError({ isLoged: false, error: ERROR_CODE_MESSAGE.NO_INTERNET }),
      );
    } else {
      const errorType = error as APIError;
      if (errorType?.errorCode === ERROR_CODE_MESSAGE.PASSWORD_FORMAT_ERROR) {
        setVisibleLoginModal(false); // Close modal login
        yield put(loadingSuccess());
        yield setItem(STORAGE_NAME.USERNAME, JSON.stringify(params.username));
        navigationRef.navigate(
          ROUTE_PATH.CHANGE_PASSWORD as never,
          { preLogin: true } as never,
        );
        return;
      }

      yield put(
        loginError({
          isLoged: false,
          error: errorType.errorCode,
          remainingLoginAttempts: _get(
            errorType.errorMessage,
            'remainingLoginAttempts',
          ),
        }),
      );
    }
  }
}

function* handleAuthenticateOkta(
  tokenOkta: MODEL.IOktaRefreshToken,
  username: string,
  isAutoLogin?: boolean,
) {
  try {
    if (canceler[LOGIN_OKTA]) {
      canceler[LOGIN_OKTA]('Cancel Request login okta');
      _set(canceler, LOGIN, undefined);
    }

    yield setStorageToken({
      accessToken: tokenOkta.access_token,
      refreshToken: tokenOkta.refresh_token,
    });

    const response: MODEL.IAuthenticate = yield call(
      AuthService.authenticateOktaService,
      {
        clientId: CLIENT_ID,
        refreshTokenOkta: tokenOkta.refresh_token,
        deviceId: {
          os: Platform.OS,
          notificationToken: tokenOkta.access_token,
        },
      },
      cancelerLogin,
    );

    if (response?.accessToken) {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'Login Success',
        unixTimestamp: dayjs().unix(),
      });
      yield setStorageToken(response);
      yield all([
        setItem(STORAGE_NAME.USERNAME, JSON.stringify(username)),
        setVitalityStorageToken(tokenOkta),
      ]);

      // Migration account
      // Call profile
      const responseProfile: MODEL.IProfileResponse = yield call(
        UserService.getProfileService,
      );
      yield put(saveProfile(responseProfile.data));

      yield setItem(
        STORAGE_NAME.FULL_NAME,
        JSON.stringify(responseProfile?.data?.fullName),
      );
      yield setItem(
        STORAGE_NAME.FIRST_NAME,
        JSON.stringify(responseProfile?.data?.firstName || ''),
      );

      const migrateParser = jwtDecode<MODEL.ITokenDecode>(response.accessToken);
      const isMigrated = migrateParser?.['x-forceChangeUsername'] === '1';
      const isVerified = migrateParser?.['x-notVerified'] === '0';
      const hasRolePO =
        _intersection(responseProfile.data?.roles, [
          roles?.POLICY_OWNER,
          roles?.V_POLICY_OWNER,
        ]).length > 0;

      // SET ADOBE USER
      NativeModuleClient.AdobeExperience.setUser(migrateParser.sub);
      // yield put(saveNewUsername(responseProfile?.data?.mobile));
      yield put(saveNewUsername(username));
      yield put(saveIsMigrated(isMigrated));
      const userNameIsNotPhoneNumber = !REGEX.PHONE_NUMBER.test(
        migrateParser?.['x-username'],
      );
      if (isMigrated && userNameIsNotPhoneNumber) {
        yield all([
          put(loginSuccess(response?.accessToken)),
          put(setRefreshTokenError(undefined)),
        ]);

        if (!isEmpty(responseProfile.data.mobile)) {
          navigationRef.navigate(ROUTE_PATH.MIGRATION as never);
        } else {
          navigationRef.navigate(
            ROUTE_PATH.MIGRATION as never,
            { isHasMobile: false } as never,
          );
        }
      } else if (
        isMigrated &&
        !userNameIsNotPhoneNumber &&
        isVerified &&
        hasRolePO
      ) {
        yield all([
          put(loginSuccess(response?.accessToken)),
          put(setRefreshTokenError(undefined)),
        ]);

        if (
          !isEmpty(responseProfile.data?.contactId) &&
          !isEmpty(responseProfile.data.mobile)
        ) {
          navigationRef.navigate(
            ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION as never,
          );
        } else {
          navigationRef.navigate(ROUTE_PATH.NO_PHONE_CONTACT as never);
        }
      } else {
        yield put(remindBiometric(true));
        if (HOME_LOCAL_STATE.isJumpNotification) {
          jsEventEmitter.emit(JS_EVENT_NAMES.ON_NAVIGATE_NOTIFICATION);
        }
        if (!__DEV__) {
          yield call(handleForceVideo, responseProfile.data);
          yield spawn(registerNotiWithCurrentUser, response.accessToken!);
        }
        yield all([
          put(loginSuccess(response?.accessToken)),
          put(setRefreshTokenError(undefined)),
        ]);
      }
    }
  } catch (error) {
    console.log(error);

    const errorType = error as APIError;
    yield put(
      loginError({
        isLoged: false,
        error: errorType.errorCode,
        isAutoLogin,
        remainingLoginAttempts: _get(
          errorType.errorMessage,
          'remainingLoginAttempts',
        ),
      }),
    );

    // Adobe Analytic
    errorType?.errorCode &&
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.LOGIN,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
        error: errorType.errorCode,
      });
  } finally {
    yield put(loadingSuccess());
  }
}

function* handleLoginOkta({ payload }: TAction) {
  const {
    password,
    username,
    setVisibleLoginModal,
    isAutoLogin = false,
  } = payload;
  try {
    yield put(loadingRequest());
    yield call(signIn, { username, password });
    const response: MODEL.IOktaRefreshToken = yield call(refreshTokens);
    yield call(handleAuthenticateOkta, response, username);
  } catch (error) {
    let errorCode = '';
    const netinfo: NetInfoState = yield call(NetInfo.fetch);
    if (!netinfo.isConnected) {
      yield put(
        loginError({ isLoged: false, error: ERROR_CODE_MESSAGE.NO_INTERNET }),
      );
      errorCode = ERROR_CODE_MESSAGE.NO_INTERNET;
    } else if (
      _get(error, ['detail', 'errorCode']) === OKTA_ERROR_CODE.PASSWORD_EXPIRED
    ) {
      yield call(setVisibleLoginModal, false);
      yield setItem(STORAGE_NAME.USERNAME, JSON.stringify(username));
      navigationRef.navigate(
        ROUTE_PATH.CHANGE_PASSWORD as never,
        { preLogin: true } as never,
      );
    } else {
      yield put(
        loginError({ isLoged: false, error: 'invalid_okta', isAutoLogin }),
      );
      errorCode = 'invalid_okta';
    }

    // Adobe Analytic
    errorCode &&
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.LOGIN,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
        error: errorCode,
      });

    yield put(loadingSuccess());
    // yield call(handleCheckLogin, { username, password }, setVisibleLoginModal);
  }
}

function* handleLogin({ payload }: TAction) {
  try {
    yield put(loadingRequest());
    const { password, phoneNumber, accessToken } = payload;

    if (canceler[LOGIN]) {
      canceler[LOGIN]('Cancel Request login');
      _set(canceler, LOGIN, undefined);
    }

    // TODO: format logic code
    const params = {
      clientId: CLIENT_ID,
      username: phoneNumber,
      password,
      deviceId: {
        os: Platform.OS,
        notificationToken: accessToken,
      },
    };
    const responseAuthen: MODEL.IAuthenticate = yield call(
      AuthService.authenticateService,
      params,
      cancelerLogin,
    );

    yield setStorageToken(responseAuthen);
    yield setItem(STORAGE_NAME.USERNAME, JSON.stringify(phoneNumber));

    // Migration account
    if (responseAuthen?.accessToken) {
      //call profile
      const responseProfile: MODEL.IProfileResponse = yield call(
        UserService.getProfileService,
      );
      yield put(saveProfile(responseProfile.data));

      yield setItem(
        STORAGE_NAME.FULL_NAME,
        JSON.stringify(responseProfile?.data?.fullName),
      );
      yield setItem(
        STORAGE_NAME.FIRST_NAME,
        JSON.stringify(responseProfile?.data?.firstName || ''),
      );

      const migrateParser = jwtDecode<MODEL.ITokenDecode>(
        responseAuthen.accessToken,
      );
      const isMigrated = migrateParser?.['x-forceChangeUsername'] === '1';
      const isVerified = migrateParser?.['x-notVerified'] === '0';
      const hasRolePO =
        _intersection(responseProfile.data?.roles, [
          roles?.POLICY_OWNER,
          roles?.V_POLICY_OWNER,
        ]).length > 0;

      // SET ADOBE USER
      NativeModuleClient.AdobeExperience.setUser(migrateParser.sub);
      // yield put(saveNewUsername(responseProfile?.data?.mobile));
      yield put(saveNewUsername(phoneNumber));
      yield put(saveIsMigrated(isMigrated));
      const userNameIsNotPhoneNumber = !REGEX.PHONE_NUMBER.test(
        migrateParser?.['x-username'],
      );

      if (isMigrated && userNameIsNotPhoneNumber) {
        yield all([
          put(loginSuccess(responseAuthen?.accessToken)),
          put(setRefreshTokenError(undefined)),
        ]);
        if (!isEmpty(responseProfile.data.mobile)) {
          navigationRef.navigate(ROUTE_PATH.MIGRATION as never);
        } else {
          navigationRef.navigate(
            ROUTE_PATH.MIGRATION as never,
            { isHasMobile: false } as never,
          );
        }
      } else if (
        isMigrated &&
        !userNameIsNotPhoneNumber &&
        isVerified &&
        hasRolePO
      ) {
        yield all([
          put(loginSuccess(responseAuthen?.accessToken)),
          put(setRefreshTokenError(undefined)),
        ]);
        if (
          !isEmpty(responseProfile.data?.contactId) &&
          !isEmpty(responseProfile.data.mobile)
        ) {
          navigationRef.navigate(
            ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION as never,
          );
        } else {
          navigationRef.navigate(ROUTE_PATH.NO_PHONE_CONTACT as never);
        }
      } else {
        yield put(remindBiometric(true));
        if (!__DEV__) {
          yield call(handleForceVideo, responseProfile.data);
          yield spawn(registerNotiWithCurrentUser, responseAuthen.accessToken!);
        }
        yield all([
          put(loginSuccess(responseAuthen?.accessToken)),
          put(setRefreshTokenError(undefined)),
        ]);
      }
    }
  } catch (error) {
    const errorType = error as APIError;
    yield put(
      loginError({
        isLoged: false,
        error: errorType.errorCode,
        remainingLoginAttempts: _get(
          errorType.errorMessage,
          'remainingLoginAttempts',
          undefined,
        ),
      }),
    );
  } finally {
    yield put(loadingSuccess());
  }
}

function* handleLogoutApi() {
  try {
    yield call(AuthService.logoutService);
  } catch (error) {
    console.error('ERROR: Logout API Fail', error);
  }
}

function* handleLogout({ payload }: TAction) {
  try {
    if (payload.neededCallApi) {
      yield put(logoutRequestApi());
    }

    if (
      navigationRef.canGoBack() &&
      !payload.isChangePassword &&
      !payload.isNotNavigate
    ) {
      const currentRoute = navigationRef.getCurrentRoute();

      if (
        [
          ROUTE_PATH.NO_PHONE_CONTACT,
          ROUTE_PATH.MIGRATION,
          ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION,
        ].includes(currentRoute?.name as keyof TStackParamList)
      ) {
        navigationRef.navigate(ROUTE_PATH.DASHBOARD as never);
      } else {
        navigationRef.goBack();
      }
    }

    if (!__DEV__) {
      yield spawn(function* () {
        yield AzureNotificationClient.clearTagsWithoutGeneral();
      });
    }

    // SET ADOBE USER
    NativeModuleClient.AdobeExperience.setUser(undefined);
    yield all([
      call(clearTokenLogout),
      call(clearVitalityStorage),
      put(saveProfileAction(undefined)),
      put(clearVitalityDataAction()),
    ]);

    if (payload.isChangePassword) {
      setTimeout(() => {
        jsEventEmitter.emit(JS_EVENT_NAMES.CHANGE_PASSWORD_SUCCESS);
      });
    }
  } catch (error) {
    console.error('ERROR: Logout Fail', error);
  } finally {
    yield put(logoutSuccess());
    yield put(savePolicies(undefined));
    yield put(saveNotificationUnRead(undefined));
    yield put(saveTransactionFilter([]));
    yield put(saveMembership(undefined));
    yield put(savePoliciesAck(undefined));
    yield put(savePoliciesIncommingPayment([]));
    yield put(saveSessionIdAction(undefined));
  }
}

function* handleRefreshToken() {
  try {
    yield put(loadingRequest());

    const token: string = yield getItem<string>(
      STORAGE_NAME.VITALITY_LOGIN_TOKEN,
      '',
    );
    const refreshToken: string = yield getItem<string>(
      STORAGE_NAME.REFRESH_TOKEN,
      '',
    );
    const refreshTokenOkta: string = yield getItem<string>(
      STORAGE_NAME.REFRESH_TOKEN_OKTA,
      '',
    );
    const response: MODEL.IAuthenticate = yield call(
      AuthService.refreshTokenService,
      {
        clientId: CLIENT_ID,
        refreshToken,
        refreshTokenOkta,
        clientIdOkta: OKTA_CLIENT_ID,
        deviceId: {
          os: Platform.OS,
          notificationToken: token,
        },
      },
    );

    yield setStorageToken(response);
    yield setVitalityStorageToken({
      expires_in: response.accessTokenOktaExpirySeconds,
      access_token: response.accessTokenOkta,
      refresh_token: response.refreshTokenOkta,
    });

    const migrateParser = jwtDecode<MODEL.ITokenDecode>(response.accessToken!);
    const isMigrated = migrateParser?.['x-forceChangeUsername'] === '1';

    const responseProfile: MODEL.IProfileResponse = yield call(
      UserService.getProfileService,
    );
    yield put(saveProfile(responseProfile.data));

    yield setItem(
      STORAGE_NAME.FULL_NAME,
      JSON.stringify(responseProfile?.data?.fullName),
    );
    yield setItem(
      STORAGE_NAME.FIRST_NAME,
      JSON.stringify(responseProfile?.data?.firstName || ''),
    );

    const isVerified = migrateParser?.['x-notVerified'] === '0';
    const hasRolePO =
      _intersection(responseProfile.data?.roles, [
        roles?.POLICY_OWNER,
        roles?.V_POLICY_OWNER,
      ]).length > 0;

    const userNameIsNotPhoneNumber = !REGEX.PHONE_NUMBER.test(
      migrateParser?.['x-username'],
    );

    if (isMigrated && userNameIsNotPhoneNumber) {
      if (!isEmpty(responseProfile?.data.mobile)) {
        navigationRef.navigate(ROUTE_PATH.MIGRATION as never);
      } else {
        navigationRef.navigate(
          ROUTE_PATH.MIGRATION as never,
          { isHasMobile: false } as never,
        );
      }
    } else if (
      isMigrated &&
      !userNameIsNotPhoneNumber &&
      isVerified &&
      hasRolePO
    ) {
      if (
        !isEmpty(responseProfile?.data?.contactId) &&
        !isEmpty(responseProfile?.data?.mobile)
      ) {
        navigationRef.navigate(ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION as never);
      } else {
        navigationRef.navigate(ROUTE_PATH.NO_PHONE_CONTACT as never);
      }
    } else {
      yield call(handleForceVideo, responseProfile.data);
    }

    yield all([
      put(loginSuccess(response?.accessToken)),
      put(getProfile()),
      put(saveIsMigrated(isMigrated)),
    ]);
    if (!__DEV__) {
      yield spawn(registerNotiWithCurrentUser, response.accessToken!);
    }
  } catch (error) {
    const netinfo: NetInfoState = yield call(NetInfo.fetch);
    const errorType = error as APIError;
    const urlProfile = `${APP_ENV.APPLICATION_API_ONE_CUSTOMER}/user/profile`;
    const tokenCiam = `${APP_ENV.APPLICATION_API_CIAM}/okta/token`;
    const refreshTokenCiam = `${APP_ENV.APPLICATION_API_CIAM}/refreshToken`;

    if (netinfo.isConnected === false) {
      yield put(
        loginError({ isLoged: false, error: ERROR_CODE_MESSAGE.NO_INTERNET }),
      );
      return;
    }

    if (errorType?.code && _values(ERROR_CODE_OKTA).includes(errorType.code)) {
      // Okta refresh token error
      yield put(setRefreshTokenError({ errorCode: 'okta', hasError: true }));
      return;
    }

    if (
      errorType.url &&
      [urlProfile, tokenCiam, refreshTokenCiam].includes(errorType.url)
    ) {
      // Error api
      const hasErrorProfile = errorType.url === urlProfile;
      yield put(
        setRefreshTokenError({
          errorCode: hasErrorProfile ? 'profile' : 'refreshToken',
          hasError: true,
        }),
      );
      return;
    }

    yield put(setRefreshTokenError({ errorCode: 'data', hasError: true }));
  } finally {
    yield put(loadingSuccess());
  }
}

function* authenticateWithPass({ payload }: TAction) {
  try {
    yield put(loadingRequest());
    yield put(putPasswordRequest());

    const { password, phoneNumber } = payload;
    const params = {
      clientId: CLIENT_ID,
      username: phoneNumber,
      password,
    };

    if (canceler[AUTHENTICATE_WITH_PASS]) {
      canceler[AUTHENTICATE_WITH_PASS]('Cancel Request Verify Password');
      _set(canceler, AUTHENTICATE_WITH_PASS, undefined);
    }

    if (OKTA_ENABLE) {
      yield call(signIn, { username: phoneNumber, password });
      const responseOkta: MODEL.IOktaRefreshToken = yield call(refreshTokens);
      yield setVitalityStorageToken(responseOkta);
      yield setStorageToken({
        accessToken: responseOkta.access_token,
        refreshToken: responseOkta.refresh_token,
      });

      const response: MODEL.IAuthenticate = yield call(
        AuthService.authenticateOktaService,
        {
          clientId: CLIENT_ID,
          refreshTokenOkta: responseOkta.refresh_token,
          deviceId: {
            os: Platform.OS,
            notificationToken: responseOkta.access_token,
          },
        },
        cancelerVerifyPass,
      );
      yield setStorageToken(response);
    } else {
      const response: MODEL.IAuthenticate = yield call(
        AuthService.authenticateService,
        params,
        cancelerVerifyPass,
      );
      yield setStorageToken(response);
    }

    yield put(putPasswordSuccess());
  } catch (error) {
    yield put(putPasswordError());
  } finally {
    yield put(loadingSuccess());
  }
}

function* handleSessionExpired() {
  try {
    // yield put(logoutRequestApi());

    if (navigationRef.canGoBack()) {
      navigationRef.dispatch(StackActions.popToTop());
    }

    yield all([
      put(saveProfileAction(undefined)),
      put(clearVitalityDataAction()),
      put(saveNotificationUnRead(undefined)),
    ]);
  } catch (error) {
    console.error('ERROR: Session Expired Fail', error);
  } finally {
    yield put(logoutSuccess());
    yield put(savePolicies(undefined));
    yield put(saveNotificationUnRead(undefined));
    yield put(saveTransactionFilter([]));
    yield put(saveMembership(undefined));
    yield put(savePoliciesAck(undefined));
    yield put(savePoliciesIncommingPayment([]));
    yield put(saveSessionIdAction(undefined));
  }
}

function* authenSaga() {
  yield takeLatest(LOGIN, handleLogin);
  yield takeLatest(LOGIN_OKTA, handleLoginOkta);
  yield takeLatest(LOGOUT, handleLogout);
  yield takeLatest(LOGOUT_API, handleLogoutApi);
  yield takeLatest(REFRESH_TOKEN, handleRefreshToken);
  yield takeLatest(AUTHENTICATE_WITH_PASS, authenticateWithPass);
  yield takeLatest(SESSION_EXPIRED, handleSessionExpired);
}

export default authenSaga;
