import React from 'react';
import {
  Platform,
  TextStyle,
  TouchableOpacity,
  ViewStyle,
  View,
  Image,
} from 'react-native';
import {
  Icon,
  useTheme,
  NativeModuleClient,
  IconPropsType,
} from 'superapp-common-component';
import {useSelector} from 'react-redux';
import {
  makeSelectActionsList,
  makeSelectLoadingSkeleton,
} from '@/stores/global/selectors';
import _find from 'lodash/find';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import {useNavigation} from '@react-navigation/native';
import {StackNavigationProp} from '@react-navigation/stack';
import dayjs from 'dayjs';
import Animated, {
  SharedValue,
  interpolate,
  interpolateColor,
  useAnimatedStyle,
} from 'react-native-reanimated';

import {Theme} from '@/config/themes.config';
import {a11y, isEmpty} from '@/utils/utils';
import {
  AML_TYPE_REQUEST,
  APPS_NAME,
  FUNCTION_TYPES,
  TYPE_NAVIGATION_POS_REQUEST,
  VALID_ROLES,
} from '@/utils/constants';
import {TStackParamList} from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import {ModalPermissionRef} from '@/components/ModalPermission';
import APP_ENV from '@/config/app.config';

import {SCROLL_TARGET} from '../../styles';
import useStyles, {HEIGHT_FULL_FUNCBAR} from './styles';
import {
  HEIGHT_SLIDER,
  HEIGHT_HEADER,
  POSITION_TOP_SLIDER,
} from '../Slider/styles';
import {Language, TElementItem, TNewActionItem} from '../types';

type Nullable<T> = T | null;

const AnimatedIcon = Animated.createAnimatedComponent(Icon);
const AnimatedImage = Animated.createAnimatedComponent(Image);
type FunctionBarNavigationProp = StackNavigationProp<TStackParamList, 'Home'>;
interface FunctionBarProps {
  scrollAnimation: SharedValue<number>;
  currentLang: Language;
  isConnected: boolean;
  isLoged: boolean;
  setLoadingOpenMiniApp: (value: boolean) => void;
  isRunningMiniApp: React.MutableRefObject<boolean>;
  permissionModalRef?: React.MutableRefObject<
    ModalPermissionRef | null | undefined
  >;
  onRequestSubmitClaim: () => void;
  checkPermissionAML: (type: string, callback: () => void) => void;
}

const POSITION_WITH_BANNER = 8;
const TYPE_OF_FUNCTION_BAR = {
  PAY: 'pay-now',
  POS_REQUEST: 'pos-request',
  SUBMIT_CLAIM: 'submit-claim',
  E_CARD: 'e-card',
  HISTORY: 'history',
};

interface IFunctionBarColor {
  history: string;
  'submit-claim': string;
  'pos-request': string;
  'e-card': string;
}

const FUNCTION_BAR_COLOR = {
  history: '#4C4794',
  'submit-claim': '#1F78AD',
  'pos-request': '#DE3200',
  'e-card': '#3DA758',
  // "pay-now": 'black'
};

const FunctionBar: React.FC<FunctionBarProps> = ({
  scrollAnimation,
  currentLang,
  isConnected,
  setLoadingOpenMiniApp,
  isRunningMiniApp,
  permissionModalRef,
  onRequestSubmitClaim,
  checkPermissionAML,
}) => {
  const navigation = useNavigation<FunctionBarNavigationProp>();
  const theme = useTheme() as Theme;
  const {colors} = theme;
  const styles = useStyles(theme);
  const actionsList = useSelector(makeSelectActionsList());
  const isLoading = useSelector(makeSelectLoadingSkeleton());

  const styleTransfromFunction = useAnimatedStyle(() => {
    const styleAndroid =
      Platform.OS === 'android'
        ? {
            borderWidth: interpolate(
              scrollAnimation.value,
              [0, 1],
              [1, -1],
              'clamp',
            ),
          }
        : {};

    return {
      ...styleAndroid,
      height: interpolate(
        scrollAnimation.value,
        [0, SCROLL_TARGET - 10],
        [HEIGHT_FULL_FUNCBAR, HEIGHT_HEADER],
        'clamp',
      ),
      transform: [
        {
          translateY: interpolate(
            scrollAnimation.value,
            [0, SCROLL_TARGET],
            [
              HEIGHT_HEADER +
                HEIGHT_SLIDER +
                POSITION_TOP_SLIDER -
                POSITION_WITH_BANNER,
              0,
            ],
            'clamp',
          ),
        },
      ],
    };
  }, [scrollAnimation]);

  const styleMarkBackground = useAnimatedStyle(
    () => ({
      opacity: interpolate(
        scrollAnimation.value,
        [0, SCROLL_TARGET / 2],
        [1, 0],
        'clamp',
      ),
    }),
    [scrollAnimation],
  );

  const styleIcon = useAnimatedStyle(
    () => ({
      transform: [
        {
          scale: interpolate(
            scrollAnimation.value,
            [0, SCROLL_TARGET],
            [1, 1.33],
            'clamp',
          ),
        },
      ],
    }),
    [scrollAnimation],
  );

  const styleCircle = useAnimatedStyle(
    () => ({
      backgroundColor: interpolateColor(
        scrollAnimation.value,
        [0, SCROLL_TARGET - 20, SCROLL_TARGET - 20],
        ['#ECF5FA', colors.white, 'transparent'],
      ),
    }),
    [scrollAnimation],
  );

  const styleText = useAnimatedStyle(
    () => ({
      marginTop: interpolate(
        scrollAnimation.value,
        [0, SCROLL_TARGET],
        [0, -30],
        'clamp',
      ),
      transform: [
        {
          scale: interpolate(
            scrollAnimation.value,
            [0, SCROLL_TARGET],
            [1, 0],
            'clamp',
          ),
        },
      ],
      opacity: interpolate(
        scrollAnimation.value,
        [0, HEIGHT_SLIDER],
        [1, 0],
        'clamp',
      ),
    }),
    [scrollAnimation],
  );

  const styleAnimationWrapperAction = useAnimatedStyle(
    () => ({
      transform: [
        {
          translateY: interpolate(
            scrollAnimation.value,
            [0, SCROLL_TARGET],
            [0, 16],
            'clamp',
          ),
        },
      ],
    }),
    [scrollAnimation],
  );

  const openMiniApp = async (
    appName: string,
    additionProps: Nullable<string>,
  ) => {
    try {
      setLoadingOpenMiniApp(true);
      await NativeModuleClient.MiniApp.runCheckUpdate(appName);
      await NativeModuleClient.MiniApp.loadApp(appName, additionProps);
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', appName);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const handlePermission = (functionType: keyof typeof VALID_ROLES) => {
    if (permissionModalRef?.current?.handlePermission(functionType)) {
      throw new Error('permission invalid');
    }
  };

  const checkActionForFuctionBar = async (id: string) => {
    try {
      switch (id) {
        case TYPE_OF_FUNCTION_BAR.SUBMIT_CLAIM:
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.HOME,
            unixTimestamp: dayjs().unix(),
            buttonText: 'Submit Claim',
          });
          handlePermission(FUNCTION_TYPES.CLAIM);
          handlePermission(FUNCTION_TYPES.POS_REQUEST);
          await checkPermissionAML(AML_TYPE_REQUEST.CLAIM, () =>
            onRequestSubmitClaim(),
          );
          // return openMiniApp(APPS_NAME.CLAIM, null);
          break;
        case TYPE_OF_FUNCTION_BAR.POS_REQUEST:
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.HOME,
            unixTimestamp: dayjs().unix(),
            buttonText: 'Send Request',
          });
          handlePermission(FUNCTION_TYPES.POS_REQUEST);
          await checkPermissionAML(AML_TYPE_REQUEST.POS_REQUEST_LANDING, () =>
            openMiniApp(
              APPS_NAME.POS_REQUEST,
              JSON.stringify({type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE}),
            ),
          );
          break;
        // return openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE }));
        case TYPE_OF_FUNCTION_BAR.HISTORY:
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.HOME,
            unixTimestamp: dayjs().unix(),
            buttonText: 'History',
          });
          handlePermission(FUNCTION_TYPES.HISTORY);
          navigation.navigate(ROUTE_PATH.TRANSACTION_HISTORY);
          break;
        case TYPE_OF_FUNCTION_BAR.E_CARD:
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.HOME,
            unixTimestamp: dayjs().unix(),
            buttonText: 'E-Card',
          });
          handlePermission(FUNCTION_TYPES.E_CARD);
          navigation.navigate(ROUTE_PATH.ECARD);
          break;
        default:
          break;
      }
    } catch (error) {}
  };

  const formatEachAction = (items: [TElementItem]) => {
    const title = _find(
      items,
      (element: TElementItem) => element?.name === 'name',
    );
    const isHidden = _find(
      items,
      (element: TElementItem) => element?.name === 'hide',
    );
    const order = _find(
      items,
      (element: TElementItem) => element?.name === 'order',
    );
    const iconName = _find(
      items,
      (element: TElementItem) => element?.name === 'iconName',
    );
    const id = _find(items, (element: TElementItem) => element?.name === 'id');
    const imageUrl = _find(
      items,
      (element: TElementItem) => element?.name === 'image',
    );
    return {
      title,
      isHidden,
      order,
      id,
      iconName,
      imageUrl,
    };
  };

  const formatActionsListData = () => {
    let formatedData: any = [];

    if (actionsList) {
      actionsList.items[currentLang].items.map(
        (item: {title: string; elements: [{name: string; value: any}]}) => {
          const {elements} = item;
          const {title, isHidden, order, id, iconName, imageUrl} =
            formatEachAction(elements);
          formatedData.push({title, isHidden, order, id, iconName, imageUrl});
        },
      );
    }
    return formatedData;
  };

  const renderSkeletionProduct = () => {
    return (
      <SkeletonPlaceholder>
        <View style={styles.skeletonWrapper}>
          <View style={styles.NewsItemSkeleton}>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
          </View>
        </View>
      </SkeletonPlaceholder>
    );
  };

  const renderActionsList = () => {
    const listActionsFormated = formatActionsListData();
    if (listActionsFormated) {
      return listActionsFormated.map((item: TNewActionItem, index: number) => {
        const {title, isHidden, id, iconName, imageUrl} = item;
        const iconColor =
          FUNCTION_BAR_COLOR[id.value as keyof IFunctionBarColor];
        return (
          !isHidden.value && (
            <TouchableOpacity
              onPress={() => checkActionForFuctionBar(id?.value)}
              key={id.value}
              style={styles.buttonFunction}
              {...a11y(`Home.btnAction.{${index}}`)}>
              <Animated.View style={[styles.circleIcon, styleCircle]}>
                <AnimatedIcon
                  size={24}
                  style={[
                    styleIcon,
                    {
                      color: interpolateColor(
                        scrollAnimation.value,
                        [0, SCROLL_TARGET],
                        [iconColor, colors.white],
                      ),
                    },
                  ]}
                  name={iconName?.value}
                />
                {/* <AnimatedImage source={{ uri: `${APP_ENV.CONTENT_AEM}${imageUrl?.value}` }} style={{ width: 26, height: 26}} /> */}
              </Animated.View>
              <Animated.Text
                style={[styleText, styles.text]}
                allowFontScaling={false}>
                {title.value}
              </Animated.Text>
            </TouchableOpacity>
          )
        );
      });
    }
  };

  if (!isConnected && isEmpty(actionsList)) return null;

  return isLoading && !actionsList ? (
    renderSkeletionProduct()
  ) : (
    <>
      <Animated.View style={[styles.contentFunction, styleTransfromFunction]}>
        <>
          <Animated.View
            style={[styles.wrapperFunction, styleMarkBackground]}
          />
          <Animated.View
            style={[styles.contentPrimary, styleAnimationWrapperAction]}>
            {renderActionsList()}
          </Animated.View>
        </>
      </Animated.View>
    </>
  );
};

export default FunctionBar;
