import React, { useCallback, useEffect, useRef, useState } from 'react';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { View, StatusBar, SafeAreaView } from 'react-native';
import { useTheme, NativeModuleClient, Snackbar } from 'superapp-common-component';
import { StackNavigationProp, StackScreenProps } from '@react-navigation/stack';
import { useFocusEffect, useNavigation, useRoute } from '@react-navigation/native';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import _get from 'lodash/get';
import TouchId from 'react-native-touch-id-passcode';
import dayjs from 'dayjs';

import { TStackParamList } from '@/navigators';
import HomeScreen from '@/pages/Home';
import ProfileScreen from '@/pages/Profile';
import ELetter from '@/pages/ELetter';
import Notifications from '@/pages/Notifications';
import ActiveBioModal, { ActiveBioModalRef } from '@/components/ActiveBioModal';
import VideoUrl, { TVideoUrlRef } from '@/components/Video';

import { getItem, setItem } from '@/utils/storage';
import { BIOMETRY_TYPE, MAX_REMIND_BIOMETRIC, STORAGE_NAME } from '@/utils/constants';
import { makeSelectCheckLogin, makeSelectIsMigrated, makeSelectIsRemindBiometric } from '@/stores/authentication/selectors';
import LoginModal, { LoginModalRef } from '@/pages/Login';
import { Theme } from '@/config/themes.config';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import ModalIntroduction, { useModalIntroduction } from '@/components/ModalIntroduction';
import { makeSelectIntroduction, makeSelectProfile } from '@/stores/global/selectors';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';
import { TabIcon, TabIconELetter } from './components/TabIcon';
import { NS_NOTIFICATION } from '../Notifications/store/constants';
import notificationSaga from '../Notifications/store/saga';
import notificationReducer from '../Notifications/store/reducer';
import { getNotificationUnRead } from '../Notifications/store/actions';
import { makeSelectNotificationUnRead } from '../Notifications/store/selectors';
import { remindBiometric } from '@/stores/authentication/actions';

const Tab = createBottomTabNavigator();

type DashboardNavigationProp = StackNavigationProp<TStackParamList, 'Dashboard'>;
type DashboardNativeStackProps = StackScreenProps<TStackParamList, 'Dashboard'>;
type DashboardRouteProps = DashboardNativeStackProps['route'];

const Dashboard = () => {
  const { t } = useTranslation();
  useInjectSaga({ key: NS_NOTIFICATION, saga: notificationSaga });
  useInjectReducer({ key: NS_NOTIFICATION, reducer: notificationReducer });
  const navigation = useNavigation<DashboardNavigationProp>();
  const loginStatus = useSelector(makeSelectCheckLogin());
  const isShowModalFirstTime = useRef<boolean>(false);
  const loginModalRef = useRef<null | LoginModalRef>();
  const route = useRoute<DashboardRouteProps>();
  const { colors } = useTheme() as Theme;
  const dispatch = useDispatch();
  const notificationUnRead = useSelector(makeSelectNotificationUnRead());
  const [modalIntroductionRef, introductionComponent] = useModalIntroduction();
  const introduction = useSelector(makeSelectIntroduction());
  const isMigrated = useSelector(makeSelectIsMigrated());
  const isRemindBiometric = useSelector(makeSelectIsRemindBiometric());
  const profile = useSelector(makeSelectProfile());
  const activeBioModalRef = useRef<null | ActiveBioModalRef>(null);
  const [biometryType, setBiometryType] = useState<null | string>(null);
  const videoRef = useRef<TVideoUrlRef>(null);
  const [isSuccessResetPass, setSuccessResetPass] = useState(false);

  const onMounting = async () => {
    if (biometryType === null) {
      (async () => {
        try {
          const biometryTypeRes = await TouchId.isSupported();
          setBiometryType(biometryTypeRes);
        } catch (error) {
          setBiometryType(null);
        }
      })();
    }
    const [isHasBio, pincode] = await Promise.all([
      getItem<boolean>(STORAGE_NAME.TOUCH_FACE_ID, false),
      getItem<string>(STORAGE_NAME.PIN_CODE, null),
    ]);

    if (isShowModalFirstTime.current && loginStatus.isLoged) return;
    if (isHasBio || pincode) {
      isShowModalFirstTime.current = true;
      loginModalRef.current?.setShowModal(true);
    }
  };

  const showPasswordModal = () => {
    navigation.navigate(ROUTE_PATH.VERIFY_PASSWORD, {
      title:
        biometryType === BIOMETRY_TYPE.FACE
          ? t('profile.input.password.modal.name.faceid')
          : t('profile.input.password.modal.name.touchid'),
      messageContent:
        biometryType === BIOMETRY_TYPE.FACE
          ? t('profile.input.password.modal.sub.title.faceid')
          : t('profile.input.password.modal.sub.title.touchid'),
      toActiveFaceId: true,
    });
  };

  const handleRemindSetupBiometric = async () => {
    const [isHasBio, intro, remainRemind] = await Promise.all([
      getItem<boolean>(STORAGE_NAME.TOUCH_FACE_ID, false),
      getItem<MODEL.IIntroduction>(STORAGE_NAME.INTRODUCTION, null),
      getItem<number | null>(STORAGE_NAME.REMIND_BIOMETRIC, null),
    ]);

    const notConfirmEtrans = loginStatus.isLoged && profile?.isOT !== 0;
    const showingIntroduction =
      introduction && introduction.lastModifiedDate !== intro?.lastModifiedDate && !_get(introduction, ['vi', 'hide']);

    if (remainRemind === null) {
      await setItem(STORAGE_NAME.REMIND_BIOMETRIC, JSON.stringify(MAX_REMIND_BIOMETRIC));
      return;
    }

    if (showingIntroduction || remainRemind === 0) return;

    if (biometryType && !isHasBio && notConfirmEtrans && remainRemind !== 0) {
      setTimeout(async () => {
        await setItem(STORAGE_NAME.REMIND_BIOMETRIC, JSON.stringify(remainRemind - 1));
        activeBioModalRef.current?.open();
      }, 400);
    }
  };

  useEffect(() => {
    if (route.params?.openModalLogin) {
      loginModalRef.current?.setShowModal(true);
      navigation.setParams({ openModalLogin: false });
    }
  }, [route.params?.openModalLogin]);

  // Noti reset password success
  useEffect(() => {
    if (loginStatus.isLoged && route.params?.isResetPassSuccess) {
      setSuccessResetPass(true);
      navigation.setParams({ isResetPassSuccess: false });
      setTimeout(() => {
        setSuccessResetPass(false);
      }, 4000);
    }

    if (!!loginStatus.error && loginStatus.isAutoLogin) {
      navigation.setParams({ isResetPassSuccess: false });
      setSuccessResetPass(false);
    }
  }, [loginStatus, route.params?.isResetPassSuccess]);

  useEffect(() => {
    if (loginStatus.isLoged && biometryType && isMigrated === false && profile && introduction && isRemindBiometric) {
      dispatch(remindBiometric(false));
      handleRemindSetupBiometric();
    }
  }, [loginStatus, introduction, profile, biometryType, isMigrated, isRemindBiometric]);

  useEffect(() => {
    if (loginStatus.isLoged) {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'Postlogin_Home',
        unixTimestamp: dayjs().unix(),
      });
      dispatch(getNotificationUnRead());
    } else {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'Prelogin_Home',
        unixTimestamp: dayjs().unix(),
      });
    }
  }, [loginStatus]);

  useEffect(() => {
    onMounting();
  }, []);

  useFocusEffect(
    useCallback(() => {
      (async () => {
        const [isHasBio, pincode, lang, intro] = await Promise.all([
          getItem<boolean>(STORAGE_NAME.TOUCH_FACE_ID, false),
          getItem<string>(STORAGE_NAME.PIN_CODE, null),
          getItem<string>(STORAGE_NAME.LANGUAGE, 'vi'),
          getItem<MODEL.IIntroduction>(STORAGE_NAME.INTRODUCTION, null),
        ]);

        if (!_get(introduction, [lang, 'value'])) return;
        const notConfirmEtrans = loginStatus.isLoged && isMigrated === false && profile?.isOT !== 0;

        if ((!isHasBio && !pincode && !loginStatus.isLoged) || notConfirmEtrans) {
          if (introduction && introduction.lastModifiedDate !== intro?.lastModifiedDate && !_get(introduction, [lang, 'hide'])) {
            await setItem(STORAGE_NAME.INTRODUCTION, JSON.stringify({ lastModifiedDate: introduction.lastModifiedDate }));
            introductionComponent().open();
          }
        }
      })();
    }, [introduction, loginStatus, profile, biometryType]),
  );

  return (
    <View style={styles.container}>
      <SafeAreaView style={styles.safeAreaHeader}>
        <StatusBar barStyle="light-content" />
      </SafeAreaView>

      <VideoUrl ref={videoRef} />
      <LoginModal ref={loginModalRef} />
      <ModalIntroduction ref={modalIntroductionRef} />
      <ActiveBioModal showPasswordModal={showPasswordModal} ref={activeBioModalRef} biometryType={biometryType} onMode={true} />
      {isSuccessResetPass && (
        <View style={styles.snackBarContainer}>
          <Snackbar
            snackBarStyle={{ backgroundColor: '#333D47' }}
            messageTextStyle={{ color: '#FFFFFF' }}
            visible={isSuccessResetPass}
            setVisible={setSuccessResetPass}
            messageText={t('forgot.password.resetPass.success')}
          />
        </View>
      )}
      <Tab.Navigator
        initialRouteName={ROUTE_PATH.HOME}
        screenOptions={{
          tabBarActiveTintColor: colors.primary,
          tabBarStyle: styles.shadowBottomBar,
          tabBarLabelStyle: styles.tabBarLabelStyle,
        }}
      >
        <Tab.Screen
          name={ROUTE_PATH.HOME}
          component={HomeScreen}
          options={{
            headerShown: false,
            title: '',
            tabBarLabel: t('home.name'),
            tabBarIcon: props => <TabIcon iconName={props.focused ? 'Home-On' : 'Home'} {...props} />,
          }}
        />
        <Tab.Screen
          name={ROUTE_PATH.NOTIFICATIONS}
          component={Notifications as never}
          options={{
            title: '',
            tabBarLabel: t('home.notifications.title.header'),
            tabBarIcon: props => {
              return (
                <View style={styles.wrapperIcon}>
                  {notificationUnRead && notificationUnRead?.unread !== 0 && (
                    <View style={styles.markBagde}>
                      <View style={styles.bagde} />
                    </View>
                  )}
                  <TabIcon iconName={props.focused ? 'Notification' : 'Notification1'} {...props} />
                </View>
              );
            },
          }}
        />
        <Tab.Screen
          name={ROUTE_PATH.ELETTER}
          component={ELetter as never}
          options={{
            title: '',
            tabBarLabel: t('home.letter.header.inbox'),
            tabBarIcon: props => <TabIconELetter iconName={props.focused ? 'Email' : 'Email1'} {...props} hasEletterUnRead={false} />,
          }}
        />
        <Tab.Screen
          name={ROUTE_PATH.PROFILE}
          component={ProfileScreen}
          options={{
            title: '',
            tabBarLabel: t('profile.header.account'),
            tabBarIcon: props => <TabIcon iconName={props.focused ? 'General-User-1' : 'General-User'} {...props} />,
          }}
        />
      </Tab.Navigator>
    </View>
  );
};

export default Dashboard;
