import React, { useLayoutEffect, useRef, useState } from 'react';
import { Image, TouchableOpacity, View } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { useTranslation } from 'react-i18next';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { Icon, Loading, NativeModuleClient } from 'superapp-common-component';
import { utils } from 'superapp-common-component';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import WebView, { TWebViewUrlRef } from '@/components/WebView';
import ROUTE_PATH from '@/navigators/routePath';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { APPS_NAME, NAVIGATION_VITALITY } from '@/utils/constants';
import { hasMaintenanceVitality } from '@/utils/utils';
import ModalConfirmError, {
  ModalConfirmErrorRef,
} from '@/components/ModalConfirmError';

import styles from './styles';

const { a11y } = utils;
type FAQNavigationProp = StackNavigationProp<TStackParamList, 'FAQ'>;

const FAQ = () => {
  const { t } = useTranslation();
  const webViewRef = useRef<TWebViewUrlRef>(null);
  const navigation = useNavigation<FAQNavigationProp>();
  const [isLoadingOpenApp, setLoadingOpenMiniApp] = useState<boolean>(false);
  const modalMaintainRef = useRef<ModalConfirmErrorRef>();
  const [isMaintenance, setMaintenance] = useState(false);

  const onPressFAQPolicy = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FAQ,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Policy',
    });
    webViewRef.current?.openURL(t('common.faq.url'));
  };

  const onPressFAQVitality = async () => {
    try {
      setLoadingOpenMiniApp(true);
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.FAQ,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Vitality',
      });

      const hasMaintenance = await hasMaintenanceVitality();
      if (hasMaintenance) {
        setMaintenance(true);
        throw new Error('app maintenance');
      }

      await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.VITALITY);
      await NativeModuleClient.MiniApp.loadApp(
        APPS_NAME.VITALITY,
        JSON.stringify({
          initialMainNavigationRouteName: NAVIGATION_VITALITY.FAQ,
        }),
      );
    } catch (error) {
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const onDismiss = async () => {
    if (isMaintenance) {
      modalMaintainRef.current?.open();
    }
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text style={HEADER_TITLE}>{t('profile.help.center.faq')}</Text>
      ),
      headerLeft: () => (
        <TouchableOpacity
          style={styles.marginLeft16}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
          {...a11y('FAQs.iconBackHeaderLeft')}>
          <Icon name="Arrow-Left" size={24} color="#fff" />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <View style={styles.container}>
      <Loading visible={isLoadingOpenApp} dismissAction={onDismiss} />
      <View style={styles.content}>
        <TouchableOpacity onPress={onPressFAQPolicy}>
          <View style={styles.item}>
            <Image
              style={styles.image}
              source={require('@/assets/images/policy.png')}
            />
            <View style={styles.contentText}>
              <Text style={styles.textApp}>{t('home.miniApp.policy')}</Text>
              <Text style={styles.textDescription}>
                {t('profile.help.center.faq.description')}
              </Text>
            </View>
            <Icon name="Arrow-Right" size={16} color="#5B636B" />
          </View>
        </TouchableOpacity>
        <TouchableOpacity onPress={onPressFAQVitality}>
          <View style={styles.item}>
            <Image
              style={styles.image}
              source={require('@/assets/images/vitality.png')}
            />
            <View style={styles.contentText}>
              <Text style={styles.textApp}>{t('home.miniApp.vitality')}</Text>
              <Text style={styles.textDescription}>
                {t('profile.help.center.faq.description')}
              </Text>
            </View>
            <Icon name="Arrow-Right" size={16} color="#5B636B" />
          </View>
        </TouchableOpacity>
      </View>
      <WebView ref={webViewRef} />
      <ModalConfirmError
        ref={modalMaintainRef}
        title={t('common.vitality.maintenance.title')}
        subtitle={t('common.vitality.maintenance.description')}
        btnText={t('common.vitality.maintenance.close')}
        onConfirm={() => setMaintenance(false)}
      />
    </View>
  );
};

export default FAQ;
