import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class OCRService extends Request {
  public constructor() {
    super(APP_ENV.ROOT_API);
    // add for test
  }

  public submitVerifyOCR = (params: any) => {
    let bodyFormData = new FormData();
    bodyFormData.append('face', params.face);
    bodyFormData.append('image_base64', params.image_base64);
    return this.instance.post<any>('/ai/ocr-idr', bodyFormData, {
      headers: { 'Content-Type': 'application/x-www-form' },
    });
  };
}

export default new OCRService();
