import { TAction } from '@/stores/types';

import {
  GET_PROFILE,
  SAVE_PROFILE,
  LOADING_REQUEST,
  LOADING_SUCCESS,
  GET_HOME_DATA,
  SAVE_HOME_DATA,
  SAVE_POLICIES,
  GET_POLICIES_DATA,
  LOADING_HOME_DATA_REQUEST,
  LOADING_HOME_DATA_SUCCESS,
  LOADING_POLICY_REQUEST,
  LOADING_POLICY_REQUEST_SUCCESS,
  SAVE_PRODUCT_IMAGE,
  PULL_TO_REFRESH_HOME,
  PULL_TO_REFRESH_PROFILE,
  SAVE_POLICIES_ACK,
  LOADING_POLICY_ACK_REQUEST,
  LOADING_POLICY_ACK_REQUEST_SUCCESS,
  GET_POLICIES_ACK,
  SAVE_POLICIES_INCOMMING_PAYMENT,
  ENCRYPT_PAYMENT_DATA,
  GET_CLAIM_DRAFT_INFO,
  SAVE_CLAIM_DRAFT_INFO,
  RESET_CLAIM_DRAFT_INFO,
  GET_ACTION_LINKS_PROFILE,
  SAVE_ACTION_LINKS_PROFILE,
  CHECK_AML,
  SAVE_HELP_CENTER_PROFILE,
  ENCRYPT_PAYMENT_MEMBERSHIP_DATA,
  GET_MEMBERSHIP,
  SAVE_MEMBERSHIP,
  LOG_ACTION,
  GET_TIER_VIP,
  SAVE_TIER_VIP,
  USER_TRACKING,
} from './constants';

type THomeDataPayload = {
  productsList?: MODEL.IProducts;
  newsList?: MODEL.IProducts;
  quickActions: MODEL.IProducts;
  featureList: MODEL.IProducts;
  banners: MODEL.IBanner;
  introduction: MODEL.IIntroduction;
};

export const getProfile = (): TAction => ({ type: GET_PROFILE });
export const saveProfile = (payload: MODEL.IProfile | undefined): TAction => ({ type: SAVE_PROFILE, payload });

export const getActionLinks = (): TAction => ({ type: GET_ACTION_LINKS_PROFILE });
export const saveActionLinks = (payload: {
  faq: MODEL.IActionLinks;
  term: MODEL.IActionLinks;
  termVitality: MODEL.IActionLinks;
  personalData: MODEL.IActionLinks;
  privacy: MODEL.IActionLinks;
}): TAction => ({ type: SAVE_ACTION_LINKS_PROFILE, payload });

export const saveHelpCenter = (payload: { vi: MODEL.IHelpCenter[]; en: MODEL.IHelpCenter[] }): TAction => ({
  type: SAVE_HELP_CENTER_PROFILE,
  payload,
});

export const getPolicies = (): TAction => ({ type: GET_POLICIES_DATA });
export const savePolicies = (payload: [MODEL.IPolicy] | undefined): TAction => ({ type: SAVE_POLICIES, payload });

export const loadingSuccess = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequest = (): TAction => ({ type: LOADING_REQUEST });

export const loadingHomeData = (): TAction => ({ type: LOADING_HOME_DATA_REQUEST });
export const loadingHomeDataSuccess = (): TAction => ({ type: LOADING_HOME_DATA_SUCCESS });

export const loadingPolicyRequest = (): TAction => ({ type: LOADING_POLICY_REQUEST });
export const loadingPolicyRequestSuccess = (): TAction => ({ type: LOADING_POLICY_REQUEST_SUCCESS });

export const getHomeData = (): TAction => ({ type: GET_HOME_DATA });
export const saveHomeData = (payload?: THomeDataPayload): TAction => ({ type: SAVE_HOME_DATA, payload });
export const saveProductImage = (payload?: MODEL.IProductImageList): TAction => ({ type: SAVE_PRODUCT_IMAGE, payload });
export const loadingPolicyAckRequest = (): TAction => ({ type: LOADING_POLICY_ACK_REQUEST });
export const loadingPolicyAckRequestSuccess = (): TAction => ({ type: LOADING_POLICY_ACK_REQUEST_SUCCESS });
export const getPoliciesAck = (): TAction => ({ type: GET_POLICIES_ACK });
export const savePoliciesAck = (payload: [MODEL.IPolicy] | undefined): TAction => ({ type: SAVE_POLICIES_ACK, payload });
export const savePoliciesIncommingPayment = (payload: MODEL.IPolicy[]): TAction => ({ type: SAVE_POLICIES_INCOMMING_PAYMENT, payload });
export const pullToRefreshHome = (resolve: () => void, reject: (value: string) => void): TAction => ({
  type: PULL_TO_REFRESH_HOME,
  resolve,
  reject,
});
export const pullToRefreshProfile = (resolve: () => void, reject: (value: string) => void): TAction => ({
  type: PULL_TO_REFRESH_PROFILE,
  resolve,
  reject,
});
export const encryptPaymentData = (payload: MODEL.IPaymentData): TAction => ({ type: ENCRYPT_PAYMENT_DATA, payload });

export const encryptPaymentMembershipData = (payload: MODEL.IPaymentMembershipData): TAction => ({
  type: ENCRYPT_PAYMENT_MEMBERSHIP_DATA,
  payload,
});

export const getClaimDraftInfo = (payload: { callback: () => void }, resolve: any): TAction => ({
  type: GET_CLAIM_DRAFT_INFO,
  payload,
  resolve,
});

export function asyncGetClaimDraftInfo(dispatch: any) {
  return function returnAsync(payload: any) {
    return new Promise(resolve => dispatch(getClaimDraftInfo(payload, resolve)));
  };
}

export const saveDraftInfo = (payload: MODEL.IDraftInfo): TAction => ({ type: SAVE_CLAIM_DRAFT_INFO, payload });
export const resetDraftInfo = (payload: any, resolve: any): TAction => ({ type: RESET_CLAIM_DRAFT_INFO, payload, resolve });

export function asyncResetClaimDraftInfo(dispatch: any) {
  return function returnAsync(payload: any) {
    return new Promise(resolve => dispatch(resetDraftInfo(payload, resolve)));
  };
}

export const checkAMLAction = (payload: any, resolve: any): TAction => ({ type: CHECK_AML, payload, resolve });

export function asyncCheckAMLAction(dispatch: any) {
  return function returnAsync(payload: any) {
    return new Promise<string>(resolve => dispatch(checkAMLAction(payload, resolve)));
  };
}

export const getMembership = (): TAction => ({ type: GET_MEMBERSHIP });
export const saveMembership = (payload?: MODEL.IMembership): TAction => ({ type: SAVE_MEMBERSHIP, payload });

export const logAction = (payload?: string): TAction => ({ type: LOG_ACTION, payload });

export const userTracking = (payload: MODEL.IUserTracking): TAction => ({ type: USER_TRACKING, payload });

export const getTierVip = (): TAction => ({ type: GET_TIER_VIP });
export const saveTierVip = (payload?: MODEL.IConfigTierVip): TAction => ({ type: SAVE_TIER_VIP, payload });
