import RequestITunes from '@/utils/itunesRequest';
import APP_ENV from '@/config/app.config';

class ITunesService extends RequestITunes {
  public constructor() {
    super(APP_ENV.ITUNES_URL);
  }

  public lookupService = (params: { bundleId: string; country: string }): Promise<MODEL.ILookupItunes>  => this.instance.get('/lookup', { params });
}

export default new ITunesService();
