import { StyleSheet, Dimensions, PixelRatio } from 'react-native';
import { Theme } from '@/config/themes.config';

// Constant height, width
const { width, height } = Dimensions.get('window');
const DOT_SIZE = 8;
const HEIGHT_HEADER = 56;
const MARGIN_HORIZONTAL = 8;
const POSITION_TOP_SLIDER = -16;
const POSITION_BOTTOM_DOT = 16;
const DEFAULT_PERCENT_TABLET = 22;
const MAX_HEIGHT_IMAGE_BANNER = 160;
const TOTAL_MARGIN = MARGIN_HORIZONTAL * 2;

const DEFAULT_PERCENT_MOBILE = (MAX_HEIGHT_IMAGE_BANNER / height) * 100;

const relativeHeight = (num: number) => (height * num) / 100;

// Check tablet
const isTablet = () => {
  const pixelDensity = PixelRatio.get();
  const adjustedWidth = width * pixelDensity;
  const adjustedHeight = height * pixelDensity;
  if (pixelDensity < 2 && (adjustedWidth >= 1000 || adjustedHeight >= 1000)) {
    return true;
  }
  return pixelDensity === 2 && (adjustedWidth >= 1920 || adjustedHeight >= 1920);
};

const HEIGHT_SLIDER = relativeHeight(isTablet() ? DEFAULT_PERCENT_TABLET : DEFAULT_PERCENT_MOBILE);

const useStyle = (theme: Theme) => {
  return StyleSheet.create({
    container: {
      position: 'absolute',
    },
    image: {
      width: width,
      height: HEIGHT_SLIDER,
      resizeMode: 'cover',
    },
    pagination: {
      height: DOT_SIZE,
      flexDirection: 'row',
      alignSelf: 'center',
      position: 'absolute',
      bottom: POSITION_BOTTOM_DOT,
    },
    dots: {
      width: DOT_SIZE,
      height: DOT_SIZE,
      borderRadius: DOT_SIZE / 2,
      backgroundColor: theme.colors.primary,
      alignItems: 'center',
      justifyContent: 'center',
      alignSelf: 'center',
      position: 'absolute',
      marginHorizontal: MARGIN_HORIZONTAL,
    },
    dot: {
      width: DOT_SIZE,
      height: DOT_SIZE,
      marginHorizontal: MARGIN_HORIZONTAL,
      borderRadius: DOT_SIZE / 2,
      backgroundColor: theme.colors.primary,
      opacity: 0.4,
    },
  });
};

export default useStyle;
export { DOT_SIZE, TOTAL_MARGIN, HEIGHT_HEADER, HEIGHT_SLIDER, MARGIN_HORIZONTAL, POSITION_TOP_SLIDER };
