import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, LOADING_REQUEST_OTP, SAVE_INFO_REGISTER, SAVE_CONTENT_HELP_CENTER, SAVE_CAPCHA } from './constants';
import initialState, { TRequestTypeState } from './state';

const requestTypeReducer = (state: TRequestTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TRequestTypeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case LOADING_REQUEST_OTP:
        draft.loadingRequestOtp = action.payload;
        break;
      case SAVE_INFO_REGISTER:
        draft.infoRegister = action.payload;
        break;
      case SAVE_CONTENT_HELP_CENTER:
        draft.contentHelpCenter = action.payload;
        break;
      case SAVE_CAPCHA:
        draft.infoCapcha = action.payload;
        break;
    }
  });
};

export default requestTypeReducer;
