import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';
import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  REQUEST_GET_PROVINCES_SUCCESS,
  SAVE_INFO_VERIFY_OCR,
  VERIFY_ACCOUNT_SUCCESS,
} from './constants';
import initialState, { TVerifyAccountState } from './state';

const requestTypeVerifyReducer = (state: TVerifyAccountState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TVerifyAccountState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case SAVE_INFO_VERIFY_OCR:
        draft.infoOCRVerified = action.payload;
        break;
      case VERIFY_ACCOUNT_SUCCESS:
        draft.responseVerifyAccount = action.payload;
        break;
      case REQUEST_GET_PROVINCES_SUCCESS:
        draft.provinces = action.payload;
        break;
    }
  });
};

export default requestTypeVerifyReducer;
