import React from 'react';
import { View, ViewStyle, StyleProp, TextStyle, Image, ImageStyle, ImageSourcePropType } from 'react-native';
import { Text } from 'superapp-common-component';
import styles from './styles';

interface EmptyProps {
  emptyContainerStyle?: StyleProp<ViewStyle>,
  emptyImageStyle?: StyleProp<ImageStyle>,
  emptyLabelStyle?: TextStyle,
  emptyImageSource?: ImageSourcePropType,
  text?: string
}


const Empty = (props: EmptyProps) => {

  const {
    emptyContainerStyle,
    emptyImageStyle,
    emptyLabelStyle,
    emptyImageSource = require('@/assets/images/no-data.png'),
    text,
  } = props;

  return (
    <View style={[styles.container, emptyContainerStyle]}>
      <Image source={emptyImageSource} style={[styles.image, emptyImageStyle]} />
      <Text style={{...styles.label, ...emptyLabelStyle}}>{text}</Text>
    </View>
  );
};

export default Empty;

