import React, {useLayoutEffect} from 'react';
import { Image, Pressable, Text, View, TouchableOpacity } from 'react-native';
import { Icon, NativeModuleClient } from 'superapp-common-component';
import {StackNavigationEventMap, StackNavigationProp} from "@react-navigation/stack";
import { useTranslation } from 'react-i18next';
import {AIA_COLOR_CHACOAL} from '@/utils/colors';
import {DOCUMENT_TYPE} from '@/pages/VerifyAccount/types';
import {TStackParamList} from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import styles from './styles';
import {useNavigation} from "@react-navigation/native";
import { a11y } from '@/utils/utils';
import CustomText from '@/components/Text';
import dayjs from 'dayjs';
type ChooseKindDocumentNavigationProp = StackNavigationProp<TStackParamList, 'ChooseTypeDocument'>;

const ChooseKindDocument = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<ChooseKindDocumentNavigationProp>();
  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <CustomText style={styles.header}>{ t('verifyAccount.page.verifyAccount')}</CustomText>,
      headerLeft: () => (
        <TouchableOpacity style={styles.marginLeft16} onPress={() => navigation.canGoBack() && navigation.goBack()} {...a11y('VerifyAccount.ChooseKindDocument.iconGoBackHeaderLeft')}>
          <Icon name="Arrow-Left" size={24} color="gray"/>
        </TouchableOpacity>
      ),
      headerRight: () => <Icon name='Info1' color={'gray'} style={styles.marginRight16} onPress={() => {
      }} {...a11y('VerifyAccount.ChooseKindDocument.iconInfoHeaderRight')}/>,
    }  as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  const handleSelectDocumentType = (documentType: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'National ID card',
    });
    if (documentType === DOCUMENT_TYPE.NATIONAL_ID_CARD) {
      navigation.push(ROUTE_PATH.VERIFY_ACCOUNT, { documentType })
    }
  }
  
  return (
    <View style={styles.containerScreen}>
      <Text style={styles.title}>{t('verifyAccount.page.verifyAccount')}</Text>
      <Text style={styles.descriptions}>{t('verifyAccount.page.verifyAccountDes')}</Text>
      <Text style={styles.textMethod}>{t('verifyAccount.page.method')}</Text>
      <View>
        <Pressable onPress={() => handleSelectDocumentType(DOCUMENT_TYPE.NATIONAL_ID_CARD)} {...a11y('VerifyAccount.ChooseKindDocument.pressNavigateToVerifyAccount')} style={styles.itemDocument} >
          <View style={styles.row}>
            <Image source={require('@/assets/images/icons/icon-user-document.png')} resizeMode='cover' height={20} width={20} />
            <Text style={styles.itemDocumentText}>{t('verifyAccount.page.nationID')}</Text>
          </View>
          <Icon name='Arrow-Right' color={AIA_COLOR_CHACOAL} />
        </Pressable>
        <Pressable onPress={() => {}}
          style={[styles.itemDocument, styles.hidden]} {...a11y('VerifyAccount.ChooseKindDocument.pressPassport')}>
          <View style={styles.row}>
            <Image source={require('@/assets/images/icons/icon-user-document.png')} resizeMode='cover' height={20} width={20} />
            <Text style={styles.itemDocumentText}>{t('verifyAccount.page.passport')}</Text>
          </View>
          <Icon name='Arrow-Right' color={AIA_COLOR_CHACOAL} />
        </Pressable>
      </View>
    </View>
  );
};

export default ChooseKindDocument;
