import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, SAVE_CONTENT_ONBOARDING } from './constants';
import initialState, { TOnboardingState } from './state';

const onboardingReducer = (state: TOnboardingState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TOnboardingState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case SAVE_CONTENT_ONBOARDING:
        draft.contents = action.payload;
        break;
    }
  });
};

export default onboardingReducer;
