import React, { useState, useEffect, useRef } from 'react';
import { ScrollView, View, Image, Dimensions } from 'react-native';
import { SafeAreaView } from 'react-native-safe-area-context';
import { useTranslation } from 'react-i18next';

import APP_ENV from '@/config/app.config';

import Text from '@/components/Text';
import WebView, { TWebViewUrlRef } from '@/components/WebView';
import { PortalModal } from '@/components/Modal';

import translation from './translation';
import styles from './styles';

type IncidentScreenProps = {
  status?: string;
  title?: string;
  description?: string;
};

const imgSource = require('@/assets/images/incident.png');

const IncidentScreen: React.FC<IncidentScreenProps> = props => {
  const { i18n } = useTranslation();
  const translator = translation[i18n.language as keyof typeof translation] || {};

  const { status, title, description } = props;
  const [imageSize, setImageSize] = useState({
    width: 0,
    height: 0,
  });
  const webviewRef = useRef<TWebViewUrlRef>(null);

  const handleResizeImage = () => {
    const imageDetail = Image.resolveAssetSource(imgSource);
    const aspectRatio = imageDetail.width / imageDetail.height;
    const width = Math.min(Dimensions.get('window').width * 0.8, imageDetail.width);
    const height = width / aspectRatio;
    setImageSize({ width, height });
  };

  const openStatusPageLink = () => {
    webviewRef.current?.openURL(APP_ENV.STATUS_PAGE_URL);
  };

  useEffect(() => {
    handleResizeImage();
    return () => {
      PortalModal.closeAll();
    };
  }, []);

  return (
    <SafeAreaView style={styles.safeArea}>
      <ScrollView contentContainerStyle={styles.container}>
        <View style={styles.content}>
          <Image
            style={[
              {
                width: imageSize.width,
                height: imageSize.height,
              },
              styles.image,
            ]}
            resizeMode="contain"
            source={imgSource}
          />
          {title && (
            <Text style={styles.title} fontFamily="bold">
              {title}
            </Text>
          )}
          {status && (
            <View style={styles.wrapSubtitle}>
              <Text style={styles.subtitle}>{translator.status[status as keyof typeof translator.status] || status}</Text>
            </View>
          )}
          {description && <Text style={styles.description}>{description}</Text>}
          <Text style={styles.tutorial}>{translator.tutorial}</Text>
          <Text style={styles.textLink} onPress={openStatusPageLink}>
            {translator.link}
          </Text>
        </View>

        <WebView ref={webviewRef} />
      </ScrollView>
    </SafeAreaView>
  );
};

export default IncidentScreen;
