import { Animated } from 'react-native';
import { type AnimationConfig } from './Animation';

type KeyboardAnimationConfig = AnimationConfig & {
  animationDuration?: number;
};

export default class KeyboardAnimation {
  animationDuration: number;

  animate: Animated.Value;

  initialValue: number;

  useNativeDriver: boolean;

  constructor({ animationDuration = 150 }: KeyboardAnimationConfig = {}) {
    this.initialValue = 0;
    this.useNativeDriver = false;
    this.animationDuration = animationDuration;
    this.animate = new Animated.Value(0);
  }

  change(height: number, onFinished?: () => void): void {
    Animated.timing(this.animate, {
      toValue: height,
      duration: this.animationDuration,
      useNativeDriver: false,
    }).start(onFinished);
  }

  getAnimations(): Object {
    return { bottom: this.animate };
  }
}
