import React, { ReactNode, useEffect } from 'react';
import { View } from 'react-native';
import Swipeable from 'react-native-gesture-handler/Swipeable';

import styles from './styles';

interface SwipeProps {
  renderRightContent: any;
  onSwipeableAcion?: () => void;
  customStyles?: Object;
  isClosed?: boolean;
}

const SwipeItem: React.FC<SwipeProps> = props => {
  const { renderRightContent, onSwipeableAcion, customStyles, isClosed, children } = props;

  const swipeRef = React.useRef<any>(null);
  useEffect(() => {
    if (isClosed) {
      swipeRef?.current?.close();
    }
  }, [isClosed]);

  return (
    <View style={[styles.container, customStyles]}>
      <Swipeable
        ref={swipeRef}
        renderRightActions={renderRightContent}
        onSwipeableOpen={() => {
          if (onSwipeableAcion) {
            onSwipeableAcion();
          }
        }}
        friction={1}
        containerStyle={styles.swipeableStyle}
      >
        {children}
      </Swipeable>
    </View>
  );
};

export default SwipeItem;
