import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Keyboard,
  KeyboardAvoidingView,
  SafeAreaView,
  ScrollView,
  StatusBar,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import { useForm, Controller } from 'react-hook-form';
import { connect } from 'react-redux';
import {
  Button,
  Checkbox,
  DateInput,
  Icon,
  NativeModuleClient,
  TextInput,
  Text,
} from 'superapp-common-component';
import { utils } from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { colors } from 'superapp-common-component';
import dayjs from 'dayjs';
import NetInfo from '@react-native-community/netinfo';

import { HITSLOP } from '@/utils/styles';
import APP_ENV from '@/config/app.config';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { checkDateValid, isIOS } from '@/utils/utils';
import { DATE_FORMATS, REGEX } from '@/utils/constants';
import { TForgotUsernameTypeState } from '@/pages/ForgotUsername/stores/state';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import WebView, { TWebViewUrlRef } from '@/components/WebView';

import styles from './styles';
import ModalError, { ModalErrorRef } from './components/ModalError';
import { TConnectState } from '@/stores/types';
import { makeSelectLoading } from './stores/selectors';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import { NS_FORGOT_USERNAME_TYPE } from './stores/constants';
import { submitForgotUsernameAction } from './stores/actions';
import forgotUsernameTypeReducer from './stores/reducer';
import forgotUsernameTypeSaga from './stores/saga';
import { formatDateServer } from '@/utils/time-utils';
import NoConnection from '@/components/NoConnection';

const { a11y } = utils;
type FormForgotUsernameValues = {
  phoneNo: string;
  dob: string;
  idCardNo: string;
};

type ForgotUsernameNavigationProp = StackNavigationProp<
  TStackParamList,
  'ForgotUsername'
>;
type ForgotUsernameProps = {
  loading: boolean;
  submitForgotUsername: (payload: FormForgotUsernameValues) => Promise<any>;
};

const ForgotUsername = (props: ForgotUsernameProps) => {
  const { submitForgotUsername, loading } = props;
  const { t } = useTranslation();
  const [isAgree, setIsAgree] = useState(false);
  const webViewRef = useRef<TWebViewUrlRef>(null);
  const modalErrorRef = useRef<null | ModalErrorRef>();
  const navigation = useNavigation<ForgotUsernameNavigationProp>();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const netInfo = NetInfo.useNetInfo();

  useInjectReducer({
    key: NS_FORGOT_USERNAME_TYPE,
    reducer: forgotUsernameTypeReducer,
  });
  useInjectSaga({ key: NS_FORGOT_USERNAME_TYPE, saga: forgotUsernameTypeSaga });

  const {
    control,
    handleSubmit,
    formState: { errors },
  } = useForm<FormForgotUsernameValues>();

  const dissmisKeyboard = () => Keyboard.dismiss();

  const showWebviewTerms = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_USERNAME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'The terms',
    });
    webViewRef.current?.openURL(t('common.termCondition.url'));
  };

  const showWebviewConditions = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_USERNAME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Conditions',
    });
    webViewRef.current?.openURL(t('common.collectionStatement.url'));
  };

  const handleSubmitUsername = async (data: FormForgotUsernameValues) => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.FORGOT_USERNAME,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
      });
      const payload = { ...data, dob: formatDateServer(data?.dob.toString()) };
      await submitForgotUsername(payload);
      navigation.navigate(ROUTE_PATH.FORGOT_USERNAME_OTP, {
        phoneNo: payload?.phoneNo,
        allowCancel: false,
      });
    } catch (error) {
      modalErrorRef.current?.open();
    }
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text.Bold style={styles.headerTitle}>
          {t('forgot.username.header.title')}
        </Text.Bold>
      ),
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('ForgotUsername.btnForgotGoback')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_USERNAME,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() &&
              navigation.navigate(ROUTE_PATH.DASHBOARD, {
                openModalLogin: true,
              });
          }}
          hitSlop={HITSLOP}>
          <Icon
            name="Arrow-Left"
            size={24}
            color={colors.primary.charcoal[80]}
            style={styles.marginLeft21}
          />
        </TouchableOpacity>
      ),
      headerRight: () => (
        <TouchableOpacity
          {...a11y('ForgotUsername.btnForgotInfo')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_USERNAME,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Info',
            });
          }}
          hitSlop={HITSLOP}>
          <Icon
            name="Info1"
            size={24}
            color={colors.primary.charcoal[80]}
            style={styles.marginRight21}
          />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.container}>
      <StatusBar barStyle="dark-content" />
      <TouchableWithoutFeedback
        {...a11y('ForgotUsername.btnHideKeyboard')}
        onPress={dissmisKeyboard}>
        <KeyboardAvoidingView
          behavior={isIOS ? 'padding' : undefined}
          style={styles.flex1}
          keyboardVerticalOffset={isIOS ? 90 : 0}>
          <ScrollView contentContainerStyle={styles.grow}>
            <View style={styles.wrapper}>
              <View>
                <View style={styles.contentText}>
                  <Text.Bold style={styles.textTitle}>
                    {t('forgot.username.title')}
                  </Text.Bold>
                  <Text style={styles.textDescription}>
                    {t('forgot.username.description')}
                  </Text>
                </View>

                <Controller
                  control={control}
                  rules={{
                    required: {
                      value: true,
                      message: t('common.error.phone.number.empty'),
                    },
                  }}
                  render={({ field: { onChange, value } }) => (
                    <TextInput
                      label={t('forgot.username.form.phoneNumber')}
                      keyboardType="numeric"
                      type={errors?.phoneNo ? 'error' : 'info'}
                      disabled={false}
                      containerStyle={styles.marginBottomInput}
                      onChangeText={onChange}
                      value={value}
                      message={errors?.phoneNo && errors?.phoneNo?.message}
                      autoFocus
                      id="ForgotUsername.phoneNo"
                    />
                  )}
                  name="phoneNo"
                />

                <Controller
                  control={control}
                  rules={{
                    required: {
                      value: true,
                      message: t('common.error.id.number.empty'),
                    },
                  }}
                  render={({ field: { onChange, value } }) => (
                    <TextInput
                      label={t('forgot.username.form.idNumber')}
                      onChangeText={onChange}
                      value={value}
                      message={errors.idCardNo?.message || undefined}
                      containerStyle={styles.marginBottomInput}
                      type={errors?.idCardNo && 'error'}
                      id="ForgotUsername.idCardNo"
                    />
                  )}
                  name="idCardNo"
                />

                <Controller
                  control={control}
                  rules={{
                    required: {
                      value: true,
                      message: t('common.error.phone.dob.empty'),
                    },
                    validate: value => {
                      let result: any = true;
                      if (
                        !(
                          value.match(REGEX.VALID_DATE) != null &&
                          checkDateValid(value)
                        )
                      ) {
                        result = t('common.error.format.date');
                      }
                      return result;
                    },
                  }}
                  render={({ field: { onChange, value } }) => (
                    <DateInput
                      label={t('forgot.username.form.dob')}
                      formatDate={DATE_FORMATS.DDMMYYYY}
                      inputProps={{
                        iconColor: 'gray',
                        containerStyle: styles.marginBottomInput,
                        message: errors?.dob?.message || undefined,
                        type: errors?.dob ? 'error' : 'info',
                      }}
                      id="ForgotUsername.DateOfBirth"
                      value={value}
                      onChange={onChange}
                    />
                  )}
                  name="dob"
                />
              </View>
              <View style={styles.wrapperButon}>
                <Checkbox
                  checked={isAgree}
                  onPress={() => {
                    setIsAgree(!isAgree);
                  }}
                  size={24}
                  customStyleChild={styles.customCheckbox}
                  id="Login.loginCheckboxIsAgree">
                  <Text style={[styles.text]}>
                    {t('login.checkbox.term1')}{' '}
                    <Text
                      style={[styles.textLink, styles.textDecorationLine]}
                      onPress={showWebviewTerms}>
                      {t('login.checkbox.term2')}
                    </Text>
                    <Text style={[styles.text]}>
                      {' '}
                      {t('login.checkbox.term3')}{' '}
                    </Text>
                    <Text
                      style={[styles.textLink, styles.textDecorationLine]}
                      onPress={showWebviewConditions}>
                      {t('login.checkbox.term4')}
                    </Text>
                  </Text>
                </Checkbox>
                <Button
                  text={t('forgot.password.buton.verify')}
                  onPress={handleSubmit(handleSubmitUsername)}
                  uppercase={false}
                  type="primary"
                  labelStyle={styles.buttonText}
                  size="large"
                  style={styles.button}
                  id="Forgot.btnForgotVerify"
                  disabled={!isAgree || loading}
                  loading={loading}
                />
              </View>
            </View>
          </ScrollView>
          <WebView ref={webViewRef} />
          <ModalError ref={modalErrorRef} />
        </KeyboardAvoidingView>
      </TouchableWithoutFeedback>
    </SafeAreaView>
  ) : (
    <View style={styles.flex1}>
      <NoConnection />
    </View>
  );
};

type CreateStructuredSelectorType = {
  loading: TForgotUsernameTypeState['loading'];
};

const mapStateToProps = createStructuredSelector<
  TConnectState,
  CreateStructuredSelectorType
>({
  loading: makeSelectLoading(),
});

type MapDispatchToPropsType = {
  submitForgotUsername: (payload: FormForgotUsernameValues) => Promise<any>;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  submitForgotUsername: (payload: FormForgotUsernameValues) =>
    new Promise((resolve, reject) =>
      dispatch(submitForgotUsernameAction(payload, resolve, reject)),
    ),
});

export default connect(mapStateToProps, mapDispatchToProps)(ForgotUsername);
