import React, { useRef, useState } from 'react';
import { Image, SafeAreaView, ScrollView, StatusBar, View } from 'react-native';
import { useTheme, Text, Checkbox, Button, NativeModuleClient } from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { useDispatch, useSelector } from 'react-redux';
import { StackNavigationProp } from '@react-navigation/stack';
import { useTranslation } from 'react-i18next';
import dayjs from 'dayjs';

import APP_ENV from '@/config/app.config';
import ROUTE_PATH from '@/navigators/routePath';
import { Theme } from '@/config/themes.config';
import { TStackParamList } from '@/navigators';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import Loading from '@/components/Loading';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import ModalConfirmSuccess, { ModalConfirmSuccessRef } from '@/components/ModalConfirmSuccess';

import verifyAccountP820Saga from './stores/saga';
import verifyAccountP820Reducer from './stores/reducer';
import { NS_VERIFY_ACCOUNT_P820 } from './stores/constants';
import { requestProcessP820Action } from './stores/actions';
import { makeSelectLoading, makeSelectProccessError } from './stores/selectors';
import { makeSelectInfoVerifyAccount } from '../VerifyAccount/stores/selectors';
import useStyles from './styles';

type VerifyAccountP820nProp = StackNavigationProp<TStackParamList, 'VerifyAccountP820'>;

const VerifyAccountP820 = () => {
  const dispatch = useDispatch();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const navigation = useNavigation<VerifyAccountP820nProp>();
  const [confirm, setConfirm] = useState<boolean>(false);
  const { t } = useTranslation();
  const modalConfirmSuccessRef = useRef<null | ModalConfirmSuccessRef>(null);
  const modalProcessErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const verifyAccount = useSelector(makeSelectInfoVerifyAccount());
  const loading = useSelector(makeSelectLoading());
  const error = useSelector(makeSelectProccessError());

  useInjectSaga({ key: NS_VERIFY_ACCOUNT_P820, saga: verifyAccountP820Saga });
  useInjectReducer({ key: NS_VERIFY_ACCOUNT_P820, reducer: verifyAccountP820Reducer });

  const closeModal = () => {
    if (modalConfirmSuccessRef.current) {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: 'VerifyAccountSuccess',
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
      });
      modalConfirmSuccessRef.current?.close();
      navigation.navigate(ROUTE_PATH.DASHBOARD);
    }
  };

  const onSuccess = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT_P820,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Skip',
    });

    if (modalConfirmSuccessRef.current) {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'VerifyAccountSuccess',
        unixTimestamp: dayjs().unix(),
      });
      modalConfirmSuccessRef.current?.open();
    }
  };

  const onUpdate = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT_P820,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Update',
    });

    if (verifyAccount?.processP820?.forceP820) {
      dispatch(
        requestProcessP820Action({
          sessionId: verifyAccount?.processP820?.sessionIdP820 || '',
          clientId: APP_ENV.CLIENT_ID,
        }),
      );
    }
  };

  const onDismiss = () => {
    if (error) {
      modalProcessErrorRef.current?.open();
    }
  };

  return (
    <SafeAreaView style={[styles.safearea, styles.flex1]}>
      <StatusBar barStyle="dark-content" />
      <ScrollView contentContainerStyle={styles.flex1}>
        <View style={styles.flex1}>
          <View style={[styles.grow1, styles.container, styles.mgHorizontal16]}>
            <Image style={styles.img} source={require('@/assets/images/verify-account-p820.png')} />
            <Text style={styles.title}>{t('verifyAccount.p820.title')}</Text>
            <Text style={styles.description}>{t('verifyAccount.p820.description')}</Text>
          </View>
          <View style={[styles.mgBottom16, styles.box]}>
            <View style={[styles.confirmbox, styles.mgHorizontal16]}>
              <Checkbox checked={confirm} size={24} onPress={() => setConfirm(!confirm)} />
              <Text style={styles.term}>
                {t('verifyAccount.p820.confirm.first')}
                <Text style={[styles.term, styles.highline]} onPress={() => {}}>
                  {t('verifyAccount.p820.confirm.center')}
                </Text>
                {t('verifyAccount.p820.confirm.last')}
              </Text>
            </View>
            <Button
              text={t('verifyAccount.p820.update')}
              disabled={!confirm}
              style={[styles.mgVertical12, styles.mgHorizontal16]}
              onPress={onUpdate}
            />
            <Button type="secondary" style={styles.mgHorizontal16} text={t('verifyAccount.p820.skip')} onPress={onSuccess} />
          </View>
        </View>
        <Loading visible={loading} onDismiss={onDismiss} />
        <ModalError
          ref={modalProcessErrorRef}
          title={t('verifyAccount.modal.error.title')}
          subtitle={t('verifyAccount.error.p820')}
          btnText={t('verifyAccount.modal.closeBtn')}
        />
        <ModalConfirmSuccess
          ref={modalConfirmSuccessRef}
          onConfirm={closeModal}
          title={t('common.successfully')}
          subtitle={t('verifyAccount.message.success')}
          btnText={t('common.continue')}
        />
      </ScrollView>
    </SafeAreaView>
  );
};

export default VerifyAccountP820;
