import React, { ReactNode, useEffect } from 'react';
import { View, StatusBar } from 'react-native';
import { SafeAreaView, useSafeAreaInsets } from 'react-native-safe-area-context';

import { COLOR_OVERLAY } from '@/utils/styles';
import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface IModalOverlayProps {
  children: ReactNode;
  isVisible: boolean;
  enableOnKeyboardChange?: boolean;
  onDismiss?: () => void;
}

const ModalOverlay = ({ children, isVisible, enableOnKeyboardChange, onDismiss}: IModalOverlayProps) => {
  const insets = useSafeAreaInsets();
  const [modalsRef, modalInstance] = useModal();

  useEffect(() => {
    if (isVisible) {
      modalInstance().show();
    } else {
      modalInstance()?.close();
    }
  }, [isVisible]);

  return (
    <Modal ref={modalsRef} modalStyle={styles.bgOverlay} animationType="slideBottom" enableOnKeyboardChange={enableOnKeyboardChange} onDismiss={onDismiss}>
      <StatusBar backgroundColor={isVisible ? COLOR_OVERLAY : undefined} />
      <SafeAreaView edges={['top']}>
        <View style={[styles.modalContainer, { paddingBottom: Math.max(insets.bottom, 16) }]}>{children}</View>
      </SafeAreaView>
    </Modal>
  );
};

export default ModalOverlay;
