import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_ECONSENT } from './constants';

const currentState = (state: TConnectState) => state?.[NS_ECONSENT] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectOtpError = () => createSelector(currentState, state => state.otpError);
const makeSelectSubmitError = () => createSelector(currentState, state => state.submitError);

export { makeSelectLoading, makeSelectOtpError, makeSelectSubmitError };
