import React, { forwardRef, useImperativeHandle, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { StackNavigationProp } from '@react-navigation/stack';
import { useNavigation } from '@react-navigation/core';

import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { LoginModalRef } from '@/pages/Login';
import { getVitalityMembershipIdFromToken } from '@/utils/utils';
import { CUSTOMER_ROLES, FUNCTION_TYPES, VALID_ROLES } from '@/utils/constants';

import ModalConfirmError, { ModalConfirmErrorRef } from '../ModalConfirmError';
import styles from './styles';

type ModalPermissionNavigationProp = StackNavigationProp<TStackParamList, 'Home'>;
interface ModalPermissionProps {
  roles?: string[];
  isGuest: boolean;
  modalLoginRef: React.MutableRefObject<LoginModalRef | null | undefined>;
}

export interface ModalPermissionRef {
  handlePermission: (functionType: keyof typeof VALID_ROLES) => boolean;
}

const ModalPermission = (props: ModalPermissionProps, ref: React.Ref<unknown> | undefined) => {
  const { roles = [], isGuest, modalLoginRef } = props;
  const { t } = useTranslation();
  const [subTitle, setSubTitle] = useState('');
  const [btnText, setBtnText] = useState('');
  const [btnStyle, setBtnStyle] = useState(styles.closeBtn);
  const [handleCustomAction, setHandleCustomAction] = useState<() => void | undefined>();
  const modalConfirmErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const navigation = useNavigation<ModalPermissionNavigationProp>();

  const customAction = () => {
    modalConfirmErrorRef?.current?.close();
    navigation.navigate(ROUTE_PATH.CHOOSE_TYPE_DOCUMENT);
  };

  const handleTitle = () => {
    setBtnText(t('home.permission.modal.btn.close'));
    setBtnStyle(styles.closeBtn);
    for (let index = 0; index < roles.length; index++) {
      switch (roles[index]) {
        case CUSTOMER_ROLES.NON_CUSTOMER:
          setSubTitle(t('home.permission.modal.subtitle.nonCus'));
          setBtnText(t('home.permission.modal.btn.cancel'));
          setHandleCustomAction(() => () => customAction());
          setBtnStyle(styles.cancelBtn);
          return;
        case CUSTOMER_ROLES.PROSPECT_CUSTOMER:
          setSubTitle(t('home.permission.modal.subtitle.prospectCus'));
          setHandleCustomAction(undefined);
          return;
        case CUSTOMER_ROLES.V_CUSTOMER:
          setSubTitle(t('home.permission.modal.subtitle.vitalityCus'));
          setHandleCustomAction(undefined);
          return;
        case CUSTOMER_ROLES.POLICY_OWNER:
          setSubTitle(t('home.permission.modal.subtitle.poCnCus'));
          setHandleCustomAction(undefined);
          return;
        case CUSTOMER_ROLES.LIFE_ASSURANCE:
        case CUSTOMER_ROLES.SUB_LIFE_ASSURANCE:
        case CUSTOMER_ROLES.V_SUB_LIFE_ASSURANCE:
        case CUSTOMER_ROLES.V_LIFE_ASSURANCE:
          setSubTitle(t('home.permission.modal.subtitle.laCus'));
          setHandleCustomAction(undefined);
          return;
        default:
          setHandleCustomAction(undefined);
          break;
      }
    }
  };

  const handleVitalityCheckMembership = async (isValidRole: boolean) => {
    if (!isValidRole) {
      if (!isValidRole) {
        modalConfirmErrorRef?.current?.open();
      } else {
        modalConfirmErrorRef?.current?.close();
      }
    } else {
      const membershipId = await getVitalityMembershipIdFromToken();
      if (!membershipId) {
        modalConfirmErrorRef?.current?.open();
      } else {
        modalConfirmErrorRef?.current?.close();
      }
    }
  };

  const handleShowPopup = (functionType: keyof typeof VALID_ROLES) => {
    if (isGuest) {
      modalLoginRef.current?.setShowModal(true);
      return true;
    }
    handleTitle();
    let isValidRole = roles?.some(role => VALID_ROLES[functionType]?.includes(role));

    if (functionType === FUNCTION_TYPES.POLICY) {
      const hasRoleLifeAssurance = roles.some(role =>
        [CUSTOMER_ROLES.V_LIFE_ASSURANCE, CUSTOMER_ROLES.V_SUB_LIFE_ASSURANCE].includes(role)
      );
      const hasRolePolicyDisabled = roles.some(role => role === CUSTOMER_ROLES.POLICY_OWNER_DISABLE);

      if (hasRoleLifeAssurance && hasRolePolicyDisabled) {
        isValidRole = true; 
      }
    }

    if (functionType === FUNCTION_TYPES.POS_REQUEST) {
      const hasRoleLifeAssurance = roles.some(role =>
        [CUSTOMER_ROLES.V_LIFE_ASSURANCE, CUSTOMER_ROLES.V_SUB_LIFE_ASSURANCE].includes(role)
      );
      const hasRolePolicyDisabled = roles.some(role => role === CUSTOMER_ROLES.POLICY_OWNER_DISABLE);

      if (hasRoleLifeAssurance && hasRolePolicyDisabled) {
        isValidRole = false; 
      }
    }

    switch (functionType) {
      case FUNCTION_TYPES.VITALITY:
        handleVitalityCheckMembership(isValidRole);
        break;
      default:
        if (!isValidRole) {
          modalConfirmErrorRef?.current?.open();
        } else {
          modalConfirmErrorRef?.current?.close();
        }
    }
    return !isValidRole;
  };

  useImperativeHandle(
    ref,
    (): ModalPermissionRef => ({
      handlePermission: (functionType: keyof typeof VALID_ROLES) => handleShowPopup(functionType),
    }),
  );

  return (
    <ModalConfirmError
      ref={modalConfirmErrorRef}
      title={t('home.permission.modal.title')}
      subtitle={subTitle}
      btnText={btnText}
      customBtnText={t('home.permission.modal.btn.verifyNow')}
      customAction={handleCustomAction}
      btnStyle={btnStyle}
      customBtnStyle={styles.closeBtn}
    />
  );
};

export default forwardRef(ModalPermission);
