import React, {
  useEffect,
  useRef,
  useState,
  forwardRef,
  useImperativeHandle,
  useMemo,
} from 'react';
import {
  View,
  Animated,
  Dimensions,
  Keyboard,
  EmitterSubscription,
} from 'react-native';
import { connect, useSelector } from 'react-redux';
import { Dispatch } from 'redux';
import { useTranslation } from 'react-i18next';
import { createStructuredSelector } from 'reselect';
import { useNavigation, useRoute } from '@react-navigation/native';
import { NativeModuleClient } from 'superapp-common-component';
import { EventEmitter } from '@okta/okta-react-native';
import dayjs from 'dayjs';

import { TConnectState } from '@/stores/types';
import {
  makeSelectCheckLogin,
  makeSelectIsMigrated,
  makeSelectLoading,
  makeSelectRefreshTokenError,
} from '@/stores/authentication/selectors';
import { TAuthenticationState } from '@/stores/authentication/state';
import {
  login as loginAction,
  loginError as loginErrorAction,
  loadingRequest,
  loadingSuccess,
} from '@/stores/authentication/actions';
import ModalConfirmError, {
  ModalConfirmErrorRef,
} from '@/components/ModalConfirmError';
import APP_ENV from '@/config/app.config';
import { isEmpty } from '@/utils/utils';
import { ERROR_CODE_MESSAGE } from '@/utils/constants';
import ROUTE_PATH from '@/navigators/routePath';
import Loading, { ILoadingRef } from '@/components/Loading';
import ModalOverlay from '@/components/ModalOverlay/index2';

import LoginStep from './components/LoginStep';
import InputPincode from './components/InputPincode';
import styles from './styles';

const width = Dimensions.get('window').width;

const BLOCK_LOGIN_ERROR = 'account_locked_out';
const { OKTA_ENABLE } = APP_ENV;
export interface LoginModalRef {
  isOpenLogin: boolean;
  setShowModal: (isOpen: boolean) => void;
}

const LoginModal = forwardRef(
  (props: any, ref: React.Ref<unknown> | undefined) => {
    const route = useRoute();
    const { t } = useTranslation();
    const [isShowModal, setShowModal] = useState<boolean>(false);
    const [step, setStep] = useState<number>(1);
    const isMigrated = useSelector(makeSelectIsMigrated());
    const modalLoginErrorRef = useRef<null | ModalConfirmErrorRef>(null);
    const modalRefreshTokenErrorRef = useRef<null | ModalConfirmErrorRef>(null);
    const modalBlockLoginErrorRef = useRef<null | ModalConfirmErrorRef>(null);
    const animation = useRef(new Animated.Value(0)).current;
    const {
      loading,
      loginStatus,
      refreshTokenError,
      login,
      loginError,
      loadingRequestDispatch,
      loadingSuccessDispatch,
      onDismiss,
    } = props;
    const navigation = useNavigation<any>();
    const loadingRef = useRef<ILoadingRef>(null);

    const handleSectionLogin = (toStep: number, durationTime: number = 250) => {
      setStep(toStep);
      Animated.timing(animation, {
        toValue: -((toStep - 1) * width),
        duration: durationTime,
        useNativeDriver: false,
      }).start();
      Keyboard.dismiss();
    };

    const onDismissLoading = () => {
      if (!isEmpty(loginStatus?.error)) {
        if (loginStatus?.error !== BLOCK_LOGIN_ERROR) {
          modalLoginErrorRef.current?.open();
        } else {
          modalBlockLoginErrorRef.current?.open();
        }
      } else if (refreshTokenError?.hasError) {
        modalRefreshTokenErrorRef.current?.open();
      }
    };

    const onConfirmLoginWithUsername = () => handleSectionLogin(1, 0);

    const setLoading = (status: boolean) => {
      if (status) {
        loadingRequestDispatch();
      } else {
        loadingSuccessDispatch();
      }
    };

    const refreshTokenErrorTitle = useMemo(() => {
      if (refreshTokenError?.hasError) {
        return refreshTokenError?.errorCode
          ? t(`login.error.title.${refreshTokenError?.errorCode}`)
          : t('login.error.title');
      }
    }, [refreshTokenError]);

    useEffect(() => {
      // Hide modal login when login success
      if (loginStatus?.isLoged && !loading) {
        if (
          (route.name === ROUTE_PATH.ONBOARDING ||
            route.name === ROUTE_PATH.REGISTER) &&
          isShowModal &&
          !isMigrated
        ) {
          navigation.reset({
            index: 0,
            routes: [{ name: ROUTE_PATH.DASHBOARD }],
          });
        }
        setShowModal(false);
      }
    }, [loginStatus, loading, isMigrated]);

    useEffect(() => {
      if (!isShowModal) {
        handleSectionLogin(1);
      }
    }, [isShowModal]);

    useEffect(() => {
      if (refreshTokenError?.hasError) {
        modalRefreshTokenErrorRef.current?.open();
      }
    }, [refreshTokenError]);

    useImperativeHandle(
      ref,
      (): LoginModalRef => ({ isOpenLogin: isShowModal, setShowModal }),
    );


    useEffect(() => {
      let subscriptionOktaError: EmitterSubscription;
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: ROUTE_PATH.LOGIN,
        unixTimestamp: dayjs().unix(),
      });

      if (OKTA_ENABLE) {
        subscriptionOktaError = EventEmitter.addListener(
          'onError',
          (event: { error_code: string; error_message: string }) => {
            loginError({ isLoged: false, error: event.error_code });
            setLoading(false);
          },
        );
      }

      return () => {
        if (OKTA_ENABLE && subscriptionOktaError) {
          subscriptionOktaError.remove();
        }
      };
    }, []);

    return (
      <ModalOverlay
        isVisible={isShowModal}
        enableOnKeyboardChange={false}
        onDismiss={onDismiss}>
        <Animated.View style={[styles.container, { marginLeft: animation }]}>
          <View style={{ width: width }}>
            <Loading
              visible={loading}
              ref={loadingRef}
              onDismiss={onDismissLoading}
            />
            <ModalConfirmError
              ref={modalLoginErrorRef}
              title={t(
                loginStatus.error === ERROR_CODE_MESSAGE.NO_INTERNET
                  ? 'login.error.title.noInternet'
                  : 'login.error.title',
              )}
              subtitle={t(
                loginStatus.error
                  ? `login.error.subtitle.${loginStatus.error}`
                  : 'login.error.title',
                {
                  remainingLoginAttempts: loginStatus.remainingLoginAttempts,
                },
              )}
              btnText={t('login.error.tryagain')}
            />
            <ModalConfirmError
              ref={modalBlockLoginErrorRef}
              title={t('login.error.title.blockLogin')}
              subtitle={t('login.error.blockLogin')}
              btnText={t('login.modal.button.close')}
            />
            <ModalConfirmError
              ref={modalRefreshTokenErrorRef}
              title={refreshTokenErrorTitle}
              subtitle={t('login.error.refreshToken')}
              btnText={t('login.modal.button.close')}
              onConfirm={onConfirmLoginWithUsername}
            />
            <LoginStep
              isRefreshTokenError={refreshTokenError?.hasError}
              setShowModal={setShowModal}
              setCurrentStep={handleSectionLogin}
              login={(data: {
                phoneNumber?: string;
                password?: string;
                access_token?: string;
                refresh_token?: string;
              }) => login(data)}
              setLoading={setLoading}
              loginError={loginError}
            />
          </View>
          <InputPincode
            setShowModal={setShowModal}
            setCurrentStep={handleSectionLogin}
            step={step}
          />
        </Animated.View>
      </ModalOverlay>
    );
  },
);

type CreateStructuredSelectorType = {
  loading: TAuthenticationState['loading'];
  loginStatus: TAuthenticationState['loginStatus'];
  refreshTokenError: TAuthenticationState['refreshTokenError'];
};

const mapStateToProps = createStructuredSelector<
  TConnectState,
  CreateStructuredSelectorType
>({
  loginStatus: makeSelectCheckLogin(),
  loading: makeSelectLoading(),
  refreshTokenError: makeSelectRefreshTokenError(),
});

type MapDispatchToPropsType = {
  login: (payload: {
    phoneNumber?: string;
    password?: string;
    access_token?: string;
    refresh_token?: string;
  }) => any;
  loginError: (payload: { isLoged: boolean; error: string }) => any;
  loadingRequestDispatch: () => any;
  loadingSuccessDispatch: () => any;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  login: payload => dispatch(loginAction(payload)),
  loginError: payload => dispatch(loginErrorAction(payload)),
  loadingRequestDispatch: () => dispatch(loadingRequest()),
  loadingSuccessDispatch: () => dispatch(loadingSuccess()),
});

export default connect(mapStateToProps, mapDispatchToProps, null, {
  forwardRef: true,
})(LoginModal);
