import React, { useEffect, useState } from 'react';
import { Image, ImageBackground, StyleProp, TouchableOpacity, View, ViewStyle } from 'react-native';
import { useTheme, Text } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { Theme } from '@/config/themes.config';
import useStyles from './styles';
import dayjs from 'dayjs';
import { ECARD_TYPES, FORMAT_DATE } from '@/utils/constants';

type ECardCanvasProps = {
  type?: 'back' | 'front';
  cardContainerStyle?: StyleProp<ViewStyle>;
  onPress?: () => void;
  ecardInfo?: any;
  sizeType?: 'default' | 'scale' | 'rotate';
};
const ECardCanvas = (props: ECardCanvasProps) => {
  const { type, cardContainerStyle, onPress, ecardInfo, sizeType = 'default' } = props;
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [isTeladoc, setIsTeladoc] = useState(false);
  const [isTypeFMH, setIsTypeFMH] = useState(false);

  useEffect(() => {
    setIsTeladoc(ecardInfo?.type === ECARD_TYPES.TELA_DOC);
    setIsTypeFMH(ecardInfo?.productGroup === ECARD_TYPES.PRODUCT_FH);
  }, [ecardInfo]);

  const renderScaleFrontCard = () => {
    return (
      <>
        {!isTeladoc && (
          <View style={[styles.headerScale, styles.rowSpaceBetween]}>
            <Image source={require('@/assets/images/insmart.png')} style={styles.headerLeftImageScale} />
            {isTypeFMH ? (
              <Image source={require('@/assets/images/logo.png')} style={styles.headerRightLogoScale} />
            ) : (
              <Image source={require('@/assets/images/aia-ecard-logo.png')} style={styles.headerRightImageScale} />
            )}
          </View>
        )}
        {isTeladoc && (
          <View style={[styles.headerScale, styles.rowFlexEnd]}>
            <Image source={require('@/assets/images/aia-teladoc-logo.png')} style={styles.headerRightImageTeladocScale} />
          </View>
        )}
        <View style={[styles.contentScale, styles.minHeightCardScale]}>
          {!isTypeFMH ? (
            <Text.Bold style={{ ...styles.font12Scale, ...styles.textColorWhite }}>
              {isTeladoc ? t('ecard.detail.teladoc.medix') : ecardInfo?.productName}
            </Text.Bold>
          ) : (
            <>
              <Text.Bold style={{ ...styles.font12Scale, ...styles.textColorWhite }}>
                {t('ecard.detail.text.titleheathInsureCard')}
              </Text.Bold>
              <Image source={require('@/assets/images/name-card-family-heath.png')} style={{ ...styles.nameCardFMHScale }} />
            </>
          )}
          <Text.Bold style={{ ...styles.font26Scale, ...styles.textColorWhite }}>{ecardInfo?.fullName}</Text.Bold>
        </View>
        <View style={[styles.footerScale, styles.rowSpaceBetween]}>
          <View>
            {isTeladoc && <Text style={{ ...styles.font12Scale, ...styles.textColorWhite }}>{t('ecard.front.policyNumber')}</Text>}
            <Text.Bold style={{ ...styles.font16Scale, ...styles.textColorWhite }}>{ecardInfo?.policyNumber}</Text.Bold>
          </View>
          <View>
            {!isTeladoc && (
              <Text.Bold style={{ ...styles.effectiveDateScale, ...styles.textColorWhite, ...styles.textAlignRight }}>
                {t('ecard.front.effectiveDate')}
              </Text.Bold>
            )}
            {isTeladoc && (
              <Text style={{ ...styles.effectiveDateScale, ...styles.textColorWhite, ...styles.textAlignRight }}>
                {t('ecard.front.effectiveDate')}
              </Text>
            )}
            <Text.Bold style={{ ...styles.font16Scale, ...styles.textColorWhite, ...styles.textAlignRight }}>
              {ecardInfo?.effectiveDate && dayjs(ecardInfo?.effectiveDate, FORMAT_DATE).format('DD/MM/YYYY')}
            </Text.Bold>
          </View>
        </View>
      </>
    );
  };

  const renderDefaultFrontCard = () => {
    return (
      <>
        {!isTeladoc && (
          <View style={[styles.header, styles.rowSpaceBetween]}>
            <Image source={require('@/assets/images/insmart.png')} style={styles.headerLeftImage} />
            {!isTypeFMH ? (
              <Image source={require('@/assets/images/aia-ecard-logo.png')} style={styles.headerRightImage} />
            ) : (
              <Image source={require('@/assets/images/logo.png')} style={styles.headerRightLogo} />
            )}
          </View>
        )}
        {isTeladoc && (
          <View style={[styles.header, styles.rowFlexEnd]}>
            <Image source={require('@/assets/images/aia-teladoc-logo.png')} style={styles.headerRightTeladocImage} />
          </View>
        )}
        <View style={[styles.content, styles.minHeightCard, isTypeFMH && { paddingTop: 0, paddingBottom: 0 }]}>
          {!isTeladoc && !isTypeFMH && (
            <Text.Bold style={{ ...styles.font12, ...styles.textColorWhite }}>{ecardInfo?.productName}</Text.Bold>
          )}
          {isTypeFMH && (
            <>
              <Text.Bold style={{ ...styles.font12, ...styles.textColorWhite }}>{t('ecard.detail.text.titleheathInsureCard')}</Text.Bold>
              <Image source={require('@/assets/images/name-card-family-heath.png')} style={{ ...styles.nameCardFMH }} />
            </>
          )}
          {isTeladoc && (
            <Text.Bold style={{ ...styles.font16, ...styles.textColorWhite, ...styles.marginBottom8 }}>
              {t('ecard.detail.teladoc.medix')}
            </Text.Bold>
          )}
          <Text.Bold style={{ ...styles.font26, ...styles.textColorWhite }}>{ecardInfo?.fullName}</Text.Bold>
        </View>
        <View style={[styles.footer, styles.rowSpaceBetween]}>
          <View>
            {isTeladoc && <Text style={{ ...styles.font16, ...styles.textColorWhite }}>{t('ecard.front.policyNumber')}</Text>}
            <Text.Bold style={{ ...styles.font16, ...styles.textColorWhite }}>{ecardInfo?.policyNumber}</Text.Bold>
          </View>
          <View>
            {!isTeladoc && (
              <Text.Bold style={{ ...styles.effectiveDate, ...styles.textColorWhite, ...styles.textAlignRight }}>
                {t('ecard.front.effectiveDate')}
              </Text.Bold>
            )}
            {isTeladoc && (
              <Text style={{ ...styles.effectiveDate, ...styles.textColorWhite, ...styles.textAlignRight }}>
                {t('ecard.front.effectiveDate')}
              </Text>
            )}
            <Text.Bold style={{ ...styles.font16, ...styles.textColorWhite, ...styles.textAlignRight }}>
              {ecardInfo?.effectiveDate && dayjs(ecardInfo?.effectiveDate, FORMAT_DATE).format('DD/MM/YYYY')}
            </Text.Bold>
          </View>
        </View>
      </>
    );
  };

  const renderRotateFrontCard = () => {
    if (isTeladoc) {
      return (
        <View style={styles.rotateFront90}>
          <View style={[styles.header, styles.rowFlexEnd]}>
            <Image
              source={require('@/assets/images/aia-teladoc-logo.png')}
              style={[styles.headerRightImageTeladocRotate, styles.marginRB170]}
            />
          </View>
          <View style={[styles.marginLeft32, styles.minHeightRotateCard]}>
            <Text.Bold style={{ ...styles.font24, ...styles.textColorWhite, ...styles.marginBottom8 }}>
              {t('ecard.detail.teladoc.medix')}
            </Text.Bold>
            <Text.Bold style={{ ...styles.font40, ...styles.textColorWhite }}>{ecardInfo?.fullName}</Text.Bold>
          </View>
          <View style={[styles.footer, styles.rowSpaceBetween]}>
            <View>
              <Text style={{ ...styles.font16, ...styles.textColorWhite }}>{t('ecard.front.policyNumber')}</Text>
              <Text.Bold style={{ ...styles.font24, ...styles.textColorWhite }}>{ecardInfo?.policyNumber}</Text.Bold>
            </View>
            <View style={styles.marginRB170}>
              <Text style={{ ...styles.font16, ...styles.textColorWhite, ...styles.textAlignRight }}>{t('ecard.front.effectiveDate')}</Text>
              <Text.Bold style={{ ...styles.font24, ...styles.textColorWhite, ...styles.textAlignRight }}>
                {ecardInfo?.effectiveDate && dayjs(ecardInfo?.effectiveDate, FORMAT_DATE).format('DD/MM/YYYY')}
              </Text.Bold>
            </View>
          </View>
        </View>
      );
    }
    return (
      <View style={styles.rotateFront90}>
        <View style={[styles.header, styles.rowSpaceBetween]}>
          <Image source={require('@/assets/images/insmart.png')} style={styles.headerLeftImage} />
          {!isTypeFMH ? (
            <Image source={require('@/assets/images/aia-ecard-logo.png')} style={[styles.headerRightImageRotate, styles.marginRB150]} />
          ) : (
            <Image source={require('@/assets/images/logo.png')} style={[styles.headerRightLogoRotate, styles.marginRB150]} />
          )}
        </View>
        <View style={[styles.marginLeft32, styles.minHeightRotateCard]}>
          {!isTypeFMH ? (
            <Text.Bold style={{ ...styles.font14, ...styles.textColorWhite }}>{ecardInfo?.productName}</Text.Bold>
          ) : (
            <>
              <Text.Bold style={{ ...styles.font12, ...styles.textColorWhite }}>{t('ecard.detail.text.titleheathInsureCard')}</Text.Bold>
              <Image source={require('@/assets/images/name-card-family-heath-rotate.png')} style={{ ...styles.nameCardFMHRotate }} />
            </>
          )}
          <Text.Bold style={{ ...styles.font40, ...styles.textColorWhite }}>{ecardInfo?.fullName}</Text.Bold>
        </View>
        <View style={[styles.footer, styles.rowSpaceBetween]}>
          <View>
            <Text.Bold style={{ ...styles.font24, ...styles.textColorWhite }}>{ecardInfo?.policyNumber}</Text.Bold>
          </View>
          <View style={styles.marginRB150}>
            <Text.Bold style={{ ...styles.effectiveDate, ...styles.textColorWhite, ...styles.textAlignRight }}>
              {t('ecard.front.effectiveDate')}
            </Text.Bold>
            <Text.Bold style={{ ...styles.font24, ...styles.textColorWhite, ...styles.textAlignRight }}>
              {ecardInfo?.effectiveDate && dayjs(ecardInfo?.effectiveDate, FORMAT_DATE).format('DD/MM/YYYY')}
            </Text.Bold>
          </View>
        </View>
      </View>
    );
  };

  const renderDefaultBackCard = () => {
    return (
      <View style={styles.containerBackCard}>
        <View style={[styles.rowSpaceBetween, styles.marginHorizontal24]}>
          <View>
            <Text.Bold style={{ ...styles.font10, ...styles.textColorWhite }}>{t('ecard.back.header.contactText.vi')}</Text.Bold>
            <Text.Bold style={{ ...styles.font10, ...styles.textColorWhite }}>{t('ecard.back.header.contactText.en')}</Text.Bold>
          </View>
          <Text.Bold style={{ ...styles.font20, ...styles.textColorWhite }}>{t('ecard.back.header.phone')}</Text.Bold>
        </View>
        <View style={[styles.marginHorizontal24, styles.marginTop48]}>
          <Text.Bold style={{ ...styles.font8, ...styles.textColorWhite }}>{t('ecard.back.content.note.vi')}</Text.Bold>
          <Text.Bold style={{ ...styles.font8, ...styles.textColorWhite }}>{t('ecard.back.content.note.en')}</Text.Bold>
          <Text.Bold style={{ ...styles.font8, ...styles.textColorWhite, ...styles.paddingVertical16 }}>{t('ecard.back.content.description.vi')}</Text.Bold>
          <Text.Bold style={{ ...styles.font8, ...styles.textColorWhite }}>{t('ecard.back.content.description.en')}</Text.Bold>
          <Text.Bold style={{ ...styles.font16, ...styles.textColorWhite, ...styles.paddingVertical16 }}>
            {t('ecard.back.footer.url')}
          </Text.Bold>
        </View>
      </View>
    );
  };

  const renderRotateBackCard = () => {
    return (
      <View style={styles.rotateBack90}>
        <View style={[styles.rowSpaceBetween]}>
          <View style={styles.marginBottom100}>
            <Text.Bold style={{ ...styles.font16Back, ...styles.textColorWhite }}>{t('ecard.back.header.contactText.vi')}</Text.Bold>
            <Text.Bold style={{ ...styles.font16Back, ...styles.textColorWhite }}>{t('ecard.back.header.contactText.en')}</Text.Bold>
          </View>
          <Text.Bold style={{ ...styles.font26, ...styles.textColorWhite }}>
            {t('ecard.back.header.phone')}
          </Text.Bold>
        </View>
        <View>
          <Text.Bold style={{ ...styles.font12Back, ...styles.textColorWhite }}>{t('ecard.back.content.note.vi')}</Text.Bold>
          <Text.Bold style={{ ...styles.font12Back, ...styles.textColorWhite, ...styles.marginBottom16 }}>{t('ecard.back.content.note.en')}</Text.Bold>
          <Text.Bold style={{ ...styles.font12Back, ...styles.textColorWhite }}>{t('ecard.back.content.description.vi')}</Text.Bold>
          <Text.Bold style={{ ...styles.font12Back, ...styles.textColorWhite, ...styles.marginBottom16 }}>{t('ecard.back.content.description.en')}</Text.Bold>
          <Text.Bold style={{ ...styles.font16, ...styles.textColorWhite }}>
            {t('ecard.back.footer.url')}
          </Text.Bold>
        </View>
      </View>
    );
  };

  const renderBackCard = () => {
    if (sizeType === 'rotate') {
      return renderRotateBackCard();
    }
    return renderDefaultBackCard();
  };

  const renderFrontCard = () => {
    switch (sizeType) {
      case 'scale':
        return renderScaleFrontCard();
      case 'rotate':
        return renderRotateFrontCard();
      default:
        return renderDefaultFrontCard();
    }
  };

  const renderBackground = () => {
    const isCHProduct = ecardInfo?.productCode?.substring(0, 2) === 'CH';
    const isFHMProduct = ecardInfo?.productGroup === ECARD_TYPES.PRODUCT_FH;
    const isBackType = type === 'back';
    if (isTeladoc) {
      return require('@/assets/images/card-teladoc-front-bgr.png');
    }
    if (isFHMProduct) {
      return require('@/assets/images/card-family-heath-bgr.png');
    }
    if (isCHProduct) {
      if (isBackType) {
        return require('@/assets/images/card-healthy-care-back-bgr.png');
      } else {
        return require('@/assets/images/card-healthy-care-front-bgr.png');
      }
    } else {
      if (isBackType) {
        return require('@/assets/images/card-hospital-back-bgr.png');
      } else {
        return require('@/assets/images/card-hospital-front-bgr.png');
      }
    }
  };

  const renderRotateBackground = () => {
    const isCHProduct = ecardInfo?.productCode?.substring(0, 2) === 'CH';
    const isFHMProduct = ecardInfo?.productGroup === ECARD_TYPES.PRODUCT_FH;
    const isBackType = type === 'back';
    if (isTeladoc) {
      return require('@/assets/images/card-teladoc-front-bgr-rotate.png');
    }
    if (isFHMProduct) {
      return require('@/assets/images/card-family-heath-bgr-rotate.png');
    }
    if (isCHProduct) {
      if (isBackType) {
        return require('@/assets/images/card-healthy-care-back-bgr-rotate.png');
      } else {
        return require('@/assets/images/card-healthy-care-front-bgr-rotate.png');
      }
    } else {
      if (isBackType) {
        return require('@/assets/images/card-hospital-back-bgr-rotate.png');
      } else {
        return require('@/assets/images/card-hospital-front-bgr-rotate.png');
      }
    }
  };

  return (
    <View style={[styles.container, cardContainerStyle]}>
      <TouchableOpacity onPress={onPress && onPress}>
        <ImageBackground
          imageStyle={{ borderRadius: sizeType === 'scale' ? 2 : 8 }}
          style={sizeType === 'scale' ? styles.scale : sizeType === 'rotate' ? styles.rotate : styles.default}
          source={sizeType === 'rotate' ? renderRotateBackground() : renderBackground()}
          resizeMode="cover"
        >
          {type === 'back' ? renderBackCard() : renderFrontCard()}
        </ImageBackground>
      </TouchableOpacity>
    </View>
  );
};

export default ECardCanvas;
