import React, { useLayoutEffect, useRef } from 'react';
import { Image, TouchableOpacity, View } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { useTranslation } from 'react-i18next';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { Icon, NativeModuleClient } from 'superapp-common-component';
import { utils } from 'superapp-common-component';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import WebView, { TWebViewUrlRef } from '@/components/WebView';
import ROUTE_PATH from '@/navigators/routePath';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';

import styles from './styles';

const { a11y } = utils;
type VideoGuildlineNavigationProp = StackNavigationProp<
  TStackParamList,
  'VideoGuildline'
>;

const VideoGuildline = () => {
  const { t } = useTranslation();
  const webViewRef = useRef<TWebViewUrlRef>(null);
  const navigation = useNavigation<VideoGuildlineNavigationProp>();

  const onPressPolicy = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VIDEO_GUILDLINE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Policy',
    });
    webViewRef.current?.openURL(t('common.userManual.url'));
  };

  const onPressVitality = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VIDEO_GUILDLINE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Vitality',
    });
    webViewRef.current?.openURL(t('common.aboutVitality.url'));
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text style={HEADER_TITLE}>{t('profile.help.center.video.title')}</Text>
      ),
      headerLeft: () => (
        <TouchableOpacity
          style={styles.marginLeft16}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.VIDEO_GUILDLINE,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}
          {...a11y('VideoGuildline.iconBackHeaderLeft')}>
          <Icon name="Arrow-Left" size={24} color="#fff" />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <View style={styles.container}>
      <View style={styles.content}>
        <TouchableOpacity onPress={onPressPolicy}>
          <View style={styles.item}>
            <Image
              style={styles.image}
              source={require('@/assets/images/policy.png')}
            />
            <View style={styles.contentText}>
              <Text style={styles.textApp}>{t('home.miniApp.policy')}</Text>
              <Text style={styles.textDescription}>
                {t('profile.help.center.video.description')}
              </Text>
            </View>
            <Icon name="Arrow-Right" size={16} color="#5B636B" />
          </View>
        </TouchableOpacity>
        <TouchableOpacity onPress={onPressVitality}>
          <View style={styles.item}>
            <Image
              style={styles.image}
              source={require('@/assets/images/vitality.png')}
            />
            <View style={styles.contentText}>
              <Text style={styles.textApp}>{t('home.miniApp.vitality')}</Text>
              <Text style={styles.textDescription}>
                {t('profile.help.center.video.description')}
              </Text>
            </View>
            <Icon name="Arrow-Right" size={16} color="#5B636B" />
          </View>
        </TouchableOpacity>
      </View>
      <WebView ref={webViewRef} />
    </View>
  );
};

export default VideoGuildline;
