import { takeLatest, call, put, all, select } from 'redux-saga/effects';
import { TAction } from '@/stores/types';
import { REQUEST_OTP_BY_SMS, MIGRATE_USERNAME } from './constants';
import {
  loadingRequestAction,
  loadingSuccessAction,
  putOtpRequest,
  putOtpSuccess,
  putCaptchaRequest,
  putCaptchaSuccess,
  putCaptchaError,
  putOtpError,
} from './actions';
import APP_ENV from '@/config/app.config';
import AuthService from '@/services/auth';
import { clearTokenLogout, clearVitalityStorage } from '@/utils/utils';
// import { saveProfile } from '@/stores/global/actions';
import { logoutSuccess } from '@/stores/authentication/actions';
import APIError from '@/utils/errors';
import { clearData as clearVitalityDataAction } from '@/stores/vitality/actions';
import { makeSelectProfile } from '@/stores/global/selectors';

const { CLIENT_ID } = APP_ENV;

function* handleRequestOtpBySms({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    yield put(putCaptchaRequest());
    const { params, setSeconds, setOtpId } = payload;
    const response: { otpId: string; } = yield call(AuthService.requestOtpLogin, params);
    setSeconds && setSeconds(120);
    setOtpId(response?.otpId);
    yield put(putCaptchaSuccess());
  } catch (error) {
    yield put(putCaptchaError((error as APIError).message));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleMigrateUsername({ payload, reject, resolve }: TAction) {
  const profile: MODEL.IProfile = yield select(makeSelectProfile());
  try {
    const { otpValue, otpId, newUsername } = payload;
    yield all([put(loadingRequestAction()), put(putOtpRequest())]);
    
    yield call(AuthService.migrateUserName, { clientId: CLIENT_ID, otpValue, otpId, newUsername: newUsername || profile?.mobile });
    resolve();
    yield all([
      put(logoutSuccess()),
      call(clearTokenLogout),
      call(clearVitalityStorage),
      // put(saveProfile(undefined)),
      put(clearVitalityDataAction()),
    ]);
    yield put(putOtpSuccess());
  } catch (error) {
    reject(error);
    yield put(putOtpError((error as APIError).message));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* migrationSaga() {
  yield takeLatest(REQUEST_OTP_BY_SMS, handleRequestOtpBySms);
  yield takeLatest(MIGRATE_USERNAME, handleMigrateUsername);
}

export default migrationSaga;
