import RequestITunes from '@/utils/itunesRequest';
import APP_ENV from '@/config/app.config';

class CloudflareService extends RequestITunes {
  public constructor() {
    super(APP_ENV.CLOUD_FLARE);
  }

  public traceService = (): Promise<string>  => this.instance.get('/cdn-cgi/trace');
}

export default new CloudflareService();
