import React from 'react';
import { View } from 'react-native';
import {
  GestureEvent,
  HandlerStateChangeEvent,
  PinchGestureHandler,
  PinchGestureHandlerEventPayload,
  State,
} from 'react-native-gesture-handler';

import styles from './styles';

type TZoomViewProps = {
  onPinchStart: () => void;
  onPinchEnd: () => void;
  onPinchProgress: (zoom: number) => void;
};

const ZoomView: React.FC<TZoomViewProps> = props => {
  const onGesturePinch = ({ nativeEvent }: GestureEvent<PinchGestureHandlerEventPayload>) => {
    props.onPinchProgress(nativeEvent.scale);
  };

  const onPinchHandlerStateChange = (event: HandlerStateChangeEvent<PinchGestureHandlerEventPayload>) => {
    const pinchEnd = event.nativeEvent.state === State.END;
    const pinchBegin = event.nativeEvent.oldState === State.BEGAN;
    const pinchActive = event.nativeEvent.state === State.ACTIVE;
    if (pinchEnd) {
      props.onPinchEnd();
    } else if (pinchBegin && pinchActive) {
      props.onPinchStart();
    }
  };

  return (
    <PinchGestureHandler onGestureEvent={onGesturePinch} onHandlerStateChange={onPinchHandlerStateChange}>
      <View style={styles.preview}>{props.children}</View>
    </PinchGestureHandler>
  );
};

export default ZoomView;
