import React, { useEffect, useRef, useState } from 'react';
import { Image, SafeAreaView, View, TouchableOpacity, FlatList, RefreshControl } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { Icon, InnerLoading, Loading, useTheme, NativeModuleClient, } from 'superapp-common-component';
import { StackNavigationProp } from '@react-navigation/stack';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';

import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { TStackParamList } from '@/navigators';
import { Theme } from '@/config/themes.config';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';

import {
  makeSelectClaimDocumentError,
  makeSelectClaimDocumentLoading,
  makeSelectClaimELetterLoading,
  makeSelectClaimRequestELetter,
  makeSelectClaimRequestIsLoadMore,
} from '../../stores/selectors';
import {
  getClaimELetterDocument,
  getClaimELetterList,
  loadingClaimELetterDocumentSuccess,
  loadMoreClaimELetter,
  putClaimELetterDocumentError,
  saveClaimELetterList,
  setLoadMoreClaimELetter,
  pullToRefreshClaimList,
} from '../../stores/actions';
import useStyles from './styles';

type ClaimELetterNavigationProp = StackNavigationProp<TStackParamList, 'ELetter'>;

const ELETTER_STATUS = {
  READ: 'read',
  UNREAD: 'unread',
};

type RequestELetterFilter = {
  paging: {
    pageIndex: number;
    pageSize: number;
  };
  searchValue: string;
  sorting: {
    sortBy: string;
    ascending: boolean;
  };
  requestType: string;
};

const initialParams = {
  paging: {
    pageIndex: 0,
    pageSize: 10,
  },
  searchValue: '',
  sorting: {
    sortBy: 'MetaData2.ReceiveDate',
    ascending: false,
  },
  requestType: 'CLAIM',
};

const FORMAT_DATE = 'YYYY-MM-DD:HH:mm:ssZZ';

const ClaimELetter = () => {
  const navigation = useNavigation<ClaimELetterNavigationProp>();
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [filterParams, setFilterParams] = useState<RequestELetterFilter>(initialParams);
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const [pathFilePdf, setPathFilepdf] = useState<string>('');
  const dispatch = useDispatch();
  const modalDocumentErrorRef = useRef<ModalConfirmErrorRef | null>(null);
  const loginStatus = useSelector(makeSelectCheckLogin());
  const claimELettetList = useSelector(makeSelectClaimRequestELetter());
  const isLoading = useSelector(makeSelectClaimELetterLoading());
  const isLoadMore = useSelector(makeSelectClaimRequestIsLoadMore());
  const documentLoading = useSelector(makeSelectClaimDocumentLoading());
  const documentError = useSelector(makeSelectClaimDocumentError());

  const dowloadDocument = async (item: MODEL.IELetter) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: `${ROUTE_PATH.ELETTER}:Claim`,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Download document',
    });
    
    // if (!item?.url) return;
    const callback = (url?: string) => setPathFilepdf(url || '')
    const dispatchDowloadDocument = () =>
      new Promise<void>((resolve, reject) => dispatch(getClaimELetterDocument({ url: item?.url, name: item?.name, id: item?.id, contentType: item?.contentType }, (url?: string) => { callback(url); resolve()}, reject)));
    try {
      dispatch(putClaimELetterDocumentError(false));
      await dispatchDowloadDocument();
    } catch (error) {
      dispatch(putClaimELetterDocumentError(true));
    } finally {
      dispatch(loadingClaimELetterDocumentSuccess());
    }
  };

  const onCancelDocumentErrorModal = () => {
    dispatch(putClaimELetterDocumentError(false));
  };

  const onDismissLoading = () => {
    if (documentError) {
      modalDocumentErrorRef?.current?.open();
    }
    // if (pathFilePdf) {
    //   setTimeout(() => {
    //     openFilePdf(pathFilePdf)
    //     setPathFilepdf('')
    //   }, 100);
    // }
  };

  const renderClaimELetter = ({ item }: { item: MODEL.IELetter }) => {
    return (
      <View style={styles.wrapperLetter} key={item?.id}>
        <View style={[item?.status === ELETTER_STATUS.UNREAD && styles.overlay]}>
          <View style={styles.topContent}>
            <View style={styles.topLeftContent}>
              <Icon name="Protect1" size={24} color="#5B636B" />
              <Text style={styles.topTitle}>{t('home.letter.title.claim')}</Text>
              {item?.status === ELETTER_STATUS.READ && <View style={styles.circle} />}
            </View>
            <Text style={styles.topTime}>{item?.meta?.receiveDate ? dayjs(item?.meta?.receiveDate, FORMAT_DATE).format('DD/MM/YYYY h:mm A') : ''}</Text>
          </View>
          <View style={styles.bodyContent}>
            <Text style={styles.policyName}>{item?.name}</Text>
            <Text style={styles.policyNumber}>{t('home.letter.title.claim.case') + (item?.requestNo || '')}</Text>
            <TouchableOpacity
              style={styles.hyperLink}
              onPress={() => {
                dowloadDocument(item);
              }}
            >
              <Text style={styles.policyConfirmation}>{item?.name}</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  };

  const renderFooterLoading = () => {
    return <InnerLoading gifStyle={styles.loadMoreGif} />;
  };

  const handleLoadMore = () => {
    if (isLoading) return;
    if (isLoadMore) {
      const paging = { pageSize: 10, pageIndex: filterParams?.paging?.pageIndex + 1 };
      setFilterParams({ ...filterParams, paging });
      dispatch(loadMoreClaimELetter({ ...filterParams, paging }));
    }
  };

  const onRefreshClaimList = async () => {
    if (!loginStatus.isLoged) return;
    const handleRefreshData = () =>
      new Promise<void>((resolve, reject) => dispatch(pullToRefreshClaimList(initialParams, resolve, reject)));
    try {
      setRefreshing(true);
      setFilterParams({ ...initialParams });
      await handleRefreshData();
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  useEffect(() => {
    if (loginStatus.isLoged) {
      dispatch(setLoadMoreClaimELetter(true));
      dispatch(getClaimELetterList({ ...filterParams }));
    }
    return () => {
      dispatch(saveClaimELetterList([]));
      dispatch(setLoadMoreClaimELetter(true));
      setFilterParams(initialParams);
    };
  }, [loginStatus?.isLoged]);

  // useEffect(() => {
  //   if (!loginStatus.isLoged) {
  //     dispatch(saveClaimELetterList([]));
  //     dispatch(setLoadMoreClaimELetter(true));
  //     setFilterParams(initialParams);
  //   }
  // }, [loginStatus.isLoged]);

  if (!(claimELettetList?.length > 0) && isLoading) {
    return (
      <View style={styles.wrapperLoading}>
        <InnerLoading gifStyle={styles.loadGif} />
      </View>
    );
  }

  return (
    <SafeAreaView style={styles.flex1}>
      {claimELettetList?.length > 0 ? (
        <View style={styles.container}>
          <FlatList
            style={styles.scrollContent}
            data={claimELettetList.filter( (item: MODEL.IELetter) => item.url )}
            keyExtractor={item => item.id}
            showsVerticalScrollIndicator={false}
            renderItem={renderClaimELetter}
            ListFooterComponent={isLoading ? renderFooterLoading() : <View style={styles.mb20} />}
            onEndReachedThreshold={0.01}
            onEndReached={handleLoadMore}
            refreshControl={<RefreshControl tintColor={theme.colors.primary} refreshing={isRefreshing} onRefresh={onRefreshClaimList} />}
          />
          <Loading visible={documentLoading} dismissAction={onDismissLoading} />
          <ModalConfirmError
            ref={modalDocumentErrorRef}
            subtitle={t('common.text.subtitle.download')}
            title={t('common.text.title.download')}
            btnText={t('common.text.cancel.download')}
            onConfirm={onCancelDocumentErrorModal}
          />
        </View>
      ) : (
        <View style={styles.wrapperImageNodata}>
          <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
          <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
        </View>
      )}
    </SafeAreaView>
  );
};

export default ClaimELetter;
