/**
 *
 * AEMResponseParser
 * @format
 * @flow
 *
 */

import {
  ITEMS_KEY,
  ROOT_KEY,
  TYPE_KEY,
  CONTENT_FRAGMENT_LIST,
  HEALTH_REPORT,
  HEALTH_LIST,
  HEALTH_VITALITY_AGE,
  HEALTH_REPORT_CONTENTS,
} from './config';

import type { RawJson, RawContentFragment, RawContentFragmentList } from './types';

const isKeyMatched = (key: string): boolean => {
  return [HEALTH_REPORT, HEALTH_LIST, HEALTH_VITALITY_AGE, HEALTH_REPORT_CONTENTS].includes(key);
};

const convertToJson = (str?: string): Object | string | undefined => {
  try {
    return JSON.parse(str || '');
  } catch {
    return str;
  }
};

// Using content fragment id as key of parsed content fragment object
const parseContentFragmentList = (
  keyName: string,
  data: RawContentFragmentList,
  takeTitle: boolean,
): { [keyName: string]: { [id: string]: Object } } => {
  const { items } = data;
  let parsedCardContent = {};
  if (items) {
    // Item property using key as key, append title if takeTitle true
    const parsedItems: Array<Object> = items.map(item => {
      const { elements, title } = item;
      let parsedItem = {};
      const keys = Object.keys(elements);
      keys.forEach(key => {
        const { value } = elements[key];
        parsedItem = Object.assign(parsedItem, { [key]: convertToJson(value) });
      });
      return takeTitle ? Object.assign(parsedItem, { title }) : parsedItem;
    });
    parsedItems.forEach(parsedItem => {
      const { id }: { id?: string } = parsedItem;
      // !  It is possible that if multiple parsedItem don't have id or have same key,
      // !  the item will be overrided
      parsedCardContent = Object.assign(parsedCardContent, {
        [id || '']: parsedItem,
      });
    });
  }
  return { [keyName]: parsedCardContent };
};

// Using key as property key
const parseContentFragment = (keyName: string, data: RawContentFragment): { [keyName: string]: Object } => {
  const { elements } = data;
  let content = {};
  if (elements) {
    const keys = Object.keys(elements);
    keys.forEach(key => {
      const { value } = elements[key];
      content = Object.assign(content, { [key]: convertToJson(value) });
    });
  }
  return { [keyName]: content };
};

const isContentFragmentList = (contentType: string): boolean => {
  return contentType.includes(CONTENT_FRAGMENT_LIST);
};

const parseNewAEMContent = (data: RawJson, takeTitle: boolean = false): { [title: string]: { [keyName: string]: Object | Object[] } } => {
  const { title } = data;
  let content = {};
  function parseContent(json: Record<string, any>) {
    const keys = Object.keys(json);
    keys.forEach(key => {
      if (key === ITEMS_KEY || key === ROOT_KEY) {
        // Recursively get into json :items > root > :items
        parseContent(json[key]);
      } else {
        // Reach content fragment or content fragment list
        const initialParseJSON: any = json[key];
        const contentType: string = initialParseJSON[TYPE_KEY];
        // Determine using which parse method
        if (contentType && isKeyMatched(key)) {
          content = Object.assign(
            content,
            isContentFragmentList(contentType)
              ? parseContentFragmentList(key, initialParseJSON, takeTitle)
              : parseContentFragment(key, initialParseJSON),
          );
        }
      }
    });
  }
  parseContent(data);

  return { [title]: content };
};

const AEMVitalityResponseParser = {
  parseNewAEMContent,
};

export default AEMVitalityResponseParser;
