import React, { useLayoutEffect, useRef } from 'react';
import { Alert, Animated, Linking, SafeAreaView, ScrollView, TouchableOpacity, View } from 'react-native';
import { useNavigation, useRoute } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp, StackScreenProps } from '@react-navigation/stack';
import { Button, Icon, NativeModuleClient, Text, useTheme } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import APP_ENV from '@/config/app.config';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { a11y } from '@/utils/utils';
import { Theme } from '@/config/themes.config';
import useStyles, { CARD_ITEM_MARGIN_RIGHT, CARD_WIDTH } from './styles';
import ECardCanvas from '../ECardCanvas';
import ECardModal, { ECardModalRef } from '../ECardModal';
import SlidingDot from '@/pages/Onboarding/components/Pagination';
import WebView from '@/components/WebView';
import dayjs from 'dayjs';
import { ECARD_TYPES, FORMAT_DATE } from '@/utils/constants';
import CustomText from '@/components/Text';
import ROUTE_PATH from '@/navigators/routePath';

type ECardDetailNavigationProp = StackNavigationProp<TStackParamList, 'ECardDetail'>;
type ECardDetailNativeStackProps = StackScreenProps<TStackParamList, 'ECardDetail'>;
type ECardDetailRouteProps = ECardDetailNativeStackProps['route'];
const ECardDetail = () => {
  const navigation = useNavigation<ECardDetailNavigationProp>();
  const route = useRoute<ECardDetailRouteProps>();
  const { ecardInfo, isTeladoc } = route.params || {};
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const ecardModalRef = useRef<null | ECardModalRef>(null);
  const WebviewRef = useRef<any>(null);
  const scrollX = useRef(new Animated.Value(0)).current;

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <CustomText style={HEADER_TITLE}>{t('ecard.detail.header.title')}</CustomText>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('ECardDetail.btnHeaderGoback')}
          style={styles.marginLeft16}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" color={theme.colors.white} size={24} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  const openNetworks = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ECARD_DETAIL,
      unixTimestamp: dayjs().unix(),
      buttonText: 'See networks',
    });
    WebviewRef?.current?.openURL(t('common.ecard.hospital.url'));
  };

  const openCallCenter = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ECARD_DETAIL,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Call 1900-633-380',
    });
    
    Alert.alert(t('ecard.detail.alert.message'), '', [
      {
        text: t('ecard.detail.alert.button.cancel'),
      },
      {
        text: t('ecard.detail.alert.button.call'),
        onPress: () => {
          Linking.openURL(`tel:${t('ecard.back.header.phone').replace(/\s/g, '')}`);
        },
      },
    ]);
  };

  const renderCardInfo = () => {
    return (
      <View style={styles.marginHorizontal16}>
        <View>
          { isTeladoc && <Text style={styles.font14}>{t('ecard.detail.teladoc.subtitle')}</Text> }
          <Text.Bold style={{ ...styles.font16, ...styles.cardInfoTitle }}>{isTeladoc ? t('ecard.detail.teladoc.medix') : (ecardInfo?.productGroup === ECARD_TYPES.PRODUCT_FH ? t('ecard.detail.text.heathInsure') : ecardInfo?.productName)}</Text.Bold>
          { isTeladoc && <View style={styles.divider} /> }
          <View style={styles.cardRow}>
            <Text style={[styles.font14, styles.width30]}>{t('ecard.detail.label.name')}</Text>
            <Text.Bold style={{ ...styles.font14, ...styles.name }}>{ecardInfo?.fullName}</Text.Bold>
          </View>
          { isTeladoc && <View style={styles.cardRow}>
            <Text style={styles.font14}>{t('ecard.detail.label.idNumber')}</Text>
            <Text.Bold style={styles.font14}>{ecardInfo?.idNumber}</Text.Bold>
          </View> }
          <View style={styles.cardRow}>
            <Text style={styles.font14}>{t('ecard.detail.label.policyNumber')}</Text>
            <Text.Bold style={styles.font14}>{ecardInfo?.policyNumber}</Text.Bold>
          </View>
          <View style={styles.cardRow}>
            <Text style={styles.font14}>{t('ecard.detail.label.effectiveDate')}</Text>
            <Text.Bold style={styles.font14}>
              {ecardInfo?.effectiveDate && dayjs(ecardInfo?.effectiveDate, FORMAT_DATE).format('DD/MM/YYYY')}
            </Text.Bold>
          </View>
        </View>
        { !isTeladoc && <>
          <View style={styles.divider} />
          <Text style={{ ...styles.font14, ...styles.cardInfoFooter }}>{t('ecard.detail.footerText')}</Text>
        </> }
      </View>
    );
  };

  const renderButtons = () => {
    return (
      <View style={[styles.marginHorizontal16, styles.buttonContainer]}>
        <Button
          style={styles.button}
          text={t('ecard.detail.button.networks')}
          onPress={openNetworks}
          {...a11y('ECardDetail.btnSeeNetworks')}
        />
        <Button
          style={styles.button}
          text={t('ecard.detail.button.assistance')}
          onPress={openCallCenter}
          type="secondary"
          {...a11y('ECardDetail.btnGetAssistance')}
        />
      </View>
    );
  };

  const renderSlidePoints = () => {
    return (
      <SlidingDot
        gapWidth={32}
        marginHorizontal={8}
        slidingIndicatorStyle={styles.circle}
        dotStyle={styles.circle}
        containerStyle={{ marginBottom: 16 }}
        data={[{}, {}]}
        scrollX={scrollX}
        dotSize={10}
      />
    );
  };

  const showModal = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ECARD_DETAIL,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Modal Ecard',
    });
    ecardModalRef?.current?.setVisibleModal(true);
  };

  return (
    <SafeAreaView style={styles.flex1}>
      <ScrollView style={styles.container}>
        <Text style={styles.guidelineText}>{t('ecard.detail.guideline')}</Text>
        <ScrollView
          {...a11y('ECardDetail.Sliding.scrollViewAnimate')}
          contentContainerStyle={styles.scrollViewContentContainer}
          disableIntervalMomentum={true}
          decelerationRate={0}
          snapToInterval={CARD_WIDTH + CARD_ITEM_MARGIN_RIGHT}
          showsHorizontalScrollIndicator={false}
          horizontal={!isTeladoc}
          pagingEnabled
          scrollEventThrottle={16}
          onScroll={Animated.event([{ nativeEvent: { contentOffset: { x: scrollX } } }], { useNativeDriver: false })}
        >
          <ECardCanvas
            type="front"
            ecardInfo={ecardInfo}
            cardContainerStyle={styles.marginRight8}
            onPress={showModal}
            {...a11y('ECardDetail.frontCard')}
          />
          {!isTeladoc && <ECardCanvas
            type="back"
            ecardInfo={ecardInfo}
            onPress={showModal}
            cardContainerStyle={styles.marginRight8}
            {...a11y('ECardDetail.backCard')}
          />}
        </ScrollView>
        {!isTeladoc && renderSlidePoints()}
        {renderCardInfo()}
        {!isTeladoc && renderButtons()}
      </ScrollView>
      <ECardModal
        ref={ecardModalRef}
        frontCard={<ECardCanvas sizeType='rotate' ecardInfo={ecardInfo} type="front" />}
        backCard={!isTeladoc && <ECardCanvas sizeType='rotate' type="back" ecardInfo={ecardInfo} />}
        isTeladoc={isTeladoc}
      />
      <WebView ref={WebviewRef} />
    </SafeAreaView>
  );
};

export default ECardDetail;
