import axios, { AxiosError, AxiosInstance, AxiosResponse, AxiosRequestConfig } from 'axios';

export default class RequestITunes {
  protected instance: AxiosInstance;

  private TIME_OUT: number = 60000;

  public constructor(baseURL: string, headers = {}) {
    this.instance = axios.create({
      baseURL,
      timeout: this.TIME_OUT,
      headers: { 'Cache-Control': 'no-store', 'Content-Type': 'application/json', Accept: 'application/json', ...headers },
    });
    this.initializeRequestInterceptor();
    this.initializeResponseInterceptor();
  }

  private initializeRequestInterceptor = () => {
    this.instance.interceptors.request.use(this.handleRequest);
  };

  private initializeResponseInterceptor = () => {
    this.instance.interceptors.response.use((response: AxiosResponse<any>) => response.data, this.handleError);
  };

  private handleRequest = async (config: AxiosRequestConfig) => {
    return config;
  };

  private handleError = async (error: AxiosError) => {
    throw error;
  };
}
