import APP_ENV from '@/config/app.config';
import jsEventEmitter from '@/utils/jsEventEmitter';

type Nullable<T> = T | null;

export interface IStatusPage {
  apiKey?: Nullable<string>;
  format?: Nullable<string>;
  pageId?: Nullable<string>;
  version?: Nullable<string>;
  secure?: Nullable<boolean>;
  protocol?: Nullable<string>;
  host?: Nullable<string>;
  host_with_port_and_protocol?: Nullable<string>;
  test?: boolean;
}

class StatusPage {
  apiKey?: Nullable<string>;

  format?: Nullable<string>;

  pageId?: Nullable<string>;

  version?: Nullable<string>;

  secure?: Nullable<boolean>;

  protocol?: Nullable<string>;

  host?: Nullable<string>;

  host_with_port_and_protocol: Nullable<string>;

  test?: boolean;

  constructor(params: IStatusPage) {
    this.apiKey = params.apiKey || null;
    this.format = params.format || 'json';
    this.pageId = params.pageId;
    this.version = params.version || 'v2';
    this.secure = !!params.secure;
    this.protocol = this.secure ? 'https' : 'http';
    this.host = params.host || 'statuspage.io';
    this.host_with_port_and_protocol = params.test ? '' : this.protocol + '://' + this.pageId + '.' + this.host;
  }

  emitIncident(value: {hasIncident: boolean, status?: string, title?:string, description?: string}) {
    jsEventEmitter.emit('changedIncidentStatus', value);
  }

  emitMaintenance(value: {hasMaintenance: boolean, status?: string, title?:string, description?: string, startTime?: string, endTime?: string}) {
    jsEventEmitter.emit('changedMaintenanceStatus', value);
  }

  subscribeIncident(listener: (...args: any[]) => void) {
    jsEventEmitter.addListener('changedIncidentStatus', listener);
    return () => {
      jsEventEmitter.removeListener('changedIncidentStatus', listener);
    }
  }

  subscribeMaintenance(listener: (...args: any[]) => void) {
    jsEventEmitter.addListener('changedMaintenanceStatus', listener);
    return () => {
      jsEventEmitter.removeListener('changedMaintenanceStatus', listener);
    }
  }

  serialize(e: string[], t?: string): string {
    const s = [],
      r = { sms: 'email_sms', webhook: 'endpoint' };
    for (let o in e)
      if (o !== 'to_sentence') {
        const i = o;
        o = o in r ? (r as any)[o] : o;
        const a = t ? t + '[' + o + ']' : o,
          n = (e as any)[i];
        s.push(typeof n === 'object' ? this.serialize(n, a) : encodeURIComponent(a) + '=' + encodeURIComponent(n));
      }
    return s.join('&');
  }

  createStatusPageCORSRequest(e: any, t: any) {
    let s: any = new XMLHttpRequest();
    return (
      "withCredentials" in s
        ? s.open(e, t, !0)
        : typeof XDomainRequest !== "undefined"  
        ? (s = new XDomainRequest()).open(e, t)
        : (s = null),
      s
    );
  }

  executeRequestAndCallbackWithResponse(e: any) {
    if (!e.path) throw new Error("A path is required to make a request");
    const t = e.path,
      s = e.method || "GET",
      r = e.success || null,
      o = e.error || this.error,
      i =
        this.host_with_port_and_protocol +
        "/api/" +
        this.version +
        "/" +
        t +
        "." +
        this.format,
      a = this.createStatusPageCORSRequest(s, i);
    if (a)
      if (
        (this.apiKey &&
          (console.log(
            "!!! API KEY IN USE - REMOVE BEFORE DEPLOYING TO PRODUCTION !!!"
          ),
          console.log(
            "!!! API KEY IN USE - REMOVE BEFORE DEPLOYING TO PRODUCTION !!!"
          ),
          console.log(
            "!!! API KEY IN USE - REMOVE BEFORE DEPLOYING TO PRODUCTION !!!"
          ),
          a.setRequestHeader("Authorization", "OAuth " + this.apiKey)),
        (a.onload = function () {
          const jsonResult = JSON.parse(a.responseText);
          r && r(jsonResult);
        }),
        (a.onerror = o),
         s === "POST" || s === "DELETE")
      ) {
        const n = e.data || {};
        a.setRequestHeader("Content-type", "application/x-www-form-urlencoded"),
          a.send(this.serialize(n));
      } else a.send();
  }

  get(e: any, t: any) {
    if (((t = t || {}), !e)) throw new Error("Path is required.");
    if (!t.success) throw new Error("Success Callback is required.");
    const s = t.success || {},
      r = t.error || {};
    this.executeRequestAndCallbackWithResponse({
      path: e,
      success: s,
      error: r,
      method: "GET",
    });
  }

  post(e: any, t: any) {
    if (((t = t || {}), !e)) throw new Error("Path is required.");
    let s: any = {};
    if (e === "subscribers") {
      if (!t.subscriber) throw new Error("Subscriber is required to post.");
      s.subscriber = t.subscriber;
    } else {
      if (!t.data) throw new Error("Data is required to post.");
      s = t.data;
    }
    const r = t.success || {},
      o = t.error || {};
    this.executeRequestAndCallbackWithResponse({
      data: s,
      path: e,
      success: r,
      error: o,
      method: "POST",
    });
  }

  delete(e: any, t: any) {
    if (((t = t || {}), !e)) throw new Error("Path is required.");
    if (!t.subscriber) throw new Error("Data is required to delete.");
    let s: any = {};
    e === "subscribers" ? (s.subscriber = t.subscriber) : (s = t.data);
    const r = t.success || {},
      o = t.error || {};
    this.executeRequestAndCallbackWithResponse({
      data: s,
      path: e,
      success: r,
      error: o,
      method: "DELETE",
    });
  }

  error() {
    console.log("There was an error with your request");
  }

  summary(e: any) {
    this.get("summary", e);
  }

  status(e: any) {
    this.get("status", e);
  }

  components(e: any) {
    this.get("components", e);
  }
  
  incidents(e: any) {
    switch (e.filter) {
      case "unresolved":
        this.get("incidents/unresolved", e);
        break;
      case "resolved":
        this.get("incidents/resolved", e);
        break;
      default:
        this.get("incidents", e);
    }
  }

  scheduled_maintenances(e: any) {
    switch (e.filter) {
      case "active":
        this.get("scheduled-maintenances/active", e);
        break;
      case "upcoming":
        this.get("scheduled-maintenances/upcoming", e);
        break;
      default:
        this.get("scheduled-maintenances", e);
    }
  }

  subscribe(e: any) {
    if (!e || !e.subscriber)
      throw new Error("A subscriber object is required.");
    this.post("subscribers", e);
  }

  unsubscribe(e: any) {
    if (!e || !e.subscriber)
      throw new Error("A subscriber object is required.");
    if (!e.subscriber.id)
      throw new Error(
        "You must supply a subscriber.id in order to cancel a subscription."
      );
    this.delete("subscribers", e);
  }
}

export default new StatusPage({ pageId: APP_ENV.PAGE_ID, secure: true });