import React from 'react';
import { TouchableOpacity, View } from 'react-native';
import { Icon, useTheme, Card, Text, NativeModuleClient } from 'superapp-common-component';
import ROUTE_PATH from '@/navigators/routePath';
import { Theme } from '@/config/themes.config';
import useStyles from './styles';
import ECardCanvas from '../ECardCanvas';
import { a11y } from '@/utils/utils';
import dayjs from 'dayjs';
import { ECARD_TYPES } from '@/utils/constants';
import { useTranslation } from 'react-i18next';

type ECardItemProps = {
  items?: any[],
  navigation?: any,
}
const ECardItem = (props: ECardItemProps) => {
  const { navigation, items } = props;
  const theme = useTheme() as Theme;
  const styles = useStyles();
  const { t } = useTranslation();

  const renderContent = (productName: string, policyNumber: string, type?: string, productGroup?: string) => {
    return (
      <View>
        <Text.Bold style={styles.font14}>{type === ECARD_TYPES.TELA_DOC ? t('ecard.detail.teladoc.medix') : (productGroup === ECARD_TYPES.PRODUCT_FH ? t('ecard.detail.text.heathInsure') : productName)}</Text.Bold>
        <Text style={styles.font14}>{policyNumber}</Text>
      </View>
    )
  }

  return (
    <View>
      {items && items[0].fullName && <Text.Bold style={{...styles.font14, ...styles.marginBottom8}}>{items[0].fullName}</Text.Bold>}
      {
        items?.map((card: any, index: number) => {
          return (
            <TouchableOpacity
              onPress={() => {
                NativeModuleClient.AdobeExperience.trackCard({
                  screen: ROUTE_PATH.ECARD,
                  // stepName: index.toString(),
                  unixTimestamp: dayjs().unix(),
                  cardTitle: items?.[index]?.fullName,
                });
                navigation.navigate(ROUTE_PATH.ECARD_DETAIL, {ecardInfo: card, isTeladoc: card?.type === ECARD_TYPES.TELA_DOC});
              }}
              {...a11y(`ECard.ecardItem.${card?.partyId}.${index}`)}
              key={index}
            >
              <Card 
                cardContainerStyle={[styles.removeShadow, styles.marginBottom16]}
                cardContentStyle={styles.cardContentStyle}
                left={<View style={styles.cardWrapper}><ECardCanvas sizeType='scale' ecardInfo={card} cardContainerStyle={styles.cardStyle}/></View>}
                content={renderContent(card.productName, card.policyNumber, card?.type, card?.productGroup)}
                centerContentStyle={[styles.centerContentStyle, styles.marginHorizontalCard]}
                leftContentStyle={[styles.leftContentStyle, styles.marginHorizontalCard]}
                right={<Icon name='Next'color={theme.colors.primary} />}
                rightContentStyle={[styles.jusFlexEnd, styles.marginHorizontalCard]}
              />
            </TouchableOpacity>
          )
        })
      }
    </View>
  );
};

export default ECardItem;
