import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

export type ELetterParam = {
  paging: {
    pageIndex: number;
    pageSize: number;
  };
  searchValue: string;
  sorting: {
    sortBy: string;
    ascending: boolean;
  };
  requestType: string;
};

export type ServiceRequestParam = {
  paging: {
    pageIndex: number;
    pageSize: number;
  };
  policyNumbers?: string[];
};

class UserService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ONE_CUSTOMER);
  }

  public getProfileService = () => this.instance.post<MODEL.IProfileResponse>('/user/profile');

  public getPoliciesService = (params?: any) => this.instance.post<MODEL.IPoliciesResponse>('/user/policies', { ...params });

  public getPaymentTransactionService = (params?: { policyNumbers?: string[] }) =>
    this.instance.post<MODEL.IPoliciesResponse>('/user/transaction', { ...params });

  public getELetter = (params: ELetterParam) => this.instance.post<MODEL.IELetterResponse>('/user/documents', { ...params });

  public getECardsService = (params?: any) => this.instance.post<MODEL.IECardResponse>('/user/e-cards', { ...params });

  public getServiceRequest = (params?: ServiceRequestParam) =>
    this.instance.post<MODEL.IServiceRequestResponse>('/user/service-request', { ...params });

  public getMembershipService = () => this.instance.post<MODEL.IMembershipResponse>('/user/membership');

  public submitEConsentService = (params: { otpValue: string; otpId: string }) =>
    this.instance.post<MODEL.IMembershipResponse>('/user/membership/submit-econsent', params);

  public userTrackingService = (params: MODEL.IUserTracking) => this.instance.post('/user/tracking', params);
}

export default new UserService();
