import React, { useCallback, useEffect, useLayoutEffect, useRef, useState } from 'react';
import {
  View,
  ScrollView,
  Dimensions,
  ImageBackground,
  Image,
  TouchableOpacity,
  SafeAreaView,
  Platform,
  UIManager,
  LayoutAnimation,
} from 'react-native';
import dayjs from 'dayjs';
import _get from 'lodash/get';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import NetInfo from '@react-native-community/netinfo';
import { useNavigation } from '@react-navigation/native';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import { Button, Icon, NativeModuleClient } from 'superapp-common-component';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';

import Text from '@/components/Text';
import APP_ENV from '@/config/app.config';
import { getItem } from '@/utils/storage';
import { a11y, isEmpty } from '@/utils/utils';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { EVENTS_USER_TRACKING, STORAGE_NAME } from '@/utils/constants';
import NoConnection from '@/components/NoConnection';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import WebView, { TWebViewUrlRef } from '@/components/WebView';
import { makeSelectTierVip, makeSelectVipRank } from '@/stores/global/selectors';
import { userTracking } from '@/stores/global/actions';

import styles from './styles';
import ModalNote, { TModalNoteRef } from './components/ModalNote';
import { IContentLang, ISliderTierVip, TItem, TPrivilege } from './config';

if (Platform.OS === 'android') {
  if (UIManager.setLayoutAnimationEnabledExperimental) {
    UIManager.setLayoutAnimationEnabledExperimental(true);
  }
}

export type TierVipNavigationProp = StackNavigationProp<TStackParamList, 'TierVip'>;

const SkeletonCard = () => (
  <View style={styles.skeletonCustomer}>
    <SkeletonPlaceholder>
      <SkeletonPlaceholder.Item marginLeft={16} marginTop={16}>
        <SkeletonPlaceholder.Item width={200} height={20} />
        <SkeletonPlaceholder.Item marginTop={6} width={250} height={20} />
        <SkeletonPlaceholder.Item marginTop={16} width={200} height={20} />
        <SkeletonPlaceholder.Item marginTop={8} width={260} height={20} />
        <SkeletonPlaceholder.Item marginTop={8} width={280} height={20} />
      </SkeletonPlaceholder.Item>
    </SkeletonPlaceholder>
  </View>
);

const TierVip = () => {
  const { t, i18n } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const dispatch = useDispatch();
  const webViewRef = useRef<TWebViewUrlRef>(null);
  const tierVip = useSelector(makeSelectTierVip());
  const modalNoteRef = useRef<TModalNoteRef>(null);
  const [currentLang, setCurrentLang] = useState('');
  const [content, setContent] = useState<IContentLang>();
  const [privilege, setPrivilege] = useState<TPrivilege>();
  const navigation = useNavigation<TierVipNavigationProp>();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const vipRank = useSelector(makeSelectVipRank());
  const scrollViewRef = useRef(null);

  const onPressViewDetail = useCallback(() => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.TIER_VIP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'View Detail',
    });
    dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.VIEW_CUSTOMER_ICARE, timestamp: dayjs().unix() }));

    if (content?.customerService.url) {
      webViewRef.current?.openURL(content?.customerService.url);
    }
  }, [content?.customerService.url]);

  const onPressNote = useCallback(() => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.TIER_VIP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Note',
    });
    modalNoteRef.current?.open();
  }, [privilege?.note]);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('home.title.header.tier.vip')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          style={styles.marginLeft16}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
          {...a11y('TierVip.iconBackHeaderLeft')}
        >
          <Icon name="Arrow-Left" size={24} color="#ffffff" />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useEffect(() => {
    if (scrollViewRef.current && privilege && content && vipRank) {
      const currentIndex = content?.slider?.findIndex((x: any) => x.rankName?.toLocaleLowerCase()?.split(' ')?.join('') === vipRank);
      if (currentIndex)
        (scrollViewRef.current as any).scrollTo({ x: (Dimensions.get('window').width -32) * currentIndex, y: 0, animated: true });
    }
  }, [scrollViewRef.current, content]);

  useEffect(() => {
    if (!isEmpty(tierVip)) {
      (async () => {
        const lang = await getItem<string>(STORAGE_NAME.LANGUAGE, 'vi');
        const contentLang: IContentLang = _get(tierVip, lang);
        setCurrentLang(lang);
        setContent(contentLang);
        setPrivilege(contentLang.slider.find(x => x.rankName?.toLocaleLowerCase()?.split(' ')?.join('') === vipRank)?.privilege);
      })();
    }
  }, [i18n.language, tierVip]);

  useEffect(() => {
    LayoutAnimation.configureNext({
      duration: 300,
      create: { type: 'linear', property: 'opacity' },
      update: { type: 'spring', springDamping: 0.7 },
      delete: { type: 'linear', property: 'opacity' },
    });
  }, [privilege]);

  if (isConnected === null) return null;

  return (
    <SafeAreaView style={styles.content}>
      {isConnected ? (
        <ScrollView showsHorizontalScrollIndicator={false} showsVerticalScrollIndicator={false}>
          <Text style={styles.textSwipe}>{content?.title}</Text>
          {isEmpty(currentLang) || isEmpty(content) ? (
            <SkeletonPlaceholder>
              <View style={styles.skeletonSlider} />
            </SkeletonPlaceholder>
          ) : (
            <ScrollView
              horizontal
              pagingEnabled
              contentContainerStyle={styles.container}
              disableIntervalMomentum={true}
              decelerationRate={0}
              scrollEventThrottle={16}
              snapToInterval={Dimensions.get('window').width - 32}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
              ref={scrollViewRef}
              onScroll={({ nativeEvent }) => {
                const index = Math.round(Number(nativeEvent.contentOffset.x) / Dimensions.get('window').width);
                content && setPrivilege(content.slider[index].privilege);
              }}
            >
              <View style={styles.wrapper}>
                {content?.slider.map((item: ISliderTierVip) => (
                  <View style={styles.slide} key={item.rankName}>
                    <ImageBackground
                      style={styles.bg}
                      imageStyle={styles.imgRadius}
                      source={{ uri: `${APP_ENV.CONTENT_AEM}${item.image}` }}
                    >
                      <View style={styles.center}>
                        <View style={styles.rankContent}>
                          <Image style={styles.wheat} source={require('@/assets/images/wheat-left.png')} />
                          <View>
                            <Text style={styles.rank}>{content.rank}</Text>
                            <Text style={styles.rankName}>{item.rankName}</Text>
                          </View>
                          <Image style={styles.wheat} source={require('@/assets/images/wheat-right.png')} />
                        </View>
                        <Text style={styles.rankDescription}>{item.description}</Text>
                      </View>
                    </ImageBackground>
                  </View>
                ))}
                <View style={styles.fakeView} />
              </View>
            </ScrollView>
          )}

          {isEmpty(privilege) ? (
            <SkeletonCard />
          ) : (
            <View style={styles.card}>
              <View style={styles.innerCard}>
                <Text style={styles.title}>{privilege?.title}</Text>
                {privilege?.items.map((pri: TItem, idx: number) => (
                  <View key={idx.toString()}>
                    <View style={[styles.row, styles.top16, styles.alignCenter]}>
                      <Image style={styles.imgIcon} source={{ uri: `${APP_ENV.CONTENT_AEM}${pri.image}` }} />
                      <View style={[styles.left8, styles.shrink1]}>
                        <Text style={styles.titleItem} numberOfLines={6}>
                          {pri.title}
                        </Text>
                        <Text style={[styles.description]}>
                          {pri.description} <Text style={styles.bold}>{pri.amount}</Text>
                        </Text>
                      </View>
                    </View>
                    <View style={styles.underline} />
                  </View>
                ))}

                <TouchableOpacity style={[styles.row, styles.top16, styles.between]} onPress={onPressNote}>
                  <Text style={styles.note}>{privilege?.note?.title}</Text>
                  <Icon name="Arrow-Right" size={20} color="#E00842" />
                </TouchableOpacity>
              </View>
            </View>
          )}

          {isEmpty(content?.customerService) ? (
            <SkeletonCard />
          ) : (
            <View style={[styles.card, styles.marginBottom16]}>
              <View style={styles.innerCard}>
                <Text style={styles.title}>{content?.customerService?.title}</Text>
                <Text style={styles.description}>{content?.customerService?.description}</Text>
                {content?.customerService.items.map((item, idx) => (
                  <View style={[styles.cardInner, styles.shadown]} key={idx}>
                    <View style={[styles.row, styles.alignCenter]}>
                      <Image style={styles.icon} source={{ uri: `${APP_ENV.CONTENT_AEM}${item.image}` }} />
                      <Text style={styles.textInner} numberOfLines={2}>
                        {item.name}
                      </Text>
                    </View>
                  </View>
                ))}
                <Button
                  style={styles.btn}
                  size="small"
                  type="secondary"
                  text={content?.customerService.detail}
                  onPress={onPressViewDetail}
                />
              </View>
            </View>
          )}
          <WebView ref={webViewRef} />
          <ModalNote ref={modalNoteRef} note={privilege?.note} />
        </ScrollView>
      ) : (
        <NoConnection />
      )}
    </SafeAreaView>
  );
};

export default TierVip;
