import React, { useRef, useLayoutEffect, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Controller, useForm } from 'react-hook-form';
import {
  KeyboardAvoidingView,
  Platform,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  View,
  Keyboard,
  TouchableWithoutFeedback,
  Alert,
  Linking,
} from 'react-native';
import { Button, Icon, TextInput, theme, Text as TextCommon, NativeModuleClient } from 'superapp-common-component';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import { useNavigation } from '@react-navigation/native';
import { useDispatch, connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';
import _isEmpty from 'lodash/isEmpty';

import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import { TFotgotPasswordTypeState } from '@/pages/ForgotPassword/stores/state';
import { TConnectState } from '@/stores/types';
import { HEADER_TITLE_BGRWHITE, HITSLOP } from '@/utils/styles';
import Text from '@/components/Text';
import { FORGOT_PASS_ERROR_CODES, REGEX, ERROR_CODE_OTP } from '@/utils/constants';
import ROUTE_PATH from '@/navigators/routePath';
import { a11y, removeSpacingsAndLower } from '@/utils/utils';
import APIError from '@/utils/errors';
import NoConnection from '@/components/NoConnection';

import ModalConfirm, { ModalConfirmRef } from './components/ModalConfirm';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
// import ModalForgotSession, { ModalForgotSessionRef } from '@/components/ModalForgotSession';
import TurnstileCapcha, { TurnstileCapchaRef } from '@/components/TurnstileCapcha';
import Loading, { ILoadingRef } from '@/components/Loading';

import {
  submitVerifyNumber,
  submitVerifyPersonalIDNumber,
  saveUserInfoSubmitAction,
  setIsCountFromScreenInfoAction,
} from './stores/actions';
import { makeSelectLoading } from './stores/selectors';
import fotgotPasswordTypeReducer from './stores/reducer';
import forgotPasswordTypeSaga from './stores/saga';
import { NS_FORGOT_PASSWORD_TYPE } from './stores/constants';
import styles from './styles';

const { colors } = theme;

type ForgotPassNavigationProp = StackNavigationProp<TStackParamList, 'ForgotPassword'>;
type ForgotPasswordProps = {
  loading: boolean;
};

const ForgotPassword = (props: ForgotPasswordProps) => {
  useInjectReducer({ key: NS_FORGOT_PASSWORD_TYPE, reducer: fotgotPasswordTypeReducer });
  useInjectSaga({ key: NS_FORGOT_PASSWORD_TYPE, saga: forgotPasswordTypeSaga });

  const { loading } = props;
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const navigation = useNavigation<ForgotPassNavigationProp>();

  const modalConfirmRef = useRef<null | ModalConfirmRef>(null);
  const modalConfirmNonActiveRef = useRef<null | ModalConfirmRef>(null);
  const modalSystemBusyRef = useRef<null | ModalConfirmRef>(null);
  // const modalForgotSessionExpiredRef = useRef<ModalForgotSessionRef>(null);
  const turnstileCapchaRef = useRef<null | TurnstileCapchaRef>(null);
  const modalBlockRequest = useRef<null | ModalConfirmErrorRef>(null);
  const loadingRef = useRef<ILoadingRef>(null);

  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [isShowInputIdNo, setShowInputIdNo] = useState<boolean>(false);
  const [isContinue, setContinue] = useState<boolean>(false);
  const [passportMode, setPassportMode] = useState(false);

  const {
    control,
    handleSubmit,
    formState: { errors },
    getValues,
    // setError,
    resetField,
    clearErrors,
  } = useForm({
    defaultValues: {
      phoneNumber: '',
      identificationNumber: '',
    },
  });

  const callHotline = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_PASSWORD,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Call hotline',
    });
    Alert.alert(`${t('forgot.password.call.hotline.title')} 028-3812-2777`, '', [
      {
        text: t('forgot.password.call.hotline.cancel'),
      },
      {
        text: t('forgot.password.call.hotline.call'),
        onPress: () => {
          Linking.openURL('tel:02838122777');
        },
        style: 'cancel',
      },
    ]);
  };

  const handleError = (errorCode: string) => {
    switch (errorCode) {
      case FORGOT_PASS_ERROR_CODES.NON_ACTIVE:
        clearErrors();
        modalConfirmNonActiveRef?.current?.open();
        break;
      case FORGOT_PASS_ERROR_CODES.NOT_FOUND:
        clearErrors();
        modalConfirmRef?.current?.open();
        break;
      default:
        clearErrors();
        modalConfirmRef?.current?.open();
        break;
    }
  };

  const onChangeFieldInputUsername = () => {
    setContinue(false);
    setShowInputIdNo(false);
    resetField('identificationNumber');
  };

  const onCheckUserName = async () => {
    const phoneNumberValue = getValues('phoneNumber');
    const handleVerifyNumber = () =>
      new Promise<boolean>((resolve, reject) => dispatch(submitVerifyNumber({ phoneNumber: phoneNumberValue }, resolve, reject)));
    try {
      if (_isEmpty(phoneNumberValue)) return;
      Keyboard.dismiss();
      const response: boolean = await handleVerifyNumber();
      if (response) {
        setShowInputIdNo(true);
      }
      setContinue(true);
    } catch (error: any) {
      if (error?.status === 429) {
        modalSystemBusyRef?.current?.open();
        return;
      }
      const errorCode = (error as APIError).errorCode;
      handleError(errorCode);
    }
  };

  const onSubmitInfoForgotPassword = (token: string) => {
    turnstileCapchaRef?.current?.close();
    const callback = (errValue: string) => {
      if (!_isEmpty(errValue)) {
        if (errValue === FORGOT_PASS_ERROR_CODES.PHONE_NULL) {
          navigation.navigate(ROUTE_PATH.NO_PHONE);
        } else if (errValue === ERROR_CODE_OTP.OTP_LOCKED) {
          modalBlockRequest?.current?.open();
        } else if (errValue === FORGOT_PASS_ERROR_CODES.NON_ACTIVE) {
          modalConfirmNonActiveRef?.current?.open();
        } else {
          modalConfirmRef?.current?.open();
        }
      } else {
        dispatch(setIsCountFromScreenInfoAction(true));
        navigation.navigate(ROUTE_PATH.REQUEST_OTP, { allowCancel: false });
      }
    };
    dispatch(submitVerifyPersonalIDNumber({ captchaValue: token, otpType: 'SMS', callback }));
  };

  const handleBeforeSubmitInfo = async (data: any) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.FORGOT_PASSWORD,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });
    Keyboard.dismiss();
    await dispatch(
      saveUserInfoSubmitAction({
        username: removeSpacingsAndLower(data?.phoneNumber),
        idNo: data?.identificationNumber,
      }),
    );
    turnstileCapchaRef?.current?.open();
  };

  const hideKeyboard = () => Keyboard.dismiss();

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <TextCommon.Bold style={HEADER_TITLE_BGRWHITE}>{t('forgot.password.title')}</TextCommon.Bold>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('Forgot.btnForgotGoback')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_PASSWORD,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
          }}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" size={24} color={colors.primary.charcoal[80]} style={styles.marginLeft21} />
        </TouchableOpacity>
      ),
      headerRight: () => (
        <TouchableOpacity
          {...a11y('Forgot.btnForgotInfo')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.FORGOT_PASSWORD,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Info',
            });
          }}
          hitSlop={HITSLOP}
        >
          <Icon name="Info1" size={24} color={colors.primary.charcoal[80]} style={styles.marginRight21} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
      headerTitleStyle: styles.mainTitle,
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useEffect(() => {
    resetField('identificationNumber');
    hideKeyboard();
  }, [passportMode]);

  if (isConnected === null) return null;

  return (
    <>
      {isConnected ? (
        <View style={styles.flex1}>
          <Loading visible={loading} ref={loadingRef} />
          <SafeAreaView style={styles.safeAreaViewStyle}>
            <TouchableWithoutFeedback {...a11y('Forgot.btnHideKeyboard')} onPress={hideKeyboard}>
              <View style={StyleSheet.flatten([styles.container])}>
                <KeyboardAvoidingView
                  behavior={Platform.OS === 'ios' ? 'padding' : undefined}
                  style={styles.flex1}
                  keyboardVerticalOffset={Platform.OS === 'ios' ? 90 : 0}
                >
                  <View style={styles.modalBox}>
                    <View style={styles.flex1}>
                      <ScrollView>
                        <View style={styles.formContainer}>
                          <Text style={styles.formTitle} fontFamily="bold">
                            {t('forgot.password.form.main.title')}
                          </Text>
                          <Text style={styles.subFormTitle}>{t('forgot.password.form.sub.title')}</Text>
                          <View>
                            <Controller
                              control={control}
                              rules={{
                                required: { value: true, message: t('forgot.password.form.error.text') },
                              }}
                              render={({ field: { onChange, value } }) => (
                                <TextInput
                                  label={t('forgot.password.form.input.label')}
                                  type={errors?.phoneNumber ? 'error' : 'info'}
                                  disabled={false}
                                  // eslint-disable-next-line @typescript-eslint/no-shadow
                                  onChangeText={value => {
                                    onChangeFieldInputUsername();
                                    onChange(value);
                                  }}
                                  onBlur={onCheckUserName}
                                  value={value}
                                  message={errors?.phoneNumber && errors?.phoneNumber?.message}
                                  autoFocus
                                  id="Forgot.ipPhoneNumber"
                                />
                              )}
                              name="phoneNumber"
                            />
                          </View>
                          {isShowInputIdNo && (
                            <>
                              <View style={styles.groupButton}>
                                <TouchableOpacity
                                  onPress={() => {
                                    NativeModuleClient.AdobeExperience.trackButton({
                                      screen: ROUTE_PATH.FORGOT_PASSWORD,
                                      unixTimestamp: dayjs().unix(),
                                      buttonText: 'National ID',
                                    });
                                    setPassportMode(false);
                                  }}
                                  style={styles.buttonWrapper}
                                  {...a11y('Forgot.btnInfoIdMode')}
                                >
                                  <View style={styles.wrapperCircle}>{!passportMode && <View style={styles.circle} />}</View>
                                  <Text style={styles.buttonItemText}>{t('forgot.password.modal.personal.info.button.id')}</Text>
                                </TouchableOpacity>

                                <TouchableOpacity
                                  onPress={() => {
                                    NativeModuleClient.AdobeExperience.trackButton({
                                      screen: ROUTE_PATH.FORGOT_PASSWORD,
                                      unixTimestamp: dayjs().unix(),
                                      buttonText: 'Passport',
                                    });
                                    setPassportMode(true);
                                  }}
                                  style={styles.buttonWrapper}
                                  {...a11y('Forgot.btnInfoPasportMode')}
                                >
                                  <View style={styles.wrapperCircle}>{passportMode && <View style={styles.circle} />}</View>
                                  <Text style={styles.buttonItemText}>{t('forgot.password.modal.personal.info.button.passport')}</Text>
                                </TouchableOpacity>
                              </View>

                              <View>
                                <View>
                                  <Controller
                                    control={control}
                                    rules={{
                                      required: {
                                        value: isShowInputIdNo,
                                        message: passportMode
                                          ? t('forgot.password.modal.personal.info.guideline.passport')
                                          : t('forgot.password.modal.personal.info.guideline.id'),
                                      },
                                      pattern: {
                                        value: passportMode ? REGEX.EXACT_8_DIGITS : REGEX.EXACT_9_DIGITS_OR_12_DIGITS,
                                        message: passportMode
                                          ? t('forgot.password.modal.personal.info.guideline.passport')
                                          : t('forgot.password.modal.personal.info.guideline.id'),
                                      },
                                    }}
                                    render={({ field: { onChange, value } }) => (
                                      <TextInput
                                        required
                                        label={t('forgot.password.modal.personal.info.form.label')}
                                        type={errors?.identificationNumber ? 'error' : 'info'}
                                        disabled={false}
                                        onChangeText={onChange}
                                        value={value}
                                        message={errors?.identificationNumber && errors?.identificationNumber?.message}
                                        keyboardType={passportMode ? 'default' : 'numeric'}
                                        iconColor="#000000"
                                        id="Forgot.ipInfoIdentification"
                                      />
                                    )}
                                    name="identificationNumber"
                                  />
                                </View>
                              </View>
                            </>
                          )}
                        </View>
                      </ScrollView>
                    </View>
                    <View style={[styles.buttonBox, styles.marginBottom16]}>
                      <Button
                        text={t('forgot.password.buton.verify')}
                        onPress={handleSubmit(handleBeforeSubmitInfo)}
                        disabled={!isContinue}
                        uppercase={false}
                        type="primary"
                        labelStyle={styles.buttonText}
                        size="large"
                        style={styles.button}
                        id="Forgot.btnForgotVerify"
                      />
                    </View>
                  </View>
                </KeyboardAvoidingView>
              </View>
            </TouchableWithoutFeedback>
            <ModalConfirm
              mainTitle={t('forgot.password.modal.registration.main.title')}
              subTitle={t('forgot.password.modal.registration.sub.title')}
              topButtonText={t('forgot.password.modal.registration.button.sign')}
              bottomButtonText={t('forgot.password.modal.registration.button.try')}
              topButtonPress={() => {
                clearErrors();
                modalConfirmRef?.current?.close();
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.FORGOT_PASSWORD,
                  unixTimestamp: dayjs().unix(),
                  buttonText: 'Close modal info',
                });
              }}
              bottomButtonPress={callHotline}
              ref={modalConfirmRef}
            />
            <ModalConfirm
              mainTitle={t('forgot.password.modal.nonActive.main.title')}
              subTitle={t('forgot.password.modal.nonActive.sub.title')}
              topButtonText={t('forgot.password.modal.nonActive.button.register')}
              bottomButtonText={t('forgot.password.modal.nonActive.button.close')}
              topButtonPress={() => {
                modalConfirmNonActiveRef?.current?.close();
                navigation.navigate(ROUTE_PATH.REGISTER);
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.FORGOT_PASSWORD,
                  unixTimestamp: dayjs().unix(),
                  buttonText: 'Close modal inactive',
                });
              }}
              bottomButtonPress={() => {
                modalConfirmNonActiveRef?.current?.close();
              }}
              ref={modalConfirmNonActiveRef}
            />
            <ModalError
              ref={modalBlockRequest}
              title={t('forgot.password.modal.otp.error.title')}
              subtitle={t('forgot.password.modal.otp.error.block.request.otp')}
              btnText={t('forgot.password.modal.otp.error.button.close')}
              onConfirm={() => navigation.navigate(ROUTE_PATH.REQUEST_OTP, { allowCancel: false })}
            />
            {/* <ModalForgotSession
          ref={modalForgotSessionExpiredRef}
          handleClose={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.PERSONAL_INFO,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Close modal session',
            });
            clearErrors();
            reset();
            setShowInputIdNo(false);
          }}
        /> */}
            <ModalConfirm
              mainTitle={t('forgot.password.modal.busy.title')}
              topButtonText={t('forgot.password.modal.nonActive.button.close')}
              topButtonPress={() => {
                clearErrors();
                modalSystemBusyRef?.current?.close();
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.FORGOT_PASSWORD,
                  unixTimestamp: dayjs().unix(),
                  buttonText: 'Close modal busy',
                });
              }}
              ref={modalSystemBusyRef}
            />
          </SafeAreaView>
        </View>
      ) : (
        <View style={styles.flex1}>
          <NoConnection />
        </View>
      )}
      <TurnstileCapcha ref={turnstileCapchaRef} onSuccess={onSubmitInfoForgotPassword} onError={() => null} />
    </>
  );
};

type CreateStructuredSelectorType = {
  loading: TFotgotPasswordTypeState['loading'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
});

export default connect(mapStateToProps, null)(ForgotPassword);
