import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import { Keyboard, KeyboardAvoidingView, Platform, TouchableOpacity, View } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useTheme, SendOTP, Text, Icon, NativeModuleClient } from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';

import { HITSLOP } from '@/utils/styles';
import { TStackParamList } from '@/navigators';
import { Theme } from '@/config/themes.config';
import { a11y, isEmpty, maskedPhoneNumber } from '@/utils/utils';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import { BLOCK_SUBMIT_OTP_ERROR_TYPE, ECONSENT_ERROR, OTP_TYPE } from '@/utils/constants';
import { makeSelectProfile } from '@/stores/global/selectors';
import Loading from '@/components/Loading';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import ROUTE_PATH from '@/navigators/routePath';

import useStyles from './styles';
import decreeEConsentSaga from './stores/saga';
import decreeEconsentReducer from './stores/reducer';
import { NS_DECREE_ECONSENT } from './stores/constants';
import { makeSelectLoading, makeSelectOtpError, makeSelectSubmitError } from './stores/selectors';
import {
  optRequesetErrorAction,
  requestOtpEConsentAction,
  submitDecreeEConsentAction,
  submitDecreeEConsentErrorAction,
} from './stores/actions';

type DecreeEConsentOtpNavigationProp = StackNavigationProp<TStackParamList, 'DecreeEConsentOtp'>;

let timeRemaining: string = '';
const INIT_OTP = { otpId: '', otpValue: '' };
const OTP_VALID_IN = 120;
type TOtpValue = {
  otpId: string;
  otpValue: string;
};

const DecreeEConsentOtp = () => {
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const [seconds, setSeconds] = useState<number>(0);
  const [otp, setOtpValue] = useState<TOtpValue>(INIT_OTP);

  const navigation = useNavigation<DecreeEConsentOtpNavigationProp>();
  const profile = useSelector(makeSelectProfile());
  const loading = useSelector(makeSelectLoading());
  const optError = useSelector(makeSelectOtpError());
  const submitError = useSelector(makeSelectSubmitError());

  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalInvalidOtpRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalDisabledOtpRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalMembershipInvalid = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockRequestOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalSubmitError = useRef<null | ModalConfirmErrorRef>(null);

  useInjectSaga({ key: NS_DECREE_ECONSENT, saga: decreeEConsentSaga });
  useInjectReducer({ key: NS_DECREE_ECONSENT, reducer: decreeEconsentReducer });

  const callback = (otpResponse: MODEL.IRequestMyOtp) => {
    timeRemaining = dayjs().toISOString();
    setSeconds(OTP_VALID_IN);
    setOtpValue({ ...otp, otpId: otpResponse?.otpId });
  };

  const onRequestOtpBySms = (type: string) => {
    const params = { otpType: 'SubmitEConsent', otpSendType: type };
    dispatch(requestOtpEConsentAction({ params, callback }));
    setOtpValue(INIT_OTP);
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.DECREE_ECONSENT_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Confirm',
    });
  };

  const onHideModalInvalid = () => {
    dispatch(submitDecreeEConsentErrorAction({ errorType: '' }));
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.DECREE_ECONSENT_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close Modal',
    });
  };

  const onHideModalDisabled = () => {
    dispatch(submitDecreeEConsentErrorAction({ errorType: '' }));
    navigation.canGoBack() && navigation.goBack();
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.DECREE_ECONSENT_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close Modal Disabled OTP',
    });
  };

  const onDismissLoading = () => {
    if (!isEmpty(optError.errorType)) {
      if (optError.errorType === BLOCK_SUBMIT_OTP_ERROR_TYPE) {
        modalBlockRequestOtp?.current?.open();
      } else {
        modalErrorRef?.current?.open();
      }
      timeRemaining = '';
      dispatch(optRequesetErrorAction({ status: false, errorType: '' }));
    } else if (submitError.errorType) {
      if (submitError.errorType === ECONSENT_ERROR.MEMBERSHIP_INVALID) {
        modalMembershipInvalid?.current?.open();
      } else if (submitError.errorType === ECONSENT_ERROR.OTP_INVALID) {
        modalInvalidOtpRef?.current?.open();
      } else if (submitError.errorType === ECONSENT_ERROR.OTP_DISABLED) {
        modalDisabledOtpRef?.current?.open();
      } else {
        modalSubmitError.current?.open();
      }
      dispatch(submitDecreeEConsentErrorAction({ errorType: '' }));
    }
  };

  const onSubmitOtp = async (setClearInput?: (value: boolean) => void, setCodeState?: (value: string) => void) => {
    if (loading) return;
    if (otp && otp?.otpValue.length === 6) {
      try {
        dispatch(optRequesetErrorAction({ status: false, errorType: '' }));
        const submitEconsent = () =>
          new Promise<void>((resolve, reject) => dispatch(submitDecreeEConsentAction({ ...otp }, resolve, reject)));

        await submitEconsent();
        navigation.navigate(ROUTE_PATH.ECONSENT_SUCCESS as never);
        setClearInput?.(true);
        setOtpValue(INIT_OTP);
        setCodeState?.('');
        NativeModuleClient.AdobeExperience.trackButton({
          screen: ROUTE_PATH.DECREE_ECONSENT_OTP,
          unixTimestamp: dayjs().unix(),
          buttonText: 'SubmitDecreeEConsent',
        });
      } catch (error) {}
    } else {
      Keyboard.dismiss();
      modalErrorRef?.current?.open();
    }
  };

  useEffect(() => {
    if (timeRemaining) {
      const secondRemaining = OTP_VALID_IN - dayjs().diff(dayjs(timeRemaining), 'second');
      secondRemaining > 0 ? setSeconds(secondRemaining) : onRequestOtpBySms(OTP_TYPE.SMS);
    } else {
      onRequestOtpBySms(OTP_TYPE.SMS);
    }
  }, []);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={styles.header}>{t('home.econsent.otp')}</Text>,
      headerTitleStyle: styles.mainTitle,
      headerLeft: () => (
        <TouchableOpacity
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.DECREE_ECONSENT_OTP,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}
          {...a11y('EconsentOtp.iconBackHeaderLeft')}
        >
          <Icon name="Arrow-Left" color={theme.colors.white} size={24} style={styles.marginLeft16} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <KeyboardAvoidingView
      contentContainerStyle={{ backgroundColor: 'red' }}
      behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
      keyboardVerticalOffset={Platform.OS === 'ios' ? 100 : 0}
      style={{ flex: 1, position: 'relative' }}
    >
      <View style={styles.content}>
        <SendOTP
          textDescription={t('common.otp.textPhone', { phone: maskedPhoneNumber(profile?.mobile!) })}
          textTimeCount={t('common.otp.text.countTimeTitle')}
          submitText={t('forgot.password.modal.otp.button.verify')}
          onChange={code => setOtpValue({ ...otp, otpValue: code })}
          sendBySMS={() => onRequestOtpBySms(OTP_TYPE.SMS)}
          sendByVoice={() => onRequestOtpBySms(OTP_TYPE.VOICE)}
          onSubmit={onSubmitOtp}
          loading={loading}
          disabled={loading}
          secondNumber={seconds}
          setSecondNumber={setSeconds}
          textSMS={t('common.otp.text.resend')}
          textVoice={t('common.otp.text.viaCall')}
          textCountSecond={t('common.text.countSecond')}
          textNotSendSMS={t('common.otp.text.noSms')}
          id="DecreeEConsent.otpRequestOtp"
        />
        <Loading visible={loading} onDismiss={onDismissLoading} />
        {/* Submit OTP */}
        <ModalError
          ref={modalMembershipInvalid}
          title={t('home.econsent.otp.title')}
          subtitle={t('home.econsent.otp.membershipInvalid')}
          btnText={t('home.econsent.otp.btnClose')}
        />
        <ModalError
          ref={modalInvalidOtpRef}
          title={t('home.econsent.otp.title')}
          subtitle={t('home.econsent.otp.otpInvalid')}
          btnText={t('home.econsent.otp.btnClose')}
          onConfirm={onHideModalInvalid}
        />
        <ModalError
          ref={modalDisabledOtpRef}
          title={t('home.econsent.otp.title')}
          subtitle={t('common.message.otpDisable')}
          btnText={t('home.econsent.otp.btnClose')}
          onConfirm={onHideModalDisabled}
        />
        <ModalError
          ref={modalSubmitError}
          title={t('home.econsent.otp.title')}
          subtitle={t('home.econsent.otp.systemError')}
          btnText={t('home.econsent.otp.btnClose')}
          onConfirm={onHideModalInvalid}
        />
        {/* Request OTP */}
        <ModalError
          ref={modalBlockRequestOtp}
          title={t('migration.modal.otp.error.title')}
          subtitle={t('migration.modal.otp.error.block.request.otp')}
          btnText={t('migration.modal.otp.error.button.close')}
        />
        <ModalError
          ref={modalErrorRef}
          title={t('migration.modal.otp.error.title')}
          subtitle={t('migration.modal.otp.error.des')}
          btnText={t('migration.modal.otp.error.button.close')}
        />
      </View>
    </KeyboardAvoidingView>
  );
};

export default DecreeEConsentOtp;
