import React from 'react';
import Fitness from './Fitness.svg';
import Smoke from './Smoke.svg';
import Vitals from './Vitals.svg';
import Nutrition from './Nutrition.svg';
import SleepAtLeast from './SleepAtLeast.svg';
import StressLevel from './StressLevel.svg';
import Lock from './Lock.svg';
import NoData from './NoData.svg';
import Outline from './Outline.svg';

export const HealthReportIcons = (
  name: string,
  extraStyle = {},
  width: string | undefined,
  height: string | undefined,
  fillColor: string | undefined,
) => {
  let widthProps = {};
  let heightProps = {};
  if (width) {
    widthProps = { width };
  }
  if (height) {
    heightProps = { height };
  }

  const icons: any = {
    Fitness: <Fitness fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Smoking: <Smoke fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Vitals: <Vitals fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Nutrition: <Nutrition fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Sleep: <SleepAtLeast fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    MentalHealth: <StressLevel fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Stress: <StressLevel fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Lock: <Lock fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    NoData: <NoData fill={fillColor} style={extraStyle} {...widthProps} {...heightProps} />,
    Outline: <Outline style={extraStyle} {...widthProps} {...heightProps} />,
  };
  return icons[name];
};

export default HealthReportIcons;
