import { createSelector } from 'reselect';

import initialState from './state';
import { NS_ELETTER_TYPE } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_ELETTER_TYPE] || initialState;
const makeSelectELetterLoading = () => createSelector(currentState, state => state.requestELetter.requestELettetLoading);
const makeSelectRequestELetter = () => createSelector(currentState, state => state.requestELetter.requestELettetList);
const makeSelectRequestIsLoadMore = () => createSelector(currentState, state => state.requestELetter.isLoadMore);
const makeSelectRequestDocumentLoading = () => createSelector(currentState, state => state.requestELetterDocument.isLoadingDocument);
const makeSelectRequestDocumentError = () => createSelector(currentState, state => state.requestELetterDocument.isError);

const makeSelectClaimELetterLoading = () => createSelector(currentState, state => state.claimELetter.claimELettetLoading);
const makeSelectClaimRequestELetter = () => createSelector(currentState, state => state.claimELetter.claimELettetList);
const makeSelectClaimRequestIsLoadMore = () => createSelector(currentState, state => state.claimELetter.isClaimLoadMore);
const makeSelectClaimDocumentLoading = () => createSelector(currentState, state => state.claimELetterDocument.isLoadingDocument);
const makeSelectClaimDocumentError = () => createSelector(currentState, state => state.claimELetterDocument.isError);

const makeSelectOtherELetterLoading = () => createSelector(currentState, state => state.otherELetter.otherELetterLoading);
const makeSelectOtherELetter = () => createSelector(currentState, state => state.otherELetter.otherELetterList);
const makeSelectOtherIsLoadMore = () => createSelector(currentState, state => state.otherELetter.isOtherLoadMore);
const makeSelectOtherDocumentLoading = () => createSelector(currentState, state => state.otherELetterDocument.isLoadingDocument);
const makeSelectOtherDocumentError = () => createSelector(currentState, state => state.otherELetterDocument.isError);

export {
  //request
  makeSelectELetterLoading,
  makeSelectRequestELetter,
  makeSelectRequestIsLoadMore,
  makeSelectRequestDocumentLoading,
  makeSelectRequestDocumentError,
  //claim
  makeSelectClaimELetterLoading,
  makeSelectClaimRequestELetter,
  makeSelectClaimRequestIsLoadMore,
  makeSelectClaimDocumentLoading,
  makeSelectClaimDocumentError,
  //other
  makeSelectOtherELetterLoading,
  makeSelectOtherELetter,
  makeSelectOtherIsLoadMore,
  makeSelectOtherDocumentLoading,
  makeSelectOtherDocumentError
};
