import React, { forwardRef, useImperativeHandle } from 'react';
import { Image, TouchableOpacity, View } from 'react-native';
import { Button, NativeModuleClient, Text } from 'superapp-common-component';
import { utils } from 'superapp-common-component';
import dayjs from 'dayjs';
import { useTranslation } from 'react-i18next';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';
import { HITSLOP } from '@/utils/styles';

import styles from '../styles';

const { a11y } = utils;

type ModalMarkReadAllPropsType = {
  onConfirm: () => void;
};

export interface ModalMarkReadAllRef {
  open: () => void;
  close: () => void;
}
const ModalMarkReadAll = (
  props: ModalMarkReadAllPropsType,
  ref: React.Ref<unknown> | undefined,
) => {
  const { t } = useTranslation();
  const { onConfirm } = props;
  const [modalsRef, modalInstance] = useModal();

  const onPressCancel = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Notification',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Cancel',
    });
    modalInstance()?.close();
  };

  const onPressConfirm = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Notification',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Confirm mark read all',
    });
    modalInstance()?.close();
    onConfirm && onConfirm();
  };

  useImperativeHandle(
    ref,
    (): ModalMarkReadAllRef => ({
      open: () => modalInstance().show(),
      close: () => modalInstance().close(),
    }),
  );

  return (
    <Modal
      ref={modalsRef}
      modalStyle={styles.modalStyles}
      overlayOpacity={0.5}
      onDismiss={() => null}>
      <View style={styles.modalContainer}>
        <View style={styles.wrapperModal}>
          <Image
            source={require('@/assets/images/warning.png')}
            style={styles.imageWarning}
          />
          <Text style={styles.modalTitle}>
            {t('common.notification.warning.title')}
          </Text>
          <Text style={styles.modalDescription}>
            {t('common.notification.warning.description')}
          </Text>
          <View style={styles.wrapperBtn}>
            <Button
              id="Button.Confirm.Mark.Read.All"
              style={styles.btnConfirm}
              text={t('common.notification.warning.confirm')}
              type="secondary"
              onPress={onPressConfirm}
            />
            <TouchableOpacity
              hitSlop={HITSLOP}
              style={styles.btnCancel}
              onPress={onPressCancel}
              {...a11y('Button.Cancel.Mark.Read.All')}>
              <Text style={styles.textCancel}>
                {t('common.notification.warning.cancel')}
              </Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalMarkReadAll);
