import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

export type TListClaimTransactionParam = {
  listLaIdCardNo: string[];
  paging: { pageNo: number; pageSize: number };
  status: string;
  haveDraft: boolean;
  sortType: string;
  searchPolicyNumbers?: string[];
};

class OneClaimService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ONE_CLAIM);
  }

  public getListLa = () => this.instance.post<MODEL.IListLaResponse>('/cms/get-list-la');

  public getDraftInfoService = () : Promise<MODEL.IClaimInfoResponse<MODEL.IDraftInfo>> => this.instance.post('/cms/get-draft');

  public checkDraftInfoService = () : Promise<MODEL.IClaimInfoResponse<MODEL.ICheckCLaimInfoResponse>> => this.instance.post('/cms/get-draft-or-updating');
  
  public resetDraftInfoService = (params: any) : Promise<MODEL.IClaimInfoResponse<MODEL.IDraftInfo>> =>   this.instance.post('/cms/reset-draft', params);

  public getListClaimTransaction = (params?: TListClaimTransactionParam) =>
    this.instance.post<MODEL.IClaimTransactionResponse>('/history/view2', { ...params });
  
  public getClaimDetailDataService = (params: any) : Promise<MODEL.IClaimTransactionResponse> => this.instance.post('/cms/get-claim-history', params);
}

export default new OneClaimService();
