import React from 'react';
import {useTranslation} from 'react-i18next';
import {
  View,
  TouchableOpacity,
  GestureResponderEvent,
  StatusBar,
  ViewStyle,
} from 'react-native';
import {useSelector} from 'react-redux';
import {theme, NativeModuleClient} from 'superapp-common-component';
import dayjs from 'dayjs';
import Animated, {
  SharedValue,
  interpolate,
  useAnimatedStyle,
} from 'react-native-reanimated';

import {a11y} from '@/utils/utils';
import Text from '@/components/Text';
import {makeSelectVipRank} from '@/stores/global/selectors';
import {makeSelectCheckLogin} from '@/stores/authentication/selectors';

import styles from './styles';
import {SCROLL_TARGET} from '@/pages/Home/styles';
import BadgeTierVip from '../BadgeTierVip';

type HeaderProps = {
  headerLeft?: any;
  scrollAnimation?: SharedValue<number>;
  onPressLogin?: () => void;
  onPressNotification?: (event: GestureResponderEvent) => void;
};

const Header: React.FC<HeaderProps> = props => {
  const {t} = useTranslation();
  const {headerLeft, scrollAnimation, onPressLogin} = props;
  const loginStatus = useSelector(makeSelectCheckLogin());
  const vipRank = useSelector(makeSelectVipRank());

  const onPressLoginButton = () => {
    if (loginStatus?.isLoged) return;
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Login',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login',
    });
    onPressLogin && onPressLogin();
  };

  const styleHeaderAnimation = useAnimatedStyle(() => {
    if (!scrollAnimation) return {};

    return {
      opacity: interpolate(
        scrollAnimation.value,
        [0, SCROLL_TARGET / 2],
        [1, 0],
      ),
    };
  }, [scrollAnimation]);

  return (
    <View style={styles.container}>
      <StatusBar
        backgroundColor={theme.colors.primary.red[100]}
        barStyle="light-content"
      />
      <View style={styles.header}>
        {!loginStatus?.isLoged && (
          <Animated.View style={styleHeaderAnimation}>
            <TouchableOpacity
              style={styles.buttonLogin}
              onPress={onPressLoginButton}
              {...a11y('Header.btnShowLoginModal')}>
              <Text style={styles.textLogin}>{t('home.title')}</Text>
            </TouchableOpacity>
          </Animated.View>
        )}
        {loginStatus?.isLoged === true && headerLeft()}
        {loginStatus?.isLoged && !!vipRank && (
          <Animated.View style={styleHeaderAnimation}>
            <BadgeTierVip rank={vipRank} />
          </Animated.View>
        )}
      </View>
    </View>
  );
};

export default Header;
