import React, { useEffect, useRef, useState } from 'react';
import { Image, SafeAreaView, View, TouchableOpacity, FlatList, RefreshControl } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { Icon, InnerLoading, Loading, useTheme, NativeModuleClient, } from 'superapp-common-component';
import { StackNavigationProp } from '@react-navigation/stack';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';

import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { TStackParamList } from '@/navigators';
import { Theme } from '@/config/themes.config';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';

import {
  makeSelectELetterLoading,
  makeSelectRequestDocumentError,
  makeSelectRequestDocumentLoading,
  makeSelectRequestELetter,
  makeSelectRequestIsLoadMore,
} from '../../stores/selectors';
import {
  getELetterList,
  getRequestELetterDocument,
  loadingRequestELetterDocumentSuccess,
  loadMoreRequestELetter,
  putRequestELetterDocumentError,
  saveELetterList,
  setLoadMoreRequestELetter,
  pullToRefreshRequestList,
} from '../../stores/actions';
import useStyles from './styles';
import { openFilePdf } from '@/utils/handleFile';

type RequestELetterNavigationProp = StackNavigationProp<TStackParamList, 'ELetter'>;
type RequestELetterFilter = {
  paging: {
    pageIndex: number;
    pageSize: number;
  };
  searchValue: string;
  sorting: {
    sortBy: string;
    ascending: boolean;
  };
  requestType: string;
};

const ELETTER_STATUS = {
  READ: 'read',
  UNREAD: 'unread',
};

const ELETTER_TYPE = {
  CLAIM_REQUEST: 'claim_request',
  PAYMENT_PREMIUM: 'payment_premium',
  PAYMENT_VITALITY_FEE: 'payment_vitality_fee',
  SUBMIT_REQUEST: 'submit_request',
};

const FORMAT_DATE = 'YYYY-MM-DD:HH:mm:ssZZ';

const initialParams = {
  paging: {
    pageIndex: 0,
    pageSize: 10,
  },
  searchValue: '',
  sorting: {
    sortBy: 'MetaData2.ReceiveDate',
    ascending: false,
  },
  requestType: 'POS',
};

const RequestELetter = () => {
  const navigation = useNavigation<RequestELetterNavigationProp>();
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const theme = useTheme() as Theme;
  const loginStatus = useSelector(makeSelectCheckLogin());
  const requestELettetList = useSelector(makeSelectRequestELetter());
  const isLoading = useSelector(makeSelectELetterLoading());
  const isLoadMore = useSelector(makeSelectRequestIsLoadMore());
  const documentLoading = useSelector(makeSelectRequestDocumentLoading());
  const modalDocumentErrorRef = useRef<ModalConfirmErrorRef | null>(null);
  const documentError = useSelector(makeSelectRequestDocumentError());
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const [pathFilePdf, setPathFilepdf] = useState<string>('');
  const [filterParams, setFilterParams] = useState<RequestELetterFilter>(initialParams);
  const styles = useStyles(theme);

  const dowloadDocument = async (item: MODEL.IELetter) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: `${ROUTE_PATH.ELETTER}:Request`,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Download document',
    });

    // if (!item?.url) return;
    const callback = (url?: string) => setPathFilepdf(url || '')
    const dispatchDowloadDocument = () =>
      new Promise<void>((resolve, reject) => dispatch(getRequestELetterDocument({ url: item.url, name: item?.name, id: item?.id, contentType: item?.contentType }, (url?: string) => { callback(url); resolve()}, reject)));
    try {
      dispatch(putRequestELetterDocumentError(false));
      await dispatchDowloadDocument();
    } catch (error) {
      dispatch(putRequestELetterDocumentError(true));
    } finally {
      dispatch(loadingRequestELetterDocumentSuccess());
    }
  };

  const onCancelDocumentErrorModal = () => {
    dispatch(putRequestELetterDocumentError(false));
  };

  const onDismissLoading = () => {
    if (documentError) {
      modalDocumentErrorRef?.current?.open();
    }
    // if (pathFilePdf) {
    //   setTimeout(() => {
    //     openFilePdf(pathFilePdf)
    //     setPathFilepdf('')
    //   }, 100);
    // }
  };

  const onRefreshRequestList = async () => {
    if (!loginStatus.isLoged) return;
    setFilterParams(initialParams);
    const handleRefreshData = () =>
      new Promise<void>((resolve, reject) => dispatch(pullToRefreshRequestList(initialParams, resolve, reject)));
    try {
      setRefreshing(true);
      await handleRefreshData();
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const renderRequestELetter = ({ item }: { item: MODEL.IELetter }) => {
    return (
      <View style={styles.wrapperLetter} key={item.id}>
        <View style={[item.status === ELETTER_STATUS.UNREAD && styles.overlay]}>
          <View style={styles.topContent}>
            <View style={styles.topLeftContent}>
              <Icon name="Report" size={24} color="#5B636B" />
              <Text style={styles.topTitle}>{t('home.letter.title.request')}</Text>
              {item.status === ELETTER_STATUS.READ && <View style={styles.circle} />}
            </View>
            <Text style={styles.topTime}>{item?.meta?.receiveDate ? dayjs(item?.meta?.receiveDate, FORMAT_DATE).format('DD/MM/YYYY h:mm A') : ''}</Text>
          </View>
          <View style={styles.bodyContent}>
            <Text style={styles.policyName}>{item?.name}</Text>
            <Text style={styles.policyNumber}>
              {t('home.letter.title.request.no')}
              {item?.requestNo}
            </Text>
            <View style={styles.wrapperLinkDownLoad}>
              <TouchableOpacity
                style={styles.hyperLink}
                onPress={() => {
                  dowloadDocument(item);
                }}
              >
                <Text style={styles.policyConfirmation}>{item?.name}</Text>
              </TouchableOpacity>
              <View style={styles.flex1} />
            </View>
          </View>
        </View>
      </View>
    );
  };

  const renderFooterLoading = () => {
    return <InnerLoading gifStyle={styles.loadMoreGif} />;
  };

  const handleLoadMore = () => {
    if (isLoading) return;
    if (isLoadMore) {
      const paging = { pageSize: 10, pageIndex: filterParams?.paging?.pageIndex + 1 };
      setFilterParams({ ...filterParams, paging });
      dispatch(loadMoreRequestELetter({ ...filterParams, paging }));
    }
  };

  useEffect(() => {
    if (loginStatus.isLoged) {
      dispatch(getELetterList({ ...filterParams }));
    }
    return () => {
      dispatch(saveELetterList([]));
      dispatch(setLoadMoreRequestELetter(true));
      setFilterParams(initialParams);
    };
  }, [loginStatus?.isLoged]);

  if (!(requestELettetList?.length > 0) && isLoading) {
    return (
      <View style={styles.wrapperLoading}>
        <InnerLoading gifStyle={styles.loadGif} />
      </View>
    );
  }

  return (
    <SafeAreaView style={styles.flex1}>
      {requestELettetList.length > 0 ? (
        <>
          <View style={styles.container}>
            <FlatList
              style={styles.scrollContent}
              data={requestELettetList.filter( (item: MODEL.IELetter) => item.url )}
              keyExtractor={item => item.id}
              showsVerticalScrollIndicator={false}
              renderItem={renderRequestELetter}
              ListFooterComponent={isLoading ? renderFooterLoading() : <View style={styles.mb20} />}
              onEndReachedThreshold={0.01}
              onEndReached={handleLoadMore}
              refreshControl={
                <RefreshControl tintColor={theme.colors.primary} refreshing={isRefreshing} onRefresh={onRefreshRequestList} />
              }
            />
          </View>
          <Loading visible={documentLoading} dismissAction={onDismissLoading} />
          <ModalConfirmError
            ref={modalDocumentErrorRef}
            subtitle={t('common.text.subtitle.download')}
            title={t('common.text.title.download')}
            btnText={t('common.text.cancel.download')}
            onConfirm={onCancelDocumentErrorModal}
          />
        </>
      ) : (
        <View style={styles.wrapperImageNodata}>
          <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
          <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
        </View>
      )}
    </SafeAreaView>
  );
};

export default RequestELetter;
