import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react';
import { Animated, SafeAreaView, StatusBar, Text, TouchableOpacity, View } from 'react-native';
import { Icon } from 'superapp-common-component';
import ImagePicker, { Image } from 'react-native-image-crop-picker';
import { useTranslation } from 'react-i18next';

import RNQRGenerator from 'rn-qr-generator';
import QRCodeScanner from 'react-native-qrcode-scanner';
import { BarCodeReadEvent, RNCamera } from 'react-native-camera';

import { isIOS } from '@/utils/utils';
import { HITSLOP_48 } from '@/utils/styles';
import ZoomView from '@/components/QRCode/ZoomView';

import styles from './styles';
import { REGEX } from '@/utils/constants';

const MAX_ZOOM = 7; // iOS only
const ZOOM_F = isIOS ? 0.007 : 0.08;

type TQRCodeProps = {
  disabled: boolean;
  onClose: () => void;
  onReadQRCode: (value: string) => void;
  onSelectImageError: (error: unknown) => void;
  onSelectImage: (image: Image, qrCode?: string) => void;
};

export type TMessage = {
  success: boolean;
  message: string;
};

export type TQRCodeRef = {
  reactivate: () => void;
  setScanning: (scan: boolean) => void;
  setShowMessage: (message: TMessage) => void;
};

export const useQRCode = (): [React.MutableRefObject<TQRCodeRef | null>, () => TQRCodeRef] => {
  const ref = useRef<TQRCodeRef>(null);
  const getInstance = () => ref.current!;

  return [ref, getInstance];
};

const QRCode: React.ForwardRefRenderFunction<TQRCodeRef, TQRCodeProps | null> = (
  { disabled, onClose, onReadQRCode, onSelectImage, onSelectImageError },
  ref,
) => {
  const { t } = useTranslation();
  const [showMessage, setShowMessage] = useState<TMessage | undefined>(undefined);
  const prevPinchRef = useRef<number>(1);
  const [zoom, setZoom] = useState<number>(0.3); // Set 0 if need pinch zoom
  const scannerRef = useRef<QRCodeScanner | null>();
  const [cameraReady, setCameraReady] = useState(false);
  const fadeInAnimation = useRef(new Animated.Value(0)).current;

  const onRead = (event: BarCodeReadEvent) => {
    const [, oldIdNo] = event.data?.split('|');
    const hasFormatIdNumber = REGEX.CMNN.test(oldIdNo);

    if (hasFormatIdNumber) {
      onReadQRCode(event.data);
    } else {
      setShowMessage({
        success: false,
        message: t('verifyAccount.message.qrcode.error'),
      });
    }
  };

  const fadeIn = (value: number, duration: number = 300) => {
    Animated.timing(fadeInAnimation, {
      toValue: value,
      duration: duration,
      useNativeDriver: false,
    }).start();
  };

  const onChooseImage = async () => {
    try {
      scannerRef.current?._setScanning(true);
      showMessage && setShowMessage(undefined);
      const image = await ImagePicker.openPicker({ multiple: false, includeBase64: true, mediaType: 'photo' });
      const infoQRCode = await RNQRGenerator.detect({ uri: isIOS ? image.sourceURL : image.path });
      onSelectImage(image, infoQRCode.values?.[0]);
    } catch (error) {
      onSelectImageError(error);
    } finally {
      scannerRef.current?._setScanning(false);
    }
  };

  const onPinchStart = () => {
    prevPinchRef.current = 1;
  };

  const onPinchEnd = () => {
    prevPinchRef.current = 1;
  };

  const onPinchProgress = (scale: number) => {
    let p2 = scale - prevPinchRef.current;
    if (p2 > 0 && p2 > ZOOM_F) {
      prevPinchRef.current = scale;
      // setZoom(Math.min(zoom + ZOOM_F, 1)); // TODO: need mode zoom must request animation frame
    } else if (p2 < 0 && p2 < -ZOOM_F) {
      prevPinchRef.current = scale;
      // setZoom(Math.max(zoom - ZOOM_F, 0));  // TODO: need mode zoom  must request animation frame
    }
  };

  useImperativeHandle(
    ref,
    () => ({
      setScanning: (scan: boolean) => scannerRef.current?._setScanning(scan),
      reactivate: () => scannerRef.current?.reactivate(),
      setShowMessage: (message: TMessage) => setShowMessage(message),
    }),
    [],
  );

  useEffect(() => {
    let timeout: NodeJS.Timeout;

    if (showMessage) {
      fadeIn(1, 0);
      timeout = setTimeout(() => {
        fadeIn(0);
        scannerRef.current?.reactivate();
      }, 5000);
    }
    return () => {
      if (timeout) {
        clearTimeout(timeout);
      }
    };
  }, [showMessage]);

  useEffect(() => {
    if (disabled) {
      scannerRef.current?.disable();
    } else {
      scannerRef.current?.enable();
    }
  }, [disabled]);

  return (
    <SafeAreaView style={styles.safe}>
      <StatusBar barStyle="light-content" />
      <ZoomView onPinchStart={onPinchStart} onPinchEnd={onPinchEnd} onPinchProgress={onPinchProgress}>
        <QRCodeScanner
          onRead={onRead}
          ref={node => {
            scannerRef.current = node;
          }}
          showMarker
          cameraProps={{ ratio: '1:1', zoom, onCameraReady: () => setCameraReady(true), maxZoom: MAX_ZOOM }}
          containerStyle={styles.scanContainer}
          flashMode={RNCamera.Constants.FlashMode.off}
          topContent={
            <View style={styles.fullContent}>
              <TouchableOpacity hitSlop={HITSLOP_48} style={styles.btnClose} onPress={onClose}>
                <Icon name="Close" color="white" />
              </TouchableOpacity>
              <View>
                <Text style={styles.textHint}>{t('common.qrcode.description')}</Text>
              </View>
              {showMessage && (
                <Animated.View
                  style={[styles.hint, styles.center, { opacity: fadeInAnimation }, showMessage?.success ? styles.success : styles.error]}
                >
                  <View style={[styles.row, styles.message, styles.center]}>
                    <Icon name={showMessage?.success ? 'Success' : 'Alert'} color={showMessage?.success ? '#3DA758' : '#BA0361'} />
                    <Text style={styles.text}>{showMessage?.message}</Text>
                  </View>
                </Animated.View>
              )}
            </View>
          }
          bottomContent={
            <View style={[styles.fullContent, styles.bottomContent]}>
              <TouchableOpacity onPress={onChooseImage} disabled={!cameraReady}>
                <View style={styles.album}>
                  <Icon name="Album" color="white" />
                  <Text style={styles.txtChoose}>{t('common.qrcode.choose.image')}</Text>
                </View>
              </TouchableOpacity>
            </View>
          }
        />
      </ZoomView>
    </SafeAreaView>
  );
};

export default forwardRef(QRCode);
