import React, { useState, useEffect, useRef, useCallback } from 'react';
import { View, SafeAreaView, FlatList, Image, TouchableOpacity, RefreshControl } from 'react-native';
import { Icon, theme, InnerLoading, Loading, NativeModuleClient } from 'superapp-common-component';
import { useFocusEffect } from '@react-navigation/native';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';

import { JS_EVENT_NAMES, TYPES_NOTIFICATION } from '@/utils/constants';
import { a11y, isEmpty } from '@/utils/utils';
import jsEventEmitter from '@/utils/jsEventEmitter';
import AzureNotificationClient from '@/clients/AzureNotification';

import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import WebView from '@/components/WebView';

import { NOTIFICATION_LOCAL_STATE } from '../index';
import styles from '../styles';
import {
  getAllNotificationBroadcast as getAllNotificationBroadcastAction,
  saveNotificationBroadcast,
  loadMoreNotificationBroadcast,
  savePaging,
} from '../store/actions';
import { makeSelectNotification, makeSelectLoading, makeSelectLoadingLoadMore, makeSelectIsLoadMoreBroadcast } from '../store/selectors';
import ModalError, { ModalErrorRef } from './ModalError';

const useHandleReceivedMessage = (props: { onPressNotifyDetail: Function }) => {
  const { onPressNotifyDetail } = props;
  const jsEventReceiveMessageRunningRef = useRef(false); // prevent duplicate invoke function

  useFocusEffect(
    useCallback(() => {
      const handleReceivedMessage = () => {
        jsEventReceiveMessageRunningRef.current = false;

        const message = NOTIFICATION_LOCAL_STATE.message;
        if (message?.data?.type === TYPES_NOTIFICATION.BROADCAST && message?.data?.link) {
          onPressNotifyDetail(message.data as unknown as MODEL.IDataByIdNotification);
          NOTIFICATION_LOCAL_STATE.resetJumpState();
        }
      };

      // prevent duplicate invoke function
      const cloneHandleReceivedMessage = () => {
        if (jsEventReceiveMessageRunningRef.current) return;

        handleReceivedMessage();
      };

      jsEventReceiveMessageRunningRef.current = true;
      setTimeout(() => {
        // wait to init webview
        handleReceivedMessage();
      }, 500);

      jsEventEmitter.addListener(JS_EVENT_NAMES.ON_NAVIGATE_NOTIFICATION_DETAIL_TAB, cloneHandleReceivedMessage);
      return () => {
        jsEventEmitter.removeListener(JS_EVENT_NAMES.ON_NAVIGATE_NOTIFICATION_DETAIL_TAB, cloneHandleReceivedMessage);
      };
    }, []),
  );
};

const { colors } = theme;
const FORMAT_DATE = 'YYYY-MM-DD:HH:mm:ssZZ';

const ComponentBroadcastNoti = () => {
  const netInfo = NetInfo.useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const [isLoadingOpenApp, setLoadingOpenMiniApp] = useState<boolean>(false);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const loading = useSelector(makeSelectLoading());
  const loadingLoadMore = useSelector(makeSelectLoadingLoadMore());
  const infoNotification = useSelector(makeSelectNotification());
  const isLoadMoreBroadcast = useSelector(makeSelectIsLoadMoreBroadcast());
  const modalErrorRef = useRef<null | ModalErrorRef>();
  const webViewRef = useRef<any>(null);

  const showWebView: (url: string) => any = (url: string) => {
    webViewRef?.current?.openURL(url);
  };

  const onPullToRequest = async () => {
    if (loading) return;
    try {
      setRefreshing(true);
      dispatch(getAllNotificationBroadcastAction());
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const loadMoreNotiRequest = () => {
    if (loadingLoadMore) return;
    isLoadMoreBroadcast && dispatch(loadMoreNotificationBroadcast());
  };

  const onPressNotifyDetail = (dataDetail: MODEL.IDataByIdNotification) => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: 'Notification:Broadcast',
        unixTimestamp: dayjs().unix(),
        buttonText: 'Press notification',
      });

      showWebView(dataDetail?.link);
    } catch {}
  };

  const renderFooterLoading = () => {
    return <InnerLoading gifStyle={styles.loadMoreGif} />;
  };

  const renderItem = ({ item }: { item: MODEL.INotification }) => {
    return (
      <TouchableOpacity onPress={() => onPressNotifyDetail(item?.data)} {...a11y('Button.Notification.Detail')}>
        <View key={item?.id} style={styles.item}>
          <View style={styles.notificationItem}>
            <View style={styles.notifyTitle}>
              <View style={styles.leftTitle}>
                <Icon name="Popular1" size={24} color={colors.primary.charcoal[80]} />
                <Text style={styles.notifyType} fontFamily="bold">
                  {t(`common.notification.tab.broadcast`)}
                </Text>
              </View>

              <View style={styles.rightTitle}>
                <Text style={styles.textRightContent}>
                  {item.requestAt ? dayjs(item?.requestAt, FORMAT_DATE).format('HH:mm - DD/MM/YYYY') : ''}
                </Text>
              </View>
            </View>

            <View style={styles.notifyContent}>
              <View style={styles.wrapContentTitle}>
                <Text style={styles.notifyContentTitle} fontFamily="bold">
                  {item.title}
                </Text>
              </View>
              <View>
                <Text style={styles.notifyContentMessage} ellipsizeMode="tail" numberOfLines={4}>
                  {item.body}
                </Text>
              </View>
            </View>
          </View>
        </View>
      </TouchableOpacity>
    );
  };

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useFocusEffect(
    useCallback(() => {
      dispatch(getAllNotificationBroadcastAction());

      if (!__DEV__) {
        AzureNotificationClient.clearTappedNotification();
      }

      return () => {
        dispatch(saveNotificationBroadcast({ dataBroadcast: [], totalPages: 0 }));
        dispatch(savePaging({ pageNumber: 0, pageSize: 10 }));
      };
    }, []),
  );

  useHandleReceivedMessage({ onPressNotifyDetail });

  if (isConnected === null) return null;

  const isFirstLoad = isEmpty(infoNotification?.dataBroadcast) && loading;
  const isEmptyData = isEmpty(infoNotification?.dataBroadcast) && !loading;

  return isConnected ? (
    <SafeAreaView style={isFirstLoad ? styles.flex1 : styles.container}>
      {/* First load */}
      {isFirstLoad && (
        <View style={styles.wrapperLoading}>
          <InnerLoading gifStyle={styles.loadGif} />
        </View>
      )}
      {/* Had data */}
      {!isEmpty(infoNotification?.dataBroadcast) && (
        <>
          <View style={styles.wrapper}>
            <FlatList
              data={infoNotification?.dataBroadcast}
              keyExtractor={item => item.id}
              showsVerticalScrollIndicator={false}
              renderItem={renderItem}
              onEndReachedThreshold={0.5}
              onEndReached={loadMoreNotiRequest}
              ListFooterComponent={loadingLoadMore ? renderFooterLoading() : <View style={styles.mb20} />}
              refreshControl={<RefreshControl tintColor={colors.primary.red[100]} refreshing={isRefreshing} onRefresh={onPullToRequest} />}
            />
          </View>
          <Loading visible={isLoadingOpenApp} />
          <ModalError ref={modalErrorRef} />
        </>
      )}
      {/* No data */}
      {isEmptyData && (
        <View style={styles.wrapperImageNodata}>
          <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
          <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
        </View>
      )}
      <WebView ref={webViewRef} />
    </SafeAreaView>
  ) : (
    <SafeAreaView style={styles.flex1}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default ComponentBroadcastNoti;
