import React, { forwardRef, useImperativeHandle } from 'react';
import { View, Image } from 'react-native';
import { Button } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import Text from '@/components/Text';
import logo from '@/assets/images/logo-change-language.png';

import styles from './styles';
import Modal from '../Modal';
import { useModal } from '../Modal/Modal';

interface ModalConfirmLanguageProps {
  language: string;
  onChangeLanguage: () => void;
}

export interface ModalConfirmLanguageRef {
  open: () => void;
  close: () => void;
}

const ModalConfirmLanguage = (props: ModalConfirmLanguageProps, ref: React.Ref<unknown> | undefined) => {
  const { language, onChangeLanguage } = props;
  const [modalRef, componentModal] = useModal();
  const { t } = useTranslation();

  useImperativeHandle(
    ref,
    (): ModalConfirmLanguageRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal()?.close(),
    }),
  );

  const changeLanguage = () => {
    onChangeLanguage();
    componentModal()?.close();
  };

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer} overlayOpacity={0.5}>
      <View style={styles.modalContainer}>
        <View style={styles.modalContent}>
          <View>
            <View style={styles.imageBox}>
              <Image style={styles.logo} resizeMode="contain" source={logo} />
            </View>
            <Text style={styles.modalMainText} fontFamily="bold">
              {language === 'en' ? t('profile.confirm.language.modal.title.vn') : t('profile.confirm.language.modal.title.en')}
            </Text>
          </View>
          <View style={styles.buttonBox}>
            <Button
              text={t('profile.confirm.language.modal.button.switch')}
              type="secondary"
              uppercase={false}
              labelStyle={styles.modalButtonText}
              size="large"
              style={styles.topButton}
              onPress={changeLanguage}
              id="ModalConfirmLanguage.btnChangeLanguage"
            />
            <Button
              text={t('profile.confirm.language.modal.button.Cancel')}
              type="secondary"
              uppercase={false}
              labelStyle={styles.modalButtonText}
              size="large"
              style={styles.bottomButton}
              onPress={() => {
                componentModal()?.close();
              }}
              id="ModalConfirmLanguage.btnCloseModalChangeLanguage"
            />
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalConfirmLanguage);
