import _isEmpty from 'lodash/isEmpty';
import _mapValues from 'lodash/mapValues';
import { TFunction } from 'react-i18next';

import { HealthReportItemSummary } from '@/stores/vitality/state';

import { AEMHealthReportCategoryType, HealthReportItemKey, HealthReportListType } from './types';

export const displayTypes = {
  RANGE: 'range',
  GOOD_BAD: 'goodBad',
  SCALE: 'scale',
  CHOICE: 'choice',
  SPECIAL: 'special',
};

export const scaleValues = {
  POOR: 'Poor',
  FAIR: 'Fair',
  GOOD: 'Good',
};

export const listOfStringToListOfObjects = (items: any) => {
  if (!items) {
    return [];
  }
  try {
    return Array.isArray(items)
      ? items.map(item => {
          let value = item;
          if (typeof item === 'string') {
            value = item ? JSON.parse(item) : {};
          }
          return value;
        })
      : [items];
  } catch (error) {
    return [];
  }
};

const getInRangeDescriptionText = (isInRangeMore: boolean, rangeCount: number, t: TFunction) => {
  let suffix = isInRangeMore ? t('healthReport.areasAreInRange') : t('healthReport.areasAreOutOfRange');

  if (rangeCount <= 1) {
    suffix = isInRangeMore ? t('healthReport.areasAreInRange') : t('healthReport.areasAreOutOfRange');
  }

  return `${rangeCount} ${suffix}`;
};

export const normalizeHealthReportCardsData = (
  healthReportItemKeys: HealthReportItemKey[],
  healthReportListItemSummary: Record<string, HealthReportItemSummary>,
  t: TFunction,
) => {
  if (_isEmpty(healthReportListItemSummary)) {
    return [];
  }

  return healthReportItemKeys.map((itemKey: string) => {
    const summary = healthReportListItemSummary[itemKey];
    const { title, inRangeCount = 0, noDataCount = 0, totalCount = 0 } = summary || {};
    const dataCount = totalCount - noDataCount;
    const outOfRangeCount = dataCount - inRangeCount;
    const isInRangeMore = inRangeCount > outOfRangeCount;

    return summary && dataCount
      ? {
          title,
          description: getInRangeDescriptionText(isInRangeMore, isInRangeMore ? inRangeCount : outOfRangeCount, t),
          iconGroup: 'healthReports',
          iconName: itemKey,
          shouldShowRedDot: !isInRangeMore,
        }
      : {};
  });
};

export const getCategoryFeedbackData = (categoryId: string, healthReportFeedback?: any): any => {
  const { feedback: feedbacks = [], feedbackIndicator: feedbackIndicators = [] } = healthReportFeedback || {};
  return {
    feedback: feedbacks.find(({ feedbackId }: { feedbackId: string }) => feedbackId === categoryId),
    feedbackIndicator: feedbackIndicators.find(({ feedbackId }: { feedbackId: string }) => feedbackId === categoryId) || {
      feedbackId: categoryId,
      feedbackTarget: 'null',
      feedbackValue: 'null',
      feedbackRangeIndicator: 'null',
      feedbackUnitOfMeasure: 'null',
    },
  };
};

// `category` is optional if only want to get `isInRange` and `isNoData`
export const getCategoryStatus = (feedbackIndicator: any, feedback: any, category?: any): any => {
  const { type, categoryDescription } = category || {};
  const specialCase = categoryDescription?.find(
    ({ inRangeIndicator }: { inRangeIndicator: string }) => inRangeIndicator === 'NA',
  )?.feedbackIndicator;
  const { feedbackDesc } = feedback || {};

  const { feedbackValue, feedbackRangeIndicator } = feedbackIndicator || {};
  const isNoData = !feedbackValue || feedbackValue === 'null';
  const isInRange =
    type === displayTypes.SCALE
      ? feedbackDesc === scaleValues.GOOD || feedbackDesc === scaleValues.FAIR
      : feedbackRangeIndicator === 'true';
  const isSpecialCase = type === displayTypes.SPECIAL && !!specialCase && specialCase === feedbackValue;

  return {
    isInRange,
    isNoData,
    isSpecialCase,
  };
};

export const getStatusesCounts = (statuses: any[]) => {
  return statuses.reduce(
    (counts: any, { isInRange, isNoData }) => {
      if (isInRange) counts.inRangeCount += 1;
      if (isNoData) counts.noDataCount += 1;
      counts.totalCount += 1;
      return counts;
    },
    {
      inRangeCount: 0,
      noDataCount: 0,
      totalCount: 0,
    },
  );
};

export const normalizeHealthReportListDataFromAEM = (reportList: HealthReportListType) => {
  return _mapValues(reportList, reportItem => {
    const { categoryList, categories } = reportItem;
    return {
      ...reportItem,
      categories: listOfStringToListOfObjects(categories),
      categoryList: listOfStringToListOfObjects(categoryList),
    };
  });
};

export const getCategoriesCounts = (categories: AEMHealthReportCategoryType[], hrFeedback: any): any => {
  if (!hrFeedback) {
    return {};
  }
  let list = categories;
  const statuses = list.map(category => {
    const { CategoryId: categoryId } = category;
    // Map category to feedbackIndicator
    const { feedbackIndicator, feedback } = getCategoryFeedbackData(categoryId, hrFeedback);
    // Map feedbackIndicator to status
    return getCategoryStatus(feedbackIndicator, feedback, category);
  });
  return getStatusesCounts(statuses);
};

export const getHealthReportDescriptionText = (age: number, diffYears: number, language: string, t: TFunction) => {
  const trans = {
    thisIs: 'healthReport.thisIs',
    years: 'healthReport.years',
    above: 'healthReport.above',
    below: 'healthReport.below',
    yourRealAge: 'healthReport.yourRealAge',
  };
  if (!age) {
    return t('healthReport.takeAssessment');
  }

  if (language === 'en') {
    return `${t(trans.thisIs)} ${diffYears} ${t(trans.years)} ${diffYears >= 0 ? t(trans.above) : t(trans.below)} ${t(trans.yourRealAge)}`;
  }

  return `${diffYears >= 0 ? t(trans.above) : t(trans.below)} ${t(trans.yourRealAge)} ${diffYears} ${t(trans.years)}`;
};
