import { takeLatest, call, put, select, all } from 'redux-saga/effects';
import { TAction } from '@/stores/types';

import CapchaService from '@/services/capcha';
import {
  VERIFY_PHONE_NUMBER,
  VERIFY_ID_PERSONAL_NUMBER,
  GET_CAPCHA,
  SUBMIT_OTP,
  SUBMIT_NEW_PASSWORD,
  CONFIRM_NEW_PHONE,
} from './constants';
import {
  loadingRequestAction,
  loadingSuccessAction,
  saveCapchaAction,
  saveSessionId,
  saveOtpValueAction,
  loadingRequestForgotPassword,
  loadingSuccessForgotPassword,
} from './actions';
import APP_ENV from '@/config/app.config';
import {
  makeSelectSessionId,
  makeSelectUserInfoSubmit,
  makeSelectOtpValue,
} from './selectors';
import AuthService from '@/services/auth';
import { clearTokenLogout, removeSpacingsAndLower } from '@/utils/utils';

const { CLIENT_ID } = APP_ENV;

function* handleVerifyPhoneNumber({ payload, resolve, reject }: TAction) {
  try {
    const { phoneNumber } = payload;
    const params = {
      clientId: CLIENT_ID,
      username: removeSpacingsAndLower(phoneNumber),
    };
    yield put(loadingRequestAction());
    const response: MODEL.ICheckUsernameRes = yield call(AuthService.checkUsername, params);
    resolve(response?.requireIdNo);
  } catch (error) {
    reject(error);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* verifyIdNo({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { captchaValue, otpType, callback } = payload;
    const infoInput: { username: string; idNo: string } = yield select(makeSelectUserInfoSubmit());
    const param = {
      clientId: CLIENT_ID,
      ...infoInput,
      captchaValue,
      otpType,
    };
    const response: MODEL.ISubmitForgotPassRes = yield call(AuthService.submitForgotPassword, param);
    yield put(saveSessionId(response));
    callback && callback();
  } catch (error: any) {
    const { callback } = payload;
    callback && callback(error?.errorCode);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleGetCapcha() {
  try {
    const response: MODEL.ICapcha = yield call(CapchaService.getCapchaService);
    yield put(saveCapchaAction(response));
  } catch (error) {
    console.log('error', error);
  } finally {
  }
}

