function* handleSubmitOtp({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestAction());
    const sessionId: {} = yield select(makeSelectSessionId());
    const { otpValue } = payload;
    const paramsVerifyOtp = {
      clientId: CLIENT_ID,
      otpValue,
      ...sessionId,
    }
    yield call(AuthService.verifyOtp, paramsVerifyOtp);
    yield put(saveOtpValueAction(otpValue));
    resolve();
  } catch (error: any) {
    reject(error?.errorCode);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleSubmitNewPassword({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestForgotPassword());
    const { newPassword } = payload;
    const [sessionId, infoUser]: Array<any> = yield all([select(makeSelectSessionId()), select(makeSelectUserInfoSubmit())]);
    const paramsResetPass = {
      clientId: CLIENT_ID,
      newPassword,
      ...sessionId,
    };
    yield call(AuthService.resetPassForgotPassword, paramsResetPass);
    yield call(clearTokenLogout);
    resolve({ username: infoUser?.username, password: newPassword });
  } catch (error: any) {
    reject(error?.errorCode);
  } finally {
    yield put(loadingSuccessForgotPassword());
  }
}

function* handleConfirmNewPhone({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { phone } = payload;
    const sessionId: string = yield select(makeSelectSessionId());
    const params = { newUsername: phone, sessionId, clientId: CLIENT_ID };
    yield call(AuthService.changeUserNameForgotPassword, params);
    resolve();
  } catch (error) {
    reject(error);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* forgotPasswordSaga() {
  yield takeLatest(VERIFY_PHONE_NUMBER, handleVerifyPhoneNumber);
  yield takeLatest(VERIFY_ID_PERSONAL_NUMBER, verifyIdNo);
  yield takeLatest(GET_CAPCHA, handleGetCapcha);
  // yield takeLatest(REQUEST_OTP_BY_SMS, handleRequestOtpBySms);
  yield takeLatest(SUBMIT_OTP, handleSubmitOtp);
  yield takeLatest(SUBMIT_NEW_PASSWORD, handleSubmitNewPassword);
  yield takeLatest(CONFIRM_NEW_PHONE, handleConfirmNewPhone);
}

export default forgotPasswordSaga;
