import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_NOTIFICATION } from './constants';

const currentState = (state: TConnectState) => state?.[NS_NOTIFICATION] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectLoadingLoadMore = () => createSelector(currentState, state => state.loadingLoadMore);
const makeSelectNotification = () => createSelector(currentState, state => state.notification);
const makeSelectPaging = () => createSelector(currentState, state => state.notification.paging);
const makeSelectNotificationUnRead = () => createSelector(currentState, state => state.notificationUnRead);
const makeSelectIsLoadMoreGeneral = () => createSelector(currentState, state => state.isLoadMoreGeneral);
const makeSelectIsLoadMoreBroadcast = () => createSelector(currentState, state => state.isLoadMoreBroadcast);

export {
  currentState,
  makeSelectLoading,
  makeSelectLoadingLoadMore,
  makeSelectNotification,
  makeSelectPaging,
  makeSelectNotificationUnRead,
  makeSelectIsLoadMoreGeneral,
  makeSelectIsLoadMoreBroadcast,
};
