import React from 'react';
import { View, Image, TouchableOpacity } from 'react-native';
import { useTheme, Icon } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import { Theme } from '@/config/themes.config';
import Text from '@/components/Text';

import useStyles from './styles';

interface NoConnectionProps {}

const NoConnection: React.FC<NoConnectionProps> = props => {
  const {} = props;
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const { colors } = theme;

  return (
    <View style={styles.container}>
      <Image source={require('@/assets/images/no-connection.png')} style={styles.image} resizeMode="cover" />
      <Text style={styles.mainText}>{t('common.no.connection')}</Text>
      <Text style={styles.subText}>{t('common.no.connection.sub')}</Text>
      <TouchableOpacity style={styles.wrapperTryAgain}>
        <Icon name="Refresh1" size={16} color={colors.status.actionable} />
        <Text style={styles.tryText}>{t('common.tryAgain')}</Text>
      </TouchableOpacity>
    </View>
  );
};

export default NoConnection;
