import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { Text, View } from 'react-native';
import { Controller, useForm } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { Button, Checkbox, DateInput, NativeModuleClient, Select, TextInput } from 'superapp-common-component';
import dayjs from 'dayjs';

import TakePhotoOCR from '@/pages/VerifyAccount/components/TakePhotoOCR';
import { DOCUMENT_VERIFY_ID_CARD, Face, OCR_TYPE_NEW } from '@/pages/VerifyAccount/types';
import { DATE_FORMATS, REGEX } from '@/utils/constants';
import { FormVerifyValues, MasterDataType } from '@/pages/VerifyAccount/modals';
import { formatDateLocal, isValidDate, parseDDMMYYYYtoYYYYMMDD } from '@/utils/time-utils';
import { isEmpty } from '@/utils/utils';
import WebView, { TWebViewUrlRef } from '@/components/WebView';
import ROUTE_PATH from '@/navigators/routePath';

import { ISSUE_PLACES, removeAccents } from '@/pages/VerifyAccount/configs';
import styles from './styles';

type PropsTypes = {
  onSubmitOCR: (data: any) => void;
  onSubmitVerifyAccount: (dataValue: any) => void;
  dataOCRFront: any;
  dataOCRBack: any;
  errorOCRFront: boolean;
  errorOCRBack: boolean;
  face?: Face;
  masterData?: MasterDataType;
};

const FormNationalIDCard = (props: PropsTypes) => {
  const { t } = useTranslation();
  const webviewRef = useRef<TWebViewUrlRef>(null);
  const [isAgree, setIsAgree] = useState(false);
  const { onSubmitVerifyAccount, errorOCRFront, errorOCRBack, dataOCRBack, dataOCRFront, masterData } = props;
  const [maxLengthIDCard, setMaxLengthIDCard] = useState<number>(12);
  const [provinces, setProvinces] = useState<Array<any> | null>(null);
  const [isDisableExpirationDate, setIsDisableExpirationDate] = useState(false);
  const {
    control,
    formState: { errors },
    handleSubmit,
    setError,
    setValue,
    getValues,
    reset,
    resetField,
    clearErrors,
  } = useForm<FormVerifyValues | any>();

  const typeDocumentCardIDItem = useMemo(
    () => [
      { value: DOCUMENT_VERIFY_ID_CARD.CMND, label: t('verifyAccount.page.idCard9') },
      { value: DOCUMENT_VERIFY_ID_CARD.CNMD_12, label: t('verifyAccount.page.idCard12') },
      { value: DOCUMENT_VERIFY_ID_CARD.CCCD, label: t('verifyAccount.page.citizenIdCard') },
      { value: DOCUMENT_VERIFY_ID_CARD.CCCD_CHIP, label: t('verifyAccount.page.citizenIdCardChip') },
    ],
    [t],
  );

  const genderItem = useMemo(
    () => [
      { value: 'Male', label: t('verifyAccount.page.genderMale') },
      { value: 'Female', label: t('verifyAccount.page.genderFemale') },
    ],
    [t],
  );

  const messageErrorNationalId = useMemo(() => {
    return maxLengthIDCard === 9 ? t('verifyAccount.form.inValid9No') : t('verifyAccount.form.inValid12No');
  }, [maxLengthIDCard, t]);

  const isDisableSubmit = useMemo(() => {
    if (!isAgree) return true;
    return !dataOCRFront || !dataOCRBack || errorOCRBack || errorOCRFront;
  }, [dataOCRFront, dataOCRBack, errorOCRBack, errorOCRFront, isAgree]);

  const onSubmit = async (dataValue: FormVerifyValues) => {
    onSubmitVerifyAccount(dataValue);
  };

  const addExpirationDateByIssueDate = useCallback(
    dateValue => {
      if (
        dataOCRFront &&
        dataOCRFront[0]?.doe &&
        dataOCRFront[0]?.doe !== 'N/A' &&
        isValidDate(dataOCRFront[0]?.doe, DATE_FORMATS.DDMMYYYY)
      ) {
        return;
      }

      const issueDate = dateValue && isValidDate(dateValue, DATE_FORMATS.DDMMYYYY) ? dateValue : getValues('issueDate');

      if (getValues('type')?.value === DOCUMENT_VERIFY_ID_CARD.CMND) {
        // set expiration Date
        const dateAfter15Year = dayjs(issueDate, DATE_FORMATS.DDMMYYYY).add(15, 'year');
        setValue('expirationDate', dateAfter15Year.format(DATE_FORMATS.DDMMYYYY));
        setIsDisableExpirationDate(true);
      } else {
        const dateAfter100Year = dayjs(issueDate, DATE_FORMATS.DDMMYYYY).add(100, 'year');
        setValue('expirationDate', dateAfter100Year.format(DATE_FORMATS.DDMMYYYY));
      }
    },
    [dataOCRFront],
  );

  useEffect(() => {
    if (errorOCRFront || errorOCRBack) {
      reset({
        ...getValues(),
      });
    }
  }, [errorOCRFront, errorOCRBack, reset]);

  useEffect(() => {
    if (masterData?.provinces) {
      const arrProvinces = masterData?.provinces.filter(item => item.code < 900);
      setProvinces(arrProvinces);
    }
  }, [masterData]);

  useEffect(() => {
    if (dataOCRFront && dataOCRFront[0]) {
      clearErrors(['no', 'fullName', 'dob', 'birthPlace', 'gender']);
      const dataFront = dataOCRFront[0];
      setValue('no', dataFront?.id);
      setValue('fullName', dataFront?.name);

      // set birthPlace
      if (dataFront?.address_entities && dataFront?.address_entities?.province && dataFront?.address_entities?.province !== 'N/A') {
        const province = masterData?.provinces
          ? masterData?.provinces.find(item => item.provinceName.toUpperCase() === dataFront?.address_entities?.province.toUpperCase())
          : { code: 'Vietnam' };
        setValue('birthPlace', { code: province?.code });
      } else {
        setValue('birthPlace', { code: 'Vietnam' });
      }

      if (dataFront?.dob && dataFront?.dob !== 'N/A' && isValidDate(dataFront?.dob, DATE_FORMATS.DDMMYYYY)) {
        setValue('dob', formatDateLocal(dataFront?.dob));
      }

      // gender
      switch (dataFront?.sex?.toUpperCase()) {
        case 'NỮ':
        case 'FEMALE':
          setValue('gender', { value: 'Female' });
          break;
        case 'NAM':
        case 'MALE':
          setValue('gender', { value: 'Male' });
          break;
        default:
          break;
      }

      // Fixbug SA-8797
      if (![OCR_TYPE_NEW.IDCARD_9_FRONT, OCR_TYPE_NEW.IDCARD_9_BACK].includes(dataFront?.type_new) && maxLengthIDCard === 9) {
        setMaxLengthIDCard(12);
      }

      switch (dataFront?.type_new) {
        case OCR_TYPE_NEW.IDCARD_9_FRONT:
        case OCR_TYPE_NEW.IDCARD_9_BACK:
          setValue('type', { value: DOCUMENT_VERIFY_ID_CARD.CMND });
          setMaxLengthIDCard(9);
          break;
        case OCR_TYPE_NEW.CCCD_CHIP_FRONT:
          setValue('type', { value: DOCUMENT_VERIFY_ID_CARD.CCCD_CHIP });
          break;
        case OCR_TYPE_NEW.IDCARD_12_FRONT:
          setValue('type', { value: DOCUMENT_VERIFY_ID_CARD.CNMD_12 });
          break;
        case OCR_TYPE_NEW.IDCARD_NEW_FRONT:
          setValue('type', { value: DOCUMENT_VERIFY_ID_CARD.CCCD });
          break;
      }
    }
  }, [dataOCRFront]);

  useEffect(() => {
    if (dataOCRBack && dataOCRBack[0]) {
      clearErrors(['ethnic', 'issueDate', 'expirationDate', 'issuePlace', 'nationality']);
      const dataBack = dataOCRBack[0];
      if (dataBack?.issue_date && dataBack?.issue_date !== 'N/A' && isValidDate(dataBack?.issue_date, DATE_FORMATS.DDMMYYYY)) {
        setValue('issueDate', formatDateLocal(dataBack?.issue_date));
      } else {
        setValue('issueDate', dayjs().format(DATE_FORMATS.DDMMYYYY));
      }

      if (
        dataOCRFront &&
        dataOCRFront[0]?.doe &&
        dataOCRFront[0]?.doe !== 'N/A' &&
        isValidDate(dataOCRFront[0]?.doe, DATE_FORMATS.DDMMYYYY)
      ) {
        setValue('expirationDate', formatDateLocal(dataOCRFront[0]?.doe));
      } else {
        addExpirationDateByIssueDate();
      }

      // set nationality
      if (masterData?.nations) {
        const nationFrontData =
          dataOCRFront &&
          dataOCRFront[0]?.nationality &&
          dataOCRFront[0]?.nationality !== 'N/A' &&
          dataOCRFront[0]?.nationality?.toUpperCase();
        const nationality = nationFrontData || 'VIETNAM';
        const nation = masterData?.nations.find(item => {
          if (item?.nameEng?.toUpperCase()?.includes(nationality) || item?.nameVi?.toUpperCase()?.includes(nationality)) {
            return item;
          }
        });
        if (nation) {
          setValue('nationality', { code: nation.code });
        } else {
          const nationDefault = masterData?.nations.find(item => item.nameEng.toUpperCase().includes('VIETNAM'));
          setValue('nationality', { code: nationDefault?.code });
        }
      }

      // set ethnic
      const shortNameEthnicDefault = 'KINH';
      if (dataBack && dataBack?.ethnicity && dataBack?.ethnicity !== 'N/A' && masterData?.ethnics) {
        const ethnicDataUpperCase = dataBack.ethnicity.toUpperCase().replace(/-/, ' ');
        const ethnicity = masterData?.ethnics.find(item => {
          if (
            item?.fullName?.toUpperCase()?.replace(/-/, ' ') === ethnicDataUpperCase ||
            item?.shortName?.toUpperCase()?.replace(/-/, ' ') === ethnicDataUpperCase
          ) {
            return item;
          } else {
            return item.shortName.toUpperCase().includes(shortNameEthnicDefault);
          }
        });
        setValue('ethnic', { code: ethnicity?.code });
      } else {
        const ethnicity = masterData?.ethnics.find(item => item.shortName.toUpperCase().includes(shortNameEthnicDefault));
        setValue('ethnic', { code: ethnicity?.code });
      }

      // set issuePlace
      if (dataBack?.issue_loc && dataBack?.issue_loc !== 'N/A' && masterData?.provinces) {
        const place = masterData?.provinces.find(item => item.provinceName.toUpperCase() === dataBack?.issue_loc.toUpperCase());
        if (place) {
          setValue('issuePlace', { code: place?.code });
        } else {
          const placeNew = ISSUE_PLACES.find(item => item.provinceName.toUpperCase() === dataBack?.issue_loc.toUpperCase());
          if (placeNew) {
            const placeMapping = masterData?.provinces.find(item => item.code === placeNew.code);
            setValue('issuePlace', { code: placeMapping?.code });
          }
        }
      }
    }
  }, [dataOCRBack]);

  const onChangeCMNDType = item => {
    setValue('type', item);
    clearErrors('no');
    if (item.value === DOCUMENT_VERIFY_ID_CARD.CMND) {
      setMaxLengthIDCard(9);
      if (getValues('expirationDate') && !errors?.expirationDate) {
        setIsDisableExpirationDate(true);
      }
    } else {
      setMaxLengthIDCard(12);
      setIsDisableExpirationDate(false);
    }
    addExpirationDateByIssueDate();
  };

  const onOpenWebViewTC = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'T&C',
    });

    webviewRef?.current?.openURL(t('common.t&c.url'));
  };

  return (
    <>
      <Text style={styles.title}>{t('verifyAccount.page.nationID')}</Text>
      <Text style={styles.descriptions}>{t('verifyAccount.page.nationIDDescription')}</Text>
      <View>
        <TakePhotoOCR
          image={dataOCRFront}
          onSubmitOCR={(res: any) =>
            props.onSubmitOCR({ image_base64: res?.data, name: res?.name, type: res?.type, face: Face.FRONT, setError })
          }
          type={Face.FRONT}
          isErrorOCR={errorOCRFront}
          label={t('verifyAccount.page.nationCardIDFront')}
        />
        <Controller
          control={control}
          render={({ field: { value } }) => (
            <Select
              label={t('verifyAccount.page.typeOfDocument')}
              items={typeDocumentCardIDItem}
              onSelectItem={onChangeCMNDType}
              valueKey="label"
              uniqueKey="value"
              multiple={false}
              type={errors?.type ? 'error' : 'info'}
              disabled={!dataOCRFront}
              closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
              inputProps={{
                disabled: !dataOCRFront,
                iconColor: 'gray',
                containerStyle: styles.marginBottomInput,
              }}
              value={value?.value}
              option={'modal'}
              id="VerifyAccount.FormNationalIDCard.selectCMNDType"
            />
          )}
          name="type"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: messageErrorNationalId },
            validate: (value: string) => {
              const idType = getValues('type');
              let message: boolean | string | any = true;
              if (isEmpty(idType)) {
                message = t('common.messageRequired');
              } else {
                const typeIdCard = idType.value;
                if (!REGEX.EXACT_9_CHARACTER.test(value) && typeIdCard === 'CMND') {
                  message = t('verifyAccount.form.inValid9No');
                } else if (
                  !REGEX.EXACT_12_CHARACTER.test(value) &&
                  (typeIdCard === 'CMND_12' || typeIdCard === 'CCCD' || typeIdCard === 'CCCD_CHIP')
                ) {
                  message = t('verifyAccount.form.inValid12No');
                }
              }
              return message;
            },
          }}
          render={({ field: { onChange, value } }) => (
            <TextInput
              label={t('verifyAccount.page.IDNumber')}
              disabled={!dataOCRFront}
              onChangeText={onChange}
              keyboardType="numeric"
              value={value}
              message={errors.no?.message || undefined}
              containerStyle={styles.marginBottomInput}
              type={errors?.no && 'error'}
              maxLength={maxLengthIDCard}
              id="VerifyAccount.FormNationalIDCard.inputIDNumber"
            />
          )}
          name="no"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('verifyAccount.form.inValidName') },
            validate: (value: string) => {
              if (value && !REGEX.CHARACTER_AND_NUMBER.test(removeAccents(value))) {
                return t('verifyAccount.form.inValidName');
              }
            },
          }}
          render={({ field: { onChange, value } }) => (
            <TextInput
              label={t('verifyAccount.page.name')}
              disabled={!dataOCRFront}
              onChangeText={onChange}
              value={value}
              message={errors.fullName?.message || undefined}
              containerStyle={styles.marginBottomInput}
              type={errors?.fullName && 'error'}
              maxLength={30}
              id="VerifyAccount.FormNationalIDCard.inputFullName"
            />
          )}
          name="fullName"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              let result: boolean | string | any = true;
              if (value.match(REGEX.VALID_DATE) != null && isValidDate(value, DATE_FORMATS.DDMMYYYY)) {
                const date = dayjs(parseDDMMYYYYtoYYYYMMDD(value));
                if (dayjs() < date) {
                  result = t('verifyAccount.form.inValidDobInPast');
                }
                if (date.isBefore(new Date(1900, 1, 1)) || date.isSame(new Date(1900, 1, 1))) {
                  result = t('verifyAccount.form.inValidDobAgo');
                }
              } else {
                result = t('verifyAccount.form.inValidFormatDate');
              }
              return result;
            },
          }}
          render={({ field: { onChange, value } }) => (
            <DateInput
              label={t('verifyAccount.page.bod')}
              formatDate={DATE_FORMATS.DDMMYYYY}
              inputProps={{
                disabled: !dataOCRFront,
                containerStyle: styles.marginBottomInput,
                message: errors?.dob?.message || undefined,
                type: errors?.dob && 'error',
                iconColor: 'gray',
              }}
              value={value}
              maximumDate={new Date()}
              minimumDate={new Date(1900, 1, 1)}
              closeTextModal={t('verifyAccount.modal.closeBtn')}
              id="VerifyAccount.FormNationalIDCard.fieldDateDOB"
              onChange={onChange}
            />
          )}
          name="dob"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              if (!value?.code) {
                return t('common.messageRequired');
              }
            },
          }}
          render={({ field: { onChange, value } }) =>
            provinces ? (
              <Select
                label={t('verifyAccount.page.birthdayPlace')}
                items={provinces}
                onSelectItem={onChange}
                multiple={false}
                allowSearch={true}
                option={'modal'}
                valueKey="provinceName"
                uniqueKey="code"
                disabled={!dataOCRFront}
                type={errors?.birthPlace ? 'error' : 'info'}
                closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
                inputProps={{
                  disabled: !dataOCRFront,
                  containerStyle: styles.marginBottomInput,
                  message: errors?.birthPlace?.message || undefined,
                  type: errors?.birthPlace ? 'error' : 'info',
                }}
                id="VerifyAccount.FormNationalIDCard.selectBirthPlace"
                value={value?.code}
                searchInputStyle={styles.mt_16}
              />
            ) : (
              <></>
            )
          }
          name="birthPlace"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('verifyAccount.form.inValidGender') },
          }}
          render={({ field: { onChange, value } }) => (
            <Select
              label={t('verifyAccount.page.gender')}
              items={genderItem}
              onSelectItem={onChange}
              value={value?.value}
              valueKey={'label'}
              uniqueKey={'value'}
              multiple={false}
              disabled={!dataOCRFront}
              option={'modal'}
              type={errors?.gender ? 'error' : 'info'}
              closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
              inputProps={{
                disabled: !dataOCRFront,
                iconColor: 'gray',
                containerStyle: styles.marginBottomInput,
                message: errors?.gender?.message || undefined,
                type: errors?.gender ? 'error' : 'info',
              }}
              id="VerifyAccount.FormNationalIDCard.selectGender"
            />
          )}
          name="gender"
        />
      </View>

      <View>
        <TakePhotoOCR
          image={dataOCRBack}
          onSubmitOCR={(res: any) =>
            props.onSubmitOCR({
              image_base64: res?.data,
              type: res?.type,
              name: res?.name,
              face: Face.BACK,
              setError,
            })
          }
          type={Face.BACK}
          isErrorOCR={errorOCRBack}
          label={t('verifyAccount.page.nationCardIDBack')}
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              if (!value?.code) {
                return t('common.messageRequired');
              }
            },
          }}
          render={({ field: { onChange, value } }) =>
            masterData?.ethnics ? (
              <Select
                label={t('verifyAccount.page.ethnic')}
                items={masterData?.ethnics}
                onSelectItem={onChange}
                valueKey="shortName"
                uniqueKey="code"
                multiple={false}
                allowSearch={true}
                value={value?.code}
                option={'modal'}
                disabled={!dataOCRBack}
                type={errors?.ethnic ? 'error' : 'info'}
                closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
                inputProps={{
                  disabled: !dataOCRBack,
                  iconColor: 'gray',
                  containerStyle: styles.marginBottomInput,
                  message: errors?.ethnic?.message || undefined,
                  type: errors?.ethnic ? 'error' : 'info',
                }}
                id="VerifyAccount.FormNationalIDCard.selectEthnic"
                searchInputStyle={styles.mt_16}
              />
            ) : (
              <></>
            )
          }
          name="ethnic"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              let result: boolean | string | any = true;
              if (value.match(REGEX.VALID_DATE) != null && isValidDate(value, DATE_FORMATS.DDMMYYYY)) {
                if (dayjs() < dayjs(parseDDMMYYYYtoYYYYMMDD(value))) {
                  result = t('verifyAccount.form.inValidIssueGreaterCurrentDate');
                }
              } else {
                result = t('verifyAccount.form.inValidFormatDate');
              }
              return result;
            },
          }}
          render={({ field: { onChange, value } }) => (
            <DateInput
              label={t('verifyAccount.page.issuedDate')}
              closeTextModal={t('verifyAccount.modal.closeBtn')}
              formatDate={DATE_FORMATS.DDMMYYYY}
              inputProps={{
                disabled: !dataOCRBack,
                iconColor: 'gray',
                containerStyle: styles.marginBottomInput,
                message: errors?.issueDate?.message || undefined,
                type: errors?.issueDate ? 'error' : 'info',
              }}
              value={value}
              maximumDate={new Date()}
              id="VerifyAccount.FormNationalIDCard.fieldDateIssueDate"
              onChange={item => {
                addExpirationDateByIssueDate(item);
                onChange(item);
              }}
            />
          )}
          name="issueDate"
        />

        {/* expiration Date*/}
        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              const issueDate = getValues('issueDate');
              const idType = getValues('type');
              let result: boolean | string | any = true;
              if (value.match(REGEX.VALID_DATE) != null && isValidDate(value, DATE_FORMATS.DDMMYYYY)) {
                const date = dayjs(parseDDMMYYYYtoYYYYMMDD(value));
                switch (true) {
                  case !issueDate:
                    result = true;
                    break;
                  case date.isBefore(dayjs(parseDDMMYYYYtoYYYYMMDD(issueDate))) || date.isSame(dayjs(parseDDMMYYYYtoYYYYMMDD(issueDate))):
                    result = t('verifyAccount.form.inValidExpirationLessThanIssue');
                    break;
                }
              } else {
                result = t('verifyAccount.form.inValidFormatDate');
              }
              if (!result && idType.value === DOCUMENT_VERIFY_ID_CARD.CMND) {
                setIsDisableExpirationDate(true);
                result = true;
              }

              return result;
            },
          }}
          render={({ field: { onChange, value } }) => (
            <DateInput
              label={t('verifyAccount.page.ExpirationDate')}
              formatDate={DATE_FORMATS.DDMMYYYY}
              inputProps={{
                disabled: !dataOCRBack || isDisableExpirationDate,
                iconColor: 'gray',
                containerStyle: styles.marginBottomInput,
                message: errors?.expirationDate?.message || undefined,
                type: errors?.expirationDate?.message ? 'error' : 'info',
              }}
              closeTextModal={t('verifyAccount.modal.closeBtn')}
              id="VerifyAccount.FormNationalIDCard.fieldDateExpirationDate"
              value={value}
              onChange={onChange}
            />
          )}
          name="expirationDate"
        />

        {/*issue Place*/}
        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              if (!value?.code) {
                return t('common.messageRequired');
              }
            },
          }}
          render={({ field: { onChange, value } }) =>
            masterData?.provinces ? (
              <Select
                label={t('verifyAccount.page.issued')}
                items={masterData?.provinces}
                onSelectItem={onChange}
                multiple={false}
                allowSearch={true}
                option={'modal'}
                valueKey="provinceName"
                uniqueKey="code"
                disabled={!dataOCRBack}
                type={errors?.issuePlace ? 'error' : 'info'}
                closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
                inputProps={{
                  disabled: !dataOCRBack,
                  containerStyle: styles.marginBottomInput,
                  iconColor: 'gray',
                  message: errors?.issuePlace?.message || undefined,
                  type: errors?.issuePlace ? 'error' : 'info',
                }}
                id="VerifyAccount.FormNationalIDCard.selectIssuePlace"
                value={value?.code}
                searchInputStyle={styles.mt_16}
              />
            ) : (
              <></>
            )
          }
          name="issuePlace"
        />

        <Controller
          control={control}
          rules={{
            required: { value: true, message: t('common.messageRequired') },
            validate: (value: any) => {
              if (!value?.code) {
                return t('common.messageRequired');
              }
            },
          }}
          render={({ field: { onChange, value } }) =>
            masterData?.nations ? (
              <Select
                label={t('verifyAccount.page.nationality')}
                items={masterData?.nations}
                onSelectItem={onChange}
                valueKey={'nameEng'}
                uniqueKey={'code'}
                multiple={false}
                value={value?.code}
                allowSearch={true}
                type={errors?.nationality ? 'error' : 'info'}
                disabled={!dataOCRBack}
                option={'modal'}
                closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
                inputProps={{
                  disabled: !dataOCRBack,
                  iconColor: 'gray',
                  containerStyle: styles.marginBottomInput,
                  message: errors?.nationality?.message || undefined,
                  type: errors?.nationality ? 'error' : 'info',
                }}
                id="VerifyAccount.FormNationalIDCard.selectNationality"
                searchInputStyle={styles.mt_16}
              />
            ) : (
              <></>
            )
          }
          name="nationality"
        />

        <Controller
          control={control}
          render={({ field: { onChange, value } }) =>
            masterData?.nations ? (
              <Select
                label={t('verifyAccount.page.otherNationality')}
                items={
                  getValues('nationality')
                    ? masterData?.nations.filter(item => item.code !== getValues('nationality')?.code)
                    : masterData?.nations
                }
                onSelectItem={item => {
                  if (item?.code === value?.code) {
                    return resetField('otherNationality');
                  }
                  onChange(item);
                }}
                valueKey={'nameEng'}
                uniqueKey={'code'}
                multiple={false}
                value={value?.code}
                allowSearch={true}
                type={errors?.otherNationality ? 'error' : 'info'}
                disabled={!dataOCRBack}
                option={'modal'}
                closeTextOptionModal={t('verifyAccount.modal.closeBtn')}
                inputProps={{
                  disabled: !dataOCRBack,
                  iconColor: 'gray',
                  containerStyle: styles.marginBottomInput,
                  message: errors?.otherNationality?.message || undefined,
                  type: errors?.otherNationality ? 'error' : 'info',
                }}
                id="VerifyAccount.FormNationalIDCard.otherNationality"
                searchInputStyle={styles.mt_16}
              />
            ) : (
              <></>
            )
          }
          name="otherNationality"
        />
      </View>
      <View>
        <Checkbox
          checked={isAgree}
          onPress={() => {
            setIsAgree(!isAgree);
          }}
          size={24}
          customStyleChild={styles.customCheckbox}
          id="VerifyAccount.loginCheckboxIsAgree"
        >
          <Text style={[styles.text]}>
            {t('common.t&c1')}{' '}
            <Text style={[styles.textLink, styles.textDecorationLine]} onPress={onOpenWebViewTC}>
              {t('common.t&c.policy')}
            </Text>
            <Text style={[styles.text]}> {t('common.t&c2')} </Text>
          </Text>
        </Checkbox>
      </View>
      <View style={styles.parentButton}>
        <Button
          text={t('common.continue')}
          onPress={handleSubmit(onSubmit)}
          disabled={isDisableSubmit}
          id="VerifyAccount.FormNationalIDCard.btnSubmitIDCardForm"
        />
      </View>
      <WebView ref={webviewRef} />
    </>
  );
};

export default FormNationalIDCard;
