import React, { useEffect, useRef, useState } from 'react';
import {
  Image,
  View,
  Animated,
  SafeAreaView,
  ScrollView,
  TouchableOpacity,
  ActivityIndicator,
  useWindowDimensions,
  StatusBar,
} from 'react-native';
import { useTranslation } from 'react-i18next';
import { Button, NativeModuleClient } from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationProp } from '@react-navigation/stack';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import _get from 'lodash/get';
import dayjs from 'dayjs';

import APP_ENV from '@/config/app.config';
import { TConnectState } from '@/stores/types';
import { getItem, setItem } from '@/utils/storage';
import { JS_EVENT_NAMES, STORAGE_NAME } from '@/utils/constants';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import { TStackParamList } from '@/navigators';
import { a11y, isEmpty } from '@/utils/utils';
import LoginModal, { LoginModalRef } from '@/pages/Login';
import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { HITSLOP } from '@/utils/styles';

import styles from './styles';
import { NS_ONBOARDING } from './store/constants';
import onboardingReducer from './store/reducer';
import onboardingSaga from './store/saga';
import { TOnboardingState } from './store/state';
import { makeSelectLoading, makeSelectContents } from './store/selectors';
import { getContentOnboarding as getContentOnboardingAction, TTransformData, OnboardingTranslation } from './store/actions';
import SlidingDot from './components/Pagination';
import jsEventEmitter from '@/utils/jsEventEmitter';

type OnboardingProps = { loading: boolean; getContentOnboarding: any; contentsGlobal: OnboardingTranslation };
type OnboardingNavigationProp = StackNavigationProp<TStackParamList, 'Onboarding'>;

const Onboarding: React.FC<OnboardingProps> = ({ loading, contentsGlobal, getContentOnboarding }) => {
  const { t, i18n } = useTranslation();
  const { width } = useWindowDimensions();
  const [language, setLanguage] = useState<string>('');
  const navigation = useNavigation<OnboardingNavigationProp>();
  const loginModalRef = useRef<null | LoginModalRef>(null);
  const sliderRef = useRef<ScrollView | null>(null);
  const [contents, setContent] = useState<Array<TTransformData>>([]);
  const refAutoPlay = useRef<boolean>(false);
  const scrollX = useRef(new Animated.Value(0)).current;

  useInjectSaga({ key: NS_ONBOARDING, saga: onboardingSaga });
  useInjectReducer({ key: NS_ONBOARDING, reducer: onboardingReducer });

  const onChangeLanguage = (lang: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ONBOARDING,
      unixTimestamp: dayjs().unix(),
      buttonText: `Change language ${lang}`,
    });
    i18n.changeLanguage(lang);
    setItem(STORAGE_NAME.LANGUAGE, JSON.stringify(lang));
    setLanguage(lang);
  };

  const onPressLogin = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ONBOARDING,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login',
    });
    loginModalRef.current?.setShowModal(true);
  };

  const onPressRegister = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ONBOARDING,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Register',
    });
    navigation.navigate(ROUTE_PATH.REGISTER);
  };

  const onPressGuest = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.ONBOARDING,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Guest',
    });
    navigation.reset({ index: 0, routes: [{ name: ROUTE_PATH.DASHBOARD }] } as never);
  };

  const onMounting = () =>
    navigation.addListener('focus', () => {
      (async () => {
        const [lang, isShowOnboarding] = await Promise.all([
          getItem<string>(STORAGE_NAME.LANGUAGE, 'en'),
          getItem<boolean>(STORAGE_NAME.ONBOARDING, false),
          setItem(STORAGE_NAME.ONBOARDING, JSON.stringify(true)),
        ]);

        if (isShowOnboarding) return;

        getContentOnboarding();
        setLanguage(lang);
      })();
    });

  const autoPlaySlide = () => {
    const maxContent = contents.length;
    let scrollValue = 0,
      scrolled = 0;
    const interval = setInterval(() => {
      scrolled += 1;
      if (scrolled < maxContent) scrollValue = scrollValue + width;
      else {
        clearInterval(interval);
      }
      sliderRef.current?.scrollTo({ x: scrollValue, animated: true });
    }, 5000);
  };

  useEffect(() => {
    if (!isEmpty(contents) && !refAutoPlay.current) {
      autoPlaySlide();
      refAutoPlay.current = true;
    }
  }, [contents]);

  useEffect(() => {
    const contentsLang = _get(contentsGlobal, language, []);
    if (!isEmpty(contentsLang) && !isEmpty(language)) {
      setContent(contentsLang);
    }
  }, [contentsGlobal, language]);

  const handleNotification = () => {
    if (loginModalRef.current?.isOpenLogin) return;
    navigation.reset({ index: 0, routes: [{ name: ROUTE_PATH.DASHBOARD }] } as never);
  };

  useEffect(() => {
    const mountingSubscription = onMounting();
    jsEventEmitter.addListener(JS_EVENT_NAMES.NOTIFICATION_TO_HOME, handleNotification);

    return () => {
      mountingSubscription();
      jsEventEmitter.removeListener(JS_EVENT_NAMES.NOTIFICATION_TO_HOME, handleNotification);
    };
  }, []);

  return (
    <View style={styles.container}>
      {loading && (
        <View style={styles.loading}>
          <ActivityIndicator size={'large'} color="#D31145" />
        </View>
      )}
      <SafeAreaView />
      <StatusBar barStyle="dark-content" />
      <View style={styles.slider}>
        <View style={styles.language}>
          <TouchableOpacity onPress={() => onChangeLanguage('vi')} {...a11y('Onboarding.btnChangeLanguageVN')} hitSlop={HITSLOP}>
            <Text style={[styles.textLanguage, language === 'vi' && styles.languageActive]}>VN</Text>
          </TouchableOpacity>
          <Text style={styles.textSpray}>|</Text>
          <TouchableOpacity onPress={() => onChangeLanguage('en')} {...a11y('Onboarding.btnChangeLanguageEN')} hitSlop={HITSLOP}>
            <Text style={[styles.textLanguage, language === 'en' && styles.languageActive]}>ENG</Text>
          </TouchableOpacity>
        </View>

        {/* Begin Slider */}
        <View>
          {!isEmpty(contents) && (
            <ScrollView
              horizontal
              pagingEnabled
              onScroll={Animated.event([{ nativeEvent: { contentOffset: { x: scrollX } } }], {
                useNativeDriver: false,
              })}
              showsHorizontalScrollIndicator={false}
              scrollEventThrottle={16}
              ref={sliderRef}
              {...a11y('Onboarding.scrollViewAnimate')}
            >
              {contents.map(content => (
                <View style={{ flex: 1 }} key={content.id}>
                  <Image style={styles.image} source={{ uri: `${APP_ENV.CONTENT_AEM}/${content.imagePath}` }} />
                  <Text style={styles.textTitle}>{content.title}</Text>
                  <Text style={styles.textDescription} numberOfLines={2}>
                    {content.description}
                  </Text>
                </View>
              ))}
            </ScrollView>
          )}
          {/* End Slider */}
          {!isEmpty(contents) && (
            <SlidingDot marginHorizontal={3} containerStyle={{ marginTop: 16 }} data={contents} scrollX={scrollX} dotSize={8} />
          )}
        </View>

        <View style={styles.wrapperButton}>
          <Button
            style={styles.buttonMargin}
            text={t('login.button.login')}
            onPress={onPressLogin}
            id="Onboarding.btnOpenLoginModal"
            uppercase={false}
          />
          <Button
            style={styles.mb10}
            text={t('login.button.signup')}
            type="secondary"
            onPress={onPressRegister}
            id="Onboarding.btnNavigateToRegister"
            uppercase={false}
          />
          <TouchableOpacity onPress={onPressGuest} {...a11y('Onboarding.btnViewAsGuest')} style={styles.marginTop}>
            <Text style={styles.textGuest}>{t('login.button.guest')}</Text>
          </TouchableOpacity>
        </View>
      </View>
      <LoginModal ref={loginModalRef} />
      <SafeAreaView />
    </View>
  );
};

type CreateStructuredSelectorType = {
  loading: TOnboardingState['loading'];
  contentsGlobal: TOnboardingState['contents'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  contentsGlobal: makeSelectContents(),
});

type MapDispatchToPropsType = {
  getContentOnboarding: () => any;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  getContentOnboarding: () => dispatch(getContentOnboardingAction()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Onboarding);
