export const isEmpty = (value: number | string | boolean | any): boolean => {
  if (typeof value === 'number') {
    return false;
  }

  if (typeof value === 'boolean') {
    return false;
  }

  return _isEmpty(value);
};

export const setVitalityStorageToken = async (tokenOkta: MODEL.IOktaRefreshToken) => {
  try {
    const { expires_in: expiresIn = 0, access_token: accessToken = '' } = tokenOkta;
    const membershipId = await getVitalityMembershipIdFromToken(accessToken);
    const expiryDate = new Date();
    expiryDate.setSeconds(expiryDate.getSeconds() + expiresIn);

    await Promise.all([
      setItem(STORAGE_NAME.VITALITY_MEMBERSHIP_ID, JSON.stringify(membershipId)),
      setItem(STORAGE_NAME.VITALITY_LOGIN_TOKEN, JSON.stringify(tokenOkta.access_token)),
      setItem(STORAGE_NAME.VITALITY_LOGIN_TOKEN_EXPIRY_DATE, JSON.stringify(expiryDate.getTime())),
      setItem(STORAGE_NAME.REFRESH_TOKEN_OKTA, JSON.stringify(tokenOkta.refresh_token)),
    ]);
  } catch (error) {}
};

export const setStorageToken = async (token: MODEL.IAuthenticate) => {
  await Promise.all([
    setItem(STORAGE_NAME.ACCESS_TOKEN, JSON.stringify(token.accessToken)),
    setItem(STORAGE_NAME.REFRESH_TOKEN, JSON.stringify(token.refreshToken)),
    setItem(STORAGE_NAME.REFRESH_TOKEN_EXPRIRY_SECONDS, JSON.stringify(token?.refreshTokenExpirySeconds || 0)), // POS Request set Cookies expired
  ]);
};

export const clearTokenLogout = async () => {
  const vitalityToken = await getItem<string>(STORAGE_NAME.VITALITY_LOGIN_TOKEN, '');
  await Promise.all([
    removeItem(STORAGE_NAME.ACCESS_TOKEN),
    removeItem(STORAGE_NAME.REFRESH_TOKEN),
    removeItem(STORAGE_NAME.PIN_CODE),
    removeItem(STORAGE_NAME.TOUCH_FACE_ID),
    removeItem(STORAGE_NAME.USERNAME),
    removeItem(STORAGE_NAME.FULL_NAME),
    removeItem(STORAGE_NAME.FIRST_NAME),
    removeItem(STORAGE_NAME.REMIND_BIOMETRIC),
    removeItem(STORAGE_NAME.REFRESH_TOKEN_OKTA),
    // Clear vitality storage items that were set when user using host app.
    !isEmpty(vitalityToken) && clearTokens(),
    removeItem(STORAGE_NAME.VITALITY_LOGIN_TOKEN),
    removeItem(STORAGE_NAME.VITALITY_MEMBERSHIP_ID),
    removeItem(STORAGE_NAME.VITALITY_LOGIN_TOKEN_EXPIRY_DATE),
  ]);
};

/**
 * Clear vitality storage items that were set when user using vitality mini app.
 */
export const clearVitalityStorage = async () => {
  const allKeys = await getItem(VITALITY_STORAGE_NAME.VITALITY_ALL_KEYS, []);
  BackgroundTimer.stopBackgroundTimer();
  await removeItem(VITALITY_STORAGE_NAME.TARGET_REFRESH_TOKEN_DATE);
  // for exclus the key include string below
  const exclusionIncludeKeys = [
    VITALITY_STORAGE_NAME.HEALTH_DATA,
    VITALITY_STORAGE_NAME.UPLOAD_DATE_TIME,
    VITALITY_STORAGE_NAME.IS_LINKED,
    VITALITY_STORAGE_NAME.PRODIACK,
    VITALITY_STORAGE_NAME.PRODIA_ASCHECK,
    VITALITY_STORAGE_NAME.FITBIT,
    VITALITY_STORAGE_NAME.GARMIN,
    VITALITY_STORAGE_NAME.IS_SHOWN_IN_APP,
    VITALITY_STORAGE_NAME.ONBOARDING_TUTORIAL,
    VITALITY_STORAGE_NAME.FOOD_TRACKER_TUTORIAL,
    VITALITY_STORAGE_NAME.VITALITY_DONE_TUTORIAL,
    '_en',
    '_vi',
  ];
  // for exclus the key exactly match below
  const exclusionMatchKeys = [
    VITALITY_STORAGE_NAME.CONFIRM_FINGERPRINT,
    VITALITY_STORAGE_NAME.REMEMBER_LOGIN_DETAILS,
    VITALITY_STORAGE_NAME.BIO_KEY,
    VITALITY_STORAGE_NAME.BIO_MEMBERSHIP_ID,
    VITALITY_STORAGE_NAME.MEMBERSHIP_ID,
    VITALITY_STORAGE_NAME.CHALLENGE_FLOW_ANSWER,
    VITALITY_STORAGE_NAME.CHALLENGE_COMPLETION_MODAL,
    VITALITY_STORAGE_NAME.SUCCESS_FIRST_LOGIN,
    VITALITY_STORAGE_NAME.TNC_LAST_UPDATE_TIME,
    VITALITY_STORAGE_NAME.USER_LANGUAGE_STORAGE_KEY,
    STORAGE_NAME.LANGUAGE,
  ];
  const diffKeys: string[] = [];
  _forEach(allKeys || [], (key: string) => {
    let isInclude = false;
    _forEach(exclusionIncludeKeys, (exclusionKey: string) => {
      if (key.includes(exclusionKey)) {
        isInclude = true;
      }
    });
    if (!isInclude) {
      diffKeys.push(key);
    }
  });
  const targetKeys: string[] = _difference(diffKeys, exclusionMatchKeys);
  await multiRemove(targetKeys);
};

