export const nanoid = (alphabet: string = 'abcdefghijklmnopqrstuvwxyz0123456789', size?: number) => {
  let id = '';
  let url = '-_';
  let length = alphabet.length;

  while (length--) {
    // 36 is radix. Number.prototype.toString(36) returns number
    // in Base36 representation. Base36 is like hex, but it uses 0â€“9 and a-z.
    url += length.toString(36);
  }

  length = alphabet.length;
  // Loop from 36 to 10 (from Z to A in Base36).
  while (length-- - 10) {
    url += length.toString(36).toUpperCase();
  }

  size = size || 32;
  while (size--) {
    // `| 0` is compact and faster alternative for `Math.floor()`
    // eslint-disable-next-line no-bitwise
    id += url[(Math.random() * 64) | 0];
  }
  return id;
};

export const removeSpacingsAndLower = (input: string) => {
  if (isEmpty(input)) {
    return '';
  }
  return input.toLowerCase().replace(/\s/g, '');
};

export const getImageType = (mime: string) => {
  switch (mime) {
    case 'image/png':
      return '.png';
    case 'image/jpeg':
      return '.jpg';
    default:
      return '.jpg';
  }
};

export const compareVersion = (version1: string, version2: string) => {
  if (!version1) return -1;
  if (!version2) return 1;

  const versions1 = version1.split('.');
  const versions2 = version2.split('.');
  const length = Math.max(versions1.length, versions2.length);

  for (let i = 0; i < length; i++) {
    const thisPart = i < versions1.length ? Number(versions1[i]) : 0;
    const thatPart = i < versions2.length ? Number(versions2[i]) : 0;

    if (thisPart < thatPart) return -1;
    if (thisPart > thatPart) return 1;
  }

  return 0;
};

export const getFileNameFromUrl = (str: string) => {
  return str ? str.split('/')?.pop()?.split('#')[0].split('?')[0] || '' + `${Math.floor(Date.now() / 1000)}.pdf` : undefined;
};

export const isPrivateIp = async () => {
  try {
    const dataInfo = await CloudflareService.traceService();
    let data = dataInfo.replace(/[\r\n]+/g, '","').replace(/\=+/g, '":"');
    data = '{"' + data.slice(0, data.lastIndexOf('","')) + '"}';
    const dataNetwork: MODEL.ITrace = JSON.parse(data);

    return dataNetwork?.ip === APP_ENV.PUBLIC_IP;
  } catch (error) {
    console.log('ERROR: Check public ip error', error);
    return false;
  }
};

export const canOpenVitality = (membership?: MODEL.IMembership, isLogin?: boolean) => {
  if (!isLogin) {
    return ERROR_CAN_OPEN_VITALITY.NON_LOGIN;
  }

  if (isEmpty(membership?.partyId) || isEmpty(membership)) {
    return ERROR_CAN_OPEN_VITALITY.NON_MEMBERSHIP;
  } else {
    if ([MEMBERSHIP_STATUS.CANCELLED, MEMBERSHIP_STATUS.INACTIVE].includes(membership?.status || '')) {
      return ERROR_CAN_OPEN_VITALITY.NON_MEMBERSHIP_ACTIVE;
    }
    if (membership?.status === MEMBERSHIP_STATUS.NEW) {
      return ERROR_CAN_OPEN_VITALITY.NON_MEMBERSHIP_FEE;
    }
  }
};

export const isValidJSON = (data: string) => {
  return /^[\],:{}\s]*$/.test(
    data
      .replace(/\\["\\\/bfnrtu]/g, '@')
      .replace(/"[^"\\\n\r]*"|true|false|null|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?/g, ']')
      .replace(/(?:^|:|,)(?:\s*\[)+/g, ''),
  );
};

export const checkPWV = (policy?: MODEL.IPolicy) => {
  return (
    (policy?.hasPOS === false || policy?.hasPOS === undefined) &&
    !!policy?.isNewPolicy &&
    policy?.policyStatus?.code === '20' &&
    !isEmpty(policy?.media?.[0]?.url)
  );
};