import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import initialState, { TOTPDecreeEConsentypeState } from './state';
import { LOADING_REQUEST, LOADING_SUCCESS, OTP_REQUEST_ERROR, SUBMIT_DECREE_ECONSENT_ERROR } from './constants';

const econsentReducer = (state: TOTPDecreeEConsentypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TOTPDecreeEConsentypeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case OTP_REQUEST_ERROR:
        draft.otpError = action.payload;
        break;
      case SUBMIT_DECREE_ECONSENT_ERROR:
        draft.submitError = action.payload;
        break;
    }
  });
};

export default econsentReducer;
