import React, { useCallback, useEffect, useRef, useState } from 'react';
import { SafeAreaView, FlatList, View, Image, TouchableOpacity, RefreshControl } from 'react-native';
import { Icon, InnerLoading, Loading, useTheme, NativeModuleClient, } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';
import { useNetInfo } from '@react-native-community/netinfo';
import _toUpper from 'lodash/toUpper';

import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { Theme } from '@/config/themes.config';
import NoConnection from '@/components/NoConnection';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import { CHANGE_TYPES_CUSTOMER, STORAGE_NAME, SUBMIT_BY_SYSTEM } from '@/utils/constants';
import { isEmpty } from '@/utils/utils';
import { getItem } from '@/utils/storage';

import IcareV2, { useIcareV2 } from '@/components/IcareV2';

import useStyles from './styles';
import initialState from '../../stores/state';
import {
  getAllServiceRequest as getAllServiceRequestAction,
  downLoadPdfServiceRequest as downLoadPdfServiceRequestAction,
  saveServiceRequest as saveServiceRequestAction,
  loadMoreServiceRequest as loadMoreServiceRequestAction,
} from '../../stores/actions';
import {
  makeServiceRequest,
  makeLoadingServiceRequest,
  makeLoadingDownloadServiceRequest,
  makeServiceRequestLoadMore,
  makeLoadingLoadMoreServiceRequest,
  makeSelectTransactionFilter,
  makeSelectTriggerFilterFlag,
} from '../../stores/selectors';

const STATUS_TRANSACTION_TYPE = {
  COMPLETED: 'COMPLETED',
  SUCCESS: 'SUCCESS',
  NEW: 'NEW',
  CANCELLED: 'CANCELLED',
  REJECTED: 'REJECTED',
  WITHDRAW: 'WITHDRAW',
  INPROGRESS: 'INPROGRESS',
};

export enum Language {
  vi = 'vi',
  en = 'en',
}

const FORMAT_DATE = 'YYYY-MM-DD:HH:mm:ss.SSS';
// 2022-06-23T00:00:00.000

const RequestHistory = () => {
  const modalDocumentErrorRef = useRef<ModalConfirmErrorRef | null>(null);
  const [downloadError, setDownloadError] = useState(false);
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles();
  const dispatch = useDispatch();
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const loading = useSelector(makeLoadingServiceRequest());
  const loadingLoadMore = useSelector(makeLoadingLoadMoreServiceRequest());
  const loadingDownload = useSelector(makeLoadingDownloadServiceRequest());
  const serviceRequest = useSelector(makeServiceRequest());
  const canLoadMore = useSelector(makeServiceRequestLoadMore());
  const transactionFilter = useSelector(makeSelectTransactionFilter());
  const triggerFilterFlag = useSelector(makeSelectTriggerFilterFlag());
  const dispatchDowloadDocument = (url: string, name: string, documentId?: string, documentContentType?: string) =>
    new Promise<void>((resolve, reject) => dispatch(downLoadPdfServiceRequestAction({ url, name, documentId, documentContentType }, resolve, reject)));
  const netInfo = useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [language, setLanguage] = useState<string>('vi');
  const [icareV2Ref, icareV2Instance] = useIcareV2();

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  const handleLoadMore = () => {
    if (loading || !canLoadMore || loadingLoadMore) return;
    dispatch(loadMoreServiceRequestAction());
  };

  const checkTypeTransaction = (status: string) => {
    switch (status) {
      case STATUS_TRANSACTION_TYPE.COMPLETED:
      case STATUS_TRANSACTION_TYPE.SUCCESS:
        return styles.successStatus;
      case STATUS_TRANSACTION_TYPE.INPROGRESS:
      case STATUS_TRANSACTION_TYPE.NEW:
        return styles.processingStatus;
      case STATUS_TRANSACTION_TYPE.CANCELLED:
      case STATUS_TRANSACTION_TYPE.REJECTED:
      case STATUS_TRANSACTION_TYPE.WITHDRAW:
        return styles.rejectedStatus;
      default:
        return styles.processingStatus;
    }
  };

  const downloadDocument = async (item?: MODEL.IServiceRequest) => {
    try {
      if (isEmpty(item?.url)) return;
      await dispatchDowloadDocument(item?.url!, item?.changeDetail!, item?.documentId, item?.documentContentType); 
    } catch (error) {
      setDownloadError(true);
    }
  };

  const handleTouchRequestHistory = (item: MODEL.IServiceRequest) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: `${ROUTE_PATH.TRANSACTION_HISTORY}:Request`,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Download document',
    });

    switch (_toUpper(item.submitBySystem)) {
      case _toUpper(SUBMIT_BY_SYSTEM.ICARE): {
        icareV2Instance().show({ ticketId: item.caseId });
        break;
      }
      default:
        downloadDocument(item);
    }
  };

  const onPullToRequest = async () => {
    if (loading) return;

    try {
      setRefreshing(true);
      dispatch(getAllServiceRequestAction());
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const getName = useCallback(
    (item: MODEL.IServiceRequest) => {
      return language === 'vi' ? item?.changeDetail : item?.changeDetailEng || '';
    },
    [language],
  );

  const renderRequestHistory = (item: MODEL.IServiceRequest) => {
    if (isEmpty(item)) return null;

    return (
      <TouchableOpacity onPress={() => handleTouchRequestHistory(item)} key={`${item.changeId}-${item.contactId}`}>
        <View style={styles.transactionWrapper}>
          <Icon name="Report" size={24} color="#5B636B" />
          <View style={styles.transactionContent}>
            <Text style={styles.transactionName} numberOfLines={2} ellipsizeMode="tail">
              {`${getName(item)}${CHANGE_TYPES_CUSTOMER.includes(item.changeCode) ? '' : ` - ${item.policyNumber}`}`}
            </Text>
            <View style={styles.wrapperTime}>
              <Text style={styles.time}>{item?.createdDate ? dayjs(item?.createdDate, FORMAT_DATE).format('DD/MM/YYYY h:mm A') : ''}</Text>
              <View style={styles.divider} />
              <Text style={styles.noText} numberOfLines={1} ellipsizeMode="tail">
                {t('home.transaction.history.request.no')}
                {item?.changeId}
              </Text>
            </View>
            <View style={styles.wrapperStatus}>
              <Text style={styles.status}>
                {t('home.transaction.history.status')}
                <Text style={checkTypeTransaction(item.status?.toUpperCase())}>
                  {item.status &&
                    Object.keys(STATUS_TRANSACTION_TYPE).includes(item.status.toUpperCase()) &&
                    t(`home.transaction.history.request.${item.status?.toLowerCase()}`)}
                </Text>
              </Text>
              {item?.url && (
                <View style={styles.iconDown}>
                  <Icon name="Down" size={24} color={theme.colors.primary} />
                </View>
              )}
            </View>
          </View>
        </View>
      </TouchableOpacity>
    );
  };

  const renderFooter = () => {
    return loadingLoadMore ? (
      <View style={styles.wapperBottomLoading}>
        <InnerLoading gifStyle={styles.loadMoreGif} />
      </View>
    ) : (
      <View style={styles.mb20} />
    );
  };

  useEffect(() => {
    dispatch(getAllServiceRequestAction());

    (async () => {
      const lang = await getItem<string>(STORAGE_NAME.LANGUAGE, 'vi');
      setLanguage(lang);
    })();

    return () => {
      dispatch(
        saveServiceRequestAction({ paging: initialState.icareRequest.paging, serviceRequests: initialState.icareRequest.serviceRequest }),
      );
    };
  }, []);

  useEffect(() => {
    if (triggerFilterFlag) {
      dispatch(getAllServiceRequestAction());
    };
  }, [transactionFilter])

  if ((!(serviceRequest?.length > 0) && loading) || (triggerFilterFlag && loading)) {
    return (
      <View style={styles.wrapperLoading}>
        <InnerLoading gifStyle={styles.loadGif} />
      </View>
    );
  }

  const onDismissLoading = () => {
    if (downloadError) {
      modalDocumentErrorRef?.current?.open();
    }
  };

  const onCancelDocumentErrorModal = () => {
    setDownloadError(false);
  };

  return (
    <SafeAreaView style={styles.flex1}>
      {isConnected ? (
        <>
          {serviceRequest.length > 0 ? (
            <>
              <FlatList
                style={styles.container}
                data={serviceRequest}
                ListFooterComponent={renderFooter}
                keyExtractor={(item, index) => item.requestId + index}
                renderItem={({ item }) => renderRequestHistory(item)}
                onEndReached={handleLoadMore}
                onEndReachedThreshold={0.01}
                showsVerticalScrollIndicator={false}
                refreshControl={<RefreshControl tintColor={theme.colors.primary} refreshing={isRefreshing} onRefresh={onPullToRequest} />}
              />
              <Loading visible={loadingDownload} dismissAction={onDismissLoading} />
              <ModalConfirmError
                ref={modalDocumentErrorRef}
                subtitle={t('common.text.subtitle.download')}
                title={t('common.text.title.download')}
                btnText={t('common.text.cancel.download')}
                onConfirm={onCancelDocumentErrorModal}
              />
              <IcareV2 ref={icareV2Ref} />
            </>
          ) : (
            <View style={styles.wrapperImageNodata}>
              <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
              <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
            </View>
          )}
        </>
      ) : (
        <NoConnection />
      )}
    </SafeAreaView>
  );
};

export default RequestHistory;
