import React, { forwardRef, useImperativeHandle } from 'react';
import { View, Image } from 'react-native';
import { Button } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import warningIcon from '@/assets/images/modal-confirm-change-profile.png';
import Text from '@/components/Text';
import Modal, { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface ModalConfirmRequestProps {
  onConfirm?: () => void;
  onCancel?: () => void;
}

export interface ModalConfirmRequestRef {
  open: () => void;
  close: () => void;
}

const ModalConfirmRequest = (props: ModalConfirmRequestProps, ref: React.Ref<unknown> | undefined) => {
  const { onConfirm, onCancel } = props;
  const [modalRef, modalInstance] = useModal();
  const { t } = useTranslation();

  const handleSubmit = () => {
    modalInstance()?.close();
    if (onConfirm) {
      onConfirm();
    }
  };

  useImperativeHandle(
    ref,
    (): ModalConfirmRequestRef => ({
      open: () => {
        modalInstance()?.show();
      },
      close: () => {
        modalInstance()?.close();
      },
    }),
    [],
  );

  return (
    <Modal ref={modalRef} animationType="fade" modalStyle={{ backgroundColor: 'transparent' }}>
      <View style={styles.modalContainer}>
        <View style={styles.modalContent}>
          <View style={{ alignItems: 'center' }}>
            <Image style={styles.imageStyles} source={warningIcon} />
          </View>
          <View>
            <Text style={styles.title} fontFamily="bold">
              {t('myprofile.modal.confirm.request.title')}
            </Text>
            <Text style={styles.subtitle}>{t('myprofile.modal.confirm.request.sub.title')}</Text>
          </View>
          <Button
            onPress={handleSubmit}
            text={t('myprofile.modal.confirm.request.button.confirm')}
            size="large"
            uppercase={false}
            labelStyle={styles.buttonText}
            style={[styles.button, styles.mt19]}
            id="ModalConfirmRequest.btnCloseModalError"
          />
          <Button
            onPress={() => {
              if (onCancel) {
                onCancel();
              }
              modalInstance().close();
            }}
            text={t('myprofile.modal.confirm.request.button.cancel')}
            type="secondary"
            size="large"
            uppercase={false}
            labelStyle={styles.buttonText}
            style={[styles.button, styles.mt16]}
            id="ModalConfirmRequest.btnCloseModalError"
          />
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalConfirmRequest);
