import React, { useEffect, useState, useRef, useImperativeHandle, forwardRef } from 'react';
import {
  View,
  TouchableOpacity,
  KeyboardAvoidingView,
  Platform,
  Animated,
  Dimensions,
  Keyboard,
  TouchableWithoutFeedback,
} from 'react-native';
import { useTranslation } from 'react-i18next';
import { Button, NativeModuleClient, PinCode } from 'superapp-common-component';
import dayjs from 'dayjs';

import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import ModalConfirmSuccess, { ModalConfirmSuccessRef } from '@/components/ModalConfirmSuccess';
import { setItem } from '@/utils/storage';
import { STORAGE_NAME } from '@/utils/constants';
import ModalOverlay from '@/components/ModalOverlay/index2';
import Text from '@/components/Text';
import { a11y } from '@/utils/utils';

import styles from './styles';
import { HITSLOP } from '@/utils/styles';

type CreatePincodeProps = {
  onGoBack?: () => void;
};

export interface ModalCreatePinRef {
  setVisibleModal: (isOpen: boolean) => void;
}

const width = Dimensions.get('window').width;
const TIME_OUT_SHOW_KEYBOARD = 300;
const STEPS_CREATE_PINCODE = { CREATE: 1, CONFIRM: 2 };

const CreatePincode = (props: CreatePincodeProps, ref: React.Ref<unknown> | undefined) => {
  const { onGoBack } = props;
  const [showModal, setShowModal] = useState(false);
  const { t } = useTranslation();
  const [password, setPassword] = useState('');
  const [step, setStep] = useState(1);
  const [confirmPassword, setConfirmPassword] = useState('');
  const modalConfirmErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalConfirmErrorCreatePinRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalConfirmSuccessRef = useRef<null | ModalConfirmSuccessRef>(null);
  const animation = useRef<any>(new Animated.Value(0)).current;
  const createPincodeRef = useRef<any>();
  const confirmPincodeRef = useRef<any>();

  const setVisibleModal = (isOpen: boolean) => setShowModal(isOpen);

  useImperativeHandle(ref, (): ModalCreatePinRef => ({ setVisibleModal }));

  const handleNextStepAnimation = (nextStep: number) => {
    Animated.timing(animation, {
      toValue: -((nextStep - 1) * width),
      duration: 250,
      useNativeDriver: false,
    }).start();
    Keyboard.dismiss();
  };

  const hideKeyboard = () => Keyboard.dismiss();

  const closeModal = () => {
    setPassword('');
    setConfirmPassword('');
    setShowModal(false);
    if (onGoBack) {
      onGoBack();
    }
  };

  useEffect(() => {
    if (!showModal) {
      setPassword('');
      setConfirmPassword('');
      setStep(1);
      return handleNextStepAnimation(1);
    }
  }, [showModal]);

  const showConfirmErrorModal = () => {
    modalConfirmErrorRef?.current?.open();
  };

  const showConfirmSuccessModal = () => {
    NativeModuleClient.AdobeExperience.trackScreen({
      screen: 'Create Pincode Success',
      unixTimestamp: dayjs().unix(),
    });
    modalConfirmSuccessRef.current?.open();
  };

  const checkPinCode = async () => {
    if (password.length === 6 && confirmPassword.length === 6) {
      if (password === confirmPassword) {
        await setItem(STORAGE_NAME.PIN_CODE, JSON.stringify(password));
        showConfirmSuccessModal();
        setShowModal(false);
      } else {
        setConfirmPassword('');
        showConfirmErrorModal();
      }
    } else if (password.length === 6 && confirmPassword.length < 6) {
      setConfirmPassword('');
      showConfirmErrorModal();
    }
  };

  const handleSubmission = async () => {
    hideKeyboard();
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Create Pincode',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });

    if (password.length === 6 && animation?._value === 0) {
      setStep(2);
      handleNextStepAnimation(2);
    } else if (password.length < 6) {
      setPassword('');
      setConfirmPassword('');
      modalConfirmErrorCreatePinRef?.current?.open();
    } else {
      checkPinCode();
    }
  };

  const renderCreatePincodeContent = () => {
    return (
      <>
        <View style={styles.wrapTitle}>
          <Text style={styles.titlePinCode} fontFamily="bold">
            {t('profile.pincode.create')}
          </Text>
          <Text style={styles.subtitlePinCode}>{t('profile.pincode.code_length_message')}</Text>
        </View>
        <View style={styles.wrapPincode}>
          <PinCode id="Profile.pcCreatePincode" pinRef={createPincodeRef} code={password} onTextChange={setPassword} />
        </View>
      </>
    );
  };

  const renderConfirmPincodeContent = () => {
    return (
      <>
        <View style={styles.wrapTitle}>
          <Text style={styles.titlePinCode} fontFamily="bold">
            {t('profile.pincode.re_enter_code')}
          </Text>
          <Text style={styles.subtitlePinCode}>{t('profile.pincode.code_length_message')}</Text>
        </View>
        <View style={styles.wrapPincode}>
          <PinCode id="Profile.pcConfirmPincode" pinRef={confirmPincodeRef} code={confirmPassword} onTextChange={setConfirmPassword} />
        </View>
      </>
    );
  };

  useEffect(() => {
    if (step === STEPS_CREATE_PINCODE.CREATE && showModal === true) {
      setTimeout(() => {
        createPincodeRef?.current?.focus();
      }, TIME_OUT_SHOW_KEYBOARD);
    } else if (step === STEPS_CREATE_PINCODE.CONFIRM && showModal === true) {
      setTimeout(() => {
        confirmPincodeRef?.current?.focus();
      }, TIME_OUT_SHOW_KEYBOARD);
    }
  }, [showModal, step]);

  const renderPincode = () => {
    return (
      <>
        <ModalOverlay isVisible={showModal}>
          <TouchableWithoutFeedback {...a11y('Profile.btnCreatePinHideKeyboard')} onPress={hideKeyboard}>
            <Animated.View style={[styles.container, { marginLeft: animation }]}>
              <View style={styles.pane}>
                <View style={styles.containerPinCode}>
                  <View>
                    <TouchableOpacity
                      {...a11y('Profile.btnCloseModalCreatePin')}
                      onPress={closeModal}
                      style={{ alignSelf: 'flex-start' }}
                      hitSlop={HITSLOP}
                    >
                      <Text style={styles.leftLabelButton}>{t('profile.pincode.button.close')}</Text>
                    </TouchableOpacity>
                    {renderCreatePincodeContent()}
                  </View>
                  <KeyboardAvoidingView
                    behavior={Platform.OS === 'ios' ? 'padding' : undefined}
                    keyboardVerticalOffset={Platform.OS === 'ios' ? 80 : 0}
                  >
                    <View style={styles.wrapBtnContinue}>
                      <Button
                        text={t('profile.pincode.continue')}
                        type="primary"
                        uppercase={false}
                        labelStyle={styles.buttonText}
                        size="large"
                        style={styles.button}
                        onPress={handleSubmission}
                        disabled={false}
                        id="Profile.btnSubmitCreatePin"
                      />
                    </View>
                  </KeyboardAvoidingView>
                </View>
              </View>

              <View style={styles.pane}>
                <View style={styles.containerPinCode}>
                  <View>
                    <TouchableOpacity
                      {...a11y('Profile.btnCloseModalConfirmPin')}
                      onPress={closeModal}
                      style={{ alignSelf: 'flex-start' }}
                      hitSlop={HITSLOP}
                    >
                      <Text style={styles.leftLabelButton}>{t('profile.pincode.button.close')}</Text>
                    </TouchableOpacity>
                    {renderConfirmPincodeContent()}
                  </View>
                  <KeyboardAvoidingView
                    behavior={Platform.OS === 'ios' ? 'padding' : undefined}
                    keyboardVerticalOffset={Platform.OS === 'ios' ? 80 : 0}
                  >
                    <View style={styles.wrapBtnContinue}>
                      <Button
                        text={t('profile.pincode.continue')}
                        type="primary"
                        uppercase={false}
                        labelStyle={styles.buttonText}
                        size="large"
                        style={styles.button}
                        onPress={handleSubmission}
                        disabled={false}
                        id="Profile.btnSubmitConfirmPin"
                      />
                    </View>
                  </KeyboardAvoidingView>
                </View>
              </View>
            </Animated.View>
          </TouchableWithoutFeedback>
          <ModalConfirmError
            ref={modalConfirmErrorCreatePinRef}
            title=""
            subtitle={t('profile.pincode.create.modal.not.correct')}
            btnText={t('profile.pincode.button.close')}
            onConfirm={() => {
              createPincodeRef?.current?.focus();
            }}
          />
          <ModalConfirmError
            ref={modalConfirmErrorRef}
            title={t('profile.pincode.error.modal.title')}
            subtitle={t('login.error.notmatch')}
            btnText={t('profile.pincode.button.close')}
            onConfirm={() => {
              confirmPincodeRef?.current?.focus();
            }}
          />
        </ModalOverlay>
        <ModalConfirmSuccess
          ref={modalConfirmSuccessRef}
          onConfirm={() => {
            closeModal();
            NativeModuleClient.AdobeExperience.trackButton({
              screen: 'Create Pincode Success',
              unixTimestamp: dayjs().unix(),
              buttonText: 'Close',
            });
          }}
          title={t('profile.pincode.create.success')}
          subtitle={t('profile.pincode.create.created')}
          btnText={t('profile.pincode.button.close')}
        />
      </>
    );
  };
  return renderPincode();
};
export default forwardRef(CreatePincode);
