import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class AEMService extends Request {
  public constructor() {
    super(APP_ENV.CONTENT_AEM_WRAPPER, { skipAuthen: true });
  }

  public getContentService = (): Promise<MODEL.IContentOnboardingResponse> =>
    this.instance.get('/superapp/vi/api/onboarding-cards.model.json');

  public getTranslateLoginService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-login.model.json');

  public getTranslateRegistrationService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-registration.model.json');

  public getTranslateProfileService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-profile.model.json');

  public getTranslateHomeService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-home.model.json');

  public getTranslateVerifyAccountService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-verify-account.model.json');

  public getTranslateCommonService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-common.model.json');

  public getTranslateEcardService = (): Promise<MODEL.IContentTranslateResponse> =>
    this.instance.get('/superapp/vi/api/i18n-ecards.model.json');

  public getLastTimeModified = (): Promise<{ [key: string]: number }> =>
    this.instance.get('/superapp/vi/api/lastmodification-content.model.json');

  public getProductImage = (): Promise<MODEL.IContentProductImageResponse> =>
    this.instance.get(`/superapp/vi/api/product-icon-list.model.json`);

  public getIntroductionService = (): Promise<MODEL.IIntroductionResponse> => this.instance.get(`/superapp/vi/api/introduction.model.json`);

  public getTierVipService = (): Promise<MODEL.IIntroductionResponse> => this.instance.get(`/superapp/vi/api/tiervip.model.json`);
}

export default new AEMService();
