import {Dimensions, Image, Platform, Pressable, TouchableOpacity, View} from 'react-native'
import React, {useEffect, useState} from 'react'
import styles from './styles';
import {Text} from 'superapp-common-component';
import ImagePicker from 'react-native-image-crop-picker';
import {useTranslation} from 'react-i18next';
import {Face} from "@/pages/VerifyAccount/types";
import { a11y } from '@/utils/utils';

type Props = {
  image?: string,
  onSubmitOCR: (data: any) => void;
  type: Face;
  label?: string;
  isErrorOCR?: boolean;
}

const TakePhotoOCR = (props: Props) => {
  const [image, setImage] = useState<any>(props.image);
  const {t} = useTranslation();
  const {isErrorOCR, label} = props;
  const TakePhoto = async () => {
    await ImagePicker.openCamera({
      cropping: true,
      multiple: false,
      maxFiles: 1, //
      includeBase64: true,
      mediaType: 'photo',
      width: Platform.OS === "ios" ? Dimensions.get('window').width : undefined,
      height: Platform.OS === "ios" ? 500 : 150,
      zoom: true,
      avoidEmptySpaceAroundImage: false,
      compressImageMaxWidth: Dimensions.get('window').width,
      compressImageQuality: 1,
      freeStyleCropEnabled: true
    }).then((img: any) => {
      setImage(img);
      props.onSubmitOCR({
        data: img?.data,
        type: img?.mime,
        name: img?.path.substring(img?.path.lastIndexOf('/') + 1, img?.path.length)
      });
    })
  }

  useEffect(() => {
    if (isErrorOCR) {
      setImage(null);
    }
  }, [isErrorOCR])
  
  return (
    <>
      <Text style={styles.textPhotoLabel}>{label}</Text>
      {
        image && !isErrorOCR ? <View style={styles.container}>
            <Image source={{uri: image?.path}} style={styles.imgCard} resizeMode='cover' height={184} width={274}/>
            <Pressable onPress={TakePhoto} style={styles.btnPhotoIconTop} {...a11y('VerifyAccount.TakePhotoOCR.btnTakePhoto1')}>
              <Image resizeMode='contain'
                     source={require('@/assets/images/icons/icon-camera-gray.png')}
                     width={40} height={40}
                     style={styles.photoIcon}/>
            </Pressable>
          </View> :
          <TouchableOpacity style={styles.container} onPress={TakePhoto} {...a11y('VerifyAccount.TakePhotoOCR.btnTakePhoto2')}>
            <Image resizeMode='contain'
                   source={isErrorOCR ? require('@/assets/images/icons/icon-try.png') : require('@/assets/images/icons/icon-camera.png')}
                   width={20} height={20}
                   style={styles.photoIcon}/>
            <Text>{isErrorOCR ? t('common.tryAgain') : t('verifyAccount.page.verifyAccountTakePhoto')}</Text>
          </TouchableOpacity>
      }
    </>
  )
}


export default TakePhotoOCR
