/**
 * Create the store with dynamic reducers
 */

import { applyMiddleware, createStore, compose } from 'redux';
import { createInjectorsEnhancer } from 'redux-injectors';
import createSagaMiddleware from 'redux-saga';
import { Store } from 'redux';
import { Saga } from 'redux-saga';

import { createReducer } from './reducers';
import createSaga from './sagas';

interface InjectedStore extends Store {
  injectedReducers: any;
  injectedSagas: any;
  runSaga(saga: Saga<any[]> | undefined, args: any | undefined): any;
}

export default function configureStore(initialState = {}) {
  const reduxSagaMonitorOptions = {};
  const sagaMiddleware = createSagaMiddleware(reduxSagaMonitorOptions);
  const { run: runSaga } = sagaMiddleware;

  // Create the store with two middlewares
  const middlewares = [sagaMiddleware];

	// Middleware redux-flipper debug
  // if (__DEV__) {
	// 	const createDebugFlipper = require('redux-flipper').default;
	// 	middlewares.push(createDebugFlipper());
  // }

  const enhancers = [
    applyMiddleware(...middlewares),
    createInjectorsEnhancer({
      createReducer,
      runSaga,
    }),
  ];

  let enhancer = compose(...enhancers) as any;
  const store = createStore(createReducer(), initialState, enhancer) as InjectedStore;

  // Extensions
  store.runSaga = sagaMiddleware.run;
  store.injectedReducers = {}; // Reducer registry
  store.injectedSagas = {}; // Saga registry
  reduxStore = store;

  // initial saga
  store.runSaga(createSaga, undefined);

  return store;
}
