const {ExitApp} = NativeModules;

dayjs.extend(customParseFormat);
dayjs.extend(isSameOrBefore);

type Nullable<T> = T | null;
type AppProps = {additionProps?: string};
type TAdditionProps = {type?: string; policyNumber?: string} | undefined;
global.fetch = fetchPolyfill;

const Stack = createStackNavigator();
const store = configureStore();

// COMMON CONFIG
const setupCommonConfig = () => {
  CommonConfig.setEnvironment(APP_ENV.AZURE_ENV);
};
setupCommonConfig();

// STATUS PAGE
const setupStatusPage = (params: {isLoop: boolean}) => {
  const internalFunc = () => {
    const COMPONENT_ID =
      Platform.OS === 'ios'
        ? APP_ENV.COMPONENT_ID_IOS
        : APP_ENV.COMPONENT_ID_ANDROID;

    StatusPage.incidents({
      success: (data: any) => {
        try {
          const incident = data?.incidents.find((item: any) => {
            return item?.components?.some((it: any) => it.id === COMPONENT_ID);
          });

          if (incident && incident?.status !== 'resolved') {
            MiniApp.popToApp('host');
            StatusPage.emitIncident({
              hasIncident: true,
              status: incident.status,
              title: incident.name,
              description: incident.incident_updates?.[0]?.body,
            });
          } else {
            StatusPage.emitIncident({hasIncident: false});
          }
        } catch {}
      },
    });
    StatusPage.scheduled_maintenances({
      success: (data: any) => {
        try {
          const maintenance = data?.scheduled_maintenances.find((item: any) => {
            return item?.components?.some((it: any) => it.id === COMPONENT_ID);
          });

          if (maintenance && maintenance?.status !== 'completed') {
            MiniApp.popToApp('host');
            StatusPage.emitMaintenance({
              hasMaintenance: true,
              status: maintenance.status,
              title: maintenance.name,
              description: maintenance.incident_updates?.[0]?.body,
              startTime: maintenance.scheduled_for,
              endTime: maintenance.scheduled_until,
            });
          } else {
            StatusPage.emitMaintenance({hasMaintenance: false});
          }
        } catch {}
      },
    });
  };

  let loopId: NodeJS.Timer;
  if (params.isLoop) {
    loopId = setInterval(internalFunc, 30 * 1000); // 30s
  } else {
    internalFunc();
  }
  return () => {
    loopId && clearInterval(loopId);
  };
};

// ADOBE EXPERIENCE
const setupAdobeExperience = async () => {
  try {
    AdobeExperience.setEnvironment(APP_ENV.AZURE_ENV as 'UAT' | 'PROD');
    const [token, language] = await Promise.all([
      getItem(STORAGE_NAME.ACCESS_TOKEN),
      getItem(STORAGE_NAME.LANGUAGE, 'vi'),
    ]);

    if (token) {
      const tokenInfo = jwtDecode<MODEL.ITokenDecode>(token);
      NativeModuleClient.AdobeExperience.setUser(tokenInfo.sub);
    }
    NativeModuleClient.AdobeExperience.setLanguage(language);
  } catch (e) {}
};

/* AZURE NOTIFICATION */
const setupAzureNotification = () => {
  if (__DEV__) return () => null;

  (async () => {
    try {
      await AzureNotificationClient.start();
      // first time init notification
      const isPushNotification = await getItem<boolean>(
        STORAGE_NAME.REQUEST_PUSH_NOTIFICATION,
        false,
      );
      if (isPushNotification) return;

      setItem(STORAGE_NAME.REQUEST_PUSH_NOTIFICATION, JSON.stringify(true));
      // add GENERAL tag
      const newTags = [GENERAL_TAG];

      // add user tag
      const token = await getItem(STORAGE_NAME.ACCESS_TOKEN);
      if (token) {
        const userId = AzureNotificationClient.getUserId(token);
        userId && newTags.push(userId);
      }
      AzureNotificationClient.addTags(newTags);
    } catch {}
  })();

  // tap notification when the application is opened from a quit state.
  AzureNotificationClient.onInitialNotification().then(message => {
    if (message) {
      /** Todo code here */
      jsEventEmitter.emit(JS_EVENT_NAMES.NOTIFICATION_TO_HOME, message);
    }
  });

  // receive message
  const unsubscribeMessage = AzureNotificationClient.onMessage(message => {
    jsEventEmitter.emit(JS_EVENT_NAMES.NOTIFICATION_ON_MESSAGE, message);
  });

  // receive background message
  // required async function
  // @ts-ignore
  const unsubscribeBackgroundMessage =
    AzureNotificationClient.onBackgroundMessage(async message => {
      jsEventEmitter.emit(JS_EVENT_NAMES.NOTIFICATION_ON_MESSAGE, message);
    });

  // tap notification, When the application is running, but in the background.
  const unsubscribeTapNotification = AzureNotificationClient.onTapNotification(
    message => {
      navigationRef.dispatch(StackActions.popToTop());
      jsEventEmitter.emit(JS_EVENT_NAMES.NOTIFICATION_TO_HOME, message);
    },
  );

  return () => {
    unsubscribeMessage();
    unsubscribeBackgroundMessage();
    unsubscribeTapNotification();
  };
};

const {OKTA_ENABLE, OKTA_CLIENT_ID, OKTA_DISCOVERY_URI, OKTA_REDIRECT_URI} =
  APP_ENV;
const App: React.FC<AppProps> = ({additionProps}) => {
  // const {feedbackRef} = useFeedback();
  const netInfo = NetInfo.useNetInfo();
  const [isLoading, setLoading] = useState<boolean>(true);
  const [isNotShowOnboarding, setShowOnboarding] = useState<boolean>(true);
  const [themePaper, setTheme] = useState<ThemePaperType>(theme.theme.light);
  const [additionPropsValues, setAdditionProps] =
    useState<TAdditionProps>(undefined);
  const [showJailBreakRestriction, setShowJailBreakRestriction] =
    useState(true);
  const [incidentDetail, setIncidentDetail] = useState<{
    hasIncident: Nullable<boolean>;
    status?: string;
    title?: string;
    description?: string;
  }>({hasIncident: null});
  const [maintenanceDetail, setMaintenanceDetail] = useState<{
    hasMaintenance: Nullable<boolean>;
    status?: string;
    title?: string;
    description?: string;
    startTime?: string;
    endTime?: string;
  }>({hasMaintenance: null});
  const appUpdate = useAppUpdate();
  const [isPrivateNetwork, setPrivateNetwork] = useState(false);

  const getStackNavigation = () =>
    isNotShowOnboarding
      ? _omit(rootStackConfig, 'onboarding')
      : rootStackConfig;

  const handleStateChangeNavigation = (state?: NavigationState) => {
    try {
      const [lastRoute] = state!.routes.slice(-1);
      if (lastRoute) {
        let routeName = lastRoute.name;
        const isTab = lastRoute?.state?.type === 'tab';
        if (isTab) {
          const {state: routeState} = lastRoute;
          routeName = routeState?.routes[routeState.index!]?.name!;
        }

        NativeModuleClient.AdobeExperience.trackScreen({
          screen: routeName,
          unixTimestamp: dayjs().unix(),
        });
      }
    } catch (err) {
      console.error(err);
    }
  };

  const handleAppUpdate = async () => {
    try {
      const info = await appUpdate.fetchClientGet();
      if (info?.hasNewVersion && info?.skipVersion !== info.version) {
        MiniApp.popToApp(APPS_NAME.HOST);
      }
    } catch (error) {
      console.log('ERROR: Check version app', error);
    }
  };

  const handleEvents = (payload: string = '') => {
    const parsePayload = JSON.parse(payload);

    if (parsePayload.type === EVENT_NAMES.ON_APP_FOREGROUND) {
      handleAppUpdate();
    }
  };

  useEffect(() => {
    const subscription = MiniAppEventEmitter?.addListener(
      MINI_APP_EVENTS_EMITTER.MINI_APP_EVENT_EMITTER,
      handleEvents,
    );

    return () => subscription?.remove();
  }, [appUpdate]);

  useEffect(() => {
    (async () => {
      const isShow = await getItem<boolean>(STORAGE_NAME.ONBOARDING, false);
      await i18n.getResourcesTranslation();

      // initial page
      let routeName = 'onboarding';
      if (!isShow) {
        routeName = 'Home';
      }

      /* SETUP ADOBE EXPERIENCE */
      await setupAdobeExperience();

      setShowOnboarding(isShow);
      setLoading(false);

      NativeModuleClient.AdobeExperience.trackScreen({
        screen: routeName,
        unixTimestamp: dayjs().unix(),
      });
      try {
        appInfo.getAppInfo({appName: 'host', environment: 'UAT', os: 'IOS'});
      } catch {}
    })();

    const setUpOkta = async () => {
      await createConfig({
        clientId: OKTA_CLIENT_ID,
        discoveryUri: OKTA_DISCOVERY_URI,
        redirectUri: OKTA_REDIRECT_URI,
        endSessionRedirectUri: OKTA_REDIRECT_URI,
        scopes: ['openid', 'membership', 'offline_access'],
        requireHardwareBackedKeyStore: false,
      });
    };

    if (OKTA_ENABLE) {
      setUpOkta();
    }

    /* SETUP NOTIFICATION */
    const unsubscribeAzureNotification = setupAzureNotification();

    /* SETUP STATUS PAGE */
    const unsubscribeStatusPage = setupStatusPage({isLoop: true});
    const unsubscribeIncident = StatusPage.subscribeIncident(
      (value: {
        hasIncident: boolean;
        status?: string;
        title?: string;
        description?: string;
      }) => {
        const {hasIncident, status, title, description} = value || {};
        setIncidentDetail({hasIncident, status, title, description});
      },
    );
    const unsubscribeMaintenance = StatusPage.subscribeMaintenance(
      (value: {
        hasMaintenance: boolean;
        status?: string;
        title?: string;
        description?: string;
        startTime?: string;
        endTime?: string;
      }) => {
        const {hasMaintenance, status, title, description, startTime, endTime} =
          value || {};
        setMaintenanceDetail({
          hasMaintenance,
          status,
          title,
          description,
          startTime,
          endTime,
        });
      },
    );

    return () => {
      unsubscribeAzureNotification();
      // #region STATUS PAGE
      unsubscribeStatusPage();
      unsubscribeIncident();
      unsubscribeMaintenance();
      // #endregion STATUS PAGE
    };
  }, []);

  // first check app after started
  useEffect(() => {
    (async () => {
      const isPrivate = await isPrivateIp();
      setPrivateNetwork(isPrivate);
      netInfo.isConnected &&
        (await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.HOST));
      setShowJailBreakRestriction(JailMonkey.isJailBroken());
    })();
    if (netInfo.isConnected) {
      setupStatusPage({isLoop: false});
    }
  }, [netInfo.isConnected]);

  const onPressExitApp = useCallback(() => {
    ExitApp.exit();
  }, []);

  useEffect(() => {
    if (!isEmpty(additionProps)) {
      const additionPropsParse = JSON.parse(additionProps || '');
      setAdditionProps(additionPropsParse);
    }
  }, [additionProps]);

  if (incidentDetail.hasIncident) {
    return (
      <>
        <IncidentScreen
          status={incidentDetail.status}
          title={incidentDetail.title}
          description={incidentDetail.description}
        />
        <PortalModal />
      </>
    );
  }

  if (maintenanceDetail.hasMaintenance && !isPrivateNetwork) {
    return (
      <>
        <MaintenanceScreen
          status={maintenanceDetail.status}
          title={maintenanceDetail.title}
          description={maintenanceDetail.description}
          startTime={maintenanceDetail.startTime}
          endTime={maintenanceDetail.endTime}
        />
        <PortalModal />
      </>
    );
  }

  if (
    appUpdate.appInfo.hasNewVersion &&
    appUpdate.appInfo.skipVersion !== appUpdate.appInfo.version
  ) {
    return (
      <PaperProvider theme={themePaper}>
        <AppUpdate
          url={appUpdate?.appInfo?.url}
          isForceUpdate={appUpdate?.appInfo?.forceUpdate}
          onClose={() => {
            !isNotShowOnboarding && setShowOnboarding(true);
            appUpdate?.onSkipVersionUpdate();
          }}
        />
      </PaperProvider>
    );
  }

  return isLoading ? (
    <SplashLoading />
  ) : (
    <Provider store={store}>
      <PaperProvider theme={themePaper}>
        {showJailBreakRestriction ? (
          <Restriction onPress={onPressExitApp} />
        ) : (
          <SafeAreaProvider>
            <NavigationContainer
              ref={navigationRef}
              onStateChange={handleStateChangeNavigation}>
              <Stack.Navigator
                initialRouteName={
                  !isEmpty(additionPropsValues?.type)
                    ? additionPropsValues?.type
                    : undefined
                }>
                {_keys(getStackNavigation()).map(key => {
                  const navigator = rootStackConfig[key];
                  const option = navigator.options || {};
                  const title = option.title ? option.title : navigator.name;
                  if (
                    navigator.name === ROUTE_PATH.TRANSACTION_HISTORY &&
                    !isEmpty(additionPropsValues?.policyNumber)
                  ) {
                    return (
                      <Stack.Screen
                        key={navigator.name}
                        initialParams={{
                          type: additionPropsValues?.type,
                          policyNumber: additionPropsValues?.policyNumber,
                        }}
                        {...navigator}
                        options={{...option, title}}
                      />
                    );
                  }
                  return (
                    <Stack.Screen
                      key={navigator.name}
                      {...navigator}
                      options={{...option, title}}
                    />
                  );
                })}
              </Stack.Navigator>
              <PortalModal />
            </NavigationContainer>
            {/* <AIAFeedback ref={feedbackRef} /> */}
          </SafeAreaProvider>
        )}
      </PaperProvider>
    </Provider>
  );
};

export default App;

