import React, { useRef, useEffect, useState } from 'react';
import { View, Animated, ViewStyle, Pressable } from 'react-native';

import useStyles from './styles';

type FlipCardProps = {
  viewFront?: JSX.Element;
  viewBack?: JSX.Element;
  containerStyle?: ViewStyle;
  frontAnimatedStyle?: ViewStyle;
  backAnimatedStyle?: ViewStyle;
};

const FlipCard: React.FC<FlipCardProps> = props => {
  const styles = useStyles();
  const { viewFront, viewBack, containerStyle } = props;
  const [isFront, setIsFront] = useState(true);
  const valueListenner = useRef<number>(0);
  const animatedValue = useRef(new Animated.Value(0)).current;

  const frontInterpolate = animatedValue.interpolate({
    inputRange: [0, 180],
    outputRange: ['0deg', '180deg'],
  });

  const backInterpolate = animatedValue.interpolate({
    inputRange: [0, 180],
    outputRange: ['180deg', '360deg'],
  });

  const stylesAnimatedFront = (): Animated.WithAnimatedObject<ViewStyle> => ({
    transform: [{ rotateY: frontInterpolate }],
  });

  const stylesAnimatedBack = (): Animated.WithAnimatedObject<ViewStyle> => ({
    transform: [{ rotateY: backInterpolate }],
  });

  const onPressFlipCard = () => {
    Animated.spring(animatedValue, {
      toValue: valueListenner.current >= 90 ? 0 : 180,
      friction: 8,
      tension: 10,
      useNativeDriver: false,
    }).start();
  };

  useEffect(() => {
    animatedValue.addListener(({ value }) => {
      valueListenner.current = value;
      if (value >= 90) {
        setIsFront(false);
      } else {
        setIsFront(true);
      }
    });

    return () => animatedValue.removeAllListeners();
  }, []);

  return (
    <View style={[styles.container, containerStyle]}>
      <Pressable onPress={onPressFlipCard}>
        <Animated.View style={[styles.card, stylesAnimatedFront(), isFront && { zIndex: 100 }]}>{viewFront}</Animated.View>
        <Animated.View style={[styles.card, styles.flipCardBack, stylesAnimatedBack(), !isFront && { zIndex: 100 }]}>
          {viewBack}
        </Animated.View>
      </Pressable>
    </View>
  );
};

export default FlipCard;
