import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';
import responseParser from '@/utils/VitalityResponseParser';

import { KEYS_TRANSLATE_AEM } from '@/utils/constants';
import { AEMAssessmentsType } from '@/utils/vitalityAssessment/types';
import { RawJson } from '@/utils/VitalityResponseParser/AEMVitalityResponseParser/types';

import AEMService from './aem';

const normalizeAssessmentAEMContent = ({
  content = {},
  name = '',
  lastModifiedDate = '',
}: {
  content?: AEMAssessmentsType;
  name?: string;
  lastModifiedDate?: string;
}) => {
  return {
    elements: [
      {
        name: name,
        lastModifiedDate: lastModifiedDate,
        value: {
          ...content,
          healthReport: content.HealthReport,
        },
      },
    ],
  };
};

class VitalityAEMService extends Request {
  public constructor() {
    super(`${APP_ENV.CONTENT_AEM}/content/vn-myaia/vitality`);
  }

  public getTranslateVitalityAssessmentsService = async (): Promise<any> => {
    const promises = ['vi', 'en'].map(lang =>
      this.getContentService({
        lang,
        filename: 'assessments',
        getContent: value => value.assessments,
      }),
    );
    const [viContent, enContent] = await Promise.all(promises);
    const lastModifiedDate = await AEMService.getLastTimeModified();
    const AEMContent = {
      lastModifiedDate: lastModifiedDate[KEYS_TRANSLATE_AEM.VITALITY_ASSESSMENTS],
      items: {
        vi: normalizeAssessmentAEMContent(viContent),
        en: normalizeAssessmentAEMContent(enContent),
      },
    };
    return AEMContent;
  };

  public getContentService = async ({
    lang = 'en',
    filename = '',
    parser = responseParser.AEMVitalityResponseParser.parseNewAEMContent,
    parserParams = [],
    getContent = (parsedData: any) => parsedData,
  } = {}): Promise<{ name: string; lastModifiedDate: string; content: any }> => {
    const resource = `/${lang}/api/${filename}.model.tidy.json`;
    const aemResult = (await this.instance.get(resource)) as RawJson;
    const parsedData = aemResult ? parser(aemResult, ...parserParams) : aemResult;
    const content = getContent(parsedData);

    return { lastModifiedDate: aemResult.lastModifiedDate, name: aemResult.title, content };
  };
}

export default new VitalityAEMService();
