import React, { useLayoutEffect, useRef, useState, useEffect, useCallback } from 'react';
import { View, TouchableOpacity, StatusBar, SafeAreaView, ScrollView, RefreshControl, BackHandler, Linking, Alert } from 'react-native';
import { useTranslation } from 'react-i18next';
import {
  Icon,
  useTheme,
  NativeModuleClient,
  theme as commonTheme,
  ModalMessage,
  ModalMessageRef,
  Loading,
} from 'superapp-common-component';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';
import NetInfo from '@react-native-community/netinfo';
import _intersection from 'lodash/intersection';
import _every from 'lodash/every';
import _size from 'lodash/size';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap } from '@react-navigation/stack';

import { makeSelectProfile } from '@/stores/global/selectors';
import { a11y, maskedPhoneNumber, maskedNumber, isEmpty } from '@/utils/utils';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import { Theme } from '@/config/themes.config';
import { pullToRefreshProfile, asyncCheckAMLAction } from '@/stores/global/actions';
import roles from '@/utils/roles';
import { AML_TYPE_REQUEST, AML_TYPE_TEXT, APPS_NAME, DOCUMENT_VERIFY, STATUS_AML, TYPE_NAVIGATION_POS_REQUEST } from '@/utils/constants';
import { getGender } from '@/utils/utils';
import ROUTE_PATH from '@/navigators/routePath';

import ModalConfirmRequest, { ModalConfirmRequestRef } from './components/ModalConfirmRequest';
import useStyles from './styles';
import { DOCUMENT_TYPE } from '../VerifyAccount/types';

const GENDER = {
  MALE: 'male',
  FEMALE: 'female',
};

const { colors } = commonTheme;

const Profile = () => {
  const { t } = useTranslation();
  const isRunningMiniApp = useRef(false);
  const navigation = useNavigation<any>();
  const netInfo = NetInfo.useNetInfo();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const dispatch = useDispatch();
  const profile = useSelector(makeSelectProfile());
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const modalConfirmRequestRef = useRef<null | ModalConfirmRequestRef>(null);
  const modalAMLPermissionRef = useRef<null | ModalMessageRef>();
  const [checkAMLStatus, setCheckAMLStatus] = useState<string>('');
  const [isLoadingProfile, setIsLoadingProfile] = useState<boolean>(false);

  const openMiniApp = async (appName: string, additionProps: string) => {
    try {
      await NativeModuleClient.MiniApp.runCheckUpdate(appName);
      await NativeModuleClient.MiniApp.loadApp(appName, additionProps);
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', appName);
    }
  };

  const checkPermissionAML = async (type: string, callback: () => void) => {
    try {
      setIsLoadingProfile(true);
      const status: string = await asyncCheckAMLAction(dispatch)({ type });
      setIsLoadingProfile(false);
      setCheckAMLStatus(status);
      // return isSuccess
      if (status !== AML_TYPE_TEXT.PASSED) {
        setTimeout(() => {
          modalAMLPermissionRef.current?.setVisibleModal(true);
        }, 300);
      } else {
        callback?.();
      }
    } catch (error) {
      console.log('errror', error);
    }
  };

  const onRefreshData = async () => {
    const handleRefreshData = () => new Promise<void>((resolve, reject) => dispatch(pullToRefreshProfile(resolve, reject)));
    try {
      setRefreshing(true);
      await handleRefreshData();
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const checkPersonalButtonActive = () => {
    // Role = PROSPECT_CUSTOMER, NON_CUSTOMER && not has contactId => active
    if (_intersection(profile?.roles, [roles?.PROSPECT_CUSTOMER, roles?.NON_CUSTOMER]).length > 0 && isEmpty(profile?.contactId)) {
      return false;
    }
    //has policy
    if (_intersection(profile?.roles, [roles?.POLICY_OWNER, roles?.V_POLICY_OWNER]).length > 0) {
      return false;
    }

    // SA-5613 enhance Vitality Customer non IP
    // if (profile?.roles.includes(roles.V_CUSTOMER_NON_IP)) {
    //   return false;
    // }

    return true;
  };

  const checkPersonalButtonAction = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.MY_PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Update Personal Info',
    });
    // Role = PROSPECT_CUSTOMER, NON_CUSTOMER && not has contactId => active
    if (_intersection(profile?.roles, [roles?.PROSPECT_CUSTOMER, roles?.NON_CUSTOMER]).length > 0 && isEmpty(profile?.contactId)) {
      return navigation.navigate(ROUTE_PATH.CHOOSE_TYPE_DOCUMENT);
    }
    //has policy
    if (_intersection(profile?.roles, [roles?.POLICY_OWNER, roles?.V_POLICY_OWNER]).length > 0) {
      // block with updating
      return checkPermissionAML(AML_TYPE_REQUEST.POS_P820, () =>
        openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P820 })),
      );
    }

    // SA-5613 enhance Vitality Customer non IP
    // if (profile?.roles.includes(roles.V_CUSTOMER_NON_IP)) {
    //   return navigation.navigate(ROUTE_PATH.VERIFY_ACCOUNT, { documentType: DOCUMENT_TYPE.NATIONAL_ID_CARD });
    // }
  };

  const checkContactButtonActive = () => {
    //Role is PROSPECT_CUSTOMER, NON_CUSTOMER => active
    if (_intersection(profile?.roles, [roles?.PROSPECT_CUSTOMER, roles?.NON_CUSTOMER]).length > 0 && isEmpty(profile?.contactId)) {
      return false;
    }
    //has policy
    if (_intersection(profile?.roles, [roles?.POLICY_OWNER, roles?.V_POLICY_OWNER]).length > 0) {
      return false;
    }

    // SA-5613 enhance Vitality Customer non IP
    // if (profile?.roles.includes(roles.V_CUSTOMER_NON_IP)) {
    //   return false;
    // }

    return true;
  };

  const checkContactButtonAction = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.MY_PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Update Contact Info',
    });
    if (_intersection(profile?.roles, [roles?.PROSPECT_CUSTOMER, roles?.NON_CUSTOMER]).length > 0 && isEmpty(profile?.contactId)) {
      return modalConfirmRequestRef?.current?.open();
    }
    //has policy
    if (_intersection(profile?.roles, [roles?.POLICY_OWNER, roles?.V_POLICY_OWNER]).length > 0) { // V_CUSTOMER_NON_IP
      return checkPermissionAML(AML_TYPE_REQUEST.POS_P840, () =>
        openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P840 })),
      );
      // return openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P840 }));
    }

    // if (profile?.roles.includes(roles.V_CUSTOMER_NON_IP)) {
    //   return openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_CONTACT }));
    // }
  };

  const navigateDeleteAccount = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.MY_PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Delete Account',
    });
    navigation.navigate(ROUTE_PATH.DELETE_ACCOUNT);
  }

  const checkInfoRequiredUpdate = () => {
    if (profile?.identificationDocumentDetail?.code === DOCUMENT_VERIFY.CMND && _size(profile?.idCardNo) === 9) {
      return !_every(
        [profile?.fullName, profile?.gender, profile?.dob, profile?.idCardNo, profile?.idCard?.issueDate, profile?.idCard?.issuePlace],
        undefined,
      );
    }
    return !_every(
      [
        profile?.fullName,
        profile?.gender,
        profile?.dob,
        profile?.idCardNo,
        profile?.idCard?.issueDate,
        profile?.idCard?.expiryDate,
        profile?.idCard?.issuePlace,
      ],
      undefined,
    );
  };

  const handleBackMiniApp = useCallback(() => {
    if (isRunningMiniApp.current) {
      isRunningMiniApp.current = false;
      NativeModuleClient.MiniApp.goBack();
      return true;
    }
    return false;
  }, []);

  const onOpenP820 = async () => {
    return openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P820 }));
  };

  const callCenter = () => {
    Alert.alert(t('common.modalAML.callHotline.title'), '', [
      {
        text: t('common.modalAML.close'),
      },
      {
        text: t('common.modalAML.callHotline.call'),
        onPress: () => {
          Linking.openURL(`tel:${t('common.modalAML.Hotline').replace(/\s/g, '')}`);
        },
      },
    ]);
  };

  useEffect(() => {
    if (!__DEV__) BackHandler.addEventListener('hardwareBackPress', handleBackMiniApp);

    return () => {
      if (!__DEV__) BackHandler.removeEventListener('hardwareBackPress', handleBackMiniApp);
    };
  }, []);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('myprofile.title.header')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('MyProfile.btnHeaderGoback')}
          style={styles.marginLeft16}
          hitSlop={HITSLOP}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
        >
          <Icon name="Arrow-Left" size={24} color="#ffffff" />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.wrapper}>
      <StatusBar hidden={false} barStyle="light-content" />
      <ScrollView
        style={styles.container}
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}
        refreshControl={<RefreshControl refreshing={isRefreshing} tintColor={theme.colors.primary} onRefresh={onRefreshData} />}
      >
        <View style={styles.container}>
          <View style={styles.accoutInfo}>
            <View>
              <View style={styles.titleWrapper}>
                <Text style={styles.accoutInfoTitle} xl={{ fontWeight: '600' }} fontFamily="bold">
                  {t('myprofile.title.info')}{' '}
                  <Text style={styles.iconStar} xl={{ fontWeight: '600' }} fontFamily="bold">
                    *
                  </Text>
                </Text>
                <TouchableOpacity
                  {...a11y('MyProfile.btnEditPersonal')}
                  style={[checkPersonalButtonActive() && styles.opacity_50]}
                  onPress={checkPersonalButtonAction}
                  disabled={checkPersonalButtonActive()}
                >
                  <Text style={styles.buttonText}>{t('myprofile.button.edit')}</Text>
                </TouchableOpacity>
              </View>
              <View style={styles.contentWrappper}>
                <View style={[styles.rowContent]}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.name')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.fullName || '-'}
                  </Text>
                </View>
                <View style={styles.rowContent}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.gender')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.gender
                      ? (getGender(profile?.gender) === GENDER.FEMALE && t('myprofile.gender.female')) ||
                        (getGender(profile?.gender) === GENDER.MALE && t('myprofile.gender.male'))
                      : '-'}
                  </Text>
                </View>
                <View style={styles.rowContent}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.birthday')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.dob ? dayjs(profile?.dob).format('DD/MM/YYYY') : '-'}
                  </Text>
                </View>
                <View style={styles.rowContent}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.id')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.idCardNo ? maskedNumber(profile?.idCardNo, 4) : '-'}
                  </Text>
                </View>
                <View style={styles.rowContent}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.issuedDate')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.idCard?.issueDate ? dayjs(profile?.idCard?.issueDate).format('DD/MM/YYYY') : '-'}
                  </Text>
                </View>
                <View style={styles.rowContent}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.expiredDate')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.identificationDocumentDetail?.code === DOCUMENT_VERIFY.CMND &&
                    _size(profile?.idCardNo) === 9 &&
                    profile?.idCard?.issueDate
                      ? dayjs(profile?.idCard?.issueDate).add(15, 'y').format('DD/MM/YYYY')
                      : (profile?.idCard?.expiryDate && dayjs(profile?.idCard?.expiryDate).format('DD/MM/YYYY')) || '-'}
                  </Text>
                </View>
                <View style={styles.rowContent}>
                  <Text style={[styles.property, styles.flex_1]}>{t('myprofile.title.issuedPlace')}</Text>
                  <Text style={[styles.value, styles.flex_1]} fontFamily="bold">
                    {profile?.idCard?.issuePlace || '-'}
                  </Text>
                </View>
              </View>
              {checkInfoRequiredUpdate() && (
                <View style={styles.errorWrapper}>
                  <Icon name="Alert" size={14} color={theme.colors.secondary.cerise[100]} style={styles.iconError} />
                  <Text style={styles.errorText}>{t('myprofile.errorInfo')}</Text>
                </View>
              )}
            </View>
          </View>

          <View style={styles.contact}>
            <View style={styles.titleWrapper}>
              <Text style={styles.contactTitle} fontFamily="bold">
                {t('myprofile.title.contact')}
              </Text>
              <TouchableOpacity
                {...a11y('MyProfile.btnEditContact')}
                style={[checkContactButtonActive() && styles.opacity_50]}
                onPress={checkContactButtonAction}
                disabled={checkContactButtonActive()}
              >
                <Text style={styles.buttonText}>{t('myprofile.button.edit')}</Text>
              </TouchableOpacity>
            </View>
            <View style={styles.contentWrappper}>
              <View style={styles.rowContent}>
                <View style={[styles.flex_1]}>
                  <Text style={styles.methodContact}>{t('myprofile.title.phone')}</Text>
                  <Text style={styles.methodContactValue} fontFamily="bold">
                    {profile?.mobile ? maskedPhoneNumber(profile?.mobile) : '-'}
                  </Text>
                </View>
              </View>
              <View style={styles.rowContent}>
                <View style={[styles.flex_1]}>
                  <Text style={styles.methodContact}>{t('myprofile.title.email')}</Text>
                  <Text style={styles.methodContactValue} fontFamily="bold">
                    {profile?.email || '-'}
                  </Text>
                </View>
              </View>
              <View style={styles.rowContent}>
                <View style={[styles.flex_1]}>
                  <Text style={styles.methodContact}>{t('myprofile.title.address')}</Text>
                  <Text style={styles.methodContactValue} fontFamily="bold">
                    {profile?.address || '-'}
                  </Text>
                </View>
              </View>
            </View>
            <View style={[styles.contentWrappper, styles.deleteAccount]}>
              <TouchableOpacity
                style={[styles.row, styles.wrapperRow]}
                {...a11y('Profile.deleteAccount')}
                onPress={navigateDeleteAccount}
              >
                <View style={styles.row}>
                  <Icon name="Delete1" color={colors.primary.charcoal[80]} style={styles.icon} size={20} />
                  <Text style={styles.textTitle}>{t('profile.account.delete')}</Text>
                </View>
                <View style={styles.row}>
                  <Icon name="Arrow-Right" color={colors.primary.charcoal[100]} size={20} />
                </View>
              </TouchableOpacity>
            </View>
          </View>
        </View>
        <View style={{ height: 40 }} />
      </ScrollView>
      <ModalConfirmRequest
        ref={modalConfirmRequestRef}
        onConfirm={() => {
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.MY_PROFILE,
            unixTimestamp: dayjs().unix(),
            buttonText: 'Confirm Attention Update Contact',
          });
          openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_CONTACT }));
        }}
        onCancel={() => {
          NativeModuleClient.AdobeExperience.trackButton({
            screen: ROUTE_PATH.MY_PROFILE,
            unixTimestamp: dayjs().unix(),
            buttonText: 'Cancel Attention Update Contact',
          });
        }}
      />
      <ModalMessage
        typeMessage="error"
        ref={modalAMLPermissionRef}
        onConfirm={checkAMLStatus === AML_TYPE_TEXT.HAVE_TO_UPDATE ? onOpenP820 : callCenter}
        title={t(`common.modalAML.title`)}
        subtitle={t(`common.modalAML.status.${checkAMLStatus}`)}
        btnConfirmText={checkAMLStatus === AML_TYPE_TEXT.HAVE_TO_UPDATE ? t('common.modalAML.update') : t('common.modalAML.callHotline')}
        btnCloseText={t('common.modalAML.close')}
      />
      <Loading visible={isLoadingProfile} />
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default Profile;
