import React, { forwardRef, useImperativeHandle, useEffect } from 'react';
import { View, Image, Keyboard } from 'react-native';

import styles from './styles';
import Modal from '../Modal';
import { useModal } from '../Modal/Modal';

export interface ILoadingProps {
  visible?: boolean;
  onDismiss?: () => void;
}

export interface ILoadingRef {
  open: () => void;
  close: () => void;
}

const Loading = ({ visible, onDismiss }: ILoadingProps, ref: React.Ref<ILoadingRef> | undefined) => {
  const [modalsRef, modalInstance] = useModal();

  useImperativeHandle(
    ref,
    (): ILoadingRef => ({
      open: () => {
        Keyboard.dismiss();
        modalInstance()?.show();
      },
      close: () => {
        modalInstance()?.close();
      },
    }),
    [],
  );

  useEffect(() => {
    if (visible) {
      Keyboard.dismiss();
      modalInstance()?.show();
    } else {
      modalInstance()?.close();
    }
  }, [visible]);

  return (
    <Modal ref={modalsRef} onDismiss={onDismiss} overlayOpacity={0.5} modalStyle={styles.modalStyle} onHardwareBackPress={() => true}>
      <View style={styles.modalContainer}>
        <Image style={styles.imageGif} source={require('@/assets/images/loading.gif')} />
      </View>
    </Modal>
  );
};

export default forwardRef(Loading);
