import { createSelector } from 'reselect';

import initialState from './state';
import { NS_FORGOT_PASSWORD_TYPE } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_FORGOT_PASSWORD_TYPE] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectInfoCapcha = () => createSelector(currentState, state => state.infoCapcha);
const makeSelectIsVerifyOtpError = () => createSelector(currentState, state => state.verifyOtpStatus);
const makeSelectIsCaptchaError = () => createSelector(currentState, state => state.isCaptchaError);
const makeSelectUserId = () => createSelector(currentState, state => state.userId);
const makeSelectSessionId = () => createSelector(currentState, state => state.sessionId);
const makeSelectPhoneNumber = () => createSelector(currentState, state => state.phoneNumber);
const makeSelectUsername = () => createSelector(currentState, state => state.username);
const makeSelectUserInfoSubmit = () => createSelector(currentState, state => state.userInfoSubmit);
const makeSelectOtpValue = () => createSelector(currentState, state => state.otpValue);
const makeSelectIsCountFromScreenInfo = () => createSelector(currentState, state => state.isCountFromScreenInfo);
const makeSelectLoadingForgotPassword = () => createSelector(currentState, state => state.loadingForgotPassword);

export {
  currentState,
  makeSelectLoading,
  makeSelectInfoCapcha,
  makeSelectIsVerifyOtpError,
  makeSelectIsCaptchaError,
  makeSelectUserId,
  makeSelectSessionId,
  makeSelectPhoneNumber,
  makeSelectUsername,
  makeSelectUserInfoSubmit,
  makeSelectOtpValue,
  makeSelectIsCountFromScreenInfo,
  makeSelectLoadingForgotPassword
};
