import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  SUBMIT_ECONSENT,
  OTP_REQUEST_ECONSENT,
  OTP_REQUEST_ERROR,
  SUBMIT_ECONSENT_ERROR,
} from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });

export const requestOtpEConsentAction = (payload: {
  params: { otpType: string; otpSendType: string };
  callback?: (otp: MODEL.IRequestMyOtp) => void;
}): TAction => ({ type: OTP_REQUEST_ECONSENT, payload });

export const submitEConsentAction = (
  payload: { otpValue: string; otpId: string },
  resolve: () => void,
  reject: (value: unknown) => void,
): TAction => ({
  type: SUBMIT_ECONSENT,
  payload,
  resolve,
  reject,
});

export const submitEConsentErrorAction = (payload: { errorType: string }): TAction => ({
  type: SUBMIT_ECONSENT_ERROR,
  payload,
});

export const optRequesetErrorAction = (payload: { status: boolean; errorType: string }): TAction => ({
  type: OTP_REQUEST_ERROR,
  payload,
});
