import React, { useState, useImperativeHandle, useRef } from 'react';
import { View, StatusBar, TouchableOpacity, SafeAreaView } from 'react-native';
import { Icon, InnerLoading } from 'superapp-common-component';
import { WebView } from 'react-native-webview';
import Text from '@/components/Text';
import { useTranslation } from 'react-i18next';
import { colors } from 'superapp-common-component';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';
import { a11y, isIOS } from '@/utils/utils';
import { HITSLOP_48 } from '@/utils/styles';

import styles from './styles';

export type TWebViewUrlRef = {
  openURL: (value: string) => void;
};

const WebViewUrl = React.forwardRef((props: any, ref: any) => {
  const { t } = useTranslation();
  const [uri, setURI] = useState<any>('');
  const [modalsRef, modalInstance] = useModal();
  const webViewRef = useRef<WebView>();

  const injectedJavaScript = `
    const notification = document.querySelector("#components-action-notification");
    const header = document.querySelector("header");
    const footer = document.querySelector("footer");
    const backgroundImage = document.getElementsByClassName("cmp-homepagehero--with-bg ")[0];

    if (notification) {
      notification.remove();
    }

    if (header) {
      header.remove();
    }

    if (backgroundImage) {
      backgroundImage.style = "margin-top: 0";
    }

    if (footer) {
      footer.remove();
    }
  `;

  const onBackdropPress = () => {
    modalInstance().close();
    setURI('');
  };

  const onOpenURL = (url: string) => {
    setURI(url);
    modalInstance().show();
  };

  useImperativeHandle(
    ref,
    () => ({
      openURL: (value: string) => onOpenURL(value),
    }),
    [],
  );

  return (
    <Modal ref={modalsRef} overlayOpacity={0.5} onDismiss={() => null}>
      <StatusBar barStyle="light-content" />
      <SafeAreaView style={{ backgroundColor: colors.primary.red[100] }} />
      <View style={styles.container}>
        <View style={styles.header}>
          <TouchableOpacity
            style={{ paddingLeft: 4 }}
            onPress={onBackdropPress}
            {...a11y('WebView.btnCloseWebViewModal')}
            hitSlop={HITSLOP_48}
          >
            <Icon name="Close" size={24} color={colors.monoTone[0]} style={styles.iconClose} />
          </TouchableOpacity>
          <View>
            <Text numberOfLines={1} style={styles.titleHeader}>
              {t('common.title.webView')}
            </Text>
          </View>
          <View style={styles.fakeView} />
        </View>
        <View style={styles.content}>
          {uri !== '' && (
            <WebView
              ref={webViewRef as any}
              textZoom={100}
              source={{ uri }}
              onMessage={() => {}}
              startInLoadingState={true}
              injectedJavaScript={isIOS ? injectedJavaScript : ''}
              renderLoading={() => (
                <View style={[styles.content, styles.bgrWhite]}>
                  <InnerLoading />
                </View>
              )}
            />
          )}
        </View>
      </View>
      <SafeAreaView style={{ backgroundColor: 'white' }} />
    </Modal>
  );
});

export default WebViewUrl;
