import React, { useEffect, useRef, useState } from 'react';
import { AnimatedCircularProgress } from 'react-native-circular-progress';
import { Text } from 'superapp-common-component';
import { colors } from 'superapp-common-component';

import styles from './styles';

const CircularProgress = ({ onDone }: { onDone: () => void }) => {
  const [percent, setPercent] = useState(100);
  const timerRef = useRef<NodeJS.Timer>();

  const onCountDown = () => {
    timerRef.current = setTimeout(() => {
      setPercent(prev => prev - 20);
    }, 1000);
    if (percent === 0) {
      onDone && onDone();
      clearTimeout(timerRef.current);
    }
  };

  useEffect(() => {
    onCountDown();

    return () => clearTimeout(timerRef.current);
  }, [percent]);

  return (
    <AnimatedCircularProgress
      size={100}
      width={5}
      style={styles.circle}
      fill={percent}
      rotation={0}
      tintColor={colors.primary.red[100]}
      backgroundColor={colors.primary.charcoal[10]}
    >
      {() => <Text style={styles.display}>{percent / 20}</Text>}
    </AnimatedCircularProgress>
  );
};

export default CircularProgress;
