import React, { useState, forwardRef, useImperativeHandle, useEffect, useRef } from 'react';
import { View, TouchableOpacity, SafeAreaView, StatusBar, Image } from 'react-native';
import { Icon, SendOTP, Button, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import _isEmpty from 'lodash/isEmpty';
import dayjs from 'dayjs';

import { HITSLOP } from '@/utils/styles';
import { REGEX, ERROR_CODE_OTP } from '@/utils/constants';
import { a11y } from '@/utils/utils';

import Loading from '@/components/Loading';
import Text from '@/components/Text';
import Modal from '@/components/Modal/Modal';
import { useModal } from '@/components/Modal/Modal';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import TurnstileCapcha, { TurnstileCapchaRef } from '@/components/TurnstileCapcha';

import AccountReady, { AccountReadyRef } from '../AccountReady';

import styles from './styles';
export interface OTPScreenRef {
  open: () => void;
  close: () => void;
  setCaptchaBefore: (isBefore: boolean) => void;
  setExistUserName: (isExist: boolean) => void;
}

const OTPScreen = (props: any, ref: React.Ref<unknown> | undefined) => {
  const { t } = useTranslation();
  const [modalRef, modalInstance] = useModal();

  const { setError, clearErrors, requestOtpBySms, loginModalRef } = props;
  const [otpValue, setOtpValue] = useState<string>('');
  const [phoneReplace, setPhoneReplace] = useState<string>('');
  const [requestOtpType, setRequestOtpType] = useState<string>('SMS');
  const [seconds, setSeconds] = useState<number>(0);
  const [isRegisterSuccess, setIsRegisterSuccess] = useState<boolean>(false);
  const [isCapchaBefore, setIsCapchaBefore] = useState(false);
  const [isExistUser, setIsExistUser] = useState(false);
  const [otpId, setOtpId] = useState('');

  const accountReadyRef = useRef<null | AccountReadyRef>(null);
  const modalErrorGlobalRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalCaptchaErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalInvalidCapchaRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalCaptchaDisable = useRef<null | ModalConfirmErrorRef>(null);
  const turnstileCapchaRef = useRef<null | TurnstileCapchaRef>(null);

  const onRequestOtp = (typeOtp: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'RegisterOTP',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Get OTP',
    });
    setRequestOtpType(typeOtp);
    setTimeout(() => {
      turnstileCapchaRef?.current?.open();
    }, 300);
  };

  const onRequestOtpForSuccessCaptcha = (token: string) => {
    turnstileCapchaRef?.current?.close();
    const params = {
      phoneNo: props?.infoRegister?.phoneNumber,
      captchaValue: token,
      otpType: 'Register',
      otpSendType: requestOtpType,
    };
    const callback = (value: string, errCode: string) => {
      if (value) {
        setOtpId(value);
      } else {
        setTimeout(() => {
          if (errCode === ERROR_CODE_OTP.INVALID_CAPTCHA) {
            setError('globalCaptchaError', {
              type: 'manual',
              message: t('register.account.form.validate.invalidCapcha'),
            });
          } else {
            if (errCode === ERROR_CODE_OTP.OTP_LOCKED) {
              setError('globalCaptchaError', {
                type: 'manual',
                message: t('register.captcha.error.blockOtp'),
              });
            } else if (errCode === 'Network Error') {
              setError('globalCaptchaError', {
                type: 'manual',
                message: t('common.connection.description'),
              });
            } else {
              setError('globalCaptchaError', {
                type: 'manual',
                message: t('register.captcha.error.fail'),
              });
            }
          }
        }, 300);
      }
    };
    requestOtpBySms({ params, setError, callback, setSeconds });
  };

  const onDismissLoading = () => {
    if (!_isEmpty(props?.errors?.globalError)) {
      modalErrorGlobalRef.current?.open();
    } else if (!_isEmpty(props?.errors?.globalCaptchaError)) {
      modalCaptchaErrorRef?.current?.open();
    } else if (!_isEmpty(props?.errors?.otpDisabled)) {
      modalCaptchaDisable?.current?.open();
    } else if (isRegisterSuccess) {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'Register Success',
        unixTimestamp: dayjs().unix(),
      });
      accountReadyRef.current?.open();
    }
  };

  const onHideModalErrorGlobal = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'RegisterOTP',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close error',
    });
    clearErrors();
  };

  const onHideModalInvalidCapcha = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'RegisterOTP',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close error captcha',
    });
    clearErrors();
  };

  const onCloseModalOTP = () => {
    clearErrors();
    setIsExistUser(false);
    setIsCapchaBefore(false);
    setSeconds(0);
    modalInstance()?.close();
  };

  const onExistLoginNow = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'RegisterExistAccount',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login now',
    });
    modalInstance()?.close();
    setIsExistUser(false);
    isCapchaBefore && setIsCapchaBefore(false);
    loginModalRef?.current?.setShowModal(true);
  }

  const onExistRegisterNewAccount = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'RegisterExistAccount',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Register new account',
    });
    setIsExistUser(false);
    setIsCapchaBefore(true);
    setTimeout(() => {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'RegisterOTP',
        unixTimestamp: dayjs().unix(),
      });
    }, 500);
  }

  const onSubmitRegister = () => {
    const callback = () => {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: 'RegisterOTP',
        unixTimestamp: dayjs().unix(),
        buttonText: 'Submit register',
      });
      setIsRegisterSuccess(true);
    };
    props.submitRegister({ ...props.infoRegister, otpValue, otpId, setError, callback });
  };

  useImperativeHandle(
    ref,
    (): OTPScreenRef => ({
      open: () => {
        modalInstance()?.show();
      },
      close: () => {
        modalInstance()?.close();
      },
      setCaptchaBefore: (isBefore: boolean) => {
        setIsCapchaBefore(isBefore);
      },
      setExistUserName: (isExist: boolean) => {
        setIsExistUser(isExist);
      },
    }),
    [],
  );

  useEffect(() => {
    if (isCapchaBefore) {
      setTimeout(() => {
        turnstileCapchaRef?.current?.open();
      }, 300);
    }
  }, [isCapchaBefore]);

  useEffect(() => {
    if (props.infoRegister?.phoneNumber) {
      const newPhone = props.infoRegister?.phoneNumber.replace(REGEX.REGEX_REPLACE_PHONE, 'XXXXXX$1') || props.infoRegister?.phoneNumber;
      setPhoneReplace(newPhone);
    }
  }, [props.infoRegister]);

  return (
    <>
      <Modal ref={modalRef} animationType="slideBottom">
        <StatusBar barStyle="dark-content" />
        <View style={styles.modalContainer}>
          <SafeAreaView />
          <View style={styles.containerTitle}>
            <View style={styles.iconBack}>
              <TouchableOpacity onPress={onCloseModalOTP} hitSlop={HITSLOP} {...a11y('Register.OTP.btnCloseOTPModal')}>
                <Icon name="Arrow-Left" color="#5B636B" />
              </TouchableOpacity>
            </View>
            <Text style={styles.textTitle} fontFamily="bold">
              {isExistUser ? '' : t('forgot.password.modal.otp.header')}
            </Text>
          </View>
          {isExistUser ? (
            <View style={styles.flexGrow}>
              <View style={styles.iconWarningContainer}>
                <Image style={styles.iconWarning} resizeMode="contain" source={require('@/assets/images/img-warning.png')} />
              </View>
              <Text style={[styles.textWarning, styles.mx_16]}>{t('register.account.warning.existUsername', { phone: phoneReplace })}</Text>
              <View style={styles.buttonWarningContainer}>
                <Button
                  text={t('register.account.warning.button.textPrimary')}
                  style={styles.mb_16}
                  onPress={onExistLoginNow}
                  id="Register.WarningExistUsername.btnLoginNow"
                />
                <Button
                  type="secondary"
                  text={t('register.account.warning.button.textSecondary')}
                  style={styles.mb_48}
                  onPress={onExistRegisterNewAccount}
                  id="Register.WarningExistUsername.btnCreateNew"
                />
              </View>
            </View>
          ) : (
            <View style={styles.contentContainer}>
              <SendOTP
                textDescription={t('common.otp.textPhone', { phone: phoneReplace })}
                textTimeCount={t('common.otp.text.countTimeTitle')}
                submitText={t('register.account.verify')}
                onChange={code => setOtpValue(code)}
                sendBySMS={() => onRequestOtp('SMS')}
                sendByVoice={() => onRequestOtp('Voice')}
                onSubmit={onSubmitRegister}
                loading={props.loading}
                disabled={props.loading}
                secondNumber={seconds}
                setSecondNumber={setSeconds}
                textSMS={t('common.otp.text.resend')}
                textVoice={t('common.otp.text.viaCall')}
                textCountSecond={t('common.text.countSecond')}
                textNotSendSMS={t('common.otp.text.noSms')}
                id="Register.OTP.sendOTP"
              />
            </View>
          )}
        </View>
      </Modal>
      <AccountReady ref={accountReadyRef} clearErrors={clearErrors} setIsRegisterSuccess={setIsRegisterSuccess} />
      <TurnstileCapcha ref={turnstileCapchaRef} onSuccess={onRequestOtpForSuccessCaptcha} onError={() => null} />
      <Loading visible={props.loading} onDismiss={onDismissLoading} />
      <ModalError
        ref={modalErrorGlobalRef}
        title={props?.errors?.globalError?.message === 'Network Error' ? t('common.no.connection') : t('register.modalError.title')}
        subtitle={
          props?.errors?.globalError?.message === 'Network Error' ? t('common.connection.description') : t('register.error.verifyFail')
        }
        btnText={t('register.modalError.button')}
        onConfirm={onHideModalErrorGlobal}
      />
      <ModalError
        ref={modalCaptchaErrorRef}
        title={props?.errors?.globalCaptchaError?.message === 'Network Error' ? t('common.no.connection') : t('register.modalError.title')}
        subtitle={props?.errors?.globalCaptchaError?.message}
        btnText={t('register.modalError.button')}
        onConfirm={onHideModalErrorGlobal}
      />
      <ModalError
        ref={modalInvalidCapchaRef}
        title={t('register.modalError.title')}
        subtitle={props.errors?.invalidCapcha?.message}
        btnText={t('register.modalError.button')}
        onConfirm={onHideModalInvalidCapcha}
      />
      <ModalError
        ref={modalCaptchaDisable}
        title={t('register.modalError.title')}
        subtitle={t('common.message.otpDisable')}
        btnText={t('register.modalError.button')}
        onConfirm={onCloseModalOTP}
      />
    </>
  );
};

export default forwardRef(OTPScreen);
