import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  CLEAR_DATA,
  FETCH_HEALTH_REPORT_FEEDBACK,
  FETCH_HEALTH_REPORT_FEEDBACK_SUCCESS,
  SET_HEALTH_REPORT_FEEDBACK,
  SET_LINKED_TRACKERS,
  SET_LOADING_LINKED_TRACKERS,
} from './constants';
import initialState, { TVitalityState } from './state';

const vitalityReducer = (state: TVitalityState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TVitalityState>) => {
    switch (action.type) {
      case FETCH_HEALTH_REPORT_FEEDBACK:
        draft.loadingHealthReport = true;
        break;
      case SET_HEALTH_REPORT_FEEDBACK:
        draft.loadingHealthReport = false;
        draft.healthReportFeedback = action.healthReportFeedback;
        break;
      case FETCH_HEALTH_REPORT_FEEDBACK_SUCCESS:
        draft.loadingHealthReport = false;
        break;
      case SET_LOADING_LINKED_TRACKERS:
        draft.linkedTrackersLoading = action.loading;
        break;
      case SET_LINKED_TRACKERS:
        draft.linkedTrackers = action.linkedTrackers;
        break;
      case CLEAR_DATA:
        draft.healthReportFeedback = undefined;
        draft.linkedTrackersLoading = undefined;
        draft.linkedTrackers = [];
        break;
    }
  });
};

export default vitalityReducer;
