import React, { FC, useEffect, useRef, useState } from 'react';
import { Animated, Keyboard, ViewStyle, BackHandler } from 'react-native';

import { isIOS } from '@/utils/utils';

// Style, components
import styles from './styles';
import Backdrop from './Backdrop';

// Animation
import Animation from './animations/Animation';
import FadeAnimation from './animations/FadeAnimation';
import SlideAnimation from './animations/SlideAnimation';
import ScaleAnimation from './animations/ScaleAnimation';
import KeyboardAnimation from './animations/KeyboardAnimation';

const getAnimation = (animationType: string, animationDuration: number) => {
  if (animationType.startsWith('slide')) {
    const type = animationType.toLowerCase().replace('slide', '');
    return new SlideAnimation({ slideFrom: type as any });
  }

  if (animationType === 'scale') {
    return new ScaleAnimation({ animationDuration });
  }

  return new FadeAnimation({ animationDuration });
};

export interface IBaseModalProps {
  key?: string;
  visible?: boolean;
  modalStyle?: ViewStyle;
  containerStyles?: ViewStyle;
  children?: React.ReactNode;
  animationType?: 'slideTop' | 'slideRight' | 'slideBottom' | 'slideLeft' | 'fade' | 'scale';
  modalAnimation?: Animation;
  animationDuration?: number;
  overlayBackgroundColor?: string;
  overlayOpacity?: number;
  enableOnKeyboardChange?: boolean;
  onDismiss?: () => void;
  onTouchOutSide?: () => void;
  onModalExcuted?: () => void;
  onHardwareBackPress?: () => boolean;
}
const MODAL_INITIAL: string = 'initial';
const MODAL_OPENING: string = 'opening';
const MODAL_OPENED: string = 'opened';
const MODAL_CLOSING: string = 'closing';
const MODAL_CLOSED: string = 'closed';

const HARDWARE_BACK_PRESS_EVENT = 'hardwareBackPress';

const BaseModal: FC<IBaseModalProps> = ({
  visible,
  modalStyle,
  children,
  animationType = 'fade',
  animationDuration = 150,
  overlayBackgroundColor,
  overlayOpacity = 0.5,
  containerStyles,
  enableOnKeyboardChange = true,
  onDismiss,
  onTouchOutSide,
  onModalExcuted,
  onHardwareBackPress = () => true,
}) => {
  const modaAnimationRef = useRef(getAnimation(animationType, animationDuration)).current;
  const [modalState, setModalState] = useState<string>(MODAL_INITIAL);
  const hidden = [MODAL_INITIAL, MODAL_CLOSED].includes(modalState) && styles.hidden;
  const [heightKeyboard, setHeightKeyboard] = useState<number>(0);
  const slideToTopAnimationRef = useRef(new KeyboardAnimation()).current;

  useEffect(() => {
    if (modalState === MODAL_OPENING) {
      modaAnimationRef.in(() => setModalState(MODAL_OPENED));
    } else if (modalState === MODAL_CLOSING) {
      modaAnimationRef.out(() => setModalState(MODAL_CLOSED));
    } else if (modalState === MODAL_CLOSED) {
      onDismiss && onDismiss();
      onModalExcuted && onModalExcuted();
    } else if (modalState === MODAL_OPENED) {
      onModalExcuted && onModalExcuted();
    }
  }, [modalState]);

  useEffect(() => {
    if (visible) {
      setModalState(MODAL_OPENING);
    } else {
      setModalState(MODAL_CLOSING);
    }
  }, [visible]);

  useEffect(() => {
    if (enableOnKeyboardChange) {
      slideToTopAnimationRef.change(heightKeyboard / 2);
    }
  }, [heightKeyboard, enableOnKeyboardChange]);

  useEffect(() => {
    const onKeyboardChange = (event: KeyboardEvent) => {
      // @ts-ignore
      setHeightKeyboard(event.endCoordinates.screenY < event?.startCoordinates?.screenY ? event.endCoordinates.height : 0);
    };

    BackHandler.addEventListener(HARDWARE_BACK_PRESS_EVENT, onHardwareBackPress);

    if (isIOS) {
      const subscription = Keyboard.addListener('keyboardWillChangeFrame', onKeyboardChange);
      return () => subscription.remove();
    }

    const subscriptions = [
      Keyboard.addListener('keyboardDidHide', onKeyboardChange),
      Keyboard.addListener('keyboardDidShow', onKeyboardChange),
    ];

    return () => {
      subscriptions.forEach(subscription => subscription.remove());
      BackHandler.removeEventListener(HARDWARE_BACK_PRESS_EVENT, onHardwareBackPress);
    };
  }, []);

  return (
    <Animated.View style={[styles.container, hidden, containerStyles, slideToTopAnimationRef.getAnimations()]}>
      <Backdrop
        onPress={onTouchOutSide}
        opacity={overlayOpacity}
        animationDuration={50}
        backgroundColor={overlayBackgroundColor}
        pointerEvents={modalState === MODAL_OPENED ? 'auto' : 'none'}
        visible={visible}
      />
      <Animated.View style={[styles.modal, modalStyle, modaAnimationRef.getAnimations()]}>{children}</Animated.View>
    </Animated.View>
  );
};

export default BaseModal;
