import React, { useLayoutEffect, useState, useRef, useEffect } from 'react';
import { View, SafeAreaView, TouchableOpacity } from 'react-native';
import {
  CheckMarkNotiIcon,
  Icon,
  NativeModuleClient,
} from 'superapp-common-component';
import { useNavigation, useIsFocused } from '@react-navigation/native';
import { TStackParamList } from '@/navigators';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { useInjectReducer, useInjectSaga } from 'redux-injectors';
import dayjs from 'dayjs';
import NetInfo from '@react-native-community/netinfo';

import { JS_EVENT_NAMES, TYPES_NOTIFICATION } from '@/utils/constants';
import ROUTE_PATH from '@/navigators/routePath';
import { isEmpty } from '@/utils/utils';
import { HITSLOP } from '@/utils/styles';
import jsEventEmitter from '@/utils/jsEventEmitter';
import Text from '@/components/Text';
import type { INotification } from '@/clients/AzureNotification';

import { makeSelectCheckLogin } from '@/stores/authentication/selectors';

import styles from './styles';
import { readAllNotification } from './store/actions';
import { makeSelectNotification } from './store/selectors';
import { NS_NOTIFICATION } from './store/constants';
import notificationReducer from './store/reducer';
import notificationSaga from './store/saga';

import ModalMarkReadAll, {
  ModalMarkReadAllRef,
} from './components/ModalMarkReadAll';
import GeneralNoti from './components/GeneralNoti';
import BroadcastNotiComponent from './components/NotiBroadcast';

const Tab = createMaterialTopTabNavigator();
const TAB_NAME = {
  BROADCAST: 'Broadcast',
  GENERAL: 'General',
};

type NotificationsNavigationProp = StackNavigationProp<
  TStackParamList,
  'Notifications'
> & {
  jumpTo: (name: string, option?: {}) => void;
};

export const NOTIFICATION_LOCAL_STATE: {
  message?: INotification;
  resetJumpState: () => void;
} = {
  // feature: navigate to tab detail
  message: undefined,
  resetJumpState: () => {
    NOTIFICATION_LOCAL_STATE.message = undefined;
  },
};

const useHandleJumpTab = (props: {
  isFocused: boolean;
  navigation: NotificationsNavigationProp;
}) => {
  const { isFocused, navigation } = props;
  const netInfo = NetInfo.useNetInfo();

  const getJumpTab = () => {
    const type = NOTIFICATION_LOCAL_STATE.message?.data?.type;
    switch (type) {
      case TYPES_NOTIFICATION.GENERAL:
        return TAB_NAME.GENERAL;
      case TYPES_NOTIFICATION.BROADCAST:
        return TAB_NAME.BROADCAST;
    }
  };

  useEffect(() => {
    if (!isFocused) return;

    const handleJump = () => {
      const tab = getJumpTab();
      tab && navigation.jumpTo(getJumpTab()!);

      jsEventEmitter.emit(JS_EVENT_NAMES.ON_NAVIGATE_NOTIFICATION_DETAIL_TAB);
    };

    // used for first render
    handleJump();

    jsEventEmitter.addListener(
      JS_EVENT_NAMES.ON_NAVIGATE_NOTIFICATION_DETAIL,
      handleJump,
    );
    return () => {
      jsEventEmitter.removeListener(
        JS_EVENT_NAMES.ON_NAVIGATE_NOTIFICATION_DETAIL,
        handleJump,
      );
      NOTIFICATION_LOCAL_STATE.resetJumpState();
    };
  }, [isFocused]);

  useEffect(() => {
    if (isFocused && netInfo.isConnected === false) {
      NOTIFICATION_LOCAL_STATE.resetJumpState();
    }
  }, [isFocused, netInfo.isConnected]);

  return { getJumpTab };
};

const Notifications = () => {
  useInjectReducer({ key: NS_NOTIFICATION, reducer: notificationReducer });
  useInjectSaga({ key: NS_NOTIFICATION, saga: notificationSaga });
  const navigation = useNavigation<NotificationsNavigationProp>();
  const [tabAnimating, setTabAnimating] = useState(false);
  const [currentTab, setCurrentTab] = useState('General');
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const infoNotification = useSelector(makeSelectNotification());
  const modalMarkAllRef = useRef<ModalMarkReadAllRef>();
  const isFocused = useIsFocused();
  const loginStatus = useSelector(makeSelectCheckLogin());
  const { getJumpTab } = useHandleJumpTab({ isFocused, navigation });

  const openModalMarkAll = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NOTIFICATIONS,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Mark All',
    });
    modalMarkAllRef.current?.open();
  };

  const headerLeft = () => (
    <View style={styles.header}>
      <View style={styles.contentHeaderWrapper}>
        <Text style={styles.textHeader}>
          {t('home.notifications.title.header')}
        </Text>
        {loginStatus.isLoged && (
          <TouchableOpacity hitSlop={HITSLOP} onPress={openModalMarkAll}>
            <CheckMarkNotiIcon size={28} />
          </TouchableOpacity>
        )}
      </View>
    </View>
  );

  useLayoutEffect(() => {
    navigation.setOptions({
      header: () => headerLeft(),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    NativeModuleClient.AdobeExperience.trackScreen({
      screen: ROUTE_PATH.NOTIFICATIONS,
      unixTimestamp: dayjs().unix(),
    });
  }, []);

  const onMarkReadAll = () => {
    if (isEmpty(infoNotification?.data)) return;
    dispatch(readAllNotification());
  };

  return (
    <SafeAreaView style={styles.container}>
      <Tab.Navigator
        initialRouteName={getJumpTab() || TAB_NAME.GENERAL}
        screenOptions={{
          tabBarStyle: styles.tabBarStyle,
          tabBarContentContainerStyle: styles.tabBarContentContainerStyle,
          tabBarIndicatorStyle: styles.tabBarIndicatorStyle,
        }}
        screenListeners={({ navigation: navigationTab, route }) => ({
          tabPress: e => {
            // This adds a debounce to fix "switching tabs rapidly" bug
            if (tabAnimating) {
              e.preventDefault();
            } else {
              setTabAnimating(true);
              setTimeout(() => setTabAnimating(false), 300);

              // This solves the "tap same tab twice breaks tabs" bug, but not the "switching rapidly" bug
              e.preventDefault();
              navigationTab.navigate(route.name);
            }
            setCurrentTab(route.name);
          },
        })}>
        <Tab.Screen
          name={TAB_NAME.GENERAL}
          component={GeneralNoti}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon
                  name={focused ? 'Notification' : 'Notification1'}
                  size={24}
                  color={focused ? '#1F78AD' : '#5B636B'}
                />
                <Text style={styles.tabBarLabelText}>
                  {t('common.notification.tab.general')}
                </Text>
              </View>
            ),
          }}
        />
        <Tab.Screen
          name={TAB_NAME.BROADCAST}
          component={BroadcastNotiComponent}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon
                  name={focused ? 'Popular' : 'Popular1'}
                  size={24}
                  color={focused ? '#1F78AD' : '#5B636B'}
                />
                <Text style={styles.tabBarLabelText}>
                  {t('common.notification.tab.broadcast')}
                </Text>
              </View>
            ),
          }}
        />
      </Tab.Navigator>
      {currentTab === 'General' && (
        <ModalMarkReadAll ref={modalMarkAllRef} onConfirm={onMarkReadAll} />
      )}
    </SafeAreaView>
  );
};

export default Notifications;
