import { TAction } from '@/stores/types';

import {
  CLEAR_DATA,
  FETCH_HEALTH_REPORT_FEEDBACK,
  SET_HEALTH_REPORT_FEEDBACK,
  SET_LINKED_TRACKERS,
  INIT_LINKED_TRACKERS,
  SET_LOADING_LINKED_TRACKERS,
  FETCH_HEALTH_REPORT_FEEDBACK_SUCCESS,
} from './constants';

export const fetchHealthReportFeedback = (isRefresh?: boolean): TAction => ({
  type: FETCH_HEALTH_REPORT_FEEDBACK,
  isRefresh,
});

export const setHealthReportFeedback = (healthReportFeedback: any): TAction => ({
  type: SET_HEALTH_REPORT_FEEDBACK,
  healthReportFeedback,
});

export const fetchHealthReportFeedbackSuccess = (): TAction => ({
  type: FETCH_HEALTH_REPORT_FEEDBACK_SUCCESS,
});

export const initLinkedTrackers = (isRefresh?: boolean): TAction => ({
  type: INIT_LINKED_TRACKERS,
  isRefresh,
});

export const setLinkedTrackers = (linkedTrackers: string[]): TAction => ({
  type: SET_LINKED_TRACKERS,
  linkedTrackers,
});

export const setLoadingLinkedTracker = (loading: boolean): TAction => ({
  type: SET_LOADING_LINKED_TRACKERS,
  loading,
});

export const clearData = (): TAction => ({
  type: CLEAR_DATA,
});
