import React from 'react';
import Icons from '@/assets/icons';

export const SvgIcon: React.FC<{ name: string; extraStyle?: {}; width?: number; height?: number; fillColor?: string; group: string }> = ({
  name,
  extraStyle,
  width,
  height,
  fillColor,
  group,
}) => {
  const icon = Icons[group]?.(name, extraStyle, width, height, fillColor);

  return icon ?? null;
};

export default SvgIcon;
