import React, { forwardRef, useImperativeHandle } from 'react';
import { View, Image, TouchableWithoutFeedback } from 'react-native';
import { Button } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useDispatch } from 'react-redux';

import logo from '@/assets/images/image-modal-logout.png';
import { logout as logoutAction } from '@/stores/authentication/actions';
import Text from '@/components/Text';
import { a11y } from '@/utils/utils';
import Modal, { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface ModalLogOutProps {}

export interface ModalLogOutRef {
  open: () => void;
  close: () => void;
}

const ModalLogOut = (props: ModalLogOutProps, ref: React.Ref<unknown> | undefined) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const [modalRef, componentModal] = useModal();

  useImperativeHandle(
    ref,
    (): ModalLogOutRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal().close(),
    }),
  );

  const handleLogout = () => {
    componentModal().close();
    dispatch(logoutAction({ neededCallApi: true }));
  };

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer}>
      <TouchableWithoutFeedback {...a11y('Profile.btnLogoutWithoutFeedback')} onPress={() => {}}>
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View>
              <View style={styles.imageBox}>
                <Image style={styles.logo} resizeMode="contain" source={logo} />
              </View>
              <Text style={styles.modalMainText}>{t('profile.logout.modal.main.title')}</Text>
              <Text style={styles.modalSubText}>{t('profile.logout.modal.sub.title')}</Text>
            </View>
            <View style={styles.buttonBox}>
              <Button
                text={t('profile.logout.modal.button.logout')}
                type="primary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                onPress={handleLogout}
                id="Profile.btnLogoutModalLogout"
                style={styles.mb16}
              />
              <Button
                text={t('profile.logout.modal.button.cancel')}
                type="secondary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                onPress={() => {
                  componentModal().close();
                }}
                id="Profile.btnLogoutModalCancel"
              />
            </View>
          </View>
        </View>
      </TouchableWithoutFeedback>
    </Modal>
  );
};

export default forwardRef(ModalLogOut);
