import { createSelector } from 'reselect';

import initialState from './state';
import { NS_REGISTER_TYPE } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_REGISTER_TYPE] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectLoadingRequestOtp = () => createSelector(currentState, state => state.loadingRequestOtp);
const makeSelectInfoRegister = () => createSelector(currentState, state => state.infoRegister);
const makeSelectContentHelpCenterRegister = () => createSelector(currentState, state => state.contentHelpCenter);
const makeSelectInfoCapcha = () => createSelector(currentState, state => state.infoCapcha);

export { currentState, makeSelectLoading, makeSelectLoadingRequestOtp, makeSelectInfoRegister, makeSelectContentHelpCenterRegister, makeSelectInfoCapcha };
