import React, { useEffect, useRef, useState } from 'react';
import { Image, TouchableOpacity, View } from 'react-native';
import Text from '@/components/Text';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import _find from 'lodash/find';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import _values from 'lodash/values';
import { NativeModuleClient } from 'superapp-common-component';
import dayjs from 'dayjs';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationProp } from '@react-navigation/stack';

import { a11y, getVitalityMembershipIdFromToken, isEmpty, delay, hasMaintenanceVitality, canOpenVitality } from '@/utils/utils';
import {
  APPS_NAME,
  FUNCTION_TYPES,
  VALID_ROLES,
  MEMBERSHIP_VITALITY_TYPE,
  ERROR_CAN_OPEN_VITALITY,
  EVENTS_USER_TRACKING,
} from '@/utils/constants';
import { makeSelectFeatureList, makeSelectLoadingSkeleton, makeSelectMembership, makeSelectProfile } from '@/stores/global/selectors';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import APP_ENV from '@/config/app.config';
import { LoginModalRef } from '@/pages/Login';
import ROUTE_PATH from '@/navigators/routePath';
import { ModalPermissionRef } from '@/components/ModalPermission';
import WebView, { TWebViewUrlRef } from '@/components/WebView';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import { TStackParamList } from '@/navigators';
import { logAction, userTracking } from '@/stores/global/actions';
import ModalPermissionVitality, { ModalPermissionVitalityRef } from '@/components/ModalPermissionVitality';

import styles from './styles';
import { Language, TElementItem, TNewActionItem } from '../types';

interface FeatureProps {
  setLoadingOpenMiniApp: (value: boolean) => void;
  isRunningMiniApp: React.MutableRefObject<boolean>;
  isLoged: boolean;
  currentLang: Language;
  isConnected: boolean;
  permissionModalRef?: React.MutableRefObject<ModalPermissionRef | null | undefined>;
  loginModalRef: React.MutableRefObject<LoginModalRef | null | undefined>;
  checkPermissionAML: (type: string, callback: () => void) => void;
}

type FeatureNavigationProp = StackNavigationProp<TStackParamList, 'Home'>;

const Feature = (props: FeatureProps) => {
  const {
    isLoged,
    isRunningMiniApp,
    isConnected,
    currentLang,
    setLoadingOpenMiniApp,
    permissionModalRef,
    loginModalRef,
    checkPermissionAML,
  } = props;
  const dispatch = useDispatch();
  const webviewRef = useRef<TWebViewUrlRef | null>(null);
  const featureList = useSelector(makeSelectFeatureList());
  const isLoading = useSelector(makeSelectLoadingSkeleton());
  const profile = useSelector(makeSelectProfile());
  const loginStatus = useSelector(makeSelectCheckLogin());
  const [membership, setMembershipId] = useState('');
  const navigation = useNavigation<FeatureNavigationProp>();
  const membershipVitality = useSelector(makeSelectMembership());
  const modalMaintainRef = useRef<ModalConfirmErrorRef>();
  const modalPermissionVitalityRef = useRef<ModalPermissionVitalityRef>();

  const { t } = useTranslation();

  const openMiniApp = async (appName: string) => {
    if (!isLoged) {
      loginModalRef.current?.setShowModal(true);
      return;
    }

    try {
      setLoadingOpenMiniApp(true);

      if (appName === APPS_NAME.VITALITY) {
        const hasMaintenance = await hasMaintenanceVitality();

        if (hasMaintenance) {
          modalMaintainRef.current?.open();
          throw new Error('app maintenance');
        }
      }

      await NativeModuleClient.MiniApp.runCheckUpdate(appName);
      await delay(300);
      await NativeModuleClient.MiniApp.loadApp(appName, null);
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', appName);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const formatEachAction = (items: [TElementItem]) => {
    const title = _find(items, (element: TElementItem) => element?.name === 'name');
    const isHidden = _find(items, (element: TElementItem) => element?.name === 'hide');
    const order = _find(items, (element: TElementItem) => element?.name === 'order');
    const imageUrl = _find(items, (element: TElementItem) => element?.name === 'image');
    const id = _find(items, (element: TElementItem) => element?.name === 'id');
    return {
      title,
      isHidden,
      imageUrl,
      order,
      id,
    };
  };

  const formatActionsListData = () => {
    let formatedData: any = [];

    if (featureList) {
      featureList.items[currentLang].items.map((item: { title: string; elements: [{ name: string; value: any }] }) => {
        const { elements } = item;
        const { title, isHidden, order, id, imageUrl } = formatEachAction(elements);
        formatedData.push({ title, isHidden, order, id, imageUrl });
      });
    }
    return formatedData;
  };

  const handlePermission = (functionType: keyof typeof VALID_ROLES) => {
    if (permissionModalRef?.current?.handlePermission(functionType)) {
      throw new Error('permission invalid');
    }
  };

  const handleVitalityCheckAppAvailable = async () => {
    const membershipId = await getVitalityMembershipIdFromToken();
    if (!membershipId) {
      throw new Error('app unavailable');
    }
  };

  const handleVitalityOpen = () => {
    const error = canOpenVitality(membershipVitality, isLoged);

    if (error === ERROR_CAN_OPEN_VITALITY.NON_LOGIN) {
      loginModalRef.current?.setShowModal(true);
      throw new Error(error);
    }

    if (!isEmpty(error)) {
      modalPermissionVitalityRef.current?.open();
      dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.NON_VITALITY_MEMBERSHIP_ICON, timestamp: dayjs().unix() }));
      throw new Error(error);
    }
  };

  const handleNavigateMiniApp = async (id: string) => {
    try {
      if (_values(APPS_NAME).includes(id) && [APPS_NAME.CLAIM, APPS_NAME.POLICY, APPS_NAME.VITALITY].includes(id)) {
        switch (id) {
          case APPS_NAME.CLAIM:
            handlePermission(FUNCTION_TYPES.CLAIM);
            openMiniApp(id);
            break;
          case APPS_NAME.POLICY:
            handlePermission(FUNCTION_TYPES.POLICY);
            openMiniApp(id);
            break;
          case APPS_NAME.VITALITY:
            // handlePermission(FUNCTION_TYPES.VITALITY);
            handleVitalityOpen();
            dispatch(logAction('ACCESS_VITALITY'));
            await handleVitalityCheckAppAvailable();

            if (
              membershipVitality &&
              membership &&
              _values(MEMBERSHIP_VITALITY_TYPE).includes(membershipVitality.type) &&
              membershipVitality.hasEConsent !== true
            ) {
              navigation.navigate(ROUTE_PATH.ECONSENT as never);
              return;
            }

            if (membershipVitality && membership && membershipVitality.isDataProtect !== true) {
              navigation.navigate(ROUTE_PATH.DECREE_ECONSENT as never);
              return;
            }

            openMiniApp(id);
            break;
          default:
            openMiniApp(id);
            break;
        }
      } else if (id === APPS_NAME.MY_VIP) {
        webviewRef.current?.openURL(APP_ENV.MYVIP_URL);
      }
    } catch (error) {}
  };

  const openVitalityWeb = () => {
    dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.NON_AIA_VITALITY, timestamp: dayjs().unix() }));
    webviewRef.current?.openURL(t('common.vitality.url'));
  };

  const renderFeatureList = () => {
    const featureListFormat = formatActionsListData();
    if (featureListFormat) {
      return featureListFormat.map((item: TNewActionItem, index: number) => {
        const { title, isHidden, id, imageUrl } = item;

        return (
          !isHidden.value && (
            <TouchableOpacity
              key={id.value}
              style={styles.app}
              {...a11y(`Home.btnFeatureOpenMiniAppPayment.{${index}}`)}
              onPress={() => {
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.HOME,
                  unixTimestamp: dayjs().unix(),
                  buttonText: `Feature ${id?.value}`,
                });
                handleNavigateMiniApp(id.value);
              }}
            >
              <Image source={{ uri: `${APP_ENV.CONTENT_AEM}${imageUrl?.value}` }} style={styles.shopImage} />
              <Text style={styles.textFeatureMiniApp} xl={{ fontSize: 14 }}>
                {title.value}
              </Text>
            </TouchableOpacity>
          )
        );
      });
    }
  };

  useEffect(() => {
    if (!isEmpty(profile) && loginStatus.isLoged) {
      (async () => {
        const membershipId = await getVitalityMembershipIdFromToken();
        setMembershipId(membershipId);
      })();
    } else {
      setMembershipId('');
    }
  }, [profile, loginStatus]);

  const renderSkeletionProduct = () => {
    return (
      <SkeletonPlaceholder>
        <View style={styles.skeletonWrapper}>
          <View style={styles.NewsItemSkeleton}>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
            <View style={styles.contentSkeletonWrapper}>
              <View style={styles.headerSkeleton} />
              <View style={styles.contentSkeleton} />
            </View>
          </View>
        </View>
      </SkeletonPlaceholder>
    );
  };

  if (!isConnected && isEmpty(featureList)) return null;

  return isLoading && !featureList ? (
    renderSkeletionProduct()
  ) : (
    <View style={styles.container}>
      <View style={styles.mb16}>
        <Text style={styles.itemTitle} xl={{ fontSize: 16 }} fontFamily="bold">
          {t('home.features')}
        </Text>
      </View>
      <View style={styles.feature}>{renderFeatureList()}</View>
      <WebView ref={webviewRef} />
      <ModalConfirmError
        ref={modalMaintainRef}
        title={t('common.vitality.maintenance.title')}
        subtitle={t('common.vitality.maintenance.description')}
        btnText={t('common.vitality.maintenance.close')}
      />
      <ModalPermissionVitality ref={modalPermissionVitalityRef} onViewIntro={openVitalityWeb} />
    </View>
  );
};

export default Feature;
