import React, { useEffect, useState } from 'react';
import { Image, Text, TouchableOpacity } from 'react-native';
import LinearGradient from 'react-native-linear-gradient';
import { Icon, NativeModuleClient } from 'superapp-common-component';
import { colors } from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';
import { useTranslation } from 'react-i18next';
import _get from 'lodash/get';
import _find from 'lodash/find';
import _lowerCase from 'lodash/lowerCase';
import _trim from 'lodash/trim';

import { isEmpty } from '@/utils/utils';
import { getItem } from '@/utils/storage';
import ROUTE_PATH from '@/navigators/routePath';
import { TierVipNavigationProp } from '@/pages/TierVip';
import { EVENTS_USER_TRACKING, STORAGE_NAME } from '@/utils/constants';
import { userTracking } from '@/stores/global/actions';
import { makeSelectTierVip } from '@/stores/global/selectors';

import styles from './styles';

interface IBadgeTierVipProps {
  rank?: string;
}

const BadgeTierVip: React.FC<IBadgeTierVipProps> = ({ rank }) => {
  const dispatch = useDispatch();
  const { i18n } = useTranslation();
  const [userBadge, setUserBadge] = useState<MODEL.IBadge>({ colors: [], title: '', id: '' });
  const navigation = useNavigation<TierVipNavigationProp>();
  const tierVip = useSelector(makeSelectTierVip());

  const onPressTierVip = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.DASHBOARD,
      unixTimestamp: dayjs().unix(),
      buttonText: rank!,
    });
    dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.CLICK_TIER_VIP, timestamp: dayjs().unix() }));

    navigation.navigate(ROUTE_PATH.TIER_VIP);
  };

  useEffect(() => {
    (async () => {
      if (!isEmpty(tierVip) && !isEmpty(rank)) {
        const lang = await getItem<string>(STORAGE_NAME.LANGUAGE, 'vi');
        const currentUserBadge = _get(tierVip, `${lang}.badges`);
        const currentBadgeByRank: MODEL.IBadge = _find(currentUserBadge, (badge: MODEL.IBadge) => badge?.id === _lowerCase(_trim(rank)));
        if (!isEmpty(currentBadgeByRank)) {
          setUserBadge(currentBadgeByRank);
        }
      }
    })();
  }, [i18n.language, tierVip, rank]);

  return userBadge && !isEmpty(userBadge.colors) ? (
    <LinearGradient colors={userBadge.colors} start={{ x: 0, y: 0 }} end={{ x: 1, y: 0 }} style={styles.linearGradient}>
      <TouchableOpacity style={styles.container} onPress={onPressTierVip}>
        <Image source={require('@/assets/images/bagde.png')} style={styles.img} />
        <Text style={styles.buttonText}>{userBadge.title}</Text>
        <Icon name="Arrow-Right" size={12} color={colors.white} style={styles.icon} />
      </TouchableOpacity>
    </LinearGradient>
  ) : (
    <></>
  );
};

export default BadgeTierVip;
