import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class VerifyAccountService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_CIAM);
  }

  public requestMyOtp = (params: any) => this.instance.post<any>('/otp/requestMyOtp', { ...params });

  public submitVerifyAccount = (params: any) => {
    return this.instance.post<any>('/verifyAccount', { ...params });
  };

  public submitPreVerifyAccount = (params: API.IPreVerifyAccount) => {
    return this.instance.post<MODEL.IVerifyAccount>('/preVerifyAccount', { ...params });
  };
}

export default new VerifyAccountService();
