import React, { forwardRef, useImperativeHandle } from 'react';
import { View, Image, ViewStyle, StyleProp } from 'react-native';
import { Button } from 'superapp-common-component';
import Text from '@/components/Text';

import warningIcon from '@/assets/images/icon-warning.png';

import styles from './styles';
import Modal, { useModal } from '../Modal/Modal';

interface ModalConfirmErrorProps {
  title?: string;
  subtitle?: string;
  btnText: string;
  customBtnText?: string;
  onConfirm?: () => void;
  customAction?: () => void;
  onDismiss?: () => void;
  btnStyle?: StyleProp<ViewStyle>;
  customBtnStyle?: StyleProp<ViewStyle>;
}

export interface ModalConfirmErrorRef {
  open: () => void;
  close: () => void;
}

const ModalConfirmError = (props: ModalConfirmErrorProps, ref: React.Ref<unknown> | undefined) => {
  const { title, subtitle, btnText, onConfirm, customAction, onDismiss, customBtnText, btnStyle, customBtnStyle } = props;
  const [modalRef, componentModal] = useModal();

  const handleSubmit = () => {
    componentModal()?.close();
    if (onConfirm) {
      onConfirm();
    }
  };

  useImperativeHandle(
    ref,
    (): ModalConfirmErrorRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal()?.close(),
    }),
  );

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer} onDismiss={onDismiss}>
      <View style={styles.modalContent}>
        <View style={{ alignItems: 'center' }}>
          <Image style={styles.imageStyles} source={warningIcon} />
        </View>
        <View>
          <Text style={styles.title} fontFamily="bold">
            {title}
          </Text>
          <Text style={styles.subtitle}>{subtitle}</Text>
        </View>
        {customAction && (
          <Button
            onPress={customAction}
            text={customBtnText}
            type="secondary"
            size="large"
            uppercase={false}
            labelStyle={styles.buttonText}
            style={[styles.button, customBtnStyle]}
            id="ModalConfirmError.btnActionModalError"
          />
        )}
        <Button
          onPress={handleSubmit}
          text={btnText}
          type="secondary"
          size="large"
          uppercase={false}
          labelStyle={styles.buttonText}
          style={[styles.button, btnStyle]}
          id="ModalConfirmError.btnCloseModalError"
        />
      </View>
    </Modal>
  );
};

export default forwardRef(ModalConfirmError);
