import { TAction } from '@/stores/types';

import {
  AUTHENTICATE_WITH_PASS,
  IS_VERIFY_PASSWORD_ERROR,
  LOADING_REQUEST,
  LOADING_SUCCESS,
  LOGIN,
  LOGIN_OKTA,
  LOGIN_ERROR,
  LOGIN_SUCCESS,
  LOGOUT,
  LOGOUT_SUCCESS,
  PUT_PASSWORD_ERROR,
  PUT_PASSWORD_REQUEST,
  PUT_PASSWORD_SUCCESS,
  REFRESH_TOKEN,
  REFRESH_TOKEN_ERROR,
  RESET_PASSWORD_ERROR,
  SAVE_NEW_USERNAME,
  SAVE_IS_MIGRATED,
  LOGOUT_API,
  SESSION_EXPIRED,
  REMIND_BIOMETRIC,
} from './constants';

export const login = (payload: { phoneNumber?: string; password?: string; accessToken?: string; refreshToken?: string }): TAction => ({
  type: LOGIN,
  payload,
});

export const loginOkta = (payload: {
  username?: string;
  password?: string;
  setVisibleLoginModal: (visible: boolean) => void;
  isAutoLogin?: boolean;
}): TAction => ({
  type: LOGIN_OKTA,
  payload,
});

export const logout = (payload: { neededCallApi: boolean; isChangePassword?: boolean; isNotNavigate?: boolean }): TAction => ({
  type: LOGOUT,
  payload,
});
export const logoutSuccess = (): TAction => ({ type: LOGOUT_SUCCESS });
export const logoutRequestApi = (): TAction => ({ type: LOGOUT_API });

export const loadingSuccess = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequest = (): TAction => ({ type: LOADING_REQUEST });

export const loginSuccess = (payload?: string): TAction => ({ type: LOGIN_SUCCESS, payload });
export const loginError = (payload: {
  isLoged: boolean;
  error: string;
  remainingLoginAttempts?: number;
  isAutoLogin?: boolean;
}): TAction => ({ type: LOGIN_ERROR, payload });

export const refreshToken = (): TAction => ({ type: REFRESH_TOKEN });
export const setRefreshTokenError = (payload?: { errorCode: string; hasError: boolean }): TAction => ({
  type: REFRESH_TOKEN_ERROR,
  payload,
});

export const authenticateWithPass = (payload: { phoneNumber: string; password: string }): TAction => ({
  type: AUTHENTICATE_WITH_PASS,
  payload,
});
export const setIsVerifyPasswordError = (payload: boolean): TAction => ({ type: IS_VERIFY_PASSWORD_ERROR, payload });

export const putPasswordRequest = (): TAction => ({ type: PUT_PASSWORD_REQUEST });
export const putPasswordSuccess = (): TAction => ({ type: PUT_PASSWORD_SUCCESS });
export const putPasswordError = (): TAction => ({ type: PUT_PASSWORD_ERROR });
export const resetPasswordError = (): TAction => ({ type: RESET_PASSWORD_ERROR });

export const saveNewUsername = (payload: string): TAction => ({ type: SAVE_NEW_USERNAME, payload });
export const saveIsMigrated = (payload: string | boolean): TAction => ({ type: SAVE_IS_MIGRATED, payload });

export const sessionExpired = (): TAction => ({ type: SESSION_EXPIRED });

export const remindBiometric = (payload: boolean): TAction => ({ type: REMIND_BIOMETRIC, payload });
