import React, { useRef, useState } from 'react';
import { Image, SafeAreaView, StatusBar, View } from 'react-native';
import { Button, NativeModuleClient, Text, useTheme } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { StackNavigationProp } from '@react-navigation/stack';
import { useNavigation } from '@react-navigation/native';
import dayjs from 'dayjs';

import { Theme } from '@/config/themes.config';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { APPS_NAME } from '@/utils/constants';
import { delay, hasMaintenanceVitality } from '@/utils/utils';
import Loading from '@/components/Loading';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';

import useStyles from './styles';

type EConsentSuccessNavigationProp = StackNavigationProp<TStackParamList, 'EConsentSuccess'>;

const EConsentSuccess = () => {
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const { t } = useTranslation();
  const navigation = useNavigation<EConsentSuccessNavigationProp>();
  const [isLoadingOpenApp, setLoadingOpenMiniApp] = useState<boolean>(false);
  const modalMaintainRef = useRef<ModalConfirmErrorRef>();
  const [isMaintenance, setMaintenance] = useState(false);

  const openMiniApp = async () => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.ECONSENT_SUCCESS,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Explore',
      });
      setLoadingOpenMiniApp(true);
      const hasMaintenance = await hasMaintenanceVitality();
      if (hasMaintenance) {
        setMaintenance(true);
        throw new Error('app maintenance');
      }

      await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.VITALITY);
      await delay(300);
      await NativeModuleClient.MiniApp.loadApp(APPS_NAME.VITALITY, null);
    } catch (error) {
      console.log('ERROR: Can not open mini app', APPS_NAME.VITALITY);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const onCloseMaintenance = () => {
    navigation.navigate(ROUTE_PATH.DASHBOARD);
    setMaintenance(false);
  };

  const onDismiss = async () => {
    if (isMaintenance) {
      modalMaintainRef.current?.open();
    } else {
      navigation.navigate(ROUTE_PATH.DASHBOARD);
    }
  };

  return (
    <SafeAreaView style={styles.container}>
      <StatusBar barStyle="dark-content" />
      <View style={styles.content}>
        <Image style={styles.image} source={require('@/assets/images/account-ready.png')} />
        <Text style={styles.title}>{t('home.econsent.success')}</Text>
        <Text style={styles.des}>{t('home.econsent.description')}</Text>
        <Button style={styles.explore} text={t('home.econsent.explore')} onPress={openMiniApp} />
        <Loading visible={isLoadingOpenApp} onDismiss={onDismiss} />
        <ModalConfirmError
          ref={modalMaintainRef}
          title={t('common.vitality.maintenance.title')}
          subtitle={t('common.vitality.maintenance.description')}
          btnText={t('common.vitality.maintenance.close')}
          onConfirm={onCloseMaintenance}
        />
      </View>
    </SafeAreaView>
  );
};

export default EConsentSuccess;
