import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import jwtDecode from 'jwt-decode';
import { NativeModuleClient } from 'superapp-common-component';
import dayjs from 'dayjs';

import APP_ENV from '@/config/app.config';
import { ON_CLOSE_PAYMENT_SUPER_APP, STORAGE_NAME } from '@/utils/constants';
import { getItem } from '@/utils/storage';
import { isEmpty } from '@/utils/utils';
import ROUTE_PATH from '@/navigators/routePath';
import WebViewAuthenticated, { useWebViewAuthenticated } from '@/components/WebViewAuthenticated';

import WebViewPayment, { TWebViewUrlRef } from '../WebViewPayment';

const formatDateExpired = (msSeconds: number) => {
  const d = new Date(msSeconds);
  return d.toISOString();
};

export const useIcareV2 = (): [React.MutableRefObject<TIcareV2Ref | null>, () => TIcareV2Ref] => {
  const ref = useRef<TIcareV2Ref>(null);
  const getInstance = () => ref.current!;

  return [ref, getInstance];
};

type TIcareV2Ref = {
  show: (data: Record<string, unknown>) => void;
};

const ICareV2: React.ForwardRefRenderFunction<TIcareV2Ref, {}> = (props, ref) => {
  const { i18n } = useTranslation();
  const [currentLanguage, setCurrentLanguage] = useState<'vi' | 'en'>();
  const [webViewAuthenticatedRef, webViewAuthenticatedInstance] = useWebViewAuthenticated();
  const webviewPayment = useRef<TWebViewUrlRef>();

  const openWebView = async (url: string) => {
    const [accessToken, refreshToken, refreshTokenExprirySeconds, refreshTokenOkta, refreshTokenOktaExprirySeconds] = await Promise.all([
      getItem(STORAGE_NAME.ACCESS_TOKEN),
      getItem(STORAGE_NAME.REFRESH_TOKEN),
      getItem<number>(STORAGE_NAME.REFRESH_TOKEN_EXPRIRY_SECONDS, 0),
      getItem<string>(STORAGE_NAME.REFRESH_TOKEN_OKTA),
      getItem<number>(STORAGE_NAME.VITALITY_LOGIN_TOKEN_EXPIRY_DATE),
    ]);
    const tokenInfo = jwtDecode<MODEL.ITokenDecode>(accessToken);

    webViewAuthenticatedInstance().openUri(url, {
      sharedToken: {
        url,
        accessToken,
        refreshToken,
        refreshTokenOkta,
        expiredAccessToken: formatDateExpired(tokenInfo.exp * 1000),
        expiredRefreshToken: formatDateExpired((tokenInfo.nbf + refreshTokenExprirySeconds) * 1000),
        expiredRefreshTokenOkta: formatDateExpired(refreshTokenOktaExprirySeconds),
      },
    });
  };

  const show = (data?: Record<string, unknown>) => {
    openWebView(`${APP_ENV.ICARE_V2_URL}/#/detail/${data?.ticketId}?source=superapp`);
  };

  const onOpenPayment = (url?: string) => {
    if (isEmpty(url)) return;

    webviewPayment.current?.openURL(url!);
  };
  const onCloseWebviewPayment = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NOTIFICATIONS,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close WebView Payment',
    });

    if (webViewAuthenticatedRef.current) {
      webViewAuthenticatedRef.current?.injectedJavaScript(`
      setTimeout(() => {
        (function() {
          document.dispatchEvent(new MessageEvent(${ON_CLOSE_PAYMENT_SUPER_APP}, {
            data: 'onClosePayment'
          }));
        })();
      }, 100);
      `);
    }
  };

  useEffect(() => {
    (async () => {
      const language = await getItem<'vi' | 'en'>(STORAGE_NAME.LANGUAGE, 'vi');
      setCurrentLanguage(language);
    })();
  }, [i18n.language]);

  useImperativeHandle(
    ref,
    () => ({
      show,
    }),
    [],
  );

  return (
    <>
      <WebViewPayment ref={webviewPayment} onClose={onCloseWebviewPayment} />
      <WebViewAuthenticated ref={webViewAuthenticatedRef} lang={currentLanguage} onOpenPayment={onOpenPayment} />
    </>
  );
};

export default forwardRef(ICareV2);
