import React, { useRef, useState } from 'react';
import { View } from 'react-native';
import { useTranslation } from 'react-i18next';
import { StackNavigationProp } from '@react-navigation/stack';
import { useNavigation } from '@react-navigation/native';
import { useDispatch, useSelector } from 'react-redux';
import { Image } from 'react-native-image-crop-picker';
import { NativeModuleClient } from 'superapp-common-component';
import dayjs from 'dayjs';

import QRCode from '@/components/QRCode';
import Loading from '@/components/Loading';
import { useQRCode } from '@/components/QRCode/QRCode';
import ModalConfirmSuccess, { ModalConfirmSuccessRef } from '@/components/ModalConfirmSuccess';

import { getImageType, isEmpty } from '@/utils/utils';
import { REGEX } from '@/utils/constants';
import APP_ENV from '@/config/app.config';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';

import styles from './styles';
import { makeSelectLoading } from './stores/selectors';
import { submitVerifyAccountQRCode } from './stores/actions';
import { saveVerifyAccount } from '../VerifyAccount/stores/actions';
import { makeSelectInfoVerifyAccount } from '../VerifyAccount/stores/selectors';
import { NS_VERIFY_ACCOUNT_QRCODE } from './stores/constants';
import verifyAccountQRCodeSaga from './stores/saga';
import verifyAccountQRCodeReducer from './stores/reducer';

type VerifyAccountQRCodeNavigationProp = StackNavigationProp<TStackParamList, 'VerifyAccountQRCode'>;

const VerifyAccountQRCode = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  useInjectReducer({ key: NS_VERIFY_ACCOUNT_QRCODE, reducer: verifyAccountQRCodeReducer });
  useInjectSaga({ key: NS_VERIFY_ACCOUNT_QRCODE, saga: verifyAccountQRCodeSaga });
  const [ref, qrCodeInstance] = useQRCode();
  const loading = useSelector(makeSelectLoading());
  const [isVerifySuccess, setVerifySuccess] = useState(false);
  const verifyInfo = useSelector(makeSelectInfoVerifyAccount());
  const navigation = useNavigation<VerifyAccountQRCodeNavigationProp>();
  const modalConfirmSuccessRef = useRef<null | ModalConfirmSuccessRef>(null);

  const closeModal = () => {
    if (modalConfirmSuccessRef.current) {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.VERIFY_ACCOUNT_QRCODE,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
      });
      modalConfirmSuccessRef.current?.close();
      navigation.navigate(ROUTE_PATH.DASHBOARD);
    }
  };

  const onSelectImage = (image: Image, qrcode = '') => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT_QRCODE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Select Image',
    });
    const [, oldIdNo] = qrcode.split('|');
    const hasFormatIdNumber = REGEX.CMNN.test(oldIdNo);

    if (qrcode && oldIdNo && hasFormatIdNumber) {
      dispatch(
        submitVerifyAccountQRCode({
          sessionId: verifyInfo?.sessionId!,
          clientId: APP_ENV.CLIENT_ID,
          oldIdNo,
          callback: () => setVerifySuccess(true),
          callbackError: () => {
            qrCodeInstance()?.setShowMessage({
              success: false,
              message: t('verifyAccount.message.qrcode.error.api'),
            });
          },
        }),
      );
    } else {
      dispatch(
        submitVerifyAccountQRCode({
          sessionId: verifyInfo?.sessionId!,
          clientId: APP_ENV.CLIENT_ID,
          ocrIdDocImages: {
            name: image.filename || `preVerifyAccount${getImageType(image.mime)}`,
            imageType: image.mime,
            imageLayout: 1,
            base64: image.data!,
          },
          callback: () => setVerifySuccess(true),
          callbackError: () => {
            qrCodeInstance()?.setShowMessage({
              success: false,
              message: t('verifyAccount.message.qrcode.error.api'),
            });
          },
        }),
      );
    }
  };

  const onReadQRCode = (value: string = '') => {
    if (value) {
      const [, oldIdNo] = value.split('|');
      const hasFormatIdNumber = REGEX.CMNN.test(oldIdNo);

      if (hasFormatIdNumber && !isEmpty(verifyInfo)) {
        dispatch(
          submitVerifyAccountQRCode({
            sessionId: verifyInfo?.sessionId!,
            clientId: APP_ENV.CLIENT_ID,
            oldIdNo,
            callback: () => setVerifySuccess(true),
            callbackError: () => {
              qrCodeInstance()?.setShowMessage({
                success: false,
                message: t('verifyAccount.message.qrcode.error.api'),
              });
            },
          }),
        );
      }
      return;
    }
  };

  const onSelectImageError = () => {};

  const onClose = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_ACCOUNT_QRCODE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });
    dispatch(saveVerifyAccount({}));
    navigation.canGoBack() && navigation.goBack();
  };

  const onDismiss = () => {
    if (isVerifySuccess) {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'Verify Account Success',
        unixTimestamp: dayjs().unix(),
      });
      modalConfirmSuccessRef.current?.open();
    }
  };

  return (
    <View style={styles.flex1}>
      <QRCode
        ref={ref}
        disabled={loading || isVerifySuccess}
        onClose={onClose}
        onReadQRCode={onReadQRCode}
        onSelectImage={onSelectImage}
        onSelectImageError={onSelectImageError}
      />
      <Loading visible={loading} onDismiss={onDismiss} />
      <ModalConfirmSuccess
        ref={modalConfirmSuccessRef}
        onConfirm={closeModal}
        title={t('common.successfully')}
        subtitle={t('verifyAccount.message.success')}
        btnText={t('common.continue')}
      />
    </View>
  );
};

export default VerifyAccountQRCode;
