import { call, put, takeLatest, select } from 'redux-saga/effects';

import OneClaimService from '@/services/oneClaimService';
import OneCustomerService, { ServiceRequestParam } from '@/services/user';
import OneDocService from '@/services/oneDocService';
import { TAction } from '@/stores/types';
import { onDowloadFilePdf } from '@/utils/handleFile';
import { isEmpty } from '@/utils/utils';

import {
  loadingServiceRequest,
  loadingClaimTransactionRequest,
  loadingClaimTransactionSuccess,
  saveClaimTransaction,
  saveLaList,
  setLoadMoreClaim,
  saveServiceRequest,
  loadingPaymentTransaction,
  savePaymentTransaction,
  loadingDownloadServiceRequest,
  setLoadMoreServiceRequest,
  loadingLoadMoreServiceRequest,
  triggerFilter,
} from './actions';
import {
  GET_CLAIM_TRANSACTION,
  LOAD_MORE_CLAIM_TRANSACTION,
  GET_ALL_SERVICE_REQUEST,
  GET_PAYMENT_TRANSACTION,
  DOWNLOAD_PDF_SERVICE_REQUEST,
  LOAD_MORE_SERVICE_REQUEST,
} from './constants';
import { makeSelectClaimTransactions, makeSelectListLa, makeServiceRequest, makePagingServiceRequest, makeSelectTransactionFilter } from './selectors';

function* handleClaimTransaction({ payload }: TAction) {
  try {
    yield put(loadingClaimTransactionRequest());
    const searchPolicyNumbers: string[] = yield select(makeSelectTransactionFilter());
    const laListResponse: MODEL.IListLaResponse = yield call(OneClaimService.getListLa);
    yield put(saveLaList(laListResponse?.result));
    const idCardNoLaList = laListResponse?.result.map(item => item?.idCardNo);
    const claimTransactionsResponse: MODEL.IClaimTransactionResponse = yield call(OneClaimService.getListClaimTransaction, {
      ...payload,
      listLaIdCardNo: idCardNoLaList,
      searchPolicyNumbers,
    });
    yield put(saveClaimTransaction(claimTransactionsResponse?.result));
  } catch (error) {
  } finally {
    yield put(triggerFilter(false));
    yield put(loadingClaimTransactionSuccess());
  }
}

function* handleServiceRequest() {
  try {
    yield put(loadingServiceRequest(true));
    yield put(setLoadMoreServiceRequest(true));
    const policyNumbers: string[] = yield select(makeSelectTransactionFilter());
    const params: ServiceRequestParam = {
      paging: {
        pageIndex: 0,
        pageSize: 10,
      },
      policyNumbers,
    };

    const { data }: MODEL.IServiceRequestResponse = yield call(OneCustomerService.getServiceRequest, params);
    const result = {
      paging: params.paging,
      serviceRequests: data,
    };
    
    yield put(saveServiceRequest(result));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(triggerFilter(false));
    yield put(loadingServiceRequest(false));
  }
}

function* handleLoadMoreServiceRequest() {
  try {
    yield put(loadingLoadMoreServiceRequest(true));
    const policyNumbers: string[] = yield select(makeSelectTransactionFilter());
    const paging: MODEL.IPaging = yield select(makePagingServiceRequest());
    const serviceRequest: MODEL.IServiceRequest[] = yield select(makeServiceRequest());
    const params: ServiceRequestParam = {
      paging: { ...paging, pageIndex: (paging.pageIndex + 1) * paging.pageSize },
      policyNumbers
    };

    const { data }: MODEL.IServiceRequestResponse = yield call(OneCustomerService.getServiceRequest, params);

    if (isEmpty(data)) {
      yield put(setLoadMoreServiceRequest(false));
    } else {
      const result = {
        paging: { ...paging, pageIndex: paging.pageIndex + 1 },
        serviceRequests: [...serviceRequest, ...data],
      };
      yield put(saveServiceRequest(result));
    }
  } catch (error) {
    yield put(setLoadMoreServiceRequest(false));
    console.log('ERROR:', error);
  } finally {
    yield put(loadingLoadMoreServiceRequest(false));
  }
}

function* handleLoadMoreClaimTransaction({ payload }: TAction) {
  try {
    yield put(loadingClaimTransactionRequest());
    const searchPolicyNumbers: string[] = yield select(makeSelectTransactionFilter());
    const laList: MODEL.IListLa[] = yield select(makeSelectListLa());
    const idCardNoLaList = laList?.map(item => item?.idCardNo);
    const claimTransactionsResponse: MODEL.IClaimTransactionResponse = yield call(OneClaimService.getListClaimTransaction, {
      ...payload,
      listLaIdCardNo: idCardNoLaList,
      searchPolicyNumbers,
    });
    const claimTransactions: MODEL.IClaimTransaction[] = yield select(makeSelectClaimTransactions());
    yield put(saveClaimTransaction(claimTransactions?.concat(claimTransactionsResponse?.result)));

    if (!(claimTransactionsResponse?.result?.length > 0)) {
      yield put(setLoadMoreClaim(false));
    }
  } catch (error) {
    yield put(setLoadMoreClaim(false));
  } finally {
    yield put(loadingClaimTransactionSuccess());
  }
}

function* handleGetPaymentTransaction() {
  try {
    yield put(loadingPaymentTransaction(true));
    const policyNumbers: string[] = yield select(makeSelectTransactionFilter());
    const response: MODEL.IPaymentTransactions = yield call(OneCustomerService.getPaymentTransactionService, {policyNumbers});
    yield put(savePaymentTransaction(response.data));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingPaymentTransaction(false));
  }
}

function* handleDownloadPdf({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingDownloadServiceRequest(true));
    const {documentContentType, documentId} = payload;
    if (documentContentType !== 'application/pdf') { 
      const blobUrl: string = yield getPdfFileByRequestId(documentId);
      yield onDowloadFilePdf(payload?.name, blobUrl, reject, resolve, false);
    }
    else {
      yield onDowloadFilePdf(payload?.name, payload.url, reject, resolve, false);
    }
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingDownloadServiceRequest(false));
  }
}

function* getPdfFileByRequestId (url: string) {
  try {
    const documentPdfResponse: MODEL.IDocumentPdf = yield call(OneDocService.getFilePdfUrlByLetterIdService, url);
    return documentPdfResponse?.url || ''
  } catch (error) {
    return ''
  } finally {
  } 
}


function* transactionHistorySaga() {
  yield takeLatest(GET_CLAIM_TRANSACTION, handleClaimTransaction);
  yield takeLatest(GET_ALL_SERVICE_REQUEST, handleServiceRequest);
  yield takeLatest(LOAD_MORE_SERVICE_REQUEST, handleLoadMoreServiceRequest);
  yield takeLatest(LOAD_MORE_CLAIM_TRANSACTION, handleLoadMoreClaimTransaction);
  yield takeLatest(GET_PAYMENT_TRANSACTION, handleGetPaymentTransaction);
  yield takeLatest(DOWNLOAD_PDF_SERVICE_REQUEST, handleDownloadPdf);
}

export default transactionHistorySaga;
