import React, { useEffect, useRef, useState } from 'react';
import {
  Image,
  SafeAreaView,
  StatusBar,
  View,
  BackHandler,
  ScrollView,
  TouchableOpacity,
} from 'react-native';
import { Button, NativeModuleClient } from 'superapp-common-component';
import NetInfo from '@react-native-community/netinfo';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import _get from 'lodash/get';
import { utils } from 'superapp-common-component';
import dayjs from 'dayjs';

import NoConnection from '@/components/NoConnection';
import Text from '@/components/Text';
import { TStackParamList } from '@/navigators';
import {
  makeSelectCheckLogin,
  makeSelectNewUsername,
} from '@/stores/authentication/selectors';
import { StackNavigationProp } from '@react-navigation/stack';
import ROUTE_PATH from '@/navigators/routePath';
import { maskedPhoneNumber } from '@/utils/utils';
import {
  APPS_NAME,
  EVENT_NAMES,
  JS_EVENT_NAMES,
  MINI_APP_EVENTS_EMITTER,
  TYPE_NAVIGATION_POS_REQUEST,
} from '@/utils/constants';
import { makeSelectProfile } from '@/stores/global/selectors';
import MiniAppEventEmitter from '@/utils/miniAppEventListener';
import jsEventEmitter from '@/utils/jsEventEmitter';
import ModalLogOut, {
  ModalLogOutRef,
} from '@/pages/Profile/components/ModalLogOut';

import styles from './styles';

const { a11y } = utils;

type UpdateContactConfirmationNavigationProp = StackNavigationProp<
  TStackParamList,
  'UpdateContactConfirmation'
>;

const UpdateContactConfirmation = () => {
  const { t } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const navigation = useNavigation<UpdateContactConfirmationNavigationProp>();
  const phoneReplace = useSelector(makeSelectNewUsername());
  const profile = useSelector(makeSelectProfile());
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const loginStatus = useSelector(makeSelectCheckLogin());
  const modalLogOutRef = useRef<null | ModalLogOutRef>(null);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  const openIcareP840 = async () => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Update phone number',
      });
      await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.POS_REQUEST);
      await NativeModuleClient.MiniApp.loadApp(
        APPS_NAME.POS_REQUEST,
        JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P840 }),
      );
    } catch (error) {
      console.log('ERROR: Can not open mini app', APPS_NAME.POS_REQUEST);
    }
  };

  const goBack = () => {
    // Nothing
    return true;
  };

  const handleEventListener = (payload: string = '') => {
    if (!loginStatus.isLoged) return;
    const parserPayload = JSON.parse(payload);

    if (parserPayload?.type) {
      switch (parserPayload.type) {
        case EVENT_NAMES.SESSION_EXPIRED:
          const routeState = navigation.getState();
          const routes = _get(routeState, 'routes', []);
          const screenName = routes[0]?.name;
          if (screenName === ROUTE_PATH.ONBOARDING) {
            navigation.reset({
              index: 0,
              routes: [{ name: ROUTE_PATH.DASHBOARD }],
            } as never);
            setTimeout(() => {
              jsEventEmitter.emit(JS_EVENT_NAMES.SESSION_EXPIRED);
            });
          }
          break;
        default:
          break;
      }
    }
  };

  const handleLogout = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Logout',
    });
    modalLogOutRef?.current?.open();
  };

  useEffect(() => {
    const backHandler = BackHandler.addEventListener(
      'hardwareBackPress',
      goBack,
    );

    return () => backHandler.remove();
  }, []);

  useEffect(() => {
    const subscription = MiniAppEventEmitter?.addListener(
      MINI_APP_EVENTS_EMITTER.MINI_APP_EVENT_EMITTER,
      handleEventListener,
    );
    return () => subscription?.remove();
  }, [loginStatus.isLoged]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <StatusBar barStyle="dark-content" />
      <ScrollView
        style={styles.pane}
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}>
        <View style={styles.container}>
          <View style={styles.contentChangeUsername}>
            <Image
              style={styles.image}
              resizeMode="contain"
              source={require('@/assets/images/login-update-contact-confirmation.png')}
            />
            <Text style={styles.textTitleChangeUser}>
              {t('login.update.contact.confirmation.title', {
                phone: profile?.mobile || '',
              })}
            </Text>
            <View style={styles.mgh32}>
              <Text style={styles.textDescriptionChangeUser} fontFamily="bold">
                {t('login.update.contact.confirmation.sub.title', {
                  phone1: profile?.mobile || '',
                  phone2: phoneReplace || '',
                })}
              </Text>
              <Text style={styles.textSubDescriptionChangeUser}>
                {t('login.update.contact.confirmation.sub.title1', {
                  phone: profile?.mobile || '',
                })}
                {/* <Text style={styles.textLink} onPress={() => onPressCallForUs()}>
                  {t('login.update.contact.confirmation.sub.title2')}
                </Text>
                {t('login.update.contact.confirmation.sub.title3')} */}
              </Text>
            </View>
          </View>
          <View style={[styles.buttonSuccessWrapper]}>
            <>
              <Button
                text={t('login.update.contact.confirmation.top.button')}
                onPress={() => {
                  NativeModuleClient.AdobeExperience.trackButton({
                    screen: ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION,
                    unixTimestamp: dayjs().unix(),
                    buttonText: 'Update username',
                  });
                  navigation.navigate(ROUTE_PATH.UPDATE_CONTACT_OTP);
                }}
                style={[styles.marginBottom10]}
                uppercase={false}
                labelStyle={styles.buttonText}
                id="UpdateContactConfirmation.btnSubmit"
              />
              <Button
                text={t('login.update.contact.confirmation.bottom.button')}
                onPress={openIcareP840}
                style={[styles.marginBottom10]}
                uppercase={false}
                labelStyle={styles.buttonText}
                type="secondary"
                id="UpdateContactConfirmation.btnCancel"
              />
            </>
            <TouchableOpacity
              style={styles.marginBottom10}
              onPress={handleLogout}
              {...a11y('UpdateContactConfirmation.btnSubmit')}>
              <Text style={[styles.buttonText, styles.logout]}>
                {t('profile.account.logout')}
              </Text>
            </TouchableOpacity>
          </View>
          <ModalLogOut ref={modalLogOutRef} />
        </View>
      </ScrollView>
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default UpdateContactConfirmation;
