import React, { forwardRef, useImperativeHandle, useState } from 'react';
import { Image, View } from 'react-native';
import { Button, NativeModuleClient, Text } from 'superapp-common-component';
import dayjs from 'dayjs';
import ErrorIcon from '@/assets/images/icon-warning.png';
import warningIcon from '@/assets/images/warning.png';
import { useTranslation } from 'react-i18next';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';

import styles from '../styles';

type ModalErrorPropsType = {};

export interface ModalErrorRef {
  open: (nextContent: TErrorModalContent) => void;
  close: () => void;
}

type TErrorModalContent = {
  title: string;
  subTitle: string;
  status: 'error' | 'warning';
};

const Icons = {
  error: ErrorIcon,
  warning: warningIcon,
};

const ModalError = (props: ModalErrorPropsType, ref: React.Ref<unknown> | undefined) => {
  const { t } = useTranslation();
  const [modalsRef, modalInstance] = useModal();
  const [content, setContent] = useState<TErrorModalContent>({
    title: '',
    subTitle: '',
    status: 'error',
  });

  const onPressClose = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Notification',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close',
    });
    modalInstance()?.close();
  };

  const openModal = (nextContent: TErrorModalContent) => {
    if (nextContent) {
      setContent(nextContent);
      modalInstance().show();
    }
  };

  useImperativeHandle(
    ref,
    (): ModalErrorRef => ({
      open: openModal,
      close: () => modalInstance().close(),
    }),
  );

  return (
    <Modal ref={modalsRef} modalStyle={styles.modalStyles} overlayOpacity={0.5} onDismiss={() => null}>
      <View style={styles.modalContainer}>
        <View style={styles.wrapperModal}>
          <Image source={Icons[content.status] || Icons.error} style={styles.imageWarning} />
          <Text style={styles.modalTitle}>{content.title}</Text>
          <Text style={styles.modalDescription}>{content.subTitle}</Text>
          <View style={styles.wrapperBtn}>
            <Button
              id="Button.Confirm.No.Permission"
              style={styles.btnConfirm}
              text={t('home.permission.modal.btn.close')}
              type="secondary"
              onPress={onPressClose}
            />
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalError);
