import React, { forwardRef, useImperativeHandle, useRef, useState, useEffect } from 'react';
import { View, StatusBar, TouchableOpacity, SafeAreaView, KeyboardAvoidingView, Platform, Animated } from 'react-native';
import { Icon, InnerLoading } from 'superapp-common-component';
import { WebView, WebViewNavigation } from 'react-native-webview';
import { useSelector } from 'react-redux';
import _get from 'lodash/get';

import { a11y } from '@/utils/utils';

import { makeSelectIntroduction } from '@/stores/global/selectors';
import { getItem } from '@/utils/storage';
import { STORAGE_NAME } from '@/utils/constants';

import styles from './styles';
import Modal, { SlideAnimation } from '../Modal';
import { useModal } from '../Modal/Modal';

export const useModalIntroduction = (): [React.MutableRefObject<TModalIntroductionRef | null>, () => TModalIntroductionRef] => {
  const ref = useRef<TModalIntroductionRef>(null);
  const getInstance = () => ref.current!;

  return [ref, getInstance];
};

export type TModalIntroductionRef = {
  open: () => void;
  close: () => void;
};

export type TModalWebviewProps = {};

type TWebview = WebView<{
  ref: unknown;
  source: {
    html: string;
  };
  onNavigationStateChange?: (evt: WebViewNavigation) => void;
}>;
const isPlatformIos = Platform.OS === 'ios';

const ModalIntroduction: React.ForwardRefRenderFunction<TModalIntroductionRef, TModalWebviewProps> = (props, ref) => {
  const webviewRef = useRef<TWebview>(null);
  const introduction = useSelector(makeSelectIntroduction());
  const [language, setLanguage] = useState<string>('vi');
  const [modalsRef, modalInstance] = useModal();

  useEffect(() => {
    (async () => {
      const lang = await getItem<string>(STORAGE_NAME.LANGUAGE, 'vi');
      setLanguage(lang);
    })();
  }, []);

  const imperativeHandle = (): TModalIntroductionRef => ({
    open: () => {
      modalInstance().show();
    },
    close: () => {
      modalInstance().close();
    },
  });

  useImperativeHandle(ref, imperativeHandle, []);

  return (
    <Modal
      ref={modalsRef}
      modalAnimation={
        new SlideAnimation({
          slideFrom: 'bottom',
        })
      }
    >
      <StatusBar barStyle="dark-content" />
      <SafeAreaView style={styles.safeContent}>
        {
          <View style={styles.flex1}>
            <KeyboardAvoidingView behavior={'padding'} keyboardVerticalOffset={isPlatformIos ? 60 : 24} style={styles.flex1}>
              <WebView
                ref={webviewRef}
                textZoom={100}
                source={{
                  html: `${_get(introduction, [language, 'value'])}`,
                }}
                startInLoadingState={true}
                renderLoading={() => (
                  <View style={[styles.flex1]}>
                    <InnerLoading />
                  </View>
                )}
              />
            </KeyboardAvoidingView>
            <Animated.View style={[styles.btnClose]}>
              <TouchableOpacity
                {...a11y('introduction.btnClose')}
                onPress={() => {
                  modalInstance().close();
                }}
              >
                <View style={styles.wrapperbBtnClose}>
                  <Icon name="Close" color="#fff" size={18} />
                </View>
              </TouchableOpacity>
            </Animated.View>
          </View>
        }
      </SafeAreaView>
    </Modal>
  );
};

export default forwardRef(ModalIntroduction);
