import React, { useState, useEffect, useRef } from 'react';
import { View, TouchableOpacity, KeyboardAvoidingView, Platform, TouchableWithoutFeedback, Keyboard, Image } from 'react-native';
import { Controller, useForm } from 'react-hook-form';
import { TextInput, Button, Icon, theme, Checkbox, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import TouchId from 'react-native-touch-id-passcode';
import { useNavigation } from '@react-navigation/native';
import { useDispatch } from 'react-redux';
import dayjs from 'dayjs';

import defaultAvatar from '@/assets/images/avatar-default.png';

import AzureNotificationClient from '@/clients/AzureNotification';
import WebView from '@/components/WebView';
import { getItem } from '@/utils/storage';
import APP_ENV from '@/config/app.config';
import { GRAY, HITSLOP } from '@/utils/styles';
import { BIOMETRY_TYPE, STORAGE_NAME } from '@/utils/constants';
import { loginOkta as loginOktaAction, logout, refreshToken as refreshTokenAction } from '@/stores/authentication/actions';
import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { a11y, isEmpty, maskedPhoneNumberCenter } from '@/utils/utils';

import styles from './styles';

const { colors } = theme;

type TUserCache = {
  username: string;
  fullname: string;
  firstName: string;
};

const OldUserlogged: React.FC<{ username?: string; fullname?: string; firstName?: string; onPressNotMe: () => void }> = props => {
  const { t } = useTranslation();

  return (
    <View style={[styles.mt_16, styles.mb_24, { alignItems: 'center' }]}>
      <Image source={defaultAvatar} style={styles.defaultAvatar} />
      <Text fontFamily="bold" style={styles.fullname} numberOfLines={1} ellipsizeMode="tail">
        {t('home.title.wellcome', { name: props?.firstName ? props.firstName : t('home.title.guest') })}
      </Text>
      <Text style={styles.username}>{maskedPhoneNumberCenter(props.username!)}</Text>
    </View>
  );
};

const BIOMETRY_TYPE_ICON = {
  TOUCH: require('@/assets/images/touchID.png'),
  FACE: require('@/assets/images/faceID.png'),
};

type LoginStepProps = {
  isRefreshTokenError: boolean;
  setShowModal: (isShow: boolean) => void;
  setCurrentStep: (step: number, durationTime?: number) => void;
  login: (data: { phoneNumber?: string; password?: string; accessToken?: string; refreshToken?: string }) => void;
  setLoading: (status: boolean) => void;
  loginError: (payload: { isLoged: boolean; error: string }) => any;
};

const LoginStep: React.FC<LoginStepProps> = props => {
  const { isRefreshTokenError, setCurrentStep, setShowModal, login } = props;
  const { t } = useTranslation();
  const navigation = useNavigation<any>();
  const [biometryType, setBiometryType] = useState<null | string>(null);
  const [isActiveBio, setIsActiveBio] = useState<boolean>(false);
  const [isSecurePassword, setSecurePassword] = useState<boolean>(true);
  const [isAgree, setIsAgree] = useState(false);
  const [userCache, setUserCache] = useState<TUserCache | undefined>();
  const WebviewRef = useRef<any>(null);
  const dispatch = useDispatch();

  const { OKTA_ENABLE } = APP_ENV;

  const handleTouchId = async () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login Biometric',
    });

    if (isRefreshTokenError) return;

    const pincode = await getItem(STORAGE_NAME.PIN_CODE);
    try {
      const isGranted = await TouchId.authenticate('', {
        passcodeFallback: false,
        fallbackLabel: pincode ? t('login.error.fallbackLabel.pin') : t('login.username.title'),
      });
      if (isGranted) {
        NativeModuleClient.AdobeExperience.trackScreen({
          screen: 'Login Success',
          unixTimestamp: dayjs().unix(),
        });
        dispatch(refreshTokenAction());
      } else {
        const step = pincode ? 2 : 1;
        setCurrentStep(step);
      }
    } catch (error: any) {
      if (Platform.OS === 'ios') {
        setCurrentStep(pincode ? 2 : 1);
      } else {
        if (error) {
          switch (error?.details) {
            case 'fallback':
              setCurrentStep(pincode ? 2 : 1);
              break;
            case 'cancelled':
              setCurrentStep(1);
              break;
          }
        }
      }
    }
  };

  const hideKeyboard = () => Keyboard.dismiss();

  const {
    control,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm({
    defaultValues: {
      phoneNumber: '',
      password: '',
    },
  });

  const onPressRegister = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Sign up now',
    });
    navigation.navigate(ROUTE_PATH.REGISTER);
    setShowModal(false);
    if (!__DEV__) {
      AzureNotificationClient.clearTappedNotification();
    }
  };

  const disabledBiometric = !(biometryType && isActiveBio);

  const handleLogin = async (data: { phoneNumber: string; password: string }) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });
    if (isAgree) {
      const { phoneNumber, password } = data;
      if (OKTA_ENABLE) {
        // TODO: hard demo
        dispatch(loginOktaAction({ username: phoneNumber, password, setVisibleLoginModal: setShowModal }));
      } else {
        login(data);
      }
    }
  };

  const loadUserCache = async () => {
    const [username, fullname, firstName] = await Promise.all([
      getItem(STORAGE_NAME.USERNAME),
      getItem(STORAGE_NAME.FIRST_NAME),
      getItem(STORAGE_NAME.FIRST_NAME),
    ]);
    if (username) {
      setUserCache({ username, fullname, firstName });
      setIsAgree(true);
      setValue('phoneNumber', username);
    }
  };

  const resetUserCache = () => {
    setUserCache(undefined);
    setIsAgree(false);
    setValue('phoneNumber', '');
    setValue('password', '');
    setIsActiveBio(false);
  };

  const handlePressNotMe = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Not me?',
    });
    dispatch(logout({ neededCallApi: false, isNotNavigate: true }));
    resetUserCache();
  };

  const navigateLoginInstruction = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Contact Info Header Right',
    });
    navigation.navigate(ROUTE_PATH.LOGIN_INSTRUCTION);
    setShowModal(false);
  };

  const showWebviewTerms = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'The terms',
    });
    WebviewRef?.current?.openURL(t('common.termCondition.url'));
  };

  const showWebviewConditions = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Conditions',
    });
    WebviewRef?.current?.openURL(t('common.collectionStatement.url'));
  };

  useEffect(() => {
    if (isRefreshTokenError) return;

    (async () => {
      try {
        const biometryTypeRes = await TouchId.isSupported();
        if (Platform.OS === 'ios') {
          if ([BIOMETRY_TYPE.TOUCH, BIOMETRY_TYPE.FACE].includes(biometryTypeRes)) {
            setBiometryType(biometryTypeRes);
          }
        } else {
          setBiometryType(biometryTypeRes ? BIOMETRY_TYPE_ICON.TOUCH : null);
        }
      } catch (err) {
        console.log('ERROR: TouchID/FaceID not support');
      }
    })();

    (async () => {
      try {
        const [isActive, pincode] = await Promise.all([
          getItem<boolean>(STORAGE_NAME.TOUCH_FACE_ID, false),
          getItem<string>(STORAGE_NAME.PIN_CODE, null),
        ]);

        if (isActive) {
          await handleTouchId();
        } else if (pincode) {
          setCurrentStep(2, 0);
        }

        setIsActiveBio(!!isActive);
      } catch (error) {
        console.log('ERROR: PinCode/Bio storage error');
      }
    })();

    //** Get old username */
    loadUserCache();
  }, []);

  return (
    <View style={styles.flex1}>
      <TouchableWithoutFeedback onPress={hideKeyboard} {...a11y('Login.backdropHideKeyboard')}>
        <KeyboardAvoidingView
          behavior={Platform.OS === 'ios' ? 'padding' : undefined}
          style={styles.flex1}
          keyboardVerticalOffset={Platform.OS === 'ios' ? 80 : 0}
        >
          <View style={styles.content}>
            <TouchableOpacity
              onPress={() => {
                setShowModal(false);
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.LOGIN,
                  unixTimestamp: dayjs().unix(),
                  buttonText: 'Close',
                });
                if (!__DEV__) {
                  AzureNotificationClient.clearTappedNotification();
                }
              }}
              {...a11y('Login.backdropHideKeyboard')}
            >
              <Text style={styles.leftLabelButton} fontFamily="bold">
                {t('login.modal.button.close')}
              </Text>
            </TouchableOpacity>
            <TouchableOpacity onPress={navigateLoginInstruction} {...a11y('Login.iconInfoHeaderRight')}>
              <Icon name="Info1" size={24} color={colors.primary.charcoal[80]} />
            </TouchableOpacity>
          </View>
          <View style={styles.container}>
            {/* without user cache */}
            {isEmpty(userCache) ? (
              <>
                <Text style={styles.mainTitle} fontFamily="bold">
                  {t('login.page.login.title')}
                </Text>

                <View style={styles.mt_24}>
                  <Controller
                    control={control}
                    rules={{
                      required: { value: true, message: t('login.page.error') },
                    }}
                    render={({ field: { onChange, value } }) => (
                      <TextInput
                        containerStyle={styles.mb_10}
                        label={t('login.page.username.placeholder')}
                        type={errors?.phoneNumber ? 'error' : 'info'}
                        disabled={false}
                        onChangeText={onChange}
                        value={value}
                        message={errors?.phoneNumber && errors?.phoneNumber?.message}
                        messageStyle={styles.messErrorFieldStyle}
                        id="Login.inputPhoneNumber"
                      />
                    )}
                    name="phoneNumber"
                  />
                  <View style={styles.asEnd}>
                    <TouchableOpacity
                      onPress={() => {
                        setShowModal(false);
                        NativeModuleClient.AdobeExperience.trackButton({
                          screen: ROUTE_PATH.LOGIN,
                          unixTimestamp: dayjs().unix(),
                          buttonText: 'Forgot username',
                        });
                        if (!__DEV__) {
                          AzureNotificationClient.clearTappedNotification();
                        }
                        navigation.navigate(ROUTE_PATH.FORGOT_USERNAME);
                      }}
                      {...a11y('Login.btnNavigateToForgotUsername')}
                    >
                      <Text style={styles.forgotTitle} fontFamily="bold">
                        {t('login.page.forgot.username')}
                      </Text>
                    </TouchableOpacity>
                  </View>
                </View>
              </>
            ) : (
              <OldUserlogged
                username={userCache?.username}
                fullname={userCache?.fullname}
                firstName={userCache?.firstName}
                onPressNotMe={handlePressNotMe}
              />
            )}

            <View>
              <Controller
                control={control}
                rules={{
                  required: { value: true, message: t('login.page.error') },
                }}
                render={({ field: { onChange, value } }) => (
                  <TextInput
                    containerStyle={styles.mb_10}
                    label={t('login.page.password')}
                    type={errors?.password ? 'error' : 'info'}
                    disabled={false}
                    onChangeText={onChange}
                    value={value}
                    message={errors?.password?.message}
                    messageStyle={styles.messErrorFieldStyle}
                    secureTextEntry={isSecurePassword}
                    right={{ source: isSecurePassword ? 'Show' : 'Mask' }}
                    iconColor={GRAY}
                    onPressIcon={() => setSecurePassword(!isSecurePassword)}
                    id="Login.inputPassword"
                    textContentType="oneTimeCode"
                    autoComplete="off"
                  />
                )}
                name="password"
              />

              {!isEmpty(userCache) && (
                <View style={[styles.mt_16, styles.wrapperBtnGroup]}>
                  <Button
                    onPress={handleSubmit(handleLogin)}
                    text={t('login.page.login')}
                    type="primary"
                    uppercase={false}
                    labelStyle={styles.buttonText}
                    style={styles.button}
                    size="large"
                    id="Login.btnSubmitUsernameToLogin"
                    disabled={!isAgree}
                  />
                  {!disabledBiometric && (
                    <TouchableOpacity disabled={disabledBiometric} onPress={handleTouchId} {...a11y('Login.btnLoginByBiometric')}>
                      <View style={styles.biometryType}>
                        {biometryType === BIOMETRY_TYPE.FACE ? (
                          <Icon name="Face-ID" size={20} color="#D31145" />
                        ) : (
                          <Icon name="Fingerprint" size={20} color="#D31145" />
                        )}
                      </View>
                    </TouchableOpacity>
                  )}
                </View>
              )}

              <View style={!isEmpty(userCache) ? styles.wrapper : styles.asEnd}>
                <TouchableOpacity
                  onPress={() => {
                    setShowModal(false);
                    NativeModuleClient.AdobeExperience.trackButton({
                      screen: ROUTE_PATH.LOGIN,
                      unixTimestamp: dayjs().unix(),
                      buttonText: 'Forgot password',
                    });
                    if (!__DEV__) {
                      AzureNotificationClient.clearTappedNotification();
                    }
                    navigation.navigate(ROUTE_PATH.FORGOT_PASSWORD);
                  }}
                  {...a11y('Login.btnNavigateToForgotPassword')}
                >
                  <Text style={styles.forgotTitle} fontFamily="bold">
                    {t('login.page.forgot.password')}
                  </Text>
                </TouchableOpacity>
                {!isEmpty(userCache) && (
                  <TouchableOpacity hitSlop={HITSLOP} onPress={handlePressNotMe}>
                    <Text fontFamily="bold" style={{ color: colors.inputError }}>
                      {t('login.page.notMe')}
                    </Text>
                  </TouchableOpacity>
                )}
              </View>
            </View>
            <View style={styles.flex1} />
            {isEmpty(userCache) && (
              <View style={styles.parentCheckbox}>
                <Checkbox
                  checked={isAgree}
                  onPress={() => {
                    setIsAgree(!isAgree);
                  }}
                  size={24}
                  customStyleChild={styles.customCheckbox}
                  id="Login.loginCheckboxIsAgree"
                >
                  <Text style={[styles.text]}>
                    {t('login.checkbox.term1')}{' '}
                    <Text style={[styles.textLink, styles.textDecorationLine]} onPress={showWebviewTerms}>
                      {t('login.checkbox.term2')}
                    </Text>
                    <Text style={[styles.text]}> {t('login.checkbox.term3')} </Text>
                    <Text style={[styles.textLink, styles.textDecorationLine]} onPress={showWebviewConditions}>
                      {t('login.checkbox.term4')}
                    </Text>
                  </Text>
                </Checkbox>
              </View>
            )}

            {isEmpty(userCache) && (
              <View style={[styles.mt_16, styles.wrapperBtnGroup]}>
                <Button
                  onPress={handleSubmit(handleLogin)}
                  text={t('login.page.login')}
                  type="primary"
                  uppercase={false}
                  labelStyle={styles.buttonText}
                  style={styles.button}
                  size="large"
                  id="Login.btnSubmitUsernameToLogin"
                  disabled={!isAgree}
                />
                {!disabledBiometric && (
                  <TouchableOpacity disabled={disabledBiometric} onPress={handleTouchId} {...a11y('Login.btnLoginByBiometric')}>
                    <View style={styles.biometryType}>
                      {biometryType === BIOMETRY_TYPE.FACE ? (
                        <Icon name="Face-ID" size={20} color="#D31145" />
                      ) : (
                        <Icon name="Fingerprint" size={20} color="#D31145" />
                      )}
                    </View>
                  </TouchableOpacity>
                )}
              </View>
            )}

            {isEmpty(userCache) && (
              <View style={[styles.mt_22, styles.registerBox]}>
                <Text style={styles.registerTitle} fontFamily="bold">
                  {t('login.page.not.user')}
                </Text>
                <TouchableOpacity onPress={onPressRegister} {...a11y('Login.btnNavigateToRegister')}>
                  <Text style={styles.registerContent} fontFamily="bold">
                    {t('login.page.register')}
                  </Text>
                </TouchableOpacity>
              </View>
            )}
          </View>
        </KeyboardAvoidingView>
      </TouchableWithoutFeedback>
      <WebView ref={WebviewRef} />
    </View>
  );
};

export default LoginStep;
