import React, { useRef, useEffect, useState, useLayoutEffect } from 'react';
import { useNavigation, useRoute, RouteProp } from '@react-navigation/native';
import { View, TouchableOpacity, Image, SafeAreaView, StatusBar, BackHandler, ScrollView } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useSelector, useDispatch } from 'react-redux';
import { Icon, Button, theme, Text as TextCommon, NativeModuleClient } from 'superapp-common-component';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import NetInfo from '@react-native-community/netinfo';
import _get from 'lodash/get';
import dayjs from 'dayjs';

import { HEADER_TITLE_BGRWHITE, HITSLOP } from '@/utils/styles';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import ROUTE_PATH from '@/navigators/routePath';
import { a11y, maskedPhoneNumber } from '@/utils/utils';
import { TStackParamList } from '@/navigators';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import { makeSelectProfile } from '@/stores/global/selectors';
import { logout } from '@/stores/authentication/actions';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import MiniAppEventEmitter from '@/utils/miniAppEventListener';
import jsEventEmitter from '@/utils/jsEventEmitter';
import { EVENT_NAMES, JS_EVENT_NAMES, MINI_APP_EVENTS_EMITTER } from '@/utils/constants';

import { requestOtpBySmsAction } from './stores/actions';
import migrationSaga from './stores/saga';
import migrationReducer from './stores/reducer';
import { NS_MIGRATION_TYPE } from './stores/constants';
import MigrationOtp, { MigrationOtpRef } from './components/MigrationOtp';
import styles from './styles';
import ModalLogOut, { ModalLogOutRef } from '../Profile/components/ModalLogOut';

const { colors } = theme;

type MigrationNavigationProp = StackNavigationProp<TStackParamList, 'Migration'>;
type MigrationRouteProps = RouteProp<TStackParamList, 'Migration'>;

const Migration = () => {
  useInjectReducer({ key: NS_MIGRATION_TYPE, reducer: migrationReducer });
  useInjectSaga({ key: NS_MIGRATION_TYPE, saga: migrationSaga });
  const { t } = useTranslation();
  const profile = useSelector(makeSelectProfile());
  const netInfo = NetInfo.useNetInfo();
  const navigation = useNavigation<MigrationNavigationProp>();
  const route = useRoute<MigrationRouteProps>();
  const dispatch = useDispatch();
  const migrationOtpErrorRef = useRef<null | MigrationOtpRef>(null);
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const modalLogOutRef = useRef<null | ModalLogOutRef>(null);
  const loginStatus = useSelector(makeSelectCheckLogin());
  const { isHasMobile } = route.params;

  const handleLogout = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.MIGRATION,
      unixTimestamp: dayjs().unix(),
      buttonText:'Logout'
    });
    modalLogOutRef?.current?.open();
  };

  const goBack = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.MIGRATION,
      unixTimestamp: dayjs().unix(),
      buttonText:'Go Back'
    });
    dispatch(logout({ neededCallApi: true }));
    navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
    return true;
  };

  const handleEventListener = (payload: string = '') => {
    if (!loginStatus.isLoged) return;
    const parserPayload = JSON.parse(payload);

    if (parserPayload?.type) {
      switch (parserPayload.type) {
        case EVENT_NAMES.SESSION_EXPIRED:
          const routeState = navigation.getState();
          const routes = _get(routeState, 'routes', []);
          const screenName = routes[0]?.name;
          if (screenName === ROUTE_PATH.ONBOARDING) {
            navigation.reset({ index: 0, routes: [{ name: ROUTE_PATH.DASHBOARD }] } as never);
            setTimeout(() => {
              jsEventEmitter.emit(JS_EVENT_NAMES.SESSION_EXPIRED);
            });
          }
          break;
        default:
          break;
      }
    }
  };

  // const handleOnConfirm = () => {
  //   const params = {
  //     otpType: 'ChangeUsername',
  //     otpSendType: 'SMS',
  //   };
  //   migrationOtpErrorRef?.current?.setVisibleModal(true);
  //   setTimeout(() => {
  //     dispatch(requestOtpBySmsAction({ params }));
  //   }, 500);
  // };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitleAlign: 'center',
      headerTitle: () => <TextCommon.Bold style={HEADER_TITLE_BGRWHITE}>{t('migration.change.username.title')}</TextCommon.Bold>,
      headerLeft: () => (
        <TouchableOpacity style={styles.marginLeft16} onPress={goBack} hitSlop={HITSLOP} {...a11y('migration.iconBackHeaderLeft')}>
          <Icon name="Arrow-Left" size={24} color={colors.primary.charcoal[80]} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    const backHandler = BackHandler.addEventListener('hardwareBackPress', goBack);
    return () => backHandler.remove();
  }, []);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useEffect(() => {
    const subscription = MiniAppEventEmitter?.addListener(MINI_APP_EVENTS_EMITTER.MINI_APP_EVENT_EMITTER, handleEventListener);
    return () => subscription?.remove();
  }, [loginStatus.isLoged]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <StatusBar barStyle="dark-content" />
      <ScrollView contentContainerStyle={styles.grow}>
        <View style={styles.container}>
          <View style={styles.contentChangeUsername}>
            <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/logo-change-username.png')} />
            {!isHasMobile ? (
              <>
                <Text style={styles.textTitleChangeUser}>{t('migration.change.username.des1')}</Text>
                <Text style={styles.textDescriptionChangeUser}>{t('migration.change.username.des4')}</Text>
                <Text style={styles.textSubDescriptionChangeUser}>{t('migration.change.username.des5')}</Text>
              </>
            ) : (
              <>
                <Text style={styles.textTitleChangeUser}>{t('migration.change.username.phone', { mobile: profile?.mobile })}</Text>
                <Text style={styles.textDescriptionChangeUser}>
                  {t('migration.change.username.des3', { phone: maskedPhoneNumber(profile?.mobile!) })}
                </Text>
                <Text style={styles.textSubDescriptionChangeUser}>{t('migration.change.username.des2')}</Text>
              </>
            )}
          </View>
          <View style={[styles.buttonSuccessWrapper]}>
            {!isHasMobile ? (
              <Button
                text={t('profile.account.logout')}
                onPress={handleLogout}
                style={[styles.marginBottom10]}
                uppercase={false}
                labelStyle={styles.buttonText}
                id="Migration.btnSubmit"
              />
            ) : (
              <View style={styles.wrapperButton}>
                <Button
                  text={t('migration.change.username.button.continue')}
                  onPress={() => {
                    NativeModuleClient.AdobeExperience.trackButton({
                      screen: ROUTE_PATH.MIGRATION,
                      unixTimestamp: dayjs().unix(),
                      buttonText:'Confirm'
                    });
                    migrationOtpErrorRef?.current?.setVisibleModal(true);
                  }}
                  style={[styles.marginBottom16]}
                  uppercase={false}
                  labelStyle={styles.buttonText}
                  id="Migration.btnSubmit"
                />
                <Button
                  text={t('profile.account.logout')}
                  onPress={handleLogout}
                  style={[styles.marginBottom10]}
                  uppercase={false}
                  labelStyle={styles.buttonText}
                  type="secondary"
                  id="Migration.btnCancel"
                />
              </View>
            )}
          </View>
          <MigrationOtp ref={migrationOtpErrorRef} />
        </View>
      </ScrollView>
      <ModalLogOut ref={modalLogOutRef} />
    </SafeAreaView>
  ) : (
    <SafeAreaView style={styles.flex1}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default Migration;
