import React from 'react';
import { Text, TextStyle, Dimensions, StyleProp } from 'react-native';
import { useTheme, ThemePaperType } from 'superapp-common-component';
import _get from 'lodash/get';
import {XL_VIEW_PORT} from '@/utils/styles'

interface CustomTextProps {
  style?: object;
  fontFamily?: string;
  [key: string]: any;
  xl?: StyleProp<TextStyle>
}

const CustomText: React.FC<CustomTextProps> = props => {
  const { style, children, fontFamily = 'regular', xl, ...restProps } = props;
  const theme = useTheme() as ThemePaperType;
  const { width } = Dimensions.get('window');
  return (
    <Text {...restProps} style={[
      _get(theme.fonts, fontFamily), 
      { color: theme.colors.CHARCOAL }, 
      style,
      width > XL_VIEW_PORT ? xl : {}
    ]}>
      {children}
    </Text>
  );
};

export default CustomText;
