import React, { useLayoutEffect, useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { SafeAreaView, TouchableOpacity, View, ScrollView } from 'react-native';
import { Icon, Checkbox, Button } from 'superapp-common-component';
import NetInfo from '@react-native-community/netinfo';

import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import { useNavigation } from '@react-navigation/native';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import Text from '@/components/Text';
import { a11y } from '@/utils/utils';
import NoConnection from '@/components/NoConnection';

import styles from './styles';

type MarketingMethodNavigationProp = StackNavigationProp<TStackParamList, 'MarketingMethod'>;
const MarketingMethod = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<MarketingMethodNavigationProp>();
  const netInfo = NetInfo.useNetInfo();
  const [smsCheck, setSmsCheck] = useState(false);
  const [emailCheck, setEmailCheck] = useState(false);
  const [letterCheck, setLetterCheck] = useState(false);
  const [offerCheck, setOfferCheck] = useState(false);
  const [agreePolicy, setAgreePolicy] = useState(false);
  const [isConnected, setConnected] = useState<null | boolean>(null);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('marketing.method.purpose.main.header')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('Marketing.btnHeaderGoback')}
          style={styles.marginLeft16}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" color="#ffffff" size={24} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <ScrollView style={{ flex: 1 }}>
        <View style={styles.container}>
          <Text style={styles.mainTitle}>{t('marketing.method.purpose.main.title')}</Text>
          <Text style={styles.subTitle}>{t('marketing.method.purpose.sub.title')}</Text>
          <View style={styles.divider} />

          <Text style={styles.question} fontFamily="bold">
            {t('marketing.method.purpose.question.title')}
          </Text>

          <View style={styles.methodContainer}>
            <View style={styles.topMethod}>
              <View style={styles.checkBoxMethod}>
                <Checkbox id="Marketing.btnCheckboxSms" color="#1F78AD" checked={smsCheck} onPress={() => setSmsCheck(!smsCheck)} />
              </View>
              <View style={styles.checkBoxTextContainer}>
                <Text style={styles.methodText}>{t('marketing.method.purpose.method.sms')}</Text>
                <Text style={styles.methodDes}>Description text</Text>
              </View>
            </View>
          </View>

          <View style={styles.methodContainer}>
            <View style={styles.topMethod}>
              <View style={styles.checkBoxMethod}>
                <Checkbox id="Marketing.btnCheckboxEmail" color="#1F78AD" checked={emailCheck} onPress={() => setEmailCheck(!emailCheck)} />
              </View>
              <View style={styles.checkBoxTextContainer}>
                <Text style={styles.methodText}>{t('marketing.method.purpose.method.email')}</Text>
                <Text style={styles.methodDes}>Description text</Text>
              </View>
            </View>
          </View>
          <View style={styles.methodContainer}>
            <View style={styles.topMethod}>
              <View style={styles.checkBoxMethod}>
                <Checkbox
                  id="Marketing.btnCheckboxLetter"
                  color="#1F78AD"
                  checked={letterCheck}
                  onPress={() => setLetterCheck(!letterCheck)}
                />
              </View>
              <View style={styles.checkBoxTextContainer}>
                <Text style={styles.methodText}>{t('marketing.method.purpose.method.letter')}</Text>
                <Text style={styles.methodDes}>Description text</Text>
              </View>
            </View>
          </View>
          <View style={[styles.methodContainer, { marginBottom: 40 }]}>
            <View style={styles.topMethod}>
              <View style={styles.checkBoxMethod}>
                <Checkbox id="Marketing.btnCheckboxOffer" color="#1F78AD" checked={offerCheck} onPress={() => setOfferCheck(!offerCheck)} />
              </View>
              <View style={styles.checkBoxTextContainer}>
                <Text style={styles.methodText}>{t('marketing.method.purpose.method.offer')}</Text>
                <Text style={styles.methodDes}>Description text</Text>
              </View>
            </View>
          </View>
          <View style={styles.checkBoxContainer}>
            <View style={styles.checkBox}>
              <Checkbox
                id="Marketing.btnCheckboxPolicy"
                color="#1F78AD"
                checked={agreePolicy}
                onPress={() => setAgreePolicy(!agreePolicy)}
              />
            </View>
            <Text style={styles.checkBoxTitle}>{t('marketing.method.purpose.checkbox.title')}</Text>
          </View>
          <View style={[styles.marginBottom30]}>
            <Button
              text={t('marketing.method.purpose.button')}
              onPress={() => {}}
              uppercase={false}
              type="primary"
              labelStyle={styles.buttonText}
              size="large"
              style={styles.button}
              id="Marketing.btnSubmit"
            />
          </View>
        </View>
      </ScrollView>
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default MarketingMethod;
