import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Image, SafeAreaView, View, Linking, ImageBackground, Dimensions } from 'react-native';
import { Button, Text } from 'superapp-common-component';

import useAppUpdate from '@/clients/hooks/useAppUpdate';

import styles from './styles';

const imgSource = require('@/assets/images/appUpdate.png');

interface AppUpdateProps {
  url?: string;
  isForceUpdate: boolean;
  onClose: () => void;
}

const AppUpdate: React.FC<AppUpdateProps> = props => {
  const { t } = useTranslation();

  const [imageSize, setImageSize] = useState({
    width: 0,
    height: 0,
  });

  const handleResizeImage = () => {
    const imageDetail = Image.resolveAssetSource(imgSource);
    const aspectRatio = imageDetail.width / imageDetail.height;
    const width = Math.min(Dimensions.get('window').width, imageDetail.width) * 0.8;
    const height = width / aspectRatio;
    setImageSize({ width, height });
  };

  const handleUpdateNow = () => {
    if (props?.url) {
      Linking.openURL(props.url);
    }
  };

  useEffect(() => {
    handleResizeImage();
  }, []);

  return (
    <View style={styles.safe}>
      <SafeAreaView />
      <ImageBackground style={styles.container} source={require('@/assets/images/backgroundUpdate.png')}>
        <View style={styles.content}>
          <Image
            style={[styles.img, { width: imageSize.width, height: imageSize.height }]}
            source={require('@/assets/images/appUpdate.png')}
          />
          <Text style={styles.title}>{t('common.appUpdate.title')}</Text>
          <Text style={styles.description}>{t('common.appUpdate.description')}</Text>
        </View>
        <View>
          <Button style={styles.btn} text={t('common.appUpdate.updateNow')} onPress={handleUpdateNow} />
          {!props?.isForceUpdate && (
            <Button style={[styles.btn, styles.btnLater]} text={t('common.appUpdate.later')} type="secondary" onPress={props.onClose} />
          )}
        </View>
      </ImageBackground>
      <SafeAreaView style={styles.safeBottom} />
    </View>
  );
};

export default AppUpdate;
