import React, { useEffect, useRef, useState, useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { AppState, View, Switch, TouchableOpacity } from 'react-native';
import { Icon, RadioButton, theme, NativeModuleClient } from 'superapp-common-component';
import { useIsFocused, useNavigation } from '@react-navigation/native';
import TouchId from 'react-native-touch-id-passcode';
import { useSelector } from 'react-redux';

import AzureNotificationClient, { AUTHORIZATION_STATUS, GENERAL_TAG } from '@/clients/AzureNotification';

import { setItem, getItem } from '@/utils/storage';
import { parseString, a11y, getAndroidTrackColor, getAndroidThumbColor } from '@/utils/utils';
import { STORAGE_NAME, BIOMETRY_TYPE } from '@/utils/constants';
import ActiveBioModal, { ActiveBioModalRef } from '@/components/ActiveBioModal';
import ModalConfirmLanguage, { ModalConfirmLanguageRef } from '@/components/ModalConfirmLanguage';
import ModalAllowAccess, { ModalAllowAccessRef } from '@/components/ModalAllowAccess';
import ROUTE_PATH from '@/navigators/routePath';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import Text from '@/components/Text';

import styles from './styles';
import dayjs from 'dayjs';
const { colors } = theme;

const hasAllowPushNotification = async () => {
  if (__DEV__) return true;

  const [authorizationStatus, isHasGeneralTag] = await Promise.all([
    AzureNotificationClient.hasPermission(),
    AzureNotificationClient.hasGeneralTag(),
  ]);
  const isGranted = authorizationStatus === AUTHORIZATION_STATUS.AUTHORIZED;
  return isGranted && isHasGeneralTag;
};

type AccountSettingProps = {
  setLoading?: (value: boolean) => void;
};

const AccountSetting: React.FC<AccountSettingProps> = props => {
  const navigation = useNavigation<any>();
  const { t, i18n } = useTranslation();
  const isFocused = useIsFocused();
  const activeBioModalRef = useRef<null | ActiveBioModalRef>(null);
  const enableBioModalRef = useRef<null | ActiveBioModalRef>(null);
  const modalConfirmLanguageRef = useRef<null | ModalConfirmLanguageRef>(null);
  const [isActiveBio, setIsActiveBio] = useState(false);
  const [language, setLanguage] = useState('');
  const [biometryType, setBiometryType] = useState<null | string>(null);
  const loginStatus = useSelector(makeSelectCheckLogin());
  const [isAllowPushNotification, setIsAllowPushNotification] = useState(false);
  const modalAllowAccessRef = useRef<ModalAllowAccessRef>();

  const handleBioOff = async () => {
    await setItem(STORAGE_NAME.TOUCH_FACE_ID, parseString(false));
    setIsActiveBio(false);
  };

  const toggleSwitchFaceID = async () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Toggle FaceID',
    });
    if (isActiveBio) {
      enableBioModalRef?.current?.open();
    } else {
      activeBioModalRef?.current?.open();
    }
  };

  const showPasswordModal = () => {
    navigation.navigate(ROUTE_PATH.VERIFY_PASSWORD, {
      title:
        biometryType === BIOMETRY_TYPE.FACE
          ? t('profile.input.password.modal.name.faceid')
          : t('profile.input.password.modal.name.touchid'),
      messageContent:
        biometryType === BIOMETRY_TYPE.FACE
          ? t('profile.input.password.modal.sub.title.faceid')
          : t('profile.input.password.modal.sub.title.touchid'),
      toActiveFaceId: true,
    });
  };

  const showPasswordConfirmCreatePin = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Create/Change PIN',
    });
    navigation.navigate(ROUTE_PATH.VERIFY_PASSWORD, {
      title: t('profile.input.password.modal.createpin.title'),
      messageContent: t('profile.input.password.modal.pincode.sub.title'),
      toActiveFaceId: false,
    });
  };

  const showChangePassword = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Change Password',
    });
    navigation.navigate(ROUTE_PATH.CHANGE_PASSWORD, { preLogin: false });
  }

  const showModalConfirmLanguage = (newLang: string) => {
    if (language === newLang) return;
    modalConfirmLanguageRef?.current?.open();
  };

  const onChangeLanguage = () => {
    const lang = language === 'en' ? 'vi' : 'en';
    i18n.changeLanguage(lang);
    setItem(STORAGE_NAME.LANGUAGE, JSON.stringify(lang));
    NativeModuleClient.AdobeExperience.setLanguage(lang);
    setLanguage(lang);
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: `Change Language To ${lang}`,
    });
  };

  const handleSwitchNotification = async () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: `Switch Notification`,
    });
    let timeoutId: number = 0;
    try {
      // prevent tap too fast
      if (props.setLoading) {
        timeoutId = setTimeout(() => {
          props.setLoading && props.setLoading(false);
        }, 1000) as any;
      }

      const value = !isAllowPushNotification;
      if (value) {
        const authorizationStatus = await AzureNotificationClient.hasPermission();
        // deny permission push notification
        if (authorizationStatus === AUTHORIZATION_STATUS.DENIED) {
          // prevent duplicate modal
          clearTimeout(timeoutId);
          setIsAllowPushNotification(false);
          // open setting
          modalAllowAccessRef.current?.open({ moduletype: 'notifications' });
          return;
        }
        props.setLoading && props.setLoading(true);

        (async () => {
          // add GENERAL tag
          const newTags = [GENERAL_TAG];
          const token = await getItem(STORAGE_NAME.ACCESS_TOKEN);
          // add user tag
          if (token) {
            const userId = AzureNotificationClient.getUserId(token); 
            if (userId) {
              newTags.push(userId);
              AzureNotificationClient.addCurrentUserTagService(userId);
            }
          }
          AzureNotificationClient.addTags(newTags);
        })();
      } else {
        props.setLoading && props.setLoading(true);
        await AzureNotificationClient.clearTags();
      }
      setIsAllowPushNotification(value);
    } catch (err) {
      if (props.setLoading) {
        clearTimeout(timeoutId);
        props.setLoading(false);
      }
      console.error('Only run on release');
    }
  };

  const onAppState = useCallback(() => {
    const subscription = AppState.addEventListener('change', async nextAppState => {
      if (nextAppState === 'active') {
        const isHasAllowPushNotification = await hasAllowPushNotification();
        setIsAllowPushNotification(isHasAllowPushNotification);
      }
    });

    return () => subscription.remove();
  }, []);

  useEffect(() => {
    (async () => {
      const [isActive, lang, isHasAllowPushNotification] = await Promise.all([
        getItem<boolean>(STORAGE_NAME.TOUCH_FACE_ID, false),
        getItem<string>(STORAGE_NAME.LANGUAGE, ''),
        hasAllowPushNotification(),
      ]);
      setIsActiveBio(isActive);
      setLanguage(lang);
      setIsAllowPushNotification(isHasAllowPushNotification);
      try {
        const biometryTypeRes = await TouchId.isSupported();
        setBiometryType(biometryTypeRes);
      } catch (error) {
        setBiometryType(null);
      }
    })();

    const unsubscribeAppState = onAppState();
    return () => {
      unsubscribeAppState();
    };
  }, []);

  useEffect(() => {
    (async () => {
      const isActive = await getItem<boolean>(STORAGE_NAME.TOUCH_FACE_ID, false);
      setIsActiveBio(isActive);

      if (isFocused) {
        const isHasAllowPushNotification = await hasAllowPushNotification();
        setIsAllowPushNotification(isHasAllowPushNotification);
      }
    })();
  }, [isFocused]);

  return (
    <View style={styles.container}>
      <Text style={styles.textCardTitle} xl={{ fontWeight: '600', fontSize: 16, lineHeight: 24 }}>
        {t('profile.account.setting')}
      </Text>

      <View style={styles.wrapper}>
        <View style={[styles.row, styles.wrapperRow]}>
          <View style={styles.row}>
            <Icon name="Globe" color={colors.primary.charcoal[80]} style={styles.icon} size={20} />
            <Text style={styles.textTitle}>{t('profile.account.setting.language')}</Text>
          </View>
          <View style={styles.row}>
            <View style={{ flexDirection: 'row', justifyContent: 'flex-end' }}>
              <RadioButton
                id="Profile.rbLanguageVn"
                value="vi"
                label="VN"
                color={colors.primary.blue[100]}
                labelStyles={styles.textLanguage}
                onPress={() => showModalConfirmLanguage('vi')}
                selected={language === 'vi'}
              />
              <RadioButton
                id="Profile.rbLanguageEn"
                value="en"
                label="ENG"
                color={colors.primary.blue[100]}
                labelStyles={[styles.textLanguage, { paddingRight: 0 }]}
                onPress={() => showModalConfirmLanguage('en')}
                selected={language === 'en'}
              />
            </View>
          </View>
        </View>

        <View style={[styles.row, styles.wrapperRow]}>
          <View style={styles.row}>
            <Icon name="Notification1" color={colors.primary.charcoal[80]} style={styles.icon} size={20} />
            <Text style={styles.textTitle}>{t('profile.account.setting.notification')}</Text>
          </View>
          <View style={styles.row}>
            <Switch
              {...a11y('Profile.swSettingNoti')}
              style={styles.switch}
              value={isAllowPushNotification}
              onChange={handleSwitchNotification}
              trackColor={getAndroidTrackColor()}
              thumbColor={getAndroidThumbColor(isAllowPushNotification)}
            />
          </View>
        </View>

        {biometryType && loginStatus.isLoged && (
          <View style={[styles.row, styles.wrapperRow]}>
            <View style={styles.row}>
              <Icon name="Face-ID" color={colors.primary.charcoal[80]} style={styles.icon} size={20} />
              <Text style={styles.textTitle}>{t('profile.account.setting.faceId')}</Text>
            </View>
            <View style={styles.row}>
              <Switch
                {...a11y('Profile.swSettingBio')}
                style={styles.switch}
                value={isActiveBio}
                onChange={toggleSwitchFaceID}
                trackColor={getAndroidTrackColor()}
                thumbColor={getAndroidThumbColor(isActiveBio)}
              />
            </View>
          </View>
        )}

        {loginStatus.isLoged && (
          <TouchableOpacity
            style={[styles.row, styles.wrapperRow]}
            {...a11y('Profile.btnPasswordConfirmCreatePin')}
            onPress={showPasswordConfirmCreatePin}
          >
            <View style={styles.row}>
              <Icon name="Lock1" color={colors.primary.charcoal[80]} style={styles.icon} size={20} />
              <Text style={styles.textTitle}>{t('profile.account.setting.pin')}</Text>
            </View>
            <View style={styles.row}>
              <Icon name="Arrow-Right" color={colors.primary.charcoal[100]} size={20} />
            </View>
          </TouchableOpacity>
        )}

        {loginStatus.isLoged && (
          <TouchableOpacity
            style={[styles.row, styles.wrapperRow]}
            {...a11y('Profile.btnShowChangePass')}
            onPress={showChangePassword}
          >
            <View style={styles.row}>
              <Icon name="Password-1" color={colors.primary.charcoal[80]} style={styles.icon} />
              <Text style={styles.textTitle}>{t('profile.account.setting.password')}</Text>
            </View>
            <View style={styles.row}>
              <Icon name="Arrow-Right" color={colors.primary.charcoal[100]} size={20} />
            </View>
          </TouchableOpacity>
        )}
      </View>

      <ActiveBioModal showPasswordModal={showPasswordModal} ref={activeBioModalRef} biometryType={biometryType} onMode={true} />
      <ActiveBioModal
        showPasswordModal={showPasswordModal}
        ref={enableBioModalRef}
        biometryType={biometryType}
        onMode={false}
        handleBioOff={handleBioOff}
      />
      <ModalConfirmLanguage ref={modalConfirmLanguageRef} language={language} onChangeLanguage={onChangeLanguage} />
      <ModalAllowAccess
        ref={modalAllowAccessRef}
        notificationProps={{
          title: t('profile.account.setting.notification.title'),
          description: t('profile.account.setting.notification.description'),
          contentStep1: t('profile.account.setting.notification.step1'),
          contentStep3: t('profile.account.setting.notification.step2'),
          contentStep2: t('profile.account.setting.notification.step3'),
          textPrimaryButton: t('profile.account.setting.notification.setting'),
          textSecondaryButton: t('profile.account.setting.notification.cancel'),
        }}
        idPrimaryButton="profile.account.setting.settingPermission"
        idSecondaryButton="profile.account.setting.cancelPermission"
      />
    </View>
  );
};

export default AccountSetting;
