import React, { useEffect, useRef } from 'react';
import { Image, Animated, TouchableOpacity, View, ScrollView, ImageBackground } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useTheme, Icon, NativeModuleClient } from 'superapp-common-component';
import { useSelector } from 'react-redux';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import _orderBy from 'lodash/orderBy';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import FlipCard from '@/components/FlipCard';
import { Theme } from '@/config/themes.config';
import { a11y, getImageByProductCode, getItemCoverageNumber1, isEmpty } from '@/utils/utils';
import { makeSelectPolicies, makeSelectPolicyLoadingSkeleton, makeSelectProductImage, makeSelectProfile } from '@/stores/global/selectors';
import { APPS_NAME, FORMAT_DATE, FUNCTION_TYPES, POLICY_STATUS_CODE, VALID_ROLES } from '@/utils/constants';
import APP_ENV from '@/config/app.config';
import ROUTE_PATH from '@/navigators/routePath';
import { HITSLOP, HITSLOP_48 } from '@/utils/styles';

import styles, { CARD_WIDTH, POLICY_ITEM_MARGIN_RIGHT } from './styles';
import { ModalPermissionRef } from '@/components/ModalPermission';

interface PolicyProps {
  isLoged: boolean;
  isConnected: boolean;
  setLoadingOpenMiniApp: (value: boolean) => void;
  isRunningMiniApp: React.MutableRefObject<boolean>;
  permissionModalRef?: React.MutableRefObject<ModalPermissionRef | null | undefined>;
}

const POLICY_NAME = {
  'home.policy.status.in.progress': ['10', '11', '19'],
  'home.policy.status.closed': ['12', '13', '14', '15', '16', '17'],
  'home.policy.status.active': ['20'],
  'home.policy.status.active.free': ['23', '24'],
  'home.policy.status.active.no.charge': ['30'],
  'home.policy.status.active.automatic.payment': ['45'],
  'home.policy.status.inactive': ['40', '41', '42', '43', '44', '47', '48', '50', '99'],
};

const Policy: React.FC<PolicyProps> = ({ isLoged, isConnected, setLoadingOpenMiniApp, isRunningMiniApp, permissionModalRef }) => {
  const { colors } = useTheme() as Theme;
  const { t } = useTranslation();
  const animationPolicy = useRef(new Animated.Value(0)).current;
  const policies = useSelector(makeSelectPolicies());
  const isLoading = useSelector(makeSelectPolicyLoadingSkeleton());
  const productImage = useSelector(makeSelectProductImage());
  const profile = useSelector(makeSelectProfile());

  const openMiniApp = async (appName: string, policyNumber?: string) => {
    try {
      setLoadingOpenMiniApp(true);
      await NativeModuleClient.MiniApp.runCheckUpdate(appName);
      await NativeModuleClient.MiniApp.loadApp(appName, JSON.stringify({ policyNumber }));
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', appName);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const checkPolicyStatus = (item?: MODEL.IPolicy) => {
    const status = item?.policyStatus?.code;

    if (!status) return;

    if (status === POLICY_STATUS_CODE.AUTO_FEE_REFUND) {
      return item?.isActive ? styles.activeText : styles.deletedText;
    }

    if (POLICY_STATUS_CODE.ACTIVE.includes(status)) {
      return styles.activeText;
    }
    if (POLICY_STATUS_CODE.POSTPONE.includes(status)) {
      return styles.postponeText;
    }
    if (POLICY_STATUS_CODE.DELETED.includes(status)) {
      return styles.deletedText;
    }
  };

  const checkPolicyStatusName = (item?: MODEL.IPolicy): string => {
    const status = item?.policyStatus?.code;

    if (!status) return '';

    if (status === POLICY_STATUS_CODE.AUTO_FEE_REFUND) {
      return item?.isActive ? t('home.policy.status.active.automatic.payment') : t('home.policy.status.inactive');
    }

    const keyOfStatusName = Object.keys(POLICY_NAME).find(key => POLICY_NAME?.[key as keyof typeof POLICY_NAME]?.includes(status));

    return t(keyOfStatusName || '');
  };

  const filterPolicyList = (policyList?: MODEL.IPolicy[]) => {
    if (policyList) {
      return _orderBy(policyList, ['policyStatus.code', 'submissionDate', 'policyNumber'], ['asc', 'desc', 'asc']);
    }
  };

  const handleSelectPolicy = (policyNumber?: string) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.HOME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Tap here to see policy detail',
    });
    if (permissionModalRef?.current?.handlePermission(FUNCTION_TYPES.POLICY)) {
      return;
    }
    openMiniApp(APPS_NAME.POLICY, policyNumber);
  };

  const renderPolicies = () => {
    if (policies && policies.length > 0) {
      return policies?.slice(0, 5).map((item: MODEL.IPolicy, index) => {
        const coverageNumber1: MODEL.ICoverage | undefined = getItemCoverageNumber1(item?.coverage);
        const listProductCodeByImage: { productCodes: string; image: string }[] =
          productImage?.items.map(codeItem => ({ productCodes: codeItem.productCodes?.value, image: codeItem?.image?.value })) || [];
        const policyImageUrl: string | undefined = getImageByProductCode(coverageNumber1?.productCode || '', listProductCodeByImage);

        return (
          <FlipCard
            key={item.policyId + index}
            viewBack={
              <View style={[styles.policyItem]} key={item?.policyId}>
                <View style={styles.headerPolicyItem}>
                  <View style={[styles.topContentContainer]}>
                    <View style={[styles.topContent]}>
                      <Text style={styles.namePolicy} xl={{ fontSize: 14, lineHeight: 20 }} fontFamily="bold" numberOfLines={2}>
                        {coverageNumber1?.name || coverageNumber1?.shortName}
                      </Text>
                      <Text style={styles.textIdPolicyItem} xl={{ fontSize: 14, lineHeight: 20, fontWeight: '400' }} fontFamily="bold">
                        {item?.policyNumber}
                      </Text>
                    </View>
                    {policyImageUrl ? (
                      <View style={styles.wrapItemPolicyImage}>
                        <Image
                          source={{
                            uri: `${APP_ENV.CONTENT_AEM}${policyImageUrl}`,
                          }}
                          style={styles.imagePolicyItem}
                        />
                      </View>
                    ) : (
                      <View style={styles.wrapItemPolicyImage}>
                        <Image source={require('@/assets/images/saving.png')} style={styles.imagePolicyItem} />
                      </View>
                    )}
                  </View>
                </View>

                <View style={styles.bodyPolicyItem}>
                  <View style={[styles.infoWrapper, styles.mb12]}>
                    <Text style={[styles.flex1, styles.policyLabel, styles.mr8]} xl={{ fontSize: 14, lineHeight: 20 }}>
                      {t('home.policy.status')}
                    </Text>
                    <Text
                      style={[styles.flex1, styles.ml8, checkPolicyStatus(item)]}
                      xl={{ fontSize: 14, lineHeight: 20, fontWeight: '500' }}
                      numberOfLines={2}
                    >
                      {checkPolicyStatusName(item)}
                    </Text>
                  </View>
                  <View style={[styles.infoWrapper, styles.mb12]}>
                    <Text style={[styles.flex1, styles.policyLabel, styles.mr8]} xl={{ fontSize: 14, lineHeight: 20 }}>
                      {t('home.policy.owner')}
                    </Text>
                    <Text
                      style={[styles.flex1, styles.policyValue, styles.ml8]}
                      xl={{ fontSize: 14, lineHeight: 20, fontWeight: '500' }}
                      numberOfLines={1}
                    >
                      {item?.policyOwner?.fullName}
                    </Text>
                  </View>
                  <View style={styles.infoWrapper}>
                    <Text style={[styles.flex1, styles.policyLabel, styles.mr8]} xl={{ fontSize: 14, lineHeight: 20 }}>
                      {t('home.policy.insured')}
                    </Text>
                    <Text
                      style={[styles.flex1, styles.policyValue, styles.ml8]}
                      xl={{ fontSize: 14, lineHeight: 20, fontWeight: '500' }}
                      numberOfLines={1}
                    >
                      {item?.insured?.fullName}
                    </Text>
                  </View>
                </View>

                <View style={styles.bottomPolicyItem}>
                  <TouchableOpacity
                    style={styles.touchBottomPolicyItem}
                    hitSlop={HITSLOP}
                    onPress={() => handleSelectPolicy(item?.policyNumber)}
                    {...a11y(`Home.iconBackSeeDetailPolicies${item?.policyId}`)}
                  >
                    <Text style={styles.textTouchBottom} xl={{ fontSize: 14 }}>
                      {t('home.policy.tap.action.text')}
                    </Text>
                    <Icon name="Next" size={20} color={colors.primary} />
                  </TouchableOpacity>
                </View>
              </View>
            }
            viewFront={
              <ImageBackground
                source={require('@/assets/images/background-policy.png')}
                resizeMode="cover"
                style={[styles.policyItem, styles.borderPrimary]}
              >
                <View style={styles.headerPolicyItemBack}>
                  <Text style={styles.namePolicyBack} xl={{ fontSize: 14, lineHeight: 20 }} fontFamily="bold">
                    {coverageNumber1?.name || coverageNumber1?.shortName}
                  </Text>
                  <Image source={require('@/assets/images/icon-aia-policy.png')} style={styles.imagePolicyItemBack} />
                </View>

                <View>
                  <Text style={styles.ownerBack} xl={{ fontSize: 14, lineHeight: 20 }}>
                    {item?.insured?.fullName}
                  </Text>
                  <View style={styles.bottomPolicyBack}>
                    <Text style={styles.policyNoId} xl={{ fontSize: 14, lineHeight: 20 }}>
                      {t('home.policy.policy.no')}
                      {item?.policyNumber}
                    </Text>
                    <TouchableOpacity
                      {...a11y(`Home.iconFrontSeeDetailPolicies${item?.policyId}`)}
                      hitSlop={HITSLOP_48}
                      onPress={() => handleSelectPolicy(item?.policyNumber)}
                    >
                      <Icon name="Next" size={20} color={colors.primary} />
                    </TouchableOpacity>
                  </View>
                </View>
              </ImageBackground>
            }
          />
        );
      });
    }
  };

  const renderSkeletionPolicies = () => {
    return (
      <SkeletonPlaceholder>
        <View style={styles.rowDirection}>
          <View style={styles.policyItemSkeleton}>
            <View style={styles.rowDirection}>
              <View style={styles.headleftSkeleton} />
              <View style={styles.headRightSkeleton} />
            </View>
            <View>
              <View style={styles.mb40} />
              <View>
                <View style={[styles.rowDirection, styles.mb12]}>
                  <View style={styles.bottomLeftPolicySkeleton} />
                  <View style={styles.bottomRightPolicySkeleton} />
                </View>

                <View style={[styles.rowDirection, styles.mb12]}>
                  <View style={styles.bottomLeftPolicySkeleton} />
                  <View style={styles.bottomRightPolicySkeleton} />
                </View>
              </View>
            </View>
          </View>
        </View>
      </SkeletonPlaceholder>
    );
  };

  useEffect(() => {
    if (!isEmpty(policies)) {
      Animated.spring(animationPolicy, { toValue: 1, useNativeDriver: false }).start();
    }
  }, [policies]);

  if (!isConnected && isEmpty(policies)) return null;
  if (isEmpty(profile) || !profile?.roles.some(role => VALID_ROLES.POLICY.includes(role)) || isEmpty(policies)) return null;

  return isLoading && isEmpty(policies) ? (
    <View>{renderSkeletionPolicies()}</View>
  ) : isLoged && policies && policies?.length > 0 ? (
    <Animated.View style={[styles.container, { opacity: animationPolicy }]}>
      <View style={[styles.mb16, styles.headerComponent]}>
        <Text style={styles.itemTitle} fontFamily="bold">
          {t('home.policy.title')} {`(${policies?.length})`}
        </Text>
        <TouchableOpacity onPress={() => handleSelectPolicy()} style={styles.itemHeaderRight} {...a11y('Home.iconSeeAllPolicies')}>
          <Text fontFamily="bold" style={styles.textItemHeaderRight} xl={{ fontSize: 14 }}>
            {t('home.seeAll')}
          </Text>
          {/* <Icon name="Arrow-Right" color={colors.primary} size={20} /> */}
        </TouchableOpacity>
      </View>
      <ScrollView
        contentContainerStyle={styles.scrollViewContentContainer}
        disableIntervalMomentum={true}
        decelerationRate={0}
        snapToInterval={CARD_WIDTH + POLICY_ITEM_MARGIN_RIGHT}
        showsHorizontalScrollIndicator={false}
        {...a11y('Home.sliderPolicies')}
        horizontal
      >
        {renderPolicies()}
      </ScrollView>
    </Animated.View>
  ) : null;
};

export default Policy;
