import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  PUT_CAPTCHA_ERROR,
  PUT_CAPTCHA_REQUEST,
  PUT_CAPTCHA_SUCCESS,
  PUT_OTP_ERROR,
  PUT_OTP_REQUEST,
  PUT_OTP_SUCCESS,
  RESET_CAPTCHA_ERROR,
  RESET_OTP_ERROR,
  SAVE_CAPCHA,
  SAVE_USER_ID,
  SAVE_SESSION_ID,
  SAVE_PHONE_NUMBER,
  SAVE_USERNAME,
  SAVE_USER_INFO_SUBMIT,
  SAVE_OTP_VALUE,
  SET_IS_COUNT_FROM_SCREEN_INFO,
  LOADING_SUCCESS_FORGOT_PASSWORD,
  LOADING_REQUEST_FORGOT_PASSWORD,
} from './constants';
import initialState, { TFotgotPasswordTypeState } from './state';

const requestTypeReducer = (state: TFotgotPasswordTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TFotgotPasswordTypeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case LOADING_REQUEST_FORGOT_PASSWORD:
        draft.loadingForgotPassword = true;
        break;
      case LOADING_SUCCESS_FORGOT_PASSWORD:
        draft.loadingForgotPassword = false;
        break;
      case SAVE_CAPCHA:
        draft.infoCapcha = action.payload;
        break;
      case PUT_OTP_REQUEST:
        draft.verifyOtpStatus = { isVerifyOtpError: false, errorType: '' };
        break;
      case PUT_OTP_SUCCESS:
        draft.verifyOtpStatus = { isVerifyOtpError: false, errorType: '' };
        break;
      case PUT_OTP_ERROR:
        draft.verifyOtpStatus = { isVerifyOtpError: true, errorType: action.payload };
        break;
      case RESET_OTP_ERROR:
        draft.verifyOtpStatus = { isVerifyOtpError: undefined, errorType: '' };
        break;
      case PUT_CAPTCHA_REQUEST:
        draft.isCaptchaError = {
          status: false,
          errorType: '',
        };
        break;
      case PUT_CAPTCHA_SUCCESS:
        draft.isCaptchaError = {
          status: false,
          errorType: '',
        };
        break;
      case PUT_CAPTCHA_ERROR:
        draft.isCaptchaError = {
          status: true,
          errorType: action.payload,
        };
        break;
      case RESET_CAPTCHA_ERROR:
        draft.isCaptchaError = {
          status: undefined,
          errorType: '',
        };
        break;
      case SAVE_USER_ID:
        draft.userId = action.payload;
        break;
      case SAVE_SESSION_ID:
        draft.sessionId = action.payload;
        break;
      case SAVE_PHONE_NUMBER:
        draft.phoneNumber = action.payload;
        break;
      case SAVE_USERNAME:
        draft.username = action.payload;
        break;
      case SAVE_USER_INFO_SUBMIT:
        draft.userInfoSubmit = action.payload;
        break;
      case SAVE_OTP_VALUE:
        draft.otpValue = action.payload;
        break;
      case SET_IS_COUNT_FROM_SCREEN_INFO:
        draft.isCountFromScreenInfo = action.payload;
        break;
    }
  });
};

export default requestTypeReducer;
