/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { forwardRef, useEffect, useImperativeHandle, useRef, useState } from 'react';
import { TouchableOpacity, View, Platform, ScrollView } from 'react-native';
import { Icon, useTheme, Text, Loading, NativeModuleClient, Checkbox, Button, InnerLoading } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import { Theme } from '@/config/themes.config';
import ModalOverlay from '@/components/ModalOverlay/index2';
import ROUTE_PATH from '@/navigators/routePath';
import { a11y, getItemCoverageNumber1 } from '@/utils/utils';

import useStyles from './styles';
import { connect } from 'react-redux';
import { TTransactionHistoryTypeState } from '../../stores/state';
import { TConnectState } from '@/stores/types';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import { TGlobalState } from '@/stores/global/state';
import { makeSelectPolicies, makeSelectPolicyLoadingSkeleton } from '@/stores/global/selectors';
import { makeSelectTransactionFilter } from '../../stores/selectors';
import { saveTransactionFilter, triggerFilter } from '../../stores/actions';
import { is } from 'immer/dist/internal';

type FilterModalProps = {
  policies: TGlobalState['policies']['data'],
  loading: boolean,
  transactionFilter: string[],
  setTransactionFilter: (payload: string[]) => any,
  setTriggerFilterFlag: (payload: boolean) => any,
};
export interface FilterModalRef {
  setVisibleModal: (isOpen: boolean) => void;
}
const FilterModal = forwardRef((props: FilterModalProps, ref: React.Ref<unknown> | undefined) => {
  const { policies, loading, transactionFilter, setTransactionFilter, setTriggerFilterFlag } = props;
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [isVisible, setIsVisible] = useState(false);
  const [selectedPolicies, setSelectedPolicies] = useState<string[]>([]);

  useImperativeHandle(
    ref,
    (): FilterModalRef => ({
      setVisibleModal: (isOpen: boolean) => setIsVisible(isOpen),
    }),
  );

  const handleSelectPolicy = (polNum: string) => {
    const currentPolicyIndex = selectedPolicies?.findIndex(item => item === polNum);
    if (currentPolicyIndex !== -1) {
      const result = selectedPolicies.filter(item => item !== polNum);
      setSelectedPolicies(result);
      return;
    }
    setSelectedPolicies([...selectedPolicies, polNum]);
  }

  const handleSelectAll = () => {
    if (selectedPolicies.length === policies?.length) {
      setSelectedPolicies([]);
      return;
    }
    let result: string[] = [];
    policies?.map(item => result.push(item?.policyNumber));
    setSelectedPolicies(result);
  }

  const handleReset = () => {
    setTriggerFilterFlag(true);
    setSelectedPolicies([]);
    setIsVisible(false);
    setTransactionFilter([]);
  }

  const handleApply = () => {
    setTriggerFilterFlag(true);
    setTransactionFilter(selectedPolicies);
    setIsVisible(false);
  }

  useEffect(() => {
    if (isVisible) {
      setSelectedPolicies(transactionFilter);
    }
  }, [isVisible])

  const renderPolicyCard = (item: MODEL.IPolicy) => {
    const coverageNumber1: MODEL.ICoverage | undefined = getItemCoverageNumber1(item?.coverage);
    const productName = coverageNumber1?.name || coverageNumber1?.shortName;
    return (
      productName && item?.policyNumber && item?.insured?.fullName &&
      <View style={styles.policyCard} key={item?.policyNumber}>
        <View style={styles.checkbox}>
          <Checkbox 
            size={24}
            checked={selectedPolicies.includes(item?.policyNumber)}
            onPress={() => handleSelectPolicy(item?.policyNumber)}
          />
        </View>
        <View style={styles.productNameWrapper}>
          <Text.Bold>{productName}</Text.Bold>
          <Text>{item?.policyNumber} - {item?.insured?.fullName}</Text>
        </View>
      </View>
    )
  }

  const renderModalContent = () => {
    return (
      <View style={styles.modalContainer}>
        <View style={[styles.modalHeader, styles.pdHorizontal16]}>
          <TouchableOpacity onPress={() => setIsVisible(false)} {...a11y('TransactionHistory.FilterModal.btnCancel')}><Text>{t('home.transaction.history.filter.btnClose')}</Text></TouchableOpacity>
          <Text.Bold style={styles.font16}>{t('home.transaction.history.filter.title')}</Text.Bold>
          <TouchableOpacity {...a11y('TransactionHistory.FilterModal.btnReset')} onPress={handleReset}><Text.Bold style={styles.btnReset}>{t('home.transaction.history.filter.btnReset')}</Text.Bold></TouchableOpacity>
        </View>
        { loading ? <View style={styles.wrapperLoading}>
          <InnerLoading />
        </View> : <View style={styles.flex1}>
          <View style={[styles.headerWrapper, styles.pdHorizontal16]}>
            <Text.Bold>{t('home.transaction.history.filter.policy')}</Text.Bold>
            <TouchableOpacity {...a11y('TransactionHistory.FilterModal.btnSelectAll')} onPress={handleSelectAll}>
              <Text.Bold style={styles.btnReset}>{t('home.transaction.history.filter.selectAll')}</Text.Bold>
            </TouchableOpacity>
          </View>
          <ScrollView>
            {
              policies && policies?.length > 0 && policies?.map(item => renderPolicyCard(item))
            }
          </ScrollView>
        </View> }
        <View style={[styles.buttonWrapper, styles.pdHorizontal16]}>
          <Button
            text={t('home.transaction.history.filter.btnApply')}
            uppercase={false}
            type="primary"
            size="large"
            id="TransactionHistory.FilterModal.btnApply"
            labelStyle={styles.buttonText}
            style={styles.button}
            onPress={handleApply}
          />
        </View>
      </View>
    )
  }

  return (
    <ModalOverlay 
        isVisible={isVisible}
      >
      {renderModalContent()}
    </ModalOverlay>
  );
});

type CreateStructuredSelectorType = {
  policies: TGlobalState['policies']['data'],
  loading: TGlobalState['policies']['isLoadingPolicy'],
  transactionFilter: TTransactionHistoryTypeState['transactionFilter'],
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  policies: makeSelectPolicies(),
  loading: makeSelectPolicyLoadingSkeleton(),
  transactionFilter: makeSelectTransactionFilter(),
});

type MapDispatchToPropsType = {
  setTransactionFilter: (payload: string[]) => any,
  setTriggerFilterFlag: (payload: boolean) => any,
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  setTransactionFilter: (payload: string[]) => dispatch(saveTransactionFilter(payload)),
  setTriggerFilterFlag: (payload: boolean) => dispatch(triggerFilter(payload))
});

export default connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true})(FilterModal);
