import React, { useCallback, useRef, useState } from 'react';
import {
  View,
  SafeAreaView,
  StyleSheet,
  KeyboardAvoidingView,
  Platform,
  TouchableOpacity,
  TouchableWithoutFeedback,
  Keyboard,
} from 'react-native';
import { Icon, Button, TextInput, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { Controller, useForm } from 'react-hook-form';
import { Dispatch } from 'redux';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import TouchId from 'react-native-touch-id-passcode';
import { StackNavigationProp } from '@react-navigation/stack';
import { useRoute, useNavigation } from '@react-navigation/native';
import _debounce from 'lodash/debounce';
import { Loading } from 'superapp-common-component';
import dayjs from 'dayjs';

import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import {
  authenticateWithPass as authenticateWithPassAction,
  resetPasswordError as resetPasswordErrorAction,
} from '@/stores/authentication/actions';
import { getItem, setItem } from '@/utils/storage';
import { STORAGE_NAME } from '@/utils/constants';
import { TAuthenticationState } from '@/stores/authentication/state';
import { makeSelectLoading, makeIsVerifyPasswordError } from '@/stores/authentication/selectors';
import { TConnectState } from '@/stores/types';
import CreatePinCodeModal, { ModalCreatePinRef } from '@/pages/Profile/components/CreatePincode';
import { a11y, parseString } from '@/utils/utils';
import { TStackParamList } from '@/navigators';
import type { StackScreenProps } from '@react-navigation/stack';
import Text from '@/components/Text';
import { HITSLOP } from '@/utils/styles';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';

interface VerifyPasswordPropsType {
  title?: string;
  loading: boolean;
  messageContent?: string;
  isVerifyPasswordError: boolean | undefined;
  onSubmit: () => void;
  authenticateWithPass: (payload: { phoneNumber: string; password: string }) => any;
  resetPasswordError: () => void;
}

type VerifyPasswordNavigationProps = StackNavigationProp<TStackParamList, 'VerifyPassword'>;
type VerifyPasswordNativeStackProps = StackScreenProps<TStackParamList, 'VerifyPassword'>;
type VerifyPasswordRouteProps = VerifyPasswordNativeStackProps['route'];

const VerifyPassword: React.FC<VerifyPasswordPropsType> = props => {
  const { authenticateWithPass, loading, isVerifyPasswordError, resetPasswordError } = props;
  const route = useRoute<VerifyPasswordRouteProps>();
  const { title, messageContent, toActiveFaceId } = route.params;
  const navigation = useNavigation<VerifyPasswordNavigationProps>();
  const [showPassword, setShowPassword] = useState(false);
  const modalCreatePincode = useRef<null | ModalCreatePinRef>(null);
  const { t } = useTranslation();
  const modalConfirmErrorRef = useRef<null | ModalConfirmErrorRef>(null);

  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    defaultValues: {
      password: '',
    },
  });

  const onConfirmPasswordToCreatePin = () => {
    NativeModuleClient.AdobeExperience.trackScreen({
      screen: 'Create Pincode',
      unixTimestamp: dayjs().unix(),
    });
    modalCreatePincode?.current?.setVisibleModal(true);
  };

  const onSubmitPassWordToActiveFaceId = async () => {
    try {
      const isGranted = await TouchId.authenticate('', {
        passcodeFallback: true,
        fallbackLabel: 'Enter Passcode',
      });
      await setItem(STORAGE_NAME.TOUCH_FACE_ID, parseString(isGranted));
    } catch (err) {
      await setItem(STORAGE_NAME.TOUCH_FACE_ID, parseString(false));
    } finally {
      navigation.goBack();
      reset();
    }
  };

  const dismissFunction = () => {
    if (isVerifyPasswordError && !loading) {
      modalConfirmErrorRef?.current?.open();
      return;
    }

    if (!isVerifyPasswordError && !loading) {
      if (toActiveFaceId) {
        onSubmitPassWordToActiveFaceId();
      } else {
        onConfirmPasswordToCreatePin();
      }
    }
  };

  const handleSubmitPassword = async (data: { password: string }) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_PASSWORD,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue',
    });
    if (loading) return;
    resetPasswordError();
    const { password } = data;
    const phoneNumber = await getItem(STORAGE_NAME.USERNAME);
    Keyboard.dismiss();
    authenticateWithPass({ phoneNumber, password });
  };

  const debounceHandleSubmitPassword = useCallback(_debounce(handleSubmitPassword, 400), []);

  const closePage = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.VERIFY_PASSWORD,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Go back',
    });
    resetPasswordError();
    navigation.goBack();
  };

  const hideKeyboard = () => Keyboard.dismiss();

  return (
    <View style={{ flex: 1 }}>
      <TouchableWithoutFeedback onPress={hideKeyboard} {...a11y('VerifyPassword.backdropHideKeyBoard')}>
        <View style={{ flex: 1 }}>
          <SafeAreaView style={styles.safeAreaViewStyle}>
            <Loading visible={loading} dismissAction={dismissFunction} />

            <View style={StyleSheet.flatten([styles.container])}>
              <KeyboardAvoidingView
                behavior={Platform.OS === 'ios' ? 'padding' : undefined}
                style={{ flex: 1 }}
                keyboardVerticalOffset={Platform.OS === 'ios' ? 50 : 25}
              >
                <View style={styles.modalBox}>
                  <View>
                    <View style={styles.topModal}>
                      <TouchableOpacity onPress={closePage} {...a11y('VerifyPassword.btnGoback')} hitSlop={HITSLOP}>
                        <Icon name="Arrow-Left" size={24} color="#5B636B" />
                      </TouchableOpacity>
                      <View>
                        <Text style={styles.mainTitle} fontFamily="bold">
                          {title}
                        </Text>
                      </View>
                      <TouchableOpacity onPress={() => {}} {...a11y('VerifyPassword.iconInfo1')} hitSlop={HITSLOP}>
                        <Icon name="Info1" size={24} color="#5B636B" />
                      </TouchableOpacity>
                    </View>
                    <View style={styles.formContainer}>
                      <Text style={styles.formTitle} fontFamily="bold">
                        {t('profile.input.password.modal.main.title')}
                      </Text>
                      <Text style={styles.subFormTitle}>{messageContent}</Text>
                      <View>
                        <Controller
                          control={control}
                          rules={{
                            required: { value: true, message: t('profile.input.password.modal.error') },
                          }}
                          render={({ field: { onChange, value } }) => (
                            <TextInput
                              label={t('profile.input.password.modal.label')}
                              type={errors?.password ? 'error' : 'info'}
                              disabled={false}
                              onChangeText={onChange}
                              value={value}
                              message={errors?.password && errors?.password?.message}
                              secureTextEntry={!showPassword}
                              right={showPassword ? { source: 'Mask' } : { source: 'Show' }}
                              iconColor="#000000"
                              onPressIcon={() => setShowPassword(!showPassword)}
                              id="VerifyPassword.inputPassWord"
                              textContentType='oneTimeCode'
                              autoComplete="off"
                            />
                          )}
                          name="password"
                        />
                      </View>
                    </View>
                  </View>
                  <View style={[styles.buttonBox, styles.marginBottom10]}>
                    <Button
                      text={t('profile.input.password.modal.button')}
                      onPress={handleSubmit(debounceHandleSubmitPassword)}
                      uppercase={false}
                      type="primary"
                      labelStyle={styles.buttonText}
                      size="large"
                      style={styles.button}
                      id="VerifyPassword.btnSubmitForm"
                    />
                  </View>
                </View>
              </KeyboardAvoidingView>
              <ModalConfirmError
                ref={modalConfirmErrorRef}
                title={t('profile.input.password.modal.error.title')}
                subtitle={t('profile.input.password.modal.error.subtitle')}
                btnText={t('profile.account.setting.modal.error.button.cancel')}
                onConfirm={() => {}}
              />
              <CreatePinCodeModal
                ref={modalCreatePincode}
                onGoBack={() => {
                  navigation.goBack();
                  reset();
                  NativeModuleClient.AdobeExperience.trackButton({
                    screen: 'Create Pincode',
                    unixTimestamp: dayjs().unix(),
                    buttonText: 'Go back',
                  });
                }}
              />
            </View>
          </SafeAreaView>
        </View>
      </TouchableWithoutFeedback>
    </View>
  );
};

type CreateStructuredSelectorType = {
  loading: TAuthenticationState['loading'];
  isVerifyPasswordError: TAuthenticationState['isVerifyPasswordError'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  isVerifyPasswordError: makeIsVerifyPasswordError(),
});

type MapDispatchToPropsType = {
  authenticateWithPass: (payload: { phoneNumber: string; password: string }) => any;
  resetPasswordError: () => any;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  authenticateWithPass: payload => dispatch(authenticateWithPassAction(payload)),
  resetPasswordError: () => dispatch(resetPasswordErrorAction()),
});

export default connect(mapStateToProps, mapDispatchToProps)(VerifyPassword);
