import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class AppInfoService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ONE_APPINFO);
  }

  public getAppInfo = (params: API.IGetAppInfo): Promise<MODEL.IAppInfo[]> => this.instance.post('/appInfo/clientGet', params);
}

export default new AppInfoService();
