import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_VITALITY } from './constants';

const currentState = (state: TConnectState) => state?.[NS_VITALITY] || initialState;
const makeSelectHealthReportFeedback = () => createSelector(currentState, state => state.healthReportFeedback);
const makeSelectLinkedTrackers = () => createSelector(currentState, state => state.linkedTrackers || []);

// loading selector
const makeSelectHealthReportLoading = () => createSelector(currentState, state => state.loadingHealthReport);
const makeSelectLinkedTrackersLoading = () => createSelector(currentState, state => state.linkedTrackersLoading);

export {
  currentState,
  makeSelectHealthReportFeedback,
  makeSelectLinkedTrackers,
  // loading selector
  makeSelectHealthReportLoading,
  makeSelectLinkedTrackersLoading,
};
