import { takeLatest, call, put, all } from 'redux-saga/effects';

import APIError from '@/utils/errors';
import { TAction } from '@/stores/types';
import AuthService from '@/services/auth';
import { navigationRef } from '@/navigators/navigation';
import ROUTE_PATH from '@/navigators/routePath';

import { REQUEST_PROCESS_P820 } from './constants';
import { loadingRequestAction, loadingSuccessAction, requestProcessP820ErrorAction, saveSessionIdAction } from './actions';

function* handleRequestOtp({ payload }: TAction) {
  try {
    yield all([
      put(loadingRequestAction()), 
      put(requestProcessP820ErrorAction(undefined))
    ]);

    const response: MODEL.IProcessP820 = yield call(AuthService.requestProcessP820Service, payload);

    yield put(saveSessionIdAction(response.sessionId));
    navigationRef.navigate(ROUTE_PATH.VERIFY_ACCOUNT_P820_OTP as never);

  } catch (error) {
    const apiError = error as APIError;
    yield put(requestProcessP820ErrorAction(apiError.errorCode));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* verifyAccountP820Saga() {
  yield takeLatest(REQUEST_PROCESS_P820, handleRequestOtp);
}

export default verifyAccountP820Saga;
