import React, { useEffect, useState, useRef, useLayoutEffect } from 'react';
import { View, TouchableOpacity, KeyboardAvoidingView, Platform, Keyboard, TouchableWithoutFeedback } from 'react-native';
import { useTranslation } from 'react-i18next';
import { Button, NativeModuleClient, PinCode } from 'superapp-common-component';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';

import { getItem } from '@/utils/storage';
import { STORAGE_NAME } from '@/utils/constants';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import ModalForgotPin, { ModalForgotPindRef } from '@/components/ModalForgotPin';
import { refreshToken as refreshTokenAction } from '@/stores/authentication/actions';
import Text from '@/components/Text';
import { makeSelectProfile } from '@/stores/global/selectors';
import { a11y, getFirstName } from '@/utils/utils';
import { HITSLOP } from '@/utils/styles';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';

type CreatePincodeProps = { setShowModal: (isShow: boolean) => void; setCurrentStep: (step: number) => void; step: number };

const TIME_OUT_SHOW_KEYBOARD = 300;
const TIME_OUT_HIDE_KEYBOARD = 100;
const STEPS_LOGIN = { USERNAME: 1, PINCODE: 2 };
const MAX_RETRY = 5;

const CreatePincode: React.FC<CreatePincodeProps> = props => {
  const { setCurrentStep, setShowModal, step } = props;
  const { t } = useTranslation();
  const [password, setPassword] = useState<string>('');
  const [isLoginBack, setIsLoginBack] = useState<boolean>(false);
  const [countdown, setCountdown] = useState<number>(MAX_RETRY);
  const [fullName, setFullName] = useState<string>('');
  const modalConfirmErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalForgotPasswordRef = useRef<null | ModalForgotPindRef>(null);
  const modalConfirmBlockErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const enterPincodeRef = useRef<any>();
  const dispatch = useDispatch();
  const profile = useSelector(makeSelectProfile());

  const hideKeyboard = () => Keyboard.dismiss();

  useEffect(() => {
    (async () => {
      const pincode = await getItem(STORAGE_NAME.PIN_CODE);
      if (pincode) {
        setIsLoginBack(true);
      }
    })();
  }, []);

  useEffect(() => {
    if (step === STEPS_LOGIN.PINCODE) {
      setTimeout(() => {
        enterPincodeRef?.current?.focus();
      }, TIME_OUT_SHOW_KEYBOARD);
    } else {
      setTimeout(() => {
        enterPincodeRef?.current?.blur();
      }, TIME_OUT_HIDE_KEYBOARD);
    }
  }, [step]);

  useLayoutEffect(() => {
    (async () => {
      const firstName = await getItem<string>(STORAGE_NAME.FULL_NAME, '');
      if (firstName) {
        setFullName(firstName);
      }
    })();
  }, [profile]);

  const showConfirmErrorModal = () => {
    modalConfirmErrorRef?.current?.open();
  };

  const showConfirmBlockErrorModal = () => {
    modalConfirmBlockErrorRef?.current?.open();
  };

  const showForgotPasswordModal = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Forgot PIN?',
    });
    modalForgotPasswordRef?.current?.open();
  };

  const handleSubmission = async () => {
    Keyboard.dismiss();
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.LOGIN,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Continue PinCode',
    });
    const pincode = await getItem<string>(STORAGE_NAME.PIN_CODE, null);

    if (pincode === password) {
      dispatch(refreshTokenAction());
    } else {
      setPassword('');
      if (countdown > 0) {
        setCountdown(countdown - 1);
      }
    }
  };

  useEffect(() => {
    if (countdown === 0) {
      setCurrentStep(1);
      showConfirmBlockErrorModal();
    } else if (countdown < MAX_RETRY) {
      showConfirmErrorModal();
    }
  }, [countdown]);

  return (
    <View style={styles.pane}>
      <View style={styles.containerPinCode}>
        <TouchableWithoutFeedback onPress={hideKeyboard} {...a11y('Login.backdropHidePincodeKeyboard')}>
          <View style={{ flex: 1 }}>
            <TouchableOpacity
              onPress={() => {
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.LOGIN,
                  unixTimestamp: dayjs().unix(),
                  buttonText: 'Continue',
                });
                setShowModal(false);
              }}
              {...a11y('Login.btnClosePincodeLoginModal')}
              style={{ alignSelf: 'flex-start' }}
              hitSlop={HITSLOP}
            >
              <Text style={styles.leftLabelButton} fontFamily="bold">
                {t('login.modal.button.close')}
              </Text>
            </TouchableOpacity>

            {isLoginBack && (
              <>
                <View style={styles.wrapTitle}>
                  <Text style={styles.titlePinCode} fontFamily="bold">
                    {t('login.pincode.relogin', { firstName: getFirstName(fullName || '') })}
                  </Text>
                  <Text style={styles.subtitlePinCode}>{t('login.pincode.code_length_message')}</Text>
                </View>
                <View style={styles.wrapPincode}>
                  <PinCode pinRef={enterPincodeRef} code={password} onTextChange={setPassword} id="Login.pincode" />
                  <TouchableOpacity onPress={showForgotPasswordModal} {...a11y('Login.btnShowForgotPinModal')}>
                    <Text style={styles.forgotPinBtn} fontFamily="bold">
                      {t('login.modal.forgot.open.button')}
                    </Text>
                  </TouchableOpacity>
                </View>
              </>
            )}
          </View>
        </TouchableWithoutFeedback>
        <KeyboardAvoidingView
          behavior={Platform.OS === 'ios' ? 'padding' : undefined}
          keyboardVerticalOffset={Platform.OS === 'ios' ? 60 : 0}
        >
          <View style={styles.wrapBtnContinue}>
            <Button
              text={t('login.pincode.continue')}
              type="primary"
              uppercase={false}
              labelStyle={styles.buttonText}
              size="large"
              style={styles.button}
              onPress={handleSubmission}
              id="Login.btnSubmitPincodeToLogin"
              disabled={false}
            />
          </View>
        </KeyboardAvoidingView>
        <ModalForgotPin ref={modalForgotPasswordRef} setCurrentStep={setCurrentStep} />
      </View>
      <ModalConfirmError
        ref={modalConfirmBlockErrorRef}
        title={t('login.error.pin.title.login-failed-5-times')}
        subtitle={t('login.error.pin.subtitle.login-failed-5-times')}
        btnText={t('login.error.button.close')}
      />
      <ModalConfirmError
        ref={modalConfirmErrorRef}
        title={t('login.error.errortitle')}
        subtitle={t('login.error.pin.incorrect')}
        btnText={t('login.error.tryagain')}
        onDismiss={() => {
          enterPincodeRef.current?.focus();
        }}
      />
    </View>
  );
};

export default CreatePincode;
