import React, { useLayoutEffect } from 'react';
import { View, ScrollView, SafeAreaView, TouchableOpacity, Alert, Linking } from 'react-native';
import { Icon, StepBar, theme, Text as TextCommon } from 'superapp-common-component';
import _isEmpty from 'lodash/isEmpty';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE_BGRWHITE, HITSLOP } from '@/utils/styles';
import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { a11y } from '@/utils/utils';

import styles from './styles';

const { colors } = theme;

type LoginInstructionNavigationProp = StackNavigationProp<TStackParamList, 'LoginInstruction'>;
type LoginInstructionProps = {};

const LoginInstruction: React.FC<LoginInstructionProps> = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<LoginInstructionNavigationProp>();

  const onPressCallForUsSuperApp = () => {
    Alert.alert(t('profile.title.superapp'), '', [
      {
        text: t('profile.title.cancel'),
        style: 'cancel',
      },
      {
        text: t('profile.title.call'),
        onPress: () => {
          Linking.openURL('tel:02838122777');
        },
      },
    ]);
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <TextCommon.Bold style={HEADER_TITLE_BGRWHITE}>{t('login.instruction.header')}</TextCommon.Bold>,
      headerLeft: () => (
        <TouchableOpacity
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          {...a11y('LoginInstruction.Instruction.iconGobackHeaderLeft')}
        >
          <Icon name="Arrow-Left" color={colors.primary.charcoal[80]} size={24} style={styles.marginLeft16} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView>
        <View style={styles.containerContactUs}>
          <View style={styles.topContent}>
            <Text style={styles.instructionText}>{t('login.instruction.title')}</Text>
            <Text style={styles.instructionRegister}>
              {t('login.instruction.sub.title')}
              <Text
                onPress={() => {
                  navigation.navigate(ROUTE_PATH.REGISTER);
                }}
                style={styles.linkRegister}
              >
                {t('login.instruction.sign.up')}
              </Text>
            </Text>
          </View>

          <View style={styles.contentInfoContactUs}>
            <View style={styles.contentInfoContactUsChild}>
              <Icon name="Phone1" size={20} color="#000000" style={styles.marginRight8} />
              <View>
                <Text style={styles.titleInfoContact}>{t('login.instruction.call.us', { phone: '028-3812-2777' })}</Text>
                <Text style={styles.descriptionInfoContact}>{t('login.instruction.sub.call.us')}</Text>
              </View>
            </View>
            <TouchableOpacity
              onPress={() => onPressCallForUsSuperApp()}
              hitSlop={HITSLOP}
              {...a11y('LoginInstruction.Instruction.btnCallForUsSuperApp')}
            >
              <Icon name="Arrow-Right" size={20} color={colors.primary.charcoal[100]} />
            </TouchableOpacity>
          </View>
          <View style={styles.contentInfoContactUs}>
            <View style={styles.contentInfoContactUsChild}>
              <Icon name="Email1" size={20} color="#000000" style={styles.marginRight8} />
              <View>
                <Text style={styles.titleInfoContact}>{t('login.instruction.email')}</Text>
                <Text style={styles.descriptionInfoContact}>{t('login.instruction.sub.email')}</Text>
              </View>
            </View>
            <TouchableOpacity
              onPress={() => Linking.openURL('mailto:vn.customer@aia.com')}
              hitSlop={HITSLOP}
              {...a11y('LoginInstruction.Instruction.btnLinkingAIAmail')}
            >
              <Icon name="Arrow-Right" size={20} color={colors.primary.charcoal[100]} />
            </TouchableOpacity>
          </View>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};

export default LoginInstruction;
