import React, { useLayoutEffect } from 'react';
import { View, ScrollView, SafeAreaView, TouchableOpacity, Alert, Linking } from 'react-native';
import { Icon, StepBar, theme } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';

import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';

import Text from '@/components/Text';
import { a11y } from '@/utils/utils';

import styles from './styles';

const { colors } = theme;

type InstructionRegisterNavigationProp = StackNavigationProp<TStackParamList, 'InstructionRegister'>;
type InstructionRegisterProps = {};

const InstructionRegister: React.FC<InstructionRegisterProps> = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<InstructionRegisterNavigationProp>();

  const onPressCallForUsSuperApp = () => {
    Alert.alert(t('profile.title.superapp'), '', [
      {
        text: t('profile.title.cancel'),
        style: 'cancel',
      },
      {
        text: t('profile.title.call'),
        onPress: () => {
          Linking.openURL('tel:02838122777');
        },
      },
    ]);
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('register.instruction.title')}</Text>,
      headerLeft: () => (
        <TouchableOpacity onPress={() => (navigation.canGoBack() ? navigation.goBack() : navigation.replace('Register'))} {...a11y('Register.Instruction.iconGobackHeaderLeft')}>
          <Icon name="Arrow-Left" color="#5B636B" size={20} style={styles.marginLeft16} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView>
        <View style={{ paddingHorizontal: 16, paddingTop: 32 }}>
          <StepBar 
            isShowStepButtons={false}
            stepActive={0}
            mode="vertical"
            iconWrapperStyles={styles.wrapStep}
            stepIconProps={{ 
                isActiveStyle: styles.customStyleStep,
                stepStyle: styles.customStyleStep,
                stepTextStyle: styles.customTextStep,
                dashStyle: { marginVertical: 8 }
            }}>
            <View>
              <Text style={styles.titleStep}>{t('register.instruction.step1.title')}</Text>
              <Text style={styles.descriptionStep}>{t('register.instruction.step1.description')}</Text>
            </View>
            <View>
              <Text style={styles.titleStep}>{t('register.instruction.step2.title')}</Text>
              <Text style={styles.descriptionStep}>{t('register.instruction.step2.description')}</Text>
            </View>
            <View>
              <Text style={styles.titleStep}>{t('register.instruction.step3.title')}</Text>
              <Text style={styles.descriptionStep}>{t('register.instruction.step3.description')}</Text>
            </View>
          </StepBar>
        </View>
        <View style={styles.containerContactUs}>
          <Text style={styles.titleContainerContact}>{t('register.instruction.contact.title')}</Text>
          <View style={styles.contentInfoContactUs}>
            <View style={styles.contentInfoContactUsChild}>
              <Icon name="Phone1" size={20} color="#000000" style={styles.marginRight8} />
              <View>
                <Text style={styles.titleInfoContact}>{t('profile.title.superapp')}</Text>
                <Text style={styles.descriptionInfoContact}>{t('profile.title.superapp.description')}</Text>
              </View>
            </View>
            <TouchableOpacity onPress={() => onPressCallForUsSuperApp()} hitSlop={HITSLOP} {...a11y('Register.Instruction.btnCallForUsSuperApp')}>
              <Icon name="Arrow-Right" size={20} color={colors.primary.charcoal[100]} />
            </TouchableOpacity>
          </View>
          <View style={styles.contentInfoContactUs}>
            <View style={styles.contentInfoContactUsChild}>
              <Icon name="Email1" size={20} color="#000000" style={styles.marginRight8} />
              <View>
                <Text style={styles.titleInfoContact}>{t('profile.title.email')}</Text>
                <Text style={styles.descriptionInfoContact}>{t('profile.title.email.description')}</Text>
              </View>
            </View>
            <TouchableOpacity onPress={() => Linking.openURL('mailto:vn.customer@aia.com')} hitSlop={HITSLOP} {...a11y('Register.Instruction.btnLinkingAIAmail')}>
              <Icon name="Arrow-Right" size={20} color={colors.primary.charcoal[100]} />
            </TouchableOpacity>
          </View>
        </View>
      </ScrollView>
    </SafeAreaView>
  );
};

export default InstructionRegister;
