import React, { useEffect, useLayoutEffect, useState } from 'react';
import { useNavigation } from '@react-navigation/native';
import { View, TouchableOpacity, Image, SafeAreaView, StatusBar } from 'react-native';
import { useTranslation } from 'react-i18next';
import { Icon, Button, theme, NativeModuleClient } from 'superapp-common-component';
import { StackNavigationProp, StackNavigationEventMap } from '@react-navigation/stack';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';

import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { a11y } from '@/utils/utils';
import { TStackParamList } from '@/navigators';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import ROUTE_PATH from '@/navigators/routePath';

import styles from './styles';

const { colors } = theme;

type NoPhoneNavigationProp = StackNavigationProp<TStackParamList, 'NoPhone'>;

const NoPhone = () => {
  const { t } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const navigation = useNavigation<NoPhoneNavigationProp>();
  const [isConnected, setConnected] = useState<null | boolean>(null);

  // const callHotline = () => {
  //   Alert.alert(`${t('forgot.password.call.hotline.title')} 028-3812-2777`, '', [
  //     {
  //       text: t('forgot.password.call.hotline.cancel'),
  //     },
  //     {
  //       text: t('forgot.password.call.hotline.call'),
  //       onPress: () => {
  //         Linking.openURL('tel:02838122777');
  //       },
  //       style: 'cancel',
  //     },
  //   ]);
  // };

  const backToHomepage = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.NO_PHONE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Back to home',
    });
    navigation.reset({ index: 0, routes: [{ name: ROUTE_PATH.DASHBOARD }] } as never);
  };

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('forgot.password.no.phone.title')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          style={styles.marginLeft16}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.NO_PHONE,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}
          {...a11y('NoPhone.iconBackHeaderLeft')}
        >
          <Icon name="Arrow-Left" size={24} color={colors.CHARCOAL_80} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <StatusBar barStyle="dark-content" />
      <View style={styles.pane}>
        <View style={styles.container}>
          <View style={styles.contentChangeUsername}>
            <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/logo-change-username.png')} />
            <Text style={styles.textTitleChangeUser}>{t('forgot.password.no.phone.main.title')}</Text>
            <Text style={styles.textDescriptionChangeUser}>{t('forgot.password.no.phone.sub.title')}</Text>
            <Text style={styles.textSubDescriptionChangeUser}>{t('migration.change.username.des5')}</Text>
          </View>
          <View style={[styles.buttonSuccessWrapper]}>
            <Button
              text={t('forgot.password.no.phone.button')}
              onPress={backToHomepage}
              style={[styles.marginBottom10]}
              uppercase={false}
              labelStyle={styles.buttonText}
              id="NoPhone.btnSubmitBackToHomepage"
            />
          </View>
        </View>
      </View>
    </SafeAreaView>
  ) : (
    <SafeAreaView style={styles.flex1}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default NoPhone;
