import { takeLatest, put, call, select } from 'redux-saga/effects';

import { TAction } from '@/stores/types';
import OneCommunication from '@/services/oneCommunication';

import {
  GET_ALL_NOTIFICATION,
  GET_ALL_NOTIFICATION_BROADCAST,
  READ_ALL_NOTIFICATION,
  READ_NOTIFICATION_BY_ID,
  LOAD_MORE_NOTIFICATION,
  LOAD_MORE_NOTIFICATION_BROADCAST,
  DELETE_NOTIFICATION,
  GET_NOTIFICATION_UNREAD,
  GET_CLAIM_DETAIL_DATA,
} from './constants';
import { makeSelectNotification } from './selectors';
import {
  loadingRequest,
  loadingSuccess,
  saveNotification,
  saveNotificationBroadcast,
  loadingLoadMoreRequest,
  loadingLoadMoreSuccess,
  savePaging,
  saveNotificationUnRead,
  getNotificationUnRead,
  isLoadMoreBroadcast,
  isLoadMoreGeneral,
} from './actions';
import oneClaimService from '@/services/oneClaimService';

function* getAllNotification() {
  try {
    yield put(loadingRequest());
    const response: API.INotification = yield call(OneCommunication.getAllNotification, { pageNumber: 0, pageSize: 10, userId: '' });
    yield put(saveNotification({ data: response.content, totalPages: response.totalPages }));
    yield put(isLoadMoreGeneral(!(response?.content?.length < 10)));
    yield put(savePaging({ pageNumber: 0, pageSize: 10 }));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingSuccess());
  }
}

function* getAllNotificationBroadcast() {
  try {
    yield put(loadingRequest());
    const response: API.INotification = yield call(OneCommunication.getAllGeneralNotification, { pageNumber: 0, pageSize: 10, userId: '' });
    yield put(saveNotificationBroadcast({ dataBroadcast: response.content, totalPages: response.totalPages }));
    yield put(isLoadMoreBroadcast(!(response?.content?.length < 10)));
    yield put(savePaging({ pageNumber: 0, pageSize: 10 }));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingSuccess());
  }
}

function* readAllNotification() {
  try {
    // yield put(loadingRequest());
    const { data } = yield select(makeSelectNotification());
    const response: [] = yield call(OneCommunication.readAllNotification);
    yield put(getNotificationUnRead());
    if (response) {
      const lstRenewStatusReaded = data?.map((item: MODEL.INotification) => ({ ...item, status: 'readed' }));
      yield put(saveNotification({ data: lstRenewStatusReaded }));
    }
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    // yield put(loadingSuccess());
  }
}

function* readNotificationById({ payload }: TAction) {
  try {
    // yield put(loadingRequest());
    const { data } = yield select(makeSelectNotification());
    const response: MODEL.INotification[] = yield call(OneCommunication.readNotificationById, payload);
    yield put(getNotificationUnRead());
    if (response[0]?.id) {
      const lstNotiReaded = data?.map((item: MODEL.INotification) => (item.id === response[0].id ? response[0] : item));
      yield put(saveNotification({ data: lstNotiReaded }));
    }
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    // yield put(loadingSuccess());
  }
}

function* loadMoreNotification() {
  try {
    yield put(loadingLoadMoreRequest());
    const { data, paging, totalPages } = yield select(makeSelectNotification());
    const paramsPaging: MODEL.IPagingNoti = {
      pageNumber: paging.pageNumber + 1,
      pageSize: 10,
    };
    if (paramsPaging.pageNumber === totalPages) {
      return;
    } else {
      const response: API.INotification = yield call(OneCommunication.getAllNotification, { ...paramsPaging, userId: '' });
      yield put(saveNotification({ data: [...data, ...response.content], totalPages: response.totalPages }));
      yield put(isLoadMoreGeneral(!(response?.content?.length < 10)));
      yield put(savePaging(paramsPaging));
    }
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingLoadMoreSuccess());
  }
}

function* loadMoreNotificationBroadcast() {
  try {
    yield put(loadingLoadMoreRequest());
    const { dataBroadcast, paging, totalPages } = yield select(makeSelectNotification());
    const paramsPaging: MODEL.IPagingNoti = {
      pageNumber: paging.pageNumber + 1,
      pageSize: 10,
    };
    if (paramsPaging.pageNumber === totalPages) {
      return;
    } else {
      const response: API.INotification = yield call(OneCommunication.getAllGeneralNotification, { ...paramsPaging, userId: '' });
      yield put(saveNotificationBroadcast({ dataBroadcast: [...dataBroadcast, ...response.content], totalPages: response.totalPages }));
      yield put(isLoadMoreBroadcast(!(response?.content?.length < 10)));
      yield put(savePaging(paramsPaging));
    }
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingLoadMoreSuccess());
  }
}

function* deleteNotification({ payload }: TAction) {
  try {
    yield put(loadingRequest());
    const { data } = yield select(makeSelectNotification());
    if (payload[0]?.id) {
      const lstNotiDeleted = data?.filter((item: MODEL.INotification) => item.id !== payload[0].id);
      yield put(saveNotification({ data: lstNotiDeleted }));
    }
    yield call(OneCommunication.deleteNotificationById, payload);
    yield put(getNotificationUnRead()); 
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingSuccess());
  }
}

function* handleGetNotificationUnRead() {
  try {
    const response: MODEL.INotificationUnRead = yield call(OneCommunication.getNotificationUnRead);
    yield put(saveNotificationUnRead(response));
  } catch (error) {
    console.log('ERROR:', error);
  }
}

function* handleGetClaimDetailData({payload, resolve, reject}: TAction) {
  try {
    const response: MODEL.IClaimTransactionResponse = yield call(oneClaimService.getClaimDetailDataService, payload);
    const hasClaimDetail = response?.result && response?.result[0];
    if (hasClaimDetail) {
      resolve(response?.result[0]);
    }
    resolve({details: []});
  } catch (error) {
    reject()
    console.log('ERROR:', error);
  }
}

function* notificationSaga() {
  yield takeLatest(GET_ALL_NOTIFICATION, getAllNotification);
  yield takeLatest(GET_ALL_NOTIFICATION_BROADCAST, getAllNotificationBroadcast);
  yield takeLatest(READ_ALL_NOTIFICATION, readAllNotification);
  yield takeLatest(READ_NOTIFICATION_BY_ID, readNotificationById);
  yield takeLatest(LOAD_MORE_NOTIFICATION, loadMoreNotification);
  yield takeLatest(LOAD_MORE_NOTIFICATION_BROADCAST, loadMoreNotificationBroadcast);
  yield takeLatest(DELETE_NOTIFICATION, deleteNotification);
  yield takeLatest(GET_NOTIFICATION_UNREAD, handleGetNotificationUnRead);
  yield takeLatest(GET_CLAIM_DETAIL_DATA, handleGetClaimDetailData);
}

export default notificationSaga;
