import React, { useEffect, useLayoutEffect, useState } from 'react';
import { SafeAreaView, ScrollView, TouchableOpacity, View } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { Icon, Loading, useTheme } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useInjectReducer, useInjectSaga } from 'redux-injectors';
import _groupBy from 'lodash/groupBy';
import _values from 'lodash/values';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { a11y } from '@/utils/utils';
import { Theme } from '@/config/themes.config';
import { NS_ECARD_TYPE } from './stores/constants';
import ECardReducer from './stores/reducer';
import ECardSaga from './stores/saga';
import useStyles from './styles';
import ECardItem from './components/ECardItem';
import { connect } from 'react-redux';
import { makeSelectEcards, makeSelectLoading } from './stores/selectors';
import { TECardTypeState } from './stores/state';
import { createStructuredSelector } from 'reselect';
import { getEcardsAction } from './stores/actions';
import { Dispatch } from 'redux';
import { TConnectState } from '@/stores/types';
import Empty from '@/components/Empty';
import Text from '@/components/Text';

type ECardNavigationProp = StackNavigationProp<TStackParamList, 'ECard'>;
type ECardProps = {
  getEcards: () => any;
  ecards: MODEL.IEcard[];
  loading: boolean
}
const ECard = (props: ECardProps) => {
  const { getEcards, ecards, loading } = props;
  useInjectReducer({ key: NS_ECARD_TYPE, reducer: ECardReducer });
  useInjectSaga({ key: NS_ECARD_TYPE, saga: ECardSaga });
  const navigation = useNavigation<ECardNavigationProp>();
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [ecardData, setEcardData] = useState<any>([]);

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('ecard.header.title')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('ECard.btnHeaderGoback')}
          style={styles.marginLeft16}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" color={theme.colors.white} size={24} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  useEffect(() => {
    const result = _groupBy(ecards, item => item.fullName);
    setEcardData(_values(result));
  }, [ecards])

  useEffect(() => {
    getEcards();
  }, [getEcards]);

  return (
    <SafeAreaView style={styles.flex1}>
      <Loading visible={loading} />
      {
        !loading && (ecardData?.length > 0 ? <ScrollView style={styles.container}>
          <View style={{marginTop: 24}}>
          {
            ecardData.map((item: any, index: number) => {
              return (
                <ECardItem key={index} navigation={navigation} items={item} />
              )
            })
          }
          </View>
        </ScrollView> : <Empty text={t('ecard.text.nodata')} />)
      }
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TECardTypeState['loading'];
  ecards: TECardTypeState['ecards'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  ecards: makeSelectEcards(),
});

type MapDispatchToPropsType = {
  getEcards: () => any;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  getEcards: () => dispatch(getEcardsAction()),
});

export default connect(mapStateToProps, mapDispatchToProps)(ECard);
