import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  PUT_CAPTCHA_ERROR,
  PUT_CAPTCHA_REQUEST,
  PUT_CAPTCHA_SUCCESS,
  PUT_OTP_ERROR,
  PUT_OTP_REQUEST,
  PUT_OTP_SUCCESS,
  RESET_CAPTCHA_ERROR,
  RESET_OTP_ERROR,
} from './constants';
import initialState, { TMigrationTypeState } from './state';

const migrationReducer = (state: TMigrationTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TMigrationTypeState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case PUT_OTP_REQUEST:
        draft.verifyOtpStatus = { isVerifyOtpError: false, errorType: '' };
        break;
      case PUT_OTP_SUCCESS:
        draft.verifyOtpStatus = { isVerifyOtpError: false, errorType: '' };
        break;
      case PUT_OTP_ERROR:
        draft.verifyOtpStatus = { isVerifyOtpError: true, errorType: action.payload };
        break;
      case RESET_OTP_ERROR:
        draft.verifyOtpStatus = { isVerifyOtpError: undefined, errorType: '' };
        break;
      case PUT_CAPTCHA_REQUEST:
        draft.isCaptchaError = { status: false, errorType: '' };
        break;
      case PUT_CAPTCHA_SUCCESS:
        draft.isCaptchaError = { status: false, errorType: '' };
        break;
      case PUT_CAPTCHA_ERROR:
        draft.isCaptchaError = { status: true, errorType: action.payload };
        break;
      case RESET_CAPTCHA_ERROR:
        draft.isCaptchaError = { status: undefined, errorType: '' };
        break;
    }
  });
};

export default migrationReducer;
