import { useEffect, useState } from 'react';
import { Linking, Platform } from 'react-native';
import NetInfo from '@react-native-community/netinfo';
import Device from 'react-native-device-info';
import { CommonConfig } from 'superapp-common-component';
import DeviceInfo from 'react-native-device-info';
import _every from 'lodash/every';

import { compareVersion, isEmpty, isIOS } from '@/utils/utils';
import ITunesService from '@/services/itunesService';
import AppInfoService from '@/services/appInfo';
import { APPS_NAME } from '@/utils/constants';

const appVersion = DeviceInfo.getVersion();
const useAppUpdate = () => {
  const netInfo = NetInfo.useNetInfo();
  const [appInfo, setAppInfo] = useState<{
    url: string;
    forceUpdate: boolean;
    version: string;
    currentVersion: string;
    skipVersion: string;
    hasNewVersion?: boolean;
  }>({
    url: '',
    version: '',
    forceUpdate: false,
    currentVersion: '',
    hasNewVersion: false,
    skipVersion: '',
  });

  const onSkipVersionUpdate = () => {
    setAppInfo({
      ...appInfo,
      skipVersion: appInfo.version,
    });
  };

  const fetchClientGet = async () => {
    try {
      const [info] = await AppInfoService.getAppInfo({
        appName: APPS_NAME.HOST,
        environment: CommonConfig.getEnvironment(),
        os: Platform.OS.toUpperCase(),
      });

      const newInfo = {
        ...appInfo,
        version: info?.storeVersion,
        hasNewVersion: _every([compareVersion(info?.storeVersion, appVersion) === 1, info?.storeVersion]),
        forceUpdate: _every([info?.isForceUpdate, compareVersion(info?.storeVersion, appVersion) === 1, info?.storeVersion]),
      };

      setAppInfo({ ...newInfo });
      return newInfo;
    } catch (error) {}
  };

  useEffect(() => {
    if (!netInfo.isConnected || !isEmpty(appInfo.url)) return;

    (async () => {
      try {
        const bundleId = Device.getBundleId();

        const [info] = await AppInfoService.getAppInfo({
          appName: APPS_NAME.HOST,
          environment: CommonConfig.getEnvironment(),
          os: Platform.OS.toUpperCase(),
        });

        if (isIOS) {
          const lookupItunes: MODEL.ILookupItunes = await ITunesService.lookupService({ bundleId, country: 'vn' });
          const appInfoLookup = lookupItunes?.results[0] || undefined;

          if (!isEmpty(appInfoLookup) && appInfoLookup.trackId) {
            const appStoreURI = `itms-apps://apps.apple.com/app/id${appInfoLookup?.trackId}?mt=8`;
            const appStoreURL = `https://apps.apple.com/app/id${appInfoLookup?.trackId}?mt=8`;
            const supported = await Linking.canOpenURL(appStoreURI);
            const hasNewVersion = _every([compareVersion(info?.storeVersion, appVersion) === 1, info?.storeVersion]);

            setAppInfo({
              ...appInfo,
              hasNewVersion,
              currentVersion: appVersion,
              version: info?.storeVersion,
              url: supported ? appStoreURI : appStoreURL,
              forceUpdate: _every([info?.isForceUpdate, hasNewVersion]),
            });
          }
        } else {
          const appStoreURL = `https://play.google.com/store/apps/details?id=${bundleId}`;
          const supported = await Linking.canOpenURL(appStoreURL);
          const hasNewVersion = _every([compareVersion(info?.storeVersion, appVersion) === 1, info?.storeVersion]);

          setAppInfo({
            ...appInfo,
            hasNewVersion,
            version: info?.storeVersion,
            currentVersion: appVersion,
            url: supported ? appStoreURL : '',
            forceUpdate: _every([info?.isForceUpdate, hasNewVersion]),
          });
        }
      } catch (error) {}
    })();
  }, [netInfo.isConnected]);

  return { appInfo, fetchClientGet, onSkipVersionUpdate };
};

export default useAppUpdate;
