import React, { forwardRef, useImperativeHandle } from 'react';
import { TouchableOpacity, View } from 'react-native';
import { Icon, Text } from 'superapp-common-component';
import { SafeAreaView, useSafeAreaInsets } from 'react-native-safe-area-context';

import Modal from '@/components/Modal';
import { HITSLOP_48 } from '@/utils/styles';
import { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface IModalNoteProps {
  note?: {
    title: string;
    items: string[];
  };
}

export type TModalNoteRef = {
  open: () => void;
  close: () => void;
};

const ModalNote: React.ForwardRefRenderFunction<TModalNoteRef, IModalNoteProps> = (props, ref) => {
  const insets = useSafeAreaInsets();
  const [modalsRef, modalInstance] = useModal();

  const onPressClose = () => {
    modalInstance().close();
  };

  const imperativeHandle = (): TModalNoteRef => ({
    open: () => {
      modalInstance().show();
    },
    close: () => {
      modalInstance().close();
    },
  });

  useImperativeHandle(ref, imperativeHandle, []);

  return (
    <Modal ref={modalsRef} modalStyle={styles.bgOverlay} containerStyles={{ justifyContent: 'flex-end' }} animationType="slideBottom">
      <SafeAreaView edges={['top']}>
        <View style={[styles.modalContainer, { paddingBottom: Math.max(insets.bottom, 16) }]}>
          <View style={styles.header}>
            <Text style={styles.title}>{props.note?.title}</Text>
            <TouchableOpacity hitSlop={HITSLOP_48} onPress={onPressClose}>
              <Icon name="Close" size={24} />
            </TouchableOpacity>
          </View>
          <View style={styles.content}>
            {props.note?.items.map((item: string, idx) => (
              <View style={[styles.row, styles.alignCenter]} key={idx}>
                <View style={styles.dot} />
                <Text style={styles.text}>{item}</Text>
              </View>
            ))}
          </View>
        </View>
      </SafeAreaView>
    </Modal>
  );
};

export default forwardRef(ModalNote);
