import React, { useImperativeHandle } from 'react';
import { View, StatusBar, TouchableOpacity, SafeAreaView } from 'react-native';
import { Icon } from 'superapp-common-component';
import Text from '@/components/Text';
import { useTranslation } from 'react-i18next';
import { colors } from 'superapp-common-component';
import PDFView from 'react-native-view-pdf';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';
import { a11y } from '@/utils/utils';
import { HITSLOP_48 } from '@/utils/styles';

import styles from './styles';

export type TPdfViewerRef = {
  openPdf: () => void;
};

type PdfViewerProps = {
  uri: string | any;
  type?: string;
};

const PdfViewer = React.forwardRef((props: PdfViewerProps, ref: any) => {
  const { t } = useTranslation();
  const [modalsRef, modalInstance] = useModal();

  const onBackdropPress = () => {
    modalInstance().close();
  };

  const onOpenPdf = () => {
    modalInstance().show();
  };

  useImperativeHandle(
    ref,
    () => ({
      openPdf: () => onOpenPdf(),
    }),
    [],
  );

  return (
    <Modal ref={modalsRef} overlayOpacity={0.5} onDismiss={() => null}>
      <StatusBar barStyle="light-content" />
      <SafeAreaView style={{ backgroundColor: colors.primary.red[100] }} />
      <View style={styles.container}>
        <View style={styles.header}>
          <TouchableOpacity
            style={{ paddingLeft: 4 }}
            onPress={onBackdropPress}
            {...a11y('ViewPdf.btnCloseViewModal')}
            hitSlop={HITSLOP_48}
          >
            <Icon name="Close" size={24} color={colors.monoTone[0]} style={styles.iconClose} />
          </TouchableOpacity>
          <View>
            <Text numberOfLines={1} style={styles.titleHeader}>
              {t('common.title.webView')}
            </Text>
          </View>
          <View style={styles.fakeView} />
        </View>
        <View style={styles.content}>
          {props.uri !== '' && (
            <PDFView
              fadeInDuration={250.0}
              style={{ flex: 1 }}
              resource={props.uri}
              resourceType={props.type as any}
            />
          )}
        </View>
      </View>
      <SafeAreaView style={{ backgroundColor: 'white' }} />
    </Modal>
  );
});

export default PdfViewer;
