import jwtDecode from 'jwt-decode';
import { Platform, NativeModules, NativeEventEmitter, DeviceEventEmitter, DeviceEventEmitterStatic, AppRegistry } from 'react-native';

import config from '@/config/app.config';
import authService from '@/services/auth';
import { Nullable } from '@/nativeModules';

export interface INotification {
  notification: { body: string; title: string };
  data?: {
    type: string;
    miniApp: string;
    link: string;
    userId: string;
    action: string;
  };
}

const { NotificationModule } = NativeModules;

const isIOS = Platform.OS === 'ios';

export const GENERAL_TAG = 'GENERAL';

const NOTIFICATION_EVENT = {
  ON_REQUEST_AUTHORIZATION: 'Notification_onRequestAuthorization',
  ON_MESSAGE: 'Notification_onMessage',
  ON_BACKGROUND_MESSAGE: 'Notification_onBackgroundMessage', // only ios
  ON_TAP_NOTIFICATION: 'Notification_onTapNotification',
};

export const AUTHORIZATION_STATUS = {
  NOT_DETERMINED: -1,
  DENIED: 0,
  AUTHORIZED: 1,
  PROVISIONAL: 2,
  EPHEMERAL: 3,
};

class AzureNotification {
  notificationEmitter: NativeEventEmitter | DeviceEventEmitterStatic;

  private tappedNotification: Nullable<INotification> = null;

  constructor() {
    if (isIOS) {
      this.notificationEmitter = new NativeEventEmitter(NotificationModule);
      this.initIos();
    } else {
      this.notificationEmitter = DeviceEventEmitter;
    }
  }

  private initIos() {
    this.onRequestAuthorization(isGranted => {
      !isGranted && this.clearTags();
    });
  }

  /**
   * When the application is opened from a quit state.
   * @returns message
   */
  async onInitialNotification() {
    const message = await NotificationModule.getInitialNotification<INotification>();
    this.tappedNotification = message;
    return message;
  }

  start() {
    return NotificationModule.start();
  }

  async hasGeneralTag(tags?: string[]) {
    let resultTags = tags || (await NotificationModule.getTags());
    return resultTags.includes(GENERAL_TAG);
  }

  async addCurrentUserTagService(userSubId: string) {
    try {
      const pushChannelId = await this.getPushChannelId();
      await authService.registerDeviceInfo({
        clientId: config.CLIENT_ID,
        tag: userSubId,
        os: Platform.OS,
        deviceId: pushChannelId,
      });
    } catch {
    }
  }

  async clearTagsWithoutGeneral(tags?: string[]) {
    const resultTags = tags || (await this.getTags());
    const filterTags = resultTags.filter(tag => tag !== GENERAL_TAG);
    this.removeTags(filterTags);
  }

  hasPermission() {
    return NotificationModule.hasPermission();
  }

  getInstallationId() {
    return NotificationModule.getInstallationId();
  }

  getPushChannelId() {
    return NotificationModule.getPushChannelId();
  }

  async getTags() {
    const tags = await NotificationModule.getTags();
    return tags || [];
  }

  addTag(tag: string) {
    NotificationModule.addTag(tag);
  }

  addTags(tags: string[]) {
    try {
      if (!tags.length) return;

      NotificationModule.addTags(tags);
    } catch {}
  }

  removeTag(tag: string) {
    NotificationModule.removeTag(tag);
  }

  removeTags(tags: string[]) {
    try {
      if (!tags.length) return;

      NotificationModule.removeTags(tags);
    } catch {}
  }

  clearTags() {
    NotificationModule.clearTags();
  }

  /**
   *
   * @param fnCallbackMessage
   * @returns function unsubscription
   */
  onRequestAuthorization(fnOnRequestAuthorization: (isGranted: boolean) => void) {
    if (!isIOS) return () => null;

    const subscription = this.notificationEmitter.addListener(NOTIFICATION_EVENT.ON_REQUEST_AUTHORIZATION, async (isGranted: boolean) => {
      fnOnRequestAuthorization(isGranted);
    });
    return () => subscription.remove();
  }

  /**
   *
   * @param fnCallbackMessage
   * @returns function unsubscription
   */
  onMessage<T = any>(fnCallbackMessage: (value: T) => void) {
    const subscription = this.notificationEmitter.addListener(NOTIFICATION_EVENT.ON_MESSAGE, values => {
      fnCallbackMessage(values);
    });

    return () => subscription.remove();
  }

  /**
   *
   * @param backgroundMessageHandler is a promise function
   * @returns
   */
  onBackgroundMessage(backgroundMessageHandler: Function) {
    if (isIOS) {
      const subscription = this.notificationEmitter.addListener(NOTIFICATION_EVENT.ON_BACKGROUND_MESSAGE, values => {
        backgroundMessageHandler(values);
      });

      return () => subscription.remove();
    } else {
      AppRegistry.registerHeadlessTask('NotificationHeadlessServiceTask', () => {
        if (!backgroundMessageHandler) {
          return () => Promise.resolve();
        }
        return remoteMessage => backgroundMessageHandler(remoteMessage);
      });

      return () => null;
    }
  }

  /**
   * When the application is running, but in the background.
   * @param fnCallbackMessage
   * @returns function unsubscription
   */
  onTapNotification(fnCallbackMessage: (value: INotification) => void) {
    const subscription = this.notificationEmitter.addListener(NOTIFICATION_EVENT.ON_TAP_NOTIFICATION, values => {
      // store notification when user tapped
      this.tappedNotification = values;
      fnCallbackMessage(values);
    });

    return () => subscription.remove();
  }

  getTappedNotification() {
    return this.tappedNotification;
  }

  clearTappedNotification() {
    this.tappedNotification = null;
  }

  getUserId(token: string) {
    try {
      const tokenInfo = jwtDecode<MODEL.ITokenDecode>(token);
      return tokenInfo?.sub;
    } catch {
      return undefined;
    }
  }
}

const instance = !__DEV__ ? new AzureNotification() : ({} as AzureNotification);

export default instance;
