import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, SUBMIT_P820, OTP_REQUEST_OTP_P820, OTP_REQUEST_ERROR, SUBMIT_P820_ERROR } from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });

export const requestOtpP820Action = (payload: {
  params: { otpType: string; otpSendType: string };
  callback?: (number: MODEL.IRequestMyOtp) => void;
}): TAction => ({ type: OTP_REQUEST_OTP_P820, payload });

export const submitP820Action = (
  payload: { otpValue: string; sessionId: string; clientId: string },
  resolve: () => void,
  reject: (value: unknown) => void,
): TAction => ({
  type: SUBMIT_P820,
  payload,
  resolve,
  reject,
});

export const submitP820ErrorAction = (payload: { errorType: string }): TAction => ({
  type: SUBMIT_P820_ERROR,
  payload,
});

export const optRequesetErrorAction = (payload: { status: boolean; errorType: string }): TAction => ({
  type: OTP_REQUEST_ERROR,
  payload,
});
