import React, { useState, useEffect } from 'react';
import { View, Image, StatusBar, SafeAreaView } from 'react-native';
import { Button, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { StackNavigationProp } from '@react-navigation/stack';
import NetInfo from '@react-native-community/netinfo';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigation } from '@react-navigation/native';
import dayjs from 'dayjs';

import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';

import { saveProfile } from '@/stores/global/actions';
import { makeSelectProfile } from '@/stores/global/selectors';

import styles from './styles';
import { maskedPhoneNumber } from '@/utils/utils';

interface ModalSuccessProps {}
type ModalSuccessNavigationProp = StackNavigationProp<TStackParamList, 'MigrateSuccessModal'>;

const ModalSuccess: React.FC<ModalSuccessProps> = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const navigation = useNavigation<ModalSuccessNavigationProp>();
  const netInfo = NetInfo.useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const profile = useSelector(makeSelectProfile());

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <View style={styles.pane}>
        <StatusBar barStyle="dark-content" />
        <View style={styles.container}>
          <View style={styles.contentWrapper}>
            <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/account-ready.png')} />
            <Text style={styles.textTitle} fontFamily="bold">
              {t('migration.modal.success.main.title')}
            </Text>
            <Text style={styles.textDescription}>
              {t('login.update.contact.otp.success.modal.sub.title', { username: maskedPhoneNumber(profile?.mobile!) })}
            </Text>
          </View>
          <View style={[styles.buttonSuccessWrapper]}>
            <Button
              text={t('migration.login.button')}
              onPress={() => {
                dispatch(saveProfile(undefined));
                navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.MIGRATION_SUCCESS_MODAL,
                  unixTimestamp: dayjs().unix(),
                  buttonText: 'Login',
                });
              }}
              uppercase={false}
              labelStyle={styles.buttonText}
              style={styles.borderWhite}
              id="Migration.btnOtpNavigateLogin"
            />
          </View>
        </View>
      </View>
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default ModalSuccess;
