import React, { useLayoutEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { Image, SafeAreaView, TouchableOpacity, View, Alert, Linking } from 'react-native';
import { Button, Icon, NativeModuleClient } from 'superapp-common-component';
import dayjs from 'dayjs';
import { useNavigation } from '@react-navigation/core';
import { StackNavigationEventMap } from '@react-navigation/stack';

import { utils } from 'superapp-common-component';
import Text from '@/components/Text';
import ROUTE_PATH from '@/navigators/routePath';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';

import styles from './styles';

const { a11y } = utils;

const DeleteAccount = () => {
  const { t } = useTranslation();
  const navigation = useNavigation<any>();

  const onPressPhoneCall = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.DELETE_ACCOUNT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Call Hotline',
    });

    Alert.alert(t('deleteAccount.alert.callTitle'), '', [
      {
        text: t('deleteAccount.alert.cancel'),
        style: 'cancel',
      },
      {
        text: t('deleteAccount.alert.call'),
        onPress: () => {
          Linking.openURL(`tel:${t('deleteAccount.contactNumber')}`);
        },
      },
    ]);
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('deleteAccount.title.header')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('DeleteAccount.btnHeaderGoback')}
          style={styles.marginLeft16}
          hitSlop={HITSLOP}
          onPress={() => navigation.canGoBack() && navigation.goBack()}
        >
          <Icon name="Arrow-Left" size={24} color="#ffffff" />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  return (
    <SafeAreaView style={styles.flex1}>
      <View style={styles.successModalContainer}>
        <View style={styles.topContent}>
          <View style={styles.warpperImage}>
            <Image style={styles.imageStyle} resizeMode="contain" source={require('@/assets/images/img-warning.png')} />
          </View>
          <Text style={styles.title} fontFamily="bold">
            {t('deleteAccount.warning.title')}
          </Text>
          <Text style={styles.subTitle}>{t('deleteAccount.warning.subtitle1')}</Text>
          <Text style={styles.subTitle}>{t('deleteAccount.warning.subtitle2')}</Text>
        </View>
        <View style={styles.wrapperButton}>
          <Button
            type="secondary"
            text={t('deleteAccount.btnCallHotline')}
            onPress={onPressPhoneCall}
            style={styles.buttonStyle}
            labelStyle={styles.labelStyleButton}
          />
        </View>
      </View>
    </SafeAreaView>
  );
};

export default DeleteAccount;
