import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_GLOBAL } from './constants';

const currentState = (state: TConnectState) => state?.[NS_GLOBAL] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectProfile = () => createSelector(currentState, state => state.profile);
const makeSelectUserIdCard = () => createSelector(currentState, state => state.profile?.idCardNo);
const makeSelectBanners = () => createSelector(currentState, state => state.AEMData.banners);
const makeSelectProductsList = () => createSelector(currentState, state => state.AEMData.productsList);
const makeSelectNewsList = () => createSelector(currentState, state => state.AEMData.newsList);
const makeSelectPolicies = () => createSelector(currentState, state => state.policies?.data);
const makeSelectHomeData = () => createSelector(currentState, state => state.AEMData);
const makeSelectLoadingSkeleton = () => createSelector(currentState, state => state.AEMData.isLoading);
const makeSelectPolicyLoadingSkeleton = () => createSelector(currentState, state => state.policies?.isLoadingPolicy);
const makeSelectFeatureList = () => createSelector(currentState, state => state.AEMData.featureList);
const makeSelectActionsList = () => createSelector(currentState, state => state.AEMData.quickActions);
const makeSelectProductImage = () => createSelector(currentState, state => state.productImage.data);
const makeSelectPoliciesAck = () => createSelector(currentState, state => state.policiesAck.data);
const makeSelectLoadingPolicyAck = () => createSelector(currentState, state => state.policiesAck.isLoadingPolicyAck);
const makeSelectPoliciesIncommingPayment = () => createSelector(currentState, state => state.policiesIncommingPayment);
const makeSelectClaimDraftInfo = () => createSelector(currentState, state => state.draftInfo);
const makeSelectActionsLinks = () => createSelector(currentState, state => state.actionLinks);
const makeSelectIntroduction = () => createSelector(currentState, state => state.AEMData.introduction);
const makeSelectHelpCenter = () => createSelector(currentState, state => state.helpCenter);
const makeSelectMembership = () => createSelector(currentState, state => state.membership);
const makeSelectTierVip = () => createSelector(currentState, state => state.tierVip);
const makeSelectPaymentMembership = () =>
  createSelector(currentState, state => (state.membership?.billingIndicator ? state.membership : undefined));
const makeSelectVipRank = () =>
  createSelector(currentState, state => state.profile?.vipRank?.match(/[^\s_]+/g)?.join('') || '');

export {
  currentState,
  makeSelectLoading,
  makeSelectProfile,
  makeSelectBanners,
  makeSelectProductsList,
  makeSelectHomeData,
  makeSelectNewsList,
  makeSelectPolicies,
  makeSelectLoadingSkeleton,
  makeSelectPolicyLoadingSkeleton,
  makeSelectActionsList,
  makeSelectFeatureList,
  makeSelectProductImage,
  makeSelectPoliciesAck,
  makeSelectPoliciesIncommingPayment,
  makeSelectUserIdCard,
  makeSelectClaimDraftInfo,
  makeSelectActionsLinks,
  makeSelectIntroduction,
  makeSelectHelpCenter,
  makeSelectPaymentMembership,
  makeSelectMembership,
  makeSelectTierVip,
  makeSelectVipRank,
  makeSelectLoadingPolicyAck,
};
