import React, { useEffect, useState } from 'react';
import { TouchableOpacity, View } from 'react-native';
import { useTranslation } from 'react-i18next';
import { StackNavigationProp } from '@react-navigation/stack';
import { utils } from 'superapp-common-component';
import { Icon, NativeModuleClient } from 'superapp-common-component';
import dayjs from 'dayjs';
import { useNavigation } from '@react-navigation/native';
import { useSelector } from 'react-redux';
import _get from 'lodash/get';

import { makeSelectHelpCenter } from '@/stores/global/selectors';
import Text from '@/components/Text';
import ROUTE_PATH from '@/navigators/routePath';
import { TStackParamList } from '@/navigators';

import styles from './styles';
import { getItem } from '@/utils/storage';
import { STORAGE_NAME } from '@/utils/constants';

const { a11y } = utils;

type HelpCenterNavigationProp = StackNavigationProp<TStackParamList, 'Profile'>;

const HELP_CENTER_TYPES = {
  FAQS: 'faqs',
  VIDEO: 'video-tutorial',
  CONTACT: 'contact-us',
};

const HelpCenter = () => {
  const { t, i18n } = useTranslation();
  const navigation = useNavigation<HelpCenterNavigationProp>();
  const helpCenter = useSelector(makeSelectHelpCenter());
  const [currentLang, setCurrentLang] = useState('vi');

  const onPressContact = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Contact us',
    });
    navigation.navigate(ROUTE_PATH.CONTACT);
  };

  const onPressFAQ = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'FAQs',
    });
    navigation.navigate(ROUTE_PATH.FAQ);
  };

  const onPressVideo = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Video tutorial',
    });
    navigation.navigate(ROUTE_PATH.VIDEO_GUILDLINE);
  };

  const onPressButton = (id?: string) => {
    switch (id) {
      case HELP_CENTER_TYPES.FAQS:
        onPressFAQ();
        break;
      case HELP_CENTER_TYPES.VIDEO:
        onPressVideo();
        break;
      case HELP_CENTER_TYPES.CONTACT:
        onPressContact();
        break;
    }
  };

  const getIcon = (id?: string) => {
    switch (id) {
      case HELP_CENTER_TYPES.FAQS:
        return 'Ask-For-Help';
      case HELP_CENTER_TYPES.VIDEO:
        return 'Video-1';
      case HELP_CENTER_TYPES.CONTACT:
        return 'Phone1';
      default:
        return 'Ask-For-Help';
    }
  };

  useEffect(() => {
    (async () => {
      const lang = await getItem<string>(STORAGE_NAME.LANGUAGE, 'vi');
      setCurrentLang(lang);
    })();
  }, [i18n.language]);

  return helpCenter && _get(helpCenter, currentLang, []).length > 0 ? (
    <View style={styles.container}>
      <Text
        style={styles.textCardTitle}
        xl={{ fontWeight: '600', fontSize: 16, lineHeight: 24 }}>
        {t('profile.help.center.setting')}
      </Text>
      <View style={styles.wrapperContent}>
        {_get(helpCenter, currentLang, []).map((item: MODEL.IHelpCenter) =>
          !item.hide ? (
            <TouchableOpacity
              key={item?.id}
              style={[styles.row, styles.marginHorizontal]}
              {...a11y('Profile.HelpCenter.btnFAQs')}
              onPress={() => onPressButton(item?.id)}>
              <View style={styles.row}>
                <Icon
                  name={item?.iconName ? item.iconName : getIcon(item?.id)}
                  size={24}
                  color="#5B636B"
                />
                <Text style={styles.textContent}>{item.title}</Text>
              </View>
              <Icon
                name="Arrow-Right"
                size={20}
                color="#5B636B"
                style={styles.alignSelfCenter}
              />
            </TouchableOpacity>
          ) : null,
        )}
      </View>
    </View>
  ) : null;
};

export default HelpCenter;
