import React, { useLayoutEffect, useRef, useState } from 'react';
import { SafeAreaView, ScrollView, TouchableOpacity, View } from 'react-native';
import {
  useTheme,
  Text,
  Icon,
  Checkbox,
  Button,
  NativeModuleClient,
} from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { utils } from 'superapp-common-component';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import dayjs from 'dayjs';

import { HITSLOP } from '@/utils/styles';
import { getGender } from '@/utils/utils';
import ROUTE_PATH from '@/navigators/routePath';
import { Theme } from '@/config/themes.config';
import { TStackParamList } from '@/navigators';
import PdfViewer, { TPdfViewerRef } from '@/components/PdfViewer';
import { makeSelectProfile } from '@/stores/global/selectors';

import useStyles from './styles';
const { a11y } = utils;
const GENDER = {
  MALE: 'male',
  FEMALE: 'female',
};

type EConsentOtpNavigationProp = StackNavigationProp<
  TStackParamList,
  'DecreeEConsent'
>;

const DecreeEConsent = () => {
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const tcViewerRef = useRef<TPdfViewerRef>();
  const profile = useSelector(makeSelectProfile());
  const navigation = useNavigation<EConsentOtpNavigationProp>();
  const [confirm, setConfirm] = useState<{ checked: boolean }>({
    checked: false,
  });
  const { t } = useTranslation();

  const showWebviewTC = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.DECREE_ECONSENT,
      unixTimestamp: dayjs().unix(),
      buttonText: 'T&C',
    });
    tcViewerRef?.current?.openPdf();
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text style={styles.header}>{t('home.econsent.title')}</Text>
      ),
      headerTitleStyle: styles.mainTitle,
      headerLeft: () => (
        <TouchableOpacity
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.DECREE_ECONSENT,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}
          {...a11y('DecreeEConsentOtp.iconBackHeaderLeft')}>
          <Icon
            name="Arrow-Left"
            color={theme.colors.white}
            size={24}
            style={styles.marginLeft16}
          />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.safearea}>
      <ScrollView contentContainerStyle={styles.flex}>
        <View style={[styles.flex, styles.between]}>
          <View>
            <View style={styles.head}>
              <Text style={styles.intro}>{t('common.t&c.policy')}</Text>
            </View>

            <View style={styles.container}>
              <View style={styles.content}>
                <Text style={styles.title}>
                  {t('home.econsent.decree.info')}
                </Text>
              </View>
              <View style={styles.wrapper}>
                <Text style={styles.info}>{t('home.econsent.name')}</Text>
                <Text style={styles.des}>{profile?.fullName}</Text>
              </View>
              <View style={styles.wrapper}>
                <Text style={styles.info}>{t('home.econsent.dob')}</Text>
                <Text style={styles.des}>
                  {' '}
                  {profile?.dob ? dayjs(profile?.dob).format('DD/MM/YYYY') : ''}
                </Text>
              </View>
              <View style={styles.wrapper}>
                <Text style={styles.info}>{t('home.econsent.gender')}</Text>
                <Text style={styles.des}>
                  {profile?.gender
                    ? (getGender(profile?.gender) === GENDER.FEMALE &&
                        t('myprofile.gender.female')) ||
                      (getGender(profile?.gender) === GENDER.MALE &&
                        t('myprofile.gender.male'))
                    : ''}
                </Text>
              </View>
              <View style={styles.wrapper}>
                <Text style={styles.info}>{t('home.econsent.id')}</Text>
                <Text style={styles.des}>
                  {profile?.idCardNo ? profile?.idCardNo : ''}
                </Text>
              </View>
              <View style={styles.wrapper}>
                <Text style={styles.info}>{t('home.econsent.email')}</Text>
                <Text style={styles.des}> {profile?.email || ''}</Text>
              </View>
              <View style={styles.wrapper}>
                <Text style={styles.info}>{t('home.econsent.address')}</Text>
                <Text style={styles.des}> {profile?.address || ''}</Text>
              </View>
            </View>
          </View>

          <View style={styles.confirminfo}>
            <View style={styles.line} />

            <View style={styles.box}>
              <View style={styles.confirmbox}>
                <Checkbox
                  checked={confirm.checked}
                  size={24}
                  onPress={() =>
                    setConfirm({ ...confirm, checked: !confirm.checked })
                  }
                />
                <Text style={styles.term}>
                  {t('home.econsent.decree.confirm1')}{' '}
                  <Text
                    style={[styles.term, styles.highline]}
                    onPress={showWebviewTC}>
                    {t('common.t&c.policy')}
                  </Text>
                  {t('home.econsent.decree.confirm2')}
                </Text>
              </View>
              <View style={styles.confirmbox}>
                <Button
                  style={styles.flex}
                  id="DecreeEConsent.btnConfirm"
                  text={t('home.econsent.confirm')}
                  onPress={() => {
                    navigation.navigate(
                      ROUTE_PATH.DECREE_ECONSENT_OTP as never,
                    );
                    NativeModuleClient.AdobeExperience.trackButton({
                      screen: ROUTE_PATH.DECREE_ECONSENT,
                      unixTimestamp: dayjs().unix(),
                      buttonText: 'Confirm',
                    });
                  }}
                  disabled={!confirm.checked}
                />
              </View>
            </View>
          </View>
        </View>
        <PdfViewer
          ref={tcViewerRef}
          uri={t('home.econsent.t&c.url')}
          type="url"
        />
      </ScrollView>
    </SafeAreaView>
  );
};

export default DecreeEConsent;
