import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  SAVE_NOTIFICATION,
  SAVE_NOTIFICATION_BROADCAST,
  SAVE_PAGING,
  LOADING_LOAD_MORE_SUCCESS,
  LOADING_LOAD_MORE_REQUEST,
  SAVE_NOTIFICATION_UNREAD,
  IS_LOAD_MORE_GENERAL,
  IS_LOAD_MORE_BROADCAST,
} from './constants';
import initialState, { TNotificationState } from './state';

const notificationReducer = (state: TNotificationState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TNotificationState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case LOADING_LOAD_MORE_REQUEST:
        draft.loadingLoadMore = true;
        break;
      case LOADING_LOAD_MORE_SUCCESS:
        draft.loadingLoadMore = false;
        break;
      case SAVE_NOTIFICATION:
        draft.notification.data = action.payload.data;
        draft.notification.totalPages = action.payload.totalPages;
        break;
      case SAVE_NOTIFICATION_BROADCAST:
        draft.notification.dataBroadcast = action.payload.dataBroadcast;
        draft.notification.totalPages = action.payload.totalPages;
        break;
      case SAVE_PAGING:
        draft.notification.paging = action.payload;
        break;
      case SAVE_NOTIFICATION_UNREAD:
        draft.notificationUnRead = action.payload;
        break;
      case IS_LOAD_MORE_GENERAL:
        draft.isLoadMoreGeneral = action.payload;
        break;
      case IS_LOAD_MORE_BROADCAST:
        draft.isLoadMoreBroadcast = action.payload;
        break;
    }
  });
};

export default notificationReducer;
