export interface IErrorMessage {
  [key: string]: any;
}

export interface IResponseError {
  errorCode: string;
  errorMessage: IErrorMessage;
  status?: number;
  code?: string; // okta api
  url?: string;
}

class APIError extends Error {
  public errorCode: string;

  public errorMessage: IErrorMessage;

  public status?: number;

  public code?: string;
  
  public url?: string;

  constructor(responseError: IResponseError) {
    super(responseError.errorCode);
    this.errorCode = responseError.errorCode;
    this.errorMessage = responseError.errorMessage;
    this.status = responseError.status;
    this.code = responseError.code; // okta api
    this.url = responseError.url;
  }
}

export default APIError;
