import { TAction } from '@/stores/types';
import { put, takeLatest, call, select } from 'redux-saga/effects';

import UserService from '@/services/user';
import OneDocService from '@/services/oneDocService';

import {
  loadingClaimELetterDocumentRequest,
  loadingClaimELetterDocumentSuccess,
  loadingClaimELetterRequest,
  loadingClaimELetterSuccess,
  loadingELetterRequest,
  loadingELetterSuccess,
  loadingRequestELetterDocumentRequest,
  loadingRequestELetterDocumentSuccess,
  saveClaimELetterList,
  saveELetterList,
  setLoadMoreClaimELetter,
  setLoadMoreRequestELetter,
  getELetterList,
  getClaimELetterList,
  loadingOtherELetterRequest,
  loadingOtherELetterSuccess,
  saveOtherELetterList,
  setLoadMoreOtherELetter,
  loadingOtherELetterDocumentRequest,
  loadingOtherELetterDocumentSuccess,
  getOtherELetterList,
  asyncGetPdfFileFromId,
  getPdfFileFromId
} from './actions';
import {
  GET_CLAIM_ELETTER_DOCUMENT,
  GET_CLAIM_ELETTER_LIST,
  GET_ELETTER_LIST,
  GET_REQUEST_ELETTER_DOCUMENT,
  LOAD_MORE_CLAIM_ELETTER,
  LOAD_MORE_REQUEST_ELETTER,
  PULL_TO_REFRESH_REQUEST_LIST,
  PULL_TO_REFRESH_CLAIM_LIST,
  GET_OTHER_ELETTER_LIST,
  LOAD_MORE_OTHER_ELETTER,
  GET_OTHER_ELETTER_DOCUMENT,
  PULL_TO_REFRESH_OTHER_LIST,
  GET_PDF_FILE_FROM_ID
} from './constants';
import { makeSelectClaimRequestELetter, makeSelectOtherELetter, makeSelectRequestELetter } from './selectors';
import { onDowloadFilePdf, onJustDowloadFilePdf } from '@/utils/handleFile';


function* handleGetELetter({ payload }: TAction) {
  try {
    yield put(loadingELetterRequest());
    const requestELetterResponse: MODEL.IELetterResponse = yield call(UserService.getELetter, { ...payload });

    yield put(setLoadMoreRequestELetter(!(requestELetterResponse?.data.length < 10)));
    yield put(saveELetterList(requestELetterResponse?.data));
  } catch (error) {
  } finally {
    yield put(loadingELetterSuccess());
  }
}

function* handlePullToRefreshRequestList({ payload, resolve, reject }: TAction) {
  try {
    yield put(getELetterList(payload));
    yield put(setLoadMoreRequestELetter(true));

    // Delay show ui dot loading
    resolve();
  } catch (error) {
    reject('ERROR: Fetching data Request list error');
  } finally {
    yield put(loadingELetterSuccess());
  }
}

function* handlePullToRefreshClaimList({ payload, resolve, reject }: TAction) {
  try {
    yield put(getClaimELetterList(payload));
    yield put(setLoadMoreClaimELetter(true));

    // Delay show ui dot loading
    resolve();
  } catch (error) {
    reject('ERROR: Fetching data Claim list error');
  } finally {
    yield put(loadingClaimELetterSuccess());
  }
}

function* handleLoadMoreRequestEletter({ payload }: TAction) {
  try {
    yield put(loadingELetterRequest());

    const requestELetterResponse: MODEL.IELetterResponse = yield call(UserService.getELetter, { ...payload });
    const requestELetterList: MODEL.IELetter[] = yield select(makeSelectRequestELetter());
    yield put(saveELetterList(requestELetterList?.concat(requestELetterResponse?.data)));

    if (!(requestELetterResponse?.data?.length > 0)) {
      yield put(setLoadMoreRequestELetter(false));
    }
  } catch (error) {
  } finally {
    yield put(loadingELetterSuccess());
  }
}

function* getRequestELetterDocument({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestELetterDocumentRequest());
    const { url, name, id, contentType } = payload;
    if (contentType !== 'application/pdf') {
      const blobUrl: string = yield getPdfFileByRequestId(id);
      yield onDowloadFilePdf(name, blobUrl, reject, resolve, false);
    } else {
      yield onDowloadFilePdf(name, url, reject, resolve, false);
    }
  } catch (error) {
  } finally {
    yield put(loadingRequestELetterDocumentSuccess());
  }
}

function* handleGetClaimELetter({ payload }: TAction) {
  try {
    yield put(loadingClaimELetterRequest());

    const claimELetterResponse: MODEL.IELetterResponse = yield call(UserService.getELetter, { ...payload });
    yield put(saveClaimELetterList(claimELetterResponse?.data));
  } catch (error) {
  } finally {
    yield put(loadingClaimELetterSuccess());
  }
}

