function* handlePullToRefreshOtherList({ payload, resolve, reject }: TAction) {
  try {
    yield put(getOtherELetterList(payload));
    yield put(setLoadMoreOtherELetter(true));

    resolve();
  } catch (error) {
    reject('ERROR: Fetching data Other list error');
  } finally {
    yield put(loadingOtherELetterSuccess());
  }
}

function* getPdfFileByRequestId (url: string) {
  try {
    const documentPdfResponse: MODEL.IDocumentPdf = yield call(OneDocService.getFilePdfUrlByLetterIdService, url);
    return documentPdfResponse?.url || ''
  } catch (error) {
    return ''
  } finally {
  } 
}


function* eLetterHistorySaga() {
  //request
  yield takeLatest(GET_ELETTER_LIST, handleGetELetter);
  yield takeLatest(LOAD_MORE_REQUEST_ELETTER, handleLoadMoreRequestEletter);
  yield takeLatest(GET_REQUEST_ELETTER_DOCUMENT, getRequestELetterDocument);
  yield takeLatest(PULL_TO_REFRESH_REQUEST_LIST, handlePullToRefreshRequestList);
  //claim
  yield takeLatest(GET_CLAIM_ELETTER_LIST, handleGetClaimELetter);
  yield takeLatest(LOAD_MORE_CLAIM_ELETTER, handleLoadMoreClaimEletter);
  yield takeLatest(GET_CLAIM_ELETTER_DOCUMENT, getClaimELetterDocument);
  yield takeLatest(PULL_TO_REFRESH_CLAIM_LIST, handlePullToRefreshClaimList);
  //other 
  yield takeLatest(GET_OTHER_ELETTER_LIST, handleGetOtherELetter);
  yield takeLatest(LOAD_MORE_OTHER_ELETTER, handleLoadMoreOtherEletter);
  yield takeLatest(GET_OTHER_ELETTER_DOCUMENT, getOtherELetterDocument);
  yield takeLatest(PULL_TO_REFRESH_OTHER_LIST, handlePullToRefreshOtherList);

  // yield takeLatest(GET_PDF_FILE_FROM_ID, getPdfFileByRequestId);

}

export default eLetterHistorySaga;
