import React, { useEffect, useState } from 'react';
import { Image, SafeAreaView, FlatList, View, RefreshControl } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationProp } from '@react-navigation/stack';
import { Icon, InnerLoading, useTheme } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import dayjs from 'dayjs';
import { useNetInfo } from '@react-native-community/netinfo'
;
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import { TStackParamList } from '@/navigators';
import { formatToCurrencyComma } from '@/utils/utils';
import { Theme } from '@/config/themes.config';

import useStyles from './styles';
import { getPaymentTransaction, savePaymentTransaction } from '../../stores/actions';
import { makeLoadingPaymentTransaction, makePaymentTransaction, makeSelectTransactionFilter, makeSelectTriggerFilterFlag } from '../../stores/selectors';


type PaymentHistoryNavigationProp = StackNavigationProp<TStackParamList, 'TransactionHistory'>;

const STATUS_TRANSACTION_TYPE = {
  SUCCESS: 'Success',
  PROCESSING: 'Processing',
};

const FORMAT_DATE = 'YYYY-MM-DD:HH:mm:ssZZ';

const PaymentHistory = () => {
  const navigation = useNavigation<PaymentHistoryNavigationProp>();
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const dispatch = useDispatch();
  const paymentTransactions = useSelector(makePaymentTransaction());
  const loading = useSelector(makeLoadingPaymentTransaction());
  const transactionFilter = useSelector(makeSelectTransactionFilter());
  const triggerFilterFlag = useSelector(makeSelectTriggerFilterFlag());
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const netInfo = useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  const checkTypeTransaction = (status: string) => {
    switch (status) {
      case STATUS_TRANSACTION_TYPE.SUCCESS:
        return styles.successStatus;
      case STATUS_TRANSACTION_TYPE.PROCESSING:
        return styles.processingStatus;
      default:
        return styles.successStatus;
    }
  };

  const onPullToRequest = async () => {
    try {
      setRefreshing(true);
      await dispatch(getPaymentTransaction());
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const renderPaymentHistory = ({ item }: { item: MODEL.IPaymentTransaction }) => {
    return (
      <View style={styles.transactionWrapper} key={item?.id}>
        <Icon name="Application" size={24} color="#5B636B" />
        <View style={styles.transactionContent}>
          <Text style={styles.transactionName} numberOfLines={2}>
            {item?.type?.fullName} - {item?.policyNumber}
          </Text>
          {item?.paymentDate && <View style={styles.wrapperTime}>
            <Text style={styles.time}>{dayjs(item?.paymentDate, FORMAT_DATE).format('DD/MM/YYYY h:mm A')}</Text>
          </View>}
          <View style={styles.wrapperBottomContent}>
            <Text style={styles.status}>
              {t('home.transaction.history.status')}
              <Text style={styles.successStatus}>{t('home.transaction.history.request.success')}</Text>
            </Text>
            <Text style={styles.money}>{formatToCurrencyComma(item.premium)} đ</Text>
          </View>
        </View>
      </View>
    );
  };

  useEffect(() => {
    dispatch(getPaymentTransaction());
    return () => {
      dispatch(savePaymentTransaction([]));
    };
  }, []);

  useEffect(() => {
    if (triggerFilterFlag) { 
      dispatch(getPaymentTransaction());
    }
  }, [transactionFilter])

  if ((!(paymentTransactions?.length > 0) && loading) || (triggerFilterFlag && loading)) {
    return (
      <View style={styles.wrapperLoading}>
        <InnerLoading gifStyle={styles.loadGif} />
      </View>
    );
  }

  return (
    <SafeAreaView style={styles.flex1}>
      {isConnected ? (
        <>
          {paymentTransactions.length > 0 ? (
            <FlatList
              style={styles.container}
              data={paymentTransactions}
              keyExtractor={item => item.id}
              showsVerticalScrollIndicator={false}
              renderItem={renderPaymentHistory}
              ListFooterComponent={<View style={styles.mb20} />}
              refreshControl={<RefreshControl tintColor={theme.colors.primary} refreshing={isRefreshing} onRefresh={onPullToRequest} />}
            />
          ) : (
            <View style={styles.wrapperImageNodata}>
              <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
              <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
            </View>
          )}
        </>
      ) : (
        <NoConnection />
      )}
    </SafeAreaView>
  );
};

export default PaymentHistory;
