import React, { forwardRef, useImperativeHandle } from 'react';
import { View, ViewStyle } from 'react-native';
import { Button, Icon } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';

import { BIOMETRY_TYPE } from '@/utils/constants';
import Text from '@/components/Text';

import styles from './styles';
import Modal, { useModal } from '../Modal/Modal';

interface ActiveBioModalPropsType {
  stylesContainer?: ViewStyle;
  showPasswordModal: () => void;
  biometryType?: string | null;
  onMode?: boolean;
  handleBioOff: () => void;
  [key: string]: any;
}

export interface ActiveBioModalRef {
  open: () => void;
  close: () => void;
}

const ActiveBioModal = (props: ActiveBioModalPropsType, ref: React.Ref<unknown> | undefined) => {
  const { showPasswordModal, biometryType, onMode, handleBioOff } = props;
  const { t } = useTranslation();
  const [modalRef, componentModal] = useModal();

  const handleShowPasswordModal = () => {
    showPasswordModal();
    componentModal()?.close();
  };

  const handleBioToOff = async () => {
    if (handleBioOff) {
      await handleBioOff();
      componentModal()?.close();
    }
  };

  useImperativeHandle(
    ref,
    (): ActiveBioModalRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal()?.close(),
    }),
  );

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer}>
      {onMode ? (
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View>
              <View style={styles.imageBox}>
                {biometryType === BIOMETRY_TYPE.FACE ? (
                  <Icon name="Face-ID" size={48} color="#007AFF" />
                ) : (
                  <Icon name="Fingerprint" size={48} color="#007AFF" />
                )}
              </View>
              {biometryType === BIOMETRY_TYPE.FACE ? (
                <Text style={styles.modalMainText} fontFamily="bold">
                  {t('profile.account.setting.modal.active.faceid')}
                </Text>
              ) : (
                <Text style={styles.modalMainText} fontFamily="bold">
                  {t('profile.account.setting.modal.active.touchid')}
                </Text>
              )}
              {biometryType === BIOMETRY_TYPE.FACE ? (
                <Text style={styles.modalSubText}>{t('profile.account.setting.modal.sub.title.faceid')}</Text>
              ) : (
                <Text style={styles.modalSubText}>{t('profile.account.setting.modal.sub.title.touchid')}</Text>
              )}
            </View>
            <View style={styles.buttonBox}>
              <Button
                text={t('profile.account.setting.modal.button.active')}
                type="secondary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                style={styles.topButton}
                onPress={handleShowPasswordModal}
                id="ModalActiveBio.btnActiveBioOnMode"
              />
              <Button
                text={t('profile.account.setting.modal.button.cancel')}
                type="secondary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                style={styles.bottomButton}
                onPress={() => {
                  componentModal().close();
                }}
                id="ModalActiveBio.btnCancelActiveBioOnMode"
              />
            </View>
          </View>
        </View>
      ) : (
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View>
              <View style={styles.imageBox}>
                {biometryType === BIOMETRY_TYPE.FACE ? (
                  <Icon name="Face-ID" size={48} color="#007AFF" />
                ) : (
                  <Icon name="Fingerprint" size={48} color="#007AFF" />
                )}
              </View>
              {biometryType === BIOMETRY_TYPE.FACE ? (
                <Text style={styles.modalMainText} fontFamily="bold">
                  {t('profile.account.setting.modal.confirm.enable.faceid')}
                </Text>
              ) : (
                <Text style={styles.modalMainText} fontFamily="bold">
                  {t('profile.account.setting.modal.confirm.enable.touchid')}
                </Text>
              )}
            </View>
            <View style={styles.buttonBox}>
              <Button
                text={t('profile.account.setting.modal.button.deactive')}
                type="secondary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                style={styles.topButton}
                onPress={handleBioToOff}
                id="ModalActiveBio.btnActiveBioNotOnMode"
              />
              <Button
                text={t('profile.account.setting.modal.button.cancel')}
                type="secondary"
                uppercase={false}
                labelStyle={styles.modalButtonText}
                size="large"
                style={styles.bottomButton}
                onPress={() => {
                  componentModal().close();
                }}
                id="ModalActiveBio.btnCancelActiveBioNotOnMode"
              />
            </View>
          </View>
        </View>
      )}
    </Modal>
  );
};

export default forwardRef(ActiveBioModal);
