import { takeLatest, call, put, all } from 'redux-saga/effects';
import { TAction } from '@/stores/types';
import AuthService from '@/services/auth';
import UserService from '@/services/user';

import APIError from '@/utils/errors';
import { ECONSENT_ERROR } from '@/utils/constants';

import { SUBMIT_ECONSENT, OTP_REQUEST_ECONSENT } from './constants';
import { loadingRequestAction, loadingSuccessAction, optRequesetErrorAction, submitEConsentErrorAction } from './actions';
import { getMembership } from '@/stores/global/actions';

const filterError = (status?: number) => {
  switch (status) {
    case 400:
      return ECONSENT_ERROR.OTP_INVALID;
    case 404:
      return ECONSENT_ERROR.MEMBERSHIP_INVALID;
    default:
      return ECONSENT_ERROR.SYSTEM_ERROR;
  }
};

function* handleSubmitEconsent({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestAction());
    yield call(UserService.submitEConsentService, payload);
    yield put(getMembership());
    resolve();
  } catch (error) {
    reject(error);
    const errorType = filterError((error as APIError).status);
    yield put(submitEConsentErrorAction({ errorType }));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleRequestOtp({ payload }: TAction) {
  try {
    const { params, callback } = payload;
    yield all([
      put(loadingRequestAction()),
      put(submitEConsentErrorAction({ errorType: '' })),
      put(optRequesetErrorAction({ status: false, errorType: '' })),
    ]);
    const response: MODEL.IRequestMyOtp = yield call(AuthService.requestOtpLogin, params);
    callback && callback(response);
  } catch (error) {
    yield put(optRequesetErrorAction({ status: true, errorType: (error as APIError).message }));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* econsentSaga() {
  yield takeLatest(SUBMIT_ECONSENT, handleSubmitEconsent);
  yield takeLatest(OTP_REQUEST_ECONSENT, handleRequestOtp);
}

export default econsentSaga;
