import React, { useEffect, useLayoutEffect, useState } from 'react';
import { Controller, useForm } from 'react-hook-form';
import NetInfo from '@react-native-community/netinfo';
import { useTranslation } from 'react-i18next';
import {
  BackHandler,
  Keyboard,
  KeyboardAvoidingView,
  Platform,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import { Button, Icon, NativeModuleClient, TextInput, theme } from 'superapp-common-component';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { useDispatch } from 'react-redux';
import dayjs from 'dayjs';

import NoConnection from '@/components/NoConnection';
import Text from '@/components/Text';
import { TStackParamList } from '@/navigators';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import { a11y } from '@/utils/utils';
import ROUTE_PATH from '@/navigators/routePath';
import { logout } from '@/stores/authentication/actions';

import styles from './styles';

const { colors } = theme;

type InputPhoneNavigationProp = StackNavigationProp<TStackParamList, 'InputPhone'>;
type InputPhoneProps = {};

const InputPhone = (props: InputPhoneProps) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const netInfo = NetInfo.useNetInfo();
  const navigation = useNavigation<InputPhoneNavigationProp>();
  const [isConnected, setConnected] = useState<null | boolean>(null);

  const {
    control,
    formState: { errors },
    handleSubmit,
  } = useForm({
    defaultValues: {
      phoneNumber: '',
    },
  });

  const hideKeyboard = () => Keyboard.dismiss();

  const onSubmit = (data: { phoneNumber: string }) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.INPUT_PHONE,
      unixTimestamp: dayjs().unix(),
      buttonText:'Continue'
    });
    navigation?.navigate(ROUTE_PATH.NEW_PHONE_OTP, { newUsername: data?.phoneNumber });
  };

  const goback = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.INPUT_PHONE,
      unixTimestamp: dayjs().unix(),
      buttonText:'Back to home'
    });
    dispatch(logout({ neededCallApi: true }));
    navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
    return true;
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={HEADER_TITLE}>{t('migration.input.phone.header')}</Text>,
      headerLeft: () => (
        <TouchableOpacity {...a11y('InputPhone.btnInputPhoneGoback')} onPress={goback} hitSlop={HITSLOP}>
          <Icon name="Arrow-Left" size={24} color={colors.primary.charcoal[80]} style={styles.marginLeft21} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
      headerTitleStyle: styles.mainTitle,
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    BackHandler.addEventListener('hardwareBackPress', goback);

    return () => BackHandler.removeEventListener('hardwareBackPress', goback);
  }, []);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <View style={styles.flex1}>
      <SafeAreaView style={styles.safeAreaViewStyle}>
        <TouchableWithoutFeedback {...a11y('InputPhone.btnHideKeyboard')} onPress={hideKeyboard}>
          <View style={StyleSheet.flatten([styles.container])}>
            <KeyboardAvoidingView
              behavior={Platform.OS === 'ios' ? 'padding' : undefined}
              style={styles.flex1}
              keyboardVerticalOffset={Platform.OS === 'ios' ? 90 : 0}
            >
              <View style={styles.modalBox}>
                <View style={styles.flex1}>
                  <ScrollView>
                    <View style={styles.formContainer}>
                      <Text style={styles.formTitle} fontFamily="bold">
                        {t('migration.input.phone.main.title')}
                      </Text>
                      <Text style={styles.subFormTitle}>{t('migration.input.phone.sub.title')}</Text>
                      <View>
                        <Controller
                          control={control}
                          rules={{
                            required: { value: true, message: t('migration.input.phone.error.text') },
                          }}
                          render={({ field: { onChange, value } }) => (
                            <TextInput
                              label={t('migration.input.phone.label')}
                              type={errors?.phoneNumber ? 'error' : 'info'}
                              disabled={false}
                              onChangeText={onChange}
                              value={value}
                              message={errors?.phoneNumber && errors?.phoneNumber?.message}
                              autoFocus
                              id="InputPhone.ipPhoneNumber"
                            />
                          )}
                          name="phoneNumber"
                        />
                      </View>
                    </View>
                  </ScrollView>
                </View>

                <View style={[styles.buttonBox, styles.marginBottom16]}>
                  <Button
                    text={t('migration.input.phone.main.button')}
                    onPress={handleSubmit(onSubmit)}
                    uppercase={false}
                    type="primary"
                    labelStyle={styles.buttonText}
                    size="large"
                    style={styles.button}
                    id="InputPhone.btnInputPhoneVerify"
                  />
                </View>
              </View>
            </KeyboardAvoidingView>
          </View>
        </TouchableWithoutFeedback>
      </SafeAreaView>
    </View>
  ) : (
    <View style={styles.flex1}>
      <NoConnection />
    </View>
  );
};

export default InputPhone;
