import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class OneCommunicationService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ONE_COMMUNICATION);
  }

  public getAllNotification = (params: MODEL.IGetNoti): Promise<MODEL.INotification[]> =>
    this.instance.post('/notification/user/getAll', params);

  public getAllGeneralNotification = (params: MODEL.IGetNoti): Promise<MODEL.INotification[]> =>
    this.instance.post('/notification/getAllGeneral', params, { headers: {Authorization: " "} });

  public readAllNotification = (): Promise<any> => this.instance.post('/notification/readAll');

  public readNotificationById = (params: [{ id: string }]): Promise<MODEL.INotification[]> =>
    this.instance.post('/notification/read', params);

  public deleteNotificationById = (params: [{ id: string }]): Promise<MODEL.INotification[]> =>
    this.instance.post('/notification/delete', params);

  public getNotificationUnRead = (): Promise<MODEL.INotificationUnRead> => this.instance.post('/notification/user/count');
  
}

export default new OneCommunicationService();
