import { takeLatest, call, put, all } from 'redux-saga/effects';
import { Platform } from 'react-native';
import jwt_decode from 'jwt-decode';
import _intersection from 'lodash/intersection';

import { TAction } from '@/stores/types';
import { loadingRequestAction, loadingSuccessAction } from './actions';
import VerifyAccountService from '@/services/verifyAccount';
import APP_ENV from '@/config/app.config';
import AuthService from '@/services/auth';
import { getItem, setItem } from '@/utils/storage';
import { REGEX, STORAGE_NAME } from '@/utils/constants';
import UserService from '@/services/user';
import { navigationRef } from '@/navigators/navigation';
import { saveProfile } from '@/stores/global/actions';
import { isEmpty, setStorageToken, setVitalityStorageToken } from '@/utils/utils';
import { loginSuccess } from '@/stores/authentication/actions';
import ROUTE_PATH from '@/navigators/routePath';
import roles from '@/utils/roles';

import { SUBMIT_VERIFY_ACCOUNT_QRCODE } from './constants';

const { CLIENT_ID, OKTA_CLIENT_ID } = APP_ENV;

function* handleSubmitVerifyAccount({ payload }: TAction) {
  try {
    const { callback, callbackError, ...restProps } = payload;
    yield put(loadingRequestAction());
    yield call(VerifyAccountService.submitPreVerifyAccount, { ...restProps });

    // Enhance Logout
    const token: string = yield getItem<string>(STORAGE_NAME.VITALITY_LOGIN_TOKEN, '');
    const refreshToken: string = yield getItem<string>(STORAGE_NAME.REFRESH_TOKEN, '');
    const refreshTokenOkta: string = yield getItem<string>(STORAGE_NAME.REFRESH_TOKEN_OKTA, '');
    const username: string = yield getItem<string>(STORAGE_NAME.USERNAME, '');

    const responseRefreshToken: MODEL.IAuthenticate = yield call(AuthService.refreshTokenService, {
      clientId: CLIENT_ID,
      refreshToken,
      refreshTokenOkta,
      clientIdOkta: OKTA_CLIENT_ID,
      deviceId: {
        os: Platform.OS,
        notificationToken: token,
      },
    });

    const migrateParser = jwt_decode<MODEL.ITokenDecode>(responseRefreshToken.accessToken!);
    const isMigrated = migrateParser?.['x-forceChangeUsername'] === '1';
    const userNameIsNotPhoneNumber = !REGEX.PHONE_NUMBER.test(migrateParser?.['x-username']);
    yield setStorageToken(responseRefreshToken);
    yield setVitalityStorageToken({
      expires_in: responseRefreshToken.accessTokenOktaExpirySeconds,
      access_token: responseRefreshToken.accessTokenOkta,
      refresh_token: responseRefreshToken.refreshTokenOkta,
    });

    const responseProfile: MODEL.IProfileResponse = yield call(UserService.getProfileService);
    const hasRolePO = _intersection(responseProfile.data?.roles, [roles?.POLICY_OWNER, roles?.V_POLICY_OWNER]).length > 0;

    yield put(saveProfile(responseProfile.data));

    yield setItem(STORAGE_NAME.FULL_NAME, JSON.stringify(responseProfile?.data?.fullName));
    yield setItem(STORAGE_NAME.FIRST_NAME, JSON.stringify(responseProfile?.data?.firstName || ''));

    //  // Force process P820
    // if (response.processP820?.forceP820) {
    //   yield all([put(saveVerifyAccount(response)), put(loginSuccess(responseRefreshToken.accessToken))]);
    //   navigationRef.navigate(ROUTE_PATH.VERIFY_ACCOUNT_P820 as never);
    //   return;
    // }

    if (isMigrated && !userNameIsNotPhoneNumber && hasRolePO) {
      if (!isEmpty(responseProfile.data.mobile) && username !== responseProfile.data.mobile) {
        navigationRef.navigate(ROUTE_PATH.UPDATE_CONTACT_CONFIRMATION as any);
      } else {
        navigationRef.navigate(ROUTE_PATH.NO_PHONE_CONTACT as any);
      }
    } else {
      callback();
    }

    yield all([put(loginSuccess(responseRefreshToken.accessToken))]);
  } catch (error) {
    const { callbackError } = payload;
    callbackError();
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* verifyAccountQRCodeSaga() {
  yield takeLatest(SUBMIT_VERIFY_ACCOUNT_QRCODE, handleSubmitVerifyAccount);
}

export default verifyAccountQRCodeSaga;
