import axios, { AxiosError, AxiosInstance, AxiosResponse, AxiosRequestConfig } from 'axios';

import { logout as logoutAction } from '@/stores/authentication/actions';
import APP_ENV from '@/config/app.config';

import APIError, { IResponseError } from './errors';
import { getItem } from './storage';
import { STATUS_CODE, STORAGE_NAME } from './constants';
import { isEmpty, setStorageToken, setVitalityStorageToken } from './utils';

const { CLIENT_ID, APPLICATION_API_CIAM, OKTA_CLIENT_ID } = APP_ENV;

export default class VitalityRequest {
  protected instance: AxiosInstance;

  protected readonly baseURL: string;

  private TIME_OUT: number = 30000;

  private MAX_RETRY_REFRESH_TOKEN: number = 2;

  private isRefreshingToken: boolean = false;

  private subscribersOriginalRequest: any[] = [];

  private retryRefreshToken: number = 0;

  private readonly URL_REFRESH_TOKEN = `${APPLICATION_API_CIAM}/refreshToken`;

  public constructor(baseURL: string, headers = {}) {
    this.baseURL = baseURL;
    this.instance = axios.create({ baseURL, timeout: this.TIME_OUT, headers: { ...headers, 'Cache-Control': 'no-store' } });
    this.initializeRequestInterceptor();
    this.initializeResponseInterceptor();
  }

  private initializeRequestInterceptor = () => {
    this.instance.interceptors.request.use(this.handleRequest);
  };

  private initializeResponseInterceptor = () => {
    this.instance.interceptors.response.use((response: AxiosResponse<any>) => response.data, this.handleError);
  };

  private handleRequest = async (config: AxiosRequestConfig) => {
    const accessToken = await getItem<string>(STORAGE_NAME.VITALITY_LOGIN_TOKEN, '');
    if (accessToken && !config.headers!.Authorization) {
      config.headers!.Authorization = `Bearer ${accessToken}`;
    }
    return config;
  };

  private handleError = async (error: AxiosError) => {
    const originalRequest = error.config;
    let accessToken = await getItem<string>(STORAGE_NAME.VITALITY_LOGIN_TOKEN, '');
    if (error.response?.status === STATUS_CODE.UNAUTHORIZED && !isEmpty(accessToken)) {
      if (!this.isRefreshingToken) {
        this.isRefreshingToken = true;
        this.refreshToken();
      }

      return new Promise(resolve =>
        this.subscribeRequest(async () => {
          accessToken = await getItem<string>(STORAGE_NAME.VITALITY_LOGIN_TOKEN, '');
          originalRequest.headers!.Authorization = `Bearer ${accessToken}`;
          resolve(this.instance(originalRequest));
        }),
      );
    }

    const dataError: IResponseError = error.response?.data;
    if (!isEmpty(dataError)) {
      throw new APIError(dataError);
    }

    throw error;
  };

  private subscribeRequest: any = (callback: () => null) => this.subscribersOriginalRequest.push(callback);

  private executeRequests = () => {
    this.subscribersOriginalRequest.map(executeRequest => executeRequest());
  };

  public async refreshToken(): Promise<void> {
    try {
      this.retryRefreshToken = this.retryRefreshToken + 1;
      const refreshToken = await getItem<string>(STORAGE_NAME.REFRESH_TOKEN, '');
      const refreshTokenOkta = await getItem<string>(STORAGE_NAME.REFRESH_TOKEN_OKTA, '');

      const response = await axios.post<MODEL.IAuthenticate>(this.URL_REFRESH_TOKEN, {
        clientId: CLIENT_ID,
        clientIdOkta: OKTA_CLIENT_ID,
        refreshToken,
        refreshTokenOkta,
      });
      await setStorageToken(response.data);
      await setVitalityStorageToken({
        expires_in: response.data?.accessTokenOktaExpirySeconds,
        access_token: response.data?.accessTokenOkta,
        refresh_token: response.data?.refreshTokenOkta,
      });
      this.executeRequests();
    } catch (error) {
      if (this.retryRefreshToken <= this.MAX_RETRY_REFRESH_TOKEN) {
        // eslint-disable-next-line @typescript-eslint/return-await
        return this.refreshToken();
      }
      this.retryRefreshToken = 0;
      reduxStore.dispatch(logoutAction({ neededCallApi: false }));
      throw error;
    } finally {
      this.isRefreshingToken = false;
      this.subscribersOriginalRequest = [];
    }
  }
}
