import { useNavigation } from '@react-navigation/native';
import React, { useCallback, useLayoutEffect, useRef, useEffect, useState, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { View, SafeAreaView, ScrollView, RefreshControl, TouchableOpacity } from 'react-native';
import { Button, Loading, useTheme, NativeModuleClient } from 'superapp-common-component';
import { connect, useDispatch, useSelector } from 'react-redux';
import { StackNavigationProp, StackNavigationEventMap } from '@react-navigation/stack';
import NetInfo from '@react-native-community/netinfo';
import _forEach from 'lodash/forEach';
import _intersection from 'lodash/intersection';
import _values from 'lodash/values';
import { Dispatch } from 'redux';
import { createStructuredSelector } from 'reselect';
import DeviceInfo from 'react-native-device-info';

import MiniAppEventEmitter from '@/utils/miniAppEventListener';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import { getCategoriesCounts, normalizeHealthReportListDataFromAEM } from '@/utils/vitalityAssessment';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import { HEALTH_LIST } from '@/utils/VitalityResponseParser/AEMVitalityResponseParser/config';
import {
  APPS_NAME,
  NAVIGATION_VITALITY,
  STORAGE_NAME,
  VALID_ROLES,
  MINI_APP_EVENTS_EMITTER,
  MEMBERSHIP_VITALITY_TYPE,
  JS_EVENT_NAMES,
  EVENTS_USER_TRACKING,
} from '@/utils/constants';
import { getItem } from '@/utils/storage';
import roles from '@/utils/roles';
import { getHealthReportDescriptionText, normalizeHealthReportCardsData } from '@/utils/vitalityAssessment';
import { HealthReportListType } from '@/utils/vitalityAssessment/types';

import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import { logAction, pullToRefreshProfile, userTracking } from '@/stores/global/actions';
import { Theme } from '@/config/themes.config';
import { makeSelectMembership, makeSelectProfile } from '@/stores/global/selectors';
import { TConnectState } from '@/stores/types';
import { NS_VITALITY } from '@/stores/vitality/constants';
import vitalityReducer from '@/stores/vitality/reducer';
import vitalitySaga from '@/stores/vitality/saga';
import {
  makeSelectHealthReportFeedback,
  makeSelectLinkedTrackers,
  makeSelectHealthReportLoading,
  makeSelectLinkedTrackersLoading,
} from '@/stores/vitality/selectors';
import { HealthReportItemSummary, TVitalityState } from '@/stores/vitality/state';
import {
  fetchHealthReportFeedback as fetchHealthReportFeedbackAction,
  initLinkedTrackers as initLinkedTrackersAction,
} from '@/stores/vitality/actions';
import { canOpenVitality, checkIsTokenExpired, getVitalityMembershipIdFromToken, hasMaintenanceVitality, isEmpty } from '@/utils/utils';
import vitalityService from '@/services/vitalityService';

import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import MyProfile from './components/MyProfile';
import Tracker from './components/Tracker';
import AccountSetting from './components/AccountSetting';
import HealthReport from './components/HealthReport';
import About from './components/About';
import ModalLogOut, { ModalLogOutRef } from './components/ModalLogOut';
import ModalVersion, { ModalVersionRef } from './components/ModalVersion';
import HelpCenter from './components/HelpCenter';
import { healthReportItemKeys } from './constants';
import ModalPermissionVitality, { ModalPermissionVitalityRef } from '@/components/ModalPermissionVitality';

import useStyles from './styles';
import dayjs from 'dayjs';
import jsEventEmitter from '@/utils/jsEventEmitter';
import WebView, { TWebViewUrlRef } from '@/components/WebView';

type ProfileNavigationProp = StackNavigationProp<TStackParamList, 'Profile'> & { jumpTo: (name: string, option?: {}) => void };

type ProfilePropsType = MapDispatchToPropsType & CreateStructuredSelectorType;

const appVersion = DeviceInfo.getVersion();

const Profile: React.FC<ProfilePropsType> = ({
  healthReportFeedback,
  healthReportLoading,
  linkedTrackersLoading,
  linkedTrackers,
  fetchHealthReportFeedback,
  initLinkedTrackers,
}) => {
  useInjectReducer({ key: NS_VITALITY, reducer: vitalityReducer });
  useInjectSaga({ key: NS_VITALITY, saga: vitalitySaga });
  const [isLoadingOpenApp, setLoadingOpenMiniApp] = useState<boolean>(false);
  const webviewRef = useRef<TWebViewUrlRef>(null);

  const { t, i18n } = useTranslation();

  const navigation = useNavigation<ProfileNavigationProp>();
  const netInfo = NetInfo.useNetInfo();
  const dispatch = useDispatch();

  const modalLogOutRef = useRef<null | ModalLogOutRef>(null);
  const modalVersionRef = useRef<null | ModalVersionRef>(null);
  const { isLoged } = useSelector(makeSelectCheckLogin());
  const membershipVitality = useSelector(makeSelectMembership());
  const profile = useSelector(makeSelectProfile());
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const [onShowHealthReportSection, setOnShowHealthReportSection] = useState(false);
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [isMaintenance, setMaintenance] = useState(false);
  const modalMaintainRef = useRef<ModalConfirmErrorRef>();
  const [membership, setMembershipId] = useState('');
  const modalPermissionVitalityRef = useRef<ModalPermissionVitalityRef>();

  const isVitalityMember = useMemo(
    () => _intersection(profile?.roles, [...VALID_ROLES.VITALITY, roles.V_CUSTOMER_NON_IP]).length > 0,
    [profile?.roles],
  );

  const showVitalityFeatures = isLoged && isVitalityMember && onShowHealthReportSection;

  const onPressContact = () => {
    navigation.navigate(ROUTE_PATH.CONTACT);
  };

  const showModalLogout = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Logout',
    });
    modalLogOutRef?.current?.open();
  };

  const handleInitVitalityData = (isRefresh?: boolean) => {
    if (showVitalityFeatures) {
      fetchHealthReportFeedback(isRefresh);
      initLinkedTrackers(isRefresh);
    }
  };

  const handleEventListener = (payload: string = '') => {
    const parserPayload = JSON.parse(payload);
    if (parserPayload?.type === 'reloadLinkedTrackers') {
      initLinkedTrackers(true);
    } else if (parserPayload?.type === 'setVitalityDoneTutorial') {
      setOnShowHealthReportSection(true);
    } else {
      // Do Nothing
    }
  };

  // useLayoutEffect(() => {
  //   navigation.setOptions({
  //     title: t('profile.header.account'),
  //     headerLeft: () => (
  //       <TouchableOpacity style={styles.marginLeft16} onPress={() => navigation.canGoBack() && navigation.goBack()} hitSlop={HITSLOP}>
  //         <Icon name="Arrow-Down-1" color={colors.white} size={24} />
  //       </TouchableOpacity>
  //     ),
  //   });
  // }, [navigation, t]);

  const headerLeft = () => (
    <View style={styles.header}>
      <Text style={styles.textHeader}>{t('profile.header.account')}</Text>
    </View>
  );

  useLayoutEffect(() => {
    navigation.setOptions({
      header: () => headerLeft(),
    } as Partial<StackNavigationEventMap>);
  }, []);

  useEffect(() => {
    handleInitVitalityData();
  }, [showVitalityFeatures]);

  useEffect(() => {
    // init
    (async () => {
      const isDoneTutorial = await getItem<boolean>(STORAGE_NAME.VITALITY_DONE_TUTORIAL, false);

      setOnShowHealthReportSection(isDoneTutorial);
    })();

    // case back form vitality
    const subscription = MiniAppEventEmitter?.addListener(MINI_APP_EVENTS_EMITTER.MINI_APP_EVENT_EMITTER, handleEventListener);
    return () => {
      subscription?.remove();
    };
  }, []);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  const getHealthReportHealthReportListSummary = () => {
    const reportList = i18n.getDataByLanguage(i18n.language)?.translation[HEALTH_LIST];

    if (reportList && typeof reportList !== 'string') {
      const list = normalizeHealthReportListDataFromAEM(reportList as HealthReportListType);
      const result: Record<string, HealthReportItemSummary> = {};
      _forEach(list, (reportItem, reportItemKey) => {
        const { categoryList = [] } = reportItem;
        result[reportItemKey] = {
          ...reportItem,
          ...getCategoriesCounts(categoryList, healthReportFeedback),
        };
      });
      return result;
    }
    return {};
  };

  const healthReportOverviewData = useMemo(() => {
    const healthReportList = getHealthReportHealthReportListSummary();
    if (!healthReportList || !healthReportFeedback) {
      return [];
    }
    const {
      actualAge,
      vitalityAge: { age },
    } = healthReportFeedback;
    const diffYears = age - actualAge;
    const healthReportCardsData = normalizeHealthReportCardsData(healthReportItemKeys, healthReportList, t);
    const vitalityAgeProps = { left: <Text style={styles.vitalityAge}>{age}</Text> };

    const vitalityAgeCardData = {
      title: t('healthReport.vitalityAge'),
      description: getHealthReportDescriptionText(age, diffYears, i18n.language, t),
      ...(age
        ? vitalityAgeProps
        : {
            iconGroup: 'healthReports',
            iconName: 'Outline',
          }),
      shouldShowRedDot: diffYears > 0,
    };

    return [vitalityAgeCardData, ...healthReportCardsData];
  }, [t, healthReportFeedback]);

  const checkVitalityTokenBeforeOpenMiniApp = async () => {
    const isTokenExpire = await checkIsTokenExpired();
    if (isTokenExpire) {
      await vitalityService.refreshVitalityToken();
    }
  };

  const navigateToHealthReport = useCallback(async () => {
    try {
      setLoadingOpenMiniApp(true);
      dispatch(logAction('ACCESS_VITALITY'));

      const hasMaintenance = await hasMaintenanceVitality();
      if (hasMaintenance) {
        setMaintenance(true);
        throw new Error('app maintenance');
      }

      const errorValidate = canOpenVitality(membershipVitality, true);

      if (!isEmpty(errorValidate)) {
        modalPermissionVitalityRef.current?.open();
        return;
      }

      if (
        membershipVitality &&
        membership &&
        _values(MEMBERSHIP_VITALITY_TYPE).includes(membershipVitality.type) &&
        membershipVitality.hasEConsent !== true
      ) {
        navigation.navigate(ROUTE_PATH.ECONSENT as never);
        return;
      }

      if (membershipVitality && membership && membershipVitality.isDataProtect !== true) {
        navigation.navigate(ROUTE_PATH.DECREE_ECONSENT as never);
        return;
      }

      await checkVitalityTokenBeforeOpenMiniApp();
      await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.VITALITY);
      await NativeModuleClient.MiniApp.loadApp(
        APPS_NAME.VITALITY,
        JSON.stringify({ initialApplicationRouteName: NAVIGATION_VITALITY.HEALTH_REPORT }),
      );
    } catch {
    } finally {
      setLoadingOpenMiniApp(false);
    }
  }, [membershipVitality, membership]);

  const navigateToWorkoutTracker = useCallback(
    async (item?: { name: string; icon: string }, itemKey?: string) => {
      try {
        setLoadingOpenMiniApp(true);

        dispatch(logAction('ACCESS_VITALITY'));
        const hasMaintenance = await hasMaintenanceVitality();
        if (hasMaintenance) {
          setMaintenance(true);
          throw new Error('app maintenance');
        }

        const errorValidate = canOpenVitality(membershipVitality, true);

        if (!isEmpty(errorValidate)) {
          dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.NON_VITALITY_MEMBERSHIP_ICON, timestamp: dayjs().unix() }));
          modalPermissionVitalityRef.current?.open();
          return;
        }

        if (
          membershipVitality &&
          membership &&
          _values(MEMBERSHIP_VITALITY_TYPE).includes(membershipVitality.type) &&
          membershipVitality.hasEConsent !== true
        ) {
          navigation.navigate(ROUTE_PATH.ECONSENT as never);
          return;
        }

        if (membershipVitality && membership && membershipVitality.isDataProtect !== true) {
          navigation.navigate(ROUTE_PATH.DECREE_ECONSENT as never);
          return;
        }

        await checkVitalityTokenBeforeOpenMiniApp();
        await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.VITALITY);
        await NativeModuleClient.MiniApp.loadApp(
          APPS_NAME.VITALITY,
          JSON.stringify({
            initialApplicationRouteName: NAVIGATION_VITALITY.WORKOUT_TRACKERS,
            initialWorkoutTrackerRouteName: NAVIGATION_VITALITY.WORKOUT_TRACKERS_LINKING,
            initDetailRouteName: itemKey,
          }),
        );
      } catch {
      } finally {
        setLoadingOpenMiniApp(false);
      }
    },
    [membershipVitality, membership],
  );

  const onRefreshData = async () => {
    const handleRefreshData = () => new Promise<void>((resolve, reject) => dispatch(pullToRefreshProfile(resolve, reject)));
    try {
      setRefreshing(true);
      handleInitVitalityData(true);
      await handleRefreshData();
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const onDismiss = () => {
    if (isMaintenance) {
      modalMaintainRef.current?.open();
    }
  };

  const openVitalityWeb = () => {
    dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.NON_AIA_VITALITY, timestamp: dayjs().unix() }));
    webviewRef.current?.openURL(t('common.vitality.url'));
  };

  useEffect(() => {
    if (!isEmpty(profile) && isLoged) {
      (async () => {
        const membershipId = await getVitalityMembershipIdFromToken();
        setMembershipId(membershipId);
      })();
    } else {
      setMembershipId('');
    }
    // Set tab Home while change password success
    const initTabHomeAfterChangePasswordSuccess = () => navigation.jumpTo(ROUTE_PATH.HOME);
    jsEventEmitter.addListener(JS_EVENT_NAMES.CHANGE_PASSWORD_SUCCESS, initTabHomeAfterChangePasswordSuccess);
    return () => {
      jsEventEmitter.removeListener(JS_EVENT_NAMES.CHANGE_PASSWORD_SUCCESS, initTabHomeAfterChangePasswordSuccess);
    };
  }, [profile, isLoged]);

  useEffect(() => {
    NativeModuleClient.AdobeExperience.trackScreen({
      screen: ROUTE_PATH.PROFILE,
      unixTimestamp: dayjs().unix(),
    });
  }, []);

  if (isConnected === null) return null;

  if (!isConnected)
    return (
      <SafeAreaView style={styles.container}>
        <NoConnection />
      </SafeAreaView>
    );

  return (
    <SafeAreaView style={styles.container}>
      <Loading visible={isLoadingOpenApp} dismissAction={onDismiss} />
      <ScrollView
        style={styles.container}
        showsVerticalScrollIndicator={false}
        showsHorizontalScrollIndicator={false}
        refreshControl={<RefreshControl refreshing={isRefreshing} tintColor={theme.colors.primary} onRefresh={onRefreshData} />}
      >
        <MyProfile />
        <View style={{ marginVertical: 6 }} />
        <AccountSetting setLoading={setLoadingOpenMiniApp} />
        <View style={{ marginVertical: 6 }} />
        <HelpCenter />
        <View style={{ marginVertical: 6 }} />
        {showVitalityFeatures && (
          <HealthReport
            leftTitle={t('profile.healthReport.title')}
            rightButtonTitle={t('profile.healthReport.seeAll')}
            emptyTitle={t('profile.healthReport.noReports')}
            loading={healthReportLoading}
            data={healthReportOverviewData}
            onClickItem={navigateToHealthReport}
            onClickRightButtonText={navigateToHealthReport}
          />
        )}
        {showVitalityFeatures && (
          <>
            <Tracker
              data={linkedTrackers}
              onPressItem={navigateToWorkoutTracker}
              onPressRightIcon={navigateToWorkoutTracker}
              loading={linkedTrackersLoading}
            />
            <View style={{ marginVertical: 6 }} />
          </>
        )}
        <About onPressContact={onPressContact} />
        <View style={{ marginVertical: 6 }} />
        {isLoged && (
          <View style={styles.buttonLogout}>
            <Button
              type="secondary"
              text={t('profile.account.logout')}
              onPress={showModalLogout}
              style={styles.button}
              id="Profile.btnShowModalLogout"
            />
          </View>
        )}
        <ModalLogOut ref={modalLogOutRef} />
        <ModalPermissionVitality ref={modalPermissionVitalityRef} onViewIntro={openVitalityWeb} />
        <WebView ref={webviewRef} />

        <ModalConfirmError
          ref={modalMaintainRef}
          title={t('common.vitality.maintenance.title')}
          subtitle={t('common.vitality.maintenance.description')}
          btnText={t('common.vitality.maintenance.close')}
          onConfirm={() => setMaintenance(false)}
        />
        <ModalVersion ref={modalVersionRef} />
        <TouchableOpacity
          onPress={() => {
            modalVersionRef.current?.open();
          }}
        >
          <Text style={styles.textVersion}>
            {t('profile.account.version')} {appVersion}
          </Text>
        </TouchableOpacity>
      </ScrollView>
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  healthReportLoading: TVitalityState['loadingHealthReport'];
  linkedTrackersLoading: TVitalityState['linkedTrackersLoading'];
  healthReportFeedback: TVitalityState['healthReportFeedback'];
  linkedTrackers: TVitalityState['linkedTrackers'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  healthReportLoading: makeSelectHealthReportLoading(),
  linkedTrackersLoading: makeSelectLinkedTrackersLoading(),
  healthReportFeedback: makeSelectHealthReportFeedback(),
  linkedTrackers: makeSelectLinkedTrackers(),
});

type MapDispatchToPropsType = {
  fetchHealthReportFeedback: (isRefresh?: boolean) => void;
  initLinkedTrackers: (isRefresh?: boolean) => void;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  fetchHealthReportFeedback: isRefresh => dispatch(fetchHealthReportFeedbackAction(isRefresh)),
  initLinkedTrackers: isRefresh => dispatch(initLinkedTrackersAction(isRefresh)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Profile);
