/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { forwardRef, useImperativeHandle, useRef, useState } from 'react';
import { TouchableOpacity, View, Platform } from 'react-native';
import { Icon, useTheme, Text, Loading, NativeModuleClient } from 'superapp-common-component';
import Share from 'react-native-share';
import { CameraRoll } from '@react-native-camera-roll/camera-roll';
import { captureRef } from 'react-native-view-shot';
import { useTranslation } from 'react-i18next';
import dayjs from 'dayjs';

import { Theme } from '@/config/themes.config';
import ModalOverlay from '@/components/ModalOverlay/index2';
import ROUTE_PATH from '@/navigators/routePath';
import { a11y } from '@/utils/utils';

import useStyles from './styles';

const isAndroid = Platform.OS === 'android';
type ECardModalProps = {
  type?: 'back' | 'front';
  onPress?: () => void;
  frontCard?: React.ReactNode;
  backCard?: React.ReactNode;
  isTeladoc?: boolean;
};
export interface ECardModalRef {
  setVisibleModal: (isOpen: boolean) => void;
}
const ECardModal = (props: ECardModalProps, ref: React.Ref<unknown> | undefined) => {
  const { frontCard, backCard, isTeladoc } = props;
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [isVisible, setIsVisible] = useState(false);
  const [loading, setLoading] = useState(false);
  const [isFront, setIsFront] = useState(true);
  const cardRef = useRef(null);

  useImperativeHandle(
    ref,
    (): ECardModalRef => ({
      setVisibleModal: (isOpen: boolean) => setIsVisible(isOpen),
    }),
  );

  const handleShare = async () => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.ECARD_DETAIL,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Share',
      });
      // capture component
      const uri = await captureRef(cardRef, {
        format: 'png',
        quality: 1,
      });
      const shareResponse = await Share.open({ url: uri });
    } catch (error) {
      console.log('error', error);
    }
  };

  const handleDownload = async () => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.ECARD_DETAIL,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Download',
      });

      setLoading(true);
      await new Promise(res => setTimeout(res, 500));
      // capture component
      const uri = await captureRef(cardRef, {
        format: 'png',
        quality: 1,
      });
      CameraRoll.saveToCameraRoll(uri, 'photo')
        .then(res => console.log(res))
        .catch(err => console.log(err));
    } catch (error) {
      console.log('error', error);
    } finally {
      setLoading(false);
    }
  };

  const renderTextStyle = (flag: boolean) => {
    const result = {
      ...styles.optionText,
    };
    if (flag) return { ...result, ...styles.optionTextActive };
    return result;
  };

  const renderEcard = () => {
    return (
      <View ref={cardRef} style={[styles.cardContainer, isTeladoc && styles.marginTeladoc]} collapsable={isAndroid ? false : undefined}>
        <View>{isFront ? frontCard : backCard}</View>
      </View>
    );
  };

  const renderModalContent = () => {
    return (
      <View style={styles.modalContainer}>
        <View style={styles.modalHeader}>
          <TouchableOpacity
            onPress={() => {
              setIsVisible(false);
              setIsFront(true);
            }}
            {...a11y('ECardDetail.eCardModal.btnCancel')}
          >
            <Text>{t('ecard.detail.modal.button.cancel')}</Text>
          </TouchableOpacity>
          <View style={styles.buttonContainer}>
            <TouchableOpacity style={styles.paddingRight16} onPress={handleDownload} {...a11y('ECardDetail.eCardModal.btnDownload')}>
              <Icon name="Download" />
            </TouchableOpacity>
            <TouchableOpacity onPress={handleShare} {...a11y('ECardDetail.eCardModal.btnDownload')}>
              <Icon name="Share1" />
            </TouchableOpacity>
          </View>
        </View>
        {!isTeladoc && (
          <View>
            <View style={[styles.optionGroup]}>
              <TouchableOpacity
                onPress={() => {
                  NativeModuleClient.AdobeExperience.trackButton({
                    screen: ROUTE_PATH.ECARD_DETAIL,
                    unixTimestamp: dayjs().unix(),
                    buttonText: 'Front',
                  });
                  setIsFront(true);
                }}
                style={[styles.optionButton, styles.borderRadiusLeft, isFront && [styles.optionActive, styles.optionActiveLeft]]}
                {...a11y('ECardDetail.eCardModal.btnFront')}
              >
                <Text style={renderTextStyle(isFront)}>{t('ecard.detail.modal.button.front')}</Text>
              </TouchableOpacity>
              <TouchableOpacity
                onPress={() => {
                  NativeModuleClient.AdobeExperience.trackButton({
                    screen: ROUTE_PATH.ECARD_DETAIL,
                    unixTimestamp: dayjs().unix(),
                    buttonText: 'Back',
                  });
                  setIsFront(false);
                }}
                style={[styles.optionButton, styles.borderRadiusRight, !isFront && [styles.optionActive, styles.optionActiveRight]]}
                {...a11y('ECardDetail.eCardModal.btnBack')}
              >
                <Text style={renderTextStyle(!isFront)}>{t('ecard.detail.modal.button.back')}</Text>
              </TouchableOpacity>
            </View>
          </View>
        )}
        {loading ? <Loading visible={loading} /> : null}
        {renderEcard()}
      </View>
    );
  };

  return <ModalOverlay isVisible={isVisible}>{renderModalContent()}</ModalOverlay>;
};

export default forwardRef(ECardModal);
