import { takeLatest, all, call, put } from 'redux-saga/effects';
import { TAction } from '@/stores/types';
import { setStorageToken } from '@/utils/utils';
import { ERROR_CODE_OTP } from '@/utils/constants'; 
import RegisterService from '@/services/register';
import CapchaService from '@/services/capcha';
import { Platform } from 'react-native';
import APP_ENV from '@/config/app.config';

import { NativeModuleClient } from 'superapp-common-component';
import dayjs from 'dayjs';

import { loginSuccess, setRefreshTokenError } from '@/stores/authentication/actions';
import { getProfile } from '@/stores/global/actions';
import APIError from '@/utils/errors';

import {
  GET_INFO_REGISTER,
  SUBMIT_REGISTER,
  REQUEST_OTP_BY_SMS,
  GET_CAPCHA,
  LOGIN_AFTER_REGISTER_SUCCESS,
  CHECK_EMAIL_PHONE_EXIST,
} from './constants';
import { loadingRequestAction, loadingSuccessAction, saveInfoRegisterAction, saveCapchaAction } from './actions';
// import { makeSelectInfoCapcha } from './selectors';

// type TElement = { name: string; value: string };

const { CLIENT_ID } = APP_ENV;

function* handleInfoRegister({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { phoneNumber, password, callback } = payload;
    yield put(saveInfoRegisterAction({ phoneNumber, password }));
    callback();
  } catch (error) {
    console.log('error', error);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleSubmitRegister({ payload }: any) {
  try {
    yield put(loadingRequestAction());
    const { phoneNumber, password, otpValue, otpId, callback } = payload;
    const response: MODEL.IRegister = yield call(RegisterService.submitRegister, { username: phoneNumber, password, otpValue, otpId });
    response && callback();
  } catch (error: any) {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'RegisterOTP',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Submit register',
      error: error?.errorCode || '',
    });
    const { setError } = payload;
    if (error?.message === 'Network Error') {
      setError('globalError', {
        type: 'manual',
        message: 'Network Error',
      });
    } else if (error?.errorCode === ERROR_CODE_OTP.OTP_DISABLED) {
      setError('otpDisabled', {
        type: 'manual',
        message: (error as APIError).message,
      });
    } else {
      setError('globalError', {
        type: 'manual',
        message: (error as APIError).message,
      });
    }
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleRequestOtpBySms({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    // const captchaInfo: MODEL.ICapcha = yield select(makeSelectInfoCapcha());
    const { params, setSeconds, callback } = payload;
    const response: { otpId: string } = yield call(RegisterService.requestOtpRegisterService, { ...params });
    setSeconds(120);
    callback && callback(response?.otpId, '')
  } catch (error: any) {
    const { callback } = payload;
    let errorCode = '';
    if (error?.message === 'Network Error') {
      errorCode = 'Network Error';
    } else {
      errorCode = (error as APIError).message;
    }
    callback && callback('', errorCode);
    console.log('error', error);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleGetCapcha() {
  try {
    const response: MODEL.ICapcha = yield call(CapchaService.getCapchaService);
    yield put(saveCapchaAction(response));
  } catch (error) {
    console.log('error', error);
  }
}

function* handleLoginAfterRegisterSuccess({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { username, password, callbackSuccess } = payload;
    const params = {
      clientId: CLIENT_ID,
      username,
      password,
      deviceId: {
        os: Platform.OS,
        notificationToken: '',
      },
    };
    const response: MODEL.IAuthenticate = yield call(RegisterService.loginService, { ...params });
    yield all([put(loginSuccess()), put(getProfile()), put(setRefreshTokenError(undefined))]);
    setStorageToken(response);
    callbackSuccess();
  } catch (error) {
    console.log('error', error);
    const { callbackLoginFail } = payload;
    callbackLoginFail();
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleCheckEmailPhoneExist({ payload, resolve }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { phoneNumber } = payload;
    const response: {result: boolean} = yield call(RegisterService.checkUsername, { username: phoneNumber });
    resolve({ isPhoneExist: response.result });
  } catch (error: any) {
    console.log('err', error);
    const { setError } = payload;
    if (error?.message === 'Network Error') {
      setError('globalErrorInputInfo', {
        type: 'manual',
        message: 'Network Error',
      });
    } else {
      setError('globalErrorInputInfo', {
        type: 'manual',
        message: (error as APIError).message,
      });
    }
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* globalSaga() {
  yield takeLatest(GET_INFO_REGISTER, handleInfoRegister);
  yield takeLatest(SUBMIT_REGISTER, handleSubmitRegister);
  yield takeLatest(REQUEST_OTP_BY_SMS, handleRequestOtpBySms);
  yield takeLatest(GET_CAPCHA, handleGetCapcha);
  yield takeLatest(LOGIN_AFTER_REGISTER_SUCCESS, handleLoginAfterRegisterSuccess);
  yield takeLatest(CHECK_EMAIL_PHONE_EXIST, handleCheckEmailPhoneExist);
}

export default globalSaga;
