import React, { useCallback, useEffect, useRef, useState } from 'react';
import { BackHandler, FlatList, Image, RefreshControl, SafeAreaView, TouchableOpacity, View } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationProp } from '@react-navigation/stack';
import { Icon, InnerLoading, Loading, useTheme, NativeModuleClient } from 'superapp-common-component';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import { useNetInfo } from '@react-native-community/netinfo';
import dayjs from 'dayjs';
import _lowerCase from 'lodash/lowerCase';

import ROUTE_PATH from '@/navigators/routePath';
import Text from '@/components/Text';
import { TStackParamList } from '@/navigators';
import { Theme } from '@/config/themes.config';
import { getItem } from '@/utils/storage';
import { APPS_NAME, STORAGE_NAME, TYPE_NAVIGATION_CLAIM } from '@/utils/constants';
import NoConnection from '@/components/NoConnection';
import { isEmpty } from '@/utils/utils';

import { makeSelectClaimTransactionLoading, makeSelectClaimTransactions, makeSelectIsLoadMoreClaim, makeSelectTransactionFilter, makeSelectTriggerFilterFlag } from '../../stores/selectors';
import {
  ClaimTransactionPayload,
  getClaimTransaction,
  loadMoreClaimTransaction,
  saveClaimTransaction,
  setLoadMoreClaim,
} from '../../stores/actions';
import useStyles from './styles';

type ClaimHistoryNavigationProp = StackNavigationProp<TStackParamList, 'TransactionHistory'>;

const STATUS_TRANSACTION_TYPE = {
  SUCCESS: 'Completed',
  PROCESSING: 'Processing',
  DRAFT: 'Draft',
};

const initialParams = {
  paging: { pageNo: 1, pageSize: 10 },
  status: 'All',
  haveDraft: false,
  sortType: 'DESC',
};

const ClaimHistory = () => {
  const isRunningMiniApp = useRef(false);
  const [isLoadingOpenApp, setLoadingOpenMiniApp] = useState<boolean>(false);
  const navigation = useNavigation<ClaimHistoryNavigationProp>();
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const claimTransactionLoading = useSelector(makeSelectClaimTransactionLoading());
  const claimHistoryTransactions = useSelector(makeSelectClaimTransactions());
  const transactionFilter = useSelector(makeSelectTransactionFilter());
  const isLoadMore = useSelector(makeSelectIsLoadMoreClaim());
  const triggerFilterFlag = useSelector(makeSelectTriggerFilterFlag());
  const [filterParams, setFilterParams] = useState<ClaimTransactionPayload>(initialParams);
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const netInfo = useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [language, setLanguage] = useState<string>('vi');

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  const checkTypeTransaction = (status: string) => {
    switch (status) {
      case STATUS_TRANSACTION_TYPE.SUCCESS:
        return styles.successStatus;
      case STATUS_TRANSACTION_TYPE.PROCESSING:
        return styles.processingStatus;
      case STATUS_TRANSACTION_TYPE.DRAFT:
        return styles.draftStatus;
    }
  };

  const handleLoadMore = () => {
    if (claimTransactionLoading) return;
    if (isLoadMore) {
      const paging = { pageSize: 10, pageNo: filterParams?.paging?.pageNo + 1 };
      setFilterParams({ ...filterParams, paging });
      dispatch(loadMoreClaimTransaction({ ...filterParams, paging }));
    }
  };

  const onPullToRequest = async () => {
    try {
      setRefreshing(true);
      setFilterParams({ ...initialParams });
      dispatch(setLoadMoreClaim(true));
      await dispatch(getClaimTransaction({ ...initialParams }));
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const handleOpenClaimDetail = async (item: MODEL.IClaimTransaction) => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: `${ROUTE_PATH.TRANSACTION_HISTORY}:Claim`,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Open claim mini app',
      });

      setLoadingOpenMiniApp(true);
      await NativeModuleClient.MiniApp.runCheckUpdate(APPS_NAME.CLAIM);
      await NativeModuleClient.MiniApp.loadApp(
        APPS_NAME.CLAIM,
        JSON.stringify({ data: item, type: TYPE_NAVIGATION_CLAIM.TYPE_CLAIM_DETAIL }),
      );
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', APPS_NAME.POS_REQUEST);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const getName = (item: MODEL.IClaimTransaction) => {
    return language === 'vi' ? item?.claimTypeMixedVnName : item?.claimTypeMixedEngName;
  };

  const Item = ({ item }: { item: MODEL.IClaimTransaction }) => {
    return (
      <TouchableOpacity onPress={() => handleOpenClaimDetail(item)}>
        <View style={styles.transactionWrapper}>
          <Icon name="Protect1" size={24} color={theme.colors.primary} />
          <View style={styles.transactionContent}>
            <Text style={styles.transactionName} numberOfLines={2}>
              {getName(item)}
            </Text>
            <View style={styles.wrapperTime}>
              <Text style={styles.time}>{item?.submittedDate && dayjs(item?.submittedDate, 'DD/MM/YYYY HH:mm:ss').format('DD/MM/YYYY h:mm A')}</Text>
              {!isEmpty(item?.submittedDate) && <View style={styles.divider} />}
              <Text style={styles.noText} numberOfLines={1} ellipsizeMode="tail">
                {t('home.transaction.history.claim.request')}
                {item?.requestId === 'Updating' ? t('common.claim.title.updating') : item?.requestId}
              </Text>
            </View>
            <View style={styles.wrapperStatus}>
              <Text style={styles.status}>
                {t('home.transaction.history.status')}
                <Text style={checkTypeTransaction(item?.requestStatus)}>
                  {[STATUS_TRANSACTION_TYPE.DRAFT, STATUS_TRANSACTION_TYPE.PROCESSING, STATUS_TRANSACTION_TYPE.SUCCESS].includes(item?.requestStatus)
                    ? t(`home.transaction.history.claim.${_lowerCase(item?.requestStatus)}`)
                    : ''}
                </Text>
              </Text>
              <View style={styles.iconDown}>
                <Icon name="Down" size={24} color={theme.colors.primary} />
              </View>
            </View>
          </View>
        </View>
      </TouchableOpacity>
    );
  };

  const renderItem = ({ item }: { item: MODEL.IClaimTransaction }) => <Item item={item} />;

  const renderFooterLoading = () => {
    return (
      <View style={styles.wapperBottomLoading}>
        <InnerLoading gifStyle={styles.loadMoreGif} />
      </View>
    );
  };

  const handleBackMiniApp = useCallback(() => {
    if (isRunningMiniApp.current) {
      isRunningMiniApp.current = false;
      NativeModuleClient.MiniApp.goBack();
      return true;
    }
    return false;
  }, []);

  useEffect(() => {
    (async () => {
      const lang = await getItem<string>(STORAGE_NAME.LANGUAGE, '');
      setLanguage(lang);
    })();

    dispatch(setLoadMoreClaim(true));
    dispatch(getClaimTransaction({ ...filterParams }));
    if (!__DEV__) BackHandler.addEventListener('hardwareBackPress', handleBackMiniApp);

    return () => {
      dispatch(saveClaimTransaction([]));
      dispatch(setLoadMoreClaim(true));
      if (!__DEV__) BackHandler.removeEventListener('hardwareBackPress', handleBackMiniApp);
    };
  }, []);

  useEffect(() => {
    if (triggerFilterFlag) {
      dispatch(setLoadMoreClaim(true));
      setFilterParams({ ...initialParams });
      dispatch(getClaimTransaction({ ...initialParams }));
    }
  }, [transactionFilter])

  if ((!(claimHistoryTransactions?.length > 0) && claimTransactionLoading) || (triggerFilterFlag && claimTransactionLoading)) {
    return (
      <View style={styles.wrapperLoading}>
        <InnerLoading gifStyle={styles.loadGif} />
      </View>
    );
  }

  return (
    <SafeAreaView style={styles.flex1}>
      {isConnected ? (
        <>
          {claimHistoryTransactions?.length > 0 ? (
            <FlatList
              style={styles.container}
              data={claimHistoryTransactions}
              renderItem={renderItem}
              keyExtractor={(item, index: number) => item?.requestId + `${index}`}
              ListFooterComponent={claimTransactionLoading ? renderFooterLoading() : <View style={styles.mb20} />}
              onEndReachedThreshold={0.5}
              onEndReached={handleLoadMore}
              showsVerticalScrollIndicator={false}
              refreshControl={<RefreshControl tintColor={theme.colors.primary} refreshing={isRefreshing} onRefresh={onPullToRequest} />}
            />
          ) : (
            <View style={styles.wrapperImageNodata}>
              <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
              <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
            </View>
          )}
          <Loading visible={isLoadingOpenApp} />
        </>
      ) : (
        <NoConnection />
      )}
    </SafeAreaView>
  );
};

export default ClaimHistory;
