import APP_ENV from '@/config/app.config';
import { STORAGE_NAME } from '@/utils/constants';
import { getItem } from '@/utils/storage';
import VitalityRequest from '@/utils/vitalityRequest';

const { X_VITALITY_LEGAL_ENTITY_ID, APPLICATION_API_VITALITY } = APP_ENV;

class VitalityService extends VitalityRequest {
  public constructor() {
    super(APPLICATION_API_VITALITY, {
      Accept: 'application/json',
      'X-Vitality-Legal-Entity-Id': X_VITALITY_LEGAL_ENTITY_ID,
    });
  }

  public fetchHealthReportFeedbackService = async (): Promise<API.IFetchHealthReportFeedback> => {
    const membershipId = await getItem(STORAGE_NAME.VITALITY_MEMBERSHIP_ID, '');
    return this.instance.get(`/vitality/core/v2/assessments/feedback`, {
      params: {
        'assessment-type': 'All',
        'membership-no': membershipId,
      },
    });
  };

  public fetchPreLinkDevices = async (): Promise<API.IFetchPreLinkDevicesResponse> => {
    const membershipId = await getItem(STORAGE_NAME.VITALITY_MEMBERSHIP_ID, '');
    return this.instance.get(`/vitality/core/v2/devices?membership-no=${membershipId}`);
  };

  public refreshVitalityToken = (): Promise<void> => this.refreshToken();
}

export default new VitalityService();
