import React, { useState, useLayoutEffect, useRef } from 'react';
import { View, ScrollView, SafeAreaView, TouchableOpacity, StatusBar } from 'react-native';
import { TextInput, Button, Checkbox, Icon, NativeModuleClient, theme } from 'superapp-common-component';
import { useForm, Controller } from 'react-hook-form';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import dayjs from 'dayjs';

import { TConnectState } from '@/stores/types';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import { REGEX } from '@/utils/constants';
import { a11y, checkRuleNoSequencedPassword, isEmpty } from '@/utils/utils';
import { HEADER_TITLE, HITSLOP } from '@/utils/styles';
import ROUTE_PATH from '@/navigators/routePath';

import Text from '@/components/Text';
import Loading from '@/components/Loading';

import { NS_REGISTER_TYPE } from './stores/constants';
import requestTypeReducer from './stores/reducer';
import requestTypeSaga from './stores/saga';
import { TRequestTypeState } from './stores/state';
import { makeSelectLoading, makeSelectLoadingRequestOtp, makeSelectInfoRegister, makeSelectInfoCapcha } from './stores/selectors';
import {
  getInfoRegisterAction,
  submitRegisterAction,
  requestOtpBySmsAction,
  getCapchaAction,
  loginAfterRegisterSuccessAction,
  asyncCheckEmailPhoneExistAction,
} from './stores/actions';

import { TStackParamList } from '../../navigators';
import OTPScreen, { OTPScreenRef } from './components/OTP';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import LoginModal, { LoginModalRef } from '@/pages/Login';
import WebView from '@/components/WebView';

import styles from './styles';
import { colors } from 'superapp-common-component';

type FormValues = {
  phoneNumber: string;
  password: string;
  confirmPass: string;
  captchaValue: string;
};

type RegisterNavigationProp = StackNavigationProp<TStackParamList, 'Register'>;
type RegisterProps = {
  loading: boolean;
  loadingRequestOtp: boolean;
  getInfoRegister: any;
  infoRegister: any;
  checkUsernameExist: any;
  submitRegister: any;
  requestOtpBySms: any;
  getCapcha: any;
  infoCapcha: any;
  loginAfterRegisterSuccess: any;
  asyncCheckEmailPhoneExist: (payload: any) => Promise<any>;
};

const Register: React.FC<RegisterProps> = props => {
  useInjectReducer({ key: NS_REGISTER_TYPE, reducer: requestTypeReducer });
  useInjectSaga({ key: NS_REGISTER_TYPE, saga: requestTypeSaga });

  const { t } = useTranslation();
  const navigation = useNavigation<RegisterNavigationProp>();
  const otpScreenRef = useRef<null | OTPScreenRef>(null);
  const modalErrorInputInfoRef = useRef<null | ModalConfirmErrorRef>(null);
  const loginModalRef = useRef<null | LoginModalRef>(null);
  const WebviewRef = useRef<any>(null);

  const {
    control,
    handleSubmit,
    formState: { errors },
    watch,
    getValues,
    setError,
    clearErrors,
  } = useForm<FormValues | any>();
  const [isAgree, setIsAgree] = useState(false);
  const [isSecurePW, setIsSecurePW] = useState(true);
  const [isSecureConfirmPW, setIsSecureConfirmPW] = useState(true);

  const onCheckRulePasswordDuplicateSequenced = (value: string) => {
    if (value) {
      if (!REGEX.NO_DUPLICATE_CHARACTER.test(value) && checkRuleNoSequencedPassword(value)) return true;
    }
    return false;
  };

  const onSubmit = async (data: FormValues) => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REGISTER,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Confirm information register',
    });
    if (isAgree) {
      const { phoneNumber } = data;
      const resValidationEmailPhone: { isPhoneExist: boolean } = await props.asyncCheckEmailPhoneExist({
        phoneNumber,
        setError,
      });
      const callback = () => {
        if (!resValidationEmailPhone.isPhoneExist) {
          NativeModuleClient.AdobeExperience.trackScreen({
            screen: 'RegisterExistAccount',
            unixTimestamp: dayjs().unix(),
          });
          otpScreenRef?.current?.setExistUserName(true);
        } else {
          NativeModuleClient.AdobeExperience.trackScreen({
            screen: 'RegisterOTP',
            unixTimestamp: dayjs().unix(),
          });
          otpScreenRef?.current?.setCaptchaBefore(true);
        }
        otpScreenRef.current?.open();
      };
      await props.getInfoRegister({
        ...data,
        callback,
      });
    }
  };

  const onDismissLoadingInputInfo = () => {
    if (!isEmpty(errors?.globalErrorInputInfo)) {
      modalErrorInputInfoRef.current?.open();
    }
  };

  const onCloseErrorInputInfo = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REGISTER,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Close error information register',
    });
    clearErrors();
  };

  const onPressLogin = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REGISTER,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Login',
    });
    loginModalRef.current?.setShowModal(true);
  };

  const showWebviewTerms = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REGISTER,
      unixTimestamp: dayjs().unix(),
      buttonText: 'View Terms',
    });
    WebviewRef?.current?.openURL(t('common.termCondition.url'));
  };

  const showWebviewConditions = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REGISTER,
      unixTimestamp: dayjs().unix(),
      buttonText: 'View Conditions',
    });
    WebviewRef?.current?.openURL(t('common.collectionStatement.url'));
  };

  const showWebviewTC = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REGISTER,
      unixTimestamp: dayjs().unix(),
      buttonText: 'T&C',
    });
    WebviewRef?.current?.openURL(t('common.t&c.url'));
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <Text style={[HEADER_TITLE, styles.colorText]}>{t('register.title')}</Text>,
      headerLeft: () => (
        <TouchableOpacity
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
          {...a11y('Register.iconGobackHeaderLeft')}
        >
          <Icon name="Arrow-Left" color="#5B636B" style={styles.marginLeft16} />
        </TouchableOpacity>
      ),
      headerRight: () => (
        <TouchableOpacity
          onPress={() => navigation.navigate('InstructionRegister')}
          hitSlop={HITSLOP}
          {...a11y('Register.btnNavigateToInstructionRegister')}
        >
          <Icon name="Info1" color="#5B636B" style={styles.marginRight16} size={28} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
      headerStyle: { borderBottomColor: colors.monoTone[20], borderBottomWidth: 1 },
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView>
        <StatusBar barStyle="dark-content" backgroundColor={theme.colors.monoTone[0]} />
        <View style={styles.parentForm}>
          <Text style={styles.titleRegister} fontFamily="bold">
            {t('register.account.form.title')}
          </Text>
          <View style={styles.contentForm}>
            {/* Phone number */}
            <Controller
              control={control}
              rules={{
                required: { value: true, message: t('register.account.form.required') },
                pattern: {
                  value: REGEX.PHONE_NUMBER,
                  message: t('register.account.form.validate.phone'),
                },
              }}
              render={({ field: { onChange, value } }) => (
                <TextInput
                  label={t('register.account.form.phone')}
                  keyboardType="numeric"
                  value={value}
                  disabled={false}
                  onChangeText={onChange}
                  type={errors.phoneNumber && 'error'}
                  containerStyle={styles.marginBottomInput}
                  message={errors.phoneNumber?.message || undefined}
                  style={styles.heightInput}
                  messageStyle={styles.messErrorFieldStyle}
                  maxLength={10}
                  id="Register.inputPhoneNumber"
                />
              )}
              name="phoneNumber"
            />
            {/* Password */}
            <Controller
              control={control}
              rules={{
                required: { value: true, message: t('register.account.form.required') },
                pattern: {
                  value: REGEX.PASSWORD,
                  message: t('register.account.form.validate.formatPass'),
                },
                validate: value => checkRuleNoSequencedPassword(value) || t('register.account.form.validate.formatPass'),
              }}
              render={({ field: { onChange, value } }) => (
                <TextInput
                  label={t('register.account.form.password')}
                  value={value}
                  disabled={false}
                  onChangeText={onChange}
                  type={errors.password && 'error'}
                  containerStyle={styles.marginBottomInput}
                  message={errors.password?.message || undefined}
                  secureTextEntry={isSecurePW}
                  right={{ source: isSecurePW ? 'Show' : 'Mask' }}
                  style={styles.heightInput}
                  onPressIcon={() => setIsSecurePW(!isSecurePW)}
                  maxLength={64}
                  messageStyle={styles.messErrorFieldStyle}
                  iconColor="#000000"
                  id="Register.inputPassword"
                  textContentType='oneTimeCode'
                  autoComplete="off"
                />
              )}
              name="password"
            />
            {/* Re-password */}
            <Controller
              control={control}
              rules={{
                required: { value: true, message: t('register.account.form.required') },
                validate: value => value === watch('password') || t('register.account.form.validate.rePass'),
              }}
              render={({ field: { onChange, value } }) => (
                <TextInput
                  label={t('register.account.form.rePassword')}
                  value={value}
                  disabled={false}
                  onChangeText={onChange}
                  type={errors.confirmPass && 'error'}
                  // containerStyle={styles.marginBottomInput}
                  message={errors.confirmPass?.message || undefined}
                  secureTextEntry={isSecureConfirmPW}
                  right={{ source: isSecureConfirmPW ? 'Show' : 'Mask' }}
                  style={styles.heightInput}
                  onPressIcon={() => setIsSecureConfirmPW(!isSecureConfirmPW)}
                  maxLength={64}
                  messageStyle={[styles.messErrorFieldStyle, styles.mb_0]}
                  iconColor="#000000"
                  id="Register.inputConfirmPassword"
                  textContentType='oneTimeCode'
                  autoComplete="off"
                />
              )}
              name="confirmPass"
            />
            <View style={styles.parentRule}>
              <View style={styles.infoRule}>
                <Icon
                  name={watch('password') && REGEX.AT_LEAST_9_CHARACTER.test(getValues('password')) ? 'Success' : 'Info1'}
                  color={watch('password') && REGEX.AT_LEAST_9_CHARACTER.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                  style={styles.iconRule}
                />
                <Text style={styles.text}>{t('register.account.form.validate.rule1')}</Text>
              </View>
              <View style={styles.infoRule}>
                <Icon
                  name={watch('password') && REGEX.AT_LEAST_UPPERCASE.test(getValues('password')) ? 'Success' : 'Info1'}
                  color={watch('password') && REGEX.AT_LEAST_UPPERCASE.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                  style={styles.iconRule}
                />
                <Text style={styles.text}>{t('register.account.form.validate.rule2')}</Text>
              </View>
              <View style={styles.infoRule}>
                <Icon
                  name={watch('password') && REGEX.AT_LEAST_LOWERCASE.test(getValues('password')) ? 'Success' : 'Info1'}
                  color={watch('password') && REGEX.AT_LEAST_LOWERCASE.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                  style={styles.iconRule}
                />
                <Text style={styles.text}>{t('register.account.form.validate.rule3')}</Text>
              </View>
              <View style={styles.infoRule}>
                <Icon
                  name={watch('password') && onCheckRulePasswordDuplicateSequenced(getValues('password')) ? 'Success' : 'Info1'}
                  color={watch('password') && onCheckRulePasswordDuplicateSequenced(getValues('password')) ? '#3DA758' : '#1F78AD'}
                  style={styles.iconRule}
                />
                <Text style={styles.text}>{t('register.account.form.validate.rule4')}</Text>
              </View>
              <View style={styles.infoRule}>
                <Icon
                  name={watch('password') && REGEX.AT_LEAST_CAPTIAL_SPECIAL_NUMBER.test(getValues('password')) ? 'Success' : 'Info1'}
                  color={watch('password') && REGEX.AT_LEAST_CAPTIAL_SPECIAL_NUMBER.test(getValues('password')) ? '#3DA758' : '#1F78AD'}
                  style={styles.iconRule}
                />
                <Text style={styles.text}>{t('register.account.form.validate.rule5')}</Text>
              </View>
            </View>
          </View>
        </View>
        {/* <View style={styles.hr} /> */}
        <View style={styles.parentCheckbox}>
          <Checkbox
            checked={isAgree}
            onPress={() => {
              setIsAgree(!isAgree);
            }}
            size={24}
            customStyleChild={styles.customCheckbox}
            id="Register.checkboxIsAgree"
          >
            <Text style={styles.text}>
              {t('register.account.form.textAgree1')}{' '}
              <Text style={[styles.textLink, styles.textDecorationLine]} onPress={showWebviewTerms}>
                {t('register.account.form.textAgree2')}
              </Text>
              <Text style={[styles.text]}>, </Text>
              <Text style={[styles.textLink, styles.textDecorationLine]} onPress={showWebviewConditions}>
                {t('register.account.form.textAgree4')}
              </Text>
              <Text style={[styles.text]}> {t('register.account.form.textAgree3')} </Text>
              <Text style={[styles.textLink, styles.textDecorationLine]} onPress={showWebviewTC}>
                {t('common.t&c.policy')}
                {t('common.t&c2')}
              </Text>
            </Text>
          </Checkbox>
        </View>
        <View style={styles.parentButton}>
          <Button
            text={t('register.account.continue')}
            onPress={handleSubmit(onSubmit)}
            id="Register.btnSubmitRegisterForm"
            disabled={props.loading || !isAgree}
          />
        </View>
        <View style={styles.containerSignIn}>
          <Text style={[styles.text, styles.textCenter, styles.fontSize16]} fontFamily="bold">
            {t('register.account.form.textAlreadySignUp')}{' '}
          </Text>
          <TouchableOpacity onPress={onPressLogin}>
            <Text Text style={[styles.textLink, styles.fontSize16]} fontFamily="bold">
              {t('login.page.login.title')}
            </Text>
          </TouchableOpacity>
        </View>
      </ScrollView>
      <WebView ref={WebviewRef} />
      <LoginModal ref={loginModalRef} />
      <ModalError
        ref={modalErrorInputInfoRef}
        title={errors?.globalErrorInputInfo?.message === 'Network Error' ? t('common.no.connection') : t('register.modalError.title')}
        subtitle={errors?.globalErrorInputInfo?.message === 'Network Error' ? t('common.connection.description') : errors?.globalErrorInputInfo?.message}
        btnText={t('register.modalError.button')}
        onConfirm={onCloseErrorInputInfo}
      />
      <OTPScreen
        ref={otpScreenRef}
        loading={props.loading}
        infoRegister={props.infoRegister}
        getCapcha={props.getCapcha}
        infoCapcha={props.infoCapcha}
        requestOtpBySms={props.requestOtpBySms}
        loginAfterRegisterSuccess={props.loginAfterRegisterSuccess}
        submitRegister={props.submitRegister}
        errors={errors}
        setError={setError}
        clearErrors={clearErrors}
        loginModalRef={loginModalRef}
        loadingRequestOtp={props.loadingRequestOtp}
      />
      <Loading visible={props.loading} onDismiss={onDismissLoadingInputInfo} />
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TRequestTypeState['loading'];
  loadingRequestOtp: TRequestTypeState['loadingRequestOtp'];
  infoRegister: TRequestTypeState['infoRegister'];
  infoCapcha: TRequestTypeState['infoCapcha'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  loadingRequestOtp: makeSelectLoadingRequestOtp(),
  infoRegister: makeSelectInfoRegister(),
  infoCapcha: makeSelectInfoCapcha(),
});

type MapDispatchToPropsType = {
  getInfoRegister: (payload: any) => any;
  submitRegister: (payload: any) => any;
  requestOtpBySms: (payload: any) => any;
  getCapcha: () => any;
  loginAfterRegisterSuccess: (payload: any) => any;
  asyncCheckEmailPhoneExist: (payload: any) => Promise<any>;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  getInfoRegister: payload => dispatch(getInfoRegisterAction(payload)),
  submitRegister: payload => dispatch(submitRegisterAction(payload)),
  requestOtpBySms: payload => dispatch(requestOtpBySmsAction(payload)),
  getCapcha: () => dispatch(getCapchaAction()),
  loginAfterRegisterSuccess: payload => dispatch(loginAfterRegisterSuccessAction(payload)),
  asyncCheckEmailPhoneExist: payload => asyncCheckEmailPhoneExistAction(dispatch)(payload),
});

export default connect(mapStateToProps, mapDispatchToProps)(Register);
