import { takeLatest, call, put, all } from 'redux-saga/effects';
import { TAction } from '@/stores/types';
import AuthService from '@/services/auth';

import APIError from '@/utils/errors';

import { SUBMIT_P820, OTP_REQUEST_OTP_P820 } from './constants';
import { loadingRequestAction, loadingSuccessAction, optRequesetErrorAction, submitP820ErrorAction } from './actions';


function* handleSubmitP820({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestAction());
    yield call(AuthService.submitProcessP820Service, payload);
    resolve();
  } catch (error) {
    reject(error);
    yield put(submitP820ErrorAction({ errorType: (error as APIError).errorCode }));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleRequestOtp({ payload }: TAction) {
  try {
    const { params, callback } = payload;
    yield all([
      put(loadingRequestAction()),
      put(submitP820ErrorAction({ errorType: '' })),
      put(optRequesetErrorAction({ status: false, errorType: '' })),
    ]);
    const response: MODEL.IRequestMyOtp = yield call(AuthService.requestOtpLogin, params);
    callback && callback(response);
  } catch (error) {
    yield put(optRequesetErrorAction({ status: true, errorType: (error as APIError).message }));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* verifyAccountP820Saga() {
  yield takeLatest(SUBMIT_P820, handleSubmitP820);
  yield takeLatest(OTP_REQUEST_OTP_P820, handleRequestOtp);
}

export default verifyAccountP820Saga;
