import React, { useState, useEffect, useRef, useCallback } from 'react';
import {
  View,
  SafeAreaView,
  FlatList,
  Image,
  TouchableOpacity,
  RefreshControl,
  Animated,
  BackHandler,
  Platform,
  UIManager,
  LayoutAnimation,
} from 'react-native';
import { Icon, theme, InnerLoading, NativeModuleClient, Loading, ClaimIcon } from 'superapp-common-component';
import { useFocusEffect, useNavigation } from '@react-navigation/native';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';
import queryString from 'query-string';
import _toUpper from 'lodash/toUpper';
import _values from 'lodash/values';
import { StackNavigationProp } from '@react-navigation/stack';

import ModalPermissionVitality, { ModalPermissionVitalityRef } from '@/components/ModalPermissionVitality';
import { makeSelectCheckLogin } from '@/stores/authentication/selectors';
import { a11y, isEmpty, getVitalityMembershipIdFromToken, hasMaintenanceVitality, canOpenVitality } from '@/utils/utils';
import AzureNotificationClient, { AUTHORIZATION_STATUS } from '@/clients/AzureNotification';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import SwipeItem from '@/components/Swipe';
import WebView from '@/components/WebView';
import IcareV2, { useIcareV2 } from '@/components/IcareV2';
import ROUTE_PATH from '@/navigators/routePath';

import styles from '../styles';
import {
  getAllNotification as getAllNotificationAction,
  saveNotification,
  readNotificationById,
  loadMoreNotification,
  savePaging,
  deleteNotification,
  getNotificationUnRead,
} from '../store/actions';
import { makeSelectNotification, makeSelectLoading, makeSelectLoadingLoadMore, makeSelectIsLoadMoreGeneral } from '../store/selectors';
import ModalError, { ModalErrorRef } from './ModalError';
import {
  APPS_NAME,
  ERROR_CAN_OPEN_VITALITY,
  EVENTS_USER_TRACKING,
  FUNCTION_TYPES,
  MAPPING_CLAIM_TYPES,
  MAPPING_SUB_CLAIM_TYPES,
  MEMBERSHIP_VITALITY_TYPE,
  NOTIFICATION_MODULE,
  TYPE_NAVIGATION_CLAIM,
  TYPE_NAVIGATION_POS_REQUEST,
  VALID_ROLES,
} from '@/utils/constants';
import { makeSelectMembership, makeSelectProfile } from '@/stores/global/selectors';
import { logAction, userTracking } from '@/stores/global/actions';
import { TStackParamList } from '@/navigators';

const { colors } = theme;
const FORMAT_DATE = 'YYYY-MM-DD:HH:mm:ssZZ';
const NOTIFICATION_TYPE = {
  PROTECT: 'policyServing',
  VITALITY: 'vitality',
  CLAIM: 'claim',
  POS_REQUEST: 'posRequest',
  GENERAL: 'general',
};

if (Platform.OS === 'android' && UIManager.setLayoutAnimationEnabledExperimental) {
  UIManager.setLayoutAnimationEnabledExperimental(true);
}

type TPolicyServingParseQueryString = {
  screen?: string;
  policyNumber?: string;
};

type TPosRequestParseQueryString = {
  screen?: string;
};

type Nullable<T> = T | null;
type GeneralNotificationsNavigationProp = StackNavigationProp<TStackParamList, 'Notifications'>;

const GeneralNoti = () => {
  const navigation = useNavigation<GeneralNotificationsNavigationProp>();
  const netInfo = NetInfo.useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [isRefreshing, setRefreshing] = useState<boolean>(false);
  const [isLoadingOpenApp, setLoadingOpenMiniApp] = useState<boolean>(false);
  // const [callOnScrollEnd, setCallOnScrollEnd] = useState(false);
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const loading = useSelector(makeSelectLoading());
  const profile = useSelector(makeSelectProfile());
  const loadingLoadMore = useSelector(makeSelectLoadingLoadMore());
  const loginStatus = useSelector(makeSelectCheckLogin());
  const infoNotification = useSelector(makeSelectNotification());
  const isLoadMoreGeneral = useSelector(makeSelectIsLoadMoreGeneral());
  const modalErrorRef = useRef<null | ModalErrorRef>();
  const isRunningMiniApp = useRef(false);
  const webViewRef = useRef<any>(null);
  const [icareV2Ref, icareV2Instance] = useIcareV2();
  const membershipVitality = useSelector(makeSelectMembership());
  const [membership, setMembershipId] = useState('');
  const modalPermissionVitalityRef = useRef<ModalPermissionVitalityRef>();

  const showWebView: (url: string) => any = (url: string) => {
    webViewRef?.current?.openURL(url);
  };

  const onPullToRequest = async () => {
    if (loading) return;
    try {
      setRefreshing(true);
      dispatch(getAllNotificationAction());
      dispatch(getNotificationUnRead());
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const loadMoreNotiRequest = () => {
    if (loadingLoadMore) return;
    isLoadMoreGeneral && dispatch(loadMoreNotification());
  };

  const openMiniApp = async (appName: string, additionProps: Nullable<string>) => {
    try {
      setLoadingOpenMiniApp(true);
      await NativeModuleClient.MiniApp.runCheckUpdate(appName);
      await NativeModuleClient.MiniApp.loadApp(appName, additionProps);
      isRunningMiniApp.current = true;
    } catch (error) {
      console.log('ERROR: Can not open mini app', appName);
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const handleBackMiniApp = useCallback(() => {
    if (isRunningMiniApp.current) {
      isRunningMiniApp.current = false;
      NativeModuleClient.MiniApp.goBack();
      return true;
    }
    return false;
  }, []);

  const renderNotifyIcon = (notifyType: string) => {
    switch (notifyType) {
      case NOTIFICATION_TYPE.PROTECT:
        return <Image source={require('@/assets/images/policy.png')} style={styles.imageNotification} />;
      case NOTIFICATION_TYPE.VITALITY:
        return <Image source={require('@/assets/images/vitality.png')} style={styles.imageNotification} />;
      case NOTIFICATION_TYPE.POS_REQUEST:
        return <Icon name="Report-On" size={24} color={colors.primary.red[100]} />;
      case NOTIFICATION_TYPE.CLAIM:
        return <ClaimIcon size={24} />;
      case NOTIFICATION_TYPE.GENERAL:
        return <Icon name="Notification1" size={24} color={colors.primary.charcoal[80]} />;
    }
  };

  const preventMutilClick = useRef<boolean>(false);

  const openVitalityApp = async (stringifyProps: string) => {
    dispatch(logAction('ACCESS_VITALITY'));

    if (preventMutilClick.current) return;

    try {
      preventMutilClick.current = true;
      const hasMaintenance = await hasMaintenanceVitality();

      if (hasMaintenance) {
        throw new Error(ERROR_CAN_OPEN_VITALITY.MAINTAIN);
      }

      // const { canOpen, errorMessage = '' } = await canOpenVitalityApp({ roles: profile?.roles });
      const errorValidate = canOpenVitality(membershipVitality, true);

      if (isEmpty(errorValidate)) {
        if (
          membershipVitality &&
          membership &&
          _values(MEMBERSHIP_VITALITY_TYPE).includes(membershipVitality.type) &&
          membershipVitality.hasEConsent !== true
        ) {
          navigation.navigate(ROUTE_PATH.ECONSENT);
          return;
        }

        if (membershipVitality && membership && membershipVitality.isDataProtect !== true) {
          navigation.navigate(ROUTE_PATH.DECREE_ECONSENT as never);
          return;
        }

        await openMiniApp(APPS_NAME.VITALITY, stringifyProps);
      } else {
        dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.NON_VITALITY_MEMBERSHIP_ICON, timestamp: dayjs().unix() }));
        modalPermissionVitalityRef.current?.open();
      }
    } catch (error) {
      const isMaintenance = (error as Error)?.message === 'MAINTAIN';
      modalErrorRef.current?.open({
        title: isMaintenance ? t('common.vitality.maintenance.title') : t('common.notification.lapsed'),
        subTitle: isMaintenance ? t('common.vitality.maintenance.description') : t('common.notification.vitalityLapsedDescription'),
        status: 'error',
      });
    } finally {
      preventMutilClick.current = false;
    }
  };

  const mappingClaimType = (claimType: string, subClaimType: string) => {
    const claimCode = MAPPING_CLAIM_TYPES[claimType];
    if (subClaimType) {
      return MAPPING_SUB_CLAIM_TYPES[claimCode][subClaimType];
    }
    return claimCode;
  };

  const openClaimApp = async (dataDetail: any) => {
    try {
      setLoadingOpenMiniApp(true);
      // const data: MODEL.IClaimTransactionResponse = await asyncGetClaimDetailData(dispatch)(params);
      if (dataDetail) {
        openMiniApp(
          APPS_NAME.CLAIM,
          JSON.stringify({
            data: {
              details: [
                {
                  ...dataDetail,
                  claimType: mappingClaimType(dataDetail?.claimType, dataDetail?.subClaimType),
                  rawClaimCauseDetail: dataDetail?.statusCode,
                },
              ],
              submittedDate: dataDetail?.receiveDate,
              requestId: dataDetail?.requestId,
            },
            type: TYPE_NAVIGATION_CLAIM.TYPE_CLAIM_DETAIL,
          }),
        );
      }
    } catch (error) {
      console.log('ERROR: Can not get claim detail data');
      modalErrorRef.current?.open({
        title: t('common.connection.title'),
        subTitle: '',
        status: 'error',
      });
    } finally {
      setLoadingOpenMiniApp(false);
    }
  };

  const handlePermission = (functionType: keyof typeof VALID_ROLES) => {
    const isValidRole = profile?.roles?.some(role => VALID_ROLES[functionType]?.includes(role));

    if (!isValidRole) {
      modalErrorRef.current?.open({
        title: t('common.notification.lapsed'),
        subTitle: t(`common.notification.${functionType.toLowerCase()}LapsedDescription`),
        status: 'error',
      });
      throw new Error('permission_invalid');
    }
  };

  const onPressNotifyDetail = (idMardRead: string, curStatus: string, dataDetail: MODEL.IDataByIdNotification & Record<string, any>) => {
    if (curStatus !== 'readed' && idMardRead) {
      const idMarkRead = [{ id: idMardRead }];
      dispatch(readNotificationById(idMarkRead));
    }
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: 'Notification:General',
        unixTimestamp: dayjs().unix(),
        buttonText: 'Press notification',
      });

      switch (dataDetail?.miniApp) {
        case NOTIFICATION_TYPE.PROTECT:
          handlePermission(FUNCTION_TYPES.POLICY);
          const queryPolicyServing = queryString.parse(dataDetail.action) as TPolicyServingParseQueryString;

          if (queryPolicyServing?.policyNumber) {
            return openMiniApp(APPS_NAME.POLICY, JSON.stringify({ policyNumber: queryPolicyServing.policyNumber }));
          }
          return openMiniApp(APPS_NAME.POLICY, null);
        case NOTIFICATION_TYPE.VITALITY:
          return openVitalityApp(JSON.stringify({ notification: dataDetail }));
        case NOTIFICATION_TYPE.POS_REQUEST:
          const queryPosRequest = queryString.parse(dataDetail.action) as TPosRequestParseQueryString;
          if (queryPosRequest.screen === TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P820) {
            return openMiniApp(APPS_NAME.POS_REQUEST, JSON.stringify({ type: TYPE_NAVIGATION_POS_REQUEST.TYPE_ICARE_P820 }));
          }
          return;
        case NOTIFICATION_TYPE.CLAIM:
          const queryClaimRequest = queryString.parse(dataDetail.action) as TPosRequestParseQueryString;
          const hasClaimDetail = queryClaimRequest.screen === TYPE_NAVIGATION_CLAIM.TYPE_CLAIM_DETAIL && dataDetail?.claimDetailId;
          if (hasClaimDetail) {
            return openClaimApp(dataDetail);
          }
          return;
        default:
          if (_toUpper(dataDetail?.module) === _toUpper(NOTIFICATION_MODULE.ICARE)) {
            icareV2Instance().show(dataDetail);
          } else {
            showWebView(dataDetail?.link);
          }
          break;
      }
    } catch (error) {}
  };

  const deleteNoticationById = (id: string) => {
    if (!id) return;

    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Notification:General',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Delete notification',
    });

    dispatch(deleteNotification([{ id }]));
    LayoutAnimation.configureNext(LayoutAnimation.Presets.easeInEaseOut);
  };

  const renderFooterLoading = () => {
    return <InnerLoading gifStyle={styles.loadMoreGif} />;
  };

  const renderItem = ({ item }: { item: MODEL.INotification }) => {
    const isReaded = item.status === 'readed';
    const notificationType = item?.data?.miniApp || 'general';

    const renderRightActions = (progress: any) => {
      const trans = progress.interpolate({
        inputRange: [0, 1],
        outputRange: [80, 0],
      });

      return (
        <Animated.View style={[styles.wrapperContainerDelete, { transform: [{ translateX: trans }] }]}>
          <TouchableOpacity style={styles.wrapperContentDelete} onPress={() => deleteNoticationById(item?.id)}>
            <Icon name="Delete1" size={20} color={colors.monoTone[0]} />
            <Text style={styles.textDelete}>{t('home.notifications.delete')}</Text>
          </TouchableOpacity>
        </Animated.View>
      );
    };

    return (
      <SwipeItem renderRightContent={renderRightActions} key={item?.id}>
        <TouchableOpacity onPress={() => onPressNotifyDetail(item.id, item?.status, item?.data)} {...a11y('Button.Notification.Detail')}>
          <View key={item?.id} style={styles.item}>
            <View style={styles.notificationItem}>
              <View style={styles.notifyTitle}>
                <View style={styles.leftTitle}>
                  {renderNotifyIcon(notificationType)}
                  <Text style={[styles.notifyType, isReaded && styles.opacity50]} fontFamily="bold">
                    {t(`common.notification.app.${notificationType}`)}
                  </Text>
                  {!isReaded && <View style={styles.unreadNotifyIcon} />}
                </View>

                <View style={styles.rightTitle}>
                  <Text style={styles.textRightContent}>
                    {item.requestAt ? dayjs(item?.requestAt, FORMAT_DATE).format('HH:mm - DD/MM/YYYY') : ''}
                  </Text>
                </View>
              </View>

              <View style={styles.notifyContent}>
                <View style={styles.wrapContentTitle}>
                  <Text style={[styles.notifyContentTitle, isReaded && styles.opacity70]} fontFamily="bold">
                    {item.title}
                  </Text>
                </View>
                <View>
                  <Text style={[styles.notifyContentMessage, isReaded && styles.opacity50]} ellipsizeMode="tail" numberOfLines={4}>
                    {item.body}
                  </Text>
                </View>
              </View>
            </View>
          </View>
        </TouchableOpacity>
      </SwipeItem>
    );
  };

  useEffect(() => {
    if (!isEmpty(profile) && loginStatus.isLoged) {
      (async () => {
        const membershipId = await getVitalityMembershipIdFromToken();
        setMembershipId(membershipId);
      })();
    } else {
      setMembershipId('');
    }
  }, [profile, loginStatus]);

  const hasAllowPushNotification = async () => {
    if (__DEV__) return true;

    const [authorizationStatus, isHasGeneralTag] = await Promise.all([
      AzureNotificationClient.hasPermission(),
      AzureNotificationClient.hasGeneralTag(),
    ]);
    const isGranted = authorizationStatus === AUTHORIZATION_STATUS.AUTHORIZED;
    return isGranted && isHasGeneralTag;
  };

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  useFocusEffect(
    useCallback(() => {
      if (loginStatus.isLoged) {
        dispatch(getAllNotificationAction());
        (async () => {
          const isAllowNotification = await hasAllowPushNotification();
          if (!isAllowNotification) {
            dispatch(getNotificationUnRead());
          }
        })();
      }

      if (!__DEV__) {
        AzureNotificationClient.clearTappedNotification();
        BackHandler.addEventListener('hardwareBackPress', handleBackMiniApp);
      }

      return () => {
        dispatch(saveNotification({ data: [], totalPages: 0 }));
        dispatch(savePaging({ pageNumber: 0, pageSize: 10 }));
        if (!__DEV__) BackHandler.removeEventListener('hardwareBackPress', handleBackMiniApp);
      };
    }, [loginStatus.isLoged]),
  );

  const openVitalityWeb = () => {
    dispatch(userTracking({ policyNumber: '', event: EVENTS_USER_TRACKING.NON_AIA_VITALITY, timestamp: dayjs().unix() }));
    webViewRef.current?.openURL(t('common.vitality.url'));
  };

  if (isConnected === null) return null;

  if (!(infoNotification?.data.length > 0) && loading) {
    return (
      <View style={styles.wrapperLoading}>
        <InnerLoading gifStyle={styles.loadGif} />
      </View>
    );
  }

  return isConnected ? (
    <SafeAreaView style={styles.container}>
      {!isEmpty(infoNotification?.data) ? (
        <>
          <View style={styles.wrapper}>
            <FlatList
              data={infoNotification?.data}
              keyExtractor={item => item.id}
              showsVerticalScrollIndicator={false}
              renderItem={renderItem}
              onEndReachedThreshold={0.5}
              onEndReached={loadMoreNotiRequest}
              // onMomentumScrollEnd={() => {
              //   callOnScrollEnd && loadMoreNotiRequest();
              //   setCallOnScrollEnd(false);
              // }}
              ListFooterComponent={loadingLoadMore ? renderFooterLoading() : <View style={styles.mb20} />}
              refreshControl={<RefreshControl tintColor={colors.primary.red[100]} refreshing={isRefreshing} onRefresh={onPullToRequest} />}
            />
          </View>
          <WebView ref={webViewRef} />
          <IcareV2 ref={icareV2Ref} />
          <Loading visible={isLoadingOpenApp} />
          <ModalError ref={modalErrorRef} />
          <ModalPermissionVitality ref={modalPermissionVitalityRef} onViewIntro={openVitalityWeb} />
        </>
      ) : (
        <View style={styles.wrapperImageNodata}>
          <Image style={styles.imageNodata} source={require('@/assets/images/no-data.png')} />
          <Text style={styles.noData}>{t('home.letter.no.data')}</Text>
        </View>
      )}
    </SafeAreaView>
  ) : (
    <SafeAreaView style={styles.flex1}>
      <NoConnection />
    </SafeAreaView>
  );
};

export default GeneralNoti;
