import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, OTP_REQUEST_ERROR, SUBMIT_P820_ERROR } from './constants';
import initialState, { TOTPVerifyAccountP820State } from './state';

const econsentReducer = (state: TOTPVerifyAccountP820State = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TOTPVerifyAccountP820State>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case OTP_REQUEST_ERROR:
        draft.otpError = action.payload;
        break;
      case SUBMIT_P820_ERROR:
        draft.submitError = action.payload;
        break;
    }
  });
};

export default econsentReducer;
