import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  VERIFY_PHONE_NUMBER,
  VERIFY_ID_PERSONAL_NUMBER,
  GET_CAPCHA,
  SAVE_CAPCHA,
  REQUEST_OTP_BY_SMS,
  SUBMIT_OTP,
  SUBMIT_NEW_PASSWORD,
  LOGIN_AFTER_CHANGE_PASS_SUCCESS,
  PUT_OTP_REQUEST,
  PUT_OTP_SUCCESS,
  PUT_OTP_ERROR,
  RESET_OTP_ERROR,
  PUT_CAPTCHA_REQUEST,
  PUT_CAPTCHA_SUCCESS,
  PUT_CAPTCHA_ERROR,
  RESET_CAPTCHA_ERROR,
  SAVE_USER_ID,
  SAVE_SESSION_ID,
  SAVE_PHONE_NUMBER,
  SAVE_USERNAME,
  CONFIRM_NEW_PHONE,
  SAVE_USER_INFO_SUBMIT,
  SAVE_OTP_VALUE,
  SET_IS_COUNT_FROM_SCREEN_INFO,
  LOADING_SUCCESS_FORGOT_PASSWORD,
  LOADING_REQUEST_FORGOT_PASSWORD,
} from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });

export const loadingSuccessForgotPassword = (): TAction => ({ type: LOADING_SUCCESS_FORGOT_PASSWORD });
export const loadingRequestForgotPassword = (): TAction => ({ type: LOADING_REQUEST_FORGOT_PASSWORD });

export const submitVerifyNumber = (
  payload: { phoneNumber: string },
  resolve: (value: boolean) => void,
  reject: (value: unknown) => void,
): TAction => ({
  type: VERIFY_PHONE_NUMBER,
  payload,
  resolve,
  reject,
});

export const submitVerifyPersonalIDNumber = (payload: any): TAction => ({ type: VERIFY_ID_PERSONAL_NUMBER, payload });

export const getCapchaAction = (): TAction => ({ type: GET_CAPCHA });
export const saveCapchaAction = (payload: MODEL.ICapcha): TAction => ({ type: SAVE_CAPCHA, payload });

export const requestOtpBySmsAction = (payload: {
  params: { captchaValue: string; otpSendType: string };
  requestOtpCallback: (number: number) => void;
}): TAction => ({ type: REQUEST_OTP_BY_SMS, payload });

export const submitOtp = (payload: { otpValue: string }, resolve: () => void, reject: (value: unknown) => void): TAction => ({
  type: SUBMIT_OTP,
  payload,
  resolve,
  reject,
});

export const submitNewPassword = (payload: { newPassword: string }, resolve: (value: unknown) => void, reject: (value: unknown) => void): TAction => ({
  type: SUBMIT_NEW_PASSWORD,
  payload,
  resolve,
  reject,
});

export const confirmNewPhone = (payload: { phone: string }, resolve: () => void, reject: (value: unknown) => void): TAction => ({
  type: CONFIRM_NEW_PHONE,
  payload,
  resolve,
  reject,
});

export const loginAfterChangePasswordSuccess = (payload: any): TAction => ({ type: LOGIN_AFTER_CHANGE_PASS_SUCCESS, payload });

export const putOtpRequest = (): TAction => ({ type: PUT_OTP_REQUEST });
export const putOtpSuccess = (): TAction => ({ type: PUT_OTP_SUCCESS });
export const putOtpError = (payload: string): TAction => ({ type: PUT_OTP_ERROR, payload });
export const resetOtpError = (): TAction => ({ type: RESET_OTP_ERROR });

export const putCaptchaRequest = (): TAction => ({ type: PUT_CAPTCHA_REQUEST });
export const putCaptchaSuccess = (): TAction => ({ type: PUT_CAPTCHA_SUCCESS });
export const putCaptchaError = (payload: string): TAction => ({ type: PUT_CAPTCHA_ERROR, payload });
export const resetCaptchaError = (): TAction => ({ type: RESET_CAPTCHA_ERROR });

export const saveUserId = (payload: string): TAction => ({ type: SAVE_USER_ID, payload });
export const saveSessionId = (payload: any): TAction => ({ type: SAVE_SESSION_ID, payload });
export const savePhoneNumber = (payload: string): TAction => ({ type: SAVE_PHONE_NUMBER, payload });
export const saveUsername = (payload: string): TAction => ({ type: SAVE_USERNAME, payload });

export const saveUserInfoSubmitAction = (payload: any): TAction => ({ type: SAVE_USER_INFO_SUBMIT, payload });
export const saveOtpValueAction = (payload: any): TAction => ({ type: SAVE_OTP_VALUE, payload });
export const setIsCountFromScreenInfoAction = (payload: any): TAction => ({ type: SET_IS_COUNT_FROM_SCREEN_INFO, payload });

