const getActionLinksFilter = (actions: { name: string; value: any }[], actionKeys: string[], key: string) => {
  let filterAction = _filter(actions, item => actionKeys.includes(item.name));
  return _reduce(
    filterAction,
    (prev, curr) => {
      if (curr.name === key) {
        return { ...prev, ...curr };
      } else {
        return { ...prev, isHide: curr.value };
      }
    },
    { name: '', value: '', isHide: true },
  );
};

const getHelpCenterFilter = (data?: MODEL.IHelpCenterItem[]): MODEL.IHelpCenter[] => {
  if (isEmpty(data)) return [];

  return _reduce(
    data,
    (prev: MODEL.IHelpCenter[], curr: MODEL.IHelpCenterItem) => {
      const item: MODEL.IHelpCenter = {
        title: curr?.title,
        id: curr?.elements.find(i => i.name === 'id')?.value,
        hide: curr?.elements.find(i => i.name === 'hide')?.value,
        iconName: curr?.elements.find(i => i.name === 'iconName')?.value,
      };
      return [...prev, item];
    },
    [],
  );
};

function* handleGetActionLinks() {
  try {
    const actionAboutResponse: MODEL.IContentTranslateResponse = yield call(AEMService.getTranslateProfileService);

    const helpCenterVi = getHelpCenterFilter(actionAboutResponse?.items?.helpcentre_vi?.items);
    const helpCenterEn = getHelpCenterFilter(actionAboutResponse?.items?.helpcentre_en?.items);

    const actions = actionAboutResponse?.items?.actionLinks?.elements || [];
    const term = getActionLinksFilter(actions, [ACTIONS_LINK.TERM, ACTIONS_LINK.TERM_HIDE], ACTIONS_LINK.TERM);
    const termVitality = getActionLinksFilter(
      actions,
      [ACTIONS_LINK.TERM_VITALITY, ACTIONS_LINK.TERM_VITALITY_HIDE],
      ACTIONS_LINK.TERM_VITALITY,
    );
    const faq = getActionLinksFilter(actions, [ACTIONS_LINK.FAQ, ACTIONS_LINK.FAQ_HIDE], ACTIONS_LINK.FAQ);
    const privacy = getActionLinksFilter(actions, [ACTIONS_LINK.PRIVACY, ACTIONS_LINK.PRIVACY_HIDE], ACTIONS_LINK.PRIVACY);
    const personalData = getActionLinksFilter(
      actions,
      [ACTIONS_LINK.PERSONAL_DATA, ACTIONS_LINK.PERSONAL_DATA_HIDE],
      ACTIONS_LINK.PERSONAL_DATA,
    );

    yield put(saveActionLinks({ faq, term, termVitality, personalData, privacy }));
    yield put(saveHelpCenter({ vi: helpCenterVi, en: helpCenterEn }));
  } catch (error) {
    console.log('ERROR: Get Action Links', error);
  }
}

function* handleGetProfile() {
  try {
    yield put(loadingRequest());

    const response: MODEL.IProfileResponse = yield call(UserService.getProfileService);

    yield put(saveProfile(response?.data));
    yield put(saveNewUsername(response?.data?.mobile));
    yield setItem(STORAGE_NAME.FULL_NAME, JSON.stringify(response?.data?.fullName));
    yield setItem(STORAGE_NAME.FIRST_NAME, JSON.stringify(response?.data?.firstName || ''));
  } catch (error) {
    console.log('ERROR:', error);
  } finally {
    yield put(loadingSuccess());
  }
}

function* handleGetHomeData() {
  try {
    yield put(loadingHomeData());
    yield put(getActionLinks());

    const [introductionResponse, homeResponse]: [MODEL.IIntroductionResponse, MODEL.IContentTranslateResponse] = yield all([
      call(AEMService.getIntroductionService),
      call(AEMService.getTranslateHomeService),
    ]);

    const productListVN: MODEL.IDataAEM = homeResponse.items['product-list_vi'];
    const productListEN: MODEL.IDataAEM = homeResponse.items['product-list_en'];
    const newsListVN: MODEL.IDataAEM = homeResponse.items['news-list_vi'];
    const newsListEN: MODEL.IDataAEM = homeResponse.items['news-list_en'];
    const featureListVN: MODEL.IDataAEM = homeResponse.items['feature-list_vi'];
    const featureListEN: MODEL.IDataAEM = homeResponse.items['feature-list_en'];
    const quickActionsVN: MODEL.IDataAEM = homeResponse.items['quick-actions_vi'];
    const quickActionsEN: MODEL.IDataAEM = homeResponse.items['quick-actions_en'];
    const bannersVN: MODEL.IDataAEM = homeResponse.items['banner-list_vi'];
    const bannersEN: MODEL.IDataAEM = homeResponse.items['banner-list_en'];
    // Introduction
    const viContent = introductionResponse?.items?.vi?.elements?.find(item => item.name === 'text');
    const viContentVisibled = introductionResponse?.items?.vi?.elements?.find(item => item.name === 'hide');
    const enContent = introductionResponse?.items?.vi?.elements?.find(item => item.name === 'text');
    const enContentVisibled = introductionResponse?.items?.vi?.elements?.find(item => item.name === 'hide');

    const introduction: MODEL.IIntroduction = {
      lastModifiedDate: introductionResponse.lastModifiedDate!,
      vi: { value: viContent?.value as string, hide: viContentVisibled?.value as boolean },
      en: { value: enContent?.value as string, hide: enContentVisibled?.value as boolean },
    };

    const productsList: MODEL.IProducts = {
      items: {
        vi: productListVN,
        en: productListEN,
      },
    };

    const newsList: MODEL.IProducts = {
      items: {
        vi: newsListVN,
        en: newsListEN,
      },
    };

    const featureList: MODEL.IProducts = {
      items: {
        vi: featureListVN,
        en: featureListEN,
      },
    };

    const quickActions: MODEL.IProducts = {
      items: {
        vi: quickActionsVN,
        en: quickActionsEN,
      },
    };

    const banners: MODEL.IBanner = {
      items: {
        vi: bannersVN,
        en: bannersEN,
      },
    };

    yield put(saveHomeData({ productsList, newsList, quickActions, featureList, banners, introduction }));
  } catch (error) {
  } finally {
    yield put(loadingHomeDataSuccess());
  }
}

const formatProductImageData = (items: Array<{ name: string; value: any }>) => {
  const productCodes = _find(items, (element: { name: string; value: any }) => element?.name === 'productCodes');
  const image = _find(items, (element: { name: string; value: any }) => element?.name === 'image');

  return {
    productCodes,
    image,
  };
};

function* handleGetPolicies() {
  try {
    yield put(loadingPolicyRequest());
    const [response, responseImage]: [MODEL.IPoliciesResponse, MODEL.IContentProductImageResponse] = yield all([
      call(UserService.getPoliciesService),
      call(AEMService.getProductImage),
    ]);
    const arrayImage: Array<MODEL.IProductImage> = responseImage?.items?.vi?.items.map(item => formatProductImageData(item?.elements));
    const policiesIncommingPayment = response.data.filter(policy => policy.billingIndicator && policy.policyStatus?.code === '20');

    yield put(saveProductImage({ items: arrayImage }));
    yield put(savePolicies(response.data));
    yield put(savePoliciesIncommingPayment(policiesIncommingPayment));
  } catch (error) {
  } finally {
    yield put(loadingPolicyRequestSuccess());
  }
}

