function* encryptPaymentMembershipData({ payload }: TAction) {
  const { membership, amount, paymentDomain, callback } = payload;
  try {
    const idCardNo: string = yield select(makeSelectUserIdCard());
    if (idCardNo && membership) {
      const params = {
        data: [membership, idCardNo, amount, 'myaia'],
      };
      const response: MODEL.IEncryptPaymentResponse = yield call(PaymentService.encryptPaymentDataService, params);
      if (response.data) {
        const url = `${paymentDomain}?data=${response?.data}&hash=${response?.hash}`;
        callback(url);
      }
    }
  } catch (error) {
    console.log('ERROR: Encrypt Payment Membership');
  }
}

function* getClaimDraftInfo({ payload, resolve }: TAction) {
  try {
    const { callback } = payload;
    callback?.(true);
    // const response: MODEL.IDraftInfoResponse = yield call(OneClaimService.getDraftInfoService);
    // const response: MODEL.IDraftInfoResponse = yield call(OneClaimService.checkDraftInfoService);
    const {
      draftInfo,
      checkInfo,
    }: { draftInfo: MODEL.IClaimInfoResponse<MODEL.IDraftInfo>; checkInfo: MODEL.IClaimInfoResponse<MODEL.ICheckCLaimInfoResponse> } =
      yield all({ draftInfo: call(OneClaimService.getDraftInfoService), checkInfo: call(OneClaimService.checkDraftInfoService) });
    if (draftInfo?.result?.id) {
      yield put(saveDraftInfo(draftInfo.result));
    }
    const { result: resCheckInfo } = checkInfo;
    yield put(saveDraftInfo(draftInfo.result));
    // const {callback} = payload;

    resolve(resCheckInfo);
  } catch (error) {
  } finally {
    const { callback } = payload;
    callback?.(false);
  }
}

function* handleResetDraft({ _, resolve }: TAction) {
  try {
    const response: MODEL.IClaimInfoResponse<MODEL.IDraftInfo> = yield call(OneClaimService.resetDraftInfoService, { listResetStep: [] });
    yield put(saveDraftInfo(response.result));
    resolve(response.succeeded);
  } catch (error) {
    console.log('ERROR RESET DRAFT: ', error);
  } finally {
  }
}

function* checkAML({ payload, resolve }: TAction) {
  try {
    const responseAML: MODEL.ICheckAML = yield call(icareService.checkAMLService);
    const { passed, pendingCodes } = responseAML;
    const { type } = payload;
    // pendingCodes = "CM06"
    // pendingCodes = "CM05,CM01"
    // pendingCodes = "CM05,CM04"
    // pendingCodes = "CM07,CM01"
    // pendingCodes = "CM07"
    if (passed || (!passed && pendingCodes.length === 1 && _includes(pendingCodes, AML_PENDING_CODE.CM04))) {
      resolve(AML_TYPE_TEXT.PASSED);
      return;
    }
    if (_includes(pendingCodes, AML_PENDING_CODE.CM06)) {
      if (type === AML_TYPE_REQUEST.POS_REQUEST_LANDING || type === AML_TYPE_REQUEST.VITALITY) {
        resolve(AML_TYPE_TEXT.PASSED);
      } else {
        resolve(AML_TYPE_TEXT.POLICIES_NOT_EFFECT);
      }
      return;
    }
    if (
      _includes(pendingCodes, AML_PENDING_CODE.CM05) &&
      (_includes(pendingCodes, AML_PENDING_CODE.CM01) ||
        _includes(pendingCodes, AML_PENDING_CODE.CM02) ||
        _includes(pendingCodes, AML_PENDING_CODE.CM03))
    ) {
      if (
        [
          AML_TYPE_REQUEST.POS_P820,
          AML_TYPE_REQUEST.POS_REQUEST_LANDING,
          AML_TYPE_REQUEST.EACKS,
          AML_TYPE_REQUEST.ETRANS,
          AML_TYPE_REQUEST.CHANGE_CUSTOMER_INFORMATION,
        ].includes(type)
      ) {
        resolve(AML_TYPE_TEXT.PASSED);
      } else {
        resolve(AML_TYPE_TEXT.HAVE_TO_UPDATE);
      }
      return;
    }
    if (
      _includes(pendingCodes, AML_PENDING_CODE.CM05) &&
      !_includes(pendingCodes, AML_PENDING_CODE.CM01) &&
      !_includes(pendingCodes, AML_PENDING_CODE.CM02) &&
      !_includes(pendingCodes, AML_PENDING_CODE.CM03)
    ) {
      resolve(AML_TYPE_TEXT.PASSED);
      return;
    }
    if (
      _includes(pendingCodes, AML_PENDING_CODE.CM07) &&
      (_includes(pendingCodes, AML_PENDING_CODE.CM01) ||
        _includes(pendingCodes, AML_PENDING_CODE.CM02) ||
        _includes(pendingCodes, AML_PENDING_CODE.CM03))
    ) {
      // setIsUpdate(false);
      resolve(AML_TYPE_TEXT.HAVE_TO_UPDATE_CALL_HOTLINE);
    }
    if (
      _includes(pendingCodes, AML_PENDING_CODE.CM07) &&
      !_includes(pendingCodes, AML_PENDING_CODE.CM01) &&
      !_includes(pendingCodes, AML_PENDING_CODE.CM02) &&
      !_includes(pendingCodes, AML_PENDING_CODE.CM03)
    ) {
      // if ([AML_TYPE_REQUEST.CLAIM, AML_TYPE_REQUEST.POS_REQUEST_LANDING, AML_TYPE_REQUEST.EACKS, AML_TYPE_REQUEST.ETRANS].includes(type)) {
      //   resolve(AML_TYPE_TEXT.PASSED);
      // } else {
      //   resolve(AML_TYPE_TEXT.POLICIES_INACTIVE);
      // }
      resolve(AML_TYPE_TEXT.PASSED);
      return;
    }
  } catch (error) {
    console.log('ERROR RESET DRAFT: ', error);
  } finally {
  }
}

function* globalSaga() {
  yield takeLatest(GET_PROFILE, handleGetProfile);
  yield takeLatest(GET_HOME_DATA, handleGetHomeData);
  yield takeLatest(GET_POLICIES_DATA, handleGetPolicies);
  yield takeLatest(GET_POLICIES_ACK, handleGetPoliciesAck);
  yield takeLatest(PULL_TO_REFRESH_HOME, handlePullToRefreshHome);
  yield takeLatest(PULL_TO_REFRESH_PROFILE, handlePullToRefreshProfile);
  yield takeLatest(ENCRYPT_PAYMENT_DATA, encryptPaymentData);
  yield takeLatest(ENCRYPT_PAYMENT_MEMBERSHIP_DATA, encryptPaymentMembershipData);
  yield takeLatest(GET_CLAIM_DRAFT_INFO, getClaimDraftInfo);
  yield takeLatest(RESET_CLAIM_DRAFT_INFO, handleResetDraft);
  yield takeLatest(GET_ACTION_LINKS_PROFILE, handleGetActionLinks);
  yield takeLatest(CHECK_AML, checkAML);
  yield takeLatest(GET_MEMBERSHIP, handleGetMembership);
  yield takeLatest(LOG_ACTION, handleLogAction);
  yield takeLatest(GET_TIER_VIP, handleGetTierVip);
  yield takeLatest(USER_TRACKING, handleUserTracking);
}

export default globalSaga;
