import React, { useLayoutEffect, useState, useEffect, useRef } from 'react';
import {
  View,
  SafeAreaView,
  Image,
  StatusBar,
  TouchableOpacity,
  Animated,
} from 'react-native';
import { Button, Icon, Loading, NativeModuleClient, Text as TextCommon } from 'superapp-common-component';
import Clipboard from '@react-native-community/clipboard';
import { useTranslation } from 'react-i18next';
import { RouteProp, useNavigation, useRoute } from '@react-navigation/native';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import NetInfo from '@react-native-community/netinfo';

import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { TFotgotPasswordTypeState } from '@/pages/ForgotPassword/stores/state';
import { makeSelectLoading } from '@/pages/ForgotPassword/stores/selectors';
import { TConnectState } from '@/stores/types';
import { confirmNewPhone as confirmNewPhoneAction } from '@/pages/ForgotPassword/stores/actions';
import Text from '@/components/Text';
import NoConnection from '@/components/NoConnection';
import dayjs from 'dayjs';

import styles from './styles';
import { a11y } from '@/utils/utils';
import { HITSLOP } from '@/utils/styles';
import ModalConfirmError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import ModalForgotSession, { ModalForgotSessionRef } from '@/components/ModalForgotSession';
import APIError from '@/utils/errors';
import { FORGOT_PASS_ERROR_CODES } from '@/utils/constants';

interface CreateNewPassNoPhonePropsType {
  loading: boolean;
  confirmNewPhone: (payload: { phone: string }) => Promise<void>;
}
type CreateNewPassNoPhoneNavigationProp = StackNavigationProp<TStackParamList, 'CreateNewPassNoPhone'>;
type CreateNewPassNoPhoneRouteProps = RouteProp<TStackParamList, 'CreateNewPassNoPhone'>;

const CreateNewPassNoPhone = (props: CreateNewPassNoPhonePropsType) => {
  const { loading, confirmNewPhone } = props;
  const navigation = useNavigation<CreateNewPassNoPhoneNavigationProp>();
  const route = useRoute<CreateNewPassNoPhoneRouteProps>();
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalForgotSessionExpiredRef = useRef<ModalForgotSessionRef>(null);
  const { t } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const [isConnected, setConnected] = useState<null | boolean>(null);
  const [errorCode, setErrorCode] = useState<string | null>(null);
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const { phone = '' } = route.params || {};

  const handleConfirm = async () => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.CREATE_NEW_PASS_NO_PHONE,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
      });
      if (loading) return;
      await confirmNewPhone({phone});
      navigation.navigate(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
    } catch (error) {
      setErrorCode((error as APIError).message || 'error');
      console.log('[ERROR] forgot password confirm: ', error);
    }
  };

  const handleCopyPhone = () => {
    Clipboard.setString('hello world');
    Animated.timing(fadeAnim, { toValue: 1, duration: 0, useNativeDriver: false }).start();
    setTimeout(() => {
      Animated.timing(fadeAnim, { toValue: 0, duration: 1000, useNativeDriver: false }).start();
    }, 2000);
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.CREATE_NEW_PASS_NO_PHONE,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Document coppy',
    });
  }

  const onDismissLoading = () => {
    if (!errorCode) return;
    if (errorCode === FORGOT_PASS_ERROR_CODES.SESSION_INVALID) {
      modalForgotSessionExpiredRef.current?.open();
    } else {
      modalErrorRef.current?.open();
    }
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitleAlign: 'center',
    }as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={styles.safeAreaStyle}>
      <Loading visible={loading} dismissAction={onDismissLoading} />
      <View style={styles.pane}>
        <StatusBar barStyle="dark-content" />
        <View style={styles.container}>
          <View style={styles.contentWrapper}>
            <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/account-ready.png')} />
            <Text style={styles.textTitle} fontFamily="bold">
              {t('forgot.password.success.noPhone.main.title')}
            </Text>
            <View style={styles.newUserInput}>
              <View style={styles.newUserInputLeft}>
                <TextCommon style={styles.font12}>{t('forgot.password.success.noPhone.input.label')}</TextCommon>
                <TextCommon.Bold style={styles.phone}>{phone}</TextCommon.Bold>
              </View>
              <View style={styles.newUserInputRight}>
                <TouchableOpacity
                  {...a11y('Forgot.btnCopyPhoneNumber')}
                  onPress={handleCopyPhone}
                  hitSlop={HITSLOP}
                >
                  <Icon name="Copy-Document" size={32} color="#007AFF" />
                </TouchableOpacity>
              </View>
            </View>
            <Text style={styles.textDescription}>{t('forgot.password.success.noPhone.sub.title')}</Text>
          </View>
          <View style={[styles.buttonSuccessWrapper]}>
            <Animated.View
              style={[
                styles.toastContainer,
                {
                  opacity: fadeAnim
                }
              ]}
            >
              <Text style={styles.toastMessage}>{t('forgot.password.success.noPhone.copied.text')}</Text>
            </Animated.View>
            <Button
              text={t('forgot.password.success.noPhone.confirm.button')}
              onPress={handleConfirm}
              uppercase={false}
              labelStyle={styles.buttonText}
              id="forgot.password.success.noPhone.confirm.button"
            />
          </View>
        </View>
      </View>
      <ModalConfirmError
        ref={modalErrorRef}
        title={t('common.connection.title')}
        subtitle={t('common.connection.description')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
        onConfirm={() => setErrorCode(null)}
      />
      <ModalForgotSession ref={modalForgotSessionExpiredRef} handleClose={() => navigation.navigate(ROUTE_PATH.FORGOT_PASSWORD)} />
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TFotgotPasswordTypeState['loading'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
});

type MapDispatchToPropsType = {
  confirmNewPhone: (payload: { phone: string }) => Promise<void>;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  confirmNewPhone: (payload: { phone: string }) =>
    new Promise((resolve, reject) => dispatch(confirmNewPhoneAction(payload, resolve, reject))),
});

export default connect(mapStateToProps, mapDispatchToProps)(CreateNewPassNoPhone);
