import { Draft, produce } from 'immer';
import jwt_decode from 'jwt-decode';

import { TAction } from '@/stores/types';

import {
  LOADING_REQUEST,
  LOADING_SUCCESS,
  LOGIN_SUCCESS,
  LOGIN_ERROR,
  REFRESH_TOKEN_ERROR,
  IS_VERIFY_PASSWORD_ERROR,
  PUT_PASSWORD_REQUEST,
  PUT_PASSWORD_SUCCESS,
  PUT_PASSWORD_ERROR,
  RESET_PASSWORD_ERROR,
  LOGOUT_SUCCESS,
  SAVE_NEW_USERNAME,
  SAVE_IS_MIGRATED,
  REMIND_BIOMETRIC,
} from './constants';
import initialState, { TAuthenticationState } from './state';

const globalReducer = (state: TAuthenticationState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TAuthenticationState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        draft.loginStatus.error = '';
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case LOGIN_SUCCESS:
        let isVerifyAccount = false;
        if (action.payload) {
          const tokenParser: MODEL.ITokenDecode = jwt_decode(action.payload);
          isVerifyAccount = tokenParser['x-notVerified'] === '0';
        }
        draft.loginStatus.isVerifyAccount = isVerifyAccount;
        draft.loginStatus.isLoged = true;
        draft.loginStatus.isAutoLogin = false;
        draft.loginStatus.remainingLoginAttempts = undefined;
        break;
      case LOGIN_ERROR:
        draft.loginStatus = action.payload;
        break;
      case LOGOUT_SUCCESS:
        draft.loginStatus.isLoged = false;
        draft.loginStatus.isVerifyAccount = false;
        draft.isMigrated = undefined;
        draft.loginStatus.isAutoLogin = false;
        draft.newUsername = '';
        draft.isRemindBiometric = false;
        break;
      case REFRESH_TOKEN_ERROR:
        draft.refreshTokenError = action.payload;
        break;
      case IS_VERIFY_PASSWORD_ERROR:
        draft.isVerifyPasswordError = action.payload;
        break;
      case PUT_PASSWORD_REQUEST:
        draft.isVerifyPasswordError = false;
        break;
      case PUT_PASSWORD_SUCCESS:
        draft.isVerifyPasswordError = false;
        break;
      case PUT_PASSWORD_ERROR:
        draft.isVerifyPasswordError = true;
        break;
      case RESET_PASSWORD_ERROR:
        draft.isVerifyPasswordError = undefined;
        break;
      case SAVE_NEW_USERNAME:
        draft.newUsername = action.payload;
        break;
      case SAVE_IS_MIGRATED:
        draft.isMigrated = action.payload;
        break;
      case REMIND_BIOMETRIC:
        draft.isRemindBiometric = action.payload;
        break;
    }
  });
};

export default globalReducer;
