import React, { useCallback } from 'react';
import { TouchableOpacity, View } from 'react-native';
import Svg, { Circle } from 'react-native-svg';
import { Card, Icon, Text, theme } from 'superapp-common-component';

import SVGIcon from '@/components/SVGIcon';

import styles, { RED_DOT_SIZE } from './styles';
import { HealthReportCardPropsType } from './types';

const { colors } = theme;

const HealthReportCard: React.FC<HealthReportCardPropsType> = ({ onClickItem, data }) => {
  const { title, description, left, shouldShowRedDot, iconName = '', iconGroup = '' } = data;

  const handleClickItem = useCallback(() => onClickItem(data), [data]);

  return (
    <Card
      cardContainerStyle={[styles.cardContainer]}
      left={
        <>
          {shouldShowRedDot && (
            <View style={[styles.redDotContainer]}>
              <Svg height={RED_DOT_SIZE} width={RED_DOT_SIZE} style={styles.redDotSvg}>
                <Circle cx={RED_DOT_SIZE / 2} cy={RED_DOT_SIZE / 2} r={RED_DOT_SIZE / 2} fill={colors.primary.red[100]} />
              </Svg>
            </View>
          )}
          <View style={styles.cardLeftIconWrapper}>
            {left || <SVGIcon name={iconName} group={iconGroup} fillColor={colors.primary.blue[100]} />}
          </View>
        </>
      }
      content={
        <View style={styles.cardContentWrapper}>
          <Text style={styles.cardContentTitle}>{title}</Text>
          <Text style={styles.description}>{description}</Text>
        </View>
      }
      right={
        <TouchableOpacity onPress={handleClickItem}>
          <View style={styles.cartRightIconWrapper}>
            <Icon name="Arrow-Right" color={colors.primary.charcoal[80]} size={20} />
          </View>
        </TouchableOpacity>
      }
    />
  );
};

HealthReportCard.defaultProps = {
  onClickItem: ({}) => {},
};

export default HealthReportCard;
