import React, { useCallback, useRef } from 'react';
import { Image, TouchableOpacity, View, ScrollView } from 'react-native';
import Text from '@/components/Text';
import _find from 'lodash/find';
import _get from 'lodash/get';
import { useTheme, Icon, NativeModuleClient } from 'superapp-common-component';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import SkeletonPlaceholder from 'react-native-skeleton-placeholder';
import dayjs from 'dayjs';

import { a11y, isEmpty } from '@/utils/utils';
import { Theme } from '@/config/themes.config';
import APP_ENV from '@/config/app.config';
import ROUTE_PATH from '@/navigators/routePath';
import { makeSelectLoadingSkeleton, makeSelectNewsList } from '@/stores/global/selectors';
import WebView, { TWebViewUrlRef } from '@/components/WebView';

import { Language } from '../types';
import styles, { CARD_WIDTH, NEWS_ITEM_MARGIN_RIGHT } from './styles';

interface NewsProps {
  currentLang: Language;
  isConnected: boolean;
}

const News = (props: NewsProps) => {
  const { colors } = useTheme() as Theme;
  const { t } = useTranslation();
  const { currentLang, isConnected } = props;
  const newsList = useSelector(makeSelectNewsList());
  const isLoading = useSelector(makeSelectLoadingSkeleton());
  const webviewRef = useRef<TWebViewUrlRef | null>(null);

  const formatProductData = (items: [{ name: string; value: any }]) => {
    const title = _find(items, (element: any) => element?.name === 'shortDescription');
    const issueDate = _find(items, (element: any) => element?.name === 'issueDate');
    const imageUrl = _find(items, (element: any) => element?.name === 'appImage');
    const id = _find(items, (element: any) => element?.name === 'id');
    const link = _find(items, (element: any) => element?.name === 'link');

    return {
      title,
      issueDate,
      imageUrl,
      id,
      link,
    };
  };

  const onOpenLink = (url?: string) => {
    if (!isEmpty(url)) {
      // Linking.openURL(url!);
      webviewRef.current?.openURL(url!);
    }
  };

  const onPressViewAll = useCallback(() => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.HOME,
      unixTimestamp: dayjs().unix(),
      buttonText: 'See all news',
    });
    const linkViewAll = _get(newsList, ['items', currentLang, 'showAllCta', 'link']);
    if (currentLang && !isEmpty(linkViewAll)) {
      onOpenLink(linkViewAll);
    }
  }, [currentLang, newsList]);

  const renderNewsList = () => {
    if (newsList?.items[currentLang]) {
      return newsList.items[currentLang].items.slice(0, 5).map((item: { title: string; elements: [{ name: string; value: any }] }, key) => {
        const { elements } = item;
        const { title, issueDate, imageUrl, id, link } = formatProductData(elements);
        const formatIssueDate = issueDate?.value ? dayjs(issueDate?.value).format('DD/MM/YYYY') : '';

        return (
          <View style={styles.newsItem} key={id?.value + key}>
            <TouchableOpacity
              onPress={() => {
                NativeModuleClient.AdobeExperience.trackButton({
                  screen: ROUTE_PATH.HOME,
                  unixTimestamp: dayjs().unix(),
                  buttonText: title?.value,
                });
                onOpenLink(link?.value);
              }}
            >
              <View style={styles.mb8}>
                <Image source={{ uri: `${APP_ENV.CONTENT_AEM}${imageUrl?.value}` }} style={styles.imageWhatNews} />
              </View>
              <View style={styles.headerNewsItem}>
                <View>
                  <Text
                    style={styles.textWhatNewsItem}
                    xl={{ fontSize: 14, lineHeight: 20 }}
                    ellipsizeMode="tail"
                    numberOfLines={2}
                    fontFamily="bold"
                  >
                    {title?.value}
                  </Text>
                </View>
                <View style={styles.itemNewBottom}>
                  <Icon name="Time1" color={colors.CHARCOAL_80} size={12} />
                  <Text style={styles.subTextNews} xl={{ fontSize: 12, lineHeight: 16 }}>
                    {formatIssueDate}
                  </Text>
                </View>
              </View>
            </TouchableOpacity>
          </View>
        );
      });
    }
  };

  const renderSkeletionProduct = () => {
    return (
      <SkeletonPlaceholder>
        <View style={styles.rowDirection}>
          <View style={styles.NewsItemSkeleton}>
            <View style={styles.headerSkeleton} />
            <View>
              <View style={styles.mb40}>
                <View style={styles.imageNewsSkeleton} />
              </View>
              <View>
                <View style={styles.bottomNewsSkeleton} />
              </View>
            </View>
          </View>

          <View style={styles.NewsItemSkeleton}>
            <View style={styles.headerSkeleton} />
            <View>
              <View style={styles.mb40}>
                <View style={styles.imageNewsSkeleton} />
              </View>
              <View>
                <View style={styles.bottomNewsSkeleton} />
              </View>
            </View>
          </View>
        </View>
      </SkeletonPlaceholder>
    );
  };

  if (!isConnected && isEmpty(newsList)) return null;

  return isLoading && isEmpty(newsList?.items[currentLang]) ? (
    <View style={styles.marginHorizontal16}>{renderSkeletionProduct()}</View>
  ) : !isEmpty(newsList?.items[currentLang]) ? (
    <View style={styles.flex1}>
      <View style={[styles.mb16, styles.headerComponent]}>
        <Text style={styles.itemTitle} fontFamily="bold">
          {t('home.news.title')}
        </Text>
        <TouchableOpacity style={styles.itemHeaderRight} {...a11y('Home.iconSeeAllNews')} onPress={onPressViewAll}>
          <Text fontFamily="bold" style={styles.textItemHeaderRight}>
            {t('home.seeAll')}
          </Text>
          {/* <Icon name="Arrow-Right" color={colors.primary} size={20} /> */}
        </TouchableOpacity>
      </View>
      <View style={styles.mb12}>
        <ScrollView
          contentContainerStyle={styles.scrollStyle}
          disableIntervalMomentum={true}
          decelerationRate={0}
          snapToInterval={CARD_WIDTH * 2 + 2 * NEWS_ITEM_MARGIN_RIGHT}
          showsHorizontalScrollIndicator={false}
          horizontal
          {...a11y('Home.sliderNews')}
        >
          {renderNewsList()}
        </ScrollView>
      </View>
      <WebView ref={webviewRef} />
    </View>
  ) : null;
};

export default News;
