import { Animated } from 'react-native';
import Animation, { type AnimationConfig } from './Animation';

type ScaleAnimationConfig = AnimationConfig & {
  animationDuration?: number;
};

export default class ScaleAnimation extends Animation {
  animationDuration: number;

  constructor({ initialValue = 0, useNativeDriver = false, animationDuration = 250 }: ScaleAnimationConfig = {}) {
    super({ initialValue, useNativeDriver });
    this.animationDuration = animationDuration;
  }

  in(onFinished?: () => void): void {
    Animated.timing(this.animate, {
      toValue: 1,
      duration: this.animationDuration,
      useNativeDriver: this.useNativeDriver,
    }).start(onFinished);
  }

  out(onFinished?: () => void): void {
    Animated.timing(this.animate, {
      toValue: 0,
      duration: this.animationDuration,
      useNativeDriver: this.useNativeDriver,
    }).start(onFinished);
  }

  getAnimations(): Object {
    return { transform: [{ scale: this.animate }] };
  }
}
