import Request from '@/utils/request';
import APP_ENV from '@/config/app.config';

class ICareService extends Request {
  public constructor() {
    super(APP_ENV.APPLICATION_API_ICARE);
  }

  public getAllCaseSubmitService = (params: API.IGetAllCaseSubmit) =>
    this.instance.post<MODEL.ICaseSubmits>('/case/getAllCaseSubmit', params);
  
  public checkAMLService = () =>
    this.instance.post<MODEL.ICheckAML>('/fusion/validateCustomerRule');
}

export default new ICareService();
