import React, { forwardRef, useImperativeHandle, useState } from 'react';
import { View, Image, Linking } from 'react-native';
import { Button, Text } from 'superapp-common-component';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';

import styles from './styles';

export interface IModuleContent {
  title?: string;
  description?: string;
  contentStep1?: string;
  contentStep2?: string;
  contentStep3?: string;
  textPrimaryButton?: string;
  textSecondaryButton?: string;
}

export interface ModalAllowAccessPropsType {
  idPrimaryButton?: string;
  idSecondaryButton?: string;
  cameraProps?: IModuleContent;
  photoProps?: IModuleContent;
  notificationProps?: IModuleContent;
}

export interface ModalAllowAccessRef {
  open: ({ moduletype }: { moduletype: 'photo' | 'camera' | 'notifications' }) => void;
  close: () => void;
}

const renderText = (props: {
  moduletype: 'photo' | 'camera' | 'notifications';
  cameraProps?: IModuleContent;
  photoProps?: IModuleContent;
  notificationProps?: IModuleContent;
}) => {
  const { cameraProps, moduletype, photoProps, notificationProps } = props;
  
  let result: IModuleContent = {};
  if (moduletype === 'photo') {
    return { ...photoProps };
  } else if (moduletype === 'camera') {
    return { ...cameraProps };
  } else if (moduletype === 'notifications') {
    return { ...notificationProps };
  }
  return result;
};

const ModalAllowAccess = (props: ModalAllowAccessPropsType, ref: React.Ref<unknown> | undefined) => {
  const [modalsRef, modalInstance] = useModal();
  const { idPrimaryButton, idSecondaryButton, cameraProps, photoProps, notificationProps } = props;
  const [moduletype, setModuletype] = useState<'photo' | 'camera' | 'notifications'>('photo');

  useImperativeHandle(
    ref,
    (): ModalAllowAccessRef => ({
      open: ({ moduletype }) => onShowModal({ moduletype }),
      close: () => modalInstance()?.close(),
    }),
  );

  const onShowModal = ({ moduletype }: { moduletype: 'photo' | 'camera' | 'notifications' }) => {
    setModuletype(moduletype || 'photo');
    modalInstance()?.show();
  };

  const onPressPrimaryButton = () => {
    modalInstance()?.close()
    setTimeout(() => {
      Linking.openSettings();
    }, 300);
  };

  const onPressSecondaryButton = () => {
    modalInstance()?.close()
  };

  const { title, description, contentStep1, contentStep2, contentStep3, textPrimaryButton, textSecondaryButton } = renderText({
    moduletype,
    cameraProps,
    photoProps,
    notificationProps,
  });

  return (
    <Modal
      ref={modalsRef}
      modalStyle={styles.modalAllow}
      onTouchOutSide={() => modalInstance()?.close()}
    >
      <View style={styles.modalAllowContainer}>
        <View style={styles.contentContainer}>
          <View style={styles.content}>
            <Text.Bold style={styles.titleText}>{title}</Text.Bold>
            {description && <Text style={styles.descriptionText}>{description}</Text>}
          </View>
          <View style={styles.containerStep}>
            <View style={styles.contentStep}>
              <Image style={styles.imageStyles} source={require('@/assets/images/icon-settings.png')} />
              <Text>{`1. ${contentStep1}`}</Text>
            </View>
            <View style={styles.contentStep}>
              <Image style={styles.imageStyles} source={require('@/assets/images/myAIA.png')} />
              <Text>{`2. ${contentStep2}`} </Text>
            </View>
            <View style={[styles.contentStep, styles.mb0]}>
              {moduletype === 'photo' && <Image style={styles.imageStyles} source={require('@/assets/images/icon-photos.png')} />}
              {moduletype === 'camera' && <Image style={styles.imageStyles} source={require('@/assets/images/icon-camera.png')} />}
              {moduletype === 'notifications' && (
                <Image style={styles.imageStyles} source={require('@/assets/images/icon-notification.png')} />
              )}
              <Text>{`3. ${contentStep3}`}</Text>
            </View>
          </View>
          <View style={styles.button}>
            <Button text={textPrimaryButton} onPress={onPressPrimaryButton} style={styles.marginBottom16} id={idPrimaryButton} />
            <Button text={textSecondaryButton} type="secondary" onPress={onPressSecondaryButton} id={idSecondaryButton} />
          </View>
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalAllowAccess);
