import { forwardRef, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react';

import PortalModal from './PortalModal';
import { IBaseModalProps } from './BaseModal';

export type TModalProps = { key?: string; onDismiss?: () => void };
export type TModalRef = {
  show: () => void;
  close: () => void;
};

export const useModal = (): [React.MutableRefObject<TModalRef | null>, () => TModalRef] => {
  const ref = useRef<TModalRef>(null);
  const getInstance = () => ref.current!;

  return [ref, getInstance];
};

type FuncType = (() => void) | undefined;

const Modal: React.ForwardRefRenderFunction<TModalRef, TModalProps & Omit<IBaseModalProps, 'visible'>> = (props, ref) => {
  const id = useRef<null | string>(null);
  const isModalExcuting = useRef<boolean>(false);
  const queuedActions = useRef<FuncType[]>([]);
  const [visible, setVisible] = useState<boolean>(false);
  const isSilenceRef = useRef(false); // used to prevent onDismiss if using Portal.closeAll()

  // Case PortalModal dismiss all modal
  const handleDismissAll = useCallback(() => {
    if (visible) {
      setVisible(false);
    }
    !isSilenceRef.current && props?.onDismiss?.();
    isSilenceRef.current = false; // reset default value
  }, [visible]);

  const close = useCallback(
    (isSilence: boolean) => {
      isSilenceRef.current = isSilence; // prevent to trigger onDimiss child

      if (isModalExcuting.current) {
        queuedActions.current = [];
        queuedActions.current.push(() => {
          setVisible(false);
        });
      } else {
        setVisible(false);
        queuedActions.current = [];
      }
    },
    [visible],
  );

  const onModalExcuted = () => {
    isModalExcuting.current = false;
    if (queuedActions.current.length > 0) {
      queuedActions.current.map(excuteFunc => excuteFunc && excuteFunc());
      queuedActions.current = [];
    }
  };

  const mergedProps = () => ({ ...props, onDismiss: handleDismissAll, onModalExcuted, close });

  const show = () => {
    isModalExcuting.current = true;
    const { children, ...options } = mergedProps();
    id.current = PortalModal.show(children, { ...options, visible });
  };

  const dismiss = () => {
    if (id.current !== null) {
      isModalExcuting.current = true;
      setTimeout(() => {
        PortalModal.dismiss(id.current!);
        id.current = null;
      });
    }
  };

  const update = () => {
    PortalModal.update(id.current!, mergedProps());
  };

  useImperativeHandle(
    ref,
    (): TModalRef => ({
      show: () => {
        if (isModalExcuting.current) {
          queuedActions.current = [];
          queuedActions.current.push(() => {
            setVisible(true);
          });
        } else {
          setVisible(true);
        }
      },
      close: () => close(false),
    }),
    [visible],
  );

  useEffect(() => {
    //  always re-render
    if (id.current) {
      update();
    }
  });

  useEffect(() => {
    if (visible) {
      show();
    } else {
      dismiss();
    }
  }, [visible]);

  useEffect(() => {
    if (!PortalModal.ref) {
      throw new Error(`Can not use component until ModalPortal is mounted`);
    }

    return () => {
      queuedActions.current = [];
      isModalExcuting.current = false;
      id.current && PortalModal.destroy(id.current);
    };
  }, []);

  return null;
};

export default forwardRef(Modal);
