import React, { forwardRef, useImperativeHandle, useMemo } from 'react';
import { View, ViewStyle, StyleProp } from 'react-native';
import { Button } from 'superapp-common-component';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';

import Text from '@/components/Text';

import { makeSelectMembership } from '@/stores/global/selectors';

import styles from './styles';
import Modal, { useModal } from '../Modal/Modal';
import { canOpenVitality } from '@/utils/utils';
import { ERROR_CAN_OPEN_VITALITY } from '@/utils/constants';

interface ModalPermissionVitalityProps {
  title?: string;
  subtitle?: string;
  btnText?: string;
  customBtnText?: string;
  onClose?: () => void;
  onViewIntro?: () => void;
  onDismiss?: () => void;
  btnStyle?: StyleProp<ViewStyle>;
  customBtnStyle?: StyleProp<ViewStyle>;
}

export interface ModalPermissionVitalityRef {
  open: () => void;
  close: () => void;
}

const ModalPermissionVitality = (props: ModalPermissionVitalityProps, ref: React.Ref<unknown> | undefined) => {
  const { onClose, onViewIntro, onDismiss, btnStyle, customBtnStyle } = props;
  const [modalRef, componentModal] = useModal();
  const membership = useSelector(makeSelectMembership());
  const { t } = useTranslation();

  const handleClose = () => {
    componentModal()?.close();
    if (onClose) {
      onClose();
    }
  };

  const handleSubmit = () => {
    componentModal()?.close();
    if (onViewIntro) {
      onViewIntro();
    }
  };

  const description = useMemo(() => {
    if (membership) {
      const error = canOpenVitality(membership, true);
      switch (error) {
        case ERROR_CAN_OPEN_VITALITY.NON_MEMBERSHIP:
          return t('common.vitality.non.membership');
        case ERROR_CAN_OPEN_VITALITY.NON_MEMBERSHIP_ACTIVE:
          return t('common.vitality.non.active.membership');
        case ERROR_CAN_OPEN_VITALITY.NON_MEMBERSHIP_FEE:
          return t('common.vitality.non.fee.membership');
        default:
          return t('common.vitality.non.membership');
      }
    }
  }, [membership, t]);

  useImperativeHandle(
    ref,
    (): ModalPermissionVitalityRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal()?.close(),
    }),
  );

  return (
    <Modal ref={modalRef} modalStyle={styles.modalContainer} onDismiss={onDismiss}>
      <View style={styles.modalContent}>
        <View style={styles.mgBottom8}>
          <Text style={styles.title} fontFamily="bold">
            {t('common.vitality.non.membership.title')}
          </Text>
          <Text style={styles.subtitle}>{description}</Text>
        </View>

        <Button
          onPress={handleSubmit}
          text={t('common.vitality.non.membership.btn.intro')}
          type="primary"
          size="large"
          uppercase={false}
          labelStyle={styles.buttonText}
          style={[styles.button, btnStyle]}
          id="ModalConfirmErrorVitality.btnOnOpenIntroVitality"
        />
        <Button
          onPress={handleClose}
          text={t('common.vitality.non.membership.btn.close')}
          type="secondary"
          size="large"
          uppercase={false}
          labelStyle={styles.buttonText}
          style={[styles.button, customBtnStyle]}
          id="ModalConfirmErrorVitality.btnOnClose"
        />
      </View>
    </Modal>
  );
};

export default forwardRef(ModalPermissionVitality);
