import { TAction } from '@/stores/types';

import {
  GET_ALL_NOTIFICATION,
  GET_ALL_NOTIFICATION_BROADCAST,
  SAVE_NOTIFICATION,
  SAVE_NOTIFICATION_BROADCAST,
  SAVE_PAGING,
  LOADING_REQUEST,
  LOADING_SUCCESS,
  READ_ALL_NOTIFICATION,
  READ_NOTIFICATION_BY_ID,
  LOAD_MORE_NOTIFICATION,
  LOAD_MORE_NOTIFICATION_BROADCAST,
  DELETE_NOTIFICATION,
  LOADING_LOAD_MORE_REQUEST,
  LOADING_LOAD_MORE_SUCCESS,
  GET_NOTIFICATION_UNREAD,
  SAVE_NOTIFICATION_UNREAD,
  IS_LOAD_MORE_GENERAL,
  IS_LOAD_MORE_BROADCAST,
  GET_CLAIM_DETAIL_DATA,
} from './constants';

export const loadingSuccess = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequest = (): TAction => ({ type: LOADING_REQUEST });

export const loadingLoadMoreSuccess = (): TAction => ({ type: LOADING_LOAD_MORE_SUCCESS });
export const loadingLoadMoreRequest = (): TAction => ({ type: LOADING_LOAD_MORE_REQUEST });

export const getAllNotification = (): TAction => ({ type: GET_ALL_NOTIFICATION });
export const getAllNotificationBroadcast = (): TAction => ({ type: GET_ALL_NOTIFICATION_BROADCAST });
export const savePaging = (payload: MODEL.IPagingNoti): TAction => ({ type: SAVE_PAGING, payload });
export const saveNotification = (payload: { data: MODEL.INotification[]; totalPages?: number }): TAction => ({
  type: SAVE_NOTIFICATION,
  payload,
});
export const saveNotificationBroadcast = (payload: { dataBroadcast: MODEL.INotification[]; totalPages?: number }): TAction => ({
  type: SAVE_NOTIFICATION_BROADCAST,
  payload,
});

export const getNotificationUnRead = (): TAction => ({ type: GET_NOTIFICATION_UNREAD });
export const saveNotificationUnRead = (payload?: MODEL.INotificationUnRead): TAction => ({ type: SAVE_NOTIFICATION_UNREAD, payload });

export const readAllNotification = (): TAction => ({ type: READ_ALL_NOTIFICATION });
export const readNotificationById = (payload: any): TAction => ({ type: READ_NOTIFICATION_BY_ID, payload });
export const loadMoreNotification = (): TAction => ({ type: LOAD_MORE_NOTIFICATION });
export const loadMoreNotificationBroadcast = (): TAction => ({ type: LOAD_MORE_NOTIFICATION_BROADCAST });
export const deleteNotification = (payload: any): TAction => ({ type: DELETE_NOTIFICATION, payload });

export const isLoadMoreGeneral = (payload: boolean): TAction => ({ type: IS_LOAD_MORE_GENERAL, payload });
export const isLoadMoreBroadcast = (payload: boolean): TAction => ({ type: IS_LOAD_MORE_BROADCAST, payload });

export const getClaimDetailData = (payload: any, resolve: any, reject: any): TAction => ({ type: GET_CLAIM_DETAIL_DATA, payload, resolve, reject });

export function asyncGetClaimDetailData(dispatch: any) {
  return function returnAsync(payload: any) {
    return new Promise<MODEL.IClaimTransactionResponse>((resolve, reject) => dispatch(getClaimDetailData(payload, resolve, reject)));
  };
}
