import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  SAVE_PROFILE,
  LOADING_REQUEST,
  LOADING_SUCCESS,
  SAVE_HOME_DATA,
  SAVE_POLICIES,
  LOADING_HOME_DATA_REQUEST,
  LOADING_HOME_DATA_SUCCESS,
  LOADING_POLICY_REQUEST_SUCCESS,
  LOADING_POLICY_REQUEST,
  SAVE_PRODUCT_IMAGE,
  SAVE_POLICIES_ACK,
  LOADING_POLICY_ACK_REQUEST,
  LOADING_POLICY_ACK_REQUEST_SUCCESS,
  SAVE_POLICIES_INCOMMING_PAYMENT,
  SAVE_CLAIM_DRAFT_INFO,
  SAVE_ACTION_LINKS_PROFILE,
  SAVE_HELP_CENTER_PROFILE,
  SAVE_MEMBERSHIP,
  SAVE_TIER_VIP,
} from './constants';
import initialState, { TGlobalState } from './state';

const globalReducer = (state: TGlobalState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TGlobalState>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case SAVE_PROFILE:
        draft.profile = action.payload;
        break;
      case LOADING_HOME_DATA_REQUEST:
        draft.AEMData.isLoading = true;
        break;
      case LOADING_HOME_DATA_SUCCESS:
        draft.AEMData.isLoading = false;
        break;
      case LOADING_POLICY_REQUEST:
        draft.policies.isLoadingPolicy = true;
        break;
      case LOADING_POLICY_REQUEST_SUCCESS:
        draft.policies.isLoadingPolicy = false;
        break;
      case SAVE_HOME_DATA:
        draft.AEMData = action.payload;
        break;
      case SAVE_POLICIES:
        draft.policies.data = action.payload;
        break;
      case SAVE_PRODUCT_IMAGE:
        draft.productImage.data = action.payload;
        break;
      case SAVE_POLICIES_ACK:
        draft.policiesAck.data = action.payload;
        break;
      case LOADING_POLICY_ACK_REQUEST:
        draft.policiesAck.isLoadingPolicyAck = true;
        break;
      case LOADING_POLICY_ACK_REQUEST_SUCCESS:
        draft.policiesAck.isLoadingPolicyAck = false;
        break;
      case SAVE_POLICIES_INCOMMING_PAYMENT:
        draft.policiesIncommingPayment = action.payload;
        break;
      case SAVE_CLAIM_DRAFT_INFO:
        draft.draftInfo = action.payload;
        break;
      case SAVE_ACTION_LINKS_PROFILE:
        draft.actionLinks = action.payload;
        break;
      case SAVE_HELP_CENTER_PROFILE:
        draft.helpCenter = action.payload;
        break;
      case SAVE_MEMBERSHIP:
        draft.membership = action.payload;
        break;
      case SAVE_TIER_VIP:
        draft.tierVip = action.payload;
        break;
    }
  });
};

export default globalReducer;
