import { TAction } from '@/stores/types';

import { 
    LOADING_REQUEST,
    LOADING_SUCCESS,
    SUBMIT_CHANGE_PASSWORD,
    SUBMIT_CHANGE_PASSWORD_PRE_LOGIN,
} from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });

export const submitChangePasswordAction = (payload: any,resolve: () => void, reject: (value: unknown) => void): TAction => ({ type: SUBMIT_CHANGE_PASSWORD, payload,resolve, reject });
export const submitChangePasswordPreLoginAction = (payload: any,resolve: () => void, reject: (value: unknown) => void): TAction => ({ type: SUBMIT_CHANGE_PASSWORD_PRE_LOGIN, payload,resolve, reject });
