import { takeLatest, call, put, select, all, delay } from 'redux-saga/effects';
import { TAction } from '@/stores/types';

import CapchaService from '@/services/capcha';
import {
  VERIFY_ID_PERSONAL_NUMBER,
  GET_CAPCHA,
  REQUEST_OTP_BY_SMS,
  SUBMIT_OTP,
  SUBMIT_FORGOT_USERNAME,
} from './constants';
import {
  loadingRequestAction,
  loadingSuccessAction,
  saveCaptchaAction,
  putOtpRequest,
  putOtpError,
  putCaptchaRequest,
  putCaptchaSuccess,
  putCaptchaError,
  saveUserId,
  savePhoneNumber,
  saveDob,
  saveIdNo,
  saveSessionId,
  saveOtpId,
} from './actions';
import APP_ENV from '@/config/app.config';
import { makeSelectDob, makeSelectIdNo, makeSelectPhoneNumber, makeSelectSessionId, makeSelectUserId, makeSelectOtpId } from './selectors';
import AuthService from '@/services/auth';
import APIError from '@/utils/errors';
import { removeSpacingsAndLower } from '@/utils/utils';

const { CLIENT_ID } = APP_ENV;

function* verifyIdNo({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { identificationNumber, idDocType } = payload;
    const userId: string = yield select(makeSelectUserId());
    const param = {
      userId,
      idNo: identificationNumber,
      clientId: CLIENT_ID,
      idDocType,
    };
    const response: MODEL.ICheckIdNoRes = yield call(AuthService.checkIdno, param);
    yield put(saveUserId(response?.userId));
  } catch (error) {
    const { setError } = payload;
    setError('identificationNumber', {
      type: 'manual',
      message: (error as APIError).message,
    });
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleGetCapcha() {
  try {
    const response: MODEL.ICapcha = yield call(CapchaService.getCapchaService);
    yield put(saveCaptchaAction(response));
  } catch (error) {
    console.log('error', error);
  } finally {
  }
}

function* handleRequestOtpBySms({ payload }: TAction) {
  try {
    yield put(loadingRequestAction());
    yield put(putCaptchaRequest());
    const [sessionId, phoneNo, idCardNo, dob]: Array<string> = yield all([
      select(makeSelectSessionId()), 
      select(makeSelectPhoneNumber()),
      select(makeSelectIdNo()),
      select(makeSelectDob()),
    ]);
    const { params, requestOtpCallback } = payload;
    const fullParams = { ...params, clientId: CLIENT_ID, sessionId, phoneNo, idCardNo, dob };
    yield delay(450);
    const response: MODEL.IForgotUsernameOtpRes = yield call(AuthService.forgotUsernameReqOtp, fullParams);
    requestOtpCallback(120);
    yield put(putCaptchaSuccess());
    yield put(saveSessionId(response?.sessionId));
    yield put(saveOtpId(response?.otpId));
  } catch (error) {
    yield put(putCaptchaError((error as APIError).message));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleSubmitOtp({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestAction());
    yield put(putOtpRequest());
    const [sessionId, phoneNo, idCardNo, dob, otpId]: Array<string> = yield all([
      select(makeSelectSessionId()), 
      select(makeSelectPhoneNumber()),
      select(makeSelectIdNo()),
      select(makeSelectDob()),
      select(makeSelectOtpId()),
    ]);
    const { otpValue } = payload;
    const params = { otpValue, clientId: CLIENT_ID, sessionId, phoneNo, idCardNo, dob, otpId };
    const response: MODEL.IForgotUsernameConfirmRes = yield call(AuthService.forgotUsernameConfirm, params);
    resolve(response?.username);
  } catch (error) {
    reject(error);
    yield put(putOtpError((error as APIError).message));
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* handleSubmitForgotUsername({ payload, resolve, reject }: TAction) {
  try {
    yield put(loadingRequestAction());
    const { phoneNo, idCardNo, dob } = payload;
    const params = { phoneNo: removeSpacingsAndLower(phoneNo), idCardNo: removeSpacingsAndLower(idCardNo), dob, clientId: CLIENT_ID };
    const response: MODEL.ISubmitForgotUsernameRes = yield call(AuthService.submitForgotUsername, params);
    yield all([
      put(savePhoneNumber(removeSpacingsAndLower(phoneNo))), 
      put(saveDob(dob)), 
      put(saveIdNo(removeSpacingsAndLower(idCardNo))), 
      put(saveSessionId(response?.sessionId)),
    ])
    resolve(response);
  } catch (error) {
    reject((error as APIError).message);
  } finally {
    yield put(loadingSuccessAction());
  }
}

function* forgotUsernameSaga() {
  yield takeLatest(VERIFY_ID_PERSONAL_NUMBER, verifyIdNo);
  yield takeLatest(GET_CAPCHA, handleGetCapcha);
  yield takeLatest(REQUEST_OTP_BY_SMS, handleRequestOtpBySms);
  yield takeLatest(SUBMIT_OTP, handleSubmitOtp);
  yield takeLatest(SUBMIT_FORGOT_USERNAME, handleSubmitForgotUsername);
}

export default forgotUsernameSaga;
