import React, { useLayoutEffect, useRef, useState } from 'react';
import { View, ScrollView, SafeAreaView, TouchableOpacity, StatusBar } from 'react-native';
import { TextInput, Button, Icon, NativeModuleClient } from 'superapp-common-component';
import { useForm, Controller } from 'react-hook-form';
import _isEmpty from 'lodash/isEmpty';
import { useTranslation } from 'react-i18next';
import { useNavigation, useRoute, RouteProp } from '@react-navigation/native';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import { useDispatch } from 'react-redux';

import { logout as logoutAction } from '@/stores/authentication/actions';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import Text from '@/components/Text';

import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import dayjs from 'dayjs';

import { TConnectState } from '@/stores/types';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';
import { a11y, checkRuleNoSequencedPassword } from '@/utils/utils';
import { FORGOT_PASS_ERROR_CODES, REGEX } from '@/utils/constants';
import { HEADER_TITLE_BGRWHITE, HITSLOP } from '@/utils/styles';
import Loading, { ILoadingRef } from '@/components/Loading';
import ROUTE_PATH from '@/navigators/routePath';

import { loginOkta } from '@/stores/authentication/actions';

import { NS_CHANGE_PASSWORD_TYPE } from './stores/constants';
import changePasswordTypeReducer from './stores/reducer';
import changePasswordTypeSaga from './stores/saga';
import { TChangePasswordTypeState } from './stores/state';
import { makeSelectLoading } from './stores/selectors';
import { submitChangePasswordAction, submitChangePasswordPreLoginAction } from './stores/actions';

import styles from './styles';

type FormChangePasswordValues = {
  currentPassword: string;
  newPassword: string;
  confirmPass: string;
};
type ChangePasswordNavigationProp = StackNavigationProp<TStackParamList, 'ChangePassword'>;
type ChangePasswordProps = { loading: boolean; submitChangePassword: any; submitChangePasswordPreLogin: any };
type ChangePasswordRouteProps = RouteProp<TStackParamList, 'ChangePassword'>;

const ChangePassword: React.FC<ChangePasswordProps> = ({ loading, submitChangePassword, submitChangePasswordPreLogin }) => {
  useInjectReducer({ key: NS_CHANGE_PASSWORD_TYPE, reducer: changePasswordTypeReducer });
  useInjectSaga({ key: NS_CHANGE_PASSWORD_TYPE, saga: changePasswordTypeSaga });

  const { t } = useTranslation();
  const dispatch = useDispatch();

  const route = useRoute<ChangePasswordRouteProps>();
  const navigation = useNavigation<ChangePasswordNavigationProp>();
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const loadingRef = useRef<ILoadingRef>(null);

  const [isSecureCurrentPW, setIsSecureCurrentPW] = useState(true);
  const [isSecureNewPW, setIsSecureNewPW] = useState(true);
  const [isSecureConfirmPW, setIsSecureConfirmPW] = useState(true);
  const [infoAfterChangePassSuccess, setInfoAfterChangePassSuccess] = useState<{ username: string; password: string }>({
    username: '',
    password: '',
  });

  const {
    control,
    handleSubmit,
    formState: { errors },
    watch,
    getValues,
    setError,
    clearErrors,
  } = useForm<FormChangePasswordValues | any>();

  const { preLogin } = route.params;

  const onCheckRulePasswordDuplicateSequenced = (value: string) => {
    if (value) {
      if (!REGEX.NO_DUPLICATE_CHARACTER.test(value) && checkRuleNoSequencedPassword(value)) return true;
    }
    return false;
  };

  const onSubmit = async (data: FormChangePasswordValues) => {
    try {
      NativeModuleClient.AdobeExperience.trackButton({
        screen: ROUTE_PATH.CHANGE_PASSWORD,
        unixTimestamp: dayjs().unix(),
        buttonText: 'Continue',
      });
      let infoSuccess: { username: string; password: string } = {
        username: '',
        password: ''
      };
      if (preLogin) {
        const newInfo = await submitChangePasswordPreLogin({ ...data });
        infoSuccess = { ...newInfo };
      } else {
        const newInfo = await submitChangePassword({ ...data });
        infoSuccess = { ...newInfo };
      }
      setInfoAfterChangePassSuccess(infoSuccess);
      await dispatch(logoutAction({ neededCallApi: true, isChangePassword: true }));
    } catch (error) {
      setError('globalError', {
        type: 'manual',
        message: error === FORGOT_PASS_ERROR_CODES.PASSS_HISTORY_INVALID ? t('forgot.password.changePass.error.duplicateHistory') : t('changePassword.submitFail'),
      });
    }
  };

  const onHandleSuccess = async () => {
    navigation.navigate(ROUTE_PATH.DASHBOARD, { isResetPassSuccess: true });
    dispatch(loginOkta({ ...infoAfterChangePassSuccess, setVisibleLoginModal: () => null, isAutoLogin: true }));
  };

  const onHideModalErrorGlobal = () => {
    clearErrors();
  };

  const onDismissLoading = () => {
    if (!_isEmpty(errors?.globalError)) {
      modalErrorRef?.current?.open();
    } else {
      NativeModuleClient.AdobeExperience.trackScreen({
        screen: 'ChangePassword Success',
        unixTimestamp: dayjs().unix(),
      });
      onHandleSuccess();
    }
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => (
        <Text style={HEADER_TITLE_BGRWHITE} fontFamily="bold">
          {t('changePassword.title')}
        </Text>
      ),
      headerTitleStyle: styles.mainTitle,
      headerLeft: () => (
        <TouchableOpacity
          onPress={() => navigation.canGoBack() && navigation.goBack()}
          hitSlop={HITSLOP}
          {...a11y('ChangePassword.iconBackHeaderLeft')}
        >
          <Icon name="Arrow-Left" color="#5B636B" size={24} style={styles.marginLeft16} />
        </TouchableOpacity>
      ),
      headerRight: () => (
        <TouchableOpacity onPress={() => null} {...a11y('ChangePassword.iconInfoHeaderRight')} hitSlop={HITSLOP}>
          <Icon name="Info1" size={24} color="#5B636B" style={styles.marginRight16} />
        </TouchableOpacity>
      ),
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  return (
    <SafeAreaView style={styles.container}>
      <StatusBar barStyle="dark-content" />
      <ScrollView>
        <View style={styles.parent}>
          <View>
            {/* Current Password */}
            <Controller
              control={control}
              rules={{
                required: { value: true, message: t('changePassword.form.validate.required') },
              }}
              render={({ field: { onChange, value } }) => (
                <TextInput
                  label={t('changePassword.form.currentPass')}
                  value={value}
                  disabled={false}
                  onChangeText={onChange}
                  type={errors.currentPassword && 'error'}
                  containerStyle={styles.marginBottomInput}
                  message={errors.currentPassword?.message || undefined}
                  secureTextEntry={isSecureCurrentPW}
                  right={{ source: isSecureCurrentPW ? 'Show' : 'Mask' }}
                  iconColor="#000000"
                  style={styles.heightInput}
                  onPressIcon={() => setIsSecureCurrentPW(!isSecureCurrentPW)}
                  messageStyle={styles.messErrorFieldStyle}
                  maxLength={64}
                  id="ChangePassword.inputCurrentPassword"
                  textContentType="oneTimeCode"
                  autoComplete="off"
                />
              )}
              name="currentPassword"
            />
            {/* Password */}
            <Controller
              control={control}
              rules={{
                required: { value: true, message: t('changePassword.form.validate.required') },
                pattern: {
                  value: REGEX.PASSWORD,
                  message: t('changePassword.form.validate.password'),
                },
                validate: value => checkRuleNoSequencedPassword(value) || t('changePassword.form.validate.password'),
              }}
              render={({ field: { onChange, value } }) => (
                <TextInput
                  label={t('changePassword.form.newPass')}
                  value={value}
                  disabled={false}
                  onChangeText={onChange}
                  type={errors.newPassword && 'error'}
                  containerStyle={styles.marginBottomInput}
                  message={errors.newPassword?.message || undefined}
                  secureTextEntry={isSecureNewPW}
                  right={{ source: isSecureNewPW ? 'Show' : 'Mask' }}
                  iconColor="#000000"
                  style={styles.heightInput}
                  onPressIcon={() => setIsSecureNewPW(!isSecureNewPW)}
                  messageStyle={styles.messErrorFieldStyle}
                  maxLength={64}
                  id="ChangePassword.inputNewPassword"
                  textContentType="oneTimeCode"
                  autoComplete="off"
                />
              )}
              name="newPassword"
            />
            {/* Re-password */}
            <Controller
              control={control}
              rules={{
                required: { value: true, message: t('changePassword.form.validate.required') },
                validate: value => value === watch('newPassword') || t('changePassword.form.validate.confirmPassword'),
              }}
              render={({ field: { onChange, value } }) => (
                <TextInput
                  label={t('changePassword.form.confirmPass')}
                  value={value}
                  disabled={false}
                  onChangeText={onChange}
                  type={errors.confirmPass && 'error'}
                  // containerStyle={styles.marginBottomInput}
                  message={errors.confirmPass?.message || undefined}
                  secureTextEntry={isSecureConfirmPW}
                  right={{ source: isSecureConfirmPW ? 'Show' : 'Mask' }}
                  iconColor="#000000"
                  style={styles.heightInput}
                  onPressIcon={() => setIsSecureConfirmPW(!isSecureConfirmPW)}
                  maxLength={64}
                  messageStyle={[styles.messErrorFieldStyle, styles.mb_0]}
                  id="ChangePassword.inputConfirmPassword"
                  textContentType="oneTimeCode"
                  autoComplete="off"
                />
              )}
              name="confirmPass"
            />
          </View>
          <View style={styles.rule}>
            <View style={styles.infoRule}>
              <Icon
                name={watch('newPassword') && REGEX.AT_LEAST_9_CHARACTER.test(getValues('newPassword')) ? 'Success' : 'Info1'}
                color={watch('newPassword') && REGEX.AT_LEAST_9_CHARACTER.test(getValues('newPassword')) ? '#3DA758' : '#1F78AD'}
                style={styles.iconRule}
              />
              <Text style={styles.text}>{t('changePassword.content.rule1')}</Text>
            </View>
            <View style={styles.infoRule}>
              <Icon
                name={watch('newPassword') && REGEX.AT_LEAST_UPPERCASE.test(getValues('newPassword')) ? 'Success' : 'Info1'}
                color={watch('newPassword') && REGEX.AT_LEAST_UPPERCASE.test(getValues('newPassword')) ? '#3DA758' : '#1F78AD'}
                style={styles.iconRule}
              />
              <Text style={styles.text}>{t('changePassword.content.rule2')}</Text>
            </View>
            <View style={styles.infoRule}>
              <Icon
                name={watch('newPassword') && REGEX.AT_LEAST_LOWERCASE.test(getValues('newPassword')) ? 'Success' : 'Info1'}
                color={watch('newPassword') && REGEX.AT_LEAST_LOWERCASE.test(getValues('newPassword')) ? '#3DA758' : '#1F78AD'}
                style={styles.iconRule}
              />
              <Text style={styles.text}>{t('changePassword.content.rule3')}</Text>
            </View>
            <View style={styles.infoRule}>
              <Icon
                name={watch('newPassword') && onCheckRulePasswordDuplicateSequenced(getValues('newPassword')) ? 'Success' : 'Info1'}
                color={watch('newPassword') && onCheckRulePasswordDuplicateSequenced(getValues('newPassword')) ? '#3DA758' : '#1F78AD'}
                style={styles.iconRule}
              />
              <Text style={styles.text}>{t('changePassword.content.rule4')}</Text>
            </View>
            <View style={styles.infoRule}>
              <Icon
                name={watch('newPassword') && REGEX.AT_LEAST_CAPTIAL_SPECIAL_NUMBER.test(getValues('newPassword')) ? 'Success' : 'Info1'}
                color={watch('newPassword') && REGEX.AT_LEAST_CAPTIAL_SPECIAL_NUMBER.test(getValues('newPassword')) ? '#3DA758' : '#1F78AD'}
                style={styles.iconRule}
              />
              <Text style={styles.text}>{t('changePassword.content.rule5')}</Text>
            </View>
          </View>
        </View>
        <View style={styles.button}>
          <Button
            text={t('changePassword.button.continue')}
            onPress={handleSubmit(onSubmit)}
            disabled={loading}
            id="ChangePassword.btnSubmitChangePassword"
          />
        </View>
        <ModalError
          ref={modalErrorRef}
          title={t('changePassword.modalError.title')}
          subtitle={errors?.globalError?.message}
          btnText={t('changePassword.result.button')}
          onConfirm={onHideModalErrorGlobal}
        />
        <Loading visible={loading} ref={loadingRef} onDismiss={onDismissLoading} />
      </ScrollView>
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TChangePasswordTypeState['loading'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
});

type MapDispatchToPropsType = {
  submitChangePassword: (payload: any) => Promise<void>;
  submitChangePasswordPreLogin: (payload: any) => Promise<void>;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  submitChangePassword: payload => new Promise((resolve, reject) => dispatch(submitChangePasswordAction(payload, resolve, reject))),
  submitChangePasswordPreLogin: payload =>
    new Promise((resolve, reject) => dispatch(submitChangePasswordPreLoginAction(payload, resolve, reject))),
});

export default connect(mapStateToProps, mapDispatchToProps)(ChangePassword);
