import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_AUTHENTICATION } from './constants';

const currentState = (state: TConnectState) => state?.[NS_AUTHENTICATION] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectCheckLogin = () => createSelector(currentState, state => state.loginStatus);
const makeSelectRefreshTokenError = () => createSelector(currentState, state => state.refreshTokenError);
const makeIsVerifyPasswordError = () => createSelector(currentState, state => state.isVerifyPasswordError);
const makeSelectNewUsername = () => createSelector(currentState, state => state.newUsername);
const makeSelectIsMigrated = () => createSelector(currentState, state => state.isMigrated);
const makeSelectIsRemindBiometric = () => createSelector(currentState, state => state.isRemindBiometric);

export {
  currentState,
  makeSelectLoading,
  makeSelectCheckLogin,
  makeSelectRefreshTokenError,
  makeIsVerifyPasswordError,
  makeSelectNewUsername,
  makeSelectIsMigrated,
  makeSelectIsRemindBiometric,
};
