import React, {useState, useEffect, useMemo} from 'react';
import {View, ScrollView, Image} from 'react-native';
import {useTranslation} from 'react-i18next';
import {useTheme} from 'superapp-common-component';

import Text from '@/components/Text';
import {Button} from 'superapp-common-component';

import Styles from './styles';
import {trans} from './configs';

interface PropsType {
  onPress: () => void,
};

const Restriction = (props: PropsType) => {
  const {onPress} = props;
  const theme = useTheme();
  const {t} = useTranslation();
  const [show, setShow] = useState<boolean>(false);

  useEffect(() => {
    setTimeout(() => {
      setShow(true);
    }, 2000);
  }, []);

  const renderImage = useMemo(() => {
    return (
      <View style={Styles.imageContainer}>
        <Image
          style={Styles.image}
					source={require('@/assets/images/maintance.png')}
        />
      </View>
    );
  }, [trans, show]);

  const renderContent = useMemo(() => {
    return (
      <View style={Styles.contentContainer}>
        <Text style={[Styles.title, theme.fonts.semiBold]}>{t(trans.title)}</Text>
        <Text style={Styles.description}>{t(trans.description)}</Text>
        <Text style={Styles.description}>{t(trans.description2)}</Text>
      </View>
    );
  }, [trans, theme, show]);

  const renderButton = useMemo(() => {
    return (
      <View style={Styles.buttonContainer}>
        <Button
          type="primary"
          accessibilityLabel={t(trans.button)}
          onPress={onPress}
          text={t(trans.button)}
        >
          {t(trans.button)}
        </Button>
      </View>
    );
  }, [trans, show]);

  const content = (
    <View style={Styles.container}>
      <ScrollView centerContent>
        {renderImage}
        {renderContent}
      </ScrollView>
      {renderButton}
    </View>
  );
  return show ? content : null;
};

export default Restriction;
