import React, { forwardRef, useState, useImperativeHandle, useRef } from 'react';
import { Button, NativeModuleClient } from 'superapp-common-component';
import { View, Image } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useNavigation } from '@react-navigation/native';
import dayjs from 'dayjs';

import ROUTE_PATH from '@/navigators/routePath';

import Text from '@/components/Text';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import { PortalModal } from '@/components/Modal';
import Modal, { useModal } from '@/components/Modal/Modal';

import styles from './styles';
export interface AccountReadyRef {
  open: () => void;
  close: () => void;
}

const AccountReady = (props: any, ref: React.Ref<unknown> | undefined) => {
  const { t } = useTranslation();
  const [modalRef, modalInstance] = useModal();
  const [messageErrorPin, setMessageErrorPin] = useState<string>('');

  const modalErrorPin = useRef<null | ModalConfirmErrorRef>(null);

  const navigation = useNavigation<any>();

  const onHideModalErrorPin = () => {
    setMessageErrorPin('');
  };

  useImperativeHandle(
    ref,
    (): AccountReadyRef => ({
      open: () => {
        modalInstance()?.show();
      },
      close: () => {
        modalInstance()?.close();
      },
    }),
    [],
  );

  const onConfirmSuccess = () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: 'Register Success',
      unixTimestamp: dayjs().unix(),
      buttonText: 'Back Dashboard',
    });
    props?.setIsRegisterSuccess(false);
    // wait for all modal dismiss
    PortalModal.closeAll().then(() => {
      navigation.replace(ROUTE_PATH.DASHBOARD, { openModalLogin: true });
    });
  };

  return (
    <Modal ref={modalRef} modalStyle={styles.containerAccountReady} animationType="slideRight">
      <View style={styles.container}>
        <View style={styles.contentWrapper}>
          <Image style={styles.image} resizeMode="contain" source={require('@/assets/images/account-ready.png')} />
          <Text style={styles.textTitle} fontFamily="bold">
            {t('register.account.ready')}
          </Text>
        </View>
        <View style={styles.marginBottom}>
          <Button
            text={t('register.button.backHome')}
            onPress={() => onConfirmSuccess()}
            id="Register.AccountReady.btnSkipSetting"
            style={styles.button}
            type="primary"
            uppercase={false}
          />
        </View>
      </View>
      <ModalError
        ref={modalErrorPin}
        title={t('register.modalError.title')}
        subtitle={messageErrorPin}
        btnText={t('register.modalError.button')}
        onConfirm={onHideModalErrorPin}
      />
    </Modal>
  );
};

export default forwardRef(AccountReady);
