import React, { FC, useEffect, useRef } from 'react';
import { Animated, GestureResponderEvent, StyleSheet, TouchableOpacity } from 'react-native';

export interface IBackdropProps {
  visible?: boolean;
  opacity?: number;
  animationDuration?: number;
  pointerEvents?: 'box-none' | 'none' | 'box-only' | 'auto';
  backgroundColor?: string;
  onPress?: (event: GestureResponderEvent) => void;
}

const defaultStyles = StyleSheet.create({
  backdrop: {
    ...StyleSheet.absoluteFillObject,
  },
});

const Backdrop: FC<IBackdropProps> = ({ visible, opacity = 0.2, backgroundColor, pointerEvents, animationDuration, onPress }) => {
  const animationOpacityRef = useRef(new Animated.Value(0)).current;

  useEffect(() => {
    Animated.timing(animationOpacityRef, {
      toValue: visible ? opacity! : 0,
      duration: animationDuration || 100,
      useNativeDriver: true,
    }).start();
  }, [visible]);

  return (
    <Animated.View
      pointerEvents={pointerEvents}
      style={StyleSheet.flatten([
        defaultStyles.backdrop,
        {
          opacity: animationOpacityRef,
          backgroundColor: backgroundColor || '#000',
        },
      ])}
    >
      <TouchableOpacity onPress={onPress} style={StyleSheet.absoluteFill} />
    </Animated.View>
  );
};

export default Backdrop;
