import React, { forwardRef, useImperativeHandle } from 'react';
import { View, Image, SafeAreaView, StatusBar } from 'react-native';
import { Button } from 'superapp-common-component';

import personImage from '@/assets/images/pincode-success-person.png';
import Text from '@/components/Text';
import Modal, { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface ModalConfirmSuccessProps {
  title?: string;
  subtitle?: string;
  btnText?: string;
  onConfirm?: () => void;
}

export interface ModalConfirmSuccessRef {
  open: () => void;
  close: () => void;
}

const ModalConfirmSuccess = (props: ModalConfirmSuccessProps, ref: React.Ref<unknown> | undefined) => {
  const { onConfirm, title, subtitle, btnText } = props;
  const [modalRef, componentModal] = useModal();

  useImperativeHandle(
    ref,
    (): ModalConfirmSuccessRef => ({
      open: () => componentModal()?.show(),
      close: () => componentModal()?.close(),
    }),
    [modalRef.current]
  );

  const handleSubmit = () => {
    componentModal()?.close()
    if (onConfirm) {
      onConfirm();
    }
  };

  return (
    <Modal ref={modalRef} animationType="slideRight">
      <StatusBar barStyle="dark-content" />
      <SafeAreaView />
      <View style={styles.successModalContainer}>
        <View>
          <View style={{ alignItems: 'center' }}>
            <Image style={styles.imageStyle} resizeMode="contain" source={personImage} />
          </View>
          <Text style={styles.successTitle} fontFamily="bold">{title}</Text>
          <Text style={styles.subSuccessTitle}>{subtitle}</Text>
        </View>

        <View style={styles.buttonBox}>
          <Button
            onPress={handleSubmit}
            text={btnText ? btnText : 'Close'}
            size="large"
            type="primary"
            uppercase={false}
            id="ModalConfirmSuccess.btnCloseModalConfirmSuccess"
          />
        </View>
      </View>
    </Modal>
  );
};

export default forwardRef(ModalConfirmSuccess);
