import React, { forwardRef, useImperativeHandle } from 'react';
import { View, TouchableWithoutFeedback, Text } from 'react-native';
import { Button, Icon } from 'superapp-common-component';

import { a11y } from '@/utils/utils';
import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';

import styles from './styles';

interface ModalConfirmPropsType {
  mainTitle: string;
  subTitle: string;
  topButtonText?: string;
  bottomButtonText?: string;
  topButtonPress?: (param?: any) => void;
  bottomButtonPress?: (param?: any) => void;
  [key: string]: any;
}

export interface ModalConfirmRef {
  open: () => void;
  close: () => void;
}

const ModalConfirm = (props: ModalConfirmPropsType, ref: React.Ref<unknown> | undefined) => {
  const { topButtonText, bottomButtonText, topButtonPress, bottomButtonPress, mainTitle, subTitle } = props;
  const [modalRef, modalInstance] = useModal();

  useImperativeHandle(
    ref,
    (): ModalConfirmRef => ({
      open: () => modalInstance()?.show(),
      close: () => modalInstance()?.close(),
    }),
  );

  return (
    <Modal animationType="fade" ref={modalRef} modalStyle={styles.modalContainer}>
      <TouchableWithoutFeedback {...a11y('Forgot.btnModalRegisWithoutFeedback')} onPress={() => {}}>
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View>
              <View style={styles.imageBox}>
                <Icon name="Alert" size={60} color="#fff" />
              </View>
              {mainTitle && <Text style={styles.modalMainText}>{mainTitle}</Text>}
              {subTitle && <Text style={styles.modalSubText}>{subTitle}</Text>}
            </View>
            <View style={styles.buttonBox}>
              {topButtonText && (
                <Button
                  text={topButtonText}
                  type="secondary"
                  uppercase={false}
                  labelStyle={styles.modalButtonText}
                  size="large"
                  style={[styles.mt32]}
                  onPress={() => topButtonPress && topButtonPress()}
                  id="Forgot.btnModalRegisNavigate"
                />
              )}
              {bottomButtonText && (
                <Button
                  text={bottomButtonText}
                  type="secondary"
                  uppercase={false}
                  labelStyle={styles.modalButtonText}
                  size="large"
                  style={[styles.bottomButton]}
                  onPress={() => bottomButtonPress && bottomButtonPress()}
                  id="Forgot.btnModalRegisCancel"
                />
              )}
            </View>
          </View>
        </View>
      </TouchableWithoutFeedback>
    </Modal>
  );
};

export default forwardRef(ModalConfirm);
