import React, { useEffect, useLayoutEffect, useState } from 'react';
import { SafeAreaView, View } from 'react-native';
import { useTranslation } from 'react-i18next';
import { useIsFocused, useNavigation } from '@react-navigation/native';
import { Icon, NativeModuleClient, useTheme } from 'superapp-common-component';
import {
  StackNavigationEventMap,
  StackNavigationProp,
} from '@react-navigation/stack';
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs';
import dayjs from 'dayjs';

import Text from '@/components/Text';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import { Theme } from '@/config/themes.config';
import { useInjectReducer, useInjectSaga } from '@/utils/injectors';

import RequestELetter from './components/RequestELetter';
import ClaimELetter from './components/ClaimELetter';
import OtherEletter from './components/OtherELetter';
import { NS_ELETTER_TYPE } from './stores/constants';
import eLetterReducer from './stores/reducer';
import eLetterHistorySaga from './stores/saga';
import useStyles from './styles';

type ELetterNavigationProp = StackNavigationProp<TStackParamList, 'ELetter'>;

const ELetter = () => {
  useInjectReducer({ key: NS_ELETTER_TYPE, reducer: eLetterReducer });
  useInjectSaga({ key: NS_ELETTER_TYPE, saga: eLetterHistorySaga });
  const navigation = useNavigation<ELetterNavigationProp>();
  const { t } = useTranslation();
  const theme = useTheme() as Theme;
  const styles = useStyles(theme);
  const [tabAnimating, setTabAnimating] = useState(false);
  const Tab = createMaterialTopTabNavigator();
  const isFocused = useIsFocused();

  const headerLeft = () => (
    <View style={styles.header}>
      <Text style={styles.textHeader}>{t('home.letter.header.inbox')}</Text>
    </View>
  );

  useLayoutEffect(() => {
    navigation.setOptions({
      header: () => headerLeft(),
    } as Partial<StackNavigationEventMap>);
  }, [navigation, t]);

  useEffect(() => {
    NativeModuleClient.AdobeExperience.trackScreen({
      screen: ROUTE_PATH.ELETTER,
      unixTimestamp: dayjs().unix(),
    });
  }, []);

  return (
    <SafeAreaView style={styles.flex1}>
      <Tab.Navigator
        initialRouteName="Request"
        screenOptions={{
          tabBarStyle: styles.tabBarStyle,
          tabBarContentContainerStyle: styles.tabBarContentContainerStyle,
          tabBarIndicatorStyle: styles.tabBarIndicatorStyle,
        }}
        screenListeners={({ navigation: navigationTab, route }) => ({
          tabPress: e => {
            // This adds a debounce to fix "switching tabs rapidly" bug
            if (tabAnimating) {
              e.preventDefault();
            } else {
              setTabAnimating(true);
              setTimeout(() => setTabAnimating(false), 300);

              // This solves the "tap same tab twice breaks tabs" bug, but not the "switching rapidly" bug
              e.preventDefault();
              navigationTab.navigate(route.name);
            }
          },
        })}>
        <Tab.Screen
          name="Request"
          component={RequestELetter}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon
                  name={focused ? 'Report-On' : 'Report'}
                  size={24}
                  color={focused ? '#1F78AD' : '#5B636B'}
                />
                <Text style={styles.tabBarLabelText}>
                  {t('home.letter.tab.request')}
                </Text>
              </View>
            ),
          }}
        />
        <Tab.Screen
          name="Claim"
          component={ClaimELetter}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon
                  name={focused ? 'Protect1' : 'Protect'}
                  size={24}
                  color={focused ? '#1F78AD' : '#5B636B'}
                />
                <Text style={styles.tabBarLabelText}>
                  {t('home.letter.tab.claim')}
                </Text>
              </View>
            ),
          }}
        />
        <Tab.Screen
          name="Others"
          component={OtherEletter}
          options={{
            tabBarLabel: ({ focused }) => (
              <View style={styles.tabBarLabelWrapper}>
                <Icon
                  name={focused ? 'Tag-On' : 'Tag'}
                  size={24}
                  color={focused ? '#1F78AD' : '#5B636B'}
                />
                <Text style={styles.tabBarLabelText}>
                  {t('home.letter.tab.other')}
                </Text>
              </View>
            ),
          }}
        />
      </Tab.Navigator>
    </SafeAreaView>
  );
};

export default ELetter;
