import { Platform, NativeModules, NativeEventEmitter, DeviceEventEmitter } from 'react-native';

export default !__DEV__
  ? Platform.select({
      android: DeviceEventEmitter,
      ios: new NativeEventEmitter(NativeModules.MiniAppEventEmitterModule),
    })
  : {
      remove: () => null,
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      addListener: (type: string, listener: (event: any) => void, context?: Object) => null,
    };
