import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import { LOADING_REQUEST, LOADING_SUCCESS, REQUEST_PROCESS_P820_ERROR, SAVE_SESSION_ID } from './constants';
import initialState, { TVerifyAccountP820State } from './state';

const verifyAccountP820Reducer = (state: TVerifyAccountP820State = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TVerifyAccountP820State>) => {
    switch (action.type) {
      case LOADING_REQUEST:
        draft.loading = true;
        break;
      case LOADING_SUCCESS:
        draft.loading = false;
        break;
      case REQUEST_PROCESS_P820_ERROR:
        draft.error = action.payload;
        break;
      case SAVE_SESSION_ID:
        draft.sessionId = action.payload;
        break;
    }
  });
};

export default verifyAccountP820Reducer;
