import React, { forwardRef, useImperativeHandle } from 'react'
import { View, Image } from 'react-native'
import { Button, Text } from 'superapp-common-component';

import Modal from '@/components/Modal';
import { useModal } from '@/components/Modal/Modal';

import styles from './styles'

export interface ModalMessageProps {
  typeMessage: 'error' | 'warning' | 'confirm'
  title?: string
  subtitle?: string
  btnCloseText: string
  btnConfirmText?: string
  onClose?: () => void
  onConfirm?: () => void
}

export interface ModalMessageRef {
  open: () => void;
  close: () => void;
}

const ModalMessage = (
  props: ModalMessageProps,
  ref: React.Ref<unknown> | undefined
) => {
  const {
    title,
    subtitle,
    btnCloseText,
    btnConfirmText,
    typeMessage = 'error',
    onClose,
    onConfirm,
  } = props
  // const [isVisible, setVisible] = useState(false)
  const [modalsRef, modalInstance] = useModal();

  const handleClose = () => {
    modalInstance()?.close();
    onClose && onClose();
  }

  const handleConfirm = () => {
    modalInstance()?.close();
    onConfirm && onConfirm();
  }

  useImperativeHandle(
    ref,
    (): ModalMessageRef => ({
      open: () => modalInstance()?.show(),
      close: () => modalInstance()?.close(),
    }),
  );

  return (
    <>
      <Modal ref={modalsRef} modalStyle={styles.modalStyle}>
        <View style={styles.modalContainer}>
          <View style={styles.modalContent}>
            <View style={styles.wrapImage}>
              {typeMessage === 'error' && (
                <Image
                  style={styles.imageStyles}
                  source={require('@/assets/images/icon-warning.png')}
                />
              )}
              {typeMessage === 'warning' && (
                <Image
                  style={styles.imageStyles}
                  source={require('@/assets/images/icon-warning.png')}
                />
              )}
              {typeMessage === 'confirm' && (
                <Image
                  style={styles.imageStyles}
                  source={require('@/assets/images/icon-confirm.png')}
                />
              )}
            </View>
            <View>
              <Text.Bold style={styles.title}>{title}</Text.Bold>
              <Text style={styles.subtitle}>{subtitle}</Text>
            </View>
            <View>
              {onConfirm && (
                <Button
                  text={btnConfirmText}
                  onPress={handleConfirm}
                  size='large'
                  uppercase={false}
                  labelStyle={styles.buttonText}
                  style={styles.mt_16}
                  id='ModalMessage.btnConfirmModalMessage'
                />
              )}
              <Button
                onPress={handleClose}
                text={btnCloseText}
                type='secondary'
                size='large'
                uppercase={false}
                labelStyle={styles.buttonText}
                style={styles.button}
                id='ModalMessage.btnCloseModalMessage'
              />
            </View>
          </View>
        </View>
      </Modal>
    </>
  )
}

export default forwardRef(ModalMessage)
