import React, { useState, useRef, useLayoutEffect, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { TouchableOpacity, View, KeyboardAvoidingView, Platform, SafeAreaView } from 'react-native';
import { connect, useSelector } from 'react-redux';
import { SendOTP, Icon, theme, Text as TextCommon, NativeModuleClient } from 'superapp-common-component';
import { createStructuredSelector } from 'reselect';
import { Dispatch } from 'redux';
import { useNavigation, useRoute, RouteProp } from '@react-navigation/native';
import NetInfo from '@react-native-community/netinfo';
import dayjs from 'dayjs';
import _isEmpty from 'lodash/isEmpty';

import { TFotgotPasswordTypeState } from '@/pages/ForgotPassword/stores/state';
import { TConnectState } from '@/stores/types';
import {
  makeSelectLoading,
  makeSelectIsVerifyOtpError,
  makeSelectInfoCapcha,
  makeSelectPhoneNumber,
  makeSelectIsCountFromScreenInfo,
} from '@/pages/ForgotPassword/stores/selectors';
import {
  requestOtpBySmsAction,
  submitOtp,
  resetOtpError as resetOtpErrorAction,
  getCapchaAction,
  submitVerifyPersonalIDNumber,
  setIsCountFromScreenInfoAction,
} from '@/pages/ForgotPassword/stores/actions';
import { StackNavigationEventMap, StackNavigationProp } from '@react-navigation/stack';
import { TStackParamList } from '@/navigators';
import ROUTE_PATH from '@/navigators/routePath';
import ModalError, { ModalConfirmErrorRef } from '@/components/ModalConfirmError';
import TurnstileCapcha, { TurnstileCapchaRef } from '@/components/TurnstileCapcha';
import Loading, { ILoadingRef } from '@/components/Loading';

import { HEADER_TITLE_BGRWHITE, HITSLOP } from '@/utils/styles';
import { a11y, maskedPhoneNumber } from '@/utils/utils';
import NoConnection from '@/components/NoConnection';

import styles from './styles';
import ModalForgotSession, { ModalForgotSessionRef } from '@/components/ModalForgotSession';
import { ERROR_CODE_OTP } from '@/utils/constants';

type RequestOtpProps = {
  loading: boolean;
  requestOtpBySms: (payload: {
    params: { captchaValue: string; otpSendType: string };
    requestOtpCallback: (number: number) => void;
  }) => void;
  handleSubmitOtp: (payload: { otpValue: string }) => Promise<void>;
  verifyOtpStatus: {
    isVerifyOtpError: boolean | undefined;
    errorType: string;
  };
  resetOtpError: () => void;
  getCapcha: () => MODEL.ICapcha;
  infoCapcha: any;
  submitVerifyPersonalIDNumberDispatch: (payload: any) => any;
  isCountFromScreenInfo: boolean;
  setIsCountFromScreenInfoActionDispatch: (payload: boolean) => void;
};
type RequestOtpNavigationProp = StackNavigationProp<TStackParamList, 'RequestOtp'>;
type ForgotPasswordOtpRouteProps = RouteProp<TStackParamList, 'ForgotUsernameOtp'>;

const { colors } = theme;

const BLOCK_SUBMIT_OTP_ERROR_TYPE = 'request_otp_locked_out';
const OTP_TYPE = {
  voice: 'Voice',
  sms: 'SMS',
};

const RequestOtp = (props: RequestOtpProps) => {
  const navigation = useNavigation<RequestOtpNavigationProp>();
  const { loading, isCountFromScreenInfo, handleSubmitOtp, submitVerifyPersonalIDNumberDispatch, setIsCountFromScreenInfoActionDispatch } =
    props;
  const phoneReplace = useSelector(makeSelectPhoneNumber());
  const { t } = useTranslation();
  const netInfo = NetInfo.useNetInfo();
  const route = useRoute<ForgotPasswordOtpRouteProps>();
  const { allowCancel } = route.params || {};
  const modalErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const turnstileCapchaRef = useRef<TurnstileCapchaRef>(null);
  const modalCommonErrorRef = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockSubmitOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalBlockRequestOtp = useRef<null | ModalConfirmErrorRef>(null);
  const modalForgotSessionExpiredRef = useRef<ModalForgotSessionRef>(null);
  const loadingRef = useRef<ILoadingRef>(null);

  const [otpValue, setOtpValue] = useState<string>('');
  const [seconds, setSeconds] = useState<number>(0);
  const [otpType, setOtpType] = useState<any>(null);
  const [isConnected, setConnected] = useState<null | boolean>(null);

  const onCloseModalErrorCommon = () => {
    navigation.navigate(ROUTE_PATH.FORGOT_PASSWORD);
  };

  const onHandleTypeResend = (type: string) => {
    setOtpType(type);
    setOtpValue('');
    turnstileCapchaRef?.current?.open();
  };

  const onResendOtp = (token: string) => {
    turnstileCapchaRef?.current?.close();
    const callback = (errValue: string) => {
      if (!_isEmpty(errValue)) {
        if (errValue === BLOCK_SUBMIT_OTP_ERROR_TYPE) {
          modalBlockRequestOtp?.current?.open();
        } else {
          modalCommonErrorRef?.current?.open();
        }
      } else {
        setSeconds(120);
      }
    };
    submitVerifyPersonalIDNumberDispatch({ captchaValue: token, otpType, callback });
  };

  const onSubmitOtp = async () => {
    NativeModuleClient.AdobeExperience.trackButton({
      screen: ROUTE_PATH.REQUEST_OTP,
      unixTimestamp: dayjs().unix(),
      buttonText: 'Verify',
    });
    if (loading) return;
    if (otpValue.length === 6) {
      try {
        await handleSubmitOtp({ otpValue });
        navigation.navigate(ROUTE_PATH.CREATE_NEW_PASS);
      } catch (error) {
        if (error === ERROR_CODE_OTP.OTP_DISABLED) {
          modalBlockSubmitOtp?.current?.open();
        } else {
          modalErrorRef?.current?.open();
        }
      }
    } else {
      modalErrorRef?.current?.open();
    }
  };

  useLayoutEffect(() => {
    navigation.setOptions({
      headerTitle: () => <TextCommon.Bold style={HEADER_TITLE_BGRWHITE}>{t('forgot.password.modal.otp.header')}</TextCommon.Bold>,
      headerLeft: () => (
        <TouchableOpacity
          {...a11y('Forgot.btnOtpHeaderGoback')}
          onPress={() => {
            NativeModuleClient.AdobeExperience.trackButton({
              screen: ROUTE_PATH.REQUEST_OTP,
              unixTimestamp: dayjs().unix(),
              buttonText: 'Go back',
            });
            navigation.canGoBack() && navigation.goBack();
          }}
          hitSlop={HITSLOP}
        >
          <Icon name="Arrow-Left" size={24} color={colors.primary.charcoal[80]} style={styles.marginLeft21} />
        </TouchableOpacity>
      ),
      headerTitleAlign: 'center',
    } as Partial<StackNavigationEventMap>);
  }, [navigation]);

  useEffect(() => {
    if (isCountFromScreenInfo) {
      setSeconds(120);
    }
    return () => setIsCountFromScreenInfoActionDispatch(false);
  }, []);

  useEffect(() => {
    if (netInfo.isConnected !== null) {
      setConnected(netInfo.isConnected);
    }
  }, [netInfo]);

  if (isConnected === null) return null;

  return isConnected ? (
    <SafeAreaView style={{ flex: 1, backgroundColor: 'white' }}>
      <View style={{ flex: 1 }}>
        <KeyboardAvoidingView
          behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
          style={{ flex: 1 }}
          keyboardVerticalOffset={Platform.OS === 'ios' ? 75 : 0}
        >
          <View style={styles.containerOtp}>
            <View style={styles.otpContent}>
              <SendOTP
                textDescription={t('common.otp.textPhone', { phone: maskedPhoneNumber(phoneReplace) })}
                textTimeCount={t('common.otp.text.countTimeTitle')}
                submitText={t('forgot.password.modal.otp.button.verify')}
                onChange={code => setOtpValue(code)}
                sendBySMS={() => onHandleTypeResend(OTP_TYPE.sms)}
                sendByVoice={() => onHandleTypeResend(OTP_TYPE.voice)}
                onSubmit={onSubmitOtp}
                loading={loading}
                disabled={loading}
                secondNumber={seconds}
                setSecondNumber={setSeconds}
                textSMS={t('common.otp.text.resend')}
                textVoice={t('common.otp.text.viaCall')}
                textCountSecond={t('common.text.countSecond')}
                textNotSendSMS={t('common.otp.text.noSms')}
                id="Forgot.otpRequestOtp"
              />
            </View>
          </View>
        </KeyboardAvoidingView>
      </View>
      <TurnstileCapcha ref={turnstileCapchaRef} onSuccess={onResendOtp} onError={() => null} />
      <ModalError
        ref={modalErrorRef}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('forgot.password.modal.otp.error.des')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
      />
      <ModalError
        ref={modalCommonErrorRef}
        title={t('forgot.password.modal.otp.wrong.title')}
        subtitle={t('forgot.password.modal.change.pass.sub.error')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
        onConfirm={onCloseModalErrorCommon}
      />
      <ModalError
        ref={modalBlockSubmitOtp}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('common.message.otpDisable')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
        onConfirm={() => navigation.canGoBack() && navigation.goBack()}
      />
      <ModalError
        ref={modalBlockRequestOtp}
        title={t('forgot.password.modal.otp.error.title')}
        subtitle={t('forgot.password.modal.otp.error.block.request.otp')}
        btnText={t('forgot.password.modal.otp.error.button.close')}
      />
      <ModalForgotSession ref={modalForgotSessionExpiredRef} handleClose={() => navigation.navigate(ROUTE_PATH.FORGOT_PASSWORD)} />
      <Loading visible={loading} ref={loadingRef} />
    </SafeAreaView>
  ) : (
    <SafeAreaView style={{ flex: 1 }}>
      <NoConnection />
    </SafeAreaView>
  );
};

type CreateStructuredSelectorType = {
  loading: TFotgotPasswordTypeState['loading'];
  verifyOtpStatus: TFotgotPasswordTypeState['verifyOtpStatus'];
  infoCapcha: TFotgotPasswordTypeState['infoCapcha'];
  isCountFromScreenInfo: TFotgotPasswordTypeState['isCountFromScreenInfo'];
};

const mapStateToProps = createStructuredSelector<TConnectState, CreateStructuredSelectorType>({
  loading: makeSelectLoading(),
  verifyOtpStatus: makeSelectIsVerifyOtpError(),
  infoCapcha: makeSelectInfoCapcha(),
  isCountFromScreenInfo: makeSelectIsCountFromScreenInfo(),
});

type MapDispatchToPropsType = {
  requestOtpBySms: (payload: {
    params: { captchaValue: string; otpSendType: string };
    requestOtpCallback: (number: number) => void;
  }) => any;
  handleSubmitOtp: (payload: { otpValue: string }) => Promise<void>;
  resetOtpError: () => void;
  getCapcha: () => any;
  submitVerifyPersonalIDNumberDispatch: (payload: any) => void;
  setIsCountFromScreenInfoActionDispatch: (payload: boolean) => void;
};

const mapDispatchToProps = (dispatch: Dispatch): MapDispatchToPropsType => ({
  requestOtpBySms: payload => dispatch(requestOtpBySmsAction(payload)),
  handleSubmitOtp: (payload: { otpValue: string }) => new Promise((resolve, reject) => dispatch(submitOtp(payload, resolve, reject))),
  resetOtpError: () => dispatch(resetOtpErrorAction()),
  getCapcha: () => dispatch(getCapchaAction()),
  submitVerifyPersonalIDNumberDispatch: (payload: any) => dispatch(submitVerifyPersonalIDNumber(payload)),
  setIsCountFromScreenInfoActionDispatch: (payload: boolean) => dispatch(setIsCountFromScreenInfoAction(payload)),
});

export default connect(mapStateToProps, mapDispatchToProps)(RequestOtp);
