import { Draft, produce } from 'immer';

import { TAction } from '@/stores/types';

import {
  LOADING_ECARD_REQUEST,
  LOADING_ECARD_SUCCESS,
  SAVE_ECARDS,
} from './constants';
import initialState, { TECardTypeState } from './state';

const eCardReducer = (state: TECardTypeState = initialState, action: TAction) => {
  return produce(state, (draft: Draft<TECardTypeState>) => {
    switch (action.type) {
      case LOADING_ECARD_REQUEST: {
        draft.loading = true;
        break;
      }
      case LOADING_ECARD_SUCCESS: {
        draft.loading = false;
        break;
      }
      case SAVE_ECARDS: {
        draft.ecards = action?.payload?.sort((a: any, b: any) => a.fullName?.localeCompare(b.fullName)
          || a.policyNumber?.localeCompare(b.policyNumber) 
          || a.productName?.localeCompare(b.productName)
        );
        break;
      }
    }
  });
};

export default eCardReducer;
