import { createSelector } from 'reselect';

import initialState from './state';
import { NS_FORGOT_USERNAME_TYPE } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_FORGOT_USERNAME_TYPE] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectInfoCaptcha = () => createSelector(currentState, state => state.infoCaptcha);
const makeSelectIsVerifyOtpError = () => createSelector(currentState, state => state.verifyOtpStatus);
const makeSelectIsCaptchaError = () => createSelector(currentState, state => state.isCaptchaError);
const makeSelectUserId = () => createSelector(currentState, state => state.userId);
const makeSelectSessionId = () => createSelector(currentState, state => state.sessionId);
const makeSelectPhoneNumber = () => createSelector(currentState, state => state.phoneNumber);
const makeSelectUsername = () => createSelector(currentState, state => state.username);
const makeSelectIdNo = () => createSelector(currentState, state => state.idno);
const makeSelectDob = () => createSelector(currentState, state => state.dob);
const makeSelectOtpId = () => createSelector(currentState, state => state.otpId);

export {
  currentState,
  makeSelectLoading,
  makeSelectInfoCaptcha,
  makeSelectIsVerifyOtpError,
  makeSelectIsCaptchaError,
  makeSelectUserId,
  makeSelectSessionId,
  makeSelectPhoneNumber,
  makeSelectUsername,
  makeSelectIdNo,
  makeSelectDob,
  makeSelectOtpId,
};
