import { createSelector } from 'reselect';

import initialState from './state';
import { NS_VERIFY_ACCOUNT } from './constants';
import { TConnectState } from '@/stores/types';

const currentState = (state: TConnectState) => state?.[NS_VERIFY_ACCOUNT] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectInfoVerifyAccount = () => createSelector(currentState, state => state.responseVerifyAccount);
export { currentState, makeSelectLoading , makeSelectInfoVerifyAccount};
