import { Platform, PermissionsAndroid, Alert } from 'react-native';
import RNFetchBlob from 'react-native-blob-util';
import dayjs from 'dayjs';
import DeviceInfo from 'react-native-device-info';
import _toLength from 'lodash/toLength';

import { getItem } from './storage';
import { STORAGE_NAME } from './constants';
import { isEmpty, nanoid } from './utils';

const ANDROID_VERSION = 13;
const NANOID_LENGTH = 16;
const MAX_LENGTH_FILE_NAME = 127;
const apiLevel = DeviceInfo.getSystemVersion();
const currentVersionLower13 = Number(apiLevel) < ANDROID_VERSION;

const getFileNameAndExt = (name?: string) => {
  if (!name) {
    return {};
  }
  const lastDot = name.lastIndexOf('.');
  const fileName = name.substring(0, lastDot);
  const ext = name.substring(lastDot + 1);

  return {
    fileName,
    ext,
  };
};

const checkMaxLengthFileName = (fileName?: string) => {
  if (isEmpty(fileName)) {
    return `download-${nanoid(undefined, NANOID_LENGTH)}.pdf`;
  }

  const lengthFileName = _toLength(fileName);
  const file = getFileNameAndExt(fileName);

  if (lengthFileName >= MAX_LENGTH_FILE_NAME || lengthFileName + NANOID_LENGTH >= MAX_LENGTH_FILE_NAME) {
    return `${file.fileName?.slice(0, 100)}-${nanoid(undefined, NANOID_LENGTH)}.${file.ext}`;
  }

  return `${file.fileName}-${nanoid(undefined, NANOID_LENGTH)}.${file.ext}`;
};

const onDowloadFilePdf = async (
  contentForm: string,
  url: string,
  callbackFail: () => void,
  callbackSuccess: () => void,
  isAuthen: boolean = true,
) => {
  let grantedAndroid: any = {};

  if (Platform.OS === 'android' && currentVersionLower13) {
    grantedAndroid = await PermissionsAndroid.requestMultiple([
      PermissionsAndroid.PERMISSIONS.READ_EXTERNAL_STORAGE,
      PermissionsAndroid.PERMISSIONS.WRITE_EXTERNAL_STORAGE,
    ]);
  }
  const token = await getItem(STORAGE_NAME.ACCESS_TOKEN);
  const {
    dirs: { DownloadDir, DocumentDir },
  } = RNFetchBlob.fs;
  const directoryPath = Platform.select({
    ios: DocumentDir,
    android: DownloadDir,
  });
  const filePathDefault = `${directoryPath}/${'download' + '-' + dayjs().format('h-mm-ss-DD-MM-YYYY')}.pdf`;
  const filePathDownload = contentForm
    ? `${directoryPath}/${checkMaxLengthFileName(`${contentForm.replace('/', ' - ')}-${dayjs().format('h-mm-ss-DD-MM-YYYY')}.pdf`)}`
    : filePathDefault;

  const configOptions = Platform.select({
    ios: {
      fileCache: true,
      path: filePathDownload,
      notification: true,
      appendExt: 'pdf',
    },
    android: {
      fileCache: true,
      addAndroidDownloads: {
        path: filePathDownload,
        useDownloadManager: true,
        mediaScannable: true,
        notification: true,
        mime: 'application/pdf',
      },
    },
  });

  RNFetchBlob.config({ ...configOptions })
    .fetch(
      'GET',
      url,
      isAuthen
        ? {
            Authorization: `Bearer ${token}`,
            'Cache-Control': 'no-store',
          }
        : {
            'Cache-Control': 'no-store',
          },
    )
    .then((res: any) => {
      switch (Platform.OS) {
        case 'ios': {
          if (res?.respInfo?.status === 200) {
            callbackSuccess();
            setTimeout(() => {
              RNFetchBlob.ios.openDocument(res?.data);
            }, 1000);
          } else {
            throw new Error('Dowload fail');
          }
          break;
        }
        case 'android': {
          if (
            (grantedAndroid[PermissionsAndroid.PERMISSIONS.READ_EXTERNAL_STORAGE] === PermissionsAndroid.RESULTS.GRANTED &&
              grantedAndroid[PermissionsAndroid.PERMISSIONS.READ_EXTERNAL_STORAGE] === PermissionsAndroid.RESULTS.GRANTED) ||
            !currentVersionLower13
          ) {
            callbackSuccess();
            setTimeout(() => {
              RNFetchBlob.android.actionViewIntent(res.path(), 'application/pdf');
            }, 600);
          } else {
            throw new Error('Dowload fail');
          }
          break;
        }
        default:
      }
    })
    .catch(() => {
      callbackFail();
    });
};

