import { createSelector } from 'reselect';

import { TConnectState } from '@/stores/types';

import initialState from './state';
import { NS_VERIFY_ACCOUNT_P820 } from './constants';

const currentState = (state: TConnectState) => state?.[NS_VERIFY_ACCOUNT_P820] || initialState;
const makeSelectLoading = () => createSelector(currentState, state => state.loading);
const makeSelectSessionId = () => createSelector(currentState, state => state.sessionId);
const makeSelectProccessError = () => createSelector(currentState, state => state.error);

export { makeSelectLoading, makeSelectSessionId, makeSelectProccessError };
