export const getELetterList = (payload?: ELetterParam): TAction => ({ type: GET_ELETTER_LIST, payload });
export const saveELetterList = (payload: MODEL.IELetter[]): TAction => ({ type: SAVE_ELETTER_LIST, payload });
export const setLoadMoreRequestELetter = (payload: boolean): TAction => ({ type: SET_LOAD_MORE_REQUEST_ELETTER, payload });
