import { TAction } from '@/stores/types';

import { 
    LOADING_REQUEST,
    LOADING_SUCCESS,
    LOADING_REQUEST_OTP,
    GET_INFO_REGISTER,
    SAVE_INFO_REGISTER,
    REQUEST_OTP_BY_SMS,
    SUBMIT_REGISTER,
    GET_CONTENT_HELP_CENTER,
    SAVE_CONTENT_HELP_CENTER,
    VERIFY_OTP,
    GET_CAPCHA,
    SAVE_CAPCHA,
    LOGIN_AFTER_REGISTER_SUCCESS,
    CHECK_EMAIL_PHONE_EXIST
} from './constants';

export const loadingSuccessAction = (): TAction => ({ type: LOADING_SUCCESS });
export const loadingRequestAction = (): TAction => ({ type: LOADING_REQUEST });
export const loadingRequestOtpAction = (payload: boolean): TAction => ({ type: LOADING_REQUEST_OTP, payload });

export const getInfoRegisterAction = (payload: any): TAction => ({ type: GET_INFO_REGISTER, payload });
export const saveInfoRegisterAction = (payload: any): TAction => ({ type: SAVE_INFO_REGISTER, payload });
export const requestOtpBySmsAction = (payload: any): TAction => ({ type: REQUEST_OTP_BY_SMS, payload });
export const submitRegisterAction = (payload: any): TAction => ({ type: SUBMIT_REGISTER, payload });
export const getContentHelpCenterAction = (): TAction => ({ type: GET_CONTENT_HELP_CENTER });
export const saveContentHelpCenterAction = (payload: any): TAction => ({ type: SAVE_CONTENT_HELP_CENTER, payload });

export const verifyOtpAction = (payload: any): TAction => ({ type: VERIFY_OTP, payload });

export const getCapchaAction = (): TAction => ({ type: GET_CAPCHA });
export const saveCapchaAction = (payload: any): TAction => ({ type: SAVE_CAPCHA, payload });

export const loginAfterRegisterSuccessAction = (payload: any): TAction => ({ type: LOGIN_AFTER_REGISTER_SUCCESS, payload });

export const checkEmailPhoneExistAction = (payload: any, resolve: any): TAction => ({ type: CHECK_EMAIL_PHONE_EXIST, payload, resolve });
export function asyncCheckEmailPhoneExistAction(dispatch: any) {
    return function returnAsync(payload: any) {
      return new Promise(resolve => dispatch(checkEmailPhoneExistAction(payload, resolve)));
    };
}